import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import MediaServerEventConnectionRetryManager from 'app/js/managers/MediaServerEventConnectionRetryManager';
import ServerEventManager from 'app/js/managers/ServerEventManager';
import config from 'app/js/models/config';
import capabilities from 'app/js/utils/device/capabilities';

function isValidServer(server: any) {
  return server.get('isFullOwnedServer') || server.get('isFullServer');
}

export default function mediaServerEventInitializer() {
  let serverEventManager: any;

  if (capabilities.hasWebSocket) {
    serverEventManager = new ServerEventManager({
      name: 'MediaServerEventManager',
      appModel: resolve('appModel'),
      isValidServer,
      supportsLongPolling: false,
      allowMultipleConnections: config.allowMultipleWebSocketConnections,
      ServerEventConnection: resolve('mediaServerEventConnection'),
      ServerRetryManager: MediaServerEventConnectionRetryManager,
    });

    serverEventManager.start();
  }

  return $.Deferred().resolve().promise();
}
