/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectUserExperienceSettings from 'app/js/stores/selectors/user/selectUserExperienceSettings';

const selectSubtitleSettings = createSelector(
  selectUserExperienceSettings,
  (experienceSettings) => {
    return {
      color: experienceSettings.subtitleColor,
      position: experienceSettings.subtitlePosition,
      size: experienceSettings.subtitleSize,
    };
  }
);

export default selectSubtitleSettings;
