import BaseCollection from 'app/js/models/BaseCollection';
import VersionClientProfileModel from 'app/js/models/library/convert/VersionClientProfileModel';
import proxyTypes from 'app/js/utils/constants/proxyTypes';

const VersionClientProfileCollection = BaseCollection.extend({
  //
  // Properties

  model: VersionClientProfileModel,
  url: '/clients/profiles?type=' + proxyTypes.LOCAL,

  //
  // Control

  parse: function (response: any) {
    return this.convert(response, 'Profile');
  },
});

export default VersionClientProfileCollection;
