function formatResolution(
  resolution: string | null | undefined,
  scanType: string | null | undefined
): string {
  if (!resolution) {
    return '';
  }

  if (/^\d+$/.test(resolution)) {
    const scanTypeAbbrev = scanType?.length ? scanType[0] : 'p';
    return resolution + scanTypeAbbrev;
  } else {
    return resolution.toUpperCase();
  }
}

export default formatResolution;
