/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { METADATA_XML_ELEMENT_NAMES } from './replaceMetadataXMLElementNames';

const metadataChildrenTypes = _.reduce(
  METADATA_XML_ELEMENT_NAMES,
  (acc, elementName) => {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    acc[elementName] = 'Metadata';
    return acc;
  },
  {}
);

function splitChildren(json: any) {
  if (!json || !json._children) {
    return json;
  }

  // Legacy JSON from the server will use a single `_children` array while our
  // models expect a separate array for each collection.

  const parent = _.clone(json);
  const children = parent._children;

  if (Array.isArray(children)) {
    children.forEach((child) => {
      const elementType = child._elementType;
      child._elementType = null;
      child.elementName = elementType;

      if (elementType) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        const key = metadataChildrenTypes[elementType] || elementType;
        const existing = parent[key];
        const splitChild = splitChildren(child);

        if (existing) {
          if (Array.isArray(existing)) {
            existing.push(splitChild);
          } else {
            parent[key] = [existing, splitChild];
          }
        } else {
          parent[key] = splitChild;
        }
      }
    });

    parent._children = null;
  }

  return parent;
}

export default splitChildren;
