/* eslint-plugin-disable @typescript-eslint */

import { getAllSourceTypes, UNKNOWN } from 'app/js/utils/types/sourceTypes';
import isDirectoryForSourceType from './isDirectoryForSourceType';

export default function getSourceTypeForDirectory(
  provider: any,
  directory: any
) {
  if (!provider || !directory || directory.id === 'hubs') {
    return;
  }

  const allTypes = getAllSourceTypes();
  for (let i = 0, len = allTypes.length; i < len; i++) {
    const type = allTypes[i];

    if (isDirectoryForSourceType(type, provider, directory)) {
      return type;
    }
  }

  return allTypes.find((type: any) => type.key === UNKNOWN);
}
