/* eslint-plugin-disable @typescript-eslint */

import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';

export default function isLibraryItem(
  providerIdentifier: any,
  metadataKey: any
) {
  if (providerIdentifier) {
    return providerIdentifiers.matches(
      providerIdentifier,
      providerIdentifiers.LIBRARY
    );
  } else {
    return /^(\/sync\/[\w\d-/]*)?\/library\//.test(metadataKey);
  }
}
