/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import config from 'app/js/models/config';
import AbstractStorageModel from 'app/js/models/storage/AbstractStorageModel';
import * as preferInsecureConnectionsOptions from 'app/js/utils/constants/preferInsecureConnectionsOptions';

const DesktopSettingsModel = AbstractStorageModel.extend({
  //
  // Properties

  name: 'DesktopSettingsModel',
  storageKey: 'settingsv2',

  syncedAttrs: {
    // General
    preferInsecureConnections: attrTypes.string,

    // Sync
    'syncQuality!video': attrTypes.integer,
    'syncQuality!music': attrTypes.integer,
    'syncQuality!photo': attrTypes.integer,

    // Hidden
    viewedUnclaimedServers: attrTypes.list,
    dismissedUnclaimedServers: attrTypes.list,
  },

  defaults() {
    return {
      // General
      preferInsecureConnections: config.shouldPreferInsecureLocalConnections
        ? preferInsecureConnectionsOptions.SAME_NETWORK
        : preferInsecureConnectionsOptions.NEVER,

      // Sync
      'syncQuality!video': 8,
      'syncQuality!music': 2,
      'syncQuality!photo': 2,

      // Hidden
      viewedUnclaimedServers: [],
      dismissedUnclaimedServers: [],
    };
  },
});

export default DesktopSettingsModel;
