/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import config from 'app/js/models/config';
import BaseClass from 'app/js/utils/BaseClass';
import * as metricsEvents from 'app/js/utils/constants/metrics/metricsEvents';
import assert from 'app/js/utils/log/assert';
import log from 'app/js/utils/log/log';
import getPerformanceMeasureName from 'app/js/utils/metrics/getPerformanceMeasureName';
import performance from 'app/js/utils/metrics/performance';
import roundMetricsDuration from 'app/js/utils/metrics/roundMetricsDuration';
import updateSessionLastInteractionTime from 'app/js/utils/metrics/updateSessionLastInteractionTime';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import uuidV4 from 'app/js/utils/string/uuidV4';
import { toMetricsProperties } from 'app/js/v4/core/ui/metrics/components/MetricsContextProvider';

declare global {
  interface Window {
    AF_SDK: {
      PLUGINS?: {
        PBA?: {
          turnOnDevMode(): void;
        };
      };
      utils?: {
        cookie?: {
          get(name: string): string | undefined;
        };
      };
    };
  }
}

const OFF = 'off';
const ANONYMOUS = 'anonymous';
export const INTERACTION_SESSION_TIMEOUT = 1800000; // 30 minutes
// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const MetricsManager = BaseClass.extend({
  mixins: eventMixin,
  //
  // Initialize
  initialize(options: any) {
    this.privacySettings = null;
    this.defaults = {};
    this._pendingPrivacyRequest = null;
    this._pendingEvents = [];
    // See onUserIDChange for more info about this debounce
    this.debouncedFetchPrivacySettings = _.debounce(
      this.fetchPrivacySettings,
      250
    );
    if (__HOSTED__ && __DEV__) {
      window.AF_SDK?.PLUGINS?.PBA?.turnOnDevMode();
    }
    if (__DEV__) {
      assert(
        options.isPlaybackActive,
        'An `isPlaybackActive` function must be provided to a metrics manager to aid in determining session idle state'
      );
    }
    this.isPlaybackActive = options.isPlaybackActive;
    this.enable();
  },
  //
  // Control
  enable() {
    this.enabled = true;
  },
  disable() {
    this.enabled = false;
  },
  set(options: any) {
    this.defaults = {
      ...this.defaults,
      ...options,
    };
  },
  setUser(user: any) {
    this.set({ userId: user.id });
    if (!this.defaults.context) {
      this.defaults.context = {};
    }
    this.defaults.context.user = user.get('subscriptions').reduce(
      (acc: any, subscription: any) => {
        acc[subscription.get('type')] = subscription.get('mode');
        return acc;
      },
      {
        anonymous: user.get('anonymous'),
      }
    );
  },
  fetchPrivacySettings() {
    // If we're re-fetching privacy settings, reset so events are not sent until
    // we get a response.
    this.privacySettings = null;
    if (this._pendingPrivacyRequest) {
      this._pendingPrivacyRequest.abort();
      this._pendingPrivacyRequest = null;
    }
    const user = resolve('appModel').get('user');
    if (!user.server.get('isConnected')) {
      return $.Deferred().reject().promise();
    }
    const xhrOptions = user.server.getXHROptions('/api/v2/user/privacy', {
      dataType: 'json',
    });
    this._pendingPrivacyRequest = $.ajax(xhrOptions);
    return this._pendingPrivacyRequest
      .promise()
      .done((privacySettings: any) => {
        this.setUser(user);
        this.setPrivacySettings(privacySettings);
        this.trackPendingEvents();
        this.sendMMPEvent();
      })
      .always(() => {
        this._pendingPrivacyRequest = null;
      });
  },
  setPrivacySettings(rawPrivacySettings: any) {
    if (!rawPrivacySettings || !rawPrivacySettings.metrics) {
      return;
    }
    this.privacySettings = {
      ...rawPrivacySettings,
      metrics: rawPrivacySettings.metrics.reduce((acc: any, metric: any) => {
        acc[metric.event] = metric.status;
        return acc;
      }, {}),
    };
    if (!__DEV__) {
      const metricsReporter = resolve('metricsReporter');
      metricsReporter.setBaseUrl(rawPrivacySettings.baseUrl);
    }
  },
  sendMMPEvent() {
    if (!__HOSTED__) {
      return;
    }

    // Appsflyer SDK has no 'onready' event to bind to, so we have to rely on
    // checking the object for initialization on an interval. Average
    // initialization time on 3Mbps connection is about 50ms from the first
    // time this function is called.

    this._appsflyerIntervalID = window.setInterval(() => {
      const afUserId = window.AF_SDK?.utils?.cookie?.get('afUserId');

      if (afUserId) {
        window.clearInterval(this._appsflyerIntervalID);

        if (afUserId) {
          resolve('metrics').trackEvent({
            event: metricsEvents.CLIENT_MMP_START,
            interaction: false,
            properties: {
              identifier: afUserId,
            },
          });
        }
      }
    }, 50);

    this._appsflyerTimeoutID = window.setTimeout(() => {
      // Clear interval in case SDK fails or is blocked.
      window.clearInterval(this._appsflyerIntervalID);
    }, 1500);
  },
  incrementPlaybackCount() {
    const key = 'playbackCount';
    const sessionStats = resolve('sessionStats');
    sessionStats.save(key, sessionStats.get(key) + 1);
  },
  addPlaybackTime(elapsedTime: any) {
    const key = 'playbackTime';
    const sessionStats = resolve('sessionStats');
    sessionStats.save(key, sessionStats.get(key) + elapsedTime);
  },
  trackView(properties: any, options: any) {
    const event = metricsEvents.CLIENT_VIEW;
    const payload = {
      event,
      interaction: true,
      properties,
      ...options,
    };
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 3 arguments, but got 2.
    const measure = performance.measure(
      getPerformanceMeasureName(event, properties),
      `${event}:${properties.page}-start`
    );
    if (measure) {
      payload.properties = {
        ...payload.properties,
        latency: Math.round(measure.duration),
      };
    }
    this.trackEvent(payload);
  },
  trackClick(properties: any, options: any) {
    this.trackEvent({
      event: metricsEvents.CLIENT_CLICK,
      interaction: true,
      properties,
      ...options,
    });
  },
  trackEvent(options: any) {
    if (!this.enabled) {
      return;
    }
    const contextTuple = options.properties?.contextTuple;
    if (contextTuple) {
      delete options.properties.contextTuple;
      Object.assign(options.properties, toMetricsProperties(contextTuple));
    }
    const sessionStats = resolve('sessionStats');
    const sessionIdentifier = sessionStats.get('sessionId');
    const anonymousSessionIdentifier = sessionStats.get('anonymousSessionId');
    // If privacy settings have not been fetched yet, hold on to these metrics
    // events until they come back. We do not know which events should be
    // tracked and which events should be anonymous without them.
    if (!this.privacySettings) {
      this._pendingEvents.push({
        ...options,
        sessionIdentifier,
        anonymousSessionIdentifier,
      });
      return;
    }
    const event = $.extend(true, {}, this.defaults, options);
    // If the client:shutdown event comes from the pending queue, we do not want
    // to set the next session's identifier on the event, overriding the
    // previous session's identifier.
    if (!event.sessionIdentifier) {
      event.sessionIdentifier = sessionIdentifier;
    }
    if (!event.anonymousSessionIdentifier) {
      event.anonymousSessionIdentifier = anonymousSessionIdentifier;
    }
    const now = Date.now();
    const isFlushEvent = !(
      options.event === metricsEvents.CLIENT_SHUTDOWN ||
      options.event === metricsEvents.CLIENT_START
    );
    // Session-start and stop events skip session-freshness checking
    if (isFlushEvent) {
      const lastInteractionTime = sessionStats.get('lastInteractionTime');
      const lastPlayingHeartbeatTime = sessionStats.get(
        'lastPlayingHeartbeatTime'
      );
      const recentSessionTime = Math.max(
        lastInteractionTime,
        lastPlayingHeartbeatTime
      );
      const durationSinceRecentSessionTime = now - recentSessionTime;
      if (durationSinceRecentSessionTime > INTERACTION_SESSION_TIMEOUT) {
        this.flushSession().then(() => {
          this.trackEvent(options);
        });
        return;
      }
    }
    const allEvents = this.privacySettings.metrics;
    const status = allEvents[event.event];
    if (status) {
      if (status === OFF) {
        __DEV__ &&
          log('[Metrics] Ignoring opted out metrics event: ' + event.event);
      } else {
        if (event.interaction) {
          updateSessionLastInteractionTime();
        }
        if (status === ANONYMOUS) {
          delete event.userId;
          event.deviceIdentifier = resolve('appModel').get('anonymousClientID');
          event.sessionIdentifier = event.anonymousSessionIdentifier;
        }
        delete event.anonymousSessionIdentifier;
        const metricsReporter = resolve('metricsReporter');
        metricsReporter.trackEvent(event);
      }
    } else {
      __DEV__ &&
        log('[Metrics] Ignoring unknown metrics event: ' + event.event);
    }
  },
  trackPendingEvents() {
    const events = this._pendingEvents;
    let i = 0;
    while (i < events.length) {
      this.trackEvent(events[i]);
      i++;
    }
    this._pendingEvents = [];
  },
  start() {
    const user = resolve('appModel').get('user');
    this.flushSession()
      // Wait for the cloud server to have an access token before fetching
      // privacy settings.
      .then(() => alwaysResolve(user.server.populate()))
      // Fetch privacy settings regardless of whether the user successfully
      // signed in or not. A signed out user will use the defaults.
      .then(() => this.fetchPrivacySettings())
      .always(() => {
        this.listenTo(
          user.server,
          'change:isConnected',
          this.onCloudServerChange
        );
        this.listenTo(user, 'change:id', this.onUserIDChange);
      });
    // Do not hold up app initialization while privacy settings are fetched.
    return $.Deferred().resolve().promise();
  },
  flushSession() {
    const sessionStats = resolve('sessionStats');
    const promise = sessionStats.populate().then(
      () => {
        // Complete the session which is in storage.
        this.stopSession();
        this.startSession();
      },
      () => {
        this.startSession();
      }
    );
    return alwaysResolve(promise);
  },
  startSession() {
    const startTime = Date.now();
    const sessionStats = resolve('sessionStats');
    if (!this.interval) {
      const heartbeatInterval = config.heartbeatInterval;
      if (__DEV__) {
        assert(
          heartbeatInterval < INTERACTION_SESSION_TIMEOUT,
          'heartbeat interval should be less than interaction session timeout'
        );
      }
      this.interval = window.setInterval(() => {
        const now = Date.now();
        const attrs = {
          heartbeatTime: now,
        };
        if (this.isPlaybackActive()) {
          (attrs as any).lastPlayingHeartbeatTime = now;
        }
        sessionStats.set(attrs);
      }, heartbeatInterval);
    }
    sessionStats.save({
      sessionId: uuidV4(),
      anonymousSessionId: uuidV4(),
      startTime,
      lastInteractionTime: startTime,
      heartbeatTime: startTime,
      lastPlayingHeartbeatTime: null,
      playbackCount: 0,
      playbackTime: 0,
    });
    const event = metricsEvents.CLIENT_START;
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    const measure = performance.endPeriod(event);
    const payload = {
      event,
      interaction: true,
      properties: { mode: 'uno' },
    };
    if (measure) {
      (payload.properties as any).latency = Math.round(measure.duration);
    }
    this.trackEvent(payload);
  },
  stopSession() {
    // Track previous session shutdown.
    const sessionStats = resolve('sessionStats');
    const heartbeatTime = sessionStats.get('heartbeatTime');
    const playbackCount = sessionStats.get('playbackCount');
    const playbackTime = sessionStats.get('playbackTime');
    const startTime = sessionStats.get('startTime');
    // The `heartbeatTime` interval and other background listeners are still
    // active. They continue to update storage metrics information which spans
    // sessions.
    this.trackEvent({
      event: metricsEvents.CLIENT_SHUTDOWN,
      interaction: false,
      properties: {
        sessionLength: Math.floor((heartbeatTime - startTime) / 1000),
        playbackCount,
        playbackTime: roundMetricsDuration(playbackTime),
      },
    });
  },
  toJSON() {
    return { ...this.defaults };
  },
  //
  // Listeners
  // @ts-expect-error ts-migrate(6133) FIXME: 'cloudServer' is declared but its value is never r... Remove this comment to see the full error message
  onCloudServerChange(cloudServer: any, isConnected: any) {
    if (isConnected) {
      this.fetchPrivacySettings();
    } else {
      // If we're offline, reset privacy settings so we queue events in the
      // `pendingEvents` array.
      this.privacySettings = null;
    }
  },
  onUserIDChange() {
    if (this._appsflyerIntervalID) {
      window.clearInterval(this._appsflyerIntervalID);
    }
    if (this._appsflyerTimeoutID) {
      window.clearTimeout(this._appsflyerTimeoutID);
    }

    // Immediately clear privacy settings when the userID changes. This way
    // metrics events for a new user get queued until privacy settings are
    // fetched instead of sent immediately.
    // See: https://github.com/plexinc/plex-web-client/issues/12671
    this.privacySettings = null;
    // When switching between Plex Home users, userID briefly becomes undefined,
    // before becoming the guest user ID.
    // When signing-out, userID becomes undefined indefinitely.
    // Avoid a spurious signed-out privacy request by debouncing fetch briefly.
    this.debouncedFetchPrivacySettings();
  },
});
export default MetricsManager;
