/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import CloudCompanionCollection from 'app/js/models/player/CloudCompanionCollection';
import DeviceCollection from './DeviceCollection';
import HomeDetailsModel from './HomeDetailsModel';
import HomeUserCollection2 from './HomeUserCollection2';
import InviteCollection from './InviteCollection';
import SocialNetworkCollection from './SocialNetworkCollection';
import SubscriptionPlanCollection from './SubscriptionPlanCollection';
import WebhookCollection from './WebhookCollection';

const UserFlexModel = AbstractModel.extend({
  //
  // Properties

  name: 'UserFlexModel',
  context: ['server'],

  sessionAttrs: {
    isHomeUsersPending: attrTypes.boolean.isRequired,
    isHomeUsersPopulated: attrTypes.boolean.isRequired,
    homeUsersError: attrTypes.object,

    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
    socialNetworks: attrTypes.instanceOf(SocialNetworkCollection).isRequired,

    isPlansPending: attrTypes.boolean.isRequired,
    isPlansPopulated: attrTypes.boolean.isRequired,
    plansError: attrTypes.object,

    isUpdatingUser: attrTypes.boolean.isRequired,
    updateUserError: attrTypes.object,

    isHomeDetailsPending: attrTypes.boolean,
    isHomeDetailsPopulated: attrTypes.boolean,
    homeDetailsError: attrTypes.object,

    isInvitesPending: attrTypes.boolean,
    isInvitesPopulated: attrTypes.boolean,
    invitesError: attrTypes.object,

    isSentInvitesPending: attrTypes.boolean,
    isSentInvitesPopulated: attrTypes.boolean,
    sentInvitesError: attrTypes.object,

    isWebhooksPending: attrTypes.boolean,
    isWebhooksPopulated: attrTypes.boolean,
    webhooksError: attrTypes.object,
    isUpdatingWebhooks: attrTypes.boolean,
    isRemovingWebhooks: attrTypes.boolean,
    updateWebhooksError: attrTypes.object,
  },

  sessionRelations: {
    homeUsers: {
      relationClass: HomeUserCollection2,
    },

    devices: {
      relationClass: DeviceCollection,
    },

    cloudCompanions: {
      relationClass: CloudCompanionCollection,
    },

    homeDetails: {
      relationClass: HomeDetailsModel,
    },

    invites: {
      relationClass: InviteCollection,
    },

    sentInvites: {
      relationClass: InviteCollection,
      options: {
        subset: 'requested',
      },
    },

    plans: {
      relationClass: SubscriptionPlanCollection,
    },

    webhooks: {
      relationClass: WebhookCollection,
    },
  },

  defaults() {
    const collectionOptions = {
      server: this.server,
      parent: this,
    };

    return {
      isHomeUsersPending: false,
      isHomeUsersPopulated: false,
      homeUsersError: null,

      socialNetworks: new SocialNetworkCollection([], collectionOptions),

      isPlansPending: true,
      isPlansPopulated: false,
      plansError: null,

      isUpdatingUser: false,
      updateUserError: null,

      isHomeDetailsPending: false,
      isHomeDetailsPopulated: false,
      homeDetailsError: null,

      isInvitesPending: false,
      isInvitesPopulated: false,
      invitesError: null,

      isSentInvitesPending: false,
      isSentInvitesPopulated: false,
      sentInvitesError: null,

      isWebhooksPending: false,
      isWebhooksPopulated: false,
      webhooksError: null,
      isUpdatingWebhooks: false,
      isRemovingWebhooks: false,
      updateWebhooksError: null,
    };
  },
});

export default UserFlexModel;
