/* eslint-plugin-disable @typescript-eslint */

export default function cleanXMLTVPath(path: any) {
  // Convert back slashes to forward slashes so the URI is valid
  if (/^[a-z]:\\|\\\\/i.test(path)) {
    path = path.replace(/\\/g, '/');
  }

  // URL encode the individual parts of the path (folders and filenames)
  if (/^(\/|[a-z]:\/)/i.test(path)) {
    // @ts-expect-error ts-migrate(6133) FIXME: 'match' is declared but its value is never read.
    path = path.replace(/\/(.+?)(?=\/|$)/g, (match: any, part: any) => {
      return `/${encodeURIComponent(part)}`;
    });
  }

  // Trim the leading forward slash, it will be added when building the lineupUUID
  if (path.charAt(0) === '/') {
    path = path.substring(1);
  }

  // Encode if a URL is provided and it's not already encoded
  if (/^[a-z]+?:\/\//i.test(path)) {
    path = encodeURIComponent(path);
  }

  return path;
}
