/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import PlexPassModalContentConnector from './PlexPassModalContentConnector';

type PlexPassModalProps = {
  origin?: any; // TODO: PropTypes.oneOf(upsellOrigins.ALL)
  isOpen: boolean;
  metricsProperties?: any;
  onModalClose: (...args: any[]) => any;
};

class PlexPassModal extends Component<PlexPassModalProps> {
  //
  // Lifecycle

  shouldComponentUpdate(nextProps: PlexPassModalProps, nextState: any) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  //
  // Listeners

  onModalClose = () => {
    const metrics = resolve('metrics');

    metrics.trackClick({
      ...this.props.metricsProperties,
      page: metricsPages.PLEX_PASS,
      action: metricsActions.SKIP,
    });

    this.props.onModalClose();
  };

  //
  // Render

  render() {
    const { origin, isOpen, metricsProperties } = this.props;

    return (
      <Modal
        isOpen={isOpen}
        metricsPage={metricsPages.PLEX_PASS}
        metricsProperties={metricsProperties}
        size={sizes.LARGE}
        onModalClose={this.onModalClose}
      >
        <PlexPassModalContentConnector
          metricsProperties={metricsProperties}
          origin={origin}
          onModalClose={this.onModalClose}
        />
      </Modal>
    );
  }
}

export default PlexPassModal;
