/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import dimensionsAdapter from 'app/js/adapters/dimensionsAdapter';

function getUserAPIPromise(xhrOptions: any) {
  xhrOptions.headers = {
    ...dimensionsAdapter.getInstance(),
    ...(xhrOptions.headers || {}),
  };

  return $.ajax(xhrOptions);
}

export default getUserAPIPromise;
