/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import log from 'app/js/utils/log/log';
import parseXML from 'app/js/utils/parser/parseXML';

// DEPRECATED: Use `parser2`

// We should try to clean up the data we get back from Plex Media Server.
// For example, booleans are represented by the string '1' most times. We
// can make our lives easier by converting this to an actual boolean.

function parseIterator(fn: any, keys: any) {
  if (Array.isArray(keys)) {
    _.each(keys, fn);
  } else {
    fn(keys);
  }
}

function decodeQueryString(queryStringParts: any) {
  return _.reduce(
    queryStringParts,
    function (memo, param) {
      const parts = param.split('=');
      let key = parts[0];
      let val = parts[1];

      try {
        key = decodeURIComponent(key);
        // missing `=` should be `null`:
        // http://w3.org/TR/2012/WD-url-20120524/#collect-url-parameters
        val = val === undefined ? null : decodeURIComponent(val);

        if (!memo.hasOwnProperty(key)) {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          memo[key] = val;
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        } else if (Array.isArray(memo[key])) {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          memo[key].push(val);
        } else {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          memo[key] = [memo[key], val];
        }
      } catch (error) {
        log.warn('Unable to decode query string at ' + key);
      }

      return memo;
    },
    {}
  );
}

const parser = {
  parseXML,

  replaceXMLElementNames: function (
    xml: any,
    elementNames: any,
    replacement: any
  ) {
    if (!Array.isArray(elementNames)) {
      elementNames = [elementNames];
    }

    const joinedNames = elementNames.join('|');
    const openRegex = new RegExp('<(' + joinedNames + ')', 'g');
    const closeRegex = new RegExp('</(' + joinedNames + ')>', 'g');

    xml = xml.replace(openRegex, '<' + replacement + ' elementName="$1"');
    xml = xml.replace(closeRegex, '</' + replacement + '>');

    return xml;
  },

  parseEncodedString: function (attributes: any, keys: any) {
    if (!attributes) {
      return;
    }

    function decodeString(key: any) {
      if (attributes.hasOwnProperty(key)) {
        const value = attributes[key];

        if (value == null) {
          attributes[key] = '';
        } else {
          try {
            attributes[key] = decodeURIComponent(value);
          } catch (error) {
            // Use the encoded string if we are unable to decode.
          }
        }
      }
    }

    parseIterator(decodeString, keys);
  },

  parseInteger: function (attributes: any, keys: any) {
    if (!attributes) {
      return;
    }

    function convertToInteger(key: any) {
      if (attributes.hasOwnProperty(key)) {
        const value = attributes[key];

        if (value == null || value === '') {
          attributes[key] = 0;
        } else {
          attributes[key] = parseInt(value, 10);
        }
      }
    }

    parseIterator(convertToInteger, keys);
  },

  parseFloat: function (attributes: any, keys: any) {
    if (!attributes) {
      return;
    }

    function convertToFloat(key: any) {
      if (attributes.hasOwnProperty(key)) {
        const value = attributes[key];

        if (value === null || value === '') {
          attributes[key] = 0;
        } else {
          attributes[key] = parseFloat(value);
        }
      }
    }

    parseIterator(convertToFloat, keys);
  },

  parseBoolean: function (attributes: any, keys: any) {
    if (!attributes) {
      return;
    }

    function convertToBoolean(key: any) {
      if (attributes.hasOwnProperty(key)) {
        const value = attributes[key];

        attributes[key] = Number(value) === 1 || value === 'true';
      }
    }

    parseIterator(convertToBoolean, keys);
  },

  parseArray: function (attributes: any, keys: any) {
    if (!attributes) {
      return;
    }

    function convertToArray(key: any) {
      if (attributes.hasOwnProperty(key)) {
        const value = attributes[key];

        if (!Array.isArray(value)) {
          attributes[key] = [value];
        }
      } else {
        attributes[key] = [];
      }
    }

    parseIterator(convertToArray, keys);
  },

  // Derived from https://github.com/sindresorhus/query-string
  parseQueryString: function (attributes: any, keys: any, separator: any) {
    if (!attributes) {
      return;
    }

    separator || (separator = '&');

    function convertToObject(key: any) {
      if (!attributes.hasOwnProperty(key)) {
        return;
      }

      let queryString = attributes[key];

      // Don't try to parse again if it's already an object.
      if (_.isObject(queryString)) {
        return;
      }

      if (!_.isString(queryString)) {
        attributes[key] = {};
        return;
      }

      queryString = queryString.trim().replace(/^(\?|#)/, '');

      if (!queryString) {
        attributes[key] = {};
        return;
      }

      attributes[key] = decodeQueryString(queryString.split(separator));
    }

    parseIterator(convertToObject, keys);
  },
};

export default parser;
