import parse from 'date-fns/parse';

// Example dateString: 'Dec 01, 2018 01:01:01.001'. Chrome works correctly when
// passing strings of this format directly to the Date constructor. Although, at
// least Safari does not.
const dateFormat = 'MMM dd, yyyy HH:mm:ss.SSS';

export default function parseServerLogTimestamp(dateString: any) {
  return parse(dateString, dateFormat, new Date());
}
