import { resolve } from 'app/js/adapters/adapters';
import { garbageCollectEntities } from 'app/js/stores/actions/appActions';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import xhrTracker from 'app/js/utils/xhr/xhrTracker';

function resetUserCommand() {
  const appStore = resolve('appStore');
  const appModel = resolve('appModel');
  const ui = appModel.get('ui');
  const user = appModel.get('user');
  const fakeRouter = appModel.get('fakeRouter');
  const cloudServer = user.server;
  let promise = user.clearAccessToken();

  promise = promise
    .fail(() => {
      log.warn('Failed to clear the access token and user storage');
    })
    .always(() => {
      appModel.set('history', []);

      if (ui) {
        ui.restoreDefaults();
      }

      if (fakeRouter) {
        fakeRouter.restoreDefaults();
      }

      xhrTracker.abortAll();

      user.signOut();
      user.uninitialize();
      user.initialize();

      // Clear all servers before garbage collecting so server entities can be
      // cleaned up.

      dispatcher.trigger('clear:servers');

      // When the user is signed out, the user model is reset.  This can cause
      // dangling entities (eg: providers)

      appStore.dispatch(garbageCollectEntities({ budget: Infinity }));

      // Restore the cloud server in the server cache since the cloud server is
      // reused. This ensures that a task like switching users does not need to
      // wait for a fresh cloud server with its own connection test.

      dispatcher.trigger('clearAll:cache');
      cloudServer.cache.store(cloudServer);
    });

  return alwaysResolve(promise);
}

export default resetUserCommand;
