import BaseCollection from 'app/js/models/BaseCollection';
import BackgroundProcessingQueueModel from 'app/js/models/library/convert/BackgroundProcessingQueueModel';
import proxyTypes from 'app/js/utils/constants/proxyTypes';

const BackgroundProcessingQueueCollection = BaseCollection.extend({
  //
  // Properties

  model: BackgroundProcessingQueueModel,
  url: '/playQueues?type=' + proxyTypes.LOCAL,

  //
  // Control

  parse: function (response: any) {
    return this.convert(response, 'PlayQueue');
  },
});

export default BackgroundProcessingQueueCollection;
