/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import BasicVolatileStorage from 'app/js/adapters/storage/BasicVolatileStorage';
import storageAdapter from 'app/js/adapters/storageAdapter';
import volatileStorageAdapter from 'app/js/adapters/volatileStorageAdapter';
import migrateExperienceSettings from 'app/js/initializers/migrations/migrateExperienceSettings';
import migrateExperienceSettingsToDeviceSettings from 'app/js/initializers/migrations/migrateExperienceSettingsToDeviceSettings';
import migrateLocalStorageUserExperienceSettings from 'app/js/initializers/migrations/migrateLocalStorageUserExperienceSettings';
import migrateSettingsToDeviceSettings from 'app/js/initializers/migrations/migrateSettingsToDeviceSettings';
import migrateSettingsToUserExperienceSettings from 'app/js/initializers/migrations/migrateSettingsToUserExperienceSettings';
import log from 'app/js/utils/log/log';

const PLEX_TEST_TOKEN = 'plexTestToken';
function determineStorageModule() {
  // Check if `myPlexAccessToken` is in the volatile storage, if it is, the user
  // has previously signed in with `Remember Me` unchecked and we need to switch
  // the storage.
  const deferred = $.Deferred();
  const promise = volatileStorageAdapter.get('myPlexAccessToken');
  promise.done(() => {
    // Storage will always successfully get the token if it is provided through
    // the global PLEXWEB object (e.g. by plex.tv).
    if (
      !(window as any).PLEXWEB ||
      !(window as any).PLEXWEB.myPlexAccessToken
    ) {
      storageAdapter.register(volatileStorageAdapter.getInstance());
    }
  });
  promise.always(() => {
    deferred.resolve();
  });
  return deferred.promise();
}
function testStorageModule() {
  // Perform a test to see if the storage module can operate with the current
  // browser settings. Safari in private mode will block localStorage and in
  // such cases we need to swap in a more basic storage method.
  const deferred = $.Deferred();
  const promise = storageAdapter.set(PLEX_TEST_TOKEN, 1);
  promise.done(() => {
    storageAdapter.remove(PLEX_TEST_TOKEN);
    deferred.resolve();
  });
  promise.fail((error: any) => {
    const basicVolatileStorage = new BasicVolatileStorage();
    log.warn(
      `Storage module swapped for ${basicVolatileStorage.name} due to exception: "${error}"`
    );
    volatileStorageAdapter.register(basicVolatileStorage);
    storageAdapter.register(basicVolatileStorage, { ignorePrevious: true });
    deferred.resolve();
  });
  return deferred.promise();
}
export default function desktopStorageInitializer() {
  // All migrations are expected to resolve.  If a migration fails, it is best
  // to continue and migrate what we can.
  // First run migrations that massage storage into new structure.  These
  // migrations must run before any models are populated.
  return determineStorageModule()
    .then(testStorageModule)
    .then(migrateSettingsToDeviceSettings)
    .then(migrateExperienceSettingsToDeviceSettings)
    .then(migrateExperienceSettings)
    .then(migrateSettingsToUserExperienceSettings)
    .then(migrateLocalStorageUserExperienceSettings);
}
