import history from 'app/js/v4/history';
import routes from 'app/js/v4/routes/routes';
import urlForRoute from 'app/js/v4/routes/urlForRoute';

export default function navigateToRoute(
  routeKey: keyof typeof routes,
  params = {},
  queryParams = {},
  replace = false
): void {
  const url = urlForRoute(routeKey, params, queryParams);

  if (replace) {
    history.replace(url);
  } else {
    history.push(url);
  }
}

export function navigateToServerSettings(): void {
  navigateToRoute('serverSettings', {
    settingsSection: 'manage',
    settingsGroup: 'libraries',
  });
}
