/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';

type MarionetteComponentProps = {
  className?: string;
  view: (...args: any[]) => any;
  viewOptions?: any;
};

class MarionetteComponent extends Component<MarionetteComponentProps> {
  // eslint-disable-next-line react/sort-comp
  _el: any;
  _view: any;

  componentDidMount() {
    const View = this.props.view;
    // @ts-expect-error ts-migrate(7009) FIXME: 'new' expression, whose target lacks a construct s... Remove this comment to see the full error message
    this._view = new View(this.props.viewOptions);

    this._view.render();

    this._el.appendChild(this._view.el);
    this._view.triggerMethod('show');
  }

  shouldComponentUpdate() {
    return false;
  }

  componentWillUnmount() {
    if (this._view.close) {
      this._view.close();
    }
  }

  render() {
    return (
      <div ref={(el) => (this._el = el)} className={this.props.className} />
    );
  }
}

export default MarionetteComponent;
