/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function disableVersionGeneratorCommand(options: any) {
  const generator = options.generator;
  const url = _.result(generator, 'url') + '/' + options.ratingKey + '/disable';
  const confirmModal = showConfirmation();

  const promise = confirmModal.promise.then(function () {
    const xhrOptions = _.extend(generator.server.getXHROptions(url), {
      type: 'PUT',
    });
    const disablePromise = $.ajax(xhrOptions).promise();

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    disablePromise.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem disabling this version.'),
      });
    });

    disablePromise.always(function () {
      confirmModal.hide();
    });

    return disablePromise;
  });

  return promise;
}

function showConfirmation() {
  const confirmModal = new ConfirmModal({
    title: t('Remove Version'),
    message: t(
      'Are you sure you want to delete this version? You can enable it again in Optimized Versions Settings.'
    ),
    confirmLabel: t('Remove'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  dispatcher.trigger('show:modal', confirmModal);

  return confirmModal;
}

export default disableVersionGeneratorCommand;
