/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import ProviderCountryModel from 'app/js/models/provider/ProviderCountryModel';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';

const ProviderCountryCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ProviderCountryCollection',
  context: ['server', 'provider'],
  url() {
    const locationFeature = this.provider.get('features').findWhere({
      type: providerFeatureTypes.LOCATION,
    });

    return `${locationFeature.get('key')}/countries`;
  },

  model: ProviderCountryModel,
});

export default ProviderCountryCollection;
