/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectAllServerEntities from './selectAllServerEntities';

const selectServers = createSelector(
  selectAllServerEntities,
  (appState) => appState.servers,
  (appState) => appState.embeddedServer,
  (appState) => appState.transientServer,
  (
    serverEntities,
    serverEntityIDs,
    embeddedServerEntityID,
    transientServerEntityID
  ) => {
    const servers = serverEntityIDs
      ? serverEntityIDs.map(
          (serverEntityID: any) => serverEntities[serverEntityID]
        )
      : [];

    if (embeddedServerEntityID && serverEntities[embeddedServerEntityID]) {
      servers.push(serverEntities[embeddedServerEntityID]);
    }

    if (transientServerEntityID && serverEntities[transientServerEntityID]) {
      servers.push(serverEntities[transientServerEntityID]);
    }

    return servers;
  }
);

export default selectServers;
