/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import pageStorage from 'app/js/views/v3/helpers/pageStorage';

type PageScrollerProps = {
  className?: string;
  restoreScrollDelay: number;
  scrollPositionKey?: string;
  smoothScroll?: boolean;
  smoothScrollDuration: number;
};

type PageScrollerState = any;

class PageScroller extends Component<PageScrollerProps, PageScrollerState> {
  static defaultProps = {
    restoreScrollDelay: 100,
    scrollPositionKey: 'scrollPosition',
    smoothScroll: true,
    smoothScrollDuration: 400,
  };

  // eslint-disable-next-line react/sort-comp
  _currentScrollTop: any;
  _positionKey: any;
  _restoreScroll: any;
  _restoreScrollDelay: any;
  _restoreScrollTimeoutID: any;

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isScrolling: false,
    };

    // Set the key early and use it when the component unmounts. The hash will
    // have changed after a page change and before the component can unmount,
    // which would lead to different keys.
    this._positionKey = pageStorage.getKey(props.scrollPositionKey);
    this._currentScrollTop = 0;

    // Set delay to restore the isScrolling value.
    // This happens only after componentDidMount and does not need to change
    // when the props change.
    this._restoreScrollDelay = props.smoothScroll
      ? props.smoothScrollDuration
      : props.restoreScrollDelay;
    this._restoreScroll = false;
    this._restoreScrollTimeoutID = null;
  }

  //
  // Lifecycle

  componentDidMount() {
    this._restoreScrollPosition();
  }

  componentDidUpdate(prevProps: PageScrollerProps) {
    const { scrollPositionKey } = this.props;

    if (prevProps.scrollPositionKey !== scrollPositionKey) {
      this._positionKey = pageStorage.getKey(scrollPositionKey);
    }
  }

  componentWillUnmount() {
    this._storeScrollPosition();

    if (this._restoreScrollTimeoutID) {
      window.clearTimeout(this._restoreScrollTimeoutID);
    }
  }

  //
  // Control

  _restoreScrollPosition() {
    const scrollPosition = pageStorage.get(this._positionKey);

    if (scrollPosition) {
      this.setState({ isScrolling: true });
      this._currentScrollTop = scrollPosition.scrollTop;
      this._restoreScroll = true;

      this._restoreScrollTimeoutID = window.setTimeout(() => {
        this.setState({
          isScrolling: false,
        });
        this._restoreScroll = false;
      }, this._restoreScrollDelay);
    }
  }

  _storeScrollPosition() {
    pageStorage.set(this._positionKey, {
      scrollTop: this._currentScrollTop,
    });
  }

  //
  // Listeners

  onScrollPositionChange = ({ scrollTop }: any) => {
    if (!this.state.isScrolling) {
      this._currentScrollTop = scrollTop;
    }
  };

  //
  // Render

  render() {
    const {
      children,
      className,
      // Discarded props
      restoreScrollDelay,
      scrollPositionKey,
      // Rest props
      ...otherProps
    } = this.props;

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: ReactNode; smoothScroll?: boolea... Remove this comment to see the full error message
      <Scroller
        className={className}
        restoreScroll={this._restoreScroll}
        scrollTop={this._currentScrollTop}
        onScrollPositionChange={this.onScrollPositionChange}
        {...otherProps}
      >
        {children}
      </Scroller>
    );
  }
}

export default PageScroller;
