/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import { setDVRDeviceEnabled } from 'app/js/stores/actions/ui/dvrSetupActions';
import AlertBarView from 'app/js/views/alerts/AlertBarView';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/alert... Remove this comment to see the full error message
import tpl from 'app/templates/alerts/MediaGrabberDeviceDiscoveredAlertBarView.tpl';

const MediaGrabberDeviceDiscoveredAlertBarView = AlertBarView.extend({
  //
  // Properties

  className: 'alert-bar notify',
  template: tpl,

  events: _.extend(
    {
      'click .setup-link': 'onSetupClick',
    },
    AlertBarView.prototype.events
  ),

  //
  // Control

  serializeData() {
    return {
      model: this.model.get('model'),
      make: this.model.get('make'),
      dvrSettingsRoute: getServerSettingsRoute('manage', 'dvr'),
    };
  },

  disableDevice() {
    const appStore = resolve('appStore');
    const serverEntityID = this.options.serverEntityID;

    if (serverEntityID) {
      appStore.dispatch(
        setDVRDeviceEnabled({
          serverEntityID,
          deviceID: this.model.id,
          enabled: false,
        })
      );
    }
  },

  //
  // Listeners

  onSetupClick() {
    this.hide();
    this.disableDevice();
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onCloseClick(event: any) {
    // eslint-disable-next-line prefer-rest-params
    AlertBarView.prototype.onCloseClick.apply(this, arguments);

    this.disableDevice();
  },
});

export default MediaGrabberDeviceDiscoveredAlertBarView;
