import { Action } from 'redux';
import { call, CallEffect } from 'redux-saga/effects';
import { resolve } from 'app/js/adapters/adapters';

function dispatchLegacyAction<LegacyAction extends Action>(
  action: LegacyAction
): void {
  resolve('appStore').dispatch(action);
}

/**
 * Creates a call effect that calls the legacy appStore dispatch method with a
 * legacy action
 *
 * #### Notes
 *
 * Use this effect carefully, as state does not update immediately in the legacy
 * store. You should not use this effect expecting to see state changes
 * immediately after dispatching to the legacy store
 *
 * @param action A legacy action that dispatches a type expected on the legacy
 * action handlers
 */
function putLegacyActionDangerously<LegacyAction extends Action>(
  action: LegacyAction
): CallEffect<void> {
  return call(dispatchLegacyAction, action);
}

export default putLegacyActionDangerously;
