/* eslint-plugin-disable @typescript-eslint */

export default function migrateToAutoHome(userExperienceSettings: any) {
  // If there is a value for autoHomeHubsEnabled it has been migrated already
  if (userExperienceSettings.autoHomeHubsEnabled != null) {
    return userExperienceSettings;
  }

  const homeSettings = userExperienceSettings.homeSettings;

  if (homeSettings && homeSettings.hasCustomizations) {
    userExperienceSettings.autoHomeHubsEnabled = false;
  } else {
    userExperienceSettings.autoHomeHubsEnabled = true;
  }

  return userExperienceSettings;
}
