import { SagaIterator } from 'redux-saga';
import {
  all,
  call,
  cancel,
  fork,
  put,
  take,
  takeLeading,
} from 'redux-saga/effects';
import path from 'app/js/utils/url/path';
import liveTVSlice from 'app/js/v4/features/livetv/liveTVSlice';
import {
  APIGuideChannelMediaContainerResponse,
  GuideChannelsFetchRequestedAction,
} from 'app/js/v4/features/livetv/liveTVTypes';
import normalizeChannelsResponse from 'app/js/v4/features/livetv/utils/normalizeChannelsResponse';
import api from 'app/js/v4/utils/data/api';

//
// Workers

export function* fetchGuideChannels({
  payload,
}: GuideChannelsFetchRequestedAction): SagaIterator {
  const { server, provider } = payload;

  try {
    const url = server.isCloud
      ? '/lineups/plex/channels'
      : path.join([provider.identifier, '/lineups/dvr/channels']);

    const response: APIGuideChannelMediaContainerResponse = yield call(
      api.get,
      url,
      {
        server,
        provider,
        providerVersionOverride: '3.3',
      }
    );

    yield put(
      liveTVSlice.actions.guideChannelsFetchSucceeded({
        data: normalizeChannelsResponse({
          server,
          provider,
          data: response.data?.MediaContainer.Channel,
        }),
      })
    );
  } catch (error) {
    yield put(
      liveTVSlice.actions.guideChannelsFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Watchers

export function* watchGuideChannelsFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      liveTVSlice.actions.guideChannelsFetchRequested,
      fetchGuideChannels
    );

    yield take(liveTVSlice.actions.reset);
    yield cancel(task);
  }
}

export default function* liveTVSaga(): SagaIterator {
  yield all([fork(watchGuideChannelsFetchRequested)]);
}
