import { createSlice } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  TopUsersFetchFailedAction,
  TopUsersFetchRequestedAction,
  TopUsersFetchSucceededAction,
  TopUsersState,
} from './dashboardTopUsersTypes';

const initialState: TopUsersState = {
  statistics: [],
  accounts: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const dashboardTopUsersSlice = createSlice({
  name: 'dashboardTopUsers',
  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    topUsersFetchRequested(state, _action: TopUsersFetchRequestedAction) {
      markPending(state);
    },

    topUsersFetchSucceeded(state, { payload }: TopUsersFetchSucceededAction) {
      markPopulated(state);
      state.statistics = payload.data.statistics;
      state.accounts = payload.data.accounts;
    },

    topUsersFetchFailed(state, { payload }: TopUsersFetchFailedAction) {
      markFailed(state, payload.error);
    },
  },
});

export default dashboardTopUsersSlice;
