import { createSelector, Selector } from 'reselect';
import {
  DeviceChannelsState,
  MediaGrabberDevice,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { initialDeviceChannelsState } from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectDVRSetup from './selectDVRSetup';

const createDeviceChannelsSelector = (
  deviceKey?: MediaGrabberDevice['key']
): Selector<LegacyRootState, DeviceChannelsState> => {
  return createSelector(selectDVRSetup, (dvrSetup) =>
    deviceKey
      ? dvrSetup.channelsByDeviceKey?.[deviceKey] ?? {
          ...initialDeviceChannelsState,
        }
      : { ...initialDeviceChannelsState }
  );
};

export default createDeviceChannelsSelector;
