import { LIBRARY } from 'app/js/utils/constants/providerIdentifiers';
import { CLOUD_SERVER_ID } from 'app/js/utils/constants/serverIDs';
import path from 'app/js/utils/url/path';

export interface SourceIdentifiers {
  isServer: boolean;
  serverIdentifier: string;
  providerIdentifier: string;
  key: string;
  ratingKey: string;
}

const matchProviderAndMetadataKey = /\/([^/]+)([^$]+)?/;
const identifiers: Record<string, SourceIdentifiers> = {};

export default function getIdentifiersFromSource(
  source: string
): SourceIdentifiers {
  if (identifiers[source]) {
    return identifiers[source];
  }

  // Parse identifiers from a source URI or source attribute.
  // See: https://docs.plex.tv/docs/api/media-providers#header-sources
  const {
    // The source type that is the scheme of the URI can be either server or
    // provider.
    protocol,
    // The host of the URI is the source identifier, which is the server’s
    // machine identifier or the provider’s identifier.
    host,
    // The provider ID at the start of the path is the provider’s identifier.
    // This component is optional when the scheme is provider, since the
    // SOURCE_ID and PROVIDER_ID would always be the same. The rest of the path
    // represents the path of the content at the provider.
    pathname,
    // The source may include additional query parameters like X-Plex- headers
    // or media query syntax for sorts and filters
    search,
  } = path.parseUri(source);

  const isProvider = protocol === 'provider:';
  const isServer = protocol === 'server:';

  if (!isProvider && !isServer) {
    throw new Error('Source identifier is lacking provider or server scheme');
  }

  const cleanPathname = pathname.replace(/\/$/, '') || undefined;

  const pathnameMatches =
    cleanPathname?.match(matchProviderAndMetadataKey) || [];

  // The embedded server doesn't return a provider with its source and will
  // always start with /media/provider
  const hasNoProvider = cleanPathname && pathnameMatches[1] === 'media';
  const serverProviderIdentifier = hasNoProvider ? LIBRARY : pathnameMatches[1];

  const key = isProvider || hasNoProvider ? cleanPathname : pathnameMatches[2];
  const ratingKey = key ? key.slice(key.lastIndexOf('/') + 1) : undefined;

  identifiers[source] = {
    isServer,
    serverIdentifier: isProvider ? CLOUD_SERVER_ID : host,
    providerIdentifier: isProvider ? host : serverProviderIdentifier || LIBRARY,
    // In order to preserve order of the query parameters do not parse the
    // search query further and reattach it directly to the path to construct
    // the key
    key: key ? `${key}${search}` : '',
    ratingKey: ratingKey || '',
  };

  return identifiers[source];
}
