// @ts-expect-error ts-migrate(2649) FIXME: Cannot augment module '_' with value exports becau... Remove this comment to see the full error message
const _ = require('underscore');
const tEscape = require('app/js/utils/t').tEscape;
// @ts-expect-error ts-migrate(2451) FIXME: Cannot redeclare block-scoped variable 'Handlebars... Remove this comment to see the full error message
const Handlebars = require('handlebars');

function translate() {
  // Handlebars automatically includes an object as the last argument for a
  // helper function. Remove that argument before sending the others to the
  // translate function.
  // eslint-disable-next-line prefer-rest-params
  const args = _.initial(arguments);
  // eslint-disable-next-line prefer-rest-params
  const hash = _.last(arguments).hash;

  if (_.isObject(hash) && !_.isEmpty(hash)) {
    // Insert the hash into the args array as it the object used for named translations.
    args.splice(1, 0, hash);
  }

  return new Handlebars.SafeString(tEscape.apply(tEscape, args));
}

module.exports = translate;
