import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  FetchUserDetailsRequestAction,
  FetchUserDetailsRequestSucceeded,
  UpdateUserDetailsRequestAction,
  UpdateUserDetailsRequestSucceeded,
  UserDetailsRequestFailed,
  UserState,
} from './userTypes';

const initialState: UserState = {
  isPopulated: false,
  isPending: false,
  error: null,
  details: {
    thumb: '',
    friendlyName: '',
    email: '',
    username: '',
    anonymous: true,
    isFullUser: false,
  },
};

const userSlice = createSlice({
  name: 'user',
  initialState,

  reducers: {
    fetchUserDetails(state, _action: FetchUserDetailsRequestAction) {
      markPending(state);
    },

    fetchUserDetailsSucceeded(
      state,
      { payload }: FetchUserDetailsRequestSucceeded
    ) {
      markPopulated(state);
      state.details = payload.details;
    },

    fetchUserDetailsFailed(state, { payload }: UserDetailsRequestFailed) {
      markFailed(state, payload.error);
    },

    updateUserDetails(state, _action: UpdateUserDetailsRequestAction) {
      markPending(state);
    },

    updateUserDetailsSucceeded(
      state,
      { payload }: UpdateUserDetailsRequestSucceeded
    ) {
      markPopulated(state);
      state.details = {
        ...state.details,
        ...payload.details,
      };
    },

    updateUserDetailsFailed(state, { payload }: UserDetailsRequestFailed) {
      markFailed(state, payload.error);
    },

    clearError(state, _action: PayloadAction) {
      state.error = null;
    },

    languageChanged() {
      // Handled by other slices for now, no state change.
    },
  },
});

export default userSlice;
