/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import storageAdapter from 'app/js/adapters/storageAdapter';
import log from 'app/js/utils/log/log';
import whenEach from 'app/js/utils/promise/whenEach';
import random from 'app/js/utils/string/random';

function isValidClientID(clientID: any) {
  return _.isString(clientID) && clientID.trim().length > 0;
}

function generateClientID(appModel: any, name: any) {
  // Generate a random client identifier and save
  // in storage so it persists for future sessions.
  const clientID = random();
  log('[appModelInitializer] Generating new %s [%s]', name, clientID);
  appModel.set(name, clientID);
  return storageAdapter.set(name, clientID);
}

// Grab the client identifier so we can include it in all xhr requests. Returns
// a promise that will resolve once a clientID has been successfully loaded
// (into the appModel) and stored (in the storageAdapter). The promise will fail
// if writing to storage fails. In either case, the value of the promise is
// unspecified.
function loadClientID(appModel: any, name: any) {
  // It may have already been populated by a platform initializer or may need to
  // be pulled out of storage. Any value defined in the appModel always takes
  // precedence over possibly stale stored values.
  let promise;
  const currentClientID = appModel.get(name);

  if (isValidClientID(currentClientID)) {
    promise = storageAdapter.set(name, currentClientID);
  } else {
    promise = storageAdapter.get(name);
    promise = promise.then(
      (clientID: any) => {
        if (isValidClientID(clientID)) {
          appModel.set(name, clientID);
        } else {
          log.error(
            '[appModelInitializer] Invalid %s found in storage [%s]',
            name,
            currentClientID
          );
          return generateClientID(appModel, name);
        }
      },
      () => {
        // This is either an actual error reading from storage or an indication
        // that there is no stored value yet (such as first-run). It is not
        // logged at error level since we cannot distinguish.
        log('[appModelInitializer] Failed to read %s from storage', name);
        return generateClientID(appModel, name);
      }
    );
  }

  return promise;
}

function loadSkipSignInFlag(appModel: any) {
  const promise = storageAdapter.get('skipSignIn');

  promise.always((value: any) => {
    appModel.set('skipSignIn', value === '1');
  });

  return promise;
}

export default function appModelInitializer() {
  const appModel = resolve('appModel');

  return whenEach([
    loadClientID(appModel, 'clientID'),
    loadClientID(appModel, 'anonymousClientID'),
    loadSkipSignInFlag(appModel),
  ]);
}
