/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';
import LibraryFailureModal from 'app/js/views/modals/LibraryFailureModal';

function updateLibraryCommand(options: any) {
  const deferred = $.Deferred();
  const section = options.section;
  const sectionID = section ? section.id : 'all';
  const cancel = options.cancel;
  const server = section ? section.server : options.server;
  const params = options.params || {};
  const data = {};
  if (params.force) {
    (data as any).force = 1;
  }
  const url = '/library/sections/' + sectionID + '/refresh';
  const xhrOptions = _.extend(server.getXHROptions(url), { data: data });
  if (cancel) {
    xhrOptions.type = 'DELETE';
    if (section) {
      section.set('refreshing', false);
    }
  }
  function updateLibrary() {
    const xhrDeferred = $.ajax(xhrOptions);
    xhrDeferred.done(function () {
      deferred.resolve();
    });
    xhrDeferred.fail(function (xhr) {
      if (xhr.status === 503) {
        dispatcher.trigger(
          'show:modal',
          new LibraryFailureModal({ model: server })
        );
      } else {
        const message = cancel
          ? t('There was a problem canceling the update.')
          : t('There was a problem updating the library.');
        dispatcher.trigger('show:alert', {
          type: 'error',
          message: message,
        });
      }
      deferred.reject(xhr);
    });
  }
  if ((data as any).force) {
    const confirmModal = new ConfirmModal({
      title: t('Refresh All'),
      message: t(
        'Forces a download of fresh media information from the internet. This can take a long time. Any locked fields are not modified.'
      ),
      confirmLabel: t('Refresh'),
      cancelLabel: t('Cancel'),
    });
    const confirmPromise = confirmModal.promise;
    confirmPromise.done(updateLibrary);
    confirmPromise.fail(function () {
      deferred.reject();
    });
    dispatcher.trigger('show:modal', confirmModal);
  } else {
    updateLibrary();
  }
  return deferred.promise();
}
export default updateLibraryCommand;
