/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import * as responseTypes from 'app/js/models/responseTypes';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import replaceMetadataXMLElementNames from 'app/js/utils/parser/replaceMetadataXMLElementNames';
import MetadataModel from './MetadataModel';

const MetadataCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'MetadataCollection',

  context: [
    'server',
    'provider',
    'section',
    'playlist',
    'identifier',
    'mediaTagPrefix',
    'mediaTagVersion',
    'allowSync',
    'containerKey',
    'includeExternalMedia',
  ],

  model: MetadataModel,

  responseType() {
    return getSupportedResponseType(this.server);
  },

  url() {
    return this.containerKey;
  },

  //
  // Control

  augmentResponseText(responseText: any, responseType: any) {
    return responseType === responseTypes.xml
      ? // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
        replaceMetadataXMLElementNames(responseText)
      : responseText;
  },

  splitByType: function () {
    const byType = {};

    // Sort the items into the object based on the type.
    this.each((metadataItem: any) => {
      const type = metadataItem.get('type');

      if (byType.hasOwnProperty(type)) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        byType[type].push(metadataItem);
      } else {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        byType[type] = [metadataItem];
      }
    });

    return byType;
  },
});

export default MetadataCollection;
