/* eslint-plugin-disable @typescript-eslint */

import config from 'app/js/models/config';
import canShareSyncSettings from 'app/js/utils/user/canShareSyncSettings';

// All requests that return user(s) should contain certain params to ensure we
// get back all the attributes and relations that we expect.
export default function getUserRequestParams(userModel: any, overrides = {}) {
  return {
    includeSubscriptions: 1,
    includeProviders: 1,

    // When a platform does not yet support settings sync we should not receive
    // settings from plex.tv.  Otherwise if for some reason settings have been
    // sync'd to plex.tv they could possibly overwrite the local settings.
    includeSettings: config.allowSyncSettings ? 1 : 0,

    includeSharedSettings: canShareSyncSettings(userModel) ? 1 : 0,
    ...overrides,
  };
}
