/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import proxyTypes from 'app/js/utils/constants/proxyTypes';
import subtitlesBurnLevels from 'app/js/utils/constants/subtitlesBurnLevels';
import assert from 'app/js/utils/log/assert';
import log from 'app/js/utils/log/log';
import getMissingParts from 'app/js/utils/media/getMissingParts';
import getSelectedAudio from 'app/js/utils/media/getSelectedAudio';
import getSelectedSubtitles from 'app/js/utils/media/getSelectedSubtitles';
import getSelectedVideo from 'app/js/utils/media/getSelectedVideo';
import getStreams from 'app/js/utils/media/getStreams';
import isBitmapSubtitles from 'app/js/utils/media/isBitmapSubtitles';
import videoPlayerQualities from 'app/js/utils/player/videoPlayerQualities';
import playerUtil from 'app/js/utils/playerUtil';
import t from 'app/js/utils/t';
import streamTypes from 'app/js/utils/types/streamTypes';
// The media decision engine determines the best media item to play given a
// transcode server and optional upper-limit quality. It returns a decision
// object with information about how it thinks the item can be played. The
// engine makes a direct play decision and makes an educated guess whether
// video and audio can be direct streamed in order to rank the possible
// media items, but the ultimate decider for direct stream vs transcode is
// still the server.
const mediaDecisionEngine = {
  //
  // Control
  /**
   * Analyzes media items and returns the analysis for the recommended media
   * item to play.
   *
   * Required *
   *
   * @param  options.type *              video|music|photo
   * @param  options.profile *           The player profile for this platform.
   * @param  options.item *              The metadata item to be played.
   * @param  options.media               An optional media item to be played.
   * @param  options.transcodeServer *   The server that will be used to transcode the item.
   * @param  options.allowDirectPlay     Allow items to be played directly.
   * @param  options.forceDirectPlay     Skip analysis and force direct play.
   * @param  options.skipCanPlayTypeTest Skip the canPlayType test and assume the client can play the mime type
   * @param  options.allowMultiTrackAudio  Avoid transcoding when the selected audio stream is not the first.
   * @param  options.allowedAC3Containers  Allow direct play of AC-3 audio in videos with these containers. Use '*' for all containers.
   * @param  options.allowedEAC3Containers Allow direct play of E-AC-3 audio in videos with these containers. Use '*' for all containers.
   * @param  options.allowedDCAContainers  Allow direct play of DTS audio in videos with these containers. Use '*' for all containers.
   * @param  options.allowedTrueHDContainers  Allow direct play of Dolby TrueHD audio in videos with these containers. Use '*' for all containers.
   * @param  options.allowDirectStreamVideoWithSoftSubtitles  Allow direct stream of video with soft subtitles
   * @param  options.subtitlesBurnLevel  The subtitles burn level setting.
   * @param  options.hasFlash            The device has the Flash player.
   * @param  options.hasNativeHLS        The device can play HLS natively.
   * @param  options.canDisplayHDR              This device can display the HDR color space.
   * @param  options.canDisplayDolbyVision      This device can display the Dolby Vision color space.
   * @param  options.quality             The max bitrate, video resolution, and video quality.
   * @param  options.transcoderOptions   Url params to be sent to the transcode server.
   * @param  options.preferredDirectPlayProtocol  Segmented protocol to prefer when direct playing HLS or DASH.
   * @param  options.videoCodecOptions   Values used to augment / override an existing video codec profile.
   *
   * @return {Object}                    The decision object for the chosen media item.
   */
  getMediaDecision: function (options: any) {
    const profile = this.augmentProfile(options);
    log.info('[MDE] Augmented profile:', profile);
    // If a specific media item is passed in, just analyze it and return the
    // decision for that item.
    if (options.media) {
      return this.analyzeMedia(options.media, profile, options);
    }
    const item = options.item;
    const mediaToAnalyze = item.get('Media');
    // The transcoder needs a media index. If the metadata item does not
    // have media items, force a direct play and hope for the best.
    if (!mediaToAnalyze.length) {
      return {
        transcodeServer: options.transcodeServer,
        canPlay: true,
        canDirectPlay: true,
      };
    }
    // If the metadata item only has one media item, don't bother trying to
    // find the best decision.
    if (mediaToAnalyze.length === 1) {
      return this.analyzeMedia(_.first(mediaToAnalyze), profile, options);
    }
    // Analyze each media item and determine which one should be played.
    const decisions = _.map(
      mediaToAnalyze,
      function (media) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        return this.analyzeMedia(media, profile, options);
      },
      this
    );
    return this.getBestDecision(decisions);
  },
  augmentProfile: function (options: any) {
    const profile = options.profile;
    if (!profile) {
      throw new Error('MDE: No player profile provided for ' + options.type);
    }
    const isVideo = options.type === playerUtil.types.VIDEO;
    // Work with deep clones so we do not modify the source profile.
    const directPlay = $.extend(true, {}, profile.directPlay);
    const directStream = $.extend(true, {}, profile.directStream);
    // Apply the generic properties to each container specific profile.
    // Merge quality options so the bitrate limit passed to the engine is
    // considered alongside bitrate limits provided in the profile.
    const generic = directPlay.generic;
    if (generic) {
      delete directPlay.generic;
    }
    let allowedAC3Containers = options.allowedAC3Containers;
    const allowAC3AllContainers = allowedAC3Containers === '*';
    if (!allowAC3AllContainers && allowedAC3Containers) {
      allowedAC3Containers = Array.isArray(allowedAC3Containers)
        ? allowedAC3Containers
        : [allowedAC3Containers];
    }
    let allowedEAC3Containers = options.allowedEAC3Containers;
    const allowEAC3AllContainers = allowedEAC3Containers === '*';
    if (!allowEAC3AllContainers && allowedEAC3Containers) {
      allowedEAC3Containers = Array.isArray(allowedEAC3Containers)
        ? allowedEAC3Containers
        : [allowedEAC3Containers];
    }
    let allowedDCAContainers = options.allowedDCAContainers;
    const allowDCAAllContainers = allowedDCAContainers === '*';
    if (!allowDCAAllContainers && allowedDCAContainers) {
      allowedDCAContainers = Array.isArray(allowedDCAContainers)
        ? allowedDCAContainers
        : [allowedDCAContainers];
    }
    let allowedTrueHDContainers = options.allowedTrueHDContainers;
    const allowTrueHDAllContainers = allowedTrueHDContainers === '*';
    if (!allowTrueHDAllContainers && allowedTrueHDContainers) {
      allowedTrueHDContainers = Array.isArray(allowedTrueHDContainers)
        ? allowedTrueHDContainers
        : [allowedTrueHDContainers];
    }
    _.each(
      directPlay,
      function (containerProfile, container) {
        // First transform the container profiles.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.mergeGenericOptions(containerProfile, generic);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.mergeMaxQualityOptions(containerProfile, options);
        if (
          allowAC3AllContainers ||
          (allowedAC3Containers && _.contains(allowedAC3Containers, container))
        ) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.addAudioCodec('ac3', containerProfile, options);
        }
        if (
          allowEAC3AllContainers ||
          (allowedEAC3Containers &&
            _.contains(allowedEAC3Containers, container))
        ) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.addAudioCodec('eac3', containerProfile, options);
        }
        if (
          allowDCAAllContainers ||
          (allowedDCAContainers && _.contains(allowedDCAContainers, container))
        ) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.addAudioCodec('dca', containerProfile, options);
        }
        if (
          allowTrueHDAllContainers ||
          (allowedTrueHDContainers &&
            _.contains(allowedTrueHDContainers, container))
        ) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.addAudioCodec('truehd', containerProfile, options);
        }
        // Then transform the codec profiles.
        const primaryStreamName = playerUtil.getStreamByType(options.type);
        const primaryStreamProfile = containerProfile[primaryStreamName];
        if (primaryStreamProfile) {
          const primaryStreamCodecs = primaryStreamProfile.codecs;
          _.each(
            primaryStreamCodecs,
            function (codecProfile, codecName) {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.mergeMaxQualityOptions(codecProfile, options, true);
              // Only merge video codec options when there is overlap between
              // the profile and the options.
              if (
                options.videoCodecOptions &&
                options.videoCodecOptions[codecName]
              ) {
                // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                this.mergeCodecOptions(
                  codecProfile,
                  options.videoCodecOptions[codecName]
                );
              }
            },
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this
          );
        }
        const secondaryStreamProfile = isVideo && containerProfile.audio;
        if (secondaryStreamProfile && options.audioCodecOptions) {
          const secondaryStreamCodecs = secondaryStreamProfile.codecs;
          _.each(
            secondaryStreamCodecs,
            function (codecProfile, codecName) {
              // Only merge audio codec options when there is overlap between
              // the profile and the options.
              if (options.audioCodecOptions[codecName]) {
                // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                this.mergeCodecOptions(
                  codecProfile,
                  options.audioCodecOptions[codecName]
                );
              }
            },
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this
          );
        }
      },
      this
    );
    // Finally transform the directStream profile.
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 3 arguments, but got 2.
    this.mergeMaxQualityOptions(directStream, options);
    if (isVideo) {
      if (options.videoCodecOptions && directStream.video) {
        _.each(
          directStream.video.codecs,
          function (codecProfile, codecName) {
            if (options.videoCodecOptions[codecName]) {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.mergeCodecOptions(
                codecProfile,
                options.videoCodecOptions[codecName]
              );
            }
          },
          this
        );
      }
      if (options.audioCodecOptions && directStream.audio) {
        _.each(
          directStream.audio.codecs,
          function (codecProfile, codecName) {
            if (options.audioCodecOptions[codecName]) {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.mergeCodecOptions(
                codecProfile,
                options.audioCodecOptions[codecName]
              );
            }
          },
          this
        );
      }
    }
    return {
      directPlay: directPlay,
      directStream: directStream,
    };
  },
  mergeGenericOptions: function (profile: any, generic: any) {
    if (!generic) {
      return;
    }
    _.defaults(profile, _.omit(generic, 'video', 'audio'));
    _.each(
      ['audio', 'video'],
      function (type) {
        const genericByType = generic[type];
        let profileByType = profile[type];
        if (!genericByType) {
          return;
        }
        if (!profileByType) {
          profileByType = {};
          profile[type] = profileByType;
        }
        // Assign all non-codec generic values. Codecs will be assigned later
        // to avoid assigning their values by reference.
        _.defaults(profileByType, _.omit(genericByType, 'codecs'));
        if (genericByType.codecs) {
          // Mix-in and create codecs entries in the profile based on generic.
          _.each(
            genericByType.codecs,
            function (codecProfile, codecName) {
              _.defaults(
                // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                this.ensureCodecs(codecName, profileByType),
                codecProfile
              );
            },
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this
          );
        }
      },
      this
    );
  },
  mergeCodecOptions: function (codecProfile: any, codecOptions: any) {
    _.each(
      codecOptions,
      function (value, key) {
        // Intentionally overwrite existing values.
        codecProfile[key] = value;
      },
      this
    );
  },
  ensureCodecs: function (codecName: any, profile: any) {
    if (!profile.codecs) {
      profile.codecs = {};
    }
    if (!profile.codecs[codecName]) {
      profile.codecs[codecName] = {};
    }
    return profile.codecs[codecName];
  },
  mergeMaxQualityOptions: function (
    profile: any,
    options: any,
    isCodecProfile: any
  ) {
    const names = [
      { option: 'bitrate', setting: 'maxBitrate' },
      { option: 'videoResolution', setting: 'maxVideoResolution' },
    ];
    _.each(
      names,
      function (name) {
        if (
          (name as any).option === 'videoResolution' &&
          options.type !== playerUtil.types.VIDEO
        ) {
          return;
        }
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const optionValue = this.getMaxQualityOption(
          profile,
          (name as any).option,
          options
        );
        // Don't change anything if a value hasn't been specified.
        if (optionValue == null) {
          return;
        }
        const setting = (name as any).setting;
        const primaryStreamName = playerUtil.getStreamByType(options.type);
        if (profile.hasOwnProperty(setting)) {
          profile[setting] = optionValue;
        } else if (profile.hasOwnProperty(primaryStreamName)) {
          profile[primaryStreamName][setting] = optionValue;
        } else if (!isCodecProfile) {
          profile[primaryStreamName] = {};
          profile[primaryStreamName][setting] = optionValue;
        }
      },
      this
    );
  },
  getMaxQualityOption: function (profile: any, name: any, options: any) {
    const quality = options.quality || {};
    // bitrate -> maxBitrate
    const maxName = 'max' + name.charAt(0).toUpperCase() + name.slice(1);
    let maxOptionsValue = quality[name];
    const maxProfileValue = this.getPrimaryStreamProperty(
      profile,
      maxName,
      options
    );
    if (name === 'videoResolution' && maxOptionsValue) {
      maxOptionsValue =
        videoPlayerQualities.getResolutionFromSize(maxOptionsValue);
    }
    if (name === 'bitrate') {
      const server = options.item.server;
      const maxUploadBitrate = server.get('maxUploadBitrate');
      if (
        maxUploadBitrate &&
        (!maxOptionsValue || maxUploadBitrate < maxOptionsValue)
      ) {
        maxOptionsValue = maxUploadBitrate;
      }
    }
    if (maxOptionsValue || maxProfileValue) {
      let maxValue;
      // If both a max value is passed in the quality option and a max value
      // is specified in the profile, take the lower of the two.
      if (maxOptionsValue && maxProfileValue) {
        maxValue = Math.min(maxOptionsValue, maxProfileValue);
      } else {
        maxValue = maxOptionsValue || maxProfileValue;
      }
      return maxValue;
    }
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  addAudioCodec: function (codec: any, profile: any, options: any) {
    profile.audio || (profile.audio = {});
    this.ensureCodecs(codec, profile.audio);
  },
  analyzeMedia: function (media: any, profile: any, options: any) {
    log.info('[MDE] Starting analysis of', this.getMediaTitle(media));
    log('[MDE] Analyzing direct play');
    const directPlayDecision = this.canDirectPlay(
      media,
      profile.directPlay,
      options
    );
    if (!directPlayDecision.result) {
      log('[MDE] Cannot direct play:', directPlayDecision.failedCheck);
    }
    const decision = {
      finalProfile: profile,
      transcodeServer: options.transcodeServer,
      preferredDirectPlayProtocol: options.preferredDirectPlayProtocol,
      item: options.item,
      media: media,
      canDirectPlay: directPlayDecision.result,
      canDirectPlayFailedCheck: directPlayDecision.failedCheck,
      // These properties are unused internally; eventually they should
      // influence Media selection. These properties are however passed through
      // for decision-based upsell logic.
      canDisplayHDR: !!options.canDisplayHDR,
      canDisplayDolbyVision: !!options.canDisplayDolbyVision,
    };
    // `transcoderOptions` may be a function that we need to pass the
    // decision object to so it has a media item reference.
    let transcoderOptions = options.transcoderOptions || {};
    if (_.isFunction(transcoderOptions)) {
      transcoderOptions = transcoderOptions(decision);
    }
    if (options.type === playerUtil.types.VIDEO) {
      log('[MDE] Analyzing video direct stream');
      const videoStream = getSelectedVideo(media);
      if (videoStream) {
        const directStreamDecision = this.canDirectStreamVideo(
          videoStream,
          profile.directStream.video,
          options,
          transcoderOptions
        );
        (decision as any).canDirectStreamVideo = directStreamDecision.result;
        (decision as any).canDirectStreamVideoFailedCheck =
          directStreamDecision.failedCheck;
      }
    }
    if (options.type !== playerUtil.types.PHOTO) {
      log('[MDE] Analyzing audio direct stream');
      const audioStream = getSelectedAudio(media);
      if (audioStream) {
        const directStreamDecision = this.canDirectStreamAudio(
          audioStream,
          profile.directStream.audio,
          options,
          transcoderOptions
        );
        (decision as any).canDirectStreamAudio = directStreamDecision.result;
        (decision as any).canDirectStreamAudioFailedCheck =
          directStreamDecision.failedCheck;
      }
    }
    // Decide if we are going to use soft subtitles or burn subtitles into
    // the video. This could affect the `canDirectPlay` and
    // `canDirectStreamVideo` decisions.
    const selectedSubtitles = getSelectedSubtitles(decision.media);
    if (selectedSubtitles) {
      log('[MDE] Analyzing subtitles');
      _.extend(decision, this.analyzeSubtitles(selectedSubtitles, options));
    }
    // Verify that the item has no missing parts. If the item cannot direct
    // play, check that the server can transcode. If the server cannot
    // remux, check that the server is not remux only.
    log('[MDE] Analyzing playability');
    _.extend(decision, this.canPlay(decision, options));
    // Extend the decision with the effective bitrate and resolution of the
    // media item. These could be lower than the media item's bitrate and
    // resolution if we are transcoding down to a lower quality.
    _.extend(
      decision,
      this.getEffectiveQuality(media, profile.directStream, options)
    );
    log(
      '[MDE] Finished analysis of:',
      this.getMediaTitle(media),
      this.getDecisionLogs(decision)
    );
    return decision;
  },
  canDirectPlay: function (media: any, directPlayProfile: any, options: any) {
    if (options.forceDirectPlay) {
      log('[MDE] Direct play forced');
      return { result: true };
    }
    if (
      options.allowDirectPlay === false &&
      !options.transcodeServer.get('isCloud')
    ) {
      log.warn('[MDE] Direct play failed; option is disabled');
      return { result: false, failedCheck: 'allowDirectPlay' };
    }
    if (
      options.type === playerUtil.types.VIDEO &&
      !this.canDirectPlayVideo(media, options)
    ) {
      return { result: false, failedCheck: 'canDirectPlayVideo' };
    }
    const containerProfile = directPlayProfile[media.container];
    if (!containerProfile) {
      log.warn(
        '[MDE] Direct play failed; unsupported container:',
        media.container
      );
      return { result: false, failedCheck: 'containerProfile' };
    }
    const keys = _.keys(containerProfile);
    if (keys && keys.length) {
      for (let i = 0, len = keys.length; i < len; i++) {
        const key = keys[i];
        const profileValue = containerProfile[key];
        // Check the mime type with a canPlayType test.
        if (key === 'mimeType') {
          if (
            !options.skipCanPlayTypeTest &&
            !this.canPlayType(
              playerUtil.getElementByType(options.type),
              profileValue
            )
          ) {
            log.warn(
              '[MDE] Direct play failed; canPlayType test, %s: %s',
              key,
              profileValue
            );
            return { result: false, failedCheck: 'canPlayType' };
          }
          // We can perform stream specific tests for video and audio.
        } else if (key === 'video' || key === 'audio') {
          // The html video element does not respect our selected audio
          // stream. If the first stream does not match the selected stream,
          // we should transcode unless explicitly told that the player
          // allows multi-track audio.
          const streamType = streamTypes.get(key);
          const streams = getStreams(media, { streamType: streamType.id });
          const selectedStream =
            key === 'video' ? getSelectedVideo(media) : getSelectedAudio(media);
          if (!selectedStream) {
            log.warn(
              '[MDE] No stream selected; will not be able to analyze stream codecs'
            );
            continue;
          }
          const requiresMultiTrackAudio =
            key === 'audio' && selectedStream !== streams[0];
          if (requiresMultiTrackAudio && !options.allowMultiTrackAudio) {
            log.warn('[MDE] Direct play failed; multi-track audio is required');
            return { result: false, failedCheck: 'allowMultiTrackAudio' };
          }
          const analyzeStream = this.analyzeStream(
            selectedStream,
            profileValue,
            options
          );
          if (!analyzeStream.result) {
            return { result: false, failedCheck: analyzeStream.failedCheck };
          }
        } else if (key === 'photo') {
          const analyzeStream = this.analyzeStream(
            _.first(media.Part),
            profileValue,
            options
          );
          if (!analyzeStream.result) {
            return { result: false, failedCheck: analyzeStream.failedCheck };
          }
        } else if (!this.validateProfileProperty(key, profileValue, media)) {
          return {
            result: false,
            failedCheck: `validateProfileProperty:${key}`,
          };
        }
      }
      return { result: true };
    }
    // Allow direct play if the container exists, but no limitations have
    // been provided for the container in the profile.
    return { result: true };
  },
  canDirectPlayVideo: function (media: any, options: any) {
    // Never try to direct play if the option is disabled.
    if (!options.allowDirectPlay && !options.transcodeServer.get('isCloud')) {
      log.warn('[MDE] Direct play failed; option is disabled');
      return false;
    }
    // Do not allow direct play of HLS videos without native HLS support.
    if (media.protocol === 'hls' && !options.hasNativeHLS) {
      log.warn('[MDE] Direct play failed; cannot play HLS');
      return false;
    }
    // Do not allow direct play of FLV videos without Flash.
    if (media.container === 'flv' && !options.hasFlash) {
      log.warn('[MDE] Direct play failed; cannot play FLV');
      return false;
    }
    // Do not allow direct play of DASH videos without DASH support.
    if (media.protocol === 'dash' && !options.hasDASH) {
      log.warn('[MDE] Direct play failed; cannot play DASH');
      return false;
    }
    return true;
  },
  canPlayType: function (elementType: any, mimeType: any) {
    if (elementType === playerUtil.elements.PHOTO) {
      return true;
    }
    const el = document.createElement(elementType);
    return !!(el.canPlayType && el.canPlayType(mimeType).replace(/^no$/, ''));
  },
  canDirectStreamVideo: function (
    stream: any,
    streamProfile: any,
    options: any,
    transcoderOptions: any
  ) {
    // Never try to direct stream video if the option is disabled.
    if (
      !transcoderOptions.directStream &&
      !options.transcodeServer.get('isCloud')
    ) {
      log.warn('[MDE] Direct stream video failed; option is disabled');
      return { result: false, failedCheck: 'options:directStream' };
    }
    return this.analyzeStream(stream, streamProfile, options);
  },
  canDirectStreamAudio: function (
    stream: any,
    streamProfile: any,
    options: any,
    transcoderOptions: any
  ) {
    // Never try to direct stream audio if the option is disabled.
    if (
      !transcoderOptions.directStreamAudio &&
      !options.transcodeServer.get('isCloud')
    ) {
      log.warn('[MDE] Direct stream audio failed; option is disabled');
      return { result: false, failedCheck: 'options:directStreamAudio' };
    }
    return this.analyzeStream(stream, streamProfile, options);
  },
  // @ts-expect-error ts-migrate(7023) FIXME: 'analyzeStream' implicitly has return type 'any' b... Remove this comment to see the full error message
  analyzeStream: function (stream: any, streamProfile: any, options: any) {
    const codecProfile =
      streamProfile &&
      streamProfile.codecs &&
      streamProfile.codecs[stream.codec];
    for (const key in streamProfile) {
      const profileValue = streamProfile[key];
      if (key === 'codecs') {
        const codecValue = profileValue[stream.codec];
        if (!codecValue) {
          return {
            result: false,
            failedCheck: `noCodecInProfile:${stream.codec}`,
          };
        }
        // @ts-expect-error ts-migrate(7022) FIXME: 'analyzeStream' implicitly has type 'any' because ... Remove this comment to see the full error message
        const analyzeStream = this.analyzeStream(stream, codecValue, options);
        if (!analyzeStream.result) {
          return { result: false, failedCheck: analyzeStream.failedCheck };
        }
      } else if (
        !(codecProfile && key in codecProfile) &&
        !this.validateProfileProperty(key, profileValue, stream)
      ) {
        return { result: false, failedCheck: `validateProfileProperty:${key}` };
      }
    }
    return { result: true };
  },
  validateProfileProperty: function (
    propertyName: any,
    profileValue: any,
    source: any
  ) {
    // Any key that starts with "max" must use the greater than operator.
    // Otherwise, use a basic equality test and check if the profile value
    // is an array that contains the property.
    const boundParam = propertyName.match(/^(min|max)(\w)(\w*)/);
    let value;
    if (boundParam) {
      const isLowerBound = boundParam[1] === 'min';
      const isUpperBound = boundParam[1] === 'max';
      // Fix the casing. maxBitDepth -> bitDepth
      propertyName = boundParam[2].toLowerCase() + boundParam[3];
      value = source[propertyName];
      if (value == null) {
        log.warn(
          '[MDE] Unable to validate %s; re-analyze the media if possible',
          propertyName
        );
      }
      // We can't directly compare resolution strings, so convert to height
      // values that can be compared.
      if (value != null && propertyName === 'videoResolution') {
        value = videoPlayerQualities.getHeightFromResolution(value);
        profileValue =
          videoPlayerQualities.getHeightFromResolution(profileValue);
      }
      if (value != null) {
        if (isLowerBound && value < profileValue) {
          log.warn(
            '[MDE] Invalid profile property; %s: %s < %s',
            propertyName,
            value,
            profileValue
          );
          return false;
        }
        if (isUpperBound && value > profileValue) {
          log.warn(
            '[MDE] Invalid profile property; %s: %s > %s',
            propertyName,
            value,
            profileValue
          );
          return false;
        }
      }
    } else {
      value = source[propertyName];
      if (value == null) {
        log.warn(
          '[MDE] Unable to validate %s; re-analyze the media if possible',
          propertyName
        );
      }
      if (value !== profileValue && !_.contains(profileValue, value)) {
        log.warn(
          '[MDE] Invalid profile property; %s: %s !== %s',
          propertyName,
          value,
          profileValue
        );
        return false;
      }
    }
    return true;
  },
  analyzeSubtitles: function (subtitles: any, options: any) {
    // Retain the current behavior of always burning subtitles into the
    // video unless specified otherwise.
    const burnLevel = options.subtitlesBurnLevel || subtitlesBurnLevels.ALWAYS;
    const codec = subtitles.codec;
    const isVodLike =
      !!subtitles.key &&
      codec === undefined &&
      options.transcodeServer.get('isCloud');
    const isCloudLiveTvWithSubtitles =
      options.transcodeServer.get('isCloud') &&
      (codec === 'vtt' || codec === 'eia_608');
    log('[MDE] Subtitle codec:', codec);
    log('[MDE] Burn level:', burnLevel);
    if (isVodLike) {
      log('[MDE] Using sidecar subtitles %s', subtitles.key);
      // Allow VOD sidecar SRT subs to be direct played. Inject this test early
      // to a) ALWAYS ignore subtitles burn level and b) avoid the `supports`
      // calls below returning `true` incorrectly when the cloud server is
      // involved.
      return {
        useSidecarSrtSubtitles: true,
      };
    }
    if (isCloudLiveTvWithSubtitles) {
      // Similar to the VOD check, allow LTVoP streams to be direct played.
      return {
        canDirectPlay: true,
      };
    }
    if (burnLevel === subtitlesBurnLevels.NEVER) {
      // If we've insisted never to burn, do not attempt soft subs.
      // This will allow the player to handle the selected subtitles itself.
      return {
        useSoftSubtitles: false,
      };
    }
    if (burnLevel === subtitlesBurnLevels.ALWAYS) {
      log.warn('[MDE] Direct play failed; asked to always burn subtitles');
      return {
        useSoftSubtitles: false,
        canDirectPlay: false,
        canDirectPlayFailedCheck: 'subtitles:burnLevelAlways',
        canDirectStreamVideo: false,
      };
    }
    if (!options.transcodeServer.supports('softSubtitles')) {
      // Server needs to meet a minimum version to use soft subtitles
      log.warn(
        '[MDE] Direct play failed; server support requires burn subtitles'
      );
      log.warn('[MDE] Soft subtitles failed; server version');
      return {
        useSoftSubtitles: false,
        canDirectPlay: false,
        canDirectPlayFailedCheck: 'subtitles:softSubtitlesUnsupported',
        canDirectStreamVideo: false,
      };
    }
    if (isBitmapSubtitles(codec)) {
      // Bitmap subtitles, as opposed to other text-based formats, must be
      // burned in to the video.
      log.warn('[MDE] Soft subtitles failed; bitmap subtitles');
      return {
        useSoftSubtitles: false,
        canDirectPlay: false,
        canDirectPlayFailedCheck: 'subtitles',
        canDirectStreamVideo: false,
      };
    } else if (
      /^(ass|ssa)$/i.test(codec) &&
      burnLevel === subtitlesBurnLevels.AUTO
    ) {
      // Auto should burn in subtitles so we can be sure to display ASS
      // subtitles with the proper styling.
      log.warn('[MDE] Soft subtitles failed; ASS subtitles');
      return {
        useSoftSubtitles: false,
        canDirectPlay: false,
        canDirectPlayFailedCheck: 'subtitles',
        canDirectStreamVideo: false,
      };
    } else if (/^eia_608$/i.test(codec)) {
      log.warn('[MDE] Soft subtitles failed; Closed captioning');
      return {
        useSoftSubtitles: false,
        canDirectPlay: false,
        canDirectPlayFailedCheck: 'subtitles',
        canDirectStreamVideo: false,
      };
    } else if (
      options.transcodeServer.supports('softSubtitlesWithDirectPlay')
    ) {
      if (options.allowDirectStreamVideoWithSoftSubtitles) {
        __DEV__ &&
          assert(
            options.allowDirectStreamVideoWithSoftSubtitles != null,
            'options.allowDirectStreamVideoWithSoftSubtitles must be set'
          );
        return {
          useSoftSubtitles: true,
        };
      }
      return {
        useSoftSubtitles: true,
        canDirectStreamVideo: false,
      };
    } else {
      // The user's PMS version does not support direct play with soft subtitles.
      log.warn('[MDE] Direct play failed; %s subtitles', codec);
      if (options.allowDirectStreamVideoWithSoftSubtitles) {
        return {
          useSoftSubtitles: true,
          canDirectPlay: false,
          canDirectPlayFailedCheck: 'subtitles',
        };
      }
      return {
        useSoftSubtitles: false,
        canDirectPlay: false,
        canDirectPlayFailedCheck: 'subtitles',
        canDirectStreamVideo: false,
      };
    }
  },
  canPlay: function (decision: any, options: any) {
    const errorMessage =
      this.validateParts(decision, options) ||
      this.validateTranscoder(decision, options);
    if (errorMessage) {
      return {
        canPlay: false,
        errorMessage: errorMessage,
      };
    }
    return { canPlay: true };
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  validateParts: function (decision: any, options: any) {
    const missingParts = getMissingParts(decision.media);
    if (!missingParts.length) {
      return;
    }
    log.warn('[MDE] Unable to play; missing parts');
    const missingPart = _.first(missingParts);
    if (Number(missingPart.exists) === 1) {
      return t('Please check the permissions for this file.');
    } else {
      return t(
        'Please check that the file exists and the necessary drive is mounted.'
      );
    }
  },
  validateTranscoder: function (decision: any, options: any) {
    if (decision.canDirectPlay) {
      return;
    }
    const type = options.type;
    const isMusic = type === playerUtil.types.MUSIC;
    const isVideo = type === playerUtil.types.VIDEO;
    const transcodeServer = options.transcodeServer;
    const transcoderAudio = transcodeServer.get('transcoderAudio');
    const transcoderVideo = transcodeServer.get('transcoderVideo');
    let notPowerfulEnoughMessage;
    // With dummy video music transcoding the `type` will be video and the
    // item will be a music track. Handle transcode requests for the music
    // `type` and any playback of a music track the same way.
    if (isMusic || options.item.get('type') === 'track') {
      notPowerfulEnoughMessage = t(
        'This server is not powerful enough to convert audio.'
      );
      if (isVideo && !transcoderVideo) {
        // Ignore the `transcoderVideoRemuxOnly` flag for transcoding a
        // track to dummy video.
        // See: https://plex.slack.com/archives/transcoder/p1458608327000385
        log.warn(
          '[MDE] Unable to play; server unable to transcode music using video transcoder'
        );
        return notPowerfulEnoughMessage;
      }
      if (isMusic && !transcoderAudio) {
        log.warn(
          '[MDE] Unable to play; server unable to transcode music using audio transcoder'
        );
        return notPowerfulEnoughMessage;
      }
    } else if (isVideo) {
      const tryIncreasingQualityMessage = t(
        'This server is not powerful enough to convert video. You may be able to enable playback by increasing your quality setting.'
      );
      const relayLimitationMessage = t(
        'This server is not powerful enough to convert video. Playback qualities are limited because an indirect connection is being used.'
      );
      const canTranscodeVideo =
        decision.canDirectStreamVideo ||
        !transcodeServer.get('transcoderVideoRemuxOnly');
      notPowerfulEnoughMessage = t(
        'This server is not powerful enough to convert video.'
      );
      if (!transcoderVideo || !canTranscodeVideo) {
        log.warn('[MDE] Unable to play; server unable to transcode video');
        if (options.quality) {
          // If original quality (index -1) was already selected
          // tell the user we can't transcode
          if (options.quality.index === videoPlayerQualities.ORIGINAL) {
            return notPowerfulEnoughMessage;
          }
          // Check if we can direct play original quality
          // so we can give a better error message.
          const originalDecisionOptions = _.clone(options);
          originalDecisionOptions.quality = _.extend(
            { index: videoPlayerQualities.ORIGINAL },
            _.pick(options.quality, 'attribute')
          );
          if (this.getMediaDecision(originalDecisionOptions).canDirectPlay) {
            return tryIncreasingQualityMessage;
          }
          if (transcodeServer.get('maxUploadBitrateReason') === 'relay') {
            return relayLimitationMessage;
          }
          return notPowerfulEnoughMessage;
        }
        return tryIncreasingQualityMessage;
      }
    }

    return;
  },
  getEffectiveQuality: function (
    media: any,
    directStreamProfile: any,
    options: any
  ) {
    const quality = options.quality;
    const effectiveQuality = {};
    const transcoderQualityOptions = {};
    if (!media.bitrate) {
      log.warn(
        '[MDE] Unable to determine effective quality; re-analyze the media if possible'
      );
    }
    // If an item is going to be transcoded to a lower quality, we should
    // consider that when comparing resolutions and bitrates between
    // decisions. A 1080p video should not be selected because it's
    // resolution is higher than a 720p video if it's going to be
    // transcoded to 720p anyway.
    if (options.type === playerUtil.types.VIDEO) {
      (effectiveQuality as any).bitrate = media.bitrate;
      (effectiveQuality as any).videoResolution =
        videoPlayerQualities.getHeightFromResolution(media.videoResolution);
      const maxVideoBitrate = this.getPrimaryStreamProperty(
        directStreamProfile,
        'maxBitrate',
        options
      );
      if (maxVideoBitrate) {
        const videoStream = getSelectedVideo(media);
        const videoBitrate = videoStream ? videoStream.bitrate : media.bitrate;
        (effectiveQuality as any).bitrate = Math.min(
          videoBitrate,
          maxVideoBitrate
        );
        (transcoderQualityOptions as any).maxVideoBitrate = maxVideoBitrate;
      }
      if (quality) {
        // We need to have an integer value that can compare to the video's
        // resolution. First convert to a resolution string and then to the
        // comparable height.
        const videoResolution = quality.videoResolution;
        if (videoResolution) {
          const maxResolution = videoPlayerQualities.getResolutionFromSize(
            quality.videoResolution
          );
          const maxHeight =
            videoPlayerQualities.getHeightFromResolution(maxResolution);
          (effectiveQuality as any).videoResolution = Math.min(
            (effectiveQuality as any).videoResolution,
            maxHeight
          );
        }
      }
    } else if (options.type === playerUtil.types.MUSIC) {
      (effectiveQuality as any).bitrate = media.bitrate;
      const maxAudioBitrate = this.getPrimaryStreamProperty(
        directStreamProfile,
        'maxBitrate',
        options
      );
      if (maxAudioBitrate) {
        (effectiveQuality as any).bitrate = Math.min(
          media.bitrate,
          maxAudioBitrate
        );
        (transcoderQualityOptions as any).musicBitrate = maxAudioBitrate;
      }
    }
    (effectiveQuality as any).transcoderQualityOptions =
      transcoderQualityOptions;
    return effectiveQuality;
  },
  getPrimaryStreamProperty: function (
    profile: any,
    propertyName: any,
    options: any
  ) {
    // First look for the property at the root, and then look in the stream
    // type specific object (e.g. profile.video and profile.audio).
    const primaryStreamName = playerUtil.getStreamByType(options.type);
    const primaryStreamProfile = profile[primaryStreamName];
    let value = profile[propertyName];
    if (value == null && primaryStreamProfile) {
      value = primaryStreamProfile[propertyName];
    }
    return value;
  },
  getMediaTitle: function (media: any) {
    let title =
      media.title || media.videoResolution || media.bitrate || media.container;
    const details = _.compact([
      media.container,
      media.videoCodec,
      media.audioCodec,
    ]);
    const video = getSelectedVideo(media);
    if (video) {
      details.push(video.level, video.profile);
    }
    if (details.length) {
      title += ' (' + details.join(', ') + ')';
    }
    return title;
  },
  getDecisionLogs: function (decision: any) {
    const logs = {
      canPlay: decision.canPlay,
      canDirectPlay: decision.canDirectPlay,
    };
    const canDirectStreamVideo = decision.canDirectStreamVideo;
    if (canDirectStreamVideo != null) {
      (logs as any).canDirectStreamVideo = canDirectStreamVideo;
    }
    const canDirectStreamAudio = decision.canDirectStreamAudio;
    if (canDirectStreamAudio != null) {
      (logs as any).canDirectStreamAudio = canDirectStreamAudio;
    }
    const useSoftSubtitles = decision.useSoftSubtitles;
    if (useSoftSubtitles != null) {
      (logs as any).useSoftSubtitles = useSoftSubtitles;
    }
    const bitrate = decision.bitrate;
    if (bitrate != null && !_.isNaN(bitrate)) {
      (logs as any).bitrate = bitrate;
    }
    const videoResolution = decision.videoResolution;
    if (videoResolution != null && !_.isNaN(videoResolution)) {
      (logs as any).videoResolution = videoResolution;
    }
    return logs;
  },
  //
  // Sorting
  getBestDecision: function (decisions: any) {
    const bestDecision = _.last(this.sortDecisions(decisions));
    log(
      '[MDE] Selected %s to play:',
      this.getMediaTitle(bestDecision.media),
      this.getDecisionLogs(bestDecision)
    );
    return bestDecision;
  },
  sortDecisions: function (decisions: any) {
    // Decisions are sorted from worst choice to best. Sort functions are
    // from least important to most important.
    // Return null values instead of undefined so `sortBy` behaves reliably.
    // Undefined values are sorted after false and true, while null is
    // sorted before.
    //
    // [null, false, true, undefined]
    // [-1, null, 0, 1, undefined]
    return _.chain(decisions)
      .reverse() // If all else is equal, prefer the first item.
      .sortBy(this.preferHigherBitrate, this)
      .sortBy(this.preferMoreAudioChannels, this)
      .sortBy(this.preferDirectStreamAudio, this)
      .sortBy(this.preferHigherResolution, this)
      .sortBy(this.preferDirectStreamVideo, this)
      .sortBy(this.preferDirectPlayMatchingPreferredProtocol, this)
      .sortBy(this.preferDirectPlay, this)
      .sortBy(this.preferHigherResolutionIfCapable, this)
      .sortBy(this.preferCloudIfRemote, this)
      .sortBy(this.preferDirectPlayOrDirectStreamIfUHD, this)
      .sortBy(this.preferPlayable, this)
      .value();
  },
  preferPlayable: function (decision: any) {
    if (decision.canPlay === undefined) {
      return null;
    }
    return decision.canPlay;
  },
  preferDirectPlayOrDirectStreamIfUHD: function (decision: any) {
    const height = decision.media.height;
    if (height === undefined) {
      return null;
    }
    // Transcoding >= 4K content is infrequently practical.
    // See: https://github.com/plexinc/plex-web-client/issues/15440
    const description = videoPlayerQualities.getResolutionFromHeight(height);
    // Expression is case insensitive as our utilities mix capitalization.
    return /^\d+k$/i.test(description)
      ? decision.canDirectPlay || decision.canDirectStreamVideo
      : true;
  },
  preferCloudIfRemote: function (decision: any) {
    // If local to the server, prefer the original media item or a local
    // proxy. If remote, prefer a cloud proxy.
    const isCloudProxy = decision.media.proxyType === proxyTypes.CLOUD;
    if (decision.transcodeServer.get('isPrivateConnection')) {
      return !isCloudProxy;
    } else {
      return isCloudProxy;
    }
  },
  preferHigherResolutionIfCapable: function (decision: any) {
    // If the transcode server can fully transcode video and the item can
    // direct play or direct stream, prefer the higher resolution item.
    const transcodeServer = decision.transcodeServer;
    const canTranscode = transcodeServer.get('transcoderVideo');
    const canRemuxOnly = transcodeServer.get('transcoderVideoRemuxOnly');
    if (
      canTranscode &&
      !canRemuxOnly &&
      (decision.canDirectPlay || decision.canDirectStreamVideo)
    ) {
      return this.preferHigherResolution(decision);
    } else {
      return null;
    }
  },
  preferDirectPlay: function (decision: any) {
    if (decision.canDirectPlay === undefined) {
      return null;
    }
    return decision.canDirectPlay;
  },
  preferDirectPlayMatchingPreferredProtocol: function (decision: any) {
    if (
      !decision.canDirectPlay ||
      !decision.media.protocol ||
      !decision.preferredDirectPlayProtocol
    ) {
      return null;
    }
    return decision.media.protocol === decision.preferredDirectPlayProtocol;
  },
  preferDirectStreamVideo: function (decision: any) {
    if (decision.canDirectStreamVideo === undefined) {
      return null;
    }
    return decision.canDirectStreamVideo;
  },
  preferHigherResolution: function (decision: any) {
    // Use the effective quality determined and set on the decision.
    if (decision.videoResolution === undefined) {
      return null;
    }
    return decision.videoResolution;
  },
  preferDirectStreamAudio: function (decision: any) {
    if (decision.canDirectStreamAudio === undefined) {
      return null;
    }
    return decision.canDirectStreamAudio;
  },
  preferMoreAudioChannels: function (decision: any) {
    const audioChannels = decision.media.audioChannels;
    if (audioChannels === undefined) {
      return null;
    }
    return audioChannels;
  },
  preferHigherBitrate: function (decision: any) {
    // Use the effective quality determined and set on the decision.
    if (decision.bitrate === undefined) {
      return null;
    }
    return decision.bitrate;
  },
};
export default mediaDecisionEngine;
