import clamp from 'app/js/utils/math/clamp';

export default function random(unclampedLength = 24): string {
  const targetLength = clamp(unclampedLength, 0, 1024);
  let result = '';

  while (result.length < targetLength) {
    result += Math.random()
      .toString(36)
      .substring(2, targetLength - result.length + 2);
  }

  return result;
}
