import parse from 'date-fns/parse';
import getUTCDate from 'app/js/utils/dateTime/getUTCDate';

// Example dateString: 'Sun, 10 Feb 2019 21:32:05 GMT'.
const dateFormat = "EEE, dd MMM yyyy HH:mm:ss 'GMT'";

export default function getServerUTCTime(dateString: string): number {
  const parsedDate = parse(dateString, dateFormat, new Date());

  const parsedUTCDate = getUTCDate(parsedDate);

  return parsedUTCDate.getTime();
}
