/* eslint-plugin-disable @typescript-eslint */

import shallowEqual from './shallowEqual';

const getDisplayName =
  __DEV__ &&
  ((instance: any) => {
    return (
      instance.displayName ||
      instance.constructor.displayName ||
      instance.constructor.name
    );
  });

const tryShallowCompareDebug =
  __DEV__ &&
  ((instance: any, key: any, nextValue: any) => {
    const shallowCompareDebug = require('./shallowCompareDebug').default;
    // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
    const name = getDisplayName(instance);
    const enterablePath = instance.props.enterablePath;

    shallowCompareDebug(instance[key], nextValue, {
      name: `${name}${enterablePath ? `[${enterablePath}]` : ''}.${key}`,
    });
  });

export default function shouldPureComponentUpdate(
  instance: any,
  nextProps: any,
  nextState: any,
  nextContext: any,
  shallowEqual_: any
) {
  let debug = false;

  if (__DEV__) {
    const getDevOption = require('app/js/utils/dev/getDevOption').default;
    const pureComponentUpdateLogs = getDevOption('pureComponentUpdateLogs');

    if (pureComponentUpdateLogs) {
      if (pureComponentUpdateLogs === true) {
        debug = true;
      } else {
        // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
        debug = pureComponentUpdateLogs.test(getDisplayName(instance));
      }
    }
  }

  // This more terse than Babel's default value transform.
  const comparator = shallowEqual_ || shallowEqual;

  let areEqual = comparator(instance.props, nextProps);

  if (areEqual) {
    areEqual = comparator(instance.state, nextState);

    if (areEqual && nextContext) {
      // `nextContext` is unique in that it's optional.
      areEqual = comparator(instance.context, nextContext);

      if (!areEqual) {
        // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
        debug && tryShallowCompareDebug(instance, 'context', nextContext);
      }
    } else {
      // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
      debug && tryShallowCompareDebug(instance, 'state', nextState);
    }
  } else {
    // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
    debug && tryShallowCompareDebug(instance, 'props', nextProps);
  }

  return !areEqual;
}
