/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectServers from 'app/js/stores/selectors/server/selectServers';

const selectAllSourcesSidebarServers = createSelector(
  selectServers,
  (servers) => {
    return servers.filter(
      (server: any) =>
        !server.isCloud && !server.isEmbedded && !!server.friendlyName
    );
  }
);

export default selectAllSourcesSidebarServers;
