import { MutableRefObject, Ref } from 'react';

function mergeRefs<RefType>(...refs: Ref<RefType>[]): (value: RefType) => void {
  return (value) => {
    refs.forEach((ref) => {
      if (typeof ref === 'function') {
        ref(value);
      } else if (ref != null) {
        (ref as MutableRefObject<RefType>).current = value;
      }
    });
  };
}

export default mergeRefs;
