/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import parseInteger from 'app/js/utils/parser/parseInteger';
import path from 'app/js/utils/url/path';

const InviteModel = AbstractModel.extend({
  //
  // Properties

  name: 'InviteModel',

  context: ['server'],
  rootPropertyName: 'Invite',

  sessionAttrs: {
    isAccepting: attrTypes.boolean,
    isRemoving: attrTypes.boolean,
  },

  syncedAttrs: {
    id: {
      type: attrTypes.string,
      save: true,
    },
    friend: attrTypes.boolean,
    home: attrTypes.boolean,
    server: attrTypes.boolean,
    friendlyName: attrTypes.string,
    username: attrTypes.string,
    email: attrTypes.string,
    thumb: attrTypes.string,
    createdAt: attrTypes.integer,
    Server: attrTypes.array,
  },

  urlRoot: '/api/invites/requests',

  url() {
    let url;

    if (this.isNew()) {
      url = _.result(this, 'urlRoot');
    } else {
      const params = this.pick('friend', 'server', 'home');

      // Convert the boolean values to 1s and 0s.
      _.each(_.keys(params), function (key) {
        params[key] = params[key] ? '1' : '0';
      });

      url = this.collection
        ? _.result(this.collection, 'url')
        : _.result(this, 'urlRoot');
      url = url.replace(/([^/])$/, '$1/') + encodeURIComponent(this.get('id'));
      url = path.join(url, params);
    }

    return url;
  },

  augmentResponse(response: any) {
    if (!response.Server) {
      return response;
    }

    const servers = Array.isArray(response.Server)
      ? response.Server
      : [response.Server];

    servers.forEach((server: any) => {
      server.numLibraries = parseInteger(server.numLibraries);
    });

    return response;
  },

  defaults: {
    isAccepting: false,
    isRemoving: false,
  },
});

export default InviteModel;
