/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectPrimaryEligibleServers from 'app/js/stores/selectors/server/selectPrimaryEligibleServers';

const selectManageableServerEntityIDs = createSelector(
  selectPrimaryEligibleServers,
  (servers) => {
    return servers.reduce((acc: any, server: any) => {
      if (!server.isShared && !server.isOutOfDate) {
        acc.push(server.entityID);
      }

      return acc;
    }, []);
  }
);

export default selectManageableServerEntityIDs;
