/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import capabilities from 'app/js/utils/device/capabilities';
import Cache from './Cache';

const ImageCache = Cache.extend({
  //
  // Properties
  maxCycles: 3,
  maxSize: 50,
  imageParamsRegex: /&?(size|scale|width|height)=([^&]+)/g,
  //
  // Init
  initialize: function () {
    // eslint-disable-next-line prefer-rest-params
    Cache.prototype.initialize.apply(this, arguments);
    this._infoMemo = {};
  },
  //
  // Control
  store: function (item: any, key: any) {
    key || (key = this.getItemCacheKey(item));
    if (!key) {
      return;
    }
    const url = item.url;
    const info = this.getImageInfo(url);
    const image = { url: url, blobUrl: item.blobUrl, size: info.size };
    let cachedItem = this.get(key);
    if (cachedItem) {
      if (!cachedItem.images.hasOwnProperty(url)) {
        const sizes = cachedItem.sizes;
        cachedItem.images[url] = image;
        // Keep the sizes array sorted by size so we can easily find
        // alternate sizes to act as placeholders for uncached images.
        const i = _.sortedIndex(sizes, image, 'size');
        sizes.splice(i, 0, image);
      }
    } else {
      cachedItem = {
        images: {},
        sizes: [image],
      };
      cachedItem.images[url] = image;
      // Only start cycling and clearing the cache once an item has been
      // added to the cache.
      this.startListening();
      cachedItem._cacheKey = key;
      this._cache[key] = cachedItem;
      this.size++;
      if (!item.persistInCache) {
        this.setItemCycle(cachedItem, key, _.first(this.cycles));
      }
    }
  },
  getItemCacheKey: function (item: any) {
    const info = this.getImageInfo(item.url);
    return info.key;
  },
  getImageInfo: function (url: any) {
    if (this._infoMemo.hasOwnProperty(url)) {
      return this._infoMemo[url];
    }
    const params = { width: '0', scale: '1' };
    const key = url.replace(
      this.imageParamsRegex,
      function (
        // @ts-expect-error ts-migrate(6133) FIXME: 'match' is declared but its value is never read.
        match: any,
        name: any,
        value: any
      ) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        params[name] = value;
        return '';
      }
    );
    const sizeParts = (params as any).size && (params as any).size.split('-');
    const width =
      sizeParts && sizeParts.length === 2
        ? sizeParts[1]
        : parseInt(params.width, 10);
    const info = {
      key: key,
      size: width * parseInt(params.scale, 10),
    };
    this._infoMemo[url] = info;
    return info;
  },
  removeImageInfo: function (url: any) {
    if (this._infoMemo.hasOwnProperty(url)) {
      delete this._infoMemo[url];
    }
  },
  getBlobUrl: function (url: any) {
    const info = this.getImageInfo(url);
    const item = this.get(info.key);
    if (item) {
      const image = item.images[url];
      if (image) {
        return image.blobUrl;
      }
    }
  },
  getPlaceholder: function (url: any) {
    // If we have another resolution for the same image, use that as a
    // placeholder while we wait for the larger image to load.
    const info = this.getImageInfo(url);
    const item = this.get(info.key);
    if (item) {
      // If we already have this exact image cached, we don't need to use a
      // placeholder.
      if (item.images[url]) {
        return;
      }
      const largestCachedImage = _.last(item.sizes);
      return largestCachedImage.blobUrl;
    }
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  remove: function (item: any, options: any) {
    // eslint-disable-next-line prefer-rest-params
    Cache.prototype.remove.apply(this, arguments);
    _.each(
      item.sizes,
      function (image) {
        // Clean up the info memo when revoking the blob url so it doesn't
        // endlessly grow.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.removeImageInfo(image.url);
        if (capabilities.hasObjectUrl) {
          window.URL.revokeObjectURL(image.blobUrl);
        }
      },
      this
    );
  },
});
export default ImageCache;
