import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import log from 'app/js/utils/log/log';

export default function sourceInitializer() {
  log('[Sources] Initializing...');

  resolve('sourceManager').start();

  log('[Sources] Finished initialization');

  return $.Deferred().resolve().promise();
}
