/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as metricsEvents from 'app/js/utils/constants/metrics/metricsEvents';
import * as upsellOrigins from 'app/js/utils/constants/upsellOrigins';
import { getWWWPlexUrl } from 'app/js/utils/getRuntimeUrl';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import NavBarButton from 'app/js/views/v3/components/navbar/NavBarButton';
import PlexPassModal from 'app/js/views/v3/components/user/modals/PlexPassModal';

type State = any;

class NavBarPlexPassButton extends Component<
  { shouldBypassModal: boolean },
  State
> {
  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isPlexPassModalOpen: false,
    };
  }

  //
  // Listeners

  onButtonPress = (_event: any) => {
    const { shouldBypassModal } = this.props;

    resolve('metrics').trackEvent({
      event: metricsEvents.CLIENT_UPSELL,
      interaction: true,
      context: {
        experiments: {
          'upsell-experiment': shouldBypassModal ? 'direct' : 'in-app',
        },
      },
    });

    if (!shouldBypassModal) {
      this.setState({
        isPlexPassModalOpen: true,
      });
    }
  };

  onModalClose = (_event: any) => {
    this.setState({
      isPlexPassModalOpen: false,
    });
  };

  //
  // Render

  render() {
    const { shouldBypassModal } = this.props;

    const subscribeUrl = path.join(getWWWPlexUrl('/plex-pass/purchase'), {
      utm_source: 'app.plex.tv',
      utm_medium: 'app',
      utm_campaign: 'PP_in-app-upsell_01',
      utm_content: upsellOrigins.NAV_BAR,
    });

    return (
      <>
        <NavBarButton
          target="_blank"
          to={shouldBypassModal ? subscribeUrl : undefined}
          onPress={this.onButtonPress}
        >
          {t('Go Premium')}
        </NavBarButton>
        <PlexPassModal
          isOpen={this.state.isPlexPassModalOpen}
          metricsProperties={{
            context: metricsContexts.NAV_BAR,
          }}
          origin={upsellOrigins.NAV_BAR}
          onModalClose={this.onModalClose}
        />
      </>
    );
  }
}

export default NavBarPlexPassButton;
