//
// Action Types

export const INIT_PRE_PLAY_PAGE = 'INIT_PRE_PLAY_PAGE';
export const INIT_PRE_PLAY_DESCENDANT_LIST = 'INIT_PRE_PLAY_DESCENDANT_LIST';
export const INIT_PRE_PLAY_SIBLING_LIST = 'INIT_PRE_PLAY_SIBLING_LIST';
export const INIT_PRE_PLAY_AUGMENTED_HUBS = 'INIT_PRE_PLAY_AUGMENTED_HUBS';
export const PRE_FETCH_PRE_PLAY_METADATA_ITEM =
  'PRE_FETCH_PRE_PLAY_METADATA_ITEM';
export const FETCH_PRE_PLAY_METADATA_ITEM = 'FETCH_PRE_PLAY_METADATA_ITEM';
export const FETCH_PRE_PLAY_RELATED_HUBS = 'FETCH_PRE_PLAY_RELATED_HUBS';
export const FETCH_PRE_PLAY_AUGMENTED_HUBS = 'FETCH_PRE_PLAY_AUGMENTED_HUBS';
export const MAKE_PRE_PLAY_VIDEO_DECISION = 'MAKE_PRE_PLAY_VIDEO_DECISION';
export const REPOPULATE_PRE_PLAY_POPULAR_TRACKS =
  'REPOPULATE_PRE_PLAY_POPULAR_TRACKS';

//
// Action Creators

export const initPrePlayPage = createActionCreator(INIT_PRE_PLAY_PAGE);
export const initPrePlayDescendantList = createActionCreator(
  INIT_PRE_PLAY_DESCENDANT_LIST
);
export const initPrePlaySiblingList = createActionCreator(
  INIT_PRE_PLAY_SIBLING_LIST
);
export const initPrePlayAugmentedHubs = createActionCreator(
  INIT_PRE_PLAY_AUGMENTED_HUBS
);
export const preFetchPrePlayMetadataItem = createActionCreator(
  PRE_FETCH_PRE_PLAY_METADATA_ITEM
);
export const fetchPrePlayMetadataItem = createActionCreator(
  FETCH_PRE_PLAY_METADATA_ITEM
);
export const fetchPrePlayRelatedHubs = createActionCreator(
  FETCH_PRE_PLAY_RELATED_HUBS
);
export const fetchPrePlayAugmentedHubs = createActionCreator(
  FETCH_PRE_PLAY_AUGMENTED_HUBS
);
export const makePrePlayVideoDecision = createActionCreator(
  MAKE_PRE_PLAY_VIDEO_DECISION
);
export const repopulatePrePlayPopularTracks = createActionCreator(
  REPOPULATE_PRE_PLAY_POPULAR_TRACKS
);

//
// Helpers

function findPrePlayPageModel(appModel: any) {
  return appModel.get('ui').get('prePlayPage');
}

function isMetadataForPage(appModel: any, payload: any) {
  const { metadataItemEntityID, containerKeyPattern } = payload;

  if (containerKeyPattern) {
    return false;
  }

  const serverModel = findServerModel(appModel, payload);
  const prePlayPageModel = findPrePlayPageModel(appModel);
  const metadataItemModel = prePlayPageModel.get('metadataItem');

  if (!metadataItemModel || metadataItemModel.server !== serverModel) {
    return false;
  }

  if (
    metadataItemEntityID &&
    metadataItemModel.entityID() !== metadataItemEntityID
  ) {
    return false;
  }

  return true;
}

// @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
function repopulatePageChildren(appModel: any, payload: any, dispatch: any) {
  const prePlayPageModel = findPrePlayPageModel(appModel);

  const relatedHubs = prePlayPageModel.get('relatedHubs');

  if (relatedHubs) {
    dispatch(fetchPrePlayRelatedHubs());
  }
}

function setFileUnavailable(appModel: any, payload: any) {
  const serverModel = findServerModel(appModel, payload);
  const prePlayPageModel = findPrePlayPageModel(appModel);
  const metadataItem = prePlayPageModel.get('metadataItem');

  if (!metadataItem || metadataItem.server !== serverModel) {
    return;
  }

  const context = payload.activity.Context;

  if (!context || context.key !== metadataItem.get('key')) {
    return;
  }

  if (context.exists == null && context.accessible == null) {
    return;
  }

  const isFileUnavailable = !context.exists || !context.accessible;
  prePlayPageModel.set('isFileUnavailable', isFileUnavailable);

  if (isFileUnavailable) {
    // `fetchPrePlayMetadataItemHelper` flips `isPreFetched` to `true` when the
    // request completes. If the file is unavailable the decision will fail
    // so we need to flip `isPreFetched` back to `false` so that we can check
    // if the file exists again if we retry.
    // Deferred to avoid race condition with metadataItem.fetch promise.
    window.setTimeout(() => {
      metadataItem.set('isPreFetched', false);
    }, 0);
  }
}

export const prePlayPageActionHandlers = createActionHandler({
  [INIT_PRE_PLAY_PAGE](appModel: any, payload: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const server = findServerModel(appModel, payload);
    const provider = findProviderModel(appModel, payload);
    const { providerEntityID, metadataKey } = payload;

    if (
      providerEntityID === prePlayPageModel.get('providerEntityID') &&
      metadataKey === prePlayPageModel.get('metadataKey')
    ) {
      return;
    }

    const metadataItem = new MetadataModel(
      { key: metadataKey },
      { server, provider }
    );

    const hubKey = path.join([
      metadataKey.replace(/(\/library)?\/metadata/, '/hubs/metadata'),
      'related',
    ]);

    const includeExternalMedia = hasUserFeature(
      appModel,
      features.ADD_TO_LIBRARY
    );

    const relatedHubs = new HubCollection([], {
      server,
      provider,
      metadataItem,
      hubKey,
      includeExternalMedia,
      // Indicate to PMS that we expect augmented content for the item
      includeExternalMetadata: true,
      asyncAugmentMetadata: true,
    });

    prePlayPageModel.restoreDefaults({
      providerEntityID,
      metadataKey,
      metadataItem,
      // Try to maintain the same sibling list so navigating between siblings
      // doesn't need to constantly re-fetch them.
      siblingKey: prePlayPageModel.get('siblingKey'),
      siblingList: prePlayPageModel.get('siblingList'),
      relatedHubs,
    });
  },

  [INIT_PRE_PLAY_DESCENDANT_LIST](appModel: any, payload: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const server = findServerModel(appModel, payload);
    const provider = findProviderModel(appModel, payload);
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const { providerEntityID, descendantKey } = payload;

    if (!descendantKey) {
      prePlayPageModel.set({
        descendantKey,
        descendantList: undefined,
      });
      return;
    }

    if (
      providerEntityID === prePlayPageModel.get('providerEntityID') &&
      descendantKey === prePlayPageModel.get('descendantKey')
    ) {
      return;
    }

    const includeExternalMedia = hasUserFeature(
      appModel,
      features.ADD_TO_LIBRARY
    );

    // We need to pass includeRelated=1 when fetching tracks in order to support
    // the Plex Mix action, which depends on a related directory within each
    // track item.
    const includeRelated =
      metadataItemModel.get('type') === 'album' ||
      (metadataItemModel.get('type') === 'artist' &&
        metadataItemModel.get('skipChildren'));

    const descendantList = new MetadataListModel(
      { containerKey: descendantKey },
      {
        server,
        provider,
        excludeAllLeaves: true,
        includeExternalMedia,
        includeRelated,
      }
    );

    prePlayPageModel.set({
      descendantKey,
      descendantList,
    });
  },

  [INIT_PRE_PLAY_SIBLING_LIST](appModel: any, payload: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const server = findServerModel(appModel, payload);
    const provider = findProviderModel(appModel, payload);
    const { providerEntityID, siblingKey } = payload;

    if (!siblingKey) {
      prePlayPageModel.set({
        siblingKey,
        siblingList: undefined,
      });
      return;
    }

    if (
      providerEntityID === prePlayPageModel.get('providerEntityID') &&
      siblingKey === prePlayPageModel.get('siblingKey')
    ) {
      return;
    }

    const includeExternalMedia = hasUserFeature(
      appModel,
      features.ADD_TO_LIBRARY
    );

    const siblingList = new MetadataListModel(
      { containerKey: siblingKey },
      {
        server,
        provider,
        excludeAllLeaves: true,
        includeExternalMedia,
      }
    );

    prePlayPageModel.set({
      siblingKey,
      siblingList,
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [INIT_PRE_PLAY_AUGMENTED_HUBS](appModel: any, payload: any, dispatch: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const metadataItem = prePlayPageModel.get('metadataItem');
    const augmentationKey = metadataItem.get('augmentationKey');

    const musicAugmentationTypes = [metadataTypes.ARTIST, metadataTypes.ALBUM];
    const metadataAugmentationTypes = [
      metadataTypes.MOVIE,
      metadataTypes.SHOW,
      metadataTypes.SEASON,
      metadataTypes.EPISODE,
    ];
    const type = metadataItem.get('type');

    // Only allow users with the tunefind feature to get movie/TV augmentation
    const allowAugmentation =
      musicAugmentationTypes.includes(type) ||
      metadataAugmentationTypes.includes(type);

    if (!allowAugmentation || !augmentationKey) {
      prePlayPageModel.set({
        isAugmentedHubsPending: false,
        isAugmentedHubsPopulated: true,
      });
      return;
    }

    const augmentedHubs = new HubCollection([], {
      server: metadataItem.server,
      provider: metadataItem.provider,
      hubKey: metadataItem.get('augmentationKey'),
      excludeFields: '', // We want to show the summary
      wait: 1,
    });

    prePlayPageModel.set({
      augmentedHubs,
    });

    dispatch(fetchPrePlayAugmentedHubs());
  },

  [PRE_FETCH_PRE_PLAY_METADATA_ITEM](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    fetchPrePlayMetadataItemHelper(metadataItemModel, { forceFetch: true });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [FETCH_PRE_PLAY_METADATA_ITEM](appModel: any, payload: any, dispatch: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const metadataItemModel = prePlayPageModel.get('metadataItem');

    const previousPending = prePlayPageModel.get('isMetadataItemPending');
    prePlayPageModel.set('isMetadataItemPending', true);

    fetchPrePlayMetadataItemHelper(metadataItemModel, { forceFetch: true })
      .done(() => {
        prePlayPageModel.set({
          isMetadataItemPending: false,
          isMetadataItemPopulated: true,
          metadataItemError: null,
        });

        dispatch(initPrePlayAugmentedHubs());
      })
      // @ts-expect-error ts-migrate(6133) FIXME: 'instance' is declared but its value is never read... Remove this comment to see the full error message
      .fail((instance: any, xhr: any) => {
        if (xhr.aborting) {
          prePlayPageModel.set('isMetadataItemPending', previousPending);
          return;
        }

        prePlayPageModel.set({
          isMetadataItemPending: false,
          metadataItemError: xhrErrorFilter(xhr),
        });
      });
  },

  [FETCH_PRE_PLAY_RELATED_HUBS]: createFetchHandler({
    key: 'relatedHubs',
    getModel: findPrePlayPageModel,
  }),

  [FETCH_PRE_PLAY_AUGMENTED_HUBS]: createFetchHandler({
    key: 'augmentedHubs',
    getModel: findPrePlayPageModel,
  }),

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [MAKE_PRE_PLAY_VIDEO_DECISION](appModel: any, payload: any, dispatch: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const experienceSettingsModel = appModel
      .get('user')
      .get('experienceSettings');
    const metadataItemModel = prePlayPageModel.get('metadataItem');

    const decisionOptions = resolve('getDecisionOptions')(metadataItemModel);

    const quality = videoPlayerQualities.getCurrentQuality(
      metadataItemModel,
      experienceSettingsModel,
      (updatedSettings: any) =>
        dispatch(saveExperienceSettings(updatedSettings))
    );

    const mediaDecision = mediaDecisionEngine.getMediaDecision({
      item: metadataItemModel,
      transcodeServer: metadataItemModel.server,
      quality,
      ...decisionOptions,
    });

    prePlayPageModel.set('decisionMediaItem', mediaDecision.media);
  },

  [UPDATE_ACTIVITY](appModel: any, payload: any) {
    if (payload.activity.type === activityTypes.LIBRARY_REFRESH_ITEMS) {
      setFileUnavailable(appModel, payload);
    }
  },

  [FINISH_ACTIVITY](appModel: any, payload: any) {
    if (payload.activity.type === activityTypes.LIBRARY_REFRESH_ITEMS) {
      setFileUnavailable(appModel, payload);
    }
  },

  [REPOPULATE_METADATA_ITEM](appModel: any, payload: any, dispatch: any) {
    if (isMetadataForPage(appModel, payload)) {
      repopulatePageChildren(appModel, payload, dispatch);
    }
  },

  [REPOPULATE_PAGE](appModel: any, payload: any, dispatch: any) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const metadataItemModel = prePlayPageModel.get('metadataItem');

    if (isMetadataForPage(appModel, payload)) {
      // Pass the `skipRefresh` param so on-the-fly media analysis does not
      // happen for items while a scan is in progress. This helps to avoid
      // overwhelming low-powered servers.

      metadataItemModel.fetch({
        data: { skipRefresh: 1 },
        usePopulatedParams: true,
      });

      repopulatePageChildren(appModel, payload, dispatch);
    }
  },

  [REPOPULATE_PRE_PLAY_POPULAR_TRACKS](
    appModel: any,
    payload: any,
    dispatch: any
  ) {
    const prePlayPageModel = findPrePlayPageModel(appModel);
    const metadataItemModel = prePlayPageModel.get('metadataItem');
    const albumModel = tryFindMetadataItemModel(appModel, {
      metadataItemEntityID: payload.albumMetadataItemEntityID,
    });

    if (
      metadataItemModel &&
      albumModel &&
      metadataItemModel.get('key') === albumModel.get('parentKey')
    ) {
      dispatch(
        repopulateMetadataItem({
          serverEntityID: payload.serverEntityID,
          metadataItemEntityID: metadataItemModel.entityID(),
        })
      );
    }
  },
});
