/* eslint-plugin-disable @typescript-eslint */

import AbstractStorage from 'app/js/adapters/storage/AbstractStorage';

const BasicVolatileStorage = AbstractStorage.extend({
  //
  // Properties

  name: 'BasicVolatileStorage',

  //
  // Init

  initialize() {
    const storage = {};

    this.api = {
      getItem(key: any) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        return storage[key];
      },

      setItem(key: any, value: any) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        storage[key] = value;
      },

      removeItem(key: any) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        delete storage[key];
      },

      clear() {
        for (const prop in storage) {
          if (storage.hasOwnProperty(prop)) {
            // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
            delete storage[prop];
          }
        }
      },
    };
  },

  //
  // Control

  getStorage() {
    return this.api;
  },
});

export default BasicVolatileStorage;
