/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

// A ReminderModel is created the first time a reminder is displayed. It is then
// responsible for influencing the timing of future reminders.
const ReminderModel = AbstractModel.extend({
  //
  // Properties

  name: 'ReminderModel',

  syncedAttrs: {
    data: attrTypes.object,
    id: attrTypes.string.isRequired,
    // Next date to show a reminder formatted as an ISO8601 string.
    remindAfter: attrTypes.string.isRequired,
    // The number of times the user has been reminded not counting the initial
    // notification.
    times: attrTypes.integer.isRequired,
  },
});

export default ReminderModel;
