/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import commands from 'app/js/commands/commands';
import { redirectToRoute } from 'app/js/stores/actions/appActions';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import SignOutPage from './SignOutPage';

const mapStateToProps = createSelector(selectUser, ({ isSignedIn }) => ({
  isSignedIn,
}));

function createMapDispatchToProps(dispatch: any) {
  return {
    onSignOut() {
      commands.execute('signOut').then(() => {
        dispatch(redirectToRoute({ route: 'login', replace: true }));
      });
    },
  };
}

export default connect(mapStateToProps, createMapDispatchToProps)(SignOutPage);
