/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import attrTypes from 'app/js/models/attrTypes';
import UserEntitlementCollection from 'app/js/models/user/UserEntitlementCollection';
import UserFeatureCollection from 'app/js/models/user/UserFeatureCollection';
import UserRoleCollection from 'app/js/models/user/UserRoleCollection';
import UserServiceCollection from 'app/js/models/user/UserServiceCollection';
import UserSubscriptionCollection from 'app/js/models/user/UserSubscriptionCollection';
import StoredModel from './StoredModel';
import StoredServerCollection from './StoredServerCollection';
import StoredUserSettingCollection from './StoredUserSettingCollection';

const StoredUserModel = StoredModel.extend({
  //
  // Properties

  name: 'StoredUserModel',

  additionalRelations: ['servers'],

  syncedAttrs: {
    id: attrTypes.integer,
    authToken: attrTypes.string,
    anonymous: attrTypes.boolean,
    username: attrTypes.string,
    email: attrTypes.string,
    title: attrTypes.string,
    thumb: attrTypes.string,
    pin: attrTypes.string,
    home: attrTypes.boolean,
    restricted: attrTypes.boolean,
    guest: attrTypes.boolean,
    hasAllEntitlements: attrTypes.boolean,
    lastPrimaryServerID: attrTypes.string,
  },

  syncedRelations: {
    subscriptions: {
      relationClass: UserSubscriptionCollection,
    },
    roles: {
      relationClass: UserRoleCollection,
    },
    entitlements: {
      relationClass: UserEntitlementCollection,
    },
    services: {
      relationClass: UserServiceCollection,
    },
    features: {
      relationClass: UserFeatureCollection,
    },
    settings: {
      relationClass: StoredUserSettingCollection,
    },
    experienceSettings() {
      return {
        relationClass: resolve('StoredUserExperienceSettingsModel'),
      };
    },

    // Additional
    servers: {
      relationClass: StoredServerCollection,
    },
  },

  //
  // Control

  clearSensitiveAttributes() {
    this.unset('authToken');
    this.unset('pin');
    this.get('servers').reset();
  },
});

export default StoredUserModel;
