/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve, waitFor } from 'app/js/adapters/adapters';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import LogModel from 'app/js/models/debug/LogModel';
import debugModel from 'app/js/models/debug/debugModel';
import dispatcher from 'app/js/utils/dispatcher';

const tokenRegEx =
  /("?(auth_token|accessToken|X-Plex-Token|authToken|token)"?[:=].*?)("?[-\w]+"?)/g;
const emailRegEx = /("?(email)"?[:=]([\s]+)?)("(.*)")/g;
const ipRegEx = /("?(address|host|uri)"?[:=].*?)(".*")/g;
export function scrubResponse(response: any) {
  if (!response) {
    return;
  }
  return response
    .replace(tokenRegEx, '$1"TOKEN"')
    .replace(emailRegEx, '$1"EMAIL"')
    .replace(ipRegEx, '$1"IP ADDRESS"');
}
const passwordRegEx = /(password=)([^&]+)/;
function scrubUrl(url: any) {
  return url
    .replace(tokenRegEx, '$1TOKEN')
    .replace(passwordRegEx, '$1PASSWORD');
}
function scrubHeaders(headers: any) {
  if (headers && headers.hasOwnProperty('X-Plex-Token')) {
    headers['X-Plex-Token'] = 'TOKEN';
  }
  if (headers && headers.hasOwnProperty('Authorization')) {
    headers.Authorization = 'BASIC AUTHORIZATION';
  }
  return headers;
}
const LogCollection = Backbone.Collection.extend({
  //
  // Properties
  model: LogModel,
  //
  // Init
  // @ts-expect-error ts-migrate(6133) FIXME: 'models' is declared but its value is never read.
  initialize: function (models: any, options: any) {
    _.bindAll(this, 'onAjaxComplete', 'onAppModelRegistered');
    this.parent = options.parent;
    debugModel.parent = options.parent;
    waitFor('appModel').done(this.onAppModelRegistered);
  },
  //
  // Control
  reconfigure() {
    const debugLevel = resolve('appModel')
      .get('deviceSettings')
      .get('debugLevel');
    if (debugLevel) {
      this.start();
    } else {
      this.stop();
    }
  },
  start: function () {
    if (this.enabled) {
      return;
    }
    this.enabled = true;
    this.reset();
    this.listenTo(dispatcher, 'log:message', this.onLogMessage);
    Backbone.history.on('route', this.onRoute, this);
    $(document).on('ajaxComplete', this.onAjaxComplete);
  },
  stop: function () {
    if (!this.enabled) {
      return;
    }
    this.enabled = false;
    this.reset();
    this.stopListening(dispatcher);
    Backbone.history.off('route', this.onRoute, this);
    $(document).off('ajaxComplete', this.onAjaxComplete);
  },
  // LogCollection is used with the adapters system. Implement a destroy
  // method that will clean up after itself when called.
  destroy: function () {
    this.stop();
    this.stopListening();
  },
  add: function () {
    // I'm somewhat paranoid about this sort of debug logging using
    // up a ton of memory. So limit the number of entries to some
    // arbitrary value, users should generally pull up the logs
    // shortly after reproducing the problem.
    if (this.length > 1000) {
      this.shift();
    }
    // eslint-disable-next-line prefer-rest-params
    Backbone.Collection.prototype.add.apply(this, arguments);
  },
  toJSON: function () {
    // Always add some debug info to the beginning.
    // eslint-disable-next-line prefer-rest-params
    const models = Backbone.Collection.prototype.toJSON.apply(this, arguments);
    models.unshift(_.extend({ type: 'info' }, debugModel.toJSON()));
    return models;
  },
  toJSONString: function () {
    // Output pretty JSON.
    return JSON.stringify(this.toJSON(), null, 2);
  },
  //
  // Listeners
  onAppModelRegistered: function (appModel: any) {
    const deviceSettings = appModel.get('deviceSettings');
    this.listenTo(deviceSettings, 'change:debugLevel', this.reconfigure);
    this.reconfigure();
  },
  onLogMessage: function (message: any) {
    this.add(message);
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'router' is declared but its value is never read.
  onRoute: function (router: any, name: any, args: any) {
    const message = {
      type: 'navigation',
      route: name,
      args: args,
      fragment: window.location.hash,
    };
    this.add(message);
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onAjaxComplete: function (event: any, xhr: any, options: any) {
    const debugLevel = resolve('appModel')
      .get('deviceSettings')
      .get('debugLevel');
    const message = {
      type: 'ajax',
      url: scrubUrl(options.url),
      status: xhr.status,
    };
    // We need to scrub the headers and response in case they contain
    // tokens or other info that users would not want to share publicly.
    if (!_.isEmpty(options.headers)) {
      (message as any).headers = scrubHeaders(options.headers);
    }
    if (!_.isEmpty(options.data)) {
      (message as any).params = scrubUrl(options.data);
    }
    if (xhr.status === 200) {
      // Only include the response if the debug level is set to verbose.
      if (debugLevel === 2) {
        (message as any).response = scrubResponse(xhr.responseText);
      } else {
        (message as any).responseLength = xhr.responseText
          ? xhr.responseText.length
          : 0;
      }
    } else {
      (message as any).statusText = xhr.statusText;
      (message as any).response = scrubResponse(xhr.responseText);
    }
    this.add(message);
  },
});
export default LogCollection;
