import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertErrorView.tpl';

const ConvertErrorView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'modal-body',
  template: tpl,

  events: {
    'click .try-again-link': 'onTryAgainClick',
  },

  //
  // Control

  serializeData: function () {
    return {
      status: this.options.status,
    };
  },

  //
  // Listeners

  onTryAgainClick: function (event: any) {
    event.preventDefault();

    this.triggerMethod('request:state', 'loading');
  },
});

export default ConvertErrorView;
