/* eslint-plugin-disable @typescript-eslint */

import * as adapters from 'app/js/adapters/adapters';
import ServerModel from 'app/js/models/server/ServerModel';
import updateServerConnectionsFromResource from './updateServerConnectionsFromResource';

export default function createServerFromResource(
  resource: any,
  isAlwaysPMS = false
) {
  // Attempt to infer if a server is a full Plex Media Server or a mobile
  // server. The `version` and `serverClass` attributes are used in the
  // primary server decision and it is useful to have these set immediately.
  const isPMS = isAlwaysPMS || resource.get('product') === 'Plex Media Server';

  const server = new ServerModel(
    {
      machineIdentifier: resource.id,
      friendlyName: resource.get('name'),
      // The `productVersion` is not indicative of the server version for mobile
      // media servers.
      version: isPMS ? resource.get('productVersion') : undefined,
      isShared: !resource.get('owned'),
      accessToken: resource.get('accessToken'),
      sourceTitle: resource.get('sourceTitle'),
      publicAddressMatches: resource.get('publicAddressMatches'),
      httpsRequired: resource.get('httpsRequired'),
      platform: resource.get('platform'),
      platformVersion: resource.get('platformVersion'),
      presence: resource.get('presence'),
      home: resource.get('home'),
      serverClass: isPMS ? undefined : ServerModel.serverClasses.SECONDARY,
      createdAt: resource.get('createdAt'),
      lastSeenAt: resource.get('lastSeenAt'),
      ownerId: resource.get('ownerId'),
    },
    { parent: adapters.resolve('appModel') }
  );

  updateServerConnectionsFromResource(server, resource);

  return server;
}
