import $ from 'jquery';
import _ from 'underscore';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/ConfirmModal.tpl';

const ConfirmModal = BaseModal.extend({
  //
  // Properties

  className: 'confirm-modal modal modal-sm fade',
  template: tpl,

  ui: {
    doubleConfirmMessage: '.double-confirm-message',
    confirmBtn: '.confirm-btn',
    confirmLabel: '.confirm-btn .btn-label',
  },

  events: _.extend(
    {
      'click .confirm-btn': 'onConfirm',
    },
    BaseModal.prototype.events
  ),

  whitelistedOptions: [
    'title',
    'message',
    'warningMessage',
    'confirmLabel',
    'cancelLabel',
    'hasDanger',
    'hideCancel',
  ],

  //
  // Init

  initialize: function (options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);

    this.async = options.async;
    this.data = _.pick(options, this.whitelistedOptions);

    this.promise = $.Deferred();
  },

  //
  // Control

  serializeData: function () {
    return _.defaults(this.data, {
      confirmLabel: 'Yes',
      cancelLabel: 'No',
    });
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);

    window.setTimeout(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.confirmBtn.focus();
      }, this),
      this.focusDelay
    );
  },

  onHide: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onHide.apply(this, arguments);

    this.promise.reject();
  },

  onConfirm: function (event: any) {
    event.preventDefault();

    if (this.options.requireDoubleConfirm && !this.hasConfirmedOnce) {
      this.hasConfirmedOnce = true;

      const doubleConfirmMessage = this.options.doubleConfirmMessage;
      const doubleConfirmLabel = this.options.doubleConfirmLabel;

      if (doubleConfirmMessage) {
        this.ui.doubleConfirmMessage.text(doubleConfirmMessage);
      }

      if (doubleConfirmLabel) {
        this.ui.confirmLabel.text(doubleConfirmLabel);
      }

      return;
    }

    this.promise.resolve();

    if (this.async) {
      this.ui.confirmBtn.addClass('active');
    } else {
      this.hide();
    }
  },
});

export default ConfirmModal;
