/* eslint-plugin-disable @typescript-eslint */

import * as providerActionIDs from 'app/js/utils/constants/providerActionIDs';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import t from 'app/js/utils/t';
import ProviderFeatureCollection from './ProviderFeatureCollection';
import ProviderModel from './ProviderModel';

const LegacyLibraryProviderModel = ProviderModel.extend({
  //
  // Properties

  name: 'LegacyLibraryProviderModel',

  defaults() {
    const server = this.server;
    const isFullServer = server.get('isFullServer');
    const isFullOwnedServer = server.get('isFullOwnedServer');

    const features = [
      { type: providerFeatureTypes.CONTENT, key: '/library/sections' },
      { type: providerFeatureTypes.MATCH, key: '/library/matches' },
      { type: providerFeatureTypes.METADATA, key: '/library/metadata' },
    ];

    if (isFullServer) {
      features.push(
        { type: providerFeatureTypes.SEARCH, key: '/hubs/search' },
        { type: providerFeatureTypes.PLAY_QUEUE, key: '/playQueues' },
        { type: providerFeatureTypes.PLAYLIST, key: '/playlists' },
        {
          type: providerFeatureTypes.TIMELINE,
          key: '/:/timeline',
        },
        {
          type: providerFeatureTypes.ACTIONS,
          // @ts-expect-error ts-migrate(2345) FIXME: Argument of type '{ type: string; actions: ({ id: ... Remove this comment to see the full error message
          actions: [
            {
              id: providerActionIDs.SCROBBLE,
              key: '/:/scrobble',
              reverseKey: '/:/unscrobble',
            },
            {
              id: providerActionIDs.RATE,
              key: '/:/rate',
            },
          ],
        }
      );
    }

    if (isFullOwnedServer) {
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type '{ type: string; }' is not assign... Remove this comment to see the full error message
      features.push({ type: providerFeatureTypes.MANAGE });
    }

    return {
      identifier: providerIdentifiers.LIBRARY,
      title: t('Library'),
      types: ['video', 'audio', 'photo'],
      protocols: isFullOwnedServer ? ['stream', 'download'] : ['stream'],
      features: new ProviderFeatureCollection(features, {
        parse: true,
        server,
      }),
    };
  },
});

export default LegacyLibraryProviderModel;
