/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
// @ts-expect-error ts-migrate(6133) FIXME: 'forwardedRefShape' is declared but its value is n... Remove this comment to see the full error message
import forwardedRefShape from 'app/js/views/v3/helpers/props/shapes/forwardedRefShape';

interface PortalProps {
  className?: string;
  style?: any;
  isOpen?: boolean;
  closeTimeoutMilliseconds?: number;
  allowInteractionEventBubbling?: boolean;
  // @ts-expect-error ts-migrate(2749) FIXME: 'forwardedRefShape' refers to a value, but is bein... Remove this comment to see the full error message
  forwardedRef?: forwardedRefShape;
  onOpen?: (...args: any[]) => any;
  onClose?: (...args: any[]) => any;
}

type PortalState = any;

// A portal can be used to transport a component rendered in the component tree
// to the document body. This allows us to create modals, menus, etc. where they
// are needed, but render them in a way that they overlay the entire
// application.

class Portal extends Component<PortalProps, PortalState> {
  static defaultProps = {
    isOpen: true,
    closeTimeoutMilliseconds: 0,
    allowInteractionEventBubbling: false,
  };

  // eslint-disable-next-line react/sort-comp
  _closeTimeoutID: any;

  //
  // Static

  // eslint-disable-next-line react/sort-comp
  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    if (nextProps.isOpen === prevState.isOpen) {
      return null;
    }

    return {
      isOpen: nextProps.isOpen,
      shouldPreventPortalClose:
        nextProps.closeTimeoutMilliseconds &&
        !nextProps.isOpen &&
        prevState.isOpen,
    };
  }

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isOpen: props.isOpen,
      shouldPreventPortalClose: false,
    };
  }

  componentDidMount() {
    if (this.props.onOpen && this.props.isOpen) {
      this.props.onOpen();
    }
  }

  componentDidUpdate(prevProps: PortalProps, prevState: PortalState) {
    if (
      !this.state.shouldPreventPortalClose &&
      prevState.shouldPreventPortalClose
    ) {
      this._closeTimeoutID = window.clearTimeout(this._closeTimeoutID);
    }

    if (
      this.state.shouldPreventPortalClose &&
      !prevState.shouldPreventPortalClose
    ) {
      this._closeTimeoutID = window.setTimeout(
        this.onCloseTimeout,
        this.props.closeTimeoutMilliseconds
      );
    }

    if (this.props.onOpen && this.props.isOpen && !prevProps.isOpen) {
      this.props.onOpen();
    }
  }

  componentWillUnmount() {
    if (this._closeTimeoutID) {
      window.clearTimeout(this._closeTimeoutID);
    }
  }

  //
  // Listeners

  onCloseTimeout = () => {
    this._closeTimeoutID = null;
    this.setState({ shouldPreventPortalClose: false }, () => {
      if (this.props.onClose) {
        this.props.onClose();
      }
    });
  };

  onInteractionEvent = (event: any) => {
    // React 16 portals allow events to bubble up through the component tree, so
    // a click event can bubble up to the button that opened the portal. This
    // can lead to unexpected behavior, so stop propagation of interaction
    // events up from the portal.

    if (!this.props.allowInteractionEventBubbling) {
      event.stopPropagation();
    }
  };

  //
  // Render

  render() {
    const { className, style, children, forwardedRef } = this.props;

    const { isOpen, shouldPreventPortalClose } = this.state;

    if (!isOpen && !shouldPreventPortalClose) {
      return null;
    }

    return ReactDOM.createPortal(
      <div
        ref={forwardedRef}
        className={className}
        style={style}
        onBlur={this.onInteractionEvent}
        onClick={this.onInteractionEvent}
        onFocus={this.onInteractionEvent}
        onMouseDown={this.onInteractionEvent}
        onMouseUp={this.onInteractionEvent}
        onTouchCancel={this.onInteractionEvent}
        onTouchEnd={this.onInteractionEvent}
        onTouchMove={this.onInteractionEvent}
        onTouchStart={this.onInteractionEvent}
      >
        {children}
      </div>,
      document.body
    );
  }
}

export default React.forwardRef((props, ref) => {
  return <Portal {...props} forwardedRef={ref} />;
});
