/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import ProviderCountryCollection from 'app/js/models/provider/ProviderCountryCollection';
import ProviderEPGChannelCollection from 'app/js/models/provider/ProviderEPGChannelCollection';
import ProviderEPGRecentChannelCollection from 'app/js/models/provider/ProviderEPGRecentChannelCollection';
import ProviderSettingOptionsCollection from 'app/js/models/provider/ProviderSettingOptionsCollection';
import parser2 from 'app/js/utils/parser2';

const valueParsers = {
  int: parser2.parseInteger,
  bool: parser2.parseBoolean,
  double: parser2.parseFloat,
};

const ProviderSettingModel = AbstractModel.extend({
  //
  // Properties

  name: 'ProviderSettingModel',
  context: ['server', 'provider'],
  rootPropertyName: 'Setting',

  url() {
    return this.get('key');
  },

  sessionAttrs: {
    isOptionsPending: attrTypes.boolean,
    isOptionsPopulated: attrTypes.boolean,
    optionsError: attrTypes.object,
    isLocationsPending: attrTypes.boolean,
    isLocationsPopulated: attrTypes.boolean,
    locationsError: attrTypes.object,
    isLineupPending: attrTypes.boolean,
    isLineupPopulated: attrTypes.boolean,
    lineupError: attrTypes.object,
  },

  syncedAttrs: {
    country: attrTypes.string,
    countryLabel: attrTypes.string,
    hidden: attrTypes.boolean,
    id: attrTypes.string,
    key: attrTypes.string,
    label: attrTypes.string,
    multiselect: attrTypes.boolean,
    postalCode: attrTypes.string,
    required: attrTypes.boolean,
    selectedCount: attrTypes.integer,
    summary: attrTypes.string,
    type: attrTypes.string,
    value: attrTypes.oneOfType(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 3.
      attrTypes.string,
      attrTypes.boolean,
      attrTypes.float
    ),
    min: attrTypes.float,
    max: attrTypes.float,
    step: attrTypes.float,
    enumValues: attrTypes.string,
    default: attrTypes.string,
  },

  sessionRelations: {
    locations: {
      relationClass: ProviderCountryCollection,
    },

    lineup: {
      relationClass: ProviderEPGChannelCollection,
    },

    recentChannelsInGrid: {
      relationClass: ProviderEPGRecentChannelCollection,
    },
  },

  syncedRelations: {
    settings() {
      return {
        relationClass: require('./ProviderSettingCollection').default,
        rootPropertyName: 'Setting',
        deferred: true,
      };
    },

    options: {
      relationClass: ProviderSettingOptionsCollection,
      options(data: any) {
        return {
          url: data.key,
        };
      },
    },
  },

  defaults: {
    isOptionsPending: true,
    isOptionsPopulated: false,
    optionsError: null,
    isLocationsPending: true,
    isLocationsPopulated: false,
    locationsError: null,
    isLineupPending: true,
    isLineupPopulated: false,
    lineupError: null,
  },

  //
  // Control

  augmentResponse(response: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const valueParser = valueParsers[response.type];

    if (valueParser) {
      response.value = valueParser(response.value);
    }

    return response;
  },
});

export default ProviderSettingModel;
