import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as responseTypes from 'app/js/models/responseTypes';

const AgentAttributionModel = AbstractModel.extend({
  //
  // Properties

  name: 'AgentAttributionModel',
  context: ['server'],

  responseType: responseTypes.text,

  syncedAttrs: {
    content: attrTypes.string,
  },

  url: function () {
    return '/system/agents/attribution?identifier=' + this.parent.id;
  },

  //
  // Control

  augmentResponse: function (response: any) {
    return { content: response };
  },
});

export default AgentAttributionModel;
