import createDeepEqualSelector from 'app/js/stores/selectors/createDeepEqualSelector';
import selectServers from 'app/js/stores/selectors/server/selectServers';

const selectPrimaryEligibleServers = createDeepEqualSelector(
  selectServers,
  (servers) => servers.filter((server: any) => server.isPrimaryEligible)
);

export default selectPrimaryEligibleServers;
