/* eslint-plugin-disable @typescript-eslint */

import actionHandlers from './actions/actionHandlers';
import createStore from './createStore';

export default function createAppStore(appModel: any) {
  const appStore = createStore(actionHandlers, appModel);

  if (module.hot) {
    module.hot.accept('./actions/actionHandlers', () => {
      appStore.replaceActionHandlers(
        require('./actions/actionHandlers').default
      );
    });
  }

  return appStore;
}
