/* eslint-plugin-disable @typescript-eslint */

export const emptyElementUID = null;
export default function getActiveElementUID() {
  // See: https://github.com/plexinc/plex-web-client/issues/13419
  const activeElement = document.activeElement;
  const uid =
    activeElement &&
    (activeElement as any).dataset &&
    (activeElement as any).dataset.uid;
  return uid || emptyElementUID;
}
