/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

const portValidityRegex =
  /^([0-9]{1,4}|[1-5][0-9]{4}|6[0-4][0-9]{3}|65[0-4][0-9]{2}|655[0-2][0-9]|6553[0-5])$/;
const ipv4Regex = /^([0-9]{1,3})\.([0-9]{1,3})\.([0-9]{1,3})\.([0-9]{1,3})$/;
const ipv6ValidityRegex =
  /(([0-9a-fA-F]{1,4}:){7,7}[0-9a-fA-F]{1,4}|([0-9a-fA-F]{1,4}:){1,7}:|([0-9a-fA-F]{1,4}:){1,6}:[0-9a-fA-F]{1,4}|([0-9a-fA-F]{1,4}:){1,5}(:[0-9a-fA-F]{1,4}){1,2}|([0-9a-fA-F]{1,4}:){1,4}(:[0-9a-fA-F]{1,4}){1,3}|([0-9a-fA-F]{1,4}:){1,3}(:[0-9a-fA-F]{1,4}){1,4}|([0-9a-fA-F]{1,4}:){1,2}(:[0-9a-fA-F]{1,4}){1,5}|[0-9a-fA-F]{1,4}:((:[0-9a-fA-F]{1,4}){1,6})|:((:[0-9a-fA-F]{1,4}){1,7}|:)|fe80:(:[0-9a-fA-F]{0,4}){0,4}%[0-9a-zA-Z]{1,}|::(ffff(:0{1,4}){0,1}:){0,1}((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\.){3,3}(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])|([0-9a-fA-F]{1,4}:){1,4}:((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\.){3,3}(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9]))/i;
const ipv6ParsingRegex = /[a-f0-9]{1,4}/gi;

function parseV4Address(input: any) {
  const matches = ipv4Regex.exec(input);

  if (matches == null) {
    return;
  }

  return {
    octets: _.map(_.rest(matches), (value) => parseInt(value, 10)),
  };
}

function parseV6Address(input: any) {
  // Check if its valid before we try to parse it.

  if (!ipv6ValidityRegex.test(input)) {
    return;
  }

  const split = input.split('::');
  const firstHalfMatches = split[0].match(ipv6ParsingRegex);

  const result = {
    parts: [],
  };

  if (firstHalfMatches != null) {
    // If we found more than 8 matches the address isn't valid

    if (firstHalfMatches.length > 8) {
      return;
    }

    // @ts-expect-error ts-migrate(2322) FIXME: Type 'number[]' is not assignable to type 'never[]... Remove this comment to see the full error message
    result.parts = _.map(firstHalfMatches, (value) => parseInt(value, 16));
  }

  if (split.length > 1) {
    const secondHalfMatches = split[1].match(ipv6ParsingRegex);

    const secondHalfParts = _.map(secondHalfMatches, (value) =>
      parseInt(value, 16)
    );

    const difference = 8 - result.parts.length - secondHalfParts.length;

    // If we found more than 8 parts or we have no parts this isn't valid

    if (difference < 0 || difference === 8) {
      return;
    }

    _.times(difference, () => {
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'number' is not assignable to par... Remove this comment to see the full error message
      result.parts.push(0);
    });

    // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
    result.parts = result.parts.concat(secondHalfParts);
  }

  return result;
}

export function isValidPort(input: any) {
  return portValidityRegex.test(input);
}

export function isValidAddress(input: any) {
  return isValidV4Address(input) || isValidV6Address(input);
}

export function isLoopbackAddress(input: any) {
  return (
    input === 'localhost' ||
    isLoopbackV4Address(input) ||
    isLoopbackV6Address(input)
  );
}

export function isPrivateAddress(input: any) {
  return isPrivateV4Address(input) || isPrivateV6Address(input);
}

export function isPlexDirectAddress(input: any) {
  return /plex\.direct$/.test(input);
}

export function isValidV4Address(input: any) {
  const parsed = parseV4Address(input);

  return !!(
    parsed && _.all(parsed.octets, (octet) => octet >= 0 && octet <= 255)
  );
}

export function isLoopbackV4Address(input: any) {
  const parsed = parseV4Address(input);

  return !!(
    parsed &&
    (parsed.octets[0] === 127 || _.all(parsed.octets, (octet) => octet === 0))
  );
}

export function isPrivateV4Address(input: any) {
  const parsed = parseV4Address(input);

  if (parsed) {
    if (parsed.octets[0] === 10) {
      return true;
    }

    if (
      parsed.octets[0] === 172 &&
      parsed.octets[1] >= 16 &&
      parsed.octets[1] <= 31
    ) {
      return true;
    }

    if (parsed.octets[0] === 192 && parsed.octets[1] === 168) {
      return true;
    }
  }

  return false;
}

export function isValidV6Address(input: any) {
  const parsed = parseV6Address(input);

  return !!(
    parsed && _.all(parsed.parts, (part) => part >= 0 && part <= 65535)
  );
}

export function isLoopbackV6Address(input: any) {
  const parsed = parseV6Address(input);

  if (parsed) {
    const leadingZeros = _.all(_.take(parsed.parts, 7), (part) => part === 0);

    return leadingZeros && _.last(parsed.parts) === 1;
  }

  return false;
}

export function isPrivateV6Address(input: any) {
  const parsed = parseV6Address(input);

  return !!(parsed && parsed.parts[0] >= 64512 && parsed.parts[0] <= 65023);
}

export function getV4Subnet(input: any) {
  return isValidV4Address(input) ? input.split('.').slice(0, -1).join('.') : '';
}
