import classNames from 'classnames';
import React, {
  CSSProperties,
  ReactElement,
  ReactNode,
  Ref,
  useCallback,
  useEffect,
  useRef,
} from 'react';
import mergeRefs from 'app/js/v4/core/ui/utils/mergeRefs';
import 'app/js/libs/scroll-behavior-polyfill-2.0.13';
import styles from './Scroller.css';

export enum ScrollDirection {
  None = 'none',
  Vertical = 'vertical',
  Horizontal = 'horizontal',
  Omni = 'omni',
}

export interface InnerScrollerProps {
  forwardedRef: Ref<HTMLDivElement>;
  className?: string;
  style?: CSSProperties;
  children: ReactNode;
  scrollDirection?: ScrollDirection;
  autoScroll?: boolean;
  smoothScroll?: boolean;
  targetScrollTop?: number;
  targetScrollLeft?: number;
  onScrollPositionChange?(position: {
    scrollTop: number;
    scrollLeft: number;
  }): void;
}

export type ScrollerProps = Omit<InnerScrollerProps, 'forwardedRef'>;

function Scroller({
  forwardedRef,
  className = styles.scroller,
  style,
  children,
  scrollDirection = ScrollDirection.Vertical,
  autoScroll = true,
  smoothScroll = false,
  targetScrollTop,
  targetScrollLeft,
  onScrollPositionChange,
}: InnerScrollerProps): ReactElement {
  const ref = useRef<HTMLDivElement>(null);

  //
  // Callbacks

  const onScroll = useCallback(
    (event) => {
      // Avoid handling the scroll event unless this component is what triggered
      // the scroll event. Avoid stop propagation because parents sill need to
      // know if a scroll event happened.

      if (
        onScrollPositionChange &&
        ref.current != null &&
        ref.current === event.target
      ) {
        onScrollPositionChange({
          scrollTop: ref.current.scrollTop,
          scrollLeft: ref.current.scrollLeft,
        });
      }
    },
    [ref, onScrollPositionChange]
  );

  //
  // Effects

  useEffect(() => {
    if (
      ref.current != null &&
      (targetScrollTop != null || targetScrollLeft != null)
    ) {
      ref.current.scrollTo({
        top: targetScrollTop,
        left: targetScrollLeft,
        behavior: smoothScroll ? 'smooth' : 'auto',
      });
    }
  }, [ref, targetScrollTop, targetScrollLeft, smoothScroll]);

  //
  // Render

  return (
    <div
      ref={mergeRefs<HTMLDivElement>(forwardedRef, ref)}
      className={classNames(
        className,
        styles[scrollDirection],
        autoScroll && styles.auto
      )}
      style={style}
      onScroll={onScroll}
    >
      {children}
    </div>
  );
}

export default React.forwardRef<HTMLDivElement, ScrollerProps>((props, ref) => {
  return <Scroller {...props} forwardedRef={ref} />;
});
