/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import CloudPlayerCollection from 'app/js/models/player/CloudPlayerCollection';
import { UNTESTED } from 'app/js/utils/constants/player/companion/cloudCompanionLinkStates';

const CloudCompanionModel = AbstractModel.extend({
  //
  // Properties
  name: 'CloudCompanionModel',
  context: ['server'],
  idAttribute: 'identifier',
  rootPropertyName: 'Companion',
  syncedAttrs: {
    identifier: attrTypes.string,
    title: attrTypes.string,
    baseURL: attrTypes.string,
    linkURL: attrTypes.string,
    provides: attrTypes.list,
  },
  sessionAttrs: {
    linkState: attrTypes.string,
  },
  sessionRelations: {
    cloudPlayers: {
      relationClass: CloudPlayerCollection,
      options() {
        return {
          url: `${(this as any).get('baseURL')}/resources`,
          baseURL: (this as any).get('baseURL'),
        };
      },
    },
  },
  defaults: {
    linkState: UNTESTED,
  },
});
export default CloudCompanionModel;
