/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import AbstractStorageModel from 'app/js/models/storage/AbstractStorageModel';
import assert from 'app/js/utils/log/assert';
import StoredUserCollection from './StoredUserCollection';

const UserStorageModel = AbstractStorageModel.extend({
  //
  // Properties

  name: 'UserStorageModel',
  storageKey: 'users',

  sessionAttrs: {
    activeUserID: attrTypes.integer,
  },

  syncedRelations: {
    users: {
      relationClass: StoredUserCollection,
    },
  },

  //
  // Init

  initialize() {
    this.debouncedSaveTimeout = null;
  },

  //
  // Control

  debouncedSave() {
    __DEV__ &&
      assert(
        !arguments.length,
        'Set attributes before using `debouncedSave` so the model is immediately updated. Otherwise, race conditions may occur.'
      );

    if (this.debouncedSaveTimeout) {
      window.clearTimeout(this.debouncedSaveTimeout);
    }

    this.debouncedSaveTimeout = window.setTimeout(() => this.save(), 1000);
  },

  flushDebouncedSave() {
    if (this.debouncedSaveTimeout) {
      window.clearTimeout(this.debouncedSaveTimeout);

      this.save();
    }
  },

  getActiveUser() {
    const storedUsers = this.get('users');
    const activeUserID = this.get('activeUserID');

    if (activeUserID != null) {
      return storedUsers.get(activeUserID);
    }
  },

  clearActiveUser() {
    const activeUser = this.getActiveUser();

    if (activeUser) {
      activeUser.clearSensitiveAttributes();
    }

    this.unset('activeUserID');

    return this.save();
  },

  getActiveServers() {
    const activeUser = this.getActiveUser();

    if (activeUser) {
      return activeUser.get('servers');
    }
  },
});

// Singleton
export default new UserStorageModel();
