/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import LinearFilterModel from './LinearFilterModel';

const LinearFilterCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'LinearFilterCollection',
  context: ['server', 'section'],

  model: LinearFilterModel,

  url() {
    return _.result(this.section, 'url');
  },

  //
  // Control

  filterBySecondary() {
    const secondaries = this.filter((linearFilter: any) =>
      linearFilter.get('secondary')
    );

    return this.clone(secondaries);
  },
});

export default LinearFilterCollection;
