/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import contentUtil from 'app/js/utils/contentUtil';
import path from 'app/js/utils/url/path';
import PlaylistItemListModel from './PlaylistItemListModel';

const PlaylistModel = AbstractModel.extend({
  //
  // Properties
  name: 'PlaylistModel',
  context: ['server', 'containerKey'],
  idAttribute: 'ratingKey',
  rootPropertyName: 'Playlist',
  urlRoot: '/playlists',
  saveUrl() {
    if (this.server.supports('truePlaylistsPath')) {
      return _.result(this, 'url');
    }
    return `/library/metadata/${this.id}`;
  },
  sessionAttrs: {
    isPlaylist: attrTypes.boolean,
    isLibraryItem: attrTypes.boolean,
  },
  syncedAttrs: {
    ratingKey: attrTypes.string,
    key: attrTypes.string,
    guid: attrTypes.string,
    type: attrTypes.string,
    playlistType: attrTypes.string,
    title: attrTypes.string,
    titleSort: attrTypes.string,
    composite: attrTypes.string,
    summary: attrTypes.string,
    smart: attrTypes.boolean,
    allowSync: attrTypes.boolean,
    duration: attrTypes.integer,
    durationInSeconds: attrTypes.integer,
    leafCount: attrTypes.integer,
    addedAt: attrTypes.integer,
    updatedAt: attrTypes.integer,
  },
  sessionRelations: {
    children: {
      relationClass: PlaylistItemListModel,
      attrs(data: any) {
        const params = {};
        // We need to request related items for tracks so we can discover
        // Gracenote mixes.
        if (data.playlistType === 'audio') {
          (params as any).includeRelated = 1;
        }
        return {
          containerKey: path.join(
            ['/playlists', data.ratingKey, 'items'],
            params
          ),
        };
      },
      options() {
        return {
          playlist: this,
        };
      },
    },
  },
  defaults: {
    isPlaylist: true,
    isLibraryItem: true,
  },
  //
  // Control
  findItem(id: any) {
    return this.get('children')
      .getCollection()
      .findWhere({ playlistItemID: id });
  },
  moveItem(itemID: any, afterItemID: any) {
    if (itemID === undefined) {
      return;
    }
    const params = afterItemID ? { after: afterItemID } : {};
    const url = path.join(
      [_.result(this, 'url'), '/items/', itemID, '/move'],
      params
    );
    const xhrOptions = _.extend(this.server.getXHROptions(url), {
      type: 'PUT',
    });
    const xhrDeferred = $.ajax(xhrOptions);
    const items = this.get('children').getCollection();
    const item = this.findItem(itemID);
    items.remove(item);
    const afterItem = this.findItem(afterItemID);
    const spliceAt = afterItem ? items.indexOf(afterItem) + 1 : 0;
    items.add(item, { at: spliceAt });
    return xhrDeferred;
  },
  removeItem(itemID: any) {
    if (itemID === undefined) {
      return;
    }
    const url = path.join([_.result(this, 'url'), '/items/', itemID]);
    const xhrOptions = _.extend(this.server.getXHROptions(url), {
      type: 'DELETE',
    });
    const xhrDeferred = $.ajax(xhrOptions);
    xhrDeferred.then((response) => {
      this.set(this.parse(response));
    });
    const items = this.get('children').getCollection();
    const item = this.findItem(itemID);
    items.remove(item);
    // We trigger a `destroy` event here so that the paginated collection
    // updates the `totalSize` of the collection. We call `destroy` on
    // the collection rather than the item because a consequence of the
    // latter is the item being removed from every collection that contains
    // it, something that we don't want in this particular instance.
    // See https://github.com/plexinc/plex-web-client/issues/6456
    items.trigger('destroy', item, items);
    return xhrDeferred;
  },
  addContent(item: any, options: any) {
    const method = this.isNew() ? 'POST' : 'PUT';
    const segments = [_.result(this, 'url')];
    const params = {};
    if (this.isNew()) {
      (params as any).type = this.get('playlistType');
      (params as any).title = this.get('title');
      (params as any).smart = this.get('smart') ? 1 : 0;
    } else {
      segments.push('/items');
    }
    if (item.get('playQueueID')) {
      (params as any).playQueueID = item.get('playQueueID');
    } else {
      (params as any).uri = contentUtil.uri(item, options);
    }
    const url = path.join(segments, params);
    const xhrOptions = _.extend(item.server.getXHROptions(url), {
      type: method,
    });
    const xhrDeferred = $.ajax(xhrOptions);
    xhrDeferred.then((response) => {
      // Both the metadata of the playlist (returned in response) and the
      // collection of items will have changed on the server
      this.set(this.parse(response));
    });
    return xhrDeferred.promise();
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'identifier' is declared but its value is never re... Remove this comment to see the full error message
  isLibraryItem(identifier: any, key: any) {
    return true;
  },
  isPlexMusicItem() {
    return false;
  },
});
export default PlaylistModel;
