/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import dispatcher from 'app/js/utils/dispatcher';

const cloudServerCompanionProxy = {
  subscribe() {
    dispatcher.on(
      'cloudServer:processRemoteControlCommand',
      this.onCloudServerCommand,
      this
    );
  },

  unsubscribe() {
    dispatcher.off(
      'cloudServer:processRemoteControlCommand',
      this.onCloudServerCommand,
      this
    );
  },

  onCloudServerCommand(message: any) {
    const command = message.Command;

    const signal = {
      supportsTimeline: false,
      path: command.path,
      commandID: command.commandID,
      query: {
        key: [command.queryKey],
        offset: [command.queryOffset],
        mediaIndex: [command.queryMediaIndex],
        containerKey: [command.queryContainerKey],
        playQueueID: [command.queryPlayqueueid],
        type: [command.queryType],
        volume: [command.queryVolume],
        repeat: [command.queryRepeat],
        autoPlay: [command.queryAutoPlay],
        subtitleSize: [command.querySubtitleSize],
        audioStreamID: [command.queryAudioStreamID],
        subtitleStreamID: [command.querySubtitleStreamID],

        token: [command.queryToken],
        machineIdentifier: [command.queryMachineIdentifier],

        protocol: [command.queryProtocol],
        address: [command.queryAddress],
        port: [command.queryPort],

        text: [command.queryText],
        field: [command.queryField],
      },
    };

    resolve('localCompanionCommandHandler')(signal);
  },
};

export default cloudServerCompanionProxy;
