import React from 'react';
import { FORUMS_URL } from 'app/js/utils/constants/commonUrls';
import t from 'app/js/utils/t';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';

type UnexpectedErrorPageProps = {
  children?: React.ReactNode;
};

function UnexpectedErrorPage(props: UnexpectedErrorPageProps) {
  const { children, ...otherProps } = props;

  return (
    <ErrorPage
      description={t(
        'Please visit our forums if you continue to experience problems'
      )}
      title={t('There was an unexpected error')}
      {...otherProps}
    >
      <Link
        kind={kinds.PRIMARY}
        target={__EXTERNAL_LINK_TARGET__}
        to={FORUMS_URL}
      >
        {t('Go to Forums')}
      </Link>
      {children}
    </ErrorPage>
  );
}

export default UnexpectedErrorPage;
