import AbstractCollection from 'app/js/models/AbstractCollection';
import * as responseTypes from 'app/js/models/responseTypes';
import { getMyPlexUrl } from 'app/js/utils/getRuntimeUrl';
import UserFeatureModel from './UserFeatureModel';

const UserFeatureCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'UserFeatureCollection',

  context: ['server'],

  model: UserFeatureModel,

  // User features may be fetched before the cloud server connection is
  // available for signed-out users.
  url: getMyPlexUrl('/api/v2/features'),

  responseType: responseTypes.json,
});

export default UserFeatureCollection;
