import _ from 'underscore';
import { InterpolationValue } from 'app/js/utils/TranslationTypes';
import escapeRegExp from 'app/js/utils/string/escapeRegExp';

const tokenMemo: { [key: string]: RegExp } = {};

function formatInterpolationValue(value: InterpolationValue): string {
  return value == null ? '' : String(value);
}

function interpolateTokens(
  translation: string,
  tokens: { [key: string]: any }
): string {
  return translation.replace(
    /\{([a-z0-9]+?)\}/gi,
    (match: string, tokenMatch: string) =>
      tokens.hasOwnProperty(tokenMatch)
        ? formatInterpolationValue(tokens[tokenMatch])
        : match
  );
}

function interpolateIteration(
  translation: string,
  ...args: InterpolationValue[]
): string {
  // Iteratively interpolate variables, as placeholders may
  // appear in arguments other than the translation key.
  let regexp;
  let i = 1;
  const len = args.length;

  while (i <= len) {
    regexp = tokenMemo[i];
    if (!regexp) {
      regexp = new RegExp(escapeRegExp(`{${i}}`), 'g');
      tokenMemo[i] = regexp;
    }
    translation = translation.replace(
      regexp,
      formatInterpolationValue(args[i - 1])
    );
    i++;
  }

  return translation;
}

function interpolate(
  translation: string,
  ...args: InterpolationValue[]
): string {
  if (_.isObject(args[0])) {
    return interpolateTokens(translation, args[0]);
  } else if (_.isObject(args[args.length - 1])) {
    args.pop();
    return interpolateIteration(translation, ...args);
  } else {
    return interpolateIteration(translation, ...args);
  }
}

function translate(isEscaped: boolean, translation: string): string {
  if (!translation) {
    return '';
  }

  return isEscaped ? _.escape(translation) : translation;
}

type GetTranslation = (key: string) => string | undefined;

function createTranslationInterpolator(getTranslation: GetTranslation) {
  return function t(key: string, ...args: any[]): string {
    const translation = translate(
      false,
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      getTranslation.call(this, key) || key
    );

    return interpolate(translation, ...args);
  };
}

export function createEscapedTranslationInterpolator(
  getTranslation: GetTranslation
) {
  return function t(key: string, ...args: any[]): string {
    const translation = translate(
      true,
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      getTranslation.call(this, key) || key
    );

    return interpolate(translation, ...args);
  };
}

export default createTranslationInterpolator;
