/* eslint-plugin-disable @typescript-eslint */

import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import EmptyPageContent from 'app/js/v4/core/ui/pages/components/empty/EmptyPageContent';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import routes from 'app/js/v4/routes/routes';
import DownForMaintenancePage from 'app/js/views/v3/components/pages/empty/DownForMaintenancePage';
import UnreachablePage from 'app/js/views/v3/components/pages/empty/UnreachablePage';
import useEffectOnce from 'app/js/views/v3/helpers/hooks/useEffectOnce';

type UserPageProps = {
  children?: React.ReactNode;
  isWaitingForServers: boolean;
  isAnyServerAuthenticated: boolean;
  isWaitingForCloudServer: boolean;
  isAccountRequired: boolean;
  isCloudServerUnauthorized?: boolean;
  isCloudServerConnected: boolean;
  isCloudServerUnavailable: boolean;
  isCloudServerDownForMaintenance: boolean;
  hasAttemptedSignIn: boolean;
  isUserFeaturesPopulated: boolean;
  isUserFeaturesError: boolean;
  onPopulateUserFeatures: (...args: any[]) => any;
};

// When refactoring this UserPage keep in mind the following conditions:
//
// 1. Not signed in. Has local server.
// 2. Not signed in. Has local server. Has Downloads.
// 3. Not signed in. No local server.
// 4. Not signed in. No local server. Block /anonymous request.
// 5. Not signed in. No local server. Has Downloads.
// 6. Signed in. No Home.
// 7. Signed in. Has Home. Automatically signed in.
// 8. Signed in. Has Home. Not automatically signed in.
// 9. Block plex.tv. No local server.

function UserPage(props: UserPageProps) {
  const {
    children,
    isWaitingForServers,
    isAnyServerAuthenticated,
    isWaitingForCloudServer,
    isCloudServerUnauthorized,
    isCloudServerConnected,
    isCloudServerUnavailable,
    isCloudServerDownForMaintenance,
    hasAttemptedSignIn,
    isUserFeaturesPopulated,
    isUserFeaturesError,
    onPopulateUserFeatures,
  } = props;

  const history = useHistory();

  // If a request for features has already completed outside of this component,
  // retry the request, but don't block page load on it anymore.
  const [hasWaitedForUserFeatures, setHasWaitedForUserFeatures] = useState(
    isUserFeaturesError || isUserFeaturesPopulated
  );

  const isUserReady = hasAttemptedSignIn || isCloudServerUnavailable;

  const isReadyToRequestFeatures = isUserReady && isCloudServerConnected;

  const isUserFeaturesReady =
    isUserFeaturesPopulated ||
    isUserFeaturesError ||
    hasWaitedForUserFeatures ||
    isCloudServerUnavailable;

  const shouldRedirectToLogin =
    (!isWaitingForServers &&
      !isAnyServerAuthenticated &&
      !isCloudServerDownForMaintenance &&
      !isCloudServerUnavailable &&
      isUserReady) ||
    isCloudServerUnauthorized;

  // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
  useEffect(() => {
    if (isReadyToRequestFeatures) {
      // Waiting for user features blocks page load, so use a shorter timeout in
      // case the network is bad and we're unable to fetch user features, but we
      // are able to access a server.
      const timerID = window.setTimeout(
        () => setHasWaitedForUserFeatures(true),
        5000
      );

      return () => window.clearTimeout(timerID);
    }
  }, [isReadyToRequestFeatures]);

  useEffect(() => {
    if (isReadyToRequestFeatures) {
      onPopulateUserFeatures();
    }
  }, [isReadyToRequestFeatures, onPopulateUserFeatures]);

  useEffectOnce(() => {
    history.push(routes.login);
  }, shouldRedirectToLogin);

  if (
    isAnyServerAuthenticated &&
    isUserFeaturesReady &&
    isUserReady &&
    isCloudServerConnected
  ) {
    return children;
  }

  if (
    !isWaitingForCloudServer &&
    !isWaitingForServers &&
    !isAnyServerAuthenticated
  ) {
    if (isCloudServerDownForMaintenance) {
      return <DownForMaintenancePage />;
    }

    if (isCloudServerUnavailable) {
      return <UnreachablePage />;
    }
  }

  return (
    <EmptyPageContent>
      <Spinner />
    </EmptyPageContent>
  );
}

UserPage.defaultProps = {
  isAccountRequired: false,
};

export default UserPage;
