import { SagaIterator } from 'redux-saga';
import { all, call, fork, put, takeLatest } from 'redux-saga/effects';
import { fetchUser } from 'app/js/stores/actions/userActions';
import base64ImageStringToBlob from 'app/js/utils/images/base64ImageStringToBlob';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import userSlice from 'app/js/v4/core/user/userSlice';
import {
  FetchUserDetailsRequestAction,
  UpdateUserDetailsRequestAction,
  User,
} from 'app/js/v4/core/user/userTypes';
import { unescapeItem } from 'app/js/v4/core/utils/unescapeResponse';
import api, { APIResponse } from 'app/js/v4/utils/data/api';
import putLegacyActionDangerously from 'app/js/v4/utils/data/saga/putLegacyActionDangerously';

export function* fetchUserDetails({
  payload,
}: FetchUserDetailsRequestAction): SagaIterator {
  const { server } = payload;

  try {
    const data = yield call(fetchLatestUserDetails, server);

    yield put(
      userSlice.actions.fetchUserDetailsSucceeded({
        details: {
          thumb: data.thumb || '',
          friendlyName: data.friendlyName || '',
          email: data.email,
          username: data.username,
          anonymous: data.anonymous,
          isFullUser: !!data.username || !!data.anonymous,
        },
      })
    );
  } catch (error) {
    yield put(
      userSlice.actions.fetchUserDetailsFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* updateUserDetails({
  payload,
}: UpdateUserDetailsRequestAction): SagaIterator {
  const { server, update } = payload;

  const bodyFormData = new FormData();
  if (update.avatar) {
    // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Blob | null' is not assignable t... Remove this comment to see the full error message
    bodyFormData.append('avatar', base64ImageStringToBlob(update.avatar));
  }

  const params = Object.entries(update).filter(([key]) => key !== 'avatar');

  try {
    yield call(
      api.put,
      `/api/v2/user?${new URLSearchParams(params)}`,
      {
        server,
        headers: {
          accept: 'application/json',
        },
      },
      bodyFormData
    );

    const data = yield call(fetchLatestUserDetails, server);

    yield all([
      putLegacyActionDangerously(fetchUser()),
      put(
        userSlice.actions.updateUserDetailsSucceeded({
          details: {
            friendlyName: data.friendlyName || '',
            thumb: data.thumb || '',
          },
        })
      ),
    ]);
  } catch (error) {
    yield put(
      userSlice.actions.updateUserDetailsFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Helper Workers

function* fetchLatestUserDetails(server: Server): SagaIterator<User> {
  const { data }: APIResponse<User> = yield call(api.get, '/api/v2/user', {
    server,
    headers: {
      accept: 'application/json',
    },
  });

  return unescapeItem<User>(data);
}

//
// Watchers

function* watchFetchUserDetails(): SagaIterator {
  yield takeLatest(userSlice.actions.fetchUserDetails, fetchUserDetails);
}

function* watchUpdateUserDetails(): SagaIterator {
  yield takeLatest(userSlice.actions.updateUserDetails, updateUserDetails);
}

export default function* userSaga(): SagaIterator {
  yield all([fork(watchUpdateUserDetails), fork(watchFetchUserDetails)]);
}
