/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import React from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { Router } from 'react-router-dom';
import { resolve } from 'app/js/adapters/adapters';
import alertInitializer from 'app/js/initializers/alertInitializer';
import modalInitializer from 'app/js/initializers/modalInitializer';
import 'app/js/libs/bootstrap/button';
import 'app/js/libs/bootstrap/modal';
import 'app/js/libs/bootstrap/tooltip';
import 'app/js/libs/bootstrap/transition';
import config from 'app/js/models/config';
import App from 'app/js/v4/App';
import history from 'app/js/v4/history';
import ajaxInitializer from './ajaxInitializer';
import alertsInitializer from './alertsInitializer';
import appModelInitializer from './appModelInitializer';
import cloudServerEventInitializer from './cloudServerEventInitializer';
import commandsInitializer from './commandsInitializer';
import companionInitializer from './companionInitializer';
import desktopSettingsInitializer from './desktopSettingsInitializer';
import desktopStorageInitializer from './desktopStorageInitializer';
import errorTrackerInitializer from './errorTrackerInitializer';
import federatedAuthPINInitializer from './federatedAuthPINInitializer';
import languageInitializer from './languageInitializer';
import mediaElementInitializer from './mediaElementInitializer';
import mediaServerEventInitializer from './mediaServerEventInitializer';
import metricsInitializer from './metricsInitializer';
import reportersInitializer from './reportersInitializer';
import serverInitializer from './serverInitializer';
import sourceInitializer from './sourceInitializer';
import userInitializer from './userInitializer';

function renderToDOM() {
  ReactDOM.render(
    <Provider store={resolve('appStore')}>
      <Router history={history}>
        <DndProvider backend={HTML5Backend}>
          <App />
        </DndProvider>
      </Router>
    </Provider>,
    document.getElementById('plex')
  );

  return $.Deferred().resolve().promise();
}

export default function appInitializer() {
  // Start tracking errors as early as possible, in case something throws during
  // initialization and prevents the app from loading.
  let promise = errorTrackerInitializer();

  // Instantiate reporters for logs.
  promise = promise.then(reportersInitializer);

  // Register all commands the app will be using.
  promise = promise.then(commandsInitializer);

  // Initialize storage next to make sure the following operations are reading
  // from the correct storage and run all migrations before settings are
  // fetched.
  promise = promise.then(desktopStorageInitializer);

  // Check the query string for alerts to display, push them into config,
  // clean up the history.
  promise = promise.then(alertsInitializer);

  // Initialize the app model after storage is initialized so we can grab a
  // client identifier from storage or generate one if it does not already
  // exist.
  promise = promise.then(appModelInitializer);

  // Start tracking ajax requests.
  promise = promise.then(ajaxInitializer);

  // Fetch all settings from storage.
  promise = promise.then(desktopSettingsInitializer);

  // Set the active user from user storage from stored user after settings are
  // fetched. This should happen before servers are initialized so the server
  // initializer knows where to look for stored servers.
  promise = promise.then(userInitializer);

  // Can now set the language since the settings model containing language would
  // have been initialized
  promise = promise.then(languageInitializer);

  if (config.allowCloudPubSub) {
    // Start listening to open connections to receive server events before
    // servers are initialized.
    promise = promise.then(() => cloudServerEventInitializer());
  }

  // Start listening to open connections to receive server events before
  // servers are initialized.
  promise = promise.then(mediaServerEventInitializer);

  // Start listening for changes to sources before server and providers are
  // initialized
  promise = promise.then(sourceInitializer);

  // Create servers that were stored from a previous session and create
  // configured servers.
  promise = promise.then(serverInitializer);

  // Create the singleton audio media element and be ready to start silent
  // playback on interaction so auto-play works correctly in browsers that block
  // auto-play.
  promise = promise.then(mediaElementInitializer);

  promise = promise.then(companionInitializer);

  // Read PIN from query string
  promise = promise.then(federatedAuthPINInitializer);

  // Initializing metrics starts tracking immediately. Start tracking after we
  // have a client identifier to send along with events as well as the user's
  // tracking preference. Tracking is started last as a means to capture startup
  // time excluding advanced rendering and routing.
  promise = promise.then(metricsInitializer);

  promise = promise.then(modalInitializer);

  promise = promise.then(alertInitializer);

  promise = promise.then(renderToDOM);

  return promise;
}
