import $ from 'jquery';
import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/edits... Remove this comment to see the full error message
import tpl from 'app/templates/editsection/EditSectionAgentSettingsView.tpl';

const EditSectionAgentSettingsView = Marionette.ItemView.extend({
  //
  // Properties

  template: tpl,

  events: {
    'change input[name]': 'onInputChange',
    'change select[name]': 'onInputChange',
  },

  //
  // Init

  initialize: function () {
    this.changes = {};
  },

  //
  // Control

  serializeData: function () {
    return {
      settings: this.collection.toJSON(),
      escapedAgentName: _.escape(
        this.model.agents.get(this.model.get('agent')).get('name')
      ),
    };
  },

  //
  // Listeners

  onInputChange: function (event: any) {
    const $target = $(event.currentTarget);

    const value = $target.is(':checkbox')
      ? $target.prop('checked')
      : $target.val();

    const setting = this.collection.findWhere({ id: $target.attr('name') });
    setting.set('value', value);
  },
});

export default EditSectionAgentSettingsView;
