import $ from 'jquery';

function maxDuration<ResolveValue>(
  promise: JQueryPromise<ResolveValue>,
  duration: number
): JQueryPromise<ResolveValue> {
  const deferred = $.Deferred<ResolveValue>();

  const timeout = window.setTimeout(() => {
    deferred.reject();
  }, duration);

  promise.then(deferred.resolve, deferred.reject).always(() => {
    window.clearTimeout(timeout);
  });

  return deferred.promise();
}

export default maxDuration;
