import React, { ReactElement, ReactNode } from 'react';
import Scroller, {
  ScrollerProps,
} from 'app/js/v4/core/ui/scrollers/components/Scroller';
import styles from './EmptyPageContent.css';

export interface EmptyPageContentProps extends Omit<ScrollerProps, 'children'> {
  children?: ReactNode;
  className?: string;
  contentClassName?: string;
  icon?: ReactElement;
  iconSrc?: string;
  titleClassName?: string;
  title?: string;
  descriptionClassName?: string;
  description?: ReactNode;
  footerClassName?: string;
  footer?: string;
}

export default function EmptyPageContent({
  children,
  className = styles.emptyPageContentScroller,
  contentClassName = styles.content,
  icon,
  titleClassName = styles.title,
  iconSrc,
  title,
  descriptionClassName = styles.description,
  description,
  footerClassName = styles.footer,
  footer,
  ...scrollerProps
}: EmptyPageContentProps): ReactElement {
  return (
    <Scroller className={className} {...scrollerProps}>
      <div className={contentClassName}>
        {icon ? <span className={styles.icon}>{icon}</span> : null}

        {iconSrc ? <img className={styles.imgIcon} src={iconSrc} /> : null}

        {title ? <div className={titleClassName}>{title}</div> : null}

        {description ? (
          <div className={descriptionClassName}>{description}</div>
        ) : null}

        {children}
      </div>

      {footer ? <div className={footerClassName}>{footer}</div> : null}
    </Scroller>
  );
}
