/* eslint-plugin-disable @typescript-eslint */
import ProviderModel from 'app/js/models/provider/ProviderModel';
import * as pivotIdentifiers from 'app/js/utils/constants/pivotIdentifiers';
import * as pivotTypes from 'app/js/utils/constants/pivotTypes';
import { HOME_CONTENT_DIRECTORY_ID } from 'app/js/utils/constants/providerContentDirectoryIDs';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import t from 'app/js/utils/t';
import { views } from 'app/js/views/v3/helpers/routes/getViewRoute';

export default function augmentWithSyntheticHomeProvider(
  collection: any,
  server: any
) {
  if (!collection.get(providerIdentifiers.HOME_SYNTHETIC)) {
    collection.add(
      new ProviderModel(
        {
          isPending: false,
          isPopulated: true,
          identifier: providerIdentifiers.HOME_SYNTHETIC,
          title: 'Home Provider',
          features: [
            {
              type: providerFeatureTypes.CONTENT,
              directories: [
                {
                  id: HOME_CONTENT_DIRECTORY_ID,
                  title: t('Home'),
                  pivots: [
                    {
                      id: pivotIdentifiers.HOME_RECOMMENDED,
                      key: views.HOME_RECOMMENDED,
                      type: pivotTypes.VIEW,
                      title: t('Recommended'),
                    },
                    {
                      id: pivotIdentifiers.HOME_WATCHLIST,
                      context: 'content.watchlist',
                      key: views.HOME_WATCHLIST,
                      type: pivotTypes.VIEW,
                      title: t('Watchlist'),
                    },
                  ],
                },
              ],
            },
          ],
        },
        { server }
      )
    );
  }
}
