/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';

function rateCommand(options: any) {
  const item = options.item;
  const rating = options.rating;

  // Track the original rating so it can be restored should the request fail
  const originalRating = item.get('userRating');

  // Set the rating before we get a success so that our ui can update
  // immediately after the user tries to rate an item
  if (rating === -1) {
    item.unset('userRating');
  } else {
    item.set('userRating', rating);
  }

  if (options.passive) {
    return $.Deferred().resolve().promise();
  }

  const params = {
    key: item.get('ratingKey'),
    identifier: item.get('identifier'),
    rating: rating,
  };

  const url = path.join('/:/rate', params);
  const deferred = $.ajax(item.server.getXHROptions(url));

  deferred.fail(function () {
    // Revert the local rating change
    item.set('userRating', originalRating);

    dispatcher.trigger('show:alert', {
      type: 'error',
      message: t('There was a problem rating this item.'),
    });
  });

  return deferred.promise();
}

export default rateCommand;
