import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useLocation } from 'react-router-dom';
import { garbageCollectEntities } from 'app/js/stores/actions/appActions';

export default function useGarbageCollectEntities(): void {
  const dispatch = useDispatch();
  const location = useLocation();

  useEffect(() => {
    dispatch(garbageCollectEntities());
  }, [location, dispatch]);
}
