/* eslint-plugin-disable @typescript-eslint */

import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import t from 'app/js/utils/t';
import routes from 'app/js/v4/routes/routes';
import NavBarButton from 'app/js/views/v3/components/navbar/NavBarButton';

function NavBarSignUpButton() {
  const history = useHistory();

  const onSignUpPress = useCallback(() => {
    history.push(routes.signUp);
  }, [history]);

  return (
    <NavBarButton data-testid="signUpButton" onPress={onSignUpPress}>
      {t('Sign Up')}
    </NavBarButton>
  );
}

export default NavBarSignUpButton;
