/* eslint-plugin-disable @typescript-eslint */

import React, { useEffect, useRef, useState } from 'react';
import BackgroundImage from './BackgroundImage';

// From: http://css-tricks.com/snippets/html/base64-encode-of-1x1px-transparent-gif/
export const EMPTY_IMAGE =
  'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7';

function cleanup(img: any) {
  if (img) {
    img.onload = img.onabort = img.onerror = undefined;
  }
  return undefined;
}

interface PreloadImageProps {
  src?: string;
  emptySrc?: string;
  component: React.ReactElement;
  preloadOnMount?: boolean;
  onLoad?: (...args: any[]) => any;
  onError?: (...args: any[]) => any;
}

function PreloadImage(props: PreloadImageProps) {
  const {
    src,
    emptySrc,
    component: ImageComponent,
    preloadOnMount,
    onLoad,
    onError,
    ...otherProps
  } = props;

  const [preloadedSrc, setPreloadedSrc] = useState(preloadOnMount ? null : src);
  const [error, setError] = useState(null);
  const hasSatisfiedMountCondition = useRef(preloadOnMount);

  useEffect(() => {
    if (!hasSatisfiedMountCondition.current) {
      // Ignore the initial src effects when using preloadOnMount={false}. The
      // first `src` value should not be preloaded while the later ones should.
      hasSatisfiedMountCondition.current = true;
      return;
    }

    if (!src) {
      return;
    }

    let img = new window.Image();

    img.onload = () => {
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'undefined' is not assignable to type 'HTMLIm... Remove this comment to see the full error message
      img = cleanup(img);
      setPreloadedSrc(src);
      setError(null);
    };

    img.onabort = img.onerror = () => {
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'undefined' is not assignable to type 'HTMLIm... Remove this comment to see the full error message
      img = cleanup(img);
      setPreloadedSrc(null);
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Error' is not assignable to para... Remove this comment to see the full error message
      setError(new Error(`Unable to load ${src}`));
    };

    img.src = src;

    return () => {
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'undefined' is not assignable to type 'HTMLIm... Remove this comment to see the full error message
      img = cleanup(img);
    };
  }, [src]);

  useEffect(() => {
    if (!src && preloadedSrc) {
      setPreloadedSrc(null);
    }
  }, [preloadedSrc, src]);

  useEffect(() => {
    if (preloadedSrc && onLoad) {
      onLoad(preloadedSrc);
    }
  }, [preloadedSrc, onLoad]);

  useEffect(() => {
    if (error && onError) {
      onError(error);
    }
  }, [error, onError]);

  // @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'ImageComponent' does not have an... Remove this comment to see the full error message
  return <ImageComponent {...otherProps} src={preloadedSrc || emptySrc} />;
}

PreloadImage.defaultProps = {
  emptySrc: EMPTY_IMAGE,
  component: BackgroundImage,
  preloadOnMount: true,
};

export default PreloadImage;
