/* eslint-plugin-disable @typescript-eslint */

import synthesizeDirectoryID from 'app/js/utils/synthesizeDirectoryID';
import createKey from 'app/js/views/v3/helpers/createKey';

function breakSourceKey(sourceKey: any) {
  if (sourceKey) {
    return sourceKey.split('--');
  }
}

function migrateSourceKey(sourceKey: any) {
  const keyParts = breakSourceKey(sourceKey);

  // `sourceKey` doesn't look like we expect. It's likely a downloads key or
  // something else unknown. Bail on work if we don't have enough parts.

  if (keyParts.length < 3) {
    return sourceKey;
  }

  const [machineIdentifier, providerIdentifier, directoryKey, pivotKey] =
    keyParts;

  const directoryID = synthesizeDirectoryID(directoryKey);

  return createKey(
    machineIdentifier,
    providerIdentifier,
    directoryID,
    pivotKey
  );
}

function migrateStoredKeys(storedKeys: any) {
  Object.keys(storedKeys).forEach((key) => {
    const newKey = migrateSourceKey(key);
    if (newKey !== key) {
      const currentValue = storedKeys[key];
      storedKeys[newKey] = currentValue;
      delete storedKeys[key];
    }
  });
}

export default function migrateDirectoryKeyToID(userExperienceSettings: any) {
  const {
    lastSourcePivots,
    savedPivotParams,
    // @ts-expect-error ts-migrate(2525) FIXME: Initializer provides no value for this binding ele... Remove this comment to see the full error message
    sidebarSettings: { pinnedSources } = {},
  } = userExperienceSettings;

  if (lastSourcePivots) {
    migrateStoredKeys(lastSourcePivots);
  }

  if (savedPivotParams) {
    migrateStoredKeys(savedPivotParams);
  }

  if (pinnedSources) {
    pinnedSources.forEach((pinnedSource: any) => {
      const keyParts = breakSourceKey(pinnedSource.key);
      if (!keyParts) {
        return;
      }

      const directoryKey = keyParts.pop();
      const directoryID = synthesizeDirectoryID(directoryKey);

      if (directoryID !== directoryKey) {
        const newKey = createKey(...keyParts, directoryID);

        if (!pinnedSource.directoryID) {
          pinnedSource.directoryID = directoryID;
        }

        pinnedSource.key = newKey;
      }
    });
  }

  return userExperienceSettings;
}
