/* eslint-plugin-disable @typescript-eslint */

import config from 'app/js/models/config';

let reportingHost: any;

export default function getPerformanceReportingHost() {
  if (reportingHost === undefined) {
    const matches = window.location.search.match(
      new RegExp('performanceReportingHost=([^&=]+)')
    );

    reportingHost =
      (matches && matches[1]
        ? decodeURIComponent(matches[1])
        : config.performanceReportingHost) || null;
  }

  return reportingHost;
}
