import { combineReducers } from '@reduxjs/toolkit';
import dvrSetupSlice from './dvrSetupSlice';
import epgSettingsSlice from './epgSettingsSlice';

const dvrManagementReducer = combineReducers({
  dvrSetup: dvrSetupSlice.reducer,
  epgSettings: epgSettingsSlice.reducer,
});

export default dvrManagementReducer;
