import $ from 'jquery';
import stateMachineMixin from 'app/js/mixins/views/stateMachineMixin';
import BackgroundProcessingListCollection from 'app/js/models/library/convert/BackgroundProcessingListCollection';
import VersionClientProfileCollection from 'app/js/models/library/convert/VersionClientProfileCollection';
import VersionTargetCollection from 'app/js/models/library/convert/VersionTargetCollection';
import BaseModal from 'app/js/views/modals/BaseModal';
import ConvertErrorView from 'app/js/views/modals/convert/ConvertErrorView';
import ConvertFormView from 'app/js/views/modals/convert/ConvertFormView';
import ConvertLoadingView from 'app/js/views/modals/convert/ConvertLoadingView';
import ConvertSuccessView from 'app/js/views/modals/convert/ConvertSuccessView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertModal.tpl';

const ConvertModal = BaseModal.extend({
  mixins: [stateMachineMixin],
  //
  // Properties
  name: 'convertModal',
  className: 'convert-modal modal fade',
  template: tpl,
  regions: {
    bodyRegion: '.modal-body-region',
  },
  //
  // Init
  initialize: function (options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);
    this.server = this.model ? this.model.server : options.item.server;
    const collectionOptions = {
      server: this.server,
      allowInCache: true,
    };
    this.playlists = new BackgroundProcessingListCollection(
      [],
      collectionOptions
    );
    this.targets = new VersionTargetCollection([], collectionOptions);
    this.profiles = new VersionClientProfileCollection([], collectionOptions);
  },
  //
  // States
  initialState: 'loading',
  states: {
    loading: {
      nextState: 'form',
      onEnter: function () {
        (this as any).bodyRegion.show(new ConvertLoadingView());
        (this as any).listenForStateRequests();
        // Populate a metadata model to ensure we have attributes that allow us
        // to find the section locations for the item.
        const item = (this as any).options.item;
        const shouldPopulateItem = item && !item.get('isMetadataList');
        const promise = $.when(
          shouldPopulateItem ? item.populate() : null,
          (this as any).playlists.populate(),
          (this as any).targets.populate(),
          (this as any).profiles.populate(),
          // Sections are needed when editing a generator and we need to
          // list locations from a section.
          (this as any).server.get('sections').populate()
        );
        promise.done(
          (this as any).whenRendered(function () {
            // Check that we have a background processing list to add the item
            // to. If we don't, show the error.
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.playlist = this.playlists.first();
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            if (this.playlist) {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.enterNextState();
            } else {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.enterState('error');
            }
          })
        );
        promise.fail(
          (this as any).whenRendered(function () {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.enterState('error');
          })
        );
      },
    },
    form: {
      nextState: 'success',
      onEnter: function () {
        (this as any).bodyRegion.show(
          new ConvertFormView({
            model: (this as any).model,
            item: (this as any).options.item,
            items: (this as any).options.items,
            playlist: (this as any).playlist,
            targets: (this as any).targets,
            profiles: (this as any).profiles,
          })
        );
        (this as any).listenForStateRequests();
      },
    },
    success: {
      onEnter: function () {
        if ((this as any).model) {
          (this as any).hide();
        } else {
          (this as any).bodyRegion.show(
            new ConvertSuccessView({ server: (this as any).server })
          );
          (this as any).listenForStateRequests();
        }
      },
    },
    error: {
      onEnter: function (status: any) {
        (this as any).bodyRegion.show(new ConvertErrorView({ status: status }));
        (this as any).listenForStateRequests();
      },
    },
  },
  //
  // Control
  listenForStateRequests: function () {
    if (this.bodyRegion?.currentView) {
      this.listenTo(this.bodyRegion.currentView, {
        'request:state': this.enterState,
        'request:nextState': this.enterNextState,
      });
    }
  },
  //
  // Listeners
  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);
    this.enterNextState();
  },
  onStateMachineComplete: function () {
    this.hide();
  },
});
export default ConvertModal;
