/* eslint-plugin-disable @typescript-eslint */

import React, { useCallback } from 'react';
import { IconUpCircledFilled } from '@plex/react-chroma-icon';
import activityTypeWhitelist from 'app/js/utils/activityTypeWhitelist';
import * as grabOperationStatuses from 'app/js/utils/constants/mediaGrabOperationStatuses';
import t from 'app/js/utils/t';
import { navigateToServerSettings } from 'app/js/v4/routes/navigateToRoute';
import MenuItem from 'app/js/views/v3/components/core/menus/MenuItem';
import ActivityMenuStatusItem from './ActivityMenuStatusItem';
import styles from './NavBarActivityMenuStatusItem.css';

interface StatusButtonProps {
  indicator?: React.ReactNode;
  label: string;
  labelClassName?: string;
  onPress?: (...args: any[]) => any;
  to?: string;
}

function StatusButton(props: StatusButtonProps) {
  const { labelClassName, label, indicator } = props;
  return (
    <MenuItem
      className={styles.splitButton}
      to={props?.to}
      onPress={props?.onPress}
    >
      <span className={labelClassName}>{label}</span>

      {indicator ? (
        <span className={styles.statusButtonIndicator}>{indicator}</span>
      ) : null}
    </MenuItem>
  );
}

export interface NavBarActivityMenuStatusItemProps {
  friendlyName: string;
  machineIdentifier: string;
  providerEntityID: string;
  serverEntityID: string;
  sessionsCount: number;
  backgroundTranscodeJobs: any[];
  activities: any[];
  showLibraryUpgradeAvailable: boolean;
  showUpdateAvailable: boolean;
  onMenuClose: (...args: any[]) => any;
}

function NavBarActivityMenuStatusItem(
  props: NavBarActivityMenuStatusItemProps
) {
  const {
    friendlyName,
    sessionsCount,
    backgroundTranscodeJobs,
    activities,
    showUpdateAvailable,
    onMenuClose,
  } = props;

  const onUpgradeLibrariesPress = useCallback(() => {
    navigateToServerSettings();
    onMenuClose();
  }, [onMenuClose]);

  //
  // Render

  return (
    <div className={styles.statusContainer}>
      <div className={styles.statusTitle}>{friendlyName}</div>

      <StatusButton
        indicator={sessionsCount ? `${sessionsCount}` : null}
        label={t('Dashboard')}
        to="#!/status/server-dashboard"
        onPress={onMenuClose}
      />

      {backgroundTranscodeJobs.length ? (
        <StatusButton
          indicator={`${backgroundTranscodeJobs.length}`}
          label={t('Conversions')}
          to="#!/status/conversion"
          onPress={onMenuClose}
        />
      ) : null}

      {activities.length
        ? activities.map((activity) => {
            const {
              uuid,
              type,
              title,
              subtitle,
              progress,
              Context = {},
            } = activity;

            // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
            if (!activityTypeWhitelist[type]) {
              return null;
            }

            return (
              <ActivityMenuStatusItem
                key={uuid}
                isPostProcessing={
                  Context.status === grabOperationStatuses.POST_PROCESSING
                }
                progress={progress}
                subtitle={subtitle}
                title={title}
              />
            );
          })
        : null}

      {showUpdateAvailable ? (
        <StatusButton
          indicator={
            <div className={styles.alertIndicator}>
              <IconUpCircledFilled />
            </div>
          }
          label={t('Server Update Available')}
          labelClassName={styles.primaryLabel}
          to="#!/server/update"
          onPress={onMenuClose}
        />
      ) : null}

      {props.showLibraryUpgradeAvailable ? (
        <StatusButton
          indicator={
            <div className={styles.alertIndicator}>
              <IconUpCircledFilled />
            </div>
          }
          label={t('Upgrade Libraries')}
          labelClassName={styles.primaryLabel}
          onPress={onUpgradeLibrariesPress}
        />
      ) : null}
    </div>
  );
}

export default NavBarActivityMenuStatusItem;
