import { createSlice } from '@reduxjs/toolkit';
import serversSlice from 'app/js/v4/core/servers/serversSlice';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  ServerDevicesFetchFailedAction,
  ServerDevicesFetchRequestedAction,
  ServerDevicesFetchSucceededAction,
  ServerDevicesPerServerState,
  ServerDevicesState,
} from './serverDevicesTypes';

const initialState: ServerDevicesState = {};

const initialPerServerState: ServerDevicesPerServerState = {
  devices: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const serverDevicesSlice = createSlice({
  name: 'serverDevices',
  initialState,

  reducers: {
    populateRequested(_state, _action: ServerDevicesFetchRequestedAction) {
      // Handled by saga, no state change.
    },

    fetchRequested(state, { payload }: ServerDevicesFetchRequestedAction) {
      const serverState = state[payload.server.machineIdentifier];

      markPending(serverState);
    },

    fetchSucceeded(state, { payload }: ServerDevicesFetchSucceededAction) {
      const serverState = state[payload.server.machineIdentifier];

      markPopulated(serverState);
      serverState.devices = payload.data;
    },

    fetchFailed(state, { payload }: ServerDevicesFetchFailedAction) {
      const serverState = state[payload.server.machineIdentifier];

      markFailed(serverState, payload.error);
    },
  },

  extraReducers: (builder) => {
    builder.addCase(serversSlice.actions.connected, (state, { payload }) => {
      const { server } = payload;

      if (!state[server.machineIdentifier]) {
        state[server.machineIdentifier] = { ...initialPerServerState };
      }
    });
  },
});

export default serverDevicesSlice;
