import _ from 'underscore';
import Obscura from 'app/js/libs/backbone/backbone.obscura-0.1.7';

const ExclusiveAgentProxy = Obscura.extend({
  constructor: function (
    agents: any,
    exclusiveAgents: any,
    agentIdentifier: any,
    options: any
  ) {
    this.exclusiveAgents = exclusiveAgents;
    this.agentIdentifier = agentIdentifier;

    Obscura.call(this, agents, options);
    this.filterBy(_.bind(this.filterExclusiveAgent, this));
  },

  filterExclusiveAgent: function (agent: any) {
    const identifier = agent.get('identifier');
    const exclusiveAgents = this.exclusiveAgents;

    // If the sectionType has no exclusive agent list,
    // this agent passes the filter.
    if (!exclusiveAgents) {
      return true;
    }

    const currentAgent = this.agentIdentifier;
    const isCurrentFoundInExclusives =
      exclusiveAgents.indexOf(currentAgent) !== -1;
    const isAgentFoundInExclusives = exclusiveAgents.indexOf(identifier) !== -1;

    // If the section being shown belongs to an exclusive agent, only show
    // that agent. Otherwise, show every agent that isn't in the list of
    // exclusive agents.
    const result = isCurrentFoundInExclusives
      ? identifier === currentAgent
      : !isAgentFoundInExclusives;

    return result;
  },
});

export default ExclusiveAgentProxy;
