import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const ServerLogPageModel = AbstractModel.extend({
  //
  // Properties

  name: 'ServerLogPageModel',

  sessionAttrs: {
    logLines: attrTypes.array.isRequired,
    unfilteredLogLinesCount: attrTypes.integer.isRequired,
    filter: attrTypes.string.isRequired,
    logLevels: attrTypes.array.isRequired,
    highlightedLineIndex: attrTypes.integer,
    timeFilterStart: attrTypes.string.isRequired,
    timeFilterEnd: attrTypes.string.isRequired,
    isTimeFilterStartValid: attrTypes.boolean.isRequired,
    isTimeFilterEndValid: attrTypes.boolean.isRequired,
    showTimeFilter: attrTypes.boolean.isRequired,
    showRawText: attrTypes.boolean.isRequired,
  },

  defaults() {
    return {
      logLines: [],
      unfilteredLogLinesCount: 0,
      filter: '',
      logLevels: [],
      timeFilterStart: '',
      timeFilterEnd: '',
      isTimeFilterStartValid: true,
      isTimeFilterEndValid: true,
      showRawText: false,
      showTimeFilter: false,
    };
  },
});

export default ServerLogPageModel;
