/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import * as ip from 'app/js/utils/url/ip';

export const CONTEXT_FILE = 'file';
export const CONTEXT_HTTP = 'http';
export const CONTEXT_HTTP_LOOPBACK = 'http-loopback';
export const CONTEXT_HTTPS = 'https';

function getMetricsProtocolContext(location: any) {
  const { hostname, protocol } = location;
  const isFile = protocol === 'file:';
  const isHttp = protocol === 'http:';
  const isHttps = protocol === 'https:';

  let context;

  if (isHttps) {
    context = CONTEXT_HTTPS;
  } else if (isHttp) {
    context = ip.isLoopbackAddress(hostname)
      ? CONTEXT_HTTP_LOOPBACK
      : CONTEXT_HTTP;
  } else if (isFile) {
    context = CONTEXT_FILE;
  }

  __DEV__ && assert(context, 'A metrics protocol context is required');

  return context;
}

export default getMetricsProtocolContext;
