/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { resolve } from 'app/js/adapters/adapters';
import { redirectToUrl } from 'app/js/stores/actions/appActions';
import {
  populateSettings,
  saveSetting,
} from 'app/js/stores/actions/serverActions';
import createServerFlexSelector from 'app/js/stores/selectors/server/createServerFlexSelector';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import AcceptEULAPage from 'app/js/views/v3/components/pages/setup/AcceptEULAPage';

function createMapStateToProps() {
  return createSelector(createServerFlexSelector(), (serverFlex) => {
    const acceptedEULASetting = serverFlex.settings.find(
      (setting: any) => setting.id === 'AcceptedEULA'
    );

    return {
      isSettingsPopulated: serverFlex.isSettingsPopulated,
      settingsError: serverFlex.settingsError,
      hasAcceptedEULA: !!(acceptedEULASetting && acceptedEULASetting.value),
      isAcceptedEULASaving: !!(
        acceptedEULASetting && acceptedEULASetting.isSaving
      ),
    };
  });
}

function createMapDispatchToProps(dispatch: any, props: any) {
  const { serverEntityID, forwardUrl } = props;

  return {
    onPopulateSettings() {
      dispatch(populateSettings({ serverEntityID }));
    },

    onAcceptEULA() {
      dispatch(
        saveSetting({
          serverEntityID,
          setting: { id: 'AcceptedEULA', value: true },
        })
      );
    },

    onSkipSignIn() {
      resolve('metrics').trackClick({
        page: metricsPages.SIGN_IN,
        action: metricsActions.SKIP,
        firstRun: true,
      });

      dispatch(redirectToUrl({ url: forwardUrl }));
    },
  };
}

export default connect(
  createMapStateToProps,
  createMapDispatchToProps
)(AcceptEULAPage);
