function getSortedLanguageCodes() {
  const languageCodes = Object.keys(languages).filter((languageCode) => {
    return NON_LANGUAGE_CODES.indexOf(languageCode) === -1;
  });

  languageCodes.sort(languageCodeComparator);

  return languageCodes.concat(NON_LANGUAGE_CODES);
}

export function languageCodeComparator(languageCodeA: any, languageCodeB: any) {
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  return languages[languageCodeA].localeCompare(languages[languageCodeB]);
}

export const NON_LANGUAGE_CODES = [UNKNOWN_LANG_CODE, NONE_LANG_CODE];
export const sortedLanguageCodes = getSortedLanguageCodes();
export default languages;
