/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import path from 'app/js/utils/url/path';
import SortModel from './SortModel';

const SortCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'SortCollection',
  context: ['server', 'section', 'typeID'],

  model: SortModel,

  url() {
    const urlRoot = _.result(this.section, 'url');
    const typeID = this.typeID || this.section.get('currentType').id;
    const params = typeID ? { type: typeID } : {};

    return path.join([urlRoot, '/sorts'], params);
  },
});

export default SortCollection;
