/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import MediaSettingCollection from 'app/js/models/subscription/MediaSettingCollection';

export const deviceStatuses = {
  DEAD: 'dead',
  ALIVE: 'alive',
};

export const deviceStates = {
  DISABLED: 'disabled',
  ENABLED: 'enabled',
};

const MediaGrabberDeviceModel = AbstractModel.extend({
  //
  // Properties

  name: 'MediaGrabberDeviceModel',
  context: ['server'],

  rootPropertyName: 'Device',
  idAttribute: 'key',

  sessionAttrs: {
    isChannelsPending: attrTypes.boolean,
    isChannelsPopulated: attrTypes.boolean,
    channelsError: attrTypes.object,
    selectedSource: attrTypes.string,
    isSaving: attrTypes.boolean,
    saveError: attrTypes.object,
  },

  syncedAttrs: {
    key: attrTypes.integer,
    uuid: attrTypes.string,
    parentID: attrTypes.integer,
    uri: attrTypes.string,
    protocol: attrTypes.string,
    status: attrTypes.string,
    state: attrTypes.string,
    lastSeenAt: attrTypes.integer,
    make: attrTypes.string,
    model: attrTypes.string,
    modelNumber: attrTypes.string,
    source: attrTypes.integer,
    sources: attrTypes.list,
    title: attrTypes.string,
    tuners: attrTypes.integer,
    thumb: attrTypes.string,
    unsupported: attrTypes.boolean,
  },

  syncedRelations: {
    prefs: {
      relationClass: MediaSettingCollection,
      rootPropertyName: 'Setting',
      deferred: true,
      save: true,
    },
  },

  defaults: {
    isChannelsPending: false,
    isChannelsPopulated: false,
    channelsError: null,
    isSaving: false,
    saveError: null,
    unsupported: false,
  },
});

export default MediaGrabberDeviceModel;
