import { createSlice } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  TopPlayedFetchFailedAction,
  TopPlayedFetchRequestedAction,
  TopPlayedFetchSucceededAction,
  TopPlayedState,
} from './dashboardTopPlayedTypes';

const initialState: TopPlayedState = {
  moviesStatistics: [],
  tvStatistics: [],
  musicStatistics: [],
  gameStatistics: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const dashboardTopPlayedSlice = createSlice({
  name: 'dashboardTopPlayed',
  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    topPlayedFetchRequested(state, _action: TopPlayedFetchRequestedAction) {
      markPending(state);
    },

    topPlayedFetchSucceeded(state, { payload }: TopPlayedFetchSucceededAction) {
      markPopulated(state);
      state.moviesStatistics = payload.data.moviesStatistics;
      state.tvStatistics = payload.data.tvStatistics;
      state.musicStatistics = payload.data.musicStatistics;
      state.gameStatistics = payload.data.gameStatistics;
    },

    topPlayedFetchFailed(state, { payload }: TopPlayedFetchFailedAction) {
      markFailed(state, payload.error);
    },
  },
});

export default dashboardTopPlayedSlice;
