/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import ProviderActionCollection from 'app/js/models/provider/ProviderActionCollection';
import ProviderSettingCollection from 'app/js/models/provider/ProviderSettingCollection';
import ProviderDirectoryCollection from './ProviderDirectoryCollection';

const ProviderFeatureModel = AbstractModel.extend({
  //
  // Properties

  name: 'ProviderFeatureModel',
  context: ['server', 'provider'],

  sessionAttrs: {
    isSettingsPending: attrTypes.boolean,
    isSettingsPopulated: attrTypes.boolean,
    settingsError: attrTypes.object,
  },

  syncedAttrs: {
    id: attrTypes.string,
    key: attrTypes.string,
    reverseKey: attrTypes.string,
    scrobbleKey: attrTypes.string,
    unscrobbleKey: attrTypes.string,
    type: attrTypes.string,
    flavor: attrTypes.string,
    requires: attrTypes.string,
    automaticLibraryFolders: attrTypes.boolean,
    public: attrTypes.boolean,
    storageTotal: attrTypes.integer,
  },

  syncedRelations: {
    directories: {
      relationClass: ProviderDirectoryCollection,
      rootPropertyName: 'Directory',
      deferred: true,
    },
    settings: {
      relationClass: ProviderSettingCollection,
      rootPropertyName: 'Setting',
      deferred: true,
    },
    actions: {
      relationClass: ProviderActionCollection,
      rootPropertyName: 'Action',
      deferred: true,
      setOptions: {
        // The providers listed at plex.tv do not have features.  However,
        // ProviderFeatureCollection.augmentResponse adds in an empty actions
        // feature when no action feature is present.  Don't allow actions to be
        // removed in case the list of providers is fetched again.
        remove: false,
      },
    },
  },

  defaults: {
    isSettingsPending: true,
    isSettingsPopulated: false,
  },
});

export default ProviderFeatureModel;
