/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import colors from 'app/styles/variables/colors';
import styles from './Spinner.css';

interface SpinnerProps {
  className?: string;
  containerClassName?: string;
  labelClassName?: string;
  label?: string;
  loadingLabel?: string;
  size?: any; // TODO: PropTypes.oneOf(sizes.all)
  color: string;
}

function Spinner({
  className,
  containerClassName,
  labelClassName,
  size,
  color,
  label,
  loadingLabel,
}: SpinnerProps) {
  const spinnerClassName = classNames(className, styles[size]);

  const spinnerStyle = {
    borderTopColor: color,
    borderLeftColor: color,
  };

  if (!label) {
    return (
      <div
        aria-label={loadingLabel}
        className={spinnerClassName}
        style={spinnerStyle}
      />
    );
  }

  return (
    <div className={classNames(containerClassName, styles.spinnerContainer)}>
      <div className={spinnerClassName} style={spinnerStyle} />
      <div className={classNames(labelClassName, styles.spinnerLabel)}>
        {label}
      </div>
    </div>
  );
}

Spinner.defaultProps = {
  size: sizes.MEDIUM,
  color: colors.primaryColor,
};

export default Spinner;
