/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import simpleCORSFilter from 'app/js/utils/xhr/simpleCORSFilter';
import xhrTracker from 'app/js/utils/xhr/xhrTracker';

export default function ajaxInitializer() {
  // This filter makes all requests simple enough so they no longer require an
  // OPTIONS preflight to the server.
  $.ajaxPrefilter(simpleCORSFilter);

  xhrTracker.startListening();

  return $.Deferred().resolve().promise();
}
