/* eslint-plugin-disable @typescript-eslint */

import config from 'app/js/models/config';
import capabilities from 'app/js/utils/device/capabilities';

// Insecure fallback rules:
//
// If a server is unavailable and the app prevented insecure connections
// from being tested, but the server can be seen by plex.tv (presence), we
// have reason to believe that a fallback may allow the user to connect to
// the server.
//
// 1. If the app is https and does not allow mixed content, but the server
//    only has http connections, show the "Reload app insecurely" action.
// 2. If the app is https and the server has https connections, but the
//    https connections failed for some reason (DNS rebinding protection),
//    show the "Reload app insecurely" action.
// 3. If the app is http and the server has https connections, but the https
//    connections failed for some reason (DNS rebinding protection), show
//    the "Allow insecure connection" action.

function isFallbackPossible() {
  return !capabilities.isHostedHttps || !!config.allowMixedContent;
}

function isFallbackSuccessLikely(server: any) {
  return server.isFullServer && !!server.presence && !server.httpsRequired;
}

export function shouldShowInsecureAppMessage(server: any) {
  return !isFallbackPossible() && isFallbackSuccessLikely(server);
}

export function shouldAllowInsecureFallback(server: any) {
  const canCreateFallbackConnections =
    server.hasHttpsConnections && !server.hasFallbackConnections;

  return (
    isFallbackPossible() &&
    isFallbackSuccessLikely(server) &&
    canCreateFallbackConnections
  );
}
