/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

export default function updateServerConnectionsFromResource(
  server: any,
  resource: any
) {
  resource.get('connections').each((connection: any) => {
    const sources = [{ id: resource.server.id }];
    const connectionAttrs = _.extend(connection.toJSON(), {
      sources: sources,
    });

    server.addConnection(connectionAttrs);
  });
}
