/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';

export default function relativeTime({
  milliseconds,
  isShorthand,
  isFuture,
}: any) {
  let seconds = Math.abs(milliseconds) / 1000;
  if (seconds) {
    seconds = seconds < 1 ? 1 : Math.round(seconds);
  }

  const minutes = Math.round(seconds / 60);
  const hours = Math.round(minutes / 60);
  const days = Math.round(hours / 24);
  const months = Math.round(days / 30);
  const years = Math.round(days / 365);

  const time = {
    seconds,
    minutes,
    hours,
    days,
    months,
    years,
  };

  const tokens = {
    unitOfTime:
      (seconds <= 44 &&
        (isShorthand ? t('{seconds}s', time) : t('a few seconds', time))) ||
      (seconds < 45 &&
        (isShorthand ? t('{seconds}s', time) : t('{seconds} seconds', time))) ||
      (minutes <= 1 &&
        (isShorthand ? t('{minutes}m', time) : t('a minute', time))) ||
      (minutes < 45 &&
        (isShorthand ? t('{minutes}m', time) : t('{minutes} minutes', time))) ||
      (hours <= 1 &&
        (isShorthand ? t('{hours}h', time) : t('an hour', time))) ||
      (hours < 22 &&
        (isShorthand ? t('{hours}h', time) : t('{hours} hours', time))) ||
      (days <= 1 && (isShorthand ? t('{days}d', time) : t('a day', time))) ||
      (days < 26 &&
        (isShorthand ? t('{days}d', time) : t('{days} days', time))) ||
      (months <= 1 &&
        (isShorthand ? t('{months}mo', time) : t('a month', time))) ||
      (months < 11 &&
        (isShorthand ? t('{months}mo', time) : t('{months} months', time))) ||
      (years <= 1 && (isShorthand ? t('{years}y', time) : t('a year', time))) ||
      (isShorthand ? t('{years}y', time) : t('{years} years', time)),
  };
  /* eslint-enable no-multi-spaces */

  return isFuture
    ? t('in {unitOfTime}', tokens)
    : t('{unitOfTime} ago', tokens);
}
