/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import HubCollection from 'app/js/models/metadata/HubCollection';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import LyricsStreamCollection from 'app/js/models/metadata/stream/LyricsStreamCollection';
import ModelCache from 'app/js/utils/cache/ModelCache';
import parser2 from 'app/js/utils/parser2';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import streamTypes from 'app/js/utils/types/streamTypes';
import VastModel from './VastModel';

const PlayQueueItemModel = AbstractModel.extend({
  //
  // Properties
  name: 'PlayQueueItemModel',
  context: [
    'server',
    'provider',
    'identifier',
    'mediaTagPrefix',
    'mediaTagVersion',
  ],
  entityType: entityTypes.PLAY_QUEUE_ITEM,
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    return createEntityID(
      this.entityType,
      state.playQueueItemID,
      this.server.entityID()
    );
  },
  idAttribute: 'playQueueItemID',
  cache: new ModelCache(),
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'attributes' implicitly has an 'any' typ... Remove this comment to see the full error message
  cacheKey(attributes) {
    return this.server && this.server.getScopedKey(attributes.playQueueItemID);
  },
  sessionAttrs: {
    isPlayQueueItem: attrTypes.boolean,
    isLocal: attrTypes.boolean,
  },
  syncedAttrs: {
    playQueueItemID: attrTypes.string,
    queueIndex: attrTypes.integer,
    key: attrTypes.string,
    ratingKey: attrTypes.string,
    type: attrTypes.string,
    subtype: attrTypes.string,
    advertisement: attrTypes.boolean,
    allowSkipAfter: attrTypes.integer,
    librarySectionID: attrTypes.string,
    hasPremiumLyrics: attrTypes.boolean,
    displayPostplay: attrTypes.boolean,
    // Background Processing Queue items
    syncItemId: attrTypes.integer,
    generatorID: attrTypes.integer,
    generatorType: attrTypes.integer,
    clientIdentifier: attrTypes.string,
    target: attrTypes.string,
  },
  syncedRelations: {
    metadataItem: {
      relationClass: MetadataModel,
      deferred: true,
    },
    lyricsStreams: {
      relationClass: LyricsStreamCollection,
      deferred: true,
    },
    vast: {
      relationClass: VastModel,
      deferred: true,
    },
  },
  sessionRelations: {
    postPlay: {
      relationClass: HubCollection,
      options() {
        return {
          url: `hubs/metadata/${(this as any).get('metadataItem').id}/postplay`,
        };
      },
    },
  },
  defaults: {
    isPlayQueueItem: true,
    isLocal: false,
  },
  //
  // Control
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'response' implicitly has an 'any' type.
  augmentResponse(response) {
    // Separate the play queue attributes from the metadata attributes.
    const playQueueAttributes = _.pick(response, Object.keys(this.syncedAttrs));
    if (response.Ad) {
      if (Array.isArray(response.Ad)) {
        response.Ad = response.Ad[0];
      }

      playQueueAttributes.vast = response.Ad;
    }

    playQueueAttributes.metadataItem = response;
    // Until media items, parts, and streams can be relations on `MetadataModel`
    // wait until a play queue item to create a relation for lyrics streams.
    // This helps performance when large collections metadata items.
    const mediaItem = _.first(parser2.parseArray(response.Media));
    if (mediaItem) {
      const part = _.first(parser2.parseArray(mediaItem.Part));
      if (part) {
        const streams = parser2.parseArray(part.Stream);
        if (streams) {
          const lyricsType = streamTypes.get('lyrics');
          playQueueAttributes.lyricsStreams = _.filter(streams, (stream) => {
            return parser2.parseInteger(stream.streamType) === lyricsType.id;
          });
        }
      }
    }

    return playQueueAttributes;
  },
});
export default PlayQueueItemModel;
