/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import getLanguageName from 'app/js/utils/language/getLanguageName';

const LanguageModel = AbstractModel.extend({
  //
  // Properties
  name: 'LanguageModel',
  context: ['server'],
  idAttribute: 'code',
  rootPropertyName: 'Language',
  syncedAttrs: {
    code: attrTypes.string,
    title: attrTypes.string,
    default: attrTypes.boolean,
  },
  derivedAttrs: {
    name: function () {
      return (
        (this as any).get('title') || getLanguageName((this as any).get('code'))
      );
    },
  },
});
export default LanguageModel;
