import formatBitrateAsKilobits from 'app/js/utils/metadata/formatBitrateAsKilobits';
import formatBitrateAsMegabits from 'app/js/utils/metadata/formatBitrateAsMegabits';

function formatVideoBitrate(kbps: number): string {
  if (kbps >= 100) {
    return formatBitrateAsMegabits(kbps);
  }
  return formatBitrateAsKilobits(kbps);
}

export default formatVideoBitrate;
