import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsEvents from 'app/js/utils/constants/metrics/metricsEvents';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import getMetricsServerStatus from 'app/js/utils/metrics/getMetricsServerStatus';
import getMetricsServerType from 'app/js/utils/metrics/getMetricsServerType';

function trackPreferredServerEvent(server: any, interaction = true) {
  resolve('metrics').trackEvent({
    event: metricsEvents.CLIENT_CLICK,
    interaction,
    properties: {
      page: metricsPages.PREFERRED_SERVER,
      action: metricsActions.SELECT_PREFERRED_SERVER,
      serverType: getMetricsServerType(server),
      status: getMetricsServerStatus(server),
      firstRun: true,
    },
  });
}

export default trackPreferredServerEvent;
