import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import dispatcher from 'app/js/utils/dispatcher';
import whenEach from 'app/js/utils/promise/whenEach';
import t from 'app/js/utils/t';
import devicesImg from 'app/img/setup/setup-complete-devices.svg';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setup... Remove this comment to see the full error message
import tpl from 'app/templates/setup/SetupCompleteView.tpl';

const SetupCompleteView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'setup-complete-container',
  template: tpl,

  ui: {
    modalContainer: '.modal-container',
    submitBtn: '.submit-btn',
    doneBtn: '.done-btn',
  },

  collectionEvents: {
    reset: 'render',
  },

  events: {
    'change input[id]': 'onInputChange',
    'submit #setup-complete-form': 'onSubmit',
  },

  //
  // Init

  initialize: function () {
    this.collection = this.model.get(__PLATFORM__).get('settings');
    this.collection.populate({ reset: true });

    // If the server has a super token, setup is happening in the hosted app
    // and we should set this token on the server so we don't need to
    // require the user to sign in again.

    this.changes = {};

    const appModel = adapters.resolve('appModel');
    const user = appModel.get('user');

    if (user.get('isSignedIn')) {
      user.fetch().done(
        this.whenRendered(() => {
          this.ui.submitBtn.removeClass('active');
        })
      );
    } else {
      this.once('attached', () => {
        this.ui.submitBtn.removeClass('active');
      });
    }
  },

  //
  // Control

  serializeData: function () {
    const collectUsageDataSetting = this.collection.get('collectUsageData');

    return {
      devicesImg: devicesImg,
      collectUsageDataSetting: collectUsageDataSetting
        ? collectUsageDataSetting.toJSON()
        : null,
    };
  },

  submit: function () {
    const submitBtn = this.ui.submitBtn;
    if (submitBtn.hasClass('active')) {
      return;
    }

    submitBtn.addClass('active');

    const promise = this.save();

    promise.always(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.doneBtn.click();
        submitBtn.removeClass('active');
      })
    );
  },

  save: function () {
    this.saved = true;

    if (
      _.has(this.changes, 'collectUsageData') &&
      !this.changes.collectUsageData
    ) {
      adapters.resolve('metrics').trackClick({
        action: metricsActions.OPT_OUT_METRICS,
        page: metricsPages.SETUP_WIZARD,
        pane: 'complete',
        firstRun: true,
      });
    }

    // Save the settings
    const settingsPromise = this.collection.first().save(this.changes);

    // Save any unsaved default sections, treating errors as success
    const sectionPromises = this.model
      .get('sections')
      .filter((section: any) => section.isNew())
      .map((section: any) => section.save());

    // Let the requests run in parallel, wait until all are complete.
    const promise = $.when(settingsPromise, whenEach(sectionPromises));

    promise.fail(() => {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem saving these settings.'),
      });
    });

    return promise;
  },

  //
  // Listeners

  onClose: function () {
    if (!this.saved) {
      this.save();
    }
  },

  onInputChange: function (event: any) {
    const field = event.currentTarget;
    let value = field.value;

    if (field.type === 'checkbox') {
      value = field.checked ? 1 : 0;
    }

    this.changes[field.id] = value;
  },

  onSubmit: function (event: any) {
    event.preventDefault();

    this.submit();
  },
});

export default SetupCompleteView;
