/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';

const PlaylistItemModel = AbstractModel.extend({
  //
  // Properties

  name: 'PlaylistItemModel',

  context: ['server', 'provider', 'playlist'],

  entityType: entityTypes.PLAYLIST_ITEM,
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    // Smart playlists don't have a playlistItemID but have unique keys
    return createEntityID(
      this.entityType,
      state.playlistItemID || state.key,
      this.server.entityID()
    );
  },

  idAttribute: 'playlistItemID',

  syncedAttrs: {
    playlistItemID: attrTypes.string,
    key: attrTypes.string,
    type: attrTypes.string,
  },

  syncedRelations: {
    metadataItem: {
      relationClass: MetadataModel,
      deferred: true,
    },
  },

  //
  // Control

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'response' implicitly has an 'any' type.
  augmentResponse(response) {
    // Split the metadata item from the playlist item.
    return {
      playlistItemID: response.playlistItemID,
      key: response.key,
      type: response.type,
      metadataItem: response,
    };
  },
});

export default PlaylistItemModel;
