import { createSlice } from '@reduxjs/toolkit';
import * as Sentry from '@sentry/browser';
import log from 'app/js/utils/log/log';
import {
  ServerNowPlayingSessionsChangedAction,
  ServerNowPlayingSessionsFetchFailedAction,
  ServerNowPlayingSessionsFetchRequestedAction,
  ServerNowPlayingSessionsFetchSucceededAction,
  ServerNowPlayingSessionsPerServerState,
  ServerNowPlayingSessionsState,
  ServerNowPlayingSessionsStopErrorDismissedAction,
  ServerNowPlayingSessionsStopFailedAction,
  ServerNowPlayingSessionsStoppedAction,
  ServerNowPlayingSessionsStopRequestedAction,
} from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import serversSlice from 'app/js/v4/core/servers/serversSlice';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';

function reportServer(errorMessage: string, server: Server): void {
  const {
    isCloud,
    isConnected,
    isEmbedded,
    isFullOwnedServer,
    isFullServer,
    isUnauthorized,
    isUnavailable,
  } = server;

  // See: https://docs.sentry.io/platforms/javascript/enriching-events/context/#passing-context-directly
  const context = {
    extra: {
      isCloud,
      isConnected,
      isEmbedded,
      isFullOwnedServer,
      isFullServer,
      isUnauthorized,
      isUnavailable,
    },
  };

  const error = new Error(errorMessage);

  log.error(error, context.extra);

  Sentry.captureException(error, context);
}

const initialState: ServerNowPlayingSessionsState = {};

const initialPerServerState: ServerNowPlayingSessionsPerServerState = {
  sessionKeys: [],
  sessionsByKey: {},
  isPending: false,
  isPopulated: false,
  error: null,
  stopSessionError: null,
};

const serverNowPlayingSessionsSlice = createSlice({
  name: 'serverNowPlayingSessions',
  initialState,

  reducers: {
    fetchRequested(
      state,
      { payload }: ServerNowPlayingSessionsFetchRequestedAction
    ) {
      const serverState = state[payload.server.machineIdentifier];

      if (!serverState) {
        return;
      }

      markPending(serverState);
    },

    fetchSucceeded(
      state,
      { payload }: ServerNowPlayingSessionsFetchSucceededAction
    ) {
      const serverState = state[payload.server.machineIdentifier];

      if (!serverState) {
        return;
      }

      const { sessionKeys, sessionsByKey } = payload.data.reduce<{
        sessionKeys: typeof initialPerServerState.sessionKeys;
        sessionsByKey: typeof initialPerServerState.sessionsByKey;
      }>(
        (acc, session) => {
          acc.sessionKeys.push(session.sessionKey);
          acc.sessionsByKey[session.sessionKey] = session;

          return acc;
        },
        { sessionKeys: [], sessionsByKey: {} }
      );

      markPopulated(serverState);
      serverState.sessionKeys = sessionKeys;
      serverState.sessionsByKey = sessionsByKey;
    },

    fetchFailed(state, { payload }: ServerNowPlayingSessionsFetchFailedAction) {
      const serverState = state[payload.server.machineIdentifier];

      if (!serverState) {
        return;
      }

      markFailed(serverState, payload.error);
    },

    sessionChanged(state, { payload }: ServerNowPlayingSessionsChangedAction) {
      const { server, message } = payload;
      const serverState = state[server.machineIdentifier];

      // See: https://github.com/plexinc/plex-web-client/issues/16539
      if (!serverState) {
        reportServer('Session changed without serverState', server);
        return;
      }

      const existingSession = serverState.sessionsByKey[message.sessionKey];

      if (existingSession && existingSession.key === message.key) {
        existingSession.viewOffset = message.viewOffset;
        existingSession.Player.state = message.state;
      }
    },

    sessionStopped(state, { payload }: ServerNowPlayingSessionsStoppedAction) {
      const { server, sessionKey } = payload;
      const serverState = state[server.machineIdentifier];

      // See: https://github.com/plexinc/plex-web-client/issues/16539
      if (!serverState) {
        reportServer('Session stopped without serverState', server);
        return;
      }

      const existingSession = serverState.sessionsByKey[sessionKey];

      if (existingSession) {
        delete serverState.sessionsByKey[sessionKey];

        serverState.sessionKeys.splice(
          serverState.sessionKeys.indexOf(sessionKey),
          1
        );
      }
    },

    stopRequested(
      _state,
      _action: ServerNowPlayingSessionsStopRequestedAction
    ) {
      // Handled by saga, no state change.
    },

    stopFailed(state, { payload }: ServerNowPlayingSessionsStopFailedAction) {
      const serverState = state[payload.server.machineIdentifier];

      if (!serverState) {
        return;
      }

      serverState.stopSessionError = payload.error;
    },

    stopErrorDismissed(
      state,
      { payload }: ServerNowPlayingSessionsStopErrorDismissedAction
    ) {
      const serverState = state[payload.server.machineIdentifier];

      if (!serverState) {
        return;
      }

      serverState.stopSessionError = null;
    },
  },

  extraReducers: (builder) => {
    builder.addCase(serversSlice.actions.connected, (state, { payload }) => {
      const { server } = payload;

      if (!state[server.machineIdentifier]) {
        state[server.machineIdentifier] = { ...initialPerServerState };
      }
    });
  },
});

export default serverNowPlayingSessionsSlice;
