/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import toUpperCaseFirstLetter from 'app/js/utils/string/case/toUpperCaseFirstLetter';
import xhrErrorFilter from 'app/js/utils/xhr/xhrErrorFilter';

export default function createControlledFetchHandler(options: any) {
  const { key, getModel, onResolved, onRejected } = options;

  __DEV__ && assert(!!getModel, 'Missing mandatory `getModel` function');

  const keyTitle = toUpperCaseFirstLetter(key);
  const pendingKey = `is${keyTitle}Pending`;
  const populatedKey = `is${keyTitle}Populated`;
  const errorKey = key ? `${key}Error` : 'error';

  return (appModel: any, payload = {}, dispatch: any) => {
    const model = getModel(appModel, payload);
    const sessionAttrs = model.sessionAttrs;

    __DEV__ &&
      assert(
        !!sessionAttrs,
        `${model.name} is missing session attributes ${pendingKey}, ${populatedKey}, and ${errorKey}`
      );

    __DEV__ &&
      assert(
        sessionAttrs.hasOwnProperty(pendingKey),
        `${model.name} is missing a session attribute for ${pendingKey}`
      );

    __DEV__ &&
      assert(
        sessionAttrs.hasOwnProperty(populatedKey),
        `${model.name} is missing a session attribute for ${populatedKey}`
      );

    __DEV__ &&
      assert(
        sessionAttrs.hasOwnProperty(errorKey),
        `${model.name} is missing a session attribute for ${errorKey}`
      );

    const previousPending = model.get(pendingKey);
    model.set(pendingKey, true);

    options.fetch(appModel, payload, dispatch).then(
      () => {
        model.set({
          [pendingKey]: false,
          [populatedKey]: true,
          [errorKey]: null,
        });

        if (onResolved) {
          // Guarantee an optional side-effect and/or post-processing step runs
          // within the same tick as the model attribute changes above.
          onResolved(model, appModel, payload, dispatch);
        }
      },
      (xhr: any) => {
        __DEV__ &&
          assert(
            xhr,
            `An XHR error object is required fetching ${model.name} fails`
          );

        const nextAttrs = xhr.aborting
          ? {
              [pendingKey]: previousPending,
            }
          : {
              [pendingKey]: false,
              [errorKey]: xhrErrorFilter(xhr),
            };

        model.set(nextAttrs);

        if (onRejected) {
          onRejected(model, appModel, payload, dispatch);
        }
      }
    );
  };
}
