import Localizer from 'app/js/adapters/Localizer';
import api from 'app/js/v4/utils/data/api';

const DesktopLocalizer = Localizer.extend({
  //
  // Listeners

  onAppModelRegistered(appModel: any) {
    appModel.on(
      'change:user.change:experienceSettings.change:language',
      (_model: any, language: any) => {
        this.load(language).done(() => {
          api.setOption('language', language);
          this._repopulateProviders();
        });
      }
    );
  },
});

export default DesktopLocalizer;
