/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import silentMP3 from 'app/js/utils/media/silentMP3';
import {
  createMediaElement,
  releaseMediaElement,
} from 'app/js/utils/mediaElementPool';

// In some browsers, audio can only be started from user interaction.
// We start silent media playback on any user interaction in order to re-use
// the media element for later asynchronously-driven playback.

const events = ['touchstart', 'mousedown', 'click', 'keydown', 'keypress'];

export default function mediaElementInitializer() {
  function onInteract() {
    events.forEach((eventName) => {
      document.body.removeEventListener(eventName, onInteract, true);
    });

    const mediaElement = createMediaElement('audio', true);

    function onReady() {
      mediaElement.removeEventListener('playing', onReady);
      mediaElement.removeEventListener('error', onReady);

      mediaElement.src = '';
      mediaElement.removeAttribute('src');
      releaseMediaElement(mediaElement);
    }

    mediaElement.addEventListener('playing', onReady);
    mediaElement.addEventListener('error', onReady);

    mediaElement.src = silentMP3;

    const promise = mediaElement.play();

    if (promise && promise.then) {
      promise.catch(() => {
        // No-op to silence unhandled promise rejection warnings. The onReady
        // handler cleans up on `error` events.
      });
    }
  }

  events.forEach((eventName) => {
    document.body.addEventListener(eventName, onInteract, true);
  });

  return $.Deferred().resolve().promise();
}
