import { createSelector } from 'reselect';
import mapUserFeaturesByUUID from 'app/js/utils/user/mapUserFeaturesByUUID';
import createUserAttributeSelector from './createUserAttributeSelector';

const selectUserFeaturesByUUID = createSelector(
  createUserAttributeSelector('features'),
  (features) => mapUserFeaturesByUUID(features || [])
);

export default selectUserFeaturesByUUID;
