import { SagaIterator } from 'redux-saga';
import { all, call, fork, put, select } from 'redux-saga/effects';
import createServerAccountsSelector from 'app/js/v4/core/serverAccounts/selectors/createServerAccountsSelector';
import api from 'app/js/v4/utils/data/api';
import takeLeadingPerID from 'app/js/v4/utils/data/saga/takeLeadingPerID';
import serverAccountsSlice from './serverAccountsSlice';
import {
  ServerAccount,
  ServerAccountsFetchRequestedAction,
  ServerAccountsPerServerState,
} from './serverAccountsTypes';

//
// Workers

export function* populateServerAccounts({
  payload,
}: ServerAccountsFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const serverAccounts: ServerAccountsPerServerState = yield select(
      createServerAccountsSelector(server.machineIdentifier)
    );

    if (!serverAccounts.isPopulated) {
      yield put(serverAccountsSlice.actions.fetchRequested(payload));
    }
  } catch (error) {
    yield put(
      serverAccountsSlice.actions.fetchFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchServerAccounts({
  payload,
}: ServerAccountsFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const response = yield call(api.get, '/accounts', {
      server,
    });

    // The API currently returns unknown accounts with no name. Filter these
    // out from the response
    // See: https://github.com/plexinc/plex-media-server/issues/9637

    const list: ServerAccount[] = response.data.MediaContainer?.Account ?? [];

    yield put(
      serverAccountsSlice.actions.fetchSucceeded({
        server,
        data: list.filter((account) => account.name),
      })
    );
  } catch (error) {
    yield put(
      serverAccountsSlice.actions.fetchFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Watchers

function* watchPopulateRequested(): SagaIterator {
  yield takeLeadingPerID(
    serverAccountsSlice.actions.populateRequested,
    populateServerAccounts,
    ({ payload }) => payload.server.machineIdentifier
  );
}

function* watchFetchRequested(): SagaIterator {
  yield takeLeadingPerID(
    serverAccountsSlice.actions.fetchRequested,
    fetchServerAccounts,
    ({ payload }) => payload.server.machineIdentifier
  );
}

export default function* serverAccountsSaga(): SagaIterator {
  yield all([fork(watchPopulateRequested), fork(watchFetchRequested)]);
}
