/* eslint-plugin-disable @typescript-eslint */

import * as mediaTypes from 'app/js/utils/types/mediaTypes';
import path from 'app/js/utils/url/path';
import getProviderBaseRoute from 'app/js/views/v3/helpers/routes/getProviderBaseRoute';

export default function getPrePlayRoute(
  server: any,
  provider: any,
  key: any,
  routeParams = {},
  playlistType: any
) {
  if (!server || !provider || !key) {
    return;
  }

  const baseRoute = getProviderBaseRoute(server, provider);
  const encodedKey = encodeURIComponent(key);

  const playlistRoutes = {
    [mediaTypes.PHOTO]: 'album',
    [mediaTypes.VIDEO]: 'playlist',
    [mediaTypes.AUDIO]: 'playlist',
  };

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  const route = playlistType ? playlistRoutes[playlistType] : 'details';
  const url = `#!${baseRoute}/${route}?key=${encodedKey}`;

  return path.join(url, routeParams);
}
