/* eslint-plugin-disable @typescript-eslint */

import getAuthCodeExchangeModel from 'app/js/stores/actions/helpers/server/getAuthCodeExchangeModel';
import fetchPIN from 'app/js/stores/actions/helpers/user/fetchPIN';
import t from 'app/js/utils/t';

function createBeginPollingHandler() {
  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  return function (appModel: any, payload: any, dispatch: any) {
    const { pinID } = payload;
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);
    function _poll() {
      // Stop polling if pinID changes from the one originally dispatched, such
      // as if the user navigates away from, then back to Claim Server.
      const expectedPIN = authCodeExchangeModel.get('pinID');
      if (pinID !== expectedPIN) {
        return;
      }
      fetchPIN(appModel, pinID)
        .then(({ authToken }) => {
          if (authToken) {
            authCodeExchangeModel.set({
              authToken,
            });
          } else {
            schedulePoll();
          }
        })
        .fail((xhr) => {
          // When a PIN expires, we expect to see a 404 for its id
          authCodeExchangeModel.set({
            code: null,
            pinID: null,
            isBusy: false,
            fatalErrorMessage:
              xhr && (xhr as any).status === 404
                ? null
                : t('An error occurred'),
          });
        });
    }
    function schedulePoll() {
      window.setTimeout(_poll, 2000);
    }
    schedulePoll();
  };
}
export default createBeginPollingHandler;
