/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';

export default function createServerNowPlayingSessionsCountSelector() {
  return createSelector(
    createUnstableServerSelector(),
    (appState) => appState.v4.core.serverNowPlayingSessions,
    (server, serverNowPlayingSessions) => {
      // `ServerSettingsPageConnector` can call this selector before a server is
      // discovered or even if a server cannot be found matching the
      // `machineIdentifier` in the route.

      if (!server) {
        return 0;
      }

      const sessions = serverNowPlayingSessions[server.machineIdentifier];
      const sessionKeys = sessions?.sessionKeys;

      return sessionKeys?.length ?? 0;
    }
  );
}
