/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import awaitEvents from './awaitEvents';

// The promise returned by this helper:
// - Resolves with a model when a model was found and is or became ready.
// - Rejects with a model when a model was found but did not become ready.
// - Rejects with nothing when a model was not found.

export default function awaitReady(model: any) {
  return model.get('isPopulated')
    ? $.Deferred().resolve(model).promise()
    : awaitEvents({
        eventSource: model,
        events: ['change:error', 'change:isPopulated'],
      });
}
