/* eslint-plugin-disable @typescript-eslint */

import { saveExperienceSettings } from 'app/js/stores/actions/userActions';
import random from 'app/js/utils/string/random';

export default function saveHomeSettings(appModel: any, dispatch: any) {
  const experienceSettings = appModel.get('user').get('experienceSettings');
  const homeSettings = experienceSettings.get('homeSettings');

  // This identifier is changed each time `homeSettings` changes are saved such
  // that components have an easy single attribute to compare to detect changes.
  // The attribute is also useful for coupling settings with in-memory caches.
  homeSettings.set('settingsKey', random());

  dispatch(saveExperienceSettings({ homeSettings }));
}
