/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import parser2 from 'app/js/utils/parser2';

function createResponseType(typeName: any, options: any) {
  options || (options = {});

  const type = _.defaults(options, {
    typeName: typeName,
    parse: _.bind(parser2.parseType, parser2, typeName),
  });

  return type;
}

export const xml = createResponseType('xml', { accept: 'application/xml' });
export const json = createResponseType('json', { accept: 'application/json' });
// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
export const text = createResponseType('text');
