/* eslint-plugin-disable @typescript-eslint */

import createKey from 'app/js/views/v3/helpers/createKey';

export default function getSourceKey(
  sourceType: any,
  machineIdentifier: any,
  providerIdentifier: any,
  directoryID: any
) {
  return createKey(
    'source',
    sourceType,
    machineIdentifier,
    providerIdentifier,
    directoryID
  );
}
