/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import MetricsClickTracker from 'app/js/views/v3/components/core/metrics/MetricsClickTracker';
import ModalBody from 'app/js/views/v3/components/core/modals/ModalBody';
import ModalContent from 'app/js/views/v3/components/core/modals/ModalContent';
import ModalFooter from 'app/js/views/v3/components/core/modals/ModalFooter';
import ModalHeader from 'app/js/views/v3/components/core/modals/ModalHeader';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import serverDashboardImg from 'app/img/upsell/upsell-dashboard.svg';
import liveDVRImg from 'app/img/upsell/upsell-live-dvr.svg';
import mobileSyncImg from 'app/img/upsell/upsell-mobile-sync.svg';
import musicImg from 'app/img/upsell/upsell-music.svg';
import plexHomeImg from 'app/img/upsell/upsell-plex-home.svg';
import webhooksImg from 'app/img/upsell/upsell-webhooks.svg';
import PlexPassFeature from './PlexPassFeature';
import styles from './PlexPassModalContent.css';

type PlexPassModalContentProps = {
  metricsProperties?: any;
  subscribeUrl: string;
  infoUrl: string;
  plans: any[];
  isPlansPending: boolean;
  isPlansPopulated: boolean;
  plansError?: any;
  onModalClose: (...args: any[]) => any;
};

function PlexPassModalContent(props: PlexPassModalContentProps) {
  const {
    metricsProperties,
    subscribeUrl,
    infoUrl,
    plans,
    isPlansPending,
    isPlansPopulated,
    plansError,
    onModalClose,
  } = props;

  return (
    <ModalContent onModalClose={onModalClose}>
      {/* @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: any[]; className: string; }' is ... Remove this comment to see the full error message */}
      <ModalHeader className={styles.header}>
        <div className={styles.plexPassTitle}>{t('Plex Pass')}</div>
        {t('Enhance your Plex experience with exclusive premium features')}
      </ModalHeader>

      <ModalBody innerClassName={styles.body}>
        <PlexPassFeature
          description={t('Enjoy your media offline on Android and iOS devices')}
          img={mobileSyncImg}
          title={t('Downloads')}
        />
        <PlexPassFeature
          description={t(
            'Connect a tuner and antenna to record free OTA broadcasts'
          )}
          img={liveDVRImg}
          title={t('Live TV & DVR')}
        />
        <PlexPassFeature
          description={t(
            'Set up managed user accounts to create custom access restrictions'
          )}
          img={plexHomeImg}
          title={t('Parental Controls')}
        />
        <PlexPassFeature
          description={t(
            'Connect your media server to your home automation system'
          )}
          img={webhooksImg}
          title={t('Webhooks')}
        />
        <PlexPassFeature
          description={t(
            'See the play history, bandwidth, and top users on your media server'
          )}
          img={serverDashboardImg}
          title={t('Server Dashboard')}
        />
        <PlexPassFeature
          description={t('Sweet Fades, Loudness Leveling, Lyrics, and more')}
          img={musicImg}
          title={t('Premium Music Magic')}
        />
        <div className={styles.moreFeatures}>
          <Link
            className={styles.link}
            target={__EXTERNAL_LINK_TARGET__}
            to={infoUrl}
          >
            {t('...plus more exclusive features!')}
          </Link>
        </div>
      </ModalBody>

      <ModalFooter className={styles.footer}>
        {isPlansPending ? (
          <div className={styles.spinnerContainer}>
            <Spinner />
          </div>
        ) : null}
        <div
          className={classNames(
            styles.plansContainer,
            isPlansPending && styles.isPlansPending
          )}
        >
          {!isPlansPending && isPlansPopulated
            ? plans.map((plan) => (
                <MetricsClickTracker
                  key={plan.id}
                  {...metricsProperties}
                  action={metricsActions.SUBSCRIBE}
                  plan={plan.id}
                >
                  <Button
                    className={styles.planButton}
                    kind={kinds.PRIMARY}
                    size={sizes.LARGE}
                    target={__EXTERNAL_LINK_TARGET__}
                    to={path.join(subscribeUrl, {
                      plan: plan.id,
                      continue: window.location.href,
                    })}
                  >
                    {plan.formattedPrice} — {plan.name}
                  </Button>
                </MetricsClickTracker>
              ))
            : null}
          {!isPlansPending && plansError ? (
            <MetricsClickTracker
              {...metricsProperties}
              action={metricsActions.SUBSCRIBE}
              plan="unknown"
            >
              <Button
                className={styles.planButton}
                kind={kinds.PRIMARY}
                size={sizes.LARGE}
                target={__EXTERNAL_LINK_TARGET__}
                to={subscribeUrl}
              >
                {t('Subscribe')}
              </Button>
            </MetricsClickTracker>
          ) : null}
        </div>
      </ModalFooter>
    </ModalContent>
  );
}

export default PlexPassModalContent;
