/* eslint-plugin-disable @typescript-eslint */

import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';

export default function findProviderContentFeature(provider: any) {
  if (!provider || !provider.features) {
    return;
  }

  return provider.features.find(
    (feature: any) => feature.type === providerFeatureTypes.CONTENT
  );
}
