import _ from 'underscore';

// lodash-like _.omitBy
function omitBy(obj: any, predicate = _.identity) {
  // Do in-place removal of omitted items, preserving object identity
  Object.keys(obj).forEach((key) => {
    if (!predicate(obj[key])) {
      delete obj[key];
    }
  });

  return obj;
}

// lodash-like _.mapValues
function mapValues(obj: any, transform = _.identity) {
  // Do in-place mapping of values, preserving object identity

  Object.keys(obj).forEach((key) => {
    obj[key] = transform(obj[key]);
  });

  return obj;
}

// @ts-expect-error ts-migrate(7023) FIXME: 'compactDeep' implicitly has return type 'any' bec... Remove this comment to see the full error message
function compactDeep(obj: any) {
  // First remove top-level falsey values, then recurse into object values
  return mapValues(omitBy(obj), (val) =>
    _.isObject(val) ? compactDeep(val) : val
  );
}

export default compactDeep;
