/* eslint-plugin-disable @typescript-eslint */

export default function getResolution(width: any, height: any) {
  const resolutionByHeight = height ? Math.round(height / 8) * 8 : 0;
  const resolutionByWidth = width ? Math.round((width / 8) * (9 / 16)) * 8 : 0;

  return Math.max(resolutionByHeight, resolutionByWidth);
}
