/* eslint-plugin-disable @typescript-eslint */

import replaceMetadataDirectoryXMLElementNames from './replaceMetadataDirectoryXMLElementNames';
import replaceXMLElementNames from './replaceXMLElementNames';

export const METADATA_XML_ELEMENT_NAMES = [
  'Video',
  'Track',
  'Photo',
  'Playlist',
  'Game',
];

function replaceMetadataXMLElementNames(xml: any, options: any) {
  const includeDirectories = options && options.includeDirectories;
  const names = includeDirectories
    ? ['Directory', ...METADATA_XML_ELEMENT_NAMES]
    : METADATA_XML_ELEMENT_NAMES;

  const output = replaceXMLElementNames(xml, names, 'Metadata');

  if (includeDirectories) {
    return output;
  }

  return replaceMetadataDirectoryXMLElementNames(output);
}

export default replaceMetadataXMLElementNames;
