/* eslint-plugin-disable @typescript-eslint */

import eventMixin from 'app/js/utils/mixins/eventMixin';
import ServerRetryManager from './ServerRetryManager';

const MediaServerEventConnectionRetryManager = ServerRetryManager.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize({ appModel }: any) {
    // eslint-disable-next-line prefer-rest-params
    ServerRetryManager.prototype.initialize.apply(this, arguments);

    this.appModel = appModel;
    this.listenTo(
      this.appModel,
      'change:primaryServer',
      this.onPrimaryServerChange
    );
  },

  //
  // Control

  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  supportsRetry(server: any) {
    return true;
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  onPrimaryServerChange(appModel: any, primaryServer: any) {
    if (primaryServer && !primaryServer.get('hasEventConnection')) {
      // Restart the connection testing for the new primary server if
      // there isn't an established event connection to ensure the
      // retry fires immediately.

      this.stop(primaryServer);
      this.start(primaryServer);
    }
  },
});

export default MediaServerEventConnectionRetryManager;
