/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import ServerSettingModel from 'app/js/models/server/ServerSettingModel';

const ServerSettingCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ServerSettingCollection',
  context: ['server'],
  url: '/:/prefs',

  model: ServerSettingModel,

  //
  // Control

  augmentResponse(response: any) {
    if (!response.Setting) {
      return [];
    }

    const settings = Array.isArray(response.Setting)
      ? response.Setting
      : [response.Setting];

    // When PMS serializes prefs, if it gets to one that is marked as not to
    // serialize, it's too late to skip it, so it outputs <Setting/>. We
    // have no use for those, so filter them out.
    return settings.filter((setting: any) => setting.id);
  },
});

export default ServerSettingCollection;
