/* eslint-plugin-disable @typescript-eslint */

/* eslint no-console: 0  */

import AbstractModel from 'app/js/models/AbstractModel';
import assert from 'app/js/utils/log/assert';
import isPlainObject from 'app/js/utils/object/isPlainObject';

/**
 * Creates a store that is backed by an `AbstractModel`.
 *
 * The only way to change state in the model is to call `dispatch()` on the
 * store. Subscribers can listen to state changes in the model. There should
 * only be a single store in your app.
 *
 * @param {Array} actionHandlers An array of functions that can be used to mutate
 * state in the model.
 *
 * @param {AbstractModel} [model] The backing model that stores all app state.
 *
 * @returns {Store} A store that lets you read state, dispatch actions and
 * subscribe to changes.
 */
export default function createStore(actionHandlers: any, model: any) {
  __DEV__ &&
    assert(
      model instanceof AbstractModel,
      "A store's model must extend `AbstractModel`"
    );

  let currentListeners: any = [];
  let nextListeners = currentListeners;
  let currentActionHandlers: any;

  replaceActionHandlers(actionHandlers);

  function ensureCanMutateNextListeners() {
    if (nextListeners === currentListeners) {
      nextListeners = currentListeners.slice();
    }
  }

  /**
   * Reads the state tree managed by the store.
   *
   * @returns {any} The current state tree of your application.
   */
  function getState() {
    return model.getState();
  }

  /**
   * Adds a change listener. It will be called any time some part of the state
   * tree has changed. You may then call `getState()` to read the current state
   * tree inside the callback.
   *
   * @param {Function} listener A callback to be invoked on every dispatch.
   *
   * @returns {Function} A function to remove this change listener.
   */
  function subscribe(listener: any) {
    __DEV__ &&
      assert(
        typeof listener === 'function',
        'Expected listener to be a function'
      );

    let isSubscribed = true;

    ensureCanMutateNextListeners();
    nextListeners.push(listener);

    return function unsubscribe() {
      if (!isSubscribed) {
        return;
      }

      isSubscribed = false;

      ensureCanMutateNextListeners();
      const index = nextListeners.indexOf(listener);
      nextListeners.splice(index, 1);
    };
  }

  /**
   * Dispatches an action. It is the only way to trigger a state change through
   * the store.
   *
   * An action handler that matches the action type will be invoked and passed
   * the backing model, the action's payload, and the dispatch function. The
   * action handler can mutate state anywhere in the model and `statechange`
   * events will bubble up to the store and subscribers will be notified. The
   * dispatch function passed to the action handler can be used to dispatch
   * additional actions.
   *
   * @param {Object} action A plain object representing “what changed”. It is a
   * good idea to keep actions serializable. An action must have a `type`
   * property. It is a good idea to use string constants for action types.
   *
   * @returns {Object} For convenience, the same action object you dispatched.
   */
  function dispatch(action: any) {
    const { payload, type } = action;

    const handlerOrHandlers = type && currentActionHandlers[type];

    __DEV__ && assert(isPlainObject(action), 'Actions must be plain objects');
    __DEV__ &&
      assert(
        action.type != null,
        'Actions must have a "type" property. Have you misspelled a constant?'
      );

    if (__DEV__) {
      const getDevOption = require('app/js/utils/dev/getDevOption').default;

      if (getDevOption('stateLogs')) {
        console.groupCollapsed(`Dispatched "${type}"`);
        console.log('%c PAYLOAD:', 'font-weight: bold', payload);
        console.log(
          '%c HANDLERS:',
          'font-weight: bold',
          Array.isArray(handlerOrHandlers) ? handlerOrHandlers.length : 1
        );
        console.groupCollapsed('Dispatched from:');
        console.trace();
        console.groupEnd();
        console.groupEnd();
      }
    }

    if (Array.isArray(handlerOrHandlers)) {
      for (let i = 0, len = handlerOrHandlers.length; i < len; i++) {
        handlerOrHandlers[i](model, payload, dispatch);
      }
    } else if (handlerOrHandlers) {
      handlerOrHandlers(model, payload, dispatch);
    } else {
      model.dispatch(action);
    }

    return action;
  }

  /**
   * Replaces the action handlers currently registered to the store. You might
   * need this if you implement a hot reloading mechanism.
   *
   * @param {Function} nextActionHandlers The action handlers for the store to
   * use instead.
   *
   * @returns {void}
   */
  function replaceActionHandlers(nextActionHandlers: any) {
    __DEV__ &&
      assert(
        Array.isArray(nextActionHandlers),
        'Action handlers must be contained within an array'
      );

    currentActionHandlers = groupActionHandlersByType(nextActionHandlers);
  }

  /**
   * Transforms a map of callbacks by action type into a single coalesced map of
   * callbacks by type.
   *
   * @param  {Object[]} An array of action handler maps by action type.
   *
   * @return {Object} The coalesced map of all action handlers by type.
   */
  function groupActionHandlersByType(allActionHandlers: any) {
    const handlersByType = {};

    for (let i = 0, len = allActionHandlers.length; i < len; i++) {
      const handlers = allActionHandlers[i];

      for (const type in handlers) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        const handlerOrHandlers = handlersByType[type];
        const handler = handlers[type];

        if (handlerOrHandlers) {
          if (Array.isArray(handlerOrHandlers)) {
            handlerOrHandlers.push(handler);
          } else {
            // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
            handlersByType[type] = [handlerOrHandlers, handler];
          }
        } else {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          handlersByType[type] = handler;
        }
      }
    }

    return handlersByType;
  }

  /**
   * Listen for state changes in the model and update subscribers.
   *
   * @returns {void}
   */
  function onStoreModelStateChange() {
    const listeners = (currentListeners = nextListeners);

    for (let i = 0, len = listeners.length; i < len; i++) {
      listeners[i]();
    }
  }

  model.on('statechange', onStoreModelStateChange);

  return {
    dispatch,
    subscribe,
    getState,
    replaceActionHandlers,
  };
}
