/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseClass from 'app/js/utils/BaseClass';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const Cache = BaseClass.extend({
  //
  // Properties

  mixins: eventMixin,

  maxCycles: 3,
  maxSize: 50,

  isListening: false,

  //
  // Init

  initialize: function () {
    this._cache = {};
    this.size = 0;
    this.cycles = [];

    this.cycle();
  },

  startListening: function () {
    if (this.isListening) {
      return;
    }

    this.isListening = true;

    this.listenTo(dispatcher, {
      'cycle:cache': this.cycle,
      'clearWhere:cache': this.clearWhere,
      'clearAll:cache': this.clearAll,
      'clearCycles:cache': this.clearCycles,
    });
  },

  //
  // Control

  get: function (key: any) {
    const item = this._cache[key];

    // Move the item to the first cycle when it is retrieved.
    if (item && item._cacheCycle) {
      this.setItemCycle(item, key, _.first(this.cycles));
    }

    return item;
  },

  store: function (item: any, key: any) {
    key || (key = this.getItemCacheKey(item));

    // If the item already exists in the cache, remove it before storing it
    // again with an updated cache key.
    this.remove(item);

    if (!key) {
      return;
    }

    // Only start cycling and clearing the cache once an item has been added
    // to the cache.
    this.startListening();

    item._cacheKey = key;
    this._cache[key] = item;
    this.size++;

    if (!item.persistInCache) {
      this.setItemCycle(item, key, _.first(this.cycles));
    }
  },

  getItemCacheKey: function (item: any) {
    return _.result(item, 'cacheKey');
  },

  setItemCycle: function (item: any, key: any, cycle: any) {
    const previous = item._cacheCycle;

    if (previous && previous.hasOwnProperty(key)) {
      delete previous[key];
    }

    item._cacheCycle = cycle;
    cycle[key] = item;
  },

  cycle: function () {
    // If the cache has overflowed, remove everything except for items
    // stored in the newest cycle.
    if (this.size > this.maxSize) {
      log.warn('Cache overflow, size: ' + this.size);
      _.each(this.cycles.slice(1), this.removeCycle, this);
    }

    // Start the next cycle.
    this.cycles.unshift({});

    // If we have reached our cycles limit, remove the oldest cycle.
    if (this.cycles.length > Math.max(this.maxCycles, 1)) {
      this.removeCycle(_.last(this.cycles));
    }
  },

  remove: function (item: any) {
    if (!item || !item._cacheKey) {
      return;
    }

    const key = item._cacheKey;

    if (this._cache.hasOwnProperty(key)) {
      delete this._cache[key];
      this.size--;
    }

    if (item._cacheCycle) {
      if (item._cacheCycle.hasOwnProperty(key)) {
        delete item._cacheCycle[key];
      }

      delete item._cacheCycle;
    }

    delete item._cacheKey;
  },

  removeCycle: function (cycle: any) {
    _.each(_.values(cycle), this.remove, this);

    const i = _.indexOf(this.cycles, cycle);
    this.cycles.splice(i, 1);
  },

  clearWhere: function (attrs: any) {
    const matchingItems = _.where(_.values(this._cache), attrs);

    _.each(matchingItems, this.remove, this);
  },

  clearAll: function () {
    _.each(_.values(this._cache), this.remove, this);

    this.cycles.length = 0;
    this.cycle();

    this.stopListening();
    this.isListening = false;
  },

  clearCycles: function () {
    const cycles = this.cycles;

    while (cycles.length) {
      this.removeCycle(cycles[cycles.length - 1]);
    }

    this.cycle();
  },
});

export default Cache;
