/* eslint-plugin-disable @typescript-eslint */

import MediaServerCompanionProxy from 'app/js/adapters/MediaServerCompanionProxy';
import { registerAll } from 'app/js/adapters/adapters';
import createCompanionVote from 'app/js/adapters/createCompanionVote';
import desktopAdapters from 'app/js/adapters/desktopAdapters';
import baseDesktopAdaptersInitializer from 'app/js/initializers/baseDesktopAdaptersInitializer';
import DesktopAppModel from 'app/js/models/DesktopAppModel';
import SessionStatsModel from 'app/js/models/SessionStatsModel';
import CloudServerFlexModel from 'app/js/models/server/CloudServerFlexModel';
import ServerFlexModel from 'app/js/models/server/ServerFlexModel';
import StoredUserExperienceSettingsModel from 'app/js/models/storage/StoredUserExperienceSettingsModel';
import UserExperienceSettingsModel from 'app/js/models/user/UserExperienceSettingsModel';
import UserFlexModel from 'app/js/models/user/UserFlexModel';
import createAppStore from 'app/js/stores/createAppStore';

export default function desktopAdaptersInitializer() {
  //
  // Flex Models

  registerAll({
    CloudServerFlexModel,
    ServerFlexModel,
    UserFlexModel,
  });

  //
  // User Settings

  registerAll({
    UserExperienceSettingsModel,
    StoredUserExperienceSettingsModel,
  });

  //
  // App Model

  const appModel = new DesktopAppModel();
  const appStore = createAppStore(appModel);

  registerAll({
    appModel,
    appStore,
  });

  //
  // Platform implementations

  registerAll({
    companionProxy: new MediaServerCompanionProxy(),
    companionVote: createCompanionVote(),
    sessionStats: new SessionStatsModel(),
  });

  return baseDesktopAdaptersInitializer().then(desktopAdapters.registerAll);
}
