import $ from 'jquery';
import _ from 'underscore';
import BaseModal from 'app/js/views/modals/BaseModal';

const SelectModal = BaseModal.extend({
  //
  // Properties

  className: 'select-modal modal modal-sm fade',

  events: _.extend(
    {
      'click a': 'onClick',
    },
    BaseModal.prototype.events
  ),

  //
  // Init

  initialize: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);

    this.promise = $.Deferred();
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);
  },

  onHide: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onHide.apply(this, arguments);

    this.promise.reject();
  },

  onClick: function (event: any) {
    event.preventDefault();

    this.promise.resolve($(event.currentTarget).data('value'));
    this.hide();
  },
});

export default SelectModal;
