/* eslint-plugin-disable @typescript-eslint */

import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import createIsEmbeddedServerSelector from 'app/js/stores/selectors/server/createIsEmbeddedServerSelector';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import selectDismissedUnclaimedServers from 'app/js/stores/selectors/settings/selectDismissedUnclaimedServers';
import capabilities from 'app/js/utils/device/capabilities';
import ServerPage from 'app/js/views/v3/components/pages/ServerPage';

function createMapStateToProps() {
  return createSelector(
    (appState) => appState.isWaitingForServers,
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState, { hideUnclaimedPage }) => hideUnclaimedPage,
    createUnstableServerSelector(),
    createIsEmbeddedServerSelector(),
    selectDismissedUnclaimedServers,
    (
      isWaitingForServers,
      hideUnclaimedPage,
      server,
      isEmbeddedServer,
      dismissedUnclaimedServers
    ) => {
      if (!server) {
        return {
          hasServer: false,
          showSpinner: isWaitingForServers,
        };
      }
      return {
        hasServer: true,
        showSpinner:
          !server.isConnected &&
          !server.isUnauthorized &&
          !server.isUnavailable,
        machineIdentifier: server.machineIdentifier,
        isUnauthorized: server.isUnauthorized,
        isUnavailable: server.isUnavailable,
        isOutOfDate: server.isOutOfDate,
        isInstallingServerUpdate: server.isInstallingServerUpdate,
        isUnclaimed:
          server.isConnected &&
          !server.isClaimed &&
          !dismissedUnclaimedServers[server.machineIdentifier] &&
          !isEmbeddedServer &&
          !hideUnclaimedPage,
        hasHttpsConnections: server.hasHttpsConnections,
        isHostedHttps: capabilities.isHostedHttps,
      };
    }
  );
}
const ServerPageConnector = connect(createMapStateToProps)(ServerPage);
(ServerPageConnector as any).propTypes = {
  ...(ServerPageConnector as any).propTypes,
  serverEntityID: PropTypes.string.isRequired,
};
export default ServerPageConnector;
