/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

// Augment range object to full set of properties:
// start, end, size, last
function augment(range: any) {
  const r = _.clone(range);

  // Assuming range.start to be present
  if (typeof r.last !== 'undefined') {
    r.end = r.last + 1;
    r.size = r.end - r.start;
  } else if (typeof r.size !== 'undefined') {
    r.end = r.start + r.size;
    r.last = r.end - 1;
  } else if (typeof r.end !== 'undefined') {
    r.last = r.end - 1;
    r.size = r.end - r.start;
  }

  return r;
}

// Augment and filter out invalid and/or empty ranges
function fixResult(result: any, allowEmpty: any) {
  return _.filter(_.map(result, augment), function (range) {
    if (allowEmpty) {
      return range.size >= 0; // Range is invalid if size < 0
    } else {
      return range.size > 0;
    }
  });
}

const rangeUtil = {
  augment: augment,

  difference: function (a: any, b: any, allowEmpty: any) {
    a = augment(a);
    b = augment(b);

    const result = [
      {
        start: a.start,
        last: Math.min(a.last, b.start - 1),
      },
      {
        start: Math.max(a.start, b.end),
        last: a.last,
      },
    ];

    return fixResult(result, allowEmpty);
  },

  union: function (a: any, b: any, allowEmpty: any) {
    a = augment(a);
    b = augment(b);

    if (a.start > b.start) {
      // Swapping a and b to make a the first in line
      const c = a;
      a = b;
      b = c;
    }

    const result = [a];

    if (b.start <= a.end) {
      result[0].last = Math.max(b.last, a.last);
    } else {
      result.push(b);
    }

    return fixResult(result, allowEmpty);
  },

  degap: function (range: any, targetRange: any) {
    let result = augment(range);
    targetRange = augment(targetRange);

    // Ignore target range if it's empty
    if (!targetRange.size) {
      return result;
    }

    if (result.start > targetRange.end) {
      result = {
        start: targetRange.end,
        end: result.end,
      };
    }
    if (result.end < targetRange.start) {
      result = {
        start: result.start,
        end: targetRange.start,
      };
    }

    return augment(result);
  },

  each: function (range: any, iterator: any, context: any) {
    range = augment(range);

    let start = Math.min(range.start, range.end);
    const end = Math.max(range.start, range.end);

    for (; start <= end; start++) {
      iterator.call(context, start);
    }
  },

  roundByPage: function (range: any, pageSize: any) {
    range = augment(range);

    return augment({
      start: Math.floor(range.start / pageSize) * pageSize,
      end: Math.ceil(range.end / pageSize) * pageSize,
    });
  },
};

export default rangeUtil;
