/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import log from 'app/js/utils/log/log';
import xmlWriter from 'app/js/utils/xmlWriter';

//
// Helpers

function createResponse(commandID: any, code: any, status: any) {
  const body = xmlWriter.writeXml({
    Response: {
      _code: code,
      _status: status,
    },
  });
  return {
    commandID,
    body,
    responseCode: code,
  };
}

function noticeInputActivity() {
  const appModel = resolve('appModel');
  const inputActivity = appModel.get('inputActivity');
  // The input activity model only exists on TV platforms.
  if (inputActivity) {
    inputActivity.notice();
  }
}

// @ts-expect-error ts-migrate(6133) FIXME: 'query' is declared but its value is never read.
function populateProviders(serverModel: any, query: any) {
  const appModel = resolve('appModel');
  const userModel = appModel.get('user');
  const providers =
    serverModel.get('isCloud') && !serverModel.get('isTransient')
      ? userModel.get('providers')
      : serverModel.get('providers');
  serverModel.set({
    isProvidersPending: true,
    isProvidersPopulated: false,
    providersError: null,
  });
  const promise = providers.populate();
  return promise.then(
    () => {
      serverModel.set({
        isProvidersPending: false,
        isProvidersPopulated: true,
      });
      return promise;
    },
    () => {
      serverModel.set({
        isProvidersPending: false,
        providersError: {},
      });
      return promise;
    }
  );
}

export default function handleLocalCompanionCommand(command: any) {
  const { path, query, commandID, supportsTimeline } = command;
  const deferred = $.Deferred();
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  const handler = handlers[path];
  noticeInputActivity();
  if (supportsTimeline !== false) {
    // If the app is reloaded we might not get a new subscribe command. This
    // considers a companion signal as a sign that there is a controller out
    // there listening.
    resolve('localCompanionTimelineManager').start();
  }
  if (handler) {
    const promise = handler(query) || $.Deferred().resolve().promise();
    promise
      .done(() => {
        log(`[Companion] Successfully processed command from path "${path}"`);
        const controllerResponse = createResponse(commandID, 200, 'OK');
        deferred.resolve({ controllerResponse });
      })
      .fail(() => {
        log.warn(`[Companion] Failed to process command from path "${path}"`);
        const controllerResponse = createResponse(
          commandID,
          400,
          'Bad Request'
        );
        deferred.resolve({ controllerResponse });
      });
  } else {
    log.warn(
      `[Companion] Unable to handle command from unknown path "${path}"`
    );
    const controllerResponse = createResponse(commandID, 400, 'Bad Request');
    deferred.resolve({ controllerResponse });
  }
  return deferred.promise();
}
