/* eslint-plugin-disable @typescript-eslint */

// We should only match when there is an appropriate server-level equivalent.
// The list of server level hubs available are here:
// https://github.com/plexinc/plex-media-server/blob/a28c9f74a3c03a09ff083a849e4a36897b71f934/Hubs/HubsRequestHandler.cpp#L1856-L1863
const patternsByGlobalIdentifier = {
  'home.continue': [/^(movie|tv)\.inprogress\.([0-9]+)/],
  'home.movies.recent': [/^movie\.recentlyadded\.([0-9]+)/],
  'home.music.recent': [/^music\.recent\.added\.([0-9]+)/],
  'home.photos.recent': [/^photo\.recent\.([0-9]+)/],
  'home.ondeck': [/^tv\.ondeck\.([0-9]+)/],
  'home.television.recent': [/^tv\.recentlyadded\.([0-9]+)/],
  'home.videos.recent': [/^video\.recent\.([0-9]+)/],
};

function patternMatches(hubIdentifier: any, patterns: any) {
  return patterns.some((pattern: any) => pattern.test(hubIdentifier));
}

export default function toHomeHubIdentifier(hubIdentifier: any) {
  for (const globalIdentifier in patternsByGlobalIdentifier) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const patterns = patternsByGlobalIdentifier[globalIdentifier];

    if (patternMatches(hubIdentifier, patterns)) {
      return globalIdentifier;
    }
  }

  return hubIdentifier;
}
