/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import BackgroundTranscodeJobCollection from 'app/js/models/library/convert/BackgroundTranscodeJobCollection';
import SectionCollection from 'app/js/models/library/section/SectionCollection';
import PlexPlayerCollection from 'app/js/models/player/PlexPlayerCollection';
import ActivityCollection from 'app/js/models/server/ActivityCollection';
import NeighborhoodDeviceCollection from 'app/js/models/server/NeighborhoodDeviceCollection';
import ServerAlertCollection from 'app/js/models/server/ServerAlertCollection';
import ServerCloudAccountModel from 'app/js/models/server/ServerCloudAccountModel';
import ServerSettingCollection from 'app/js/models/server/ServerSettingCollection';
import ServerUpdateModel from 'app/js/models/server/ServerUpdateModel';
import TranscodeJobCollection from 'app/js/models/server/TranscodeJobCollection';

const ServerFlexModel = AbstractModel.extend({
  //
  // Properties

  name: 'ServerFlexModel',
  context: ['server'],

  sessionAttrs: {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
    alerts: attrTypes.instanceOf(ServerAlertCollection).isRequired,

    hasiTunes: attrTypes.boolean,
    isHasiTunesPopulated: attrTypes.boolean,

    isSettingsPending: attrTypes.boolean,
    isSettingsPopulated: attrTypes.boolean,
    settingsError: attrTypes.object,

    isLibrariesPending: attrTypes.boolean.isRequired,
    isLibrariesPopulated: attrTypes.boolean.isRequired,
    librariesError: attrTypes.object,

    cleanBundlesError: attrTypes.string,
    optimizeDatabaseError: attrTypes.string,
  },

  sessionRelations: {
    settings: {
      relationClass: ServerSettingCollection,
      rootPropertyName: 'Setting',
    },

    cloudAccount: {
      relationClass: ServerCloudAccountModel,
    },

    libraries: {
      relationClass: SectionCollection,
    },

    activities: {
      relationClass: ActivityCollection,
    },

    transcodeJobs: {
      relationClass: TranscodeJobCollection,
    },

    backgroundTranscodeJobs: {
      relationClass: BackgroundTranscodeJobCollection,
    },

    players: {
      relationClass: PlexPlayerCollection,
    },

    neighborhoodDevices: {
      relationClass: NeighborhoodDeviceCollection,
    },

    update: {
      relationClass: ServerUpdateModel,
    },
  },

  defaults() {
    return {
      alerts: new ServerAlertCollection([], { server: this.server }),

      hasiTunes: false,
      isHasiTunesPopulated: false,

      isSettingsPending: true,
      isSettingsPopulated: false,
      settingsError: null,

      isLibrariesPending: true,
      isLibrariesPopulated: false,
      librariesError: null,

      cleanBundlesError: null,
      optimizeDatabaseError: null,
    };
  },
});

export default ServerFlexModel;
