/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import parser from 'app/js/utils/parser';
import t from 'app/js/utils/t';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function uninstallChannelCommand(options: any) {
  const deferred = $.Deferred();
  const channel = options.channel;
  const identifier = channel.get('identifier');
  const urlRoot = identifier
    ? '/system/appstore/apps/' + identifier
    : channel.get('key');
  const url = urlRoot + '/uninstall';
  const xhrOptions = channel.server.getXHROptions(url);

  function uninstall() {
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(function (response) {
      const json = _.isObject(response) ? response : parser.parseXML(response);

      channel.set('installed', false);

      if (json.message && !options.skipConfirmation) {
        dispatcher.trigger('show:alert', {
          type: 'success',
          message: json.message,
        });
      }

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr) {
      // A 400 response indicates that this channel does not exist in `self.system.storeservice.plugins`
      // and can thus not be uninstalled. This includes iLife channels.
      // Display a popup explaining that these channels may be disabled in the settings instead.
      // See https://github.com/plexinc/plex-media-server/issues/3297

      if (xhr.status === 400) {
        const confirmModal = new ConfirmModal({
          title: t('Unable to Uninstall Plugin'),
          message: t(
            'This plugin could not be uninstalled, but you can disable it in the server settings.'
          ),
          confirmLabel: t('OK'),
          cancelLabel: t('Ignore'),
        });

        confirmModal.promise.done(function () {
          navigateToRoute('serverSettings', {
            machineIdentifier: channel.server.id,
            settingsSection: 'settings',
            settingsGroup: 'channels',
          });
        });

        dispatcher.trigger('show:modal', confirmModal);
      } else {
        dispatcher.trigger('show:alert', {
          type: 'error',
          message: t('This plugin could not be uninstalled.'),
        });
      }

      deferred.reject(xhr);
    });
  }

  if (options.skipConfirmation) {
    uninstall();
  } else {
    const confirmModal = new ConfirmModal({
      title: t('Uninstall Plugin'),
      message: t('Are you sure you want to uninstall this plugin?'),
    });

    const confirmPromise = confirmModal.promise;
    confirmPromise.done(uninstall);
    confirmPromise.fail(function () {
      deferred.reject();
    });

    dispatcher.trigger('show:modal', confirmModal);
  }

  return deferred.promise();
}

export default uninstallChannelCommand;
