/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import t from 'app/js/utils/t';
import Spinner from 'app/js/views/v3/components/core/base/Spinner';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import colors from 'app/styles/variables/colors';
import Button, { ButtonProps } from './Button';
import styles from './SpinnerButton.css';

interface SpinnerButtonProps extends Partial<ButtonProps> {
  className?: string;
  children?: React.ReactNode;
  isDisabled?: boolean;
  isSpinning?: boolean;
  loadingLabel?: string;
  spinnerSize?: any; // TODO: PropTypes.oneOf(sizes.all)
  spinnerColor?: string;
}

function SpinnerButton({
  children,
  className,
  loadingLabel = t('Loading'),
  spinnerSize = sizes.SMALL,
  spinnerColor = colors.defaultColor,
  isDisabled = false,
  isSpinning = false,
  ...props
}: SpinnerButtonProps) {
  return (
    <Button
      className={classNames(
        className,
        styles.button,
        isSpinning && styles.isSpinning
      )}
      isDisabled={isDisabled || isSpinning}
      {...props}
    >
      <span className={styles.spinnerContainer}>
        <Spinner
          className={styles.spinner}
          color={spinnerColor}
          loadingLabel={loadingLabel}
          size={spinnerSize}
        />
      </span>
      <span className={styles.label}>{children}</span>
    </Button>
  );
}

export default SpinnerButton;
