/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import DesktopSettingsModel from 'app/js/models/DesktopSettingsModel';
import DeviceSettingsModel from 'app/js/models/settings/DeviceSettingsModel';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import * as jsonStorage from 'app/js/utils/storage/jsonStorage';

const movedAttrs = [
  'chromecastAppID',
  'chromecastCustomAppID',
  'debugLevel',
  'directPlay',
  'directStream',
  'rejectDirectConnections',
  'serverUpdateDismissedVersion',
];

// Handles the migration of attributes from appModel.settings to
// appModel.deviceSettings

function migrateSettingsToDeviceSettings() {
  const settingsStorageKey = DesktopSettingsModel.prototype.storageKey;
  const deviceSettingsStorageKey = DeviceSettingsModel.prototype.storageKey;

  const promise = jsonStorage
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    .getJSON(settingsStorageKey)
    .then((existingSettings: any) => {
      const migratedSettings = _.pick(existingSettings, movedAttrs);

      if (Object.keys(migratedSettings).length === 0) {
        return;
      }

      return jsonStorage
        .getJSON(deviceSettingsStorageKey, {})
        .then((originalDeviceSettings: any) => {
          const updatedDeviceSettings = {
            ...originalDeviceSettings,
            ...migratedSettings,
          };

          return jsonStorage.setJSON(
            deviceSettingsStorageKey,
            updatedDeviceSettings
          );
        })
        .then(() => {
          // Complete the move by deleting the original attrs
          return jsonStorage.setJSON(
            settingsStorageKey,
            _.omit(existingSettings, movedAttrs)
          );
        });
    });

  return alwaysResolve(promise);
}

export default migrateSettingsToDeviceSettings;
