/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import {
  CONTENT_RATING,
  EXCLUDE_CONTENT_RATING,
  EXCLUDE_LABEL,
  LABEL,
} from 'app/js/utils/constants/filterOptionTagTypes';

function createBoolRestrictionAttrType(attrKey: any) {
  return {
    type: attrTypes.boolean,
    save() {
      return (this as any).has(attrKey);
    },
  };
}
function createFilterAttrType(attrKey: any) {
  return {
    type: attrTypes.queryString,
    separators: ['|', '&'],
    orFields: [CONTENT_RATING, LABEL],
    andFields: [EXCLUDE_CONTENT_RATING, EXCLUDE_LABEL],
    save() {
      return (this as any).has(attrKey);
    },
  };
}
const sharingSettingsAttrs = {
  allowTuners: {
    type: attrTypes.integer,
    save() {
      return (this as any).has('allowTuners');
    },
  },
  allowSync: createBoolRestrictionAttrType('allowSync'),
  allowChannels: createBoolRestrictionAttrType('allowChannels'),
  filterMovies: createFilterAttrType('filterMovies'),
  filterTelevision: createFilterAttrType('filterTelevision'),
  filterMusic: createFilterAttrType('filterMusic'),
  filterPhotos: createFilterAttrType('filterPhotos'),
};
export default sharingSettingsAttrs;
