/* eslint-plugin-disable @typescript-eslint */

import isValid from 'date-fns/isValid';

function findIndexByTime(lines: any, formattedTime: any, index: any) {
  if (!formattedTime) {
    return null;
  }

  const date = new Date(formattedTime);

  if (!isValid(date)) {
    return -1;
  }

  const time = date.getTime();

  let minIndex = 0;
  let maxIndex = lines.length - 1;
  let currentIndex;

  while (minIndex <= maxIndex) {
    currentIndex = Math.floor((minIndex + maxIndex) / 2);

    const lineA = lines[currentIndex];
    const lineB = lines[currentIndex + 1];

    const lineTimeA = new Date(lineA.time).getTime();

    if (!lineB) {
      return time > lineTimeA ? currentIndex : currentIndex + 1;
    }

    if (time === lineTimeA) {
      return index < currentIndex ? currentIndex : currentIndex + 1;
    }

    const lineTimeB = new Date(lineB.time).getTime();

    if (time > lineTimeA && time < lineTimeB) {
      return currentIndex + 1;
    }

    if (time > lineTimeA) {
      minIndex = currentIndex + 1;
    } else if (time < lineTimeA) {
      maxIndex = currentIndex - 1;
    }
  }

  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'number | undefined' is not assig... Remove this comment to see the full error message
  return Math.max(0, currentIndex);
}

export function findFirstIndexByTime(lines: any, time: any) {
  return findIndexByTime(lines, time, -Infinity);
}

export function findLastIndexByTime(lines: any, time: any) {
  return findIndexByTime(lines, time, Infinity);
}
