/* eslint-plugin-disable @typescript-eslint */
/* eslint-disable prefer-rest-params */

import $ from 'jquery';
import _ from 'underscore';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import populateMixin from 'app/js/mixins/models/populateMixin';
import ModelCache from 'app/js/utils/cache/ModelCache';
import assert from 'app/js/utils/log/assert';
import parser from 'app/js/utils/parser';
import path from 'app/js/utils/url/path';

// DEPRECATED: Use `AbstractModel`

const BaseModel = Backbone.Model.extend({
  //
  // Properties

  mixins: [populateMixin],
  cache: new ModelCache(),

  //
  // Init

  constructor: function (attributes: any, options: any) {
    attributes || (attributes = {});
    options || (options = {});

    // For all models associated with a higher level models, try to store a
    // reference to those models.
    this.server = this.findOption(options, 'server');
    this.section = this.findOption(options, 'section');
    this.channel = this.findOption(options, 'channel');
    this.device = this.findOption(options, 'device');
    this.parent = this.findOption(options, 'parent');

    const skipCache = this.findOption(options, 'skipCache');

    if (skipCache) {
      Backbone.Model.prototype.constructor.apply(this, arguments);
      return;
    }

    // If this instance is already in the cache, return the cached instance
    // instead of constructing a new instance.
    const cacheKey = this.cache.getItemCacheKey(this, attributes);
    const cached = this.cache.get(cacheKey);

    if (cached) {
      // Presumably these attributes were just fetched, so assume they're
      // fresher than what we have cached. Update the cached instance and
      // return it.

      // If we need to parse the attributes, do that before setting them on
      // the cached instance.
      if (options.parse) {
        attributes = cached.parse(attributes);
      }

      if (
        this.preserveCachedAttributes &&
        this.preserveCachedAttributes(options.url)
      ) {
        // Only overwrite undefined cached attributes if request model has
        // fresher defined values.
        _.each(attributes, function (value, attribute) {
          if (_.isUndefined(cached.get(attribute)) && !_.isUndefined(value)) {
            cached.set(attribute, value);
          }
        });
      } else {
        cached.set(attributes);
      }

      // Standalone models that skip the cache can end up overwriting these
      // properties if they would normally get them from their collection.
      // Make sure any models that should be associated with this instance
      // are applied to the cached model.
      cached.server = this.server || cached.server;
      cached.section = this.section || cached.section;
      cached.channel = this.channel || cached.channel;
      cached.device = this.device || cached.device;
      cached.parent = this.parent || cached.parent;

      if (options.collection) {
        cached.collection = options.collection;
      }

      return cached;
    }

    const onlyIfCached = this.findOption(options, 'onlyIfCached');

    if (onlyIfCached) {
      return {};
    }

    // Not in the cache, so go ahead and use the original constructor,
    // adding the result to the cache. Apply the arguments object here since
    // attributes may have been mucked with by the parse method without us
    // explicitly wanting that.
    Backbone.Model.prototype.constructor.apply(this, arguments);

    // Store the instance in the cache after the constructor so the cacheKey
    // function can use properties on the model instead of having to parse
    // through the attributes.
    this.cache.store(this, cacheKey);
  },

  //
  // Control

  findOption: function (options: any, option: any) {
    return (
      options[option] || (options.collection && options.collection[option])
    );
  },

  // Override clone to pass in our custom options.
  clone: function () {
    const options = {
      server: this.server,
      section: this.section,
      channel: this.channel,
      parent: this.parent,
      skipCache: true,
    };

    // Opera has some problems returning from a constructor when Dragonfly isn't running.
    // The || null seems to  be sufficient to stop it misbehaving. Found by ExtJS team.
    return new this.constructor(this.attributes, options) || null;
  },

  convert: function (xml: any) {
    const json = _.isObject(xml) ? xml : parser.parseXML(xml);

    return json;
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
  parse: function (response: any, xhr: any) {
    let json = this.convert(response);

    if (this.whitelistedAttrs) {
      json = _.pick(json, this.whitelistedAttrs);
    }

    return json;
  },

  // Largely copied from Backbone.Model.save. The main difference is that
  // Backbone always wants to send a complete representation of the model
  // and read a response with the updated state. We (usually) only send what
  // has changed, and put the parameters in the query string. The body is
  // also usually empty.

  save: function (key: any, value: any, options: any) {
    let attrs: any;
    let method;
    const attributes = this.attributes;
    // Handle both `"key", value` and `{key: value}` -style arguments.
    if (key == null || typeof key === 'object') {
      attrs = key;
      options = value;
    } else {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      (attrs = {})[key] = value;
    }

    options = _.extend({ validate: true }, options);

    // Get the save parameters for attributes that have changed.
    const data = this.getSaveParams(attrs, options);
    _.extend(data, options.data);

    // If we're not waiting and attributes exist, save acts as
    // `set(attr).save(null, opts)` with validation. Otherwise, check if the
    // model will be valid when the attributes, if any, are set.
    if (attrs && !options.wait) {
      if (!this.set(attrs, options)) {
        return false;
      }
    } else if (!this._validate(attrs, options)) {
      return false;
    }

    // Set temporary attributes if `{wait: true}`.
    if (attrs && options.wait) {
      this.attributes = _.extend({}, attributes, attrs);
    }

    // If we ended up with nothing to send, give up. We'll treat it as a
    // success in terms of callbacks though.
    if (_.isEmpty(data) && !options.force) {
      if (options.success) {
        options.success(this);
      }

      return $.Deferred().resolve(this);
    }

    // After a successful server-side save, the client is (optionally)
    // updated with the server-side state.
    if (options.parse === undefined) {
      options.parse = true;
    }

    const self = this;
    const success = options.success;

    // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
    options.success = function (response: any) {
      // Ensure attributes are restored during synchronous saves.
      self.attributes = attributes;

      if (response) {
        let serverAttrs = self.parse(response, options);

        if (options.wait) {
          serverAttrs = _.extend(attrs || {}, serverAttrs);
        }

        if (_.isObject(serverAttrs) && !self.set(serverAttrs, options)) {
          return false;
        }

        // Refresh the cache key in case part of the key has changed.
        self.cache.store(self);
      }

      if (success) {
        success(self, response, options);
      }

      self.trigger('sync', self, response, options);
    };
    Backbone.wrapError(this, options);

    if (this.isNew()) {
      method = 'create';
    } else {
      method = options.patch ? 'patch' : 'update';
    }

    if (method === 'patch') {
      options.attrs = attrs;
    }

    // Add the save parameters to the query string part of the url.
    const url = _.result(this, 'saveUrl') || _.result(this, 'url');

    if (!_.isEmpty(data)) {
      options.url = path.join(url, data);
    }

    options.data = '';

    const xhr = this.sync(method, this, options);

    // Restore attributes.
    if (attrs && options.wait) {
      this.attributes = attributes;
    }

    return xhr;
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  getSaveParams: function (data: any, options: any) {
    if (this.isNew()) {
      return data || this.toJSON();
    } else {
      return this.changedAttributes(data) || {};
    }
  },

  getScopedKey: function (key: any) {
    if (key != null && _.isFunction(this.cacheKey)) {
      return this.cacheKey(this.attributes) + '!' + key;
    } else {
      return key;
    }
  },

  restoreDefaults: function (options: any) {
    const defaults = _.result(this, 'defaults');

    if (!defaults) {
      return this.clear(options);
    }

    const attributes = this.attributes;
    let attr;

    for (attr in attributes) {
      if (!(attr in defaults)) {
        this.unset(attr, options);
      }
    }

    this.set(defaults, options);

    return this;
  },

  startCapturingChangeEvents: function () {
    if (this.releaseCapturedChangeEvents) {
      assert(false, 'This model is already capturing events');
      return;
    }

    const originalSet = this.set;
    let changes = {};

    this.set = function (key: any, value: any, options: any) {
      let attrs;

      // Handle both `"key", value` and `{key: value}` -style arguments.
      if (typeof key === 'object') {
        attrs = key;
        options = value;
      } else {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        (attrs = {})[key] = value;
      }

      // Silently set the attributes so the model is updated without
      // actually triggering the change events.
      const silentOptions = _.extend({}, options, { silent: true });
      const result = originalSet.call(this, attrs, silentOptions);

      // Track the attributes that have actually changed and will need
      // change events triggered later.
      _.extend(changes, this.changed);

      return result;
    };

    this.releaseCapturedChangeEvents = function () {
      _.each(
        changes,
        function (value, key) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.trigger('change:' + key, this, value);
        },
        this
      );

      if (!_.isEmpty(changes)) {
        this.trigger('change', this, {});
      }

      changes = {};
      this.set = originalSet;
      this.releaseCapturedChangeEvents = null;
    };
  },
});

// A little tricky, but make sure each subclass gets its own cache. We can't
// override extend without doing it after the initial extend call. The cache
// is what you might call a static variable, so we need to define it on the
// new class' prototype.

BaseModel.extend = function () {
  const subclass = Backbone.Model.extend.apply(this, arguments);

  subclass.prototype.cache = new ModelCache();

  return subclass;
};

export default BaseModel;
