/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import ServerModel from 'app/js/models/server/ServerModel';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import BaseClass from 'app/js/utils/BaseClass';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ServerConnectionManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Properties

  browserConnectionChangeDelay: 2000, // 2 seconds

  //
  // Init

  initialize(options: any) {
    _.bindAll(this, 'onBrowserConnectionChange');

    this.appModel = options.appModel;
    this.servers = this.appModel.get('servers');
    this.settings = this.appModel.get('settings');
  },

  //
  // Control

  start() {
    this.stop();

    const $window = $(window);
    $window.on('online', this.onBrowserConnectionChange);
    $window.on('offline', this.onBrowserConnectionChange);

    this.listenTo(dispatcher, {
      'test:servers': this.onTestAllServers,
      'test:server': this.onTestServer,
    });

    this.listenTo(this.servers, {
      add: this.onServerAdd,
      remove: this.onServerRemove,
      reset: this.onServersReset,
      'error:connection': this.onServerConnectionError,
      'change:connections.add': this.onServerConnectionAdd,
      'change:connections.remove': this.onServerConnectionRemove,
      'change:connections.reset': this.onServerConnectionsReset,
      'change:connections.change:isConnected':
        this.onServerConnectionConnectedChange,
      'change:connections.change:sources.change':
        this.onServerConnectionSourcesChange,
      'change:accessToken': this.onServerAccessTokenChange,
      'change:isConnected': this.onServerConnectedChange,
      'change:isUnavailable': this.onServerUnavailableChange,
      'change:publicAddressMatches': this.onServerPublicAddressMatchesChange,
    });

    this.listenTo(this.settings, 'sync', this.onSettingsSave);

    this.servers.invoke('testOnlyUntestedConnections');
  },

  stop() {
    this.stopListening();

    const $window = $(window);
    $window.off('online', this.onBrowserConnectionChange);
    $window.off('offline', this.onBrowserConnectionChange);
  },

  retryServerConnections: function (server: any) {
    server.testAllConnections();
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  startFailedServerRetries(server: any) {},
  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  stopFailedServerRetries(server: any) {},

  _removeConnection(connection: any) {
    const { server } = connection;
    const storedServers = userStorageModel.getActiveServers();

    log(
      '[Connections] Removed connection %s from %s',
      connection.id,
      server.get('debugName')
    );

    server.abortConnectionTest(connection);

    if (storedServers) {
      const storedServer = storedServers.get(server.id);
      const storedConnections = storedServer && storedServer.get('connections');

      if (storedConnections && storedConnections.get(connection.id)) {
        storedConnections.remove(connection.id);

        if (!storedConnections.length) {
          storedServers.remove(server.id);
        }

        userStorageModel.debouncedSave();
      }
    }
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onBrowserConnectionChange(event: any) {
    log(
      '[Connections] Browser online status changed to',
      window.navigator.onLine
    );

    // We can run into requests failing prematurely if we kick off connection
    // tests too early. This also helps protect against rapid connection
    // changes.

    window.clearTimeout(this.browserConnectionChangeTimeout);

    this.browserConnectionChangeTimeout = window.setTimeout(() => {
      this.servers.invoke('testAllConnections');
    }, this.browserConnectionChangeDelay);
  },

  onTestAllServers() {
    this.servers.invoke('testAllConnections');
  },

  onTestServer(server: any) {
    server.testAllConnections();
  },

  onServerAdd(server: any) {
    server.testOnlyUntestedConnections();
  },

  onServerRemove(server: any) {
    const storedServers = userStorageModel.getActiveServers();
    let storedServer;

    server.abortAllConnectionTests();
    this.stopFailedServerRetries(server);

    if (storedServers) {
      if (server.get('isMerged')) {
        // If the server still exists in the collection, it was merged with
        // another server. We should update the stored server to reflect the
        // changed `machineIdentifier`.

        const previousServerID = server.previous('machineIdentifier');

        if (previousServerID) {
          storedServer = storedServers.get(previousServerID);

          if (storedServer) {
            storedServer.set('machineIdentifier', server.id);
            userStorageModel.debouncedSave();
          }
        }
      } else {
        // Otherwise, the server was actually removed and we should remove it
        // from storage as well.

        storedServer = storedServers.get(server.id);

        if (storedServer) {
          storedServers.remove(server.id);
          userStorageModel.debouncedSave();
        }
      }
    }
  },

  onServersReset(servers: any, options: any) {
    const previousServers = options.previousModels;

    _.each(previousServers, (server) => {
      if (!servers.contains(server)) {
        server.abortAllConnectionTests();
        this.stopFailedServerRetries(server);
      }
    });
  },

  onServerConnectionError(server: any) {
    log.warn(
      '[Connections] Possible connection error for %s, retrying connection tests',
      server.get('debugName')
    );

    // Test all connections again in the case of an error or closed socket. If
    // the server has multiple connected connections, just checking the active
    // connection won't suffice.

    if (server.get('isCloud')) {
      // If the cloud server responds with an error, check if the user can still
      // sign in with the stored token.
      const user = this.appModel.get('user');
      user.signInUnsupervised();
    } else {
      server.testAllConnections();
    }
  },

  onServerConnectionAdd(connection: any) {
    const { server } = connection;

    log(
      '[Connections] Added connection %s for %s',
      connection.id,
      server.get('debugName')
    );

    if (connection.get('relay')) {
      // If a relay connection is discovered, only test it if every direct
      // connection test has already failed. Test all connections in case the
      // server was temporarily offline and a resources request discovered a
      // different relay connection. We do not want the relay connection test
      // to succeed ahead of a scheduled retry of the direct connections.

      if (server.get('connections').hasFailedEveryDirectConnection()) {
        server.testAllConnections();
      }
    } else {
      server.testConnection(connection);
    }
  },

  onServerConnectionRemove(connection: any) {
    this._removeConnection(connection);
  },

  onServerConnectionsReset(connections: any, options: any) {
    const previousConnections = options.previousModels;

    _.each(previousConnections, (connection) => {
      if (!connections.contains(connection)) {
        this._removeConnection(connection);
      }
    });
  },

  onServerConnectionConnectedChange(connection: any, isConnected: any) {
    // Storing a bundled connection would be repetitive since the app creates
    // bundled servers during initialization. Relayed connections can expire and
    // should be returned by plex.tv if they are still valid.

    const { server } = connection;
    const isBundled = connection.get('isBundled');
    const isFallback = connection.get('isFallback');
    const isRelay = connection.get('relay');
    const isEligibleConnection =
      isConnected && !isBundled && !isFallback && !isRelay;
    const storedServers = userStorageModel.getActiveServers();

    if (
      server.id &&
      !server.get('isCloud') &&
      isEligibleConnection &&
      storedServers
    ) {
      let storedServer = storedServers.get(server.id);

      if (storedServer) {
        storedServer.copyFromModel(server);
      } else {
        const serverAttrs = _.omit(server.toJSON(), 'connections');
        storedServer = storedServers.add(serverAttrs, {
          merge: true,
          parse: true,
        });
      }

      storedServer
        .get('connections')
        .add(connection.toJSON(), { merge: true, parse: true });
      userStorageModel.debouncedSave();
    }
  },

  onServerConnectionSourcesChange(connection: any, sources: any) {
    // If a source or added to or removed from a connection that is already in
    // storage, update storage to reflect reality.

    const { server } = connection;
    const storedServers = userStorageModel.getActiveServers();
    const storedServer = storedServers && storedServers.get(server.id);

    if (storedServer) {
      const storedConnections = storedServer.get('connections');
      const storedConnection = storedConnections.get(connection.id);

      if (storedConnection) {
        const storedSources = storedConnection.get('sources');

        storedSources.set(sources.toJSON());
        userStorageModel.debouncedSave();
      }
    }
  },

  onServerAccessTokenChange(server: any, accessToken: any) {
    // If a server gets an access token somehow, a previously failed connection
    // may now be valid, so test all connections again. The cloud server gets an
    // access token from the user, so a token change is expected with it.

    if (!server.get('isCloud')) {
      log(
        '[Connections] Access token for %s changed to %s',
        server.get('debugName'),
        accessToken
      );

      const storedServers = userStorageModel.getActiveServers();
      const storedServer = storedServers && storedServers.get(server.id);

      if (storedServer) {
        storedServer.set('accessToken', accessToken);
        userStorageModel.debouncedSave();
      }

      // Assume that the access token will allow a previously unauthorized
      // server to successfully connect. Otherwise, looking for a primary server
      // would skip this server since it would be unauthorized until after a
      // test succeeds, which may be too late.

      if (server.get('isUnauthorized')) {
        server.set(
          'connectionTestResult',
          ServerModel.connectionTestResults.PENDING
        );
      }

      server.testAllConnections();
    }
  },

  onServerConnectedChange(server: any, isConnected: any) {
    if (isConnected) {
      // Stop retrying connection tests for a failed server once it connects.
      this.stopFailedServerRetries(server);
    }
  },

  onServerUnavailableChange(server: any, isUnavailable: any) {
    if (isUnavailable) {
      // Start periodically retrying connection tests for this server.
      this.startFailedServerRetries(server);
    }
  },

  onServerPublicAddressMatchesChange(server: any, publicAddressMatches: any) {
    if (publicAddressMatches !== undefined) {
      server.testAllConnections();
    }
  },

  onSettingsSave(model: any) {
    // Retest all connections to all servers when `preferInsecureConnections`
    // has changed in order to accept or reject secure connections based on
    // it's new value.

    if (model.changed.hasOwnProperty('preferInsecureConnections')) {
      this.servers.invoke('testAllConnections');
    }
  },
});

export default ServerConnectionManager;
