/* eslint-plugin-disable @typescript-eslint */

import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { populateCloudServer } from 'app/js/stores/actions/appActions';
import selectCloudServer from 'app/js/stores/selectors/server/selectCloudServer';
import selectIsAnonymousUser from 'app/js/stores/selectors/user/selectIsAnonymousUser';
import EmptyPageContent from 'app/js/v4/core/ui/pages/components/empty/EmptyPageContent';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import routes from 'app/js/v4/routes/routes';
import UnreachablePage from 'app/js/views/v3/components/pages/empty/UnreachablePage';
import SignInLaunchPageConnector from 'app/js/views/v3/components/pages/signIn/SignInLaunchPageConnector';
import useEffectOnce from 'app/js/views/v3/helpers/hooks/useEffectOnce';
import usePrevious from 'app/js/views/v3/helpers/hooks/usePrevious';

function LoginPage() {
  const dispatch = useDispatch();

  const isAnonymousUser = useSelector(selectIsAnonymousUser);
  const {
    available: isCloudServerAvailable,
    isResolved: isCloudServerResolved,
    isRejected: isCloudServerRejected,
  } = useSelector(selectCloudServer);

  const previousIsCloudServerAvailable = usePrevious(isCloudServerAvailable);

  // Wait until `available` is known. Resetting the user cancels a
  // pending plex.tv connection test which introduces a `null` value for
  // `available` immediately before this function runs.
  const isAvailableKnown =
    isCloudServerAvailable === true || isCloudServerAvailable === false;
  const isPreviousAvailableUnknown =
    previousIsCloudServerAvailable !== true &&
    previousIsCloudServerAvailable !== false;

  useEffectOnce(() => {
    dispatch(populateCloudServer());
  });

  useEffect(() => {
    if (isPreviousAvailableUnknown && isAvailableKnown) {
      dispatch(populateCloudServer());
    }
  }, [dispatch, isAvailableKnown, isPreviousAvailableUnknown]);

  if (isCloudServerResolved) {
    if (isAnonymousUser) {
      // @ts-expect-error FIXME: Remove this comment to see the full error message
      return <SignInLaunchPageConnector />;
    } else {
      return <Redirect to={routes.home} />;
    }
  }

  if (isCloudServerRejected && isAvailableKnown) {
    if (isCloudServerAvailable) {
      // @ts-expect-error FIXME: Remove this comment to see the full error message
      return <SignInLaunchPageConnector />;
    } else {
      return <UnreachablePage />;
    }
  }

  return (
    <EmptyPageContent>
      <Spinner />
    </EmptyPageContent>
  );
}

export default LoginPage;
