/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import createShallowEqualSelector from 'app/js/stores/selectors/createShallowEqualSelector';
import selectServers from 'app/js/stores/selectors/server/selectServers';

const selectOwnedConnectedServers = createShallowEqualSelector(
  createSelector(selectServers, (allServerEntities) => {
    return allServerEntities.filter(
      (server: any) =>
        server.isFullOwnedServer && server.isConnected && !server.isTransient
    );
  }),
  (ownedConnectedServers) => ownedConnectedServers
);

export default selectOwnedConnectedServers;
