import { createSlice } from '@reduxjs/toolkit';
import serversSlice from 'app/js/v4/core/servers/serversSlice';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  ServerAccountsFetchFailedAction,
  ServerAccountsFetchRequestedAction,
  ServerAccountsFetchSucceededAction,
  ServerAccountsPerServerState,
  ServerAccountsState,
} from './serverAccountsTypes';

const initialState: ServerAccountsState = {};

const initialPerServerState: ServerAccountsPerServerState = {
  accounts: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const serverAccountsSlice = createSlice({
  name: 'serverAccounts',
  initialState,

  reducers: {
    populateRequested(_state, _action: ServerAccountsFetchRequestedAction) {
      // Handled by saga, no state change.
    },

    fetchRequested(state, { payload }: ServerAccountsFetchRequestedAction) {
      const serverState = state[payload.server.machineIdentifier];

      markPending(serverState);
    },

    fetchSucceeded(state, { payload }: ServerAccountsFetchSucceededAction) {
      const serverState = state[payload.server.machineIdentifier];

      markPopulated(serverState);
      serverState.accounts = payload.data;
    },

    fetchFailed(state, { payload }: ServerAccountsFetchFailedAction) {
      const serverState = state[payload.server.machineIdentifier];

      markFailed(serverState, payload.error);
    },
  },

  extraReducers: (builder) => {
    builder.addCase(serversSlice.actions.connected, (state, { payload }) => {
      const { server } = payload;

      if (!state[server.machineIdentifier]) {
        state[server.machineIdentifier] = { ...initialPerServerState };
      }
    });
  },
});

export default serverAccountsSlice;
