/* eslint-plugin-disable @typescript-eslint */

function splitUrlSearchHashPathAndParams(url: any) {
  const [, preHash, hash = ''] = url.match(/(^[^#]+)(?:#(.*$))?/);
  const [, base, searchParams = ''] = preHash.match(/(^[^?]+)(\?.*$)?/);
  const [, hashPath, hashParams = ''] = hash.match(/(^[^?]*)(\?.*$)?/);

  return {
    base,
    searchParams,
    hashPath,
    hashParams,
  };
}

export default splitUrlSearchHashPathAndParams;
