/* eslint-plugin-disable @typescript-eslint */

import closestLanguage from './closestLanguage';

export default function getDefaultLanguage(language: any) {
  const navigator = window.navigator;
  let languages;
  if (language) {
    languages = [language];
  } else if (navigator.languages) {
    languages = navigator.languages;
  } else if (navigator.language) {
    languages = [navigator.language];
  } else if ((navigator as any).userLanguage) {
    languages = [(navigator as any).userLanguage];
  }
  return closestLanguage(languages) || 'en';
}
