/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import storageAdapter from 'app/js/adapters/storageAdapter';
import AbstractModel from 'app/js/models/AbstractModel';
import * as responseTypes from 'app/js/models/responseTypes';
import assert from 'app/js/utils/log/assert';
// `AbstractStorageModel` provides access to a storage object like localStorage,
// as if it were any other Backbone model. This allows us to fetch and save
// using storage instead of a server.
const AbstractStorageModel = AbstractModel.extend({
  //
  // Properties
  name: 'AbstractStorageModel',
  responseType: responseTypes.json,
  //
  // Control
  save(key: any, value: any, options = {}) {
    const attributes = this.attributes;
    let attrs;
    // Handle both `"key", value` and `{ key: value }` style arguments.
    if (key == null || _.isObject(key)) {
      attrs = key;
      options = value;
    } else {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      (attrs = {})[key] = value;
    }
    options = _.extend({ validate: true }, options);
    __DEV__ && this._warnUnsupportedOptions(options);
    // If we're not waiting and attributes exist, save acts as
    // `set(attr).save(null, opts)` with validation. Otherwise, check if the
    // model will be valid when the attributes, if any, are set.
    if (attrs && !(options as any).wait) {
      if (!this.set(attrs, options)) {
        return false;
      }
    } else if (!this._validate(attrs, options)) {
      return false;
    }
    // Set temporary attributes if `{ wait: true }`.
    if (attrs && (options as any).wait) {
      this.attributes = _.extend({}, attributes, attrs);
    }
    // Assume all synced attributes and relations should be saved rather than
    // requiring the save flag for every attribute.
    const excludeDefaults =
      this._getStorageAdapter().getInstance().excludeDefaults;
    const json = this.toJSON({
      syncedOnly: true,
      excludeDefaults: excludeDefaults !== false,
    });
    const serialized = this._serialize(json);
    const promise = this._getStorageAdapter().set(
      this._getStorageKey(),
      serialized
    );
    promise.done(() => {
      this.attributes = attributes;
      this.trigger('sync', this, attributes, options);
    });
    promise.fail((error: any) => {
      this.trigger('error', this, error, options);
    });
    // Restore attributes.
    if (attrs && (options as any).wait) {
      this.attributes = attributes;
    }
    return promise;
  },
  destroy(options = {}) {
    __DEV__ && this._warnUnsupportedOptions(options);
    if (!(options as any).wait) {
      this.trigger('destroy', this, this.collection, options);
    }
    const promise = this._getStorageAdapter().remove(this._getStorageKey());
    promise.done((response: any) => {
      if ((options as any).wait) {
        this.trigger('destroy', this, this.collection, options);
      }
      this.trigger('sync', this, response, options);
    });
    promise.fail((error: any) => {
      this.trigger('error', this, error, options);
    });
    return promise;
  },
  sync() {
    this._logError('does not support syncing to a server');
  },
  _getStorageAdapter() {
    return storageAdapter;
  },
  _getStorageKey() {
    const key = _.result(this, 'storageKey');
    if (!key) {
      throw new Error(`${this.name} requires a \`storageKey\` property`);
    }
    return key;
  },
  _fetch(options = {}) {
    __DEV__ && this._warnUnsupportedOptions(options);
    const promise = this._getStorageAdapter().get(this._getStorageKey());
    promise.done((response: any) => {
      if (response) {
        this.set(this.parse(response));
      }
      this.trigger('sync', this, response, options);
    });
    promise.fail((error: any) => {
      this.trigger('error', this, error, options);
    });
    return promise;
  },
  _serialize(data: any) {
    let serialized = '';
    try {
      serialized = JSON.stringify(data);
    } catch (error) {
      this._logError('serialization error: ' + error.message);
    }
    return serialized;
  },
  _warnUnsupportedOptions(options: any) {
    if (options.parse) {
      this._logError(
        'does not support the `parse` option. The response from storage is always parsed.'
      );
    }
    if (options.success) {
      this._logError(
        'does not support a `success` callback. Use the returned promise.'
      );
    }
    if (options.error) {
      this._logError(
        'does not support an `error` callback. Use the returned promise.'
      );
    }
  },
  _logError(message: any) {
    let storageKey;
    try {
      storageKey = this._getStorageKey();
    } catch (error) {
      storageKey = 'no storageKey';
    }
    assert(false, `${this.name} (${storageKey}) ${message}`);
  },
});
export default AbstractStorageModel;
