/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import path from 'app/js/utils/url/path';

function matchCommand(options: any) {
  const item = options.item;
  const params = {
    guid: options.guid,
    name: options.name,
    year: options.year,
  };

  if (item.get('type') === 'album' && options.children) {
    options.children.reduce(function (acc: any, child: any) {
      const id = child.get('id');
      if (id) {
        acc['guid[' + id + ']'] = child.get('guid');
      }

      return acc;
    }, params);
  }

  const url = path.join(['/library/metadata/', item.id, '/match'], params);
  const xhrOptions = _.extend(item.server.getXHROptions(url), { type: 'PUT' });

  return $.ajax(xhrOptions).promise();
}

export default matchCommand;
