/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

// Changes to the composition of these models should reflect in a version bump
// in the HomeSettingsModel.

export const FORWARDED_HUB_ATTRS = {
  hubIdentifier: attrTypes.string,
  hubKey: attrTypes.string,
  key: attrTypes.string,
  style: attrTypes.string,
  subtype: attrTypes.string,
  title: attrTypes.string,
  type: attrTypes.string,
};

export const FORWARDED_SERVER_ATTRS = {
  friendlyName: attrTypes.string,
  machineIdentifier: attrTypes.string.isRequired,
  sourceTitle: attrTypes.string,
};

export const FORWARDED_PROVIDER_ATTRS = {
  identifier: attrTypes.string,
  providerIdentifier: attrTypes.string,
  title: attrTypes.string,
};

const HomeHubProviderModel = AbstractModel.extend({
  //
  // Properties

  name: 'HomeHubProviderModel',

  syncedAttrs: {
    ...FORWARDED_PROVIDER_ATTRS,
  },
});

const HomeHubServerModel = AbstractModel.extend({
  //
  // Properties

  name: 'HomeHubServerModel',

  syncedAttrs: {
    ...FORWARDED_SERVER_ATTRS,
  },
});

const HomeHubModel = AbstractModel.extend({
  //
  // Properties

  name: 'HomeHubModel',

  syncedAttrs: {
    ...FORWARDED_HUB_ATTRS,

    id: attrTypes.string.isRequired,
    directoryID: attrTypes.string.isRequired,
    directoryKey: attrTypes.string.isRequired,
    isHidden: attrTypes.boolean.isRequired,
    hiddenAt: attrTypes.integer,
  },

  syncedRelations: {
    provider: {
      relationClass: HomeHubProviderModel,
    },
    server: {
      relationClass: HomeHubServerModel,
    },
  },

  defaults: {
    isHidden: false,
  },
});

export default HomeHubModel;
