/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import DeviceModel from './DeviceModel';

const DeviceCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'DeviceCollection',
  context: ['server'],

  model: DeviceModel,
  url: '/devices.xml',

  //
  // Control

  filterByProducts(products: any) {
    const devices = this.filter((device: any) => {
      return _.contains(products, device.get('product'));
    });

    return this.clone(devices);
  },

  filterByPlatforms(platforms: any) {
    const devices = this.filter((device: any) => {
      return _.contains(platforms, device.get('platform'));
    });

    return this.clone(devices);
  },

  products() {
    const products = this.map((device: any) => {
      return device.get('product');
    });

    return _.uniq(products);
  },
});

export default DeviceCollection;
