import { ContentDirectory } from 'app/js/views/v3/helpers/props/shapes/contentDirectoryShape';
import findProviderContentFeature from 'app/js/views/v3/helpers/provider/findProviderContentFeature';

export default function getProvidersContentDirectories(
  providers: unknown[],
  types: string[]
): [string, ContentDirectory][] {
  return providers.reduce<[string, ContentDirectory][]>((acc, provider) => {
    const contentFeature = findProviderContentFeature(provider);
    const directories: ContentDirectory[] = contentFeature
      ? contentFeature.directories
      : [];

    directories
      .filter((directory) => directory.type && types.includes(directory.type))
      .map<[string, ContentDirectory]>((directory) => [
        (provider as { entityID: string }).entityID,
        directory,
      ])
      .forEach((tuple) => {
        acc.push(tuple);
      });

    return acc;
  }, []);
}
