//
// Helpers

function requestSettingSync(userModel: any, setting: any) {
  if (!userModel.get('isSignedIn') || !config.allowSyncSettings) {
    return $.Deferred().resolve().promise();
  }
  const settings = Array.isArray(setting) ? setting : [setting];
  const url = path.join('/api/v2/user/settings', {
    sharedSettings: canShareSyncSettings(userModel) ? 1 : 0,
  });
  const xhrOptions = userModel.server.getXHROptions(url, {
    type: 'POST',
    contentType: 'application/json',
    dataType: 'json',
    data: JSON.stringify({
      value: JSON.stringify(settings),
    }),
  });
  return $.ajax(xhrOptions).promise();
}

function getUpdatedAtForSetting(settings: any, settingID: any) {
  if (!settings) {
    return null;
  }
  const matchedSetting = settings.find(
    (setting: any) => setting.id === settingID
  );
  return matchedSetting && matchedSetting.updatedAt;
}

function showPINWarning(user: any) {
  const userSelectModal = new UserSelectModal({
    model: user,
    isEditing: true,
    isEditWarning: true,
  });
  dispatcher.trigger('show:modal', userSelectModal);
  return alwaysResolve(userSelectModal.promise);
}

function getUserModel(appModel: any) {
  return appModel.get('user');
}

function getSidebarSettings(appModel: any) {
  return appModel.get('user').get('experienceSettings').get('sidebarSettings');
}

function getHomeSettings(appModel: any) {
  return appModel.get('user').get('experienceSettings').get('homeSettings');
}

