/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import React, { memo, useEffect, useRef, useState } from 'react';
import ImageCache from 'app/js/utils/cache/ImageCache';
import getPixelRatio from 'app/js/utils/constants/getPixelRatio';
import * as imageSizes from 'app/js/utils/constants/imageSizes';
import assert from 'app/js/utils/log/assert';
import makeCancelable from 'app/js/utils/promise/makeCancelable';
import path from 'app/js/utils/url/path';
import PreloadImage from './PreloadImage';

export const cache = new ImageCache();

const sizeMemo = {};
const widthMemo = {};

const sortedImageSizes = [
  imageSizes.SMALL_60,
  imageSizes.SMALL_120,
  imageSizes.MEDIUM_240,
  imageSizes.MEDIUM_360,
  imageSizes.LARGE_480,
  imageSizes.LARGE_720,
  imageSizes.LARGE_1280,
  imageSizes.LARGE_1920,
].sort((a, b) => getWidthForSize(a) - getWidthForSize(b));

function getWidthForSize(imageSize: any) {
  if (widthMemo.hasOwnProperty(imageSize)) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return widthMemo[imageSize];
  }

  const width = imageSize.split('-')[1];
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  widthMemo[imageSize] = width;
  return width;
}

function getSizeForWidth(width: any) {
  if (sizeMemo.hasOwnProperty(width)) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return sizeMemo[width];
  }

  const size =
    sortedImageSizes.find((imageSize) => getWidthForSize(imageSize) >= width) ||
    sortedImageSizes[sortedImageSizes.length - 1];

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  sizeMemo[width] = size;
  return size;
}

function getDimensionsParams(props: any) {
  if (__DEV__) {
    assert(
      props.size || (props.width && (props.height || props.supportsSize)),
      'TranscodeImage requires `width` to determine `size` or both `width` and `height`'
    );
  }

  if (__DEV__) {
    assert(
      !props.size || props.supportsSize,
      'TranscodeImage cannot use `size` directly if the server does not support the param'
    );
  }

  const pxRatio = props.allowPixelRatioSize ? getPixelRatio() : 1;
  const size = props.size || getSizeForWidth(props.width);

  if (props.supportsSize) {
    return {
      size,
      scale: Math.ceil(pxRatio),
    };
  } else {
    const bracketedWidth = getWidthForSize(size);
    const aspectRatio = props.height / props.width;

    return {
      width: Math.ceil(bracketedWidth * pxRatio),
      height: Math.ceil(aspectRatio * bracketedWidth * pxRatio),
      minSize: props.minSize ? 1 : undefined,
      upscale: props.upscale ? 1 : undefined,
    };
  }
}

function appendTranscodeParams(url: any, props: any) {
  if (!url) {
    return;
  }

  const params = Object.assign(getDimensionsParams(props), props.filters);

  if (props.format) {
    params.format = props.format;
  }

  if (props.quality) {
    params.quality = props.quality;
  }

  // Move existing params to the end of the url. Plex Media Server expects
  // X-Plex- params at the end of the query string because they can be headers
  // as well as params.

  const queryIndex = url.indexOf('?');

  let urlWithParams = url;

  if (queryIndex > -1) {
    urlWithParams = path.join(url.substring(0, queryIndex), params);
    urlWithParams += urlWithParams.indexOf('?') === -1 ? '?' : '&';
    urlWithParams += url.substring(queryIndex + 1);
  } else {
    urlWithParams = path.join(urlWithParams, params);
  }

  return urlWithParams;
}

function loadBlob(url: any) {
  const deferred = $.Deferred();
  const cancelable = makeCancelable(deferred.promise());

  const request = {
    cancelable,
    xhr: null,
  };

  // Request the image data and create a Blob which is stored by the browser and
  // then referenced with an object url.

  const xhr = new window.XMLHttpRequest();
  xhr.open('GET', url, true);
  xhr.responseType = 'arraybuffer';

  // Keep a reference to the request so we can abort if necessary.
  // @ts-expect-error ts-migrate(2322) FIXME: Type 'XMLHttpRequest' is not assignable to type 'n... Remove this comment to see the full error message
  request.xhr = xhr;

  xhr.onreadystatechange = () => {
    if (xhr.readyState === 4) {
      xhr.onreadystatechange = null;

      if (xhr.status === 200) {
        const blob = new window.Blob([xhr.response], { type: 'image/jpeg' });
        const blobUrl = window.URL.createObjectURL(blob);

        // Store the blobUrl in the image cache so it can be reused later.
        cache.store({ url, blobUrl });

        deferred.resolve(blobUrl);
      } else {
        deferred.reject();
      }
    }
  };

  xhr.send();

  return request;
}

export interface TranscodeImageProps {
  url?: string;
  size?: any; // TODO: PropTypes.oneOf(sortedImageSizes)
  width?: number;
  height?: number;
  minSize?: boolean;
  upscale?: boolean;
  format?: string;
  quality?: number;
  filters?: {
    blur?: number;
    background?: string;
    opacity?: number;
    saturation?: number;
  };
  allowPixelRatioSize?: boolean;
  allowImageBlobs?: boolean;
  supportsSize?: boolean;
  onError?: (...args: any[]) => any;
}

export function TranscodeImage(props: TranscodeImageProps) {
  const {
    url,
    minSize,
    format,
    quality,
    upscale,
    filters,
    allowPixelRatioSize,
    allowImageBlobs,
    supportsSize,
    onError,
    ...otherProps
  } = props;

  const transcodeUrl = appendTranscodeParams(url, props);
  let cachedBlobUrl: any;
  let cachedPlaceholderUrl;

  if (transcodeUrl && allowImageBlobs) {
    cachedBlobUrl = cache.getBlobUrl(transcodeUrl);

    if (!cachedBlobUrl) {
      cachedPlaceholderUrl = cache.getPlaceholder(transcodeUrl);
    }
  }

  const [src, setSrc] = useState(
    cachedBlobUrl ||
      cachedPlaceholderUrl ||
      (allowImageBlobs ? null : transcodeUrl)
  );
  const isErrorRef = useRef(false);

  useEffect(() => {
    if (isErrorRef.current) {
      isErrorRef.current = false;

      // If an image failed to load, call the `onError` callback and bail early
      // so the component doesn't request the bad url again. Be careful of
      // batched state updates. The effect might not run immediately after
      // `setSrc(null)`.

      if (src == null) {
        onError && onError();
        return;
      }
    }

    let request: any;

    if (cachedBlobUrl) {
      // If we already have a blob url for this transcode url in cache, we do
      // not need to load a new blob image. If this is not the initial render,
      // we still need to set the src to the new blob url.

      if (src !== cachedBlobUrl) {
        setSrc(cachedBlobUrl);
      }
    } else if (transcodeUrl && allowImageBlobs) {
      request = loadBlob(transcodeUrl);

      request.cancelable.promise.then(
        // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'nextBlobUrl' implicitly has an 'any' ty... Remove this comment to see the full error message
        (nextBlobUrl) => {
          request = null;

          setSrc(nextBlobUrl);
        },
        // @ts-expect-error ts-migrate(2525) FIXME: Initializer provides no value for this binding ele... Remove this comment to see the full error message
        ({ isCanceled } = {}) => {
          if (!isCanceled) {
            request = null;

            if (src === null) {
              // The effect won't run again if `src` is already null, so it's
              // safe to call `onError` immediately.
              onError && onError();
            } else {
              isErrorRef.current = true;
              setSrc(null);
            }
          }
        }
      );
    } else if (transcodeUrl && !allowImageBlobs) {
      setSrc(transcodeUrl);
    } else {
      setSrc(null);
    }

    return () => {
      if (request) {
        request.cancelable.cancel();

        if (request.xhr) {
          request.xhr.onreadystatechange = null;
          request.xhr.abort();
        }
      }
    };
  }, [src, transcodeUrl, cachedBlobUrl, allowImageBlobs, onError]);

  // `PreloadImage` is responsible for calling `onLoad` after the image is
  // preloaded and rendered.
  return <PreloadImage {...otherProps} src={src} onError={onError} />;
}

TranscodeImage.defaultProps = {
  minSize: true,
  upscale: true,
  allowPixelRatioSize: true,
  allowImageBlobs: true,
};

export default memo(TranscodeImage);
