import $ from 'jquery';
import _ from 'underscore';
import BaseClass from 'app/js/utils/BaseClass';
import dispatcher from 'app/js/utils/dispatcher';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error FIXME: Expected 2 arguments, but got 1.
const ModalManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize() {
    this.modals = [];
  },

  //
  // Control

  start() {
    this.listenTo(dispatcher, {
      'show:modal': this.showModal,
      'hide:modal': this.hideModal,
    });
  },

  //
  // Listeners

  showModal(modal: any) {
    this.modals.push(modal);

    const $body = $('body');

    // Allow unique styling for transparent modals.
    if (modal.transparent) {
      $body.addClass('show-modal-transparent');
    }

    $body.append(modal.render().el);
  },

  hideModal(modal: any) {
    const i = _.indexOf(this.modals, modal);

    if (i > -1) {
      this.modals.splice(i, 1)[0].close();
    }

    // Remove our modifier class if this was the last transparent modal in
    // the stack.
    if (modal.transparent && !_.findWhere(this.modals, { transparent: true })) {
      $('body').removeClass('show-modal-transparent');
    }
  },
});

export default ModalManager;
