/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';

function getTitleList(titles: any) {
  if (!titles || !titles.length) {
    return;
  }

  const count = titles.length;

  if (count === 1) {
    return titles[0];
  }

  if (count === 2) {
    return t('{1} and {2}', titles[0], titles[1]);
  }

  if (count === 3) {
    return t('{1}, {2}, and {3}', titles[0], titles[1], titles[2]);
  }

  return t('{1}, {2}, and {3} others', titles[0], titles[1], titles.length - 2);
}

export default getTitleList;
