import isPlainObject from 'app/js/utils/object/isPlainObject';

const DEFAULT_BUDGET = 1000 / 60;
let currentTypeIndex = 0;
let currentEntityIndex = 0;
let startTypeIndex;
let startEntityIndex;
function searchForEntityID(state: any, entityID: any) {
  if (isPlainObject(state)) {
    return searchInObject(state, entityID);
  }
  if (Array.isArray(state)) {
    return searchInArray(state, entityID);
  }
  return state === entityID;
}
function searchInObject(obj: any, entityID: any) {
  // Only iterate through keys that have been provided as possible entity
  // containers. Otherwise, we would be wasting time looking for entities where
  // they can't exist and walking the tree is expensive.
  const possibleEntityContainers = obj.possibleEntityContainers;
  const len = possibleEntityContainers ? possibleEntityContainers.length : 0;
  for (let i = 0; i < len; i++) {
    const key = possibleEntityContainers[i];
    const value = obj[key];
    if (searchForEntityID(value, entityID)) {
      return true;
    }
  }
  return false;
}
function searchInArray(arr: any, entityID: any) {
  for (let i = 0, len = arr.length; i < len; i++) {
    const value = arr[i];
    if (searchForEntityID(value, entityID)) {
      return true;
    }
  }
  return false;
}
function incrementIndex(index: any, limit: any) {
  if (index + 1 >= limit) {
    return 0;
  }
  return index + 1;
}
// Walk the state tree, looking for entity ids that no longer exist as values in
// the tree. Since entity ids are unique, if an entity no longer exists
// elsewhere in the tree, we know it can be safely removed from the tree.
export default function garbageCollectEntities(state: any, options = {}) {
  const processingBudget =
    (options as any).budget != null ? (options as any).budget : DEFAULT_BUDGET;
  const start = window.performance.now();
  const allEntities = state.entities;
  const allEntityTypes = Object.keys(allEntities);
  const numTypes = allEntityTypes.length;
  const garbage = {};

  // Start from the beginning if we're trying to do a full pass.
  if (processingBudget === Infinity) {
    currentTypeIndex = 0;
    currentEntityIndex = 0;
  }

  const startingEntityType = allEntityTypes[currentTypeIndex];
  const startingEntities = allEntities[startingEntityType];
  startTypeIndex = currentTypeIndex;
  // The entity at `currentEntityIndex` may no longer exist in the state tree
  // when calling garbage collection again after it has exited early due to
  // running out of budget in a previous run. In this case, start the index
  // at the last item in the entity type list or zero.
  startEntityIndex = Math.max(
    Math.min(currentEntityIndex, Object.keys(startingEntities).length - 1),
    0
  );
  while (currentTypeIndex < numTypes) {
    const entityType = allEntityTypes[currentTypeIndex];
    const entities = allEntities[entityType];
    const entityIDs = Object.keys(entities);
    const numIDs = entityIDs.length;
    const nextTypeIndex = incrementIndex(currentTypeIndex, numTypes);
    while (currentEntityIndex < numIDs) {
      const entityID = entityIDs[currentEntityIndex];
      const nextEntityIndex = incrementIndex(currentEntityIndex, numIDs);
      if (!searchForEntityID(state, entityID)) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        if (!garbage[entityType]) {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          garbage[entityType] = [];
        }
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        garbage[entityType].push(entityID);
      }
      // If we've completed a full cycle, return early so we don't endlessly
      // loop over the tree.
      if (
        nextTypeIndex === startTypeIndex &&
        nextEntityIndex === startEntityIndex
      ) {
        currentTypeIndex = nextTypeIndex;
        currentEntityIndex = nextEntityIndex;
        return garbage;
      }
      // If we run out of budget, we can use the current indices as a starting
      // point for the next round of garbage collection to continue where we
      // left off.
      if (window.performance.now() - start >= processingBudget) {
        currentTypeIndex = nextTypeIndex;
        currentEntityIndex = nextEntityIndex;
        return garbage;
      }
      currentEntityIndex++;
    }
    currentTypeIndex = nextTypeIndex;
    currentEntityIndex = 0;
    // If we've completed a full cycle, return early so we don't endlessly
    // loop over the tree.
    if (
      currentTypeIndex === startTypeIndex &&
      currentEntityIndex === startEntityIndex
    ) {
      return garbage;
    }
  }
  return garbage;
}
