import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import { LOWEST_VIDEO_QUALITY_INDEX } from 'app/js/utils/constants/convert/conversionQualities';
import videoPlayerQualities from 'app/js/utils/player/videoPlayerQualities';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertFormTargetView.tpl';

const ConvertFormTargetView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'convert-target-container form-group',
  template: tpl,

  ui: {
    targetTitleInput: '.convert-target-title',
  },

  events: {
    'change select[name=target]': 'onTargetChange',
    'keydown input[name=targetTitle]': 'onTitleKeyDown',
    'change input[name=targetTitle]': 'onTitleChange',
    'change select[name=targetProfile]': 'onProfileChange',
    'change select[name=targetQuality]': 'onQualityChange',
  },

  //
  // Init

  initialize: function (options: any) {
    this.targets = options.targets;
    this.profiles = options.profiles;

    const profile = this.model.get('Device').profile;
    if (profile) {
      this.selectedProfile = this.profiles.get(profile);
    } else {
      this.selectedProfile = this.profiles.findWhere({ selected: true });
    }

    this.availableCustomQualities = videoPlayerQualities
      .getAvailableQualities({
        lowestQualityIndex: LOWEST_VIDEO_QUALITY_INDEX,
      })
      .reverse();

    const maxVideoBitrate = this.model
      .get('mediaSettings')
      .get('maxVideoBitrate');
    if (maxVideoBitrate) {
      const nearestIndex =
        videoPlayerQualities.getNearestIndexToBitrate(maxVideoBitrate);
      this.selectedCustomQuality =
        videoPlayerQualities.getQualityByIndex(nearestIndex);
    } else {
      this.selectedCustomQuality = _.first(this.availableCustomQualities);
    }

    if (!this.model.isNew() && !this.model.get('targetTagID')) {
      this.$el.addClass('show-custom-target');
    }
  },

  //
  // Control

  serializeData: function () {
    return {
      item: this.model.toJSON(),
      targets: this.targets.toJSON(),
      disableTarget: !this.model.isNew(),
      profiles: this.profiles.toJSON(),
      selectedProfile: this.selectedProfile.toJSON(),
      availableCustomQualities: this.availableCustomQualities,
      selectedCustomQuality: this.selectedCustomQuality,
    };
  },

  updateDeviceProfile: function () {
    const device = this.model.get('Device');
    device.profile = this.selectedProfile.get('title');
  },

  updateMediaSettings: function (quality: any) {
    const mediaSettings = this.model.get('mediaSettings');

    mediaSettings.set({
      videoQuality: quality.videoQuality,
      videoResolution: quality.videoResolution,
      maxVideoBitrate: quality.bitrate,
    });
  },

  //
  // Listeners

  onTargetChange: function (event: any) {
    const targetTagID = parseInt(event.currentTarget.value, 10);

    // The Custom target uses a synthetic id of -1.
    if (targetTagID > -1) {
      this.model.unset('target');

      // A target provided by the server already has its own set of
      // MediaSettings and client profile, so empty out any custom settings
      // that were set.

      this.model.set({
        targetTagID: targetTagID,
        Device: {},
      });

      this.model.get('mediaSettings').restoreDefaults();
    } else {
      this.model.set('target', this.ui.targetTitleInput.val());
      this.model.unset('targetTagID');

      this.updateDeviceProfile(this.selectedProfile);
      this.updateMediaSettings(this.selectedCustomQuality);
    }

    this.$el.toggleClass('show-custom-target', targetTagID === -1);
  },

  onTitleKeyDown: function (event: any) {
    // Prevent periods at the beginning of the title, since that would lead
    // to hidden directories on OSX.
    if (event.which === 190 && event.currentTarget.selectionStart === 0) {
      event.preventDefault();
    }
  },

  onTitleChange: function (event: any) {
    this.model.set('target', event.currentTarget.value);
  },

  onProfileChange: function (event: any) {
    this.selectedProfile = this.profiles.findWhere({
      title: event.currentTarget.value,
    });

    this.updateDeviceProfile(this.selectedProfile);
  },

  onQualityChange: function (event: any) {
    const qualityIndex = parseInt(event.currentTarget.value, 10);
    this.selectedCustomQuality =
      videoPlayerQualities.getQualityByIndex(qualityIndex);

    this.updateMediaSettings(this.selectedCustomQuality);
  },
});

export default ConvertFormTargetView;
