/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import log from 'app/js/utils/log/log';

const updateQueue: any = [];
let isImmediateUpdate = false;
let isProcessing = false;
let processingBudget = 1000 / 60;
let processingDelay = 1000 / 60;
let branchTimeoutID: any;
let rootTimeoutID: any;
let updateRootState: any;
function clearBranchTimeout() {
  if (branchTimeoutID) {
    branchTimeoutID = window.clearTimeout(branchTimeoutID);
  }
}
function clearRootTimeout() {
  if (rootTimeoutID) {
    rootTimeoutID = window.clearTimeout(rootTimeoutID);
  }
}
function startRootStateUpdate() {
  const currentUpdateRootState = updateRootState;
  updateRootState = null;
  // See: https://github.com/plexinc/plex-web-client/issues/7921
  clearRootTimeout();
  // See: https://github.com/plexinc/plex-web-client/issues/11554
  if (!currentUpdateRootState) {
    return;
  }
  const result = currentUpdateRootState();
  if (__DEV__) {
    const getDevOption = require('app/js/utils/dev/getDevOption').default;
    const logStateUpdate = require('./logStateUpdate').default;
    if (getDevOption('stateLogs')) {
      logStateUpdate(result);
    }
  }
}
function startProcessingUpdates() {
  clearBranchTimeout();
  clearRootTimeout();
  // Process updates until we have exceeded our budget or there are no more
  // updates. If we have exceeded our budget, schedule another update. If there
  // are no more updates, schedule the root update, which will trigger a
  // `statechange` event that a store can listen for.
  const start = window.performance.now();
  let end;
  isProcessing = true;
  while (updateQueue.length && (!end || end - start < processingBudget)) {
    updateQueue.pop()();
    end = window.performance.now();
  }
  isProcessing = false;
  if (updateQueue.length) {
    log.warn(
      '[State] Unable to process all updates within budget; %s update(s) remaining',
      updateQueue.length
    );
    branchTimeoutID = window.setTimeout(
      startProcessingUpdates,
      processingDelay
    );
  } else if (updateRootState) {
    rootTimeoutID = window.setTimeout(startRootStateUpdate, processingDelay);
  }
}
export function configure(options = {}) {
  if ((options as any).budget != null) {
    processingBudget = (options as any).budget;
  }
  if ((options as any).delay != null) {
    processingDelay = (options as any).delay;
  }
}
export function resetUpdates() {
  clearBranchTimeout();
  clearRootTimeout();
  isProcessing = false;
  isImmediateUpdate = false;
  updateRootState = null;
  updateQueue.length = 0;
}
export function scheduleImmediateUpdate(callback: any) {
  clearBranchTimeout();
  clearRootTimeout();
  isProcessing = true;
  isImmediateUpdate = true;
  while (updateQueue.length) {
    updateQueue.pop()();
  }
  // If a callback is passed invoke it before we update our state tree. Although
  // the callback itself may assert, use assert to throw an error while in dev
  // (useful for unit tests).
  if (callback) {
    try {
      callback();
    } catch (error) {
      assert(false, error.name + ': ' + error.message);
    }
  }
  if (updateRootState) {
    startRootStateUpdate();
  }
  isProcessing = false;
  isImmediateUpdate = false;
}
export function scheduleUpdate(updateState: any, instance: any) {
  if (instance.isStateTreeRoot) {
    updateRootState = updateState;
    if (!isProcessing && !rootTimeoutID) {
      rootTimeoutID = window.setTimeout(startRootStateUpdate, processingDelay);
    }
    return;
  }
  if (isImmediateUpdate) {
    updateState();
    return;
  }
  clearRootTimeout();
  // Add updates to the beginning of the array and process them from the end.
  // Only update the root once after all branches are updated to minimize
  // changes being handled in a store.
  if (updateQueue[0] !== updateState) {
    updateQueue.unshift(updateState);
  }
  if (!isProcessing && !branchTimeoutID) {
    branchTimeoutID = window.setTimeout(
      startProcessingUpdates,
      processingDelay
    );
  }
}
