import { createSelector, Selector } from 'reselect';
import { EPGLineup } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';
import selectEPGSettings from './selectEPGSettings';

const selectSetupSelectedLineups: Selector<
  LegacyRootState,
  EPGLineup[] | undefined
> = createSelector(selectEPGSettings, ({ selectedLineupGroups }) => {
  if (selectedLineupGroups.length === 0) {
    return;
  }

  return selectedLineupGroups.reduce((acc: EPGLineup[], { selectedLineup }) => {
    if (selectedLineup) {
      acc.push(selectedLineup);
    }

    return acc;
  }, []);
});

export default selectSetupSelectedLineups;
