/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import AbstractCollection from 'app/js/models/AbstractCollection';
import PlexPlayerModel from './PlexPlayerModel';

const PlexPlayerCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'PlexPlayerCollection',
  context: ['server'],

  model: PlexPlayerModel,
  url: '/clients',

  augmentResponse(response: any) {
    const appModel = resolve('appModel');
    const clientID = appModel.get('clientID');
    const size = parseInt(response.size, 10);

    if (size === 1 && response.Server.machineIdentifier === clientID) {
      delete response.Server;
      response.size = '0';
    } else if (size > 1) {
      response.Server = response.Server.filter(
        (client: any) => client.machineIdentifier !== clientID
      );
      response.size = response.Server.length.toString();
    }

    return response;
  },
});

export default PlexPlayerCollection;
