import { UserService } from 'app/js/v4/core/user/userTypes';

const unsuitableSubscriptionTypes = ['free'];

export default function hasRequiredUserService(
  userServices: UserService[],
  requires?: string
): boolean {
  // Nothing required for this feature.
  if (!requires) {
    return true;
  }

  // Otherwise check the user has this service and it's a suitable
  // subscription type. e.g. TIDAL free subscriptions aren't suitable to meet
  // the tidal requirement.

  const service = userServices.find(
    (userService) => userService.id === requires
  );

  return service
    ? !service.subscriptionType ||
        !unsuitableSubscriptionTypes.includes(service.subscriptionType)
    : false;
}
