import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/PlexPassModal.tpl';

const PlexPassModal = BaseModal.extend({
  //
  // Properties

  name: 'plexPassModal',
  template: tpl,

  //
  // Control

  serializeData: function () {
    const appModel = adapters.resolve('appModel');
    const cloudServer = appModel.get('user').server;

    return {
      plexPassUrl: cloudServer.get('plexPassUrl'),
      escapedFriendlyName: _.escape(this.model.get('friendlyName')),
      isClaimed: this.model.get('isClaimed'),
    };
  },
});

export default PlexPassModal;
