/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import * as adapters from 'app/js/adapters/adapters';
import { repopulatePage } from 'app/js/stores/actions/ui/uiActions';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deleteSectionCommand(options: any) {
  const deferred = $.Deferred();
  const section = options.section;

  function deleteSection() {
    const xhrDeferred = section.destroy({ wait: true });

    // It's possible to try and delete a partially-hydrated section model
    // from the setup flow. In that case, `destroy` returns false.
    if (!xhrDeferred) {
      deferred.resolve();
      return;
    }

    xhrDeferred.done(function () {
      const dispatch = adapters.resolve('appStore').dispatch;
      const serverEntityID = section.server.entityID();

      if (serverEntityID) {
        dispatch(repopulatePage({ serverEntityID }));
      }

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr: any) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem deleting the library.'),
      });

      deferred.reject(xhr);
    });
  }

  const confirmModal = new ConfirmModal({
    title: t('Delete Library'),
    message: t('Are you sure you want to delete {1}?', section.get('title')),
    confirmLabel: t('Delete'),
    cancelLabel: t('Cancel'),
    requireDoubleConfirm: true,
    doubleConfirmMessage: t('This cannot be undone.'),
    doubleConfirmLabel: t("Yes, I'm sure"),
    async: true,
    hasDanger: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(deleteSection);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  deferred.promise().always(() => {
    confirmModal.hide();
  });

  return deferred.promise();
}

export default deleteSectionCommand;
