/* eslint-plugin-disable @typescript-eslint */

import path from 'app/js/utils/url/path';

const hashBang = '#!';

export default function resolveDescriptor(
  descriptor: any,
  prependHashBang = true
) {
  let url = '';

  // See: https://github.com/ReactJSTraining/history/blob/master/docs/Location.md#location-descriptors
  if (descriptor) {
    if (typeof descriptor.pathname === 'string') {
      url = descriptor.query
        ? path.join(descriptor.pathname, descriptor.query)
        : descriptor.pathname;
    } else if (typeof descriptor === 'string') {
      url = descriptor;
    }
  }

  // If there is a url, prependHashbang is true and the url does not begin
  // with a hashBang then prepend one ourselves.
  if (url && prependHashBang && url.indexOf(hashBang) !== 0) {
    url = hashBang + url;
  }

  return url;
}
