/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import { MY_PLEX_SIGN_IN_STATE_OK } from 'app/js/models/server/ServerModel';
import dispatcher from 'app/js/utils/dispatcher';
import parser from 'app/js/utils/parser2';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';

function claimServer(server: any, cloudServer: any) {
  const tokenXhrOptions = cloudServer.getXHROptions('/api/claim/token');

  let promise = $.ajax(tokenXhrOptions).promise();

  promise = promise.then((response) => {
    const { token } = parser.parseXML(response);
    const claimUrl = path.join('/myplex/claim', { token });
    const claimXhrOptions = server.getXHROptions(claimUrl, { type: 'POST' });

    return $.ajax(claimXhrOptions).promise();
  });

  return promise;
}

function signInServer(username: any, password: any, server: any) {
  const url = path.join('/myplex/account', { username, password });
  const xhrOptions = server.getXHROptions(url, { type: 'PUT' });

  return $.ajax(xhrOptions).promise();
}

function signInServerCommand(options: any) {
  const { cloudAccount, username, password, claim = false } = options;

  const deferred = $.Deferred();
  const server = cloudAccount.server;
  const appModel = resolve('appModel');
  const user = appModel.get('user');
  const cloudServer = appModel.get('cloudServer');
  const previousToken = server.get('accessToken');

  // Let the server immediately use the user's token. This prevents the bundled
  // web app from getting locked out after the server is signed in.

  server.set('accessToken', user.get('authToken'));

  const promise = claim
    ? claimServer(server, cloudServer)
    : signInServer(username, password, server);

  promise.done((response) => {
    const json = _.isObject(response) ? response : parser.parseXML(response);

    if (json && json.signInState === 'invalid') {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('The username or password is incorrect.'),
      });

      deferred.reject();
    } else {
      server.set({
        myPlexSigninState: MY_PLEX_SIGN_IN_STATE_OK,
        myPlexUsername: user.get('email'),
      });

      cloudAccount.set({
        signedIn: true,
        username: user.get('email'),
      });

      deferred.resolve();
    }
  });

  promise.fail((xhr) => {
    server.set('accessToken', previousToken);

    if (cloudAccount.get('signedIn') === true) {
      cloudAccount.set('signedIn', false);
    }

    let message;

    if (xhr.status === 401) {
      message = t('The username or password is incorrect.');
    } else {
      message = t('There was a problem signing in to Plex.');
    }

    dispatcher.trigger('show:alert', {
      type: 'error',
      message: message,
    });

    deferred.reject(xhr);
  });

  return deferred.promise();
}

export default signInServerCommand;
