/* eslint-plugin-disable @typescript-eslint */

import isLocalMetadataItem from 'app/js/utils/player/isLocalMetadataItem';

/**
 * @param {MetadataModel} metadataItem
 * @param {boolean} isPrivateConnection
 * @returns string The quality attribute to be used with a model.
 */
export default function getQualityAttribute(
  metadataItem: any,
  isPrivateConnection: any
) {
  return isLocalMetadataItem(metadataItem, isPrivateConnection)
    ? 'localQuality'
    : 'remoteQuality';
}
