import cloneDeep from 'lodash/cloneDeep';
import {
  MediaGrabberDevice,
  MediaGrabberDeviceResponse,
  MediaGrabberDeviceSourceType,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import normalizeChannelMappingResponse from 'app/js/v4/features/dvrManagement/utils/normalizeChannelMappingResponse';
import { MediaContainerResponse } from 'app/js/v4/types/MediaContainerResponse';
import { ObjectValues } from 'app/js/v4/utils/TSObject';
import assert from 'app/js/v4/utils/assert';
import {
  normalizeAsNumber,
  normalizeAsString,
} from 'app/js/v4/utils/data/normalizers/normalizeUtils';
import { NormalizerOptions } from 'app/js/v4/utils/data/normalizers/normalizersTypes';

export interface NormalizeMediaGrabberDeviceResponseOptions
  extends NormalizerOptions {
  data: MediaContainerResponse;
}
export function normalizeDevice(
  deviceResponse: MediaGrabberDeviceResponse
): MediaGrabberDevice {
  const device = cloneDeep(deviceResponse) as unknown as MediaGrabberDevice;

  normalizeAsString(device, 'key');
  normalizeAsString(device, 'source');
  normalizeAsNumber(device, 'tuners');
  device.sources = deviceResponse.sources
    ?.split(',')
    ?.map<MediaGrabberDeviceSourceType>((source) => {
      const sourceType = ObjectValues(MediaGrabberDeviceSourceType).find(
        (deviceSource) => deviceSource === source
      );

      assert(sourceType, `Unknown Media Grabber Device Source Type ${source}`);

      return sourceType;
    });
  device.ChannelMapping = normalizeChannelMappingResponse(
    device.ChannelMapping
  );

  return device;
}

export default function normalizeDevicesResponse({
  data,
}: NormalizeMediaGrabberDeviceResponseOptions): MediaGrabberDevice[] {
  const devicesResponse = data.Device ?? [];

  return devicesResponse.map(normalizeDevice);
}
