import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import CloudServerRetryManager from 'app/js/managers/CloudServerRetryManager';
import ServerEventManager from 'app/js/managers/ServerEventManager';
import capabilities from 'app/js/utils/device/capabilities';
import CloudServerEventConnection from 'app/js/utils/pubsub/CloudServerEventConnection';

function isValidServer(server: any) {
  return server.get('isCloud');
}

export default function cloudServerEventInitializer() {
  let serverEventManager: any;

  if (capabilities.hasWebSocket) {
    serverEventManager = new ServerEventManager({
      name: 'CloudServerEventManager',
      appModel: resolve('appModel'),
      isValidServer,
      supportsLongPolling: true,
      allowMultipleConnections: true,
      ServerEventConnection: CloudServerEventConnection,
      ServerRetryManager: CloudServerRetryManager,
    });

    serverEventManager.start();
  }

  return $.Deferred().resolve().promise();
}
