/* eslint-plugin-disable @typescript-eslint */

import { getAllMetadataTypes } from 'app/js/utils/types/metadataTypes';

let metadataDirectoryTypes: any;

function replaceMetadataDirectoryXMLElementNames(xml: any) {
  // Defer this work since the type names are translated by
  // `getAllMetadataTypes` and a localizer won't be ready immediately.

  if (!metadataDirectoryTypes) {
    metadataDirectoryTypes = getAllMetadataTypes()
      .map((metadataType: any) => metadataType.typeString)
      .join('|');
  }

  const openRegex = new RegExp(
    `<Directory([^>]*?)type="(${metadataDirectoryTypes})"([\\s\\S]*?)</Directory>`,
    'gm'
  );
  const closedRegex = new RegExp(
    `<Directory([^>]*?)type="(${metadataDirectoryTypes})"([\\s\\S]*?)/>`,
    'gm'
  );

  return xml
    .replace(
      openRegex,
      '<Metadata$1type="$2" elementName="Directory"$3</Metadata>'
    )
    .replace(closedRegex, '<Metadata$1type="$2" elementName="Directory"$3/>');
}

export default replaceMetadataDirectoryXMLElementNames;
