/* eslint-plugin-disable @typescript-eslint */

import * as serverUpdateStates from 'app/js/utils/constants/serverUpdateStates';
import shortVersion from 'app/js/utils/string/shortVersion';

function getAvailableUpdateVersion(server: any) {
  const update = server[__PLATFORM__] && server[__PLATFORM__].update;

  if (
    update &&
    (update.state === serverUpdateStates.AVAILABLE ||
      update.state === serverUpdateStates.DOWNLOADED ||
      update.state === serverUpdateStates.DOWNLOADING ||
      update.state === serverUpdateStates.NOTIFY)
  ) {
    return shortVersion(update.version);
  }
}

export default function isUpdateAvailable(
  server: any,
  dismissedServerUpdateVersions: any,
  serverUpdateDismissedVersion: any
) {
  const serverUpdateVersion = getAvailableUpdateVersion(server);

  return !!(
    server.isConnected &&
    server.isFullOwnedServer &&
    serverUpdateVersion &&
    serverUpdateVersion !== serverUpdateDismissedVersion &&
    serverUpdateVersion !==
      dismissedServerUpdateVersions[server.machineIdentifier]
  );
}
