/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import DisclosureArrow from 'app/js/views/v3/components/core/base/DisclosureArrow';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import * as directions from 'app/js/views/v3/helpers/props/directions';
// @ts-expect-error ts-migrate(6133) FIXME: 'kinds' is declared but its value is never read.
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './DisclosureArrowButton.css';

interface DisclosureArrowButtonProps {
  className?: string;
  arrowClassName?: string;
  direction?: any; // TODO: PropTypes.oneOf(directions.all)
  kind?: any; // TODO: PropTypes.oneOf(kinds.all)
  size?: any; // TODO: PropTypes.oneOf(sizes.all)
  isSelected?: boolean;
}

class DisclosureArrowButton extends Component<DisclosureArrowButtonProps> {
  static defaultProps = {
    className: styles.disclosureArrowButton,
    arrowClassName: styles.disclosureArrow,
    direction: directions.DOWN,
    isSelected: true,
  };

  //
  // Render

  render() {
    const {
      className,
      arrowClassName,
      children,
      direction,
      kind,
      size,
      isSelected,
      ...otherProps
    } = this.props;

    const arrowSize = size || sizes.MEDIUM;

    return (
      <Link
        aria-haspopup={true}
        className={classNames(
          className,
          styles[arrowSize],
          isSelected && styles.isSelected
        )}
        isSelected={isSelected}
        kind={kind}
        size={size}
        {...otherProps}
      >
        {children}
        <DisclosureArrow
          className={classNames(
            arrowClassName,
            styles[direction],
            styles[arrowSize]
          )}
          direction={direction}
          isSelected={isSelected}
          kind={kind}
          size={arrowSize}
        />
      </Link>
    );
  }
}

export default DisclosureArrowButton;
