/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectCloudServer from 'app/js/stores/selectors/server/selectCloudServer';
import createUserFeatureSelector from 'app/js/stores/selectors/user/createUserFeatureSelector';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import * as features from 'app/js/utils/constants/user/features';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import NavBarAccountMenu from './NavBarAccountMenu';

function createMapStateToProps() {
  return createSelector(
    selectUser,
    selectCloudServer,
    createUserFeatureSelector(features.HOME_LIBRARY_ACCESS_LABELS),
    createUserFeatureSelector(features.ALLOW_COMMUNITY_FEATURES),
    (user, cloudServer, hasHomeLibraryAccessLabels: boolean) => {
      return {
        username: user.title,
        isSignedIn: user.signedIn,
        isRestricted: user.isRestricted,
        isCloudServerConnected: cloudServer.isConnected,
        isAnonymousUser: !!user.anonymous,
        isFullUser: user.isFullUser,
        appsURL: cloudServer
          ? cloudServer.appsUrl
          : 'https://plex.tv/downloads',
        hasHomeLibraryAccessLabels,
      };
    }
  );
}

// @ts-expect-error ts-migrate(6133) FIXME: 'props' is declared but its value is never read.
function createMapDispatchToProps(dispatch: any, props: any) {
  return {
    onSignInPress(event: any) {
      event.preventDefault();

      navigateToRoute('login');
    },
    onSignOutPress(event: any) {
      event.preventDefault();

      navigateToRoute('logout');
    },
  };
}

const NavBarAccountMenuConnector = connect(
  createMapStateToProps,
  createMapDispatchToProps
)(NavBarAccountMenu);

export default NavBarAccountMenuConnector;
