/* eslint-plugin-disable @typescript-eslint */

import posterPlaceholders from 'app/js/utils/posterPlaceholders';
import posterSizes from 'app/js/utils/posterSizes';
import BaseImage from 'app/js/views/images/BaseImage';

const MediaPosterImage = BaseImage.extend({
  //
  // Properties

  background: true,

  //
  // Init

  setup(options: any) {
    this.minSize = true;
    this.type = options.type;
    this.modelAttribute = options.modelAttribute || 'thumb';

    if (options.placeholder !== false) {
      if (this.model && this.model.get('isFolder')) {
        this.placeholderClass = posterPlaceholders.folder;
      } else if (!options.placeholderClass) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        this.placeholderClass = posterPlaceholders[this.type];
      }
    }

    // eslint-disable-next-line prefer-rest-params
    BaseImage.prototype.setup.apply(this, arguments);
  },

  setDimensions(options: any) {
    if (options.width) {
      options.height = posterSizes.getPosterHeight(this.type, options.width);
    } else if (options.height) {
      options.width = posterSizes.getPosterWidth(this.type, options.height);
    }

    BaseImage.prototype.setDimensions.call(this, options);
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
  setModel(model: any, options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseImage.prototype.setModel.apply(this, arguments);

    if (this.model && this.model.get('isFolder')) {
      this.placeholderClass = posterPlaceholders.folder;
    }
  },
});

export default MediaPosterImage;
