/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import canUseDOM from 'app/js/utils/device/canUseDOM';
import assert from 'app/js/utils/log/assert';
import getActiveElementUID, {
  emptyElementUID,
} from 'app/js/views/v3/helpers/focus/getActiveElementUID';
import getUniqueElementID from 'app/js/views/v3/helpers/getUniqueElementID';
import {
  IS_ACTIVE,
  IS_DISABLED,
  IS_FOCUSED,
} from 'app/js/views/v3/helpers/modifierClassNames';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import styles from './Link.css';

export interface LinkProps {
  component?: string;
  className?: string;
  download?: string;
  to?: string;
  target?: string;
  tabIndex?: string;
  role?: string;
  kind?: any; // TODO: PropTypes.oneOf(kinds.all)
  size?: any; // TODO: PropTypes.oneOf(sizes.all)
  isSelected?: boolean;
  isDisabled?: boolean;
  allowFocusOnPress?: boolean;
  focusOnEnabled?: boolean;
  onPress?: (event: any) => void;
  onTouchStart?: (event: any) => void;
  onTouchMove?: (event: any) => void;
  onTouchEnd?: (event: any) => void;
  onTouchCancel?: (event: any) => void;
  onMouseDown?: (event: any) => void;
  onMouseUp?: (event: any) => void;
  onMouseLeave?: (event: any) => void;
  onClick?: (event: any) => void;
  onFocus?: (event: any) => void;
  onBlur?: (event: any) => void;
}

let lastActiveElementUID = emptyElementUID;

if (canUseDOM) {
  window.addEventListener('blur', () => {
    // See: https://github.com/plexinc/plex-web-client/issues/12836
    lastActiveElementUID = getActiveElementUID();
  });
}

class Link extends Component<LinkProps> {
  static defaultProps = {
    component: 'button',
    kind: kinds.DEFAULT,
    isSelected: false,
    isDisabled: false,
    allowFocusOnPress: false,
  };
  // eslint-disable-next-line react/sort-comp
  _isActive: any;
  _isFocused: any;
  _isTouch: any;
  _preventFocus: any;
  _preventOnClick: any;
  _ref: any;
  _uid: any;

  //
  // Lifecycle

  constructor(props: LinkProps, context: any) {
    super(props, context);
    __DEV__ &&
      assert(
        !props.onClick,
        'Use `onPress` instead of `onClick` for reliable behavior on touch screens'
      );
    this._preventFocus = false;
    this._preventOnClick = false;
    this._isTouch = false;
    this._isActive = false;
    this._isFocused = false;
    this._uid = getUniqueElementID();
    this.state = {
      isActive: false,
      isFocused: false,
    };
    this._ref = React.createRef();
  }

  componentDidMount() {
    if (!(this.props as any).isDisabled && (this.props as any).focusOnEnabled) {
      this._ref.current.focus();
    }
  }

  shouldComponentUpdate(nextProps: any, nextState: any) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  componentDidUpdate(prevProps: any) {
    const shouldFocus =
      (this.props as any).focusOnEnabled && !(this.props as any).isDisabled;
    const previousShouldFocus =
      prevProps.focusOnEnabled && !prevProps.isDisabled;
    if (shouldFocus && !previousShouldFocus) {
      this._ref.current.focus();
    }
  }

  //
  // Control

  _activate(isActive: any) {
    // `setState` is async, so events may trigger listeners before
    // `this.state` reflects the current state. Use instance properties to
    // avoid this pitfall.
    this._isActive = isActive;
    this.setState({ isActive });
  }

  _focus(isFocused: any) {
    // `setState` is async, so events may trigger listeners before
    // `this.state` reflects the current state. Use instance properties to
    // avoid this pitfall.
    this._isFocused = isFocused;
    this.setState({ isFocused });
  }

  _shouldPreventFocus() {
    if ((this.props as any).allowFocusOnPress) {
      return false;
    }
    const activeElementUID = getActiveElementUID();
    const isAlreadyFocused = activeElementUID === this._uid;
    return !isAlreadyFocused;
  }

  _press(event: any) {
    const { onPress } = this.props;
    if (onPress) {
      onPress(event);
    }
  }

  //
  // Listeners

  onTouchStart = (event: any) => {
    this._isTouch = true;
    this._preventFocus = this._shouldPreventFocus();
    if (event.touches.length === 1) {
      this._activate(true);
    }
    if ((this.props as any).onTouchStart) {
      (this.props as any).onTouchStart(event);
    }
  };

  onTouchMove = (event: any) => {
    // The user might be scrolling, so don't consider this touch as a press.
    this._activate(false);
    if ((this.props as any).onTouchMove) {
      (this.props as any).onTouchMove(event);
    }
  };

  onTouchEnd = (event: any) => {
    const { to, target, isDisabled, onTouchEnd } = this.props;
    if (this._isActive && !isDisabled) {
      // If the pressable component is a link, navigate on touch end to avoid
      // the touch delay on some devices, which could interfere with app
      // behavior (e.g. a menu closing on press).
      if (to && (!target || target === '_self')) {
        event.preventDefault();
        window.location.href = to;
      }
      this._press(event);
    }
    this._activate(false);
    if (onTouchEnd) {
      onTouchEnd(event);
    }
  };

  onTouchCancel = (event: any) => {
    this._isTouch = false;
    this._activate(false);
    if ((this.props as any).onTouchCancel) {
      (this.props as any).onTouchCancel(event);
    }
  };

  onMouseDown = (event: any) => {
    if (this._isTouch) {
      return;
    }
    this._preventFocus = this._shouldPreventFocus();
    if (event.button === 0) {
      this._activate(true);
    }
    if ((this.props as any).onMouseDown) {
      (this.props as any).onMouseDown(event);
    }
  };

  onMouseUp = (event: any) => {
    if (this._isTouch) {
      return;
    }
    const { isDisabled, onMouseUp } = this.props;
    if (this._isActive && !isDisabled) {
      this._press(event);
      this._preventOnClick = true;
    }
    this._activate(false);
    if (onMouseUp) {
      onMouseUp(event);
    }
  };

  onMouseLeave = (event: any) => {
    if (this._isTouch) {
      return;
    }
    // The mouseup event does not fire for the pressable component if no
    // longer hovered over the component. Cancel the press if the mouse leaves
    // the component so the active state does not stick. Do not actually set
    // _isActive because there is an issue in Edge on touch screens that fires
    // onMouseUp and onMouseLeave before onMouseDown when touching a child of
    // the element.
    if (this._isActive) {
      this.setState({ isActive: false });
    }
    if ((this.props as any).onMouseLeave) {
      (this.props as any).onMouseLeave(event);
    }
  };

  onClick = (event: any) => {
    // If the browser does not focus on click, reset the `_preventFocus` flag
    // so it does not prevent future focus events.
    this._preventFocus = false;
    const { isDisabled } = this.props;
    if (
      !this._preventOnClick &&
      !this._isTouch &&
      !this._isActive &&
      this._isFocused &&
      !isDisabled
    ) {
      this._press(event);
    }
    this._preventOnClick = false;
    // Reset the `_isTouch` flag after the click event in case the user is
    // transitioning between a mouse and touch.
    this._isTouch = false;
  };

  onFocus = (event: any) => {
    if (this._preventFocus) {
      this._preventFocus = false;
      return;
    }
    if (this._uid === lastActiveElementUID) {
      lastActiveElementUID = emptyElementUID;
      return;
    }
    this._focus(true);
    if ((this.props as any).onFocus) {
      (this.props as any).onFocus(event);
    }
  };

  onBlur = (event: any) => {
    this._focus(false);
    if ((this.props as any).onBlur) {
      (this.props as any).onBlur(event);
    }
  };

  //
  // Render

  render() {
    const {
      className,
      to,
      kind,
      size,
      isSelected,
      isDisabled,
      component,
      allowFocusOnPress,
      role,
      focusOnEnabled,
      onPress,
      ...otherProps
    } = this.props;

    // @ts-expect-error ts-migrate(2339) FIXME: Property 'isActive' does not exist on type 'Readon... Remove this comment to see the full error message
    const { isActive, isFocused } = this.state;
    const linkProps = { 'data-uid': this._uid };

    let el = component;

    if (to) {
      el = 'a';
      (linkProps as any).href = to;
    }
    if (role) {
      (linkProps as any).role = role;
    } else {
      (linkProps as any).role = to ? 'link' : 'button';
    }

    (linkProps as any).className = classNames(
      className,
      styles.link,
      styles[kind],
      size && styles[size],
      isSelected && styles.isSelected,
      isActive && IS_ACTIVE,
      isFocused && IS_FOCUSED,
      isDisabled && IS_DISABLED
    );

    if (el === 'button' || el === 'input') {
      (linkProps as any).type = (otherProps as any).type || 'button';
      (linkProps as any).disabled = isDisabled;
    }

    return el
      ? React.createElement(el, {
          ...otherProps,
          ...linkProps,
          ref: this._ref,
          onTouchStart: this.onTouchStart,
          onTouchMove: this.onTouchMove,
          onTouchEnd: this.onTouchEnd,
          onTouchCancel: this.onTouchCancel,
          onMouseDown: this.onMouseDown,
          onMouseUp: this.onMouseUp,
          onMouseLeave: this.onMouseLeave,
          onClick: this.onClick,
          onFocus: this.onFocus,
          onBlur: this.onBlur,
        })
      : null;
  }
}

export default Link;
