import { LIBRARY } from 'app/js/utils/constants/providerIdentifiers';
import { CLOUD_SERVER_ID } from 'app/js/utils/constants/serverIDs';

interface SourceOptions {
  serverIdentifier: string;
  providerIdentifier?: string;
  key?: string;
}

export default function createSourceFromIdentifiers(
  options: SourceOptions
): string {
  const { providerIdentifier = LIBRARY, serverIdentifier, key } = options;

  const isServer = serverIdentifier !== CLOUD_SERVER_ID;

  // See: https://docs.plex.tv/docs/api/media-providers#header-sources
  let source = isServer
    ? `server://${serverIdentifier}/${providerIdentifier}`
    : `provider://${providerIdentifier}`;

  if (key) {
    source = `${source}${key}`;
  }

  return source;
}
