import classNames from 'classnames';
import React, { FunctionComponent, ReactElement } from 'react';
import { IconProps } from '@plex/react-chroma-icon';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import EmptyPageDescription from 'app/js/views/v3/components/pages/empty/EmptyPageDescription';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import styles from 'app/js/views/v3/components/pages/empty/EmptyPage.css';

export interface EmptyPageProps {
  className?: string;
  contentClassName?: string;
  iconClassName?: string;
  titleClassName?: string;
  footerClassName?: string;
  children?: React.ReactNode;
  scrollDirection?: any;
  iconComponent?: FunctionComponent<IconProps>;
  iconSrc?: string;
  kind?: any; // TODO: PropTypes.oneOf(kinds.all)
  title?: string;
  description?: any | string;
  footer?: string;
}

function EmptyPage(props: EmptyPageProps): ReactElement {
  const {
    className,
    contentClassName,
    iconClassName,
    titleClassName,
    footerClassName,
    children,
    kind,
    scrollDirection,
    iconComponent: Icon,
    iconSrc,
    title,
    description,
    footer,
    ...otherProps
  } = props;

  return (
    // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: (Element | null)[]; className: s... Remove this comment to see the full error message
    <Scroller
      className={className}
      scrollDirection={scrollDirection}
      {...otherProps}
    >
      <div className={contentClassName}>
        {iconSrc ? <img className={styles.imgIcon} src={iconSrc} /> : null}
        {Icon ? (
          <span className={classNames(iconClassName, styles[kind])}>
            <Icon />
          </span>
        ) : null}
        {title ? <div className={titleClassName}>{title}</div> : null}
        {description ? (
          <EmptyPageDescription>{description}</EmptyPageDescription>
        ) : null}
        {children}
      </div>

      {footer ? <div className={footerClassName}>{footer}</div> : null}
    </Scroller>
  );
}

EmptyPage.defaultProps = {
  className: styles.emptyPage,
  contentClassName: styles.content,
  iconClassName: styles.icon,
  titleClassName: styles.title,
  footerClassName: styles.footer,
  kind: kinds.DEFAULT,
};

export default EmptyPage;
