/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import parser2 from 'app/js/utils/parser2';

const SEPARATOR = '|';

export const settingsTypes = {
  INT: 'int',
  TEXT: 'text',
  BOOL: 'bool',
  ENUM: 'enum',
  DOUBLE: 'double',
};

function parseValueType(type: any, value: any) {
  switch (type) {
    case settingsTypes.INT:
    case settingsTypes.ENUM:
      return parser2.parseInteger(value);
    case settingsTypes.BOOL:
      return parser2.parseBoolean(value);
    default:
      return value;
  }
}

const ServerSettingModel = AbstractModel.extend({
  //
  // Properties

  idAttribute: 'id',
  url: '/:/prefs',
  context: ['server'],

  sessionAttrs: {
    isSaving: attrTypes.boolean,
  },

  syncedAttrs: {
    id: attrTypes.string,
    label: attrTypes.string,
    default: attrTypes.oneOfType(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 4.
      attrTypes.integer,
      attrTypes.string,
      attrTypes.boolean,
      attrTypes.array
    ),
    summary: attrTypes.string,
    value: {
      type: attrTypes.oneOfType(
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 4.
        attrTypes.integer,
        attrTypes.string,
        attrTypes.boolean,
        attrTypes.array
      ),
      save: true,
    },
    type: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 5.
      settingsTypes.INT,
      settingsTypes.TEXT,
      settingsTypes.BOOL,
      settingsTypes.ENUM,
      settingsTypes.DOUBLE
    ),
    group: attrTypes.string,
    advanced: attrTypes.boolean,
    option: attrTypes.string,
    hidden: attrTypes.boolean,
    enumValues: attrTypes.array,
    values: attrTypes.string,
  },

  defaults: {
    isSaving: false,
    type: 'string',
    summary: '',
    label: '',
    group: '',
  },

  //
  // Control

  augmentResponse(response: any) {
    response.value = parseValueType(response.type, response.value);
    response.advanced = parseValueType(settingsTypes.BOOL, response.advanced);
    response.hidden = parseValueType(settingsTypes.BOOL, response.hidden);

    if (response.enumValues && !Array.isArray(response.enumValues)) {
      response.enumValues = response.enumValues
        .split(SEPARATOR)
        .map((valueStr: any) => {
          const [value, label = value] = valueStr.split(':');

          return {
            label,
            value: parseValueType(response.type, value),
          };
        });
    }

    if (
      response.type === settingsTypes.ENUM &&
      response.values &&
      !Array.isArray(response.values)
    ) {
      let i = 0;

      response.enumValues = response.values
        .split(SEPARATOR)
        .map((valueStr: any) => {
          return {
            label: valueStr,
            value: i++,
          };
        });
    }

    return response;
  },

  getSaveParams(attrs: any) {
    // Settings are saved in a batch, not individually.  Since
    // collections don't have a save function we sort of cheat, and
    // allow the caller to set settings by calling save on a single
    // setting.

    // eslint-disable-next-line prefer-rest-params
    const params = AbstractModel.prototype.getSaveParams.apply(this, arguments);

    if (attrs && !attrs.hasOwnProperty('value')) {
      // Ensure attributes that are saved via params are not passed
      // through to Backbone's save.
      Object.keys(params).forEach((key) => delete attrs[key]);
      return params;
    } else {
      return {
        [this.get('id')]: params.value,
      };
    }
  },
});

export default ServerSettingModel;
