import PropTypes from 'prop-types';
import { Component } from 'react';
import { connect } from 'react-redux';
import { redirectToUrl } from 'app/js/stores/actions/appActions';

const mapDispatchToProps = {
  dispatchRedirectToUrl: redirectToUrl,
};
interface ConnectedRedirectToProps {
  to: string;
  replace?: boolean;
  dispatchRedirectToUrl: (...args: any[]) => any;
}
class RedirectToConnector extends Component<ConnectedRedirectToProps> {
  static propTypes = {
    to: PropTypes.string.isRequired,
    replace: PropTypes.bool,
    dispatchRedirectToUrl: PropTypes.func.isRequired,
  };
  //
  // Lifecycle
  componentDidMount() {
    const { to, replace } = this.props;
    this.props.dispatchRedirectToUrl({ url: to, replace });
  }
  //
  // Render
  render() {
    return null;
  }
}
export default connect(null, mapDispatchToProps)(RedirectToConnector);
