/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import HomeHubCollection from 'app/js/models/metadata/HomeHubCollection';
import random from 'app/js/utils/string/random';

const HomeSettingsModel = AbstractModel.extend({
  //
  // Properties

  name: 'HomeSettingsModel',

  syncedAttrs: {
    hasCustomizations: attrTypes.boolean.isRequired,
    hasSeededPreferredServerContent: attrTypes.boolean.isRequired,
    hasSeededProviderPromotedContent: attrTypes.boolean.isRequired,
    preferredServerID: attrTypes.string,
    settingsKey: attrTypes.string.isRequired,
    version: attrTypes.integer.isRequired,
  },

  syncedRelations: {
    hubs: {
      relationClass: HomeHubCollection,
    },
  },

  defaults() {
    return {
      hasCustomizations: false,
      hasSeededPreferredServerContent: false,
      hasSeededProviderPromotedContent: false,
      settingsKey: random(),
      // This value needs to be updated anytime we make a format change in the
      // settings model or the HomeHubModel. It's an attribute that can then be
      // included in logic that converts cached home settings to a newer
      // version's format.
      version: 1,
    };
  },
});

export default HomeSettingsModel;
