import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
  HubListState,
  HubOptions,
  HubsFetchFailedAction,
  HubsFetchRequestedAction,
  HubsFetchSucceededAction,
  LibraryManagementState,
  PromoteChangedAction,
  PromoteRequestedAction,
  PublishedFetchFailedAction,
  PublishedFetchRequestedAction,
  PublishedFetchSucceededAction,
  PublishedUpdateFailedAction,
  PublishedUpdateRequestedAction,
  PublishedUpdateSucceededAction,
  RemoveFailedAction,
  RemoveRequestedAction,
  RemoveSucceededAction,
  ReorderFailedAction,
  ReorderRequestedAction,
  ReorderSucceededAction,
  ResetFailedAction,
  ResetRequestedAction,
  ResetSucceededAction,
  SettingsFetchFailedAction,
  SettingsFetchRequestedAction,
  SettingsFetchSucceededAction,
  SettingsUpdateFailedAction,
  SettingsUpdateRequestedAction,
  SettingsUpdateSucceededAction,
} from 'app/js/v4/features/libraryManagement/libraryManagementTypes';
import { PlainError } from 'app/js/v4/utils/data/api';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';

const initialState: LibraryManagementState = {
  hubs: {},
  publishedItems: {},
  settings: {},
};

function hubsChangeRequested(
  hubs: HubListState | undefined,
  options: HubOptions[]
): void {
  if (hubs) {
    markPending(hubs);
    hubs.options = options;
  }
}

function hubsChangeSucceeded(hubs: HubListState | undefined): void {
  if (hubs) {
    markPopulated(hubs);
  }
}

function hubsChangeFailed(
  hubs: HubListState | undefined,
  options: HubOptions[],
  error: PlainError
): void {
  if (hubs) {
    markFailed(hubs, error);
    hubs.options = options;
  }
}

const libraryManagementSlice = createSlice({
  name: 'libraryManagement',
  initialState,

  reducers: {
    settingsFetchRequested(state, { payload }: SettingsFetchRequestedAction) {
      state.settings[payload.server.machineIdentifier] = {
        isPending: true,
        isPopulated: false,
        isUpdating: false,
        error: null,
      };
    },

    settingsFetchSucceeded(state, { payload }: SettingsFetchSucceededAction) {
      const settings = state.settings[payload.server.machineIdentifier];

      if (settings) {
        settings.isMergeRecentlyAddedEnabled =
          payload.isMergeRecentlyAddedEnabled;
        markPopulated(settings);
      }
    },

    settingsFetchFailed(state, { payload }: SettingsFetchFailedAction) {
      const settings = state.settings[payload.server.machineIdentifier];

      if (settings) {
        markFailed(settings, payload.error);
      }
    },

    settingsUpdateRequested(state, { payload }: SettingsUpdateRequestedAction) {
      const settings = state.settings[payload.server.machineIdentifier];

      if (settings) {
        settings.isUpdating = true;
      }
    },

    settingsUpdateSucceeded(state, { payload }: SettingsUpdateSucceededAction) {
      const settings = state.settings[payload.server.machineIdentifier];

      if (settings) {
        settings.isMergeRecentlyAddedEnabled =
          payload.isMergeRecentlyAddedEnabled;
        settings.isUpdating = false;
      }
    },

    settingsUpdateFailed(state, { payload }: SettingsUpdateFailedAction) {
      const settings = state.settings[payload.server.machineIdentifier];

      if (settings) {
        settings.error = payload.error;
        settings.isUpdating = false;
      }
    },

    resetHubs(_state, _action: PayloadAction) {
      // Handled by saga, no state change
    },

    hubsFetchRequested(state, { payload }: HubsFetchRequestedAction) {
      state.hubs[payload.sourceURI] = {
        options: state.hubs[payload.sourceURI]?.options || [],
        isPending: true,
        isPopulated: false,
        isResetting: false,
        error: null,
      };
    },

    hubsFetchSucceeded(state, { payload }: HubsFetchSucceededAction) {
      const hubs = state.hubs[payload.sourceURI];

      if (hubs) {
        markPopulated(hubs);
        hubs.options = payload.options;
      }
    },

    hubsFetchFailed(state, { payload }: HubsFetchFailedAction) {
      const hubs = state.hubs[payload.sourceURI];

      if (hubs) {
        markFailed(hubs, payload.error);
      }
    },

    reorderRequested(state, { payload }: ReorderRequestedAction) {
      hubsChangeRequested(state.hubs[payload.sourceURI], payload.options);
    },

    reorderSucceeded(state, { payload }: ReorderSucceededAction) {
      hubsChangeSucceeded(state.hubs[payload.sourceURI]);
    },

    reorderFailed(state, { payload }: ReorderFailedAction) {
      hubsChangeFailed(
        state.hubs[payload.sourceURI],
        payload.options,
        payload.error
      );
    },

    removeRequested(state, { payload }: RemoveRequestedAction) {
      hubsChangeRequested(state.hubs[payload.sourceURI], payload.options);
    },

    removeSucceeded(state, { payload }: RemoveSucceededAction) {
      hubsChangeSucceeded(state.hubs[payload.sourceURI]);
    },

    removeFailed(state, { payload }: RemoveFailedAction) {
      hubsChangeFailed(
        state.hubs[payload.sourceURI],
        payload.options,
        payload.error
      );
    },

    resetRequested(state, { payload }: ResetRequestedAction) {
      const hubs = state.hubs[payload.sourceURI];

      if (hubs) {
        hubs.isResetting = true;
      }
    },

    resetSucceeded(state, { payload }: ResetSucceededAction) {
      const hubs = state.hubs[payload.sourceURI];

      if (hubs) {
        hubs.isResetting = false;
        hubs.options = payload.options;
      }
    },

    resetFailed(state, { payload }: ResetFailedAction) {
      const hubs = state.hubs[payload.sourceURI];

      if (hubs) {
        hubs.isResetting = false;
      }
    },

    promoteRequested(_state, _action: PromoteRequestedAction) {
      // Handled by saga, no state change
    },

    promoteChanged(state, { payload }: PromoteChangedAction) {
      const hubs = state.hubs[payload.sourceURI];
      if (hubs) {
        const hubOptions = hubs.options.find(
          (options) => options.identifier === payload.options.identifier
        );

        if (hubOptions) {
          hubOptions.promotedToRecommended =
            payload.options.promotedToRecommended;
          hubOptions.promotedToOwnHome = payload.options.promotedToOwnHome;
          hubOptions.promotedToSharedHome =
            payload.options.promotedToSharedHome;
        }
      }
    },

    publishedFetchRequested(state, { payload }: PublishedFetchRequestedAction) {
      state.publishedItems[payload.sourceURI] = {
        isPending: true,
        isPopulated: false,
        isUpdating: false,
        error: null,
      };
    },

    publishedFetchSucceeded(state, { payload }: PublishedFetchSucceededAction) {
      const publishedItem = state.publishedItems[payload.sourceURI];

      if (publishedItem) {
        publishedItem.hub = payload.hub;
        markPopulated(publishedItem);
      }
    },

    publishedFetchFailed(state, { payload }: PublishedFetchFailedAction) {
      const publishedItem = state.publishedItems[payload.sourceURI];

      if (publishedItem) {
        markFailed(publishedItem, payload.error);
      }
    },

    publishedUpdateRequested(
      state,
      { payload }: PublishedUpdateRequestedAction
    ) {
      const publishedItem = state.publishedItems[payload.sourceURI];

      if (publishedItem) {
        publishedItem.isUpdating = true;
      }
    },

    publishedUpdateSucceeded(
      state,
      { payload }: PublishedUpdateSucceededAction
    ) {
      const publishedItem = state.publishedItems[payload.sourceURI];

      if (publishedItem) {
        publishedItem.hub = payload.hub;
        publishedItem.isUpdating = false;
      }
    },

    publishedUpdateFailed(state, { payload }: PublishedUpdateFailedAction) {
      const publishedItem = state.publishedItems[payload.sourceURI];

      if (publishedItem) {
        publishedItem.error = payload.error;
        publishedItem.isUpdating = false;
      }
    },
  },
});

export default libraryManagementSlice;
