/* eslint-plugin-disable @typescript-eslint */

import getUnixTime from 'date-fns/getUnixTime';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import ConnectionCollection from './ConnectionCollection';

const ResourceModel = AbstractModel.extend({
  //
  // Properties

  name: 'ResourceModel',
  context: ['server'],

  idAttribute: 'clientIdentifier',
  urlRoot: '/api/v2/resources',

  syncedAttrs: {
    clientIdentifier: attrTypes.string,
    name: attrTypes.string,
    sourceTitle: attrTypes.string,
    accessToken: attrTypes.string,
    product: attrTypes.string,
    productVersion: attrTypes.string,
    platform: attrTypes.string,
    platformVersion: attrTypes.string,
    device: attrTypes.string,
    provides: attrTypes.list,
    owned: attrTypes.boolean,
    home: attrTypes.boolean,
    synced: attrTypes.boolean,
    presence: attrTypes.boolean,
    publicAddressMatches: attrTypes.boolean,
    httpsRequired: attrTypes.boolean,
    createdAt: attrTypes.integer,
    lastSeenAt: attrTypes.integer,
    ownerId: attrTypes.integer,
  },

  syncedRelations: {
    connections: {
      relationClass: ConnectionCollection,
    },
  },

  //
  // Control

  augmentResponse(response: any) {
    // The old `api/resources` endpoint returned timestamps as a unix
    // timestamp. The new `api/v2/resources` endpoint returns a datetime string
    // of the form: "YYYY-MM-DDThh:mm:ssZ".
    // For backwards compatibility convert datetime strings to unix timestamps.

    if (typeof response.createdAt === 'string') {
      response.createdAt = getUnixTime(response.createdAt);
    }

    if (typeof response.lastSeenAt === 'string') {
      response.lastSeenAt = getUnixTime(response.lastSeenAt);
    }

    // The resource models from /security/resources used in
    // requestTransientResource are still XML and have a Device
    const device = response.Device;
    if (device) {
      device.connections = device.Connection;
      delete device.Connection;
      return device;
    }

    return response;
  },
});

export default ResourceModel;
