/* eslint-plugin-disable @typescript-eslint */

// Some common code references settings that are on different locations on the
// appModel depending on platform.  This settingFinder allows us to use those
// settings in common code.
//
// If a setting is added to this settingFinder, it must be added to
// settingFinder on all other platforms.
//
// Once a setting lives in the same location for all platforms, it should be
// removed from here.
export default function createSettingFinder(appModel: any) {
  const findDeviceSettingsModel = () => appModel.get('deviceSettings');
  const findUserExperienceSettingsModel = () =>
    appModel.get('user').get('experienceSettings');

  const settingModelFinders = {
    autoPlay: findUserExperienceSettingsModel,
    companion: findUserExperienceSettingsModel,
    language: findUserExperienceSettingsModel,
    shouldSyncSettings: findDeviceSettingsModel,
    subtitleColor: findUserExperienceSettingsModel,
    subtitlePosition: findUserExperienceSettingsModel,
    subtitleSize: findUserExperienceSettingsModel,
    volume: findDeviceSettingsModel,
  };

  return {
    find(attr: any) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      return settingModelFinders[attr]().get(attr);
    },
    addCompanionChangeListener(listener: any) {
      appModel.on(
        'change:user.change:experienceSettings.change:companion',
        listener
      );
    },
  };
}
