/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import * as desktopServerActions from 'app/js/stores/actions/serverActions';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import AllowHttpFallbackModal from './AllowHttpFallbackModal';

function createMapStateToProps() {
  return createSelector(createUnstableServerSelector(), (server) => {
    return {
      publicAddressMatches: server.publicAddressMatches,
    };
  });
}

const mapDispatchToProps = {
  onAllowHttpFallback: desktopServerActions.allowHttpFallback,
};

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(AllowHttpFallbackModal);
