/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import parser from 'app/js/utils/parser';
import t from 'app/js/utils/t';

function updateChannelCommand(options: any) {
  const deferred = $.Deferred();
  const channel = options.channel;
  const server = channel ? channel.server : options.server;
  const silent = options.silent;
  let url;

  // If a channel is passed in, only check for updates for that channel.
  // Otherwise, check for updates for all channels.
  if (channel) {
    const identifier = channel.get('identifier');
    const urlRoot = identifier
      ? '/system/appstore/apps/' + identifier
      : channel.get('key');
    url = urlRoot + '/update';
  } else {
    url = '/system/appstore/updates';
  }

  const xhrDeferred = $.ajax(server.getXHROptions(url));

  function resolve(response: any) {
    const json = _.isObject(response) ? response : parser.parseXML(response);

    if (!json || (!json.message && !json.Directory)) {
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
      reject();
      return;
    }

    if (json.message) {
      if (!silent) {
        dispatcher.trigger('show:alert', {
          type: 'success',
          message: json.message,
        });
      }

      deferred.resolve();
    } else {
      parser.parseArray(json, 'Directory');

      // The first Directory object in the response will have the key we
      // need to request in order to download the updates.
      const updateUrl = json.Directory[0].key;
      const updateDeferred = $.ajax(server.getXHROptions(updateUrl));

      updateDeferred.done(resolve);
      updateDeferred.fail(reject);
    }
  }

  function reject(xhr: any) {
    if (!silent) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem checking for updates.'),
      });
    }

    deferred.reject(xhr);
  }

  xhrDeferred.done(resolve);
  xhrDeferred.fail(reject);

  return deferred.promise();
}

export default updateChannelCommand;
