/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';

function deleteFileCommand(options: any) {
  const item = options.item;
  const media = options.media;
  const server = item.server;

  let promise;

  // If a specific media was selected, we need to make a special request
  // to not delete other media associated with the metadata item.

  if (media) {
    const url = '/library/metadata/' + item.id + '/media/' + media.id;
    const xhrOptions = _.extend(server.getXHROptions(url), { type: 'DELETE' });

    promise = $.ajax(xhrOptions);

    promise.done(function () {
      // If specific media item was deleted, the metadata item will remain
      // intact but we should remove that media item from the array.
      const mediaItems = options.item.get('Media');
      mediaItems.splice(_.indexOf(mediaItems, media), 1);

      // Manually trigger the change event so views are updated.
      item.trigger('change:Media', item, item.get('Media'));
    });
  } else {
    promise = item.destroy({ wait: true });
  }

  promise.fail(function (xhr: any) {
    const message =
      xhr.status === 403
        ? t(
            'Ensure that media deletion is enabled for library content in your server settings.'
          )
        : t('There was a problem deleting this item.');

    dispatcher.trigger('show:alert', {
      type: 'error',
      message: message,
    });
  });

  return promise;
}

export default deleteFileCommand;
