/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import ServerModel, {
  MY_PLEX_SIGN_IN_STATE_NONE,
  MY_PLEX_SIGN_IN_STATE_OK,
} from 'app/js/models/server/ServerModel';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function getCachedServer(serverID: any) {
  const server = new ServerModel(
    { machineIdentifier: serverID },
    { onlyIfCached: true }
  );
  if (!_.isEmpty(server)) {
    return server;
  }
}
function signOutServer(server: any) {
  const cloudAccount = server.get(__PLATFORM__).get('cloudAccount');
  const promise = cloudAccount.destroy();
  promise.done(function () {
    server.set({
      accessToken: undefined,
      myPlexSigninState: MY_PLEX_SIGN_IN_STATE_NONE,
      myPlexSubscription: false,
    });
  });
  return promise;
}
function destroyServer(server: any) {
  // Remove the model from its collection.
  server.trigger('destroy', server, server.collection);
  // Completely remove the server from our cache so the same instance cannot
  // be used again and it can be garbage collected.
  server.cache.remove(server);
}
function removeServerCommand(options: any) {
  const appModel = adapters.resolve('appModel');
  const user = appModel.get('user');
  const device = options.device;
  const server =
    options.server || getCachedServer(device.get('clientIdentifier'));
  function remove() {
    let xhrDeferred;
    if (device) {
      xhrDeferred = device.destroy({ wait: true });
    } else {
      const url = '/api/servers/' + server.id;
      const xhrOptions = _.extend(user.server.getXHROptions(url), {
        type: 'DELETE',
      });
      xhrDeferred = $.ajax(xhrOptions);
    }
    if (server) {
      // If the server is already available, try to also sign the server out
      // before removing the server entirely.
      if (
        server.get('connected') &&
        server.get('myPlexSigninState') === MY_PLEX_SIGN_IN_STATE_OK
      ) {
        const cloudAccount = server.get(__PLATFORM__).get('cloudAccount');
        xhrDeferred = xhrDeferred.then(function () {
          return cloudAccount.fetch();
        });
        xhrDeferred = xhrDeferred.then(function () {
          return signOutServer(server);
        });
      }
      if (server.get('connected') === false) {
        // If the server is unavailable to begin with, immediately destroy it.
        destroyServer(server);
      } else {
        xhrDeferred.always(function () {
          // Attempt to connect to the server again. If it fails, then we
          // should remove the server completely from the app.
          dispatcher.trigger('test:server', server);
          server.populate().fail(function () {
            destroyServer(server);
          });
        });
      }
    }
    xhrDeferred.always(function () {
      confirmModal.hide();
    });
    return xhrDeferred.promise();
  }
  let message;
  if (device) {
    message = t(
      'If you remove the server, all syncs and shares associated with the server will be lost and you will need to sign the server in again to restore access to Plex.'
    );
  } else {
    message = t('Are you sure you want to remove this server?');
  }
  const modalOptions = {
    title: t('Remove Server'),
    message: message,
    confirmLabel: t('Remove'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  };
  if (device) {
    (modalOptions as any).requireDoubleConfirm = true;
    (modalOptions as any).doubleConfirmMessage = t(
      'Are you sure you want to remove this server?'
    );
    (modalOptions as any).doubleConfirmLabel = t("Yes, I'm sure");
  }
  const confirmModal = new ConfirmModal(modalOptions);
  let promise = confirmModal.promise;
  promise = promise.then(function () {
    return remove();
  });
  dispatcher.trigger('show:modal', confirmModal);
  return promise;
}
export default removeServerCommand;
