import { Server, UnstableServer } from 'app/js/v4/core/servers/serversTypes';

export default function toStableServerObject(server: UnstableServer): Server {
  return {
    entityID: server.entityID,
    machineIdentifier: server.machineIdentifier,
    accessToken: server.accessToken,
    uri: server.activeConnection ? server.activeConnection.uri : undefined,
    version: server.version,
    platform: server.platform,
    friendlyName: server.friendlyName,
    sourceTitle: server.sourceTitle,
    countryCode: server.countryCode,
    allowSync: server.allowSync,
    allowMediaDeletion: server.allowMediaDeletion,
    backgroundProcessing: server.backgroundProcessing,
    transcoderVideo: server.transcoderVideo,
    isCloud: server.isCloud,
    isEmbedded: server.isEmbedded,
    isFullServer: server.isFullServer,
    isFullOwnedServer: server.isFullOwnedServer,
    isOutOfDate: server.isOutOfDate,
    isShared: server.isShared,
    isUnavailable: server.isUnavailable,
    isUnauthorized: server.isUnauthorized,
    isConnected: server.isConnected,
    isTransient: server.isTransient,
    livetv: server.livetv,
    connectionTestResult: server.connectionTestResult,
    ownerId: server.ownerId,
    ownerFeatures: server.ownerFeatures,
  };
}
