//
// Workers

export function* discoverDevices({
  payload,
}: DevicesDiscoverRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const response: APIMediaContainerResponse = yield call(
      api.post,
      '/media/grabbers/devices/discover',
      {
        server,
        timeout: 0,
      }
    );

    yield put(
      dvrSetupSlice.actions.devicesDiscoverSucceeded({
        server,
        data: normalizeDevicesResponse({
          server,
          data: response.data?.MediaContainer,
        }),
      })
    );
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t('There was a problem discovering devices'),
      })
    );

    yield put(
      dvrSetupSlice.actions.devicesDiscoverFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

