export function* fetchDeviceChannels({
  payload,
}: DeviceChannelsFetchRequestedAction): SagaIterator {
  const { server, deviceKey } = payload;

  try {
    const response: APIMediaContainerResponse = yield call(
      api.get,
      path.join(['/media/grabbers/devices', deviceKey, 'channels']),
      {
        server,
      }
    );

    yield put(
      dvrSetupSlice.actions.deviceChannelsFetchSucceeded({
        server,
        deviceKey,
        data: response.data.MediaContainer?.DeviceChannel ?? [],
      })
    );
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t('There was a problem getting the channels for your device'),
      })
    );

    yield put(
      dvrSetupSlice.actions.deviceChannelsFetchFailed({
        deviceKey,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* scanDeviceChannels({
  payload,
}: DeviceChannelsScanRequestedAction): SagaIterator {
  const { server, deviceKey, source, prefs, network, country } = payload;

  try {
    const response: APIMediaContainerResponse = yield call(
      api.post,
      path.join(['/media/grabbers/devices', deviceKey, 'scan']),
      {
        server,
        params: {
          source,
          prefs,
          network,
          country,
        },
      }
    );

    if (response.headers['x-plex-activity']) {
      yield put(dvrSetupSlice.actions.scanPrefsAndNetworksReset());
    } else {
      const settings = normalizeMediaSettingsResponse({
        server,
        data: response.data?.MediaContainer,
      });

      if (settings?.length) {
        yield put(
          dvrSetupSlice.actions.scanPrefsAdded({
            settings,
          })
        );
      }
    }
  } catch (_error) {
    const error = api.toPlainErrorObject(_error);

    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: error?.message ?? t('There was a problem scanning channels'),
      })
    );

    yield put(dvrSetupSlice.actions.deviceChannelsScanFailed());
  }
}

export function* cancelScanDeviceChannels({
  payload,
}: DeviceChannelsCancelScanRequestedAction): SagaIterator {
  const { server, deviceKey } = payload;

  try {
    yield call(
      api.del,
      path.join(['/media/grabbers/devices', deviceKey, 'scan']),
      {
        server,
      }
    );
  } catch (_error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t('There was a problem canceling the current channel scan'),
      })
    );

    yield put(dvrSetupSlice.actions.deviceChannelsCancelScanFailed());
  }
}
export function* saveChannelMappings({
  payload,
}: DeviceChannelMappingsSaveRequestedAction): SagaIterator {
  const { server, deviceKey, mappings } = payload;

  try {
    const deviceChannels: DeviceChannelsState = yield select(
      createDeviceChannelsSelector(deviceKey)
    );

    const enabledChannels: string[] = [];
    const channelMap: Record<string, string> = {};

    Object.values(mappings).forEach(
      ({ key, channelKey, lineupIdentifier, enabled }) => {
        const deviceChannel = deviceChannels.channels.find(
          (channel) => channel.identifier === key
        );
        const drm = deviceChannel?.drm;

        if (enabled && channelKey && lineupIdentifier && !drm) {
          enabledChannels.push(key);
          channelMap[`channelMappingByKey[${key}]`] = channelKey;
          channelMap[`channelMapping[${key}]`] = lineupIdentifier;
        }
      }
    );

    yield call(
      api.put,
      path.join(['/media/grabbers/devices', deviceKey, 'channelmap']),
      {
        server,
        params: {
          channelsEnabled: enabledChannels.toString(),
          ...channelMap,
        },
      }
    );

    yield put(dvrSetupSlice.actions.deviceChannelMappingsSaveSucceeded());
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.deviceChannelMappingsSaveFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* enableDevice({
  payload,
}: DeviceEnableRequestedAction): SagaIterator {
  const { server, deviceKey, enabled, title } = payload;

  try {
    yield call(api.put, path.join(['/media/grabbers/devices', deviceKey]), {
      server,
      params: {
        title,
        enabled: enabled ? 1 : 0,
      },
    });

    yield put(
      dvrSetupSlice.actions.deviceEnableSucceeded({
        deviceKey,
        enabled,
      })
    );
  } catch {
    yield put(dvrSetupSlice.actions.deviceEnableFailed());
  }
}

export function* addDevice({
  payload,
}: DeviceAddRequestedAction): SagaIterator {
  const { server, uri } = payload;

  let setupErrorMessage = t('There was a problem adding the device: {1}', uri);

  try {
    // When we support more than HDHR devices the user will need to pick the
    // device type instead of hardcoding 'tv.plex.grabbers.hdhomerun'
    const response: APIMediaContainerResponse = yield call(
      api.post,
      '/media/grabbers/tv.plex.grabbers.hdhomerun/devices',
      {
        server,
        params: {
          uri,
        },
      }
    );

    const devices = normalizeDevicesResponse({
      server,
      data: response.data?.MediaContainer,
    });

    if (devices.length === 0) {
      setupErrorMessage = t('No device found at {1}', uri);

      throw new Error();
    } else {
      yield put(
        dvrSetupSlice.actions.deviceAddSucceeded({
          server,
          data: devices[0],
        })
      );
    }
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: setupErrorMessage,
      })
    );

    yield put(
      dvrSetupSlice.actions.deviceAddFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchDVR({ payload }: DVRFetchRequestedAction): SagaIterator {
  const { server, dvrID } = payload;

  try {
    const requestPath = dvrID
      ? path.join(['/livetv/dvrs', dvrID])
      : '/livetv/dvrs';

    const response: APIMediaContainerResponse = yield call(
      api.get,
      requestPath,
      {
        server,
      }
    );

    yield put(
      dvrSetupSlice.actions.dvrFetchSucceeded({
        dvr: normalizeDVRResponse({
          server,
          data: response.data?.MediaContainer,
        }),
      })
    );
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.dvrFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* saveDVR({ payload }: DVRSaveRequestedAction): SagaIterator {
  const { server, device, channelMappings } = payload;

  const lineups: EPGLineup[] = yield select(selectSetupSelectedLineups);

  try {
    const { success, failure } = yield call(
      enableDeviceAndSaveChannelMappings,
      server,
      device.key,
      channelMappings,
      lineups
    );

    if (failure) {
      throw new Error();
    }

    if (success) {
      const { selectedCountry, selectedLanguage } = yield select(
        selectEPGSettings
      );

      const response: APIMediaContainerResponse = yield call(
        api.post,
        '/livetv/dvrs',
        {
          server,
          params: {
            device: device.uuid,
            lineup: lineups?.map((lineup) => lineup.uuid),
            language: selectedLanguage?.code,
            country: selectedCountry?.code,
          },
        }
      );

      yield put(
        dvrSetupSlice.actions.dvrSaveSucceeded({
          activityUUID: response.headers['x-plex-activity'],
          dvr: normalizeDVRResponse({
            server,
            data: response.data?.MediaContainer,
          }),
        })
      );

      // Refresh list of devices to avoid showing the one that was just added as
      // available in the following setup step.
      yield put(dvrSetupSlice.actions.devicesFetchRequested({ server }));

      // TODO: Remove this hack once DVR settings are moved to v4.
      yield putLegacyActionDangerously(
        fetchDVRs({ serverEntityID: server.entityID })
      );
    }
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t('There was a problem saving your DVR. Please try again.'),
      })
    );

    yield put(
      dvrSetupSlice.actions.dvrSaveFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* deleteDVR({
  payload,
}: DVRDeleteRequestedAction): SagaIterator {
  const { server, dvrID } = payload;

  try {
    yield call(api.del, path.join(['/livetv/dvrs', dvrID]), {
      server,
      timeout: 0,
    });

    yield put(dvrSetupSlice.actions.dvrDeleteSucceeded());
  } catch (error) {
    yield putLegacyActionDangerously(
      showAlert(t('There was a problem removing your DVR'))
    );
  }
}

export function* addDVRDevice({
  payload,
}: AddDVRDeviceRequestedAction): SagaIterator {
  const { server, dvrID, deviceKey, channelMappings } = payload;

  const lineups: EPGLineup[] = yield select(selectSetupSelectedLineups);

  try {
    const { success, failure } = yield call(
      enableDeviceAndSaveChannelMappings,
      server,
      deviceKey,
      channelMappings,
      lineups
    );

    if (failure) {
      throw new Error();
    }

    if (success) {
      yield call(addLineupsToDVR, server, dvrID, lineups);

      const response: APIMediaContainerResponse = yield call(
        api.put,
        path.join(['/livetv/dvrs', dvrID, 'devices', deviceKey]),
        {
          server,
        }
      );

      yield put(
        dvrSetupSlice.actions.addDVRDeviceSucceeded({
          activityUUID: response.headers['x-plex-activity'],
          dvr: normalizeDVRResponse({
            server,
            data: response.data?.MediaContainer,
          }),
        })
      );

      // Refresh list of devices to avoid showing the one that was just added as
      // available in the following setup step.
      yield put(dvrSetupSlice.actions.devicesFetchRequested({ server }));

      // TODO: Remove this hack once DVR settings are moved to v4.
      yield putLegacyActionDangerously(
        fetchDVRs({ serverEntityID: server.entityID })
      );
    }
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t(
          'There was a problem adding a new device to your DVR. Please try again.'
        ),
      })
    );

    yield put(
      dvrSetupSlice.actions.addDVRDeviceFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* removeDVRDevice({
  payload,
}: RemoveDVRDeviceRequestedAction): SagaIterator {
  const { server, dvrID, deviceKey } = payload;

  try {
    yield call(
      api.del,
      path.join(['/livetv/dvrs', dvrID, 'devices', deviceKey]),
      {
        server,
      }
    );

    yield put(dvrSetupSlice.actions.removeDVRDeviceSucceeded({ deviceKey }));
  } catch (error) {
    yield putLegacyActionDangerously(
      showAlert(t('There was a problem removing a DVR device'))
    );
    yield put(dvrSetupSlice.actions.removeDVRDeviceFailed());
  }
}

//
// Helper Workers

function* addLineupsToDVR(
  server: Server,
  dvrID: string,
  lineups: EPGLineup[]
): SagaIterator {
  const { selectedDVR }: { selectedDVR: SelectedDVRState } = yield select(
    selectDVRSetup
  );

  // Attempting to add a lineup that's already been added to a DVR will result
  // in a request that hangs indefinitely. To avoid this we filter the list of
  // lineups against those already added to the DVR.
  let lineupsToAdd = lineups;
  if (selectedDVR.dvr?.Lineup) {
    lineupsToAdd = lineupsToAdd.filter(
      (lineup) =>
        !selectedDVR.dvr?.Lineup?.find(
          (dvrLineup: DVRLineup) => dvrLineup.id === lineup.uuid
        )
    );
  }

  yield all(
    lineupsToAdd.map((lineup) =>
      call(api.put, path.join(['/livetv/dvrs', dvrID, 'lineups']), {
        server,
        params: {
          lineup: lineup.uuid,
        },
      })
    )
  );
}

function* enableDeviceAndSaveChannelMappings(
  server: Server,
  deviceKey: string,
  mappings: ChannelMappingFormData[],
  lineups?: EPGLineup[]
): SagaIterator {
  yield put(
    dvrSetupSlice.actions.deviceChannelMappingsSaveRequested({
      server,
      deviceKey,
      mappings,
    })
  );

  yield put(
    dvrSetupSlice.actions.deviceEnableRequested({
      server,
      deviceKey,
      enabled: true,
      title: lineups?.[0]?.title,
    })
  );

  // If any of the above dispatched actions fails, `failure` will by truthy. If
  // both actions succeed, `success` will be truthy.  Workers that use this
  // helper rely on these values to either continue their logic or throw an
  // error.
  return yield race({
    success: all([
      take(dvrSetupSlice.actions.deviceChannelMappingsSaveSucceeded),
      take(dvrSetupSlice.actions.deviceEnableSucceeded),
    ]),

    failure: race([
      take(dvrSetupSlice.actions.deviceChannelMappingsSaveFailed),
      take(dvrSetupSlice.actions.deviceEnableFailed),
    ]),
  });
}

//
// Watchers

export function* watchDevicesDiscoverRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      dvrSetupSlice.actions.devicesDiscoverRequested,
      discoverDevices
    );

    yield take(dvrSetupSlice.actions.reset);
    yield cancel(task);
  }
}

export function* watchDevicesFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      dvrSetupSlice.actions.devicesFetchRequested,
      fetchDevices
    );

    yield take(dvrSetupSlice.actions.reset);
    yield cancel(task);
  }
}

