/* eslint-plugin-disable @typescript-eslint */

function truncateMiddle(str: any, len: any, frontLen: any) {
  if (!str) {
    return '';
  }

  if (str.length > len) {
    frontLen = frontLen || Math.floor((len - 3) / 2);
    const backLen = len - 3 - frontLen;
    return str.substr(0, frontLen) + '...' + str.substr(str.length - backLen);
  } else {
    return str;
  }
}

export default truncateMiddle;
