/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';

function whenFirstSucceed(promises: any) {
  // If no promises passed, there would be no first succeeded one
  if (!promises.length) {
    return $.Deferred().reject().promise();
  }

  const result = $.Deferred();

  let promisesPending = promises.length;

  _.each(promises, function (promise) {
    promise
      .done(function () {
        promisesPending--;
        // First success resolves the result immediately
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        // eslint-disable-next-line prefer-rest-params
        result.resolveWith(this, arguments);
      })
      .fail(function () {
        promisesPending--;
        // If every promise failed, reject the result
        // If some of them succeeded before, reject() would be a noop.
        if (promisesPending === 0) {
          result.reject();
        }
      });
  });

  return result.promise();
}

export default whenFirstSucceed;
