/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import streamTypes from 'app/js/utils/types/streamTypes';
import getStreams from './getStreams';

function getSelectedAudio(media: any) {
  const streamType = streamTypes.get('audio').id;
  const selectedStreams = getStreams(media, { streamType, selected: true });

  return _.first(selectedStreams);
}

export default getSelectedAudio;
