/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { hasRegistered, resolve } from 'app/js/adapters/adapters';
import cloudServerCompanionProxy from 'app/js/managers/cloudServerCompanionProxy';
import { LEADER_STATUS_CHANGED } from 'app/js/utils/constants/localStorageLeaderEvents';
import log from 'app/js/utils/log/log';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';

let registeredClientID: any = null;
let currentCompanionState: any = null;
function isCompanionEnabled() {
  const vote = getVote();
  if (vote && !vote.isLeader()) {
    return false;
  }
  return resolve('settingFinder').find('companion');
}
function validatePlayerRegistration() {
  if (!isCompanionEnabled()) {
    return;
  }
  const appModel = resolve('appModel');
  const cloudServerModel = appModel.get('cloudServer');
  const clientID = appModel.get('clientID');
  if (
    !cloudServerModel ||
    !cloudServerModel.get('authorized') ||
    !clientID ||
    clientID === registeredClientID
  ) {
    return;
  }
  registeredClientID = clientID;
  log.info(
    '[Companion] Registering player with plex.tv, clientID="%s"',
    clientID
  );
  const companionProxy = resolve('companionProxy');
  companionProxy
    .registerPlayer(cloudServerModel, clientID)
    .done(() => {
      log.info(
        '[Companion] Registered player with plex.tv, clientID="%s"',
        clientID
      );
    })
    .fail((xhr: any) => {
      xhr || (xhr = {});
      registeredClientID = null;
      log.warn(
        '[Companion] Couldn\'t register player with plex.tv, status="%s", response="%s"',
        xhr.status,
        xhr.responseText && xhr.responseText.substr(0, 100)
      );
    });
}
function unregisterPlayer() {
  const appModel = resolve('appModel');
  const cloudServerModel = appModel.get('cloudServer');
  const clientID = appModel.get('clientID');
  if (!cloudServerModel.get('authorized') || !clientID) {
    return;
  }
  log.info(
    '[Companion] Unregistering player with plex.tv, clientID="%s"',
    clientID
  );
  const companionProxy = resolve('companionProxy');
  return companionProxy
    .unregisterPlayer(cloudServerModel, clientID)
    .done(() =>
      log.info(
        '[Companion] Unregistered player with plex.tv, clientID="%s"',
        clientID
      )
    )
    .fail((xhr = {}) =>
      log.warn(
        '[Companion] Couldn\'t unregister player with plex.tv, status="%s", response="%s"',
        (xhr as any).status,
        (xhr as any).responseText && (xhr as any).responseText.substr(0, 100)
      )
    );
}
function getVote() {
  return hasRegistered('companionVote') ? resolve('companionVote') : null;
}
function updateCompanionState() {
  const companionProxy = resolve('companionProxy');
  const companionState = isCompanionEnabled();
  if (currentCompanionState === companionState) {
    return $.Deferred().resolve().promise();
  }
  currentCompanionState = companionState;
  if (companionState) {
    cloudServerCompanionProxy.subscribe();
    return $.when(
      companionProxy.subscribe(resolve('localCompanionCommandHandler'))
    );
  } else {
    cloudServerCompanionProxy.unsubscribe();
    return $.when(companionProxy.unsubscribe(), unregisterPlayer());
  }
}
export default function companionInitializer() {
  const appModel = resolve('appModel');
  const cloudServerModel = appModel.get('cloudServer');
  // We might already have a clientID and an authorized cloud server at this
  // stage, so try registering the player now. Otherwise listen out for changes
  // after and register then.
  // See: https://github.com/plexinc/plex-web-client/issues/8635
  validatePlayerRegistration();
  appModel.on('change:clientID', validatePlayerRegistration);
  cloudServerModel.on('change:authorized', validatePlayerRegistration);
  resolve('settingFinder').addCompanionChangeListener(updateCompanionState);
  const vote = getVote();
  if (vote) {
    vote.on(LEADER_STATUS_CHANGED, (isLeader: any) => {
      log.info(
        isLeader
          ? '[Companion] Elected to poll leader role'
          : '[Companion] Lost poll leader role'
      );
      updateCompanionState();
    });
  }
  let promise;
  if (isCompanionEnabled()) {
    promise = updateCompanionState().then(() => {
      const companionProxy = resolve('companionProxy');
      const deepLinkPromise = companionProxy.getDeepLink();
      deepLinkPromise.fail(() => {
        log.warn('[Companion] Ignoring error getting deep link');
      });
      return alwaysResolve(deepLinkPromise);
    });
  } else {
    promise = $.Deferred().resolve().promise();
  }
  return promise;
}
