/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { MY_PLEX_SIGN_IN_STATE_OK } from 'app/js/models/server/ServerModel';
import BaseModal from 'app/js/views/modals/BaseModal';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/LibraryFailureModal.tpl';

const LibraryFailureModal = BaseModal.extend({
  //
  // Properties

  name: 'libraryFailureModal',
  className: 'confirm-modal modal modal-sm fade',
  template: tpl,

  events: _.extend(
    {
      'click .confirm-btn': 'hide',
    },
    BaseModal.prototype.events
  ),

  //
  // Control

  serializeData() {
    return {
      isSignedIn:
        this.model.get('myPlexSigninState') === MY_PLEX_SIGN_IN_STATE_OK,
      hasPlexSubscription: this.model.get('myPlexSubscription'),
      serverSettingsRoute: getServerSettingsRoute('settings', 'general'),
    };
  },
});

export default LibraryFailureModal;
