/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import parseBoolean from 'app/js/utils/parser/parseBoolean';
import parseInteger from 'app/js/utils/parser/parseInteger';
import parseJSON from 'app/js/utils/parser/parseJSON';

export const userSettingTypes = {
  BOOL: 'bool',
  JSON: 'json',
};

const valueParsers = {
  [userSettingTypes.BOOL]: parseBoolean,
  [userSettingTypes.JSON]: parseJSON,
};

const UserSettingModel = AbstractModel.extend({
  //
  // Properties

  name: 'UserSettingModel',

  context: ['server'],

  sessionAttrs: {
    isDirty: attrTypes.boolean,
  },

  syncedAttrs: {
    id: attrTypes.string,
    type: attrTypes.string,
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 2.
    value: attrTypes.oneOfType(attrTypes.boolean, attrTypes.object),
    hidden: attrTypes.boolean,
    updatedAt: attrTypes.integer,
  },

  defaults: {
    isDirty: false,
  },

  augmentResponse(response: any) {
    const isDirty = this.get('isDirty');
    const updatedAt = this.get('updatedAt');
    const responseUpdatedAt = parseInteger(response.updatedAt);

    const isModelNewerVersion =
      updatedAt && responseUpdatedAt && updatedAt > responseUpdatedAt;

    const isResponseNewerVersion =
      updatedAt && responseUpdatedAt && updatedAt < responseUpdatedAt;

    // Prefer our local values when they have not yet been synced successfully
    // and it is same version as what is returned from server.
    //
    // It is possible that this model is populated from the server before we
    // restore the stored settings.  In which case we prefer the values from
    // this model.
    if ((isDirty && updatedAt === responseUpdatedAt) || isModelNewerVersion) {
      return this.toJSON();
    }

    if (isDirty && isResponseNewerVersion) {
      response.isDirty = false;
    }

    const valueParser = valueParsers[response.type];

    if (valueParser) {
      response.value = valueParser(response.value);
    }

    return response;
  },
});

export default UserSettingModel;
