/* eslint-plugin-disable @typescript-eslint */

import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import path from 'app/js/utils/url/path';

export default function createLibraryPivotKey(directoryID: any, type: any) {
  const keyParts = ['/library/sections', directoryID, 'all'];

  if (type === metadataTypes.PHOTO) {
    return path.join(keyParts);
  }

  return path.join(keyParts, { type: metadataTypes.getMetadataType(type).id });
}
