/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import qs from 'qs';
import { resolve } from 'app/js/adapters/adapters';
import getAuthCodeExchangeModel from 'app/js/stores/actions/helpers/server/getAuthCodeExchangeModel';
import parseInteger from 'app/js/utils/parser/parseInteger';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import removeUrlParam from 'app/js/utils/url/removeUrlParam';
import splitUrlSearchHashPathAndParams from 'app/js/utils/url/splitUrlSearchHashPathAndParams';

function federatedAuthPINInitializer() {
  const { hashParams } = splitUrlSearchHashPathAndParams(window.location.href);
  const params = qs.parse(hashParams.substr(1));
  const pinID = parseInteger(params.pinID);

  if (pinID) {
    const appModel = resolve('appModel');
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);
    authCodeExchangeModel.set({ pinIDInitial: pinID });
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
    scheduleImmediateUpdate();

    window.history.replaceState(
      null,
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'null' is not assignable to param... Remove this comment to see the full error message
      null,
      removeUrlParam(window.location.href, 'pinID')
    );
  }

  return $.Deferred().resolve().promise();
}

export default federatedAuthPINInitializer;
