/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseClass from 'app/js/utils/BaseClass';
import capabilities from 'app/js/utils/device/capabilities';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ServerEventManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize(options: any) {
    this.name = options.name || 'ServerEventManager';
    this.appModel = options.appModel;
    this.servers = options.servers || this.appModel.get('servers');
    this.isValidServer = options.isValidServer;
    this.supportsLongPolling = options.supportsLongPolling;
    this.allowMultipleConnections = options.allowMultipleConnections;
    this.allowAppendingHeaders = options.allowAppendingHeaders;
    this.ServerEventConnection = options.ServerEventConnection;

    this.connections = {};

    this._serverRetryManager = new options.ServerRetryManager({
      appModel: this.appModel,
      callback: this.retryServerConnections.bind(this),
      logFormat:
        '[Connections] Retrying server events connection for %s in %s seconds',
    });
  },

  //
  // Control

  start() {
    if (this.allowMultipleConnections) {
      this.listenTo(this.servers, {
        'change:isResolved': this.onServerResolvedChange,
        'change:isRejected': this.onServerRejectedChange,
        remove: this.onServerRemove,
        reset: this.onServersReset,
      });
    } else {
      this.listenTo(
        this.appModel,
        'change:primaryServer',
        this.onPrimaryServerChange
      );
    }
  },

  stop() {
    this.stopListening();
    this._serverRetryManager.stopAll();
    this.disconnectAll();
    this.connections = {};
  },

  tryConnectServer(server: any) {
    const previouslyConnected = this.connections[server.id];

    if (this.isValidServer(server) && !previouslyConnected) {
      if (capabilities.hasWebSocket || this.supportsLongPolling) {
        this.connect({ server: server }).then(null, () => {
          this._serverRetryManager.start(server);
        });
      } else {
        server.trigger('socket:websocket', { error: true });
      }
    }
  },

  connect(options: any) {
    const server = options.server;
    let connection = this.connections[server.id];

    if (!connection) {
      connection = new this.ServerEventConnection({
        server: server,
        appendHeaders: this.allowAppendingHeaders,
      });

      this.connections[server.id] = connection;
      this.listenTo(connection, {
        open: this.onOpen,
        close: this.onClose,
        error: this.onClose,
      });
    }

    const promise = connection.open();
    const url = connection.socket ? connection.socket.url : connection.pollUrl;

    log(
      `[${this.name}] Opening server event connection to ${server.get(
        'debugName'
      )} at ${url}`
    );

    return promise;
  },

  disconnectAll() {
    _.each(this.connections, (connection) => {
      this.disconnect({ connection });
    });
  },

  disconnect(options: any) {
    let connection = options.connection;
    if (!connection) {
      connection = this.connections[options.server.id];
    }

    if (connection) {
      const url = connection.socket
        ? connection.socket.url
        : connection.pollUrl;
      const server = connection.server;

      log(
        `[${this.name}] Closing server event connection to ${server.get(
          'debugName'
        )} at ${url}`
      );

      // Stop listening before closing the connection so we don't attempt to
      // retry connecting.
      this.stopListening(connection);
      this._serverRetryManager.stop(connection.server);

      connection.close();

      delete this.connections[connection.server.id];
    }
  },

  retryServerConnections: function (server: any) {
    if (this.isValidServer(server) && server.get('isResolved')) {
      this.connect({ server: server });
    }
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  onPrimaryServerChange(appModel: any, primaryServer: any) {
    this.disconnectAll();

    if (primaryServer) {
      this.tryConnectServer(primaryServer);
    }
  },

  onServerResolvedChange(server: any, isResolved: any) {
    if (isResolved) {
      this.tryConnectServer(server);
    }
  },

  onServerRejectedChange(server: any, isRejected: any) {
    if (isRejected) {
      this.disconnect({ server: server });
    }
  },

  onServerRemove(server: any) {
    const connection = this.connections[server.id];

    if (connection) {
      this.disconnect({ connection: connection });

      // If the server was merged with another model, connect again using the
      // surviving server model.

      if (server.get('isMerged') && connection) {
        const mergedServer = this.servers.get(server.id);

        this.connect({ server: mergedServer });
      }
    }
  },

  onServersReset(servers: any, options: any) {
    const previousServers = options.previousModels;

    _.each(previousServers, (server) => {
      if (!servers.contains(server)) {
        this.disconnect({ server: server });
      }
    });
  },

  onOpen(server: any) {
    const connection = this.connections[server.id];
    const url = connection.socket ? connection.socket.url : connection.pollUrl;

    log.info(
      `[${this.name}] Opened server event connection to ${server.get(
        'debugName'
      )} at ${url}`
    );

    this._serverRetryManager.stop(server);
  },

  onClose(server: any) {
    log.warn(
      `[${
        this.name
      }] Unexpectedly closed server event connection to ${server.get(
        'debugName'
      )}`
    );
    this._serverRetryManager.start(server);
  },
});

export default ServerEventManager;
