/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import MediaGrabberDeviceCollection from 'app/js/models/provider/grabber/MediaGrabberDeviceCollection';
import MediaSettingCollection from 'app/js/models/subscription/MediaSettingCollection';

const DVRModel = AbstractModel.extend({
  //
  // Properties

  name: 'DVRModel',
  context: ['server'],

  rootPropertyName: 'Dvr',
  idAttribute: 'key',

  syncedAttrs: {
    key: attrTypes.integer,
  },

  sessionAttrs: {
    isSaving: attrTypes.boolean,
    saveError: attrTypes.object,
  },

  syncedRelations: {
    devices: {
      relationClass: MediaGrabberDeviceCollection,
      rootPropertyName: 'Device',
    },

    prefs: {
      relationClass: MediaSettingCollection,
      rootPropertyName: 'Setting',
      deferred: true,
      save: true,
    },
  },

  defaults: {
    isSaving: false,
    saveError: null,
  },
});

export default DVRModel;
