/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import MetadataCollection from 'app/js/models/metadata/MetadataCollection';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import * as responseTypes from 'app/js/models/responseTypes';
import MediaGrabOperationModel from 'app/js/models/subscription/MediaGrabOperationModel';
import replaceMetadataXMLElementNames from 'app/js/utils/parser/replaceMetadataXMLElementNames';
import createEntityID from 'app/js/utils/state/createEntityID';
import { LIVE_TV_SESSION } from 'app/js/utils/state/entityTypes';

const LiveTVSessionModel = AbstractModel.extend({
  //
  // Properties

  name: 'LiveTVSessionModel',
  context: ['server', 'provider'],

  entityType: LIVE_TV_SESSION,
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    // When creating a new live TV session, the model will not have a key, so
    // fallback to the model's cid so the state tree is more consistent.

    return createEntityID(
      this.entityType,
      state.key || this.cid,
      this.server.entityID()
    );
  },

  idAttribute: 'key',
  rootPropertyName: 'MediaSubscription',

  url() {
    return `/media/subscriptions/${this.get('key')}`;
  },

  sessionAttrs: {
    dvrID: attrTypes.integer,
    channelIdentifier: attrTypes.string,
    channelCallSign: attrTypes.string,
    channelTitle: attrTypes.string,
    activityUUID: attrTypes.string,
    currentPosition: attrTypes.integer,
    nextPosition: attrTypes.integer,
    lastSeekPosition: attrTypes.integer,
    startedAt: attrTypes.integer,
    captureBuffer: attrTypes.object,
  },

  syncedAttrs: {
    key: attrTypes.string,
    type: attrTypes.integer,
    title: attrTypes.string,
  },

  syncedRelations: {
    grabOperation: {
      relationClass: MediaGrabOperationModel,
      rootPropertyName: 'MediaGrabOperation',
      deferred: true,
    },
    timeline: {
      relationClass: MetadataCollection,
      rootPropertyName: 'Timeline',
      deferred: true,
    },
  },

  sessionRelations: {
    airingMetadataItem: {
      relationClass: MetadataModel,
      deferred: true,
    },

    upNextMetadataItem: {
      relationClass: MetadataModel,
      deferred: true,
    },
  },

  defaults: {
    nextPosition: null,
    lastSeekPosition: null,
  },

  //
  // Control

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'responseText' implicitly has an 'any' t... Remove this comment to see the full error message
  augmentResponseText(responseText, responseType) {
    return responseType === responseTypes.xml
      ? // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
        replaceMetadataXMLElementNames(responseText)
      : responseText;
  },

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'response' implicitly has an 'any' type.
  augmentResponse(response) {
    const mediaSubscription = response[this.rootPropertyName] || response;
    const timeline = mediaSubscription.Timeline;

    if (timeline) {
      mediaSubscription.Timeline = timeline.Metadata;
    }

    return response;
  },
});

export default LiveTVSessionModel;
