/* eslint-plugin-disable @typescript-eslint */

import Adapter from 'app/js/adapters/Adapter';

// Dimensions supply data to metrics platforms as well as to servers through
// X-Plex- request headers.
// See: `HEADER_NAMES` in `my-plex/app/models/device.rb`
// See: `metricsInitializer` for a breakdown of fields sent to Plex Metrics

const dimensionsAdapter = new Adapter({
  props: [
    'X-Plex-Client-Identifier', // A unique identifier for the client app.
    'X-Plex-Device', // The name of the device (e.g. iPhone).
    'X-Plex-Device-Name', // A friendly name for the device (e.g. Eric's iPhone).
    'X-Plex-Device-Screen-Resolution',
    'X-Plex-Device-Vendor',
    'X-Plex-Model', // Optional, the device model when available and dependable, otherwise `hosted` or `bundled`.
    'X-Plex-Platform', // The name of the app platform (e.g. iOS).
    'X-Plex-Platform-Version', // The version of the app platform.
    'X-Plex-Product', // The product name for the client app (e.g. Plex Web).
    'X-Plex-Version', // The version of the client app.
  ],
});

// Singleton
export default dimensionsAdapter;
