import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import parser from 'app/js/utils/parser';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';

function autoMatchCommand(options: {
  item: any;
  agent: any;
}): JQueryPromise<void> {
  const deferred = $.Deferred<void>();
  const item = options.item;
  const agent = options.agent;
  let params = { agent: agent.id, manual: 0 };
  const searchUrl = path.join(
    ['/library/metadata/', item.id, '/matches'],
    params
  );
  const searchOptions = _.extend(item.server.getXHROptions(searchUrl));
  const searchPromise = $.ajax(searchOptions);

  searchPromise.done(function (response) {
    const json = _.isObject(response) ? response : parser.parseXML(response);

    if (json.SearchResult) {
      let match = json.SearchResult;

      // Always match the first search result.
      if (Array.isArray(json.SearchResult)) {
        match = json.SearchResult[0];
      }

      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ guid: any; name: any; }' is not assignable... Remove this comment to see the full error message
      params = { guid: match.guid, name: match.name };

      const matchUrl = path.join(
        ['/library/metadata/', item.id, '/match'],
        params
      );
      const matchOptions = _.extend(item.server.getXHROptions(matchUrl), {
        type: 'PUT',
      });
      const matchPromise = $.ajax(matchOptions);

      matchPromise.done(function () {
        deferred.resolve();
      });

      matchPromise.fail(function () {
        dispatcher.trigger('show:alert', {
          type: 'error',
          message: t('There was a problem matching this item.'),
        });

        deferred.reject();
      });
    } else {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('No matches were found using this agent.'),
      });

      deferred.reject();
    }
  });

  searchPromise.fail(function (xhr) {
    dispatcher.trigger('show:alert', {
      type: 'error',
      message: t('There was a problem searching for matches.'),
    });

    deferred.reject(xhr);
  });

  return deferred.promise();
}

export default autoMatchCommand;
