/* eslint-plugin-disable @typescript-eslint */

function migrateHiddenAt(source: any) {
  if (source.isHidden && !source.hiddenAt) {
    source.hiddenAt = Date.now();
  }
}

export default function migrateSourceHiddenAt(userExperienceSettings: any) {
  const sidebarSettings = userExperienceSettings.sidebarSettings;
  if (sidebarSettings && sidebarSettings.pinnedSources) {
    sidebarSettings.pinnedSources.forEach(migrateHiddenAt);
  }

  const homeSettings = userExperienceSettings.homeSettings;
  if (homeSettings && homeSettings.hubs) {
    homeSettings.hubs.forEach(migrateHiddenAt);
  }

  return userExperienceSettings;
}
