/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import Cache from 'app/js/utils/cache/Cache';

const CollectionCache = Cache.extend({
  //
  // Properties

  maxCycles: 3,
  maxSize: 20,

  //
  // Control

  // @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
  get: function (key: any) {
    // eslint-disable-next-line prefer-rest-params
    const collection = Cache.prototype.get.apply(this, arguments);

    if (collection) {
      collection.every(function (model: any) {
        if (!model.cache || !model.cache.isListening) {
          return false;
        }

        // Cached collections should ensure that all of their models are
        // also in the cache when appropriate. This is because the
        // collection cache may have a different maximum number of cycles.
        model.cache.store(model);
        return true;
      });
    }

    return collection;
  },

  getItemCacheKey: function (collection: any) {
    let url = _.result(collection, 'url');

    if (collection.server) {
      url = collection.server.getScopedKey(url);
    }

    if (collection.provider) {
      url = collection.provider.getScopedKey(url);
    }

    return url;
  },
});

export default CollectionCache;
