import BaseList from 'app/js/views/lists/BaseList';
import UserSelectModalAddUserListItem from './UserSelectModalAddUserListItem';
import UserSelectModalUserListItem from './UserSelectModalUserListItem';

const UserSelectModalUserList = BaseList.extend({
  //
  // Properties

  tagName: 'ul',
  className: 'tile-list user-select-list',
  itemView: UserSelectModalUserListItem,

  onRender() {
    if (this.options.showAddUser) {
      this.addItemView(
        this.options.adminModel,
        UserSelectModalAddUserListItem,
        this.collection.length
      );
    }
  },
});

export default UserSelectModalUserList;
