import getUnixTime from 'date-fns/getUnixTime';
import subDays from 'date-fns/subDays';
import subHours from 'date-fns/subHours';
import subMonths from 'date-fns/subMonths';
import subWeeks from 'date-fns/subWeeks';
import subYears from 'date-fns/subYears';
import t from 'app/js/utils/t';

export enum TimePeriod {
  RealTime = 'real-time',
  TwelveHours = 'twelve-hours',
  Day = 'day',
  Week = 'week',
  Month = 'month',
  NinetyDays = 'ninety-days',
  Year = 'year',
  AllTime = 'all-time',
}

export enum Timespan {
  Year = 0,
  Month = 1,
  Week = 2,
  Day = 3,
  Hour = 4,
  Minutes = 5, // Currently unused
  RealTime = 6,
}

export function getTimePeriodLabel(timePeriod: TimePeriod): string {
  switch (timePeriod) {
    case TimePeriod.RealTime:
      return t('Real Time');
    case TimePeriod.TwelveHours:
      return t('Last 12 Hours');
    case TimePeriod.Day:
      return t('Last 24 Hours');
    case TimePeriod.Week:
      return t('Last 7 Days');
    case TimePeriod.Month:
      return t('Last 30 Days');
    case TimePeriod.NinetyDays:
      return t('Last 90 Days');
    case TimePeriod.Year:
      return t('Last Year');
    case TimePeriod.AllTime:
      return t('All Time');
    default:
      return '';
  }
}

export function getTimePeriodTimespan(timePeriod: TimePeriod): Timespan {
  switch (timePeriod) {
    case TimePeriod.RealTime:
      return Timespan.RealTime;
    case TimePeriod.TwelveHours:
    case TimePeriod.Day:
      return Timespan.Hour;
    case TimePeriod.Week:
      return Timespan.Day;
    case TimePeriod.Month:
      return Timespan.Week;
    case TimePeriod.NinetyDays:
      return Timespan.Week;
    case TimePeriod.Year:
    case TimePeriod.AllTime:
      return Timespan.Month;
    default:
      return Timespan.Day;
  }
}

export function getTimePeriodAfter(timePeriod: TimePeriod): number | undefined {
  switch (timePeriod) {
    case TimePeriod.TwelveHours:
      return getUnixTime(subHours(new Date(), 12));
    case TimePeriod.Day:
      return getUnixTime(subDays(new Date(), 1));
    case TimePeriod.Week:
      return getUnixTime(subWeeks(new Date(), 1));
    case TimePeriod.Month:
      return getUnixTime(subMonths(new Date(), 1));
    case TimePeriod.NinetyDays:
      return getUnixTime(subDays(new Date(), 90));
    case TimePeriod.Year:
      return getUnixTime(subYears(new Date(), 1));
    case TimePeriod.AllTime:
      return 0;
    default:
      // RealTime
      return;
  }
}

export function getTimePeriodDateFormat(
  timePeriod: TimePeriod,
  useLongFormat?: boolean
): string {
  switch (timePeriod) {
    case TimePeriod.TwelveHours:
      return 'H:00';
    case TimePeriod.Day:
      return useLongFormat ? 'H:00' : 'HH';
    case TimePeriod.Week:
    case TimePeriod.Month:
    case TimePeriod.NinetyDays:
      return useLongFormat ? 'MMMM do' : 'MMM d';
    case TimePeriod.Year:
      return useLongFormat ? 'MMMM' : 'MMM';
    case TimePeriod.AllTime:
      return useLongFormat ? 'MMMM yyyy' : 'MMM yy';
    default:
      return useLongFormat ? 'MMMM do' : 'MMM d';
  }
}
