/* eslint-plugin-disable @typescript-eslint */

import isEmbeddedServer from 'app/js/views/v3/helpers/isEmbeddedServer';
import getProviderRoute from 'app/js/views/v3/helpers/routes/getProviderRoute';

export default function getSourceRoute(
  server: any,
  provider: any,
  sourceDirectory: any,
  params = {},
  path?: any
) {
  if (isEmbeddedServer(server)) {
    return;
  }

  if (!sourceDirectory) {
    return getProviderRoute(server, provider, params, path);
  }

  return getProviderRoute(
    server,
    provider,
    {
      source: sourceDirectory.id,
      ...params,
    },
    path
  );
}
