/* eslint-plugin-disable @typescript-eslint */

import format from 'date-fns/format';
import getDate from 'date-fns/getDate';
import getMonth from 'date-fns/getMonth';
import toDate from 'date-fns/toDate';
import leadingZero from 'app/js/utils/number/leadingZero';
import t from 'app/js/utils/t';

function getSeasonEpisodeIndex(options: any) {
  let seasonNumber = options.seasonIndex;
  let episodeNumber = options.episodeIndex;

  // If the seasonIndex is greater than 1000, assume it's a year.
  // Separate zero equality check needed because `null >= 0 === true`
  if ((episodeNumber > 0 || episodeNumber === 0) && seasonNumber < 1000) {
    if (!options.noLeadingZero) {
      seasonNumber = leadingZero(seasonNumber);
      episodeNumber = leadingZero(episodeNumber);
    }

    const separator = options.separator;

    if (options.useLongName) {
      const translatedSeason = t('Season {seasonNumber}', { seasonNumber });
      const translatedEpisode = t('Episode {episodeNumber}', { episodeNumber });

      return `${translatedSeason}${separator || ' '}${translatedEpisode}`;
    } else {
      const translatedSeason = t('S{seasonNumber}', { seasonNumber });
      const translatedEpisode = t('E{episodeNumber}', { episodeNumber });

      return `${translatedSeason}${separator || ''}${translatedEpisode}`;
    }
  } else if (options.date) {
    const date = toDate(options.date);

    return t('{monthNumber}/{dayNumber}/{shortYear}', {
      monthNumber: getMonth(date) + 1,
      dayNumber: getDate(date),
      shortYear: format(date, 'yy'),
    });
  } else {
    return '';
  }
}

export default getSeasonEpisodeIndex;
