/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import storageAdapter from 'app/js/adapters/storageAdapter';
import migrateExperienceSettingsToAllStoredUsers from 'app/js/initializers/migrations/migrateExperienceSettingsToAllStoredUsers';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import * as jsonStorage from 'app/js/utils/storage/jsonStorage';

// Some existing settings are specific to a user and cannot be migrated.  eg:
// homeSettings refers to hubs that users might not have access too.
const userSpecificAttrs = ['homeSettings'];

// appModel.experienceSettings -> appModel.user.experienceSettings
//
// This migration handles the migration of settings from, and the deletion of
// ExperienceSettingsModel.  Previously there were platform specific experience
// settings but now there is only a single UserExperienceSettingsModel.
//
// If there are platform specific fields that need to be migrated from
// appModel.experienceSettings they need to be done before this migration runs.

function migrateExperienceSettings() {
  const StoredUserExperienceSettingsModel = resolve(
    'StoredUserExperienceSettingsModel'
  );
  const allAttrs = Object.keys({
    ...StoredUserExperienceSettingsModel.prototype.syncedAttrs,
    ...StoredUserExperienceSettingsModel.prototype.syncedRelations,
  });
  const migrationAttrs = _.without(allAttrs, ...userSpecificAttrs);

  const promise = jsonStorage
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    .getJSON('experience')
    .then((existingExperienceSettings: any) => {
      const migratedSettings = _.pick(
        existingExperienceSettings,
        migrationAttrs
      );

      return migrateExperienceSettingsToAllStoredUsers(migratedSettings).then(
        () => {
          // All attrs from experienceSettings have been migrated now, so it can be
          // deleted.
          return storageAdapter.remove('experience');
        }
      );
    });

  return alwaysResolve(promise);
}

export default migrateExperienceSettings;
