/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import config from 'app/js/models/config';
import * as ip from 'app/js/utils/url/ip';
import ConnectionModel from './ConnectionModel';

export const connectionComparator = (a: any, b: any) => {
  if (!a.get('relay') && b.get('relay')) {
    return a;
  }
  if (!b.get('relay') && a.get('relay')) {
    return b;
  }

  if (!a.get('isLoopback') && b.get('isLoopback')) {
    return b;
  }
  if (!b.get('isLoopback') && a.get('isLoopback')) {
    return a;
  }

  if (!a.get('isPrivate') && b.get('isPrivate')) {
    return b;
  }
  if (!b.get('isPrivate') && a.get('isPrivate')) {
    return a;
  }

  if (!a.get('isHttps') && b.get('isHttps')) {
    return b;
  }
  if (!b.get('isHttps') && a.get('isHttps')) {
    return a;
  }

  // Prefer the former one for stable best connection
  return a;
};

const ConnectionCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ConnectionCollection',
  context: ['server'],

  model: ConnectionModel,

  //
  // Control

  getBestConnection() {
    const allowHttpsMediaServerConnections =
      config.allowHttpsMediaServerConnections;
    const allowHttps =
      !this.server ||
      this.server.get('isCloud') ||
      allowHttpsMediaServerConnections;

    const availableConnections = this.filter(
      (connection: any) =>
        connection.get('isConnected') &&
        (allowHttps || !connection.get('isHttps'))
    );

    if (!availableConnections.length) {
      return;
    }

    return _.reduce(availableConnections, connectionComparator);
  },

  hasBetterConnectionCandidates() {
    const activeConnection = this.server && this.server.get('activeConnection');

    return this.some((connection: any) => {
      const isPending =
        connection.get('isUntested') || connection.get('isPending');
      const isBetter = activeConnection
        ? connection !== activeConnection &&
          connectionComparator(connection, activeConnection) === connection
        : true;
      return isPending && isBetter;
    });
  },

  hasOnlyFailedConnections() {
    return this.every((connection: any) => connection.get('isFailed'));
  },

  hasFailedEveryDirectConnection() {
    return this.where({ relay: false }).every((connection: any) =>
      connection.get('isFailed')
    );
  },

  hasMultiplePrivateV4Subnets() {
    let knownSubnet: any;

    // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
    return this.some((connection: any) => {
      const address = connection.get('address');
      if (connection.get('isPrivate') && ip.isValidV4Address(address)) {
        const subnet = ip.getV4Subnet(address);
        if (!knownSubnet) {
          knownSubnet = subnet;
        }
        return knownSubnet !== subnet;
      }
    });
  },
});

export default ConnectionCollection;
