/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import StoredConnectionCollection from './StoredConnectionCollection';
import StoredModel from './StoredModel';
import StoredProviderCollection from './StoredProviderCollection';

const StoredServerModel = StoredModel.extend({
  //
  // Properties

  name: 'StoredServerModel',
  idAttribute: 'machineIdentifier',

  additionalRelations: ['connections'],

  syncedAttrs: {
    machineIdentifier: attrTypes.string,
    friendlyName: attrTypes.string,
    accessToken: attrTypes.string,
    sourceTitle: attrTypes.string,
    home: attrTypes.boolean,
    serverClass: attrTypes.string, // Mobile media servers
    platform: attrTypes.string,
    isShared: attrTypes.boolean,
    version: attrTypes.string,
    publicAddressMatches: attrTypes.boolean,
    ownerId: attrTypes.integer,
    mediaProviders: attrTypes.integer,
  },

  syncedRelations: {
    providers: {
      relationClass: StoredProviderCollection,
    },

    // Additional
    connections: {
      relationClass: StoredConnectionCollection,
    },
  },
});

export default StoredServerModel;
