import React from 'react';
import t from 'app/js/utils/t';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import TooltipTrigger from 'app/js/views/v3/components/core/tooltips/TooltipTrigger';
import UnexpectedErrorModal from 'app/js/views/v3/components/navbar/UnexpectedErrorModal';
import useModalState from 'app/js/views/v3/helpers/hooks/useModalState';
import * as tooltipAttachments from 'app/js/views/v3/helpers/props/tooltipAttachments';
import IconAlert from 'app/glyphs/alert-560.svg';
import styles from './ErrorNavBar.css';

function ErrorNavBar() {
  const errorModalState = useModalState();
  const title = t('An unexpected error is impacting the navigation bar');

  return (
    <div className={styles.container}>
      <TooltipTrigger
        aria-label={title}
        // @ts-expect-error ts-migrate(2739) FIXME: Type 'typeof Link' is missing the following proper... Remove this comment to see the full error message
        component={Link}
        tooltip={title}
        tooltipAttachment={tooltipAttachments.BOTTOM_CENTER}
        onPress={errorModalState.onOpen}
      >
        <IconAlert className={styles.icon} />
      </TooltipTrigger>

      <UnexpectedErrorModal
        errorMessage={`${title}.`}
        isOpen={errorModalState.isOpen}
        onModalClose={errorModalState.onClose}
      />
    </div>
  );
}

export default ErrorNavBar;
