/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import userStorageModel from 'app/js/models/storage/userStorageModel';

function authenticateWithHashCommand(options: any) {
  const resolvedPromise = $.Deferred().resolve().promise();
  const rejectedPromise = $.Deferred().reject().promise();
  const activeUser = userStorageModel.getActiveUser();

  if (!activeUser) {
    return rejectedPromise;
  }

  const pin = activeUser.get('pin');
  const authToken = activeUser.get('authToken');

  // Allow users without a pin to authenticate.
  if (!pin && !options.pin) {
    return resolvedPromise;
  }

  const user = resolve('appModel').get('user');
  const hash = user.hashPIN(options.pin, authToken);

  if (pin === hash) {
    return resolvedPromise;
  } else {
    return rejectedPromise;
  }
}

export default authenticateWithHashCommand;
