/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import * as responseTypes from 'app/js/models/responseTypes';
import dispatcher from 'app/js/utils/dispatcher';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import replaceMetadataXMLElementNames from 'app/js/utils/parser/replaceMetadataXMLElementNames';
import toUpperCaseFirstLetter from 'app/js/utils/string/case/toUpperCaseFirstLetter';
import { CONTENT_HUBS } from 'app/js/utils/types/hubStyleTypes';
import path from 'app/js/utils/url/path';
import HubModel from './HubModel';

const HubCollection = AbstractCollection.extend({
  //
  // Properties
  name: 'HubCollection',
  context: [
    'server',
    'provider',
    'section',
    'identifier',
    'hubKey',
    'hubCount',
    'count',
    'includeEmpty',
    'libraryHubsOnly',
    'includeFeaturedTags',
    'includeLibraryPlaylists',
    'includeMeta',
    'includeGames',
    'includeStations',
    'includeExternalMedia',
    'includeExternalMetadata',
    'includeRecentChannels',
    'asyncAugmentMetadata',
    'excludes',
    'excludeFields',
    'excludeElements',
    'allowSync',
    'maxLength',
    'wait',
  ],
  responseContext: ['identifier', 'allowSync'],
  model: HubModel,
  responseType() {
    return getSupportedResponseType(this.server);
  },
  url() {
    let hubKey = '/hubs';
    if (this.hubKey) {
      hubKey = this.hubKey;
    } else if (this.section) {
      hubKey = '/hubs/sections/' + encodeURIComponent(this.section.id);
    }
    const params = {};
    // An explicit empty string means no exclusions, otherwise default to summary
    if (this.excludeFields !== '') {
      (params as any).excludeFields = this.excludeFields || 'summary';
    }
    if (this.excludeElements) {
      (params as any).excludeElements = this.excludeElements;
    }
    // Cloud providers support count:0 although PMS as of 1.13.9 still does not.
    // Request 1 additional item to avoid showing "more" with only a single
    // item behind it.
    if (this.count) {
      (params as any).count = this.count + 1;
    }
    if (this.hubCount) {
      // See: https://github.com/plexinc/plex-web-client/issues/11463
      (params as any).hubCount = this.hubCount;
    }
    if (this.includeEmpty) {
      // `includeEmpty` is unsupported for `/hubs` on servers after ~ Feb 2016.
      // https://github.com/plexinc/plex-media-server/issues/3934
      (params as any).includeEmpty = 1;
    }
    if (this.libraryHubsOnly) {
      (params as any).libraryHubsOnly = 1;
    }
    if (this.includeFeaturedTags) {
      (params as any).includeFeaturedTags = 1;
    }
    if (this.includeLibraryPlaylists) {
      (params as any).includeLibraryPlaylists = 1;
    }
    if (this.includeMeta) {
      (params as any).includeMeta = 1;
    }
    if (this.includeGames) {
      (params as any).includeGames = 1;
    }
    if (this.includeStations) {
      (params as any).includeStations = 1;
    }
    if (this.includeExternalMedia) {
      (params as any).includeExternalMedia = 1;
    }
    if (this.includeExternalMetadata) {
      (params as any).includeExternalMetadata = 1;
    }
    if (this.includeRecentChannels) {
      (params as any).includeRecentChannels = 1;
    }
    if (this.asyncAugmentMetadata) {
      (params as any).asyncAugmentMetadata = 1;
    }
    if (this.wait) {
      (params as any).wait = 1;
    }
    if (this.excludes && this.excludes.length) {
      this.excludes.forEach((type: any) => {
        // Format the param (e.g. excludePlaylists=1).
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        params['exclude' + toUpperCaseFirstLetter(type)] = 1;
      });
    }
    return path.join(hubKey, params);
  },
  //
  // Init
  initialize: function () {
    if (this.section) {
      this.listenTo(dispatcher, 'clear:sections:hubs', this.clear);
    }
  },
  //
  // Control
  fetch(options = {}) {
    const fetchOptions = _.clone(options);
    // Ignore the `onlyTransient` option on initial load.
    if ((options as any).onlyTransient && this.populatePromise) {
      // The `onlyTransient` param will return a subset of transient hubs. Do
      // not remove models from the collection that are not represented in the
      // subset.
      (fetchOptions as any).remove = false;
      (fetchOptions as any).url = path.join(_.result(this, 'url'), {
        onlyTransient: 1,
      });
    }
    return AbstractCollection.prototype.fetch.call(this, fetchOptions);
  },
  augmentResponseText(responseText: any, responseType: any) {
    return responseType === responseTypes.xml
      ? // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
        replaceMetadataXMLElementNames(responseText)
      : responseText;
  },
  augmentResponse(json: any) {
    const rootPropertyName = this.model.prototype.rootPropertyName;
    if (json && rootPropertyName) {
      if (Array.isArray(json)) {
        // `json` value for movie extras hubs is an array.
        json = { [rootPropertyName]: json };
      } else if (
        json[rootPropertyName] &&
        !Array.isArray(json[rootPropertyName])
      ) {
        json = { [rootPropertyName]: [json[rootPropertyName]] };
      }
      const Hub = json[rootPropertyName];
      // Copy MediaContainer.Meta to missing Hub.Meta
      if (json.Meta) {
        Hub.forEach((hub: any) => {
          if (!hub.Meta) {
            hub.Meta = json.Meta;
          }
        });
      }
      let count = Hub && Hub.length;
      if (count) {
        // The `includeEmpty` and `maxLength` properties work in conjunction to
        // allow low-end devices to curtail the number of Hubs presented to the
        // UI. PMS doesn't support limiting the number of hubs returned.
        if (!this.includeEmpty) {
          while (count && count--) {
            const hub = Hub[count];
            if (CONTENT_HUBS.includes(hub.style) && hub.size <= 0) {
              Hub.splice(count, 1);
            }
          }
        }
        if (this.maxLength) {
          // maxLength of zero is unsupported.
          Hub.splice(this.maxLength);
        }
        if (json.size > Hub.length) {
          // Keep the `size` member up to date for a seamless mutation.
          json.size = String(Hub.length);
        }
        if (this.count) {
          Hub.forEach((hub: any) => {
            if (hub.more && hub.size > this.count) {
              hub.Metadata.splice(this.count, hub.size - this.count);
              hub.size = this.count;
            }
          });
        }
      }
    }
    // These need to be set before relations are parsed so each metadata item
    // has the proper context on instantiation.
    this.responseContext.forEach((contextName: any) => {
      const context = json[contextName];
      if (context !== undefined) {
        this.setContext(contextName, context);
      }
    });
    return json;
  },
});
export default HubCollection;
