/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import { AnimatePresence, motion } from 'framer-motion';
import React, { Component } from 'react';
import * as keyCodes from 'app/js/utils/constants/keyCodes';
import PopperPortal from 'app/js/views/v3/components/core/base/PopperPortal';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import cssDurationToSeconds from 'app/js/views/v3/helpers/css/cssDurationToSeconds';
import * as focusManager from 'app/js/views/v3/helpers/focus/focusManager';
import getUniqueElementID from 'app/js/views/v3/helpers/getUniqueElementID';
import * as scrollDirections from 'app/js/views/v3/helpers/props/scrollDirections';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import animations from 'app/styles/variables/animations';
import dimensions from 'app/styles/variables/dimensions';
import easings from 'app/styles/variables/easings';
import styles from './Menu.css';

const MENU_SPEED_SECONDS = cssDurationToSeconds(animations.menuSpeed);
const MENU_SPEED_MILLISECONDS = MENU_SPEED_SECONDS * 1000;
const MENU_PADDING = parseInt(dimensions.menuPadding, 10);

const opacityTransition = {
  ease: 'easeOut',
  duration: MENU_SPEED_SECONDS,
};

const transformTransition = {
  ease: easings.motionEasing,
  duration: MENU_SPEED_SECONDS,
};

const transition = {
  opacity: opacityTransition,
  y: transformTransition,
  scale: transformTransition,
};

const containerVariants = {
  preEnter: {
    opacity: 0,
    y: -10,
    scale: 0.95,
    transition,
  },
  enter: {
    opacity: 1,
    y: 0,
    scale: 1,
    transition,
  },
  exit: {
    opacity: 0,
    y: 10,
    scale: 0.95,
    transition,
  },
};

const menuPopperModifiers = [
  {
    name: 'preventOverflow',
    enabled: true,
    options: {
      altAxis: true,
    },
  },
];

function getMaxHeight(props: any) {
  return Math.min(window.innerHeight - MENU_PADDING * 2, props.maxHeight);
}

interface MenuProps {
  className?: string;
  portalClassName?: string;
  target: string;
  options?: any;
  isOpen: boolean;
  isDisabled?: boolean;
  isAutoCloseDisabled?: boolean;
  maxHeight?: number;
  scrollerProps?: any;
  size?: any; // TODO: PropTypes.oneOf(sizes.all)
  onMenuClose: (...args: any[]) => any;
  onMouseEnter?: (...args: any[]) => any;
  onMouseLeave?: (...args: any[]) => any;
  onMenuCloseComplete?: (...args: any[]) => any;
}

type MenuState = any;

class Menu extends Component<MenuProps, MenuState> {
  static defaultProps = {
    className: styles.menu,
    portalClassName: styles.menuPortal,
    size: sizes.MEDIUM,
    maxHeight: 600,
    isDisabled: false,
    scrollerProps: {
      className: styles.menuScroller,
      scrollDirection: scrollDirections.VERTICAL,
      autoScroll: true,
    },
  };

  // eslint-disable-next-line react/sort-comp
  _menuID: any;
  _willMenuClose: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      maxHeight: getMaxHeight(props),
    };

    this._willMenuClose = false;

    this._menuID = getUniqueElementID();
  }

  componentDidMount() {
    if (this.props.isOpen) {
      this._enable();
    }

    window.addEventListener('resize', this.onResize);
  }

  shouldComponentUpdate(nextProps: MenuProps, nextState: MenuState) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  // @ts-expect-error ts-migrate(6133) FIXME: 'prevState' is declared but its value is never rea... Remove this comment to see the full error message
  componentDidUpdate(prevProps: MenuProps, prevState: MenuState) {
    const wasEnabled = prevProps.isOpen && !prevProps.isAutoCloseDisabled;
    const isEnabled = this.props.isOpen && !this.props.isAutoCloseDisabled;

    if (!wasEnabled && isEnabled) {
      this._enable();
    } else if (wasEnabled && !isEnabled) {
      this._disable();
    }
  }

  componentWillUnmount() {
    if (this.props.isOpen && !this.props.isAutoCloseDisabled) {
      this._disable();
    }

    window.removeEventListener('resize', this.onResize);
  }

  //
  // Control

  _enable() {
    focusManager.startScopedFocus(document.getElementById(this._menuID));

    // Avoid the touch click delay by using `mouseup` and `touchend` events.
    document.addEventListener('mousedown', this.onWindowDownPress);
    document.addEventListener('touchstart', this.onWindowDownPress);
    document.addEventListener('mouseup', this.onWindowUpPress);
    document.addEventListener('touchend', this.onWindowUpPress);
    document.addEventListener('keydown', this.onKeyDown);
  }

  _disable() {
    document.removeEventListener('mousedown', this.onWindowDownPress);
    document.removeEventListener('touchstart', this.onWindowDownPress);
    document.removeEventListener('mouseup', this.onWindowUpPress);
    document.removeEventListener('touchend', this.onWindowUpPress);
    document.removeEventListener('keydown', this.onKeyDown);
  }

  _isElementContainedOrTarget(el: any) {
    // Check to see if element is inside the menu or the component that
    // triggered the event. Do not close the menu in these cases. Only find the
    // target node if we know the event target is not in the container.

    const { target } = this.props;
    const containerElement = document.getElementById(this._menuID);
    const isContainer = containerElement && containerElement.contains(el);
    const targetElement = !isContainer && document.getElementById(target);
    const isTarget = targetElement && targetElement.contains(el);

    return isContainer || isTarget;
  }

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onResize = (event: any) => {
    this.setState({
      maxHeight: getMaxHeight(this.props),
    });
  };

  onWindowDownPress = (event: any) => {
    if (!this._isElementContainedOrTarget(event.target)) {
      this._willMenuClose = true;
    }
  };

  onWindowUpPress = (event: any) => {
    if (
      this._willMenuClose &&
      !this._isElementContainedOrTarget(event.target)
    ) {
      this.props.onMenuClose();
    }

    this._willMenuClose = false;
  };

  onKeyDown = (event: any) => {
    const keyCode = event.keyCode;

    if (keyCode === keyCodes.TAB) {
      focusManager.scopeTabEvent(event);
    } else if (keyCode === keyCodes.ESCAPE) {
      event.preventDefault();
      event.stopPropagation();

      this.props.onMenuClose();

      // Since the user used the keyboard to close the menu, attempt to
      // automatically restore the previously focused element to retain the
      // focus position.

      focusManager.restorePreviousFocus();
    }
  };

  //
  // Render

  render() {
    const {
      children,
      className,
      portalClassName,
      target,
      options,
      isOpen,
      isDisabled,
      scrollerProps,
      size,
      onMouseEnter,
      onMouseLeave,
      onMenuCloseComplete,
    } = this.props;

    const { maxHeight } = this.state;

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; className: string | und... Remove this comment to see the full error message
      <PopperPortal
        className={portalClassName}
        closeTimeoutMilliseconds={MENU_SPEED_MILLISECONDS}
        isOpen={isOpen}
        options={{
          placement: 'bottom-start',
          ...options,
          // Ensure we don't stop on any modifiers that might have been passed
          // in to this component though props
          modifiers: [...menuPopperModifiers, ...(options?.modifiers ?? [])],
        }}
        target={target}
        onClose={onMenuCloseComplete}
      >
        <AnimatePresence>
          {isOpen ? (
            <motion.div
              key="container"
              animate="enter"
              className={classNames(
                className,
                styles[size],
                isDisabled && styles.isDisabled
              )}
              exit="exit"
              id={this._menuID}
              initial="preEnter"
              variants={containerVariants}
              onMouseEnter={onMouseEnter}
              onMouseLeave={onMouseLeave}
            >
              <Scroller {...scrollerProps} style={{ maxHeight }}>
                {children}
              </Scroller>
            </motion.div>
          ) : null}
        </AnimatePresence>
      </PopperPortal>
    );
  }
}

export default Menu;
