import { useMemo } from 'react';
import { useLocation } from 'react-router-dom';
import path, { ParsedParams } from 'app/js/utils/url/path';

function useLocationSearchParams(): ParsedParams {
  const location = useLocation();
  const queryString = location.search;
  return useMemo(() => {
    const { params } = path.parseUri(queryString);

    return params;
  }, [queryString]);
}

export default useLocationSearchParams;
