/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import alerts from 'app/js/models/alerts';
import capabilities from 'app/js/utils/device/capabilities';
import parser2 from 'app/js/utils/parser2';
import path from 'app/js/utils/url/path';

function alertsInitializer() {
  if (!capabilities.hasReplaceState) {
    return $.Deferred().resolve().promise();
  }

  const deferred = $.Deferred();
  const location = window.location;
  // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
  const parsedQuery = parser2.parseQueryString(location.search);

  let parsedAlerts;

  try {
    parsedAlerts = JSON.parse(parsedQuery.alerts);
    delete parsedQuery.alerts;
  } catch (error) {
    // Ignore any errors from parsing JSON
  }

  if (parsedAlerts) {
    alerts.push(...parsedAlerts);
    const url = `${path.join(
      [`${location.origin}${location.pathname}`],
      parsedQuery
    )}${location.hash}`;

    window.history.replaceState({}, '', url);
  }

  return deferred.resolve().promise();
}

export default alertsInitializer;
