/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';
import canServerAllowHttpFallback from 'app/js/utils/server/canServerAllowHttpFallback';

function addHttpFallbackConnectionToServer(
  serverModel: any,
  address: any,
  port: any,
  allowHttpFallbackSetting: any
) {
  if (canServerAllowHttpFallback(serverModel, allowHttpFallbackSetting)) {
    const httpConnection = serverModel.addConnection({
      scheme: 'http',
      address,
      port,
      isFallback: true,
    });

    log(
      '[Connections] Added insecure fallback connection for %s at %s',
      serverModel.get('debugName'),
      httpConnection.id
    );
  } else {
    log.warn(
      '[Connections] Prevented fallback to insecure connection for',
      serverModel.get('debugName')
    );
  }
}

export default addHttpFallbackConnectionToServer;
