import { PayloadAction } from '@reduxjs/toolkit';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { PlainError } from 'app/js/v4/utils/data/api';
import { AsyncState } from 'app/js/v4/utils/data/asyncStateMarkers';

export enum MailingListState {
  Empty = '',
  Unsubscribed = 'unsubscribed',
  Active = 'active',
  Error = 'error',
  Removed = 'removed',
}

type Platforms = 'desktop' | 'qt';

type PlatformType = {
  [field in Platforms]: UserFlex;
};

export interface User extends PlatformType {
  authToken: string;
  anonymous: boolean;
  country?: string;
  email: string;
  confirmed?: boolean;
  emailOnlyAuth?: boolean;
  features: UserFeature[];
  guest?: boolean;
  hasAllEntitlements?: boolean;
  hasPassword?: boolean;
  twoFactorEnabled?: boolean;
  home?: boolean;
  homeSize?: number;
  id: number;
  locale?: string;
  mailingListActive?: boolean;
  mailingListStatus?: MailingListState;
  maxHomeSize?: number;
  pin?: string;
  queueEmail?: string;
  queueUid?: string;
  restricted?: boolean;
  services: UserService[];
  subscriptionDescription?: string;
  thumb: string;
  title?: string;
  username: string;
  friendlyName?: string;
  uuid: string;
  isAllFriendsPopulated: boolean;
  isAllFriendsPending: boolean;
  allFriends: string[];
  allFriendsError?: boolean;
  hasPlexPass: boolean;

  // Session Attrs
  isSignedIn: boolean;
  hasAttemptedSignIn: boolean;
  isFullUser: boolean;
  hasInvalidToken: boolean;

  providers: string[];
  isProvidersPending: boolean;
  isProvidersPopulated: boolean;
  providersError: object | null;
}

export interface UserState extends AsyncState {
  details: UserDetails;
}

export interface UserInvite {
  createdAt: number;
  email: string;
  friend: boolean;
  friendlyName: string;
  home: boolean;
  id: string;
  isAccepting: boolean;
  isRemoving: boolean;
  server: boolean;
  thumb: string;
  username: string;
  invitedEmail: string;
}

export interface UserFlex {
  isHomeDetailsPending: boolean;
  isHomeUsersPending: boolean;
  isInvitesPending: boolean;
  isSentInvitesPending: boolean;
  isHomeDetailsPopulated: boolean;
  isHomeUsersPopulated: boolean;
  isInvitesPopulated: boolean;
  isSentInvitesPopulated: boolean;
  invites: UserInvite[];
  homeUsers: string[];
  sentInvites: UserInvite[];
}

export interface StableUser {
  services: {
    [key: string]: UserService;
  };
  features: {
    [key: string]: boolean;
  };
}

export interface UserService {
  id: string;
  subscriptionType?: string;
}

export enum UserSubscriptionState {
  Active = 'active',
  Canceled = 'canceled',
  Ended = 'ended',
  Inactive = 'inactive',
  PendingCancellation = 'pending_cancellation',
}

export enum UserSubscriptionType {
  Arcade = 'arcade',
  PlexPass = 'plexpass',
  Tidal = 'tidal',
}

// This is a subset of available `subscription` and `pastSubscription`
// attributes.
// See: https://docs.plex.tv/docs/api/plex-tv-v2#users-get-user-data-get
export interface UserSubscription {
  state: UserSubscriptionState;
  type: UserSubscriptionType;
}

export interface UserFeature {
  uuid: string;
}

export interface SidebarSettings {
  hasCompletedSetup: boolean;
  pinnedSources: PinnedSourceSetting[];
}

export interface PinnedSourceSetting {
  machineIdentifier: string;
  providerIdentifier: string;
  directoryID: string;
  isHidden: boolean;
}

export interface UserUpdatableDetails {
  friendlyName: string;
}

export interface UserDetails extends UserUpdatableDetails {
  email: string;
  username: string;
  thumb: string;
  anonymous: boolean;
  isFullUser: boolean;
}

export interface UserUpdateDetailsRequest extends UserUpdatableDetails {
  avatar: string;
}

export interface UserUpdateDetailsSuccess {
  friendlyName: string;
  thumb: string;
}

export type FetchUserDetailsRequestAction = PayloadAction<{
  server: Server;
}>;

export type FetchUserDetailsRequestSucceeded = PayloadAction<{
  details: UserDetails;
}>;

export type UpdateUserDetailsRequestAction = PayloadAction<{
  server: Server;
  update: UserUpdateDetailsRequest;
}>;

export type UpdateUserDetailsRequestSucceeded = PayloadAction<{
  details: UserUpdateDetailsSuccess;
}>;

export type UserDetailsRequestFailed = PayloadAction<{
  error: PlainError;
}>;
