/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import StoredServerModel from 'app/js/models/storage/StoredServerModel';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import { populateSettings } from 'app/js/stores/actions/serverActions';
import BaseClass from 'app/js/utils/BaseClass';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import createServerFromResource from 'app/js/utils/server/createServerFromResource';
import toStableServerModelJSON from 'app/js/utils/server/toStableServerModelJSON';
import updateServerConnectionsFromResource from 'app/js/utils/server/updateServerConnectionsFromResource';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import serverNowPlayingSessionsSlice from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsSlice';
import serversSlice from 'app/js/v4/core/servers/serversSlice';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';

const storedServerAttrs = Object.keys(StoredServerModel.prototype.syncedAttrs);

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ServerManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize(options: any) {
    this.appModel = options.appModel;
    this.servers = this.appModel.get('servers');
  },

  //
  // Control

  start() {
    this.stop();

    this.listenTo(this.servers, {
      'change:promiseState': this.onServerPromiseStateChange,
      remove: this.onServerRemove,
      'update reset': this.onServersUpdateReset,
      'change:resources.add': this.onResourceAdd,
      'change:resources.remove': this.onResourceRemove,
      'change:resources.update': this.onResourcesUpdate,
      'change:resources.reset': this.onResourcesReset,
      'change:resources.change:presence': this.onResourcesPresenceChange,
      'change:resources.change:connections.update':
        this.onResourceConnectionsUpdate,
      'change:isConnected': this.onConnectedChange,
      [`change:${__PLATFORM__}.change:isSettingsPopulated`]:
        this.onSettingsPopulatedChange,
    });

    storedServerAttrs.forEach((attrName) => {
      this.listenTo(
        this.servers,
        'change:' + attrName,
        this.onStoredServerAttributeChange.bind(this, attrName)
      );
    });

    this.listenTo(dispatcher, 'discover:servers', this.onDiscoverServers);

    this._setAuthorized();
    this._checkFoundAllServers();
  },

  stop() {
    this.stopListening();
  },

  _checkFoundAllServers() {
    // The server collection's populate promise will be resolved or rejected at
    // the bottom, or left pending if we are still waiting on servers.

    if (this.servers.shouldWaitForInitialServers()) {
      this._waitForServers();
    } else if (this.servers.shouldWaitForResources()) {
      this._waitForServers();
    } else if (this.servers.shouldWaitForPendingServers()) {
      this._waitForServers();
    } else {
      this._foundAllServers();
    }
  },

  _waitForServers() {
    this.servers.waitForServers();
    scheduleImmediateUpdate(() => {
      this.appModel.set('isWaitingForServers', true);
    });
  },

  _foundAllServers() {
    this.servers.foundAllServers();
    scheduleImmediateUpdate(() => {
      this.appModel.set('isWaitingForServers', false);
    });
  },

  _fetchResources(cloudServer: any) {
    if (cloudServer.get('isRejected')) {
      log.warn(
        '[Servers] Failed to request resources through %s',
        cloudServer.get('debugName')
      );

      cloudServer.set({
        isResourcesPending: false,
        hasFailedResources: true,
        isDownForMaintenance: false,
      });
      return $.Deferred().reject().promise();
    }

    if (!cloudServer.get('isResolved')) {
      log(
        '[Servers] Finding resources through unresolved cloud server %o, resolving',
        cloudServer.get('debugName')
      );
      return cloudServer
        .populate()
        .then(() => this._fetchResources(cloudServer));
    }

    log('[Servers] Finding resources through %s', cloudServer.get('debugName'));

    // We know the server collection should wait if we are fetching resources.
    this._waitForServers();

    cloudServer.set('isResourcesPending', true);

    const resources = cloudServer.get('resources');
    const promise = resources
      .fetch()
      .done(() => {
        log(
          '[Servers] Found %s resources through %s',
          resources.length,
          cloudServer.get('debugName')
        );

        cloudServer.set({
          isResourcesPending: false,
          isResourcesPopulated: true,
          hasFailedResources: false,
          isDownForMaintenance: false,
        });
      })
      // @ts-expect-error ts-migrate(6133) FIXME: 'resources_' is declared but its value is never re... Remove this comment to see the full error message
      .fail((resources_: any, xhr: any) => {
        log.warn(
          '[Servers] Failed to request resources through %s',
          cloudServer.get('debugName')
        );

        cloudServer.set({
          isResourcesPending: false,
          hasFailedResources: true,
          isDownForMaintenance: xhr.status === 503,
        });
      })
      .always(() => {
        this._checkFoundAllServers();
      });

    return promise;
  },

  _convertResourceToServer(resource: any) {
    log(
      '[Servers] %s was found through %s',
      resource.get('name'),
      resource.server.get('debugName')
    );

    return createServerFromResource(resource);
  },

  _removeResource(resource: any) {
    log(
      '[Servers] %s was removed as a resource from %s',
      resource.get('name'),
      resource.server.id
    );

    const server = this.servers.get(resource.id);
    if (server) {
      server.removeSource(resource.server.id);
    }
  },

  _pruneStaleSources(cloudServer: any) {
    // After all resources for a server are known, we should check that all
    // connections with that cloud server's id as a source are cleaned up if
    // they are no longer represented by a resource. Decrement through through
    // the servers since servers may be removed as sources are removed.

    const resources = cloudServer.get('resources');
    let i = this.servers.length - 1;

    while (i >= 0) {
      const server = this.servers.at(i);
      const resource = resources.get(server.id);

      if (resource) {
        // If the resource still exists, but no longer has a connection that was
        // backed by the cloud server, remove the cloud server as a source for
        // that connection.

        const connections = server.get('connections');
        let j = connections.length - 1;

        while (j >= 0) {
          const connection = connections.at(j);
          const sources = connection.get('sources');

          if (sources.get(cloudServer.id)) {
            const resourceConnections = resource.get('connections');

            if (!resourceConnections.get(connection.id)) {
              connection.get('sources').remove(cloudServer.id);
            }
          }

          j--;
        }
      } else {
        // If the server doesn't even exist as a resource anymore, ensure that
        // the cloud server is removed as a source for all connections.

        server.removeSource(cloudServer.id);
      }

      i--;
    }
  },

  _setAuthorized() {
    // `isConnected` is more stable for media servers since the promise resets
    // to pending at times during connection testing.

    const hasConnectedMediaServer = !!this.servers.findWhere({
      isConnected: true,
      isCloud: false,
    });
    const hasResolvedCloudServer = !!this.servers.findWhere({
      isResolved: true,
      isCloud: true,
    });

    this.appModel.set(
      'authorized',
      hasConnectedMediaServer || hasResolvedCloudServer
    );
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'promiseState' is declared but its value is never ... Remove this comment to see the full error message
  onServerPromiseStateChange(server: any, promiseState: any) {
    const resources = server.get('resources');
    if (resources) {
      this._fetchResources(server);
    }

    this._setAuthorized();
    this._checkFoundAllServers();
  },

  onStoredServerAttributeChange(attrName: any, server: any, attrValue: any) {
    const storedServers = userStorageModel.getActiveServers();
    const storedServer = storedServers && storedServers.get(server.id);

    if (
      storedServer &&
      attrValue != null &&
      storedServer.get(attrName) !== attrValue
    ) {
      storedServer.set(attrName, attrValue);
      userStorageModel.debouncedSave();
    }
  },

  onServerRemove(server: any) {
    log.warn('[Servers] %s was removed', server.get('debugName'));

    server.cache.remove(server);
    dispatcher.trigger('clearWhere:cache', { server: server });
  },

  onServersUpdateReset() {
    this._setAuthorized();
    this._checkFoundAllServers();
  },

  onDiscoverServers() {
    const cloudServer = this.appModel.get('cloudServer');

    this._fetchResources(cloudServer);
  },

  onResourceAdd(resource: any) {
    if (_.contains(resource.get('provides'), 'server')) {
      const server = this._convertResourceToServer(resource);
      if (!server.get('isMobileServer')) {
        this.servers.add(server);
      }
    }
  },

  onResourceRemove(resource: any) {
    this._removeResource(resource);
  },

  onResourcesUpdate(resources: any) {
    this._pruneStaleSources(resources.server);
    this._checkFoundAllServers();
  },

  onResourcesReset(resources: any, options: any) {
    const previousResources = options.previousModels;

    _.each(previousResources, (resource) => {
      if (!resources.contains(resource)) {
        this._removeResource(resource);
      }
    });

    this._checkFoundAllServers();
  },

  onResourcesPresenceChange(resource: any) {
    const server = this.servers.get(resource.id);

    if (server) {
      server.set('presence', resource.get('presence'));
    }
  },

  onResourceConnectionsUpdate(connections: any) {
    const resource = connections.parent;
    const server = this.servers.get(resource.id);

    if (server) {
      updateServerConnectionsFromResource(server, resource);
      this._pruneStaleSources(connections.server);
      this._checkFoundAllServers();
    }
  },

  onConnectedChange(server: any, isConnected: any) {
    if (!isConnected) {
      return;
    }

    const appStore = resolve('appStore');
    const stableServer = toStableServerModelJSON(server);

    appStore.dispatch(serversSlice.actions.connected({ server: stableServer }));

    if (server.get('isFullOwnedServer')) {
      // Fetch Now Playing sessions so activity is updated immediately.
      appStore.dispatch(
        serverNowPlayingSessionsSlice.actions.fetchRequested({
          server: stableServer,
        })
      );

      // Populate settings in case this server needs to go through first run.
      appStore.dispatch(
        populateSettings({ serverEntityID: server.entityID() })
      );
    }
  },

  onSettingsPopulatedChange(serverFlex: any, isSettingsPopulated: any) {
    if (isSettingsPopulated) {
      const settings = serverFlex.get('settings');
      const setting = settings.get('AcceptedEULA');
      const isFirstRun = setting && setting.get('value') === false;

      // Check if a different server already navigated to first run.
      if (isFirstRun && !window.location.hash.includes('/setup')) {
        navigateToRoute('serverSetup', {
          machineIdentifier: settings.server.id,
        });
      }
    }
  },
});

export default ServerManager;
