/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectOwnedConnectedServers from './selectOwnedConnectedServers';

const selectOwnedConnectedServerEntityIDs = createSelector(
  selectOwnedConnectedServers,
  (ownedConnectedServers) =>
    ownedConnectedServers.map((server: any) => server.entityID)
);

export default selectOwnedConnectedServerEntityIDs;
