import $ from 'jquery';
import * as adapters from 'app/js/adapters/adapters';
import dispatcher from 'app/js/utils/dispatcher';
import EditSectionBaseView from 'app/js/views/editsection/EditSectionBaseView';
import PlexPassModal from 'app/js/views/modals/PlexPassModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/secti... Remove this comment to see the full error message
import tpl from 'app/templates/section/edit/EditSectionSelectPremiumMusicView.tpl';

const EditSectionSelectPremiumMusicView = EditSectionBaseView.extend({
  //
  // Properties

  template: tpl,

  ui: {
    chooseLibrary: '.choose-library',
  },

  events: {
    'click .choose-library': 'onChooseLibraryClick',
  },

  modelEvents: {
    'change:agent': 'onSectionAgentChange',
    'change:scanner': 'onSectionScannerChange',
  },

  onlyAfterRender: ['updateSelectedOffering'],

  //
  // Init

  initialize: function () {
    this.hasPremiumMusic = this.model.server.hasOwnerFeature(
      'premium_music_metadata'
    );

    this.updateSelectedOffering();
  },

  serializeData: function () {
    const appModel = adapters.resolve('appModel');
    const user = appModel.get('user');
    const data = {
      hasPremiumMusic: this.hasPremiumMusic,
      plexPassUrl: user.server.get('plexPassUrl'),
      hasLyrics: this.model.server.get('transcoderLyrics'),
    };

    return data;
  },

  //
  // Control

  updateSelectedOffering: function () {
    const agent = this.model.get('agent');
    const scanner = this.model.get('scanner');

    this.ui.chooseLibrary.find('input').prop('checked', false);

    let selected = this.ui.chooseLibrary
      .filter('[data-agent="' + agent + '"][data-scanner="' + scanner + '"]')
      .find('input');

    if (!selected.length) {
      selected = this.ui.chooseLibrary
        .not('[data-agent][data-scanner]')
        .find('input');
    }

    selected.prop('checked', true);
  },

  //
  // Listeners

  onChooseLibraryClick: function (event: any) {
    const $target = $(event.currentTarget);

    const agent = $target.data('agent');
    const scanner = $target.data('scanner');

    if ((agent || scanner) && !this.hasPremiumMusic) {
      dispatcher.trigger(
        'show:modal',
        new PlexPassModal({ model: this.model.server })
      );
      return;
    }

    // Reset to null so settings for the new agent are fetched.
    this.model.agentSettings = null;

    this.model.set({
      agent: agent || this.getSectionType().defaultAgent,
      scanner: scanner || this.getSectionType().defaultScanner,
    });
  },

  onSectionAgentChange: function () {
    this.updateSelectedOffering();
  },

  onSectionScannerChange: function () {
    this.updateSelectedOffering();
  },
});

export default EditSectionSelectPremiumMusicView;
