/* eslint-plugin-disable @typescript-eslint */

import Adapter from 'app/js/adapters/Adapter';

// Some platforms may not use a full localizer, like the Chromecast, and
// others may want to use their own localizer, like the XB1.

const localizerAdapter = new Adapter({
  interface: [
    'getLanguages', // () Get a hash of supported language codes and filenames.
    'load', // (language) Load the translations for a language code.
    't', // (str) Translate a string given the current language.
    'tEscape', // (str) Translate a string, html-escaping interpolated values.
  ],
});

// Singleton
export default localizerAdapter;
