import React, {
  ReactElement,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import useUniqueElementID from 'app/js/views/v3/helpers/hooks/useUniqueElementID';
import Tooltip from './Tooltip';
import TooltipWrapper from './TooltipWrapper';

export interface TooltipTriggerProps {
  id?: string;
  className?: string;
  component: React.ComponentType<{
    onMouseEnter: () => void;
    onMouseLeave: () => void;
    onFocus: () => void;
    onBlur: () => void;
    onPress: () => void;
  }>;
  tooltip?: string;
  tooltipClassName?: string;
  tooltipFocusedClassName?: string;
  tooltipAttachment?: string;
  tooltipTarget?: string;
  tooltipOptions?: any;
  showOnHover?: boolean;
  showOnFocus?: boolean;
  hideOnPress?: boolean;
  hideOnDisabled?: boolean;
  isDisabled?: boolean;
  onMouseEnter?: (...args: any[]) => void;
  onMouseLeave?: (...args: any[]) => void;
  onFocus?: (...args: any[]) => void;
  onBlur?: (...args: any[]) => void;
  onPress?: (...args: any[]) => void;
}

export default function TooltipTrigger(
  props: TooltipTriggerProps
): ReactElement {
  const {
    component: TargetComponent = TooltipWrapper,
    hideOnDisabled = true,
    hideOnPress = true,
    showOnFocus = true,
    showOnHover = true,
    tooltip,
    tooltipAttachment,
    tooltipClassName,
    tooltipFocusedClassName,
    tooltipOptions,
    ...otherProps
  } = props;

  const [isOpen, setIsOpen] = useState(false);
  const isFocusedRef = useRef(false);
  const isHoveredRef = useRef(false);

  const uniqueElementID = useUniqueElementID();
  const targetID = props.tooltipTarget || props.id || uniqueElementID;

  const onMouseEnter = useCallback(
    (...args) => {
      if (showOnHover) {
        isHoveredRef.current = true;
        setIsOpen(true);
      }
      props.onMouseEnter?.(...args);
    },
    [props, showOnHover]
  );

  const onMouseLeave = useCallback(
    (...args) => {
      if (showOnHover) {
        isHoveredRef.current = false;
        setIsOpen(isFocusedRef.current);
      }
      props.onMouseLeave?.(...args);
    },
    [props, showOnHover]
  );

  const onFocus = useCallback(
    (...args) => {
      if (showOnFocus) {
        isFocusedRef.current = true;
        setIsOpen(true);
      }
      props.onFocus?.(...args);
    },
    [props, showOnFocus]
  );

  const onBlur = useCallback(
    (...args) => {
      if (props.showOnFocus) {
        isFocusedRef.current = false;
        setIsOpen(isHoveredRef.current);
      }
      props.onBlur?.(...args);
    },
    [props]
  );

  const onPress = useCallback(
    (...args) => {
      if (hideOnPress) {
        isFocusedRef.current = false;
        isHoveredRef.current = false;
        setIsOpen(false);
      }
      props.onPress?.(...args);
    },
    [hideOnPress, props]
  );

  const finalClassName =
    isFocusedRef.current && tooltipFocusedClassName
      ? tooltipFocusedClassName
      : tooltipClassName;

  //
  // Effects

  useEffect(() => {
    if (isOpen && hideOnDisabled && props.isDisabled) {
      isFocusedRef.current = false;
      isHoveredRef.current = false;
      setIsOpen(false);
    }
  }, [hideOnDisabled, isOpen, props.isDisabled]);

  //
  // Render

  return (
    <>
      <TargetComponent
        id={targetID}
        {...otherProps}
        onBlur={onBlur}
        onFocus={onFocus}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        onPress={onPress}
      />
      {tooltip ? (
        <Tooltip
          attachment={tooltipAttachment}
          className={finalClassName}
          isOpen={isOpen}
          options={tooltipOptions}
          target={targetID}
        >
          {tooltip}
        </Tooltip>
      ) : null}
    </>
  );
}
