/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deleteSectionThumbnailsCommand(options: any) {
  const section = options.section;
  const url = '/library/sections/' + section.id + '/indexes';
  const xhrOptions = _.extend(section.server.getXHROptions(url), {
    type: 'DELETE',
  });

  function deleteSectionThumbnails() {
    const xhrDeferred = $.ajax(xhrOptions);

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.done(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'success',
        message: t(
          'The preview thumbnails for items in this library were deleted.'
        ),
      });
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t(
          'There was a problem deleting the preview thumbnails for items in this library.'
        ),
      });
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr) {
      confirmModal.hide();
    });

    return xhrDeferred;
  }

  const confirmModal = new ConfirmModal({
    title: t('Delete Preview Thumbnails'),
    message: t(
      'Are you sure you want to delete the preview thumbnails for items in this library? This cannot be undone.'
    ),
    confirmLabel: t('Delete'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  let promise = confirmModal.promise;
  promise = promise.then(function () {
    return deleteSectionThumbnails();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return promise;
}

export default deleteSectionThumbnailsCommand;
