import { LegacyRootState, RootState } from './rootTypes';

function isLegacyRootState(
  appState: LegacyRootState | RootState
): appState is LegacyRootState {
  return !!(appState as LegacyRootState).v4;
}

export default function selectRoot(
  appState: LegacyRootState | RootState
): RootState {
  // Selectors from components will see v4, while selectors from within sagas
  // only see appState from within the v4 slice.
  return isLegacyRootState(appState) ? appState.v4 : appState;
}
