/* eslint-plugin-disable @typescript-eslint */

import migrateUserExperienceSettings from 'app/js/initializers/migrations/migrateUserExperienceSettings';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import UserExperienceSettingsModel from 'app/js/models/user/UserExperienceSettingsModel';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import * as jsonStorage from 'app/js/utils/storage/jsonStorage';

export default function migrateLocalStorageUserExperienceSettings() {
  const promise = jsonStorage
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    .getJSON(userStorageModel.storageKey)
    .then((json: any) => {
      const isMigrationRequired =
        json.users &&
        json.users.some(
          (user: any) =>
            user.experienceSettings &&
            user.experienceSettings.schemaVersion !==
              UserExperienceSettingsModel.schemaVersion
        );

      if (!isMigrationRequired) {
        return;
      }

      json.users.forEach((user: any) => {
        user.experienceSettings = migrateUserExperienceSettings(
          user.experienceSettings
        );
      });

      return jsonStorage.setJSON(userStorageModel.storageKey, json);
    });

  return alwaysResolve(promise);
}
