import $ from 'jquery';
import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import dispatcher from 'app/js/utils/dispatcher';

let visibleModalsCount = 0;
const closeableModalsStack: any = [];

const BaseModal = Marionette.Layout.extend({
  //
  // Properties

  className: 'modal fade',

  // Modals with a transition cannot have elements focused immediately in
  // onRender. A slight delay must be used before attempting to focus.
  focusDelay: 500,
  animationDelay: 500,

  events: {
    'hide.bs.modal': 'onHide',
  },

  //
  // Init

  initialize: function () {
    this.once('render', function () {
      visibleModalsCount += 1;
      dispatcher.trigger('anyModalOpen');
    });
    this.once('close', function () {
      visibleModalsCount -= 1;
      if (visibleModalsCount === 0) {
        dispatcher.trigger('allModalsClosed');
      }
    });
  },

  //
  // Control

  hide: function () {
    if (!this.isClosed && !this.isHidden) {
      this.$el.modal('hide');
    }
  },

  //
  // Listeners

  onRender: function () {
    // If a modal wants to re-render itself, we don't want to trigger
    // bootstrap to try to show the modal again.
    if (this.rerender) {
      return;
    }

    if (this.isForced) {
      this.$el.modal({
        backdrop: false,
        keyboard: false,
      });
    } else {
      this.listenTo(
        dispatcher,
        'shortcut:escape',
        _.bind(function () {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          if (closeableModalsStack[0] === this) {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.hide();
          }
        }, this)
      );

      this.listenToOnce(this, 'close', function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const i = closeableModalsStack.indexOf(this);
        if (i !== -1) {
          closeableModalsStack.splice(i, 1);
        }
      });

      closeableModalsStack.unshift(this);
      this.$el.modal();
    }

    // Don't hold focus on elements outside of the modal
    $(':focus').blur();
  },

  onHide: function () {
    this.isHidden = true;

    _.each(
      this.panes,
      function (paneDef) {
        const onModalClose = paneDef.constructor.onModalClose;
        if (onModalClose) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          onModalClose.apply(this);
        }
      },
      this
    );

    // Wait for the modal to animate out.
    window.setTimeout(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        dispatcher.trigger('hide:modal', this);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.trigger('close');
      }, this),
      this.animationDelay
    );
  },
});

export default BaseModal;
