import { PlainError } from './api';

export interface AsyncState {
  isPending: boolean;
  isPopulated: boolean;
  error: PlainError | null;
}

export function markPending(state: AsyncState): void {
  state.isPending = true;
  state.error = null;
}

export function markPopulated(state: AsyncState): void {
  state.isPending = false;
  state.isPopulated = true;
}

export function markFailed(state: AsyncState, error: PlainError): void {
  state.isPending = false;
  state.error = error;
}

export function markCancelled(state: AsyncState): void {
  state.isPending = false;
  state.error = null;
}
