/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import {
  pinSources,
  saveExperienceSettings,
} from 'app/js/stores/actions/userActions';
import BaseClass from 'app/js/utils/BaseClass';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import getSource from 'app/js/utils/source/getSource';
import getSourceForStorage from 'app/js/utils/source/getSourceForStorage';
import getSourceKey from 'app/js/utils/source/getSourceKey';
import getSourceTypeForDirectory from 'app/js/utils/types/getSourceTypeForDirectory';
import createKey from 'app/js/views/v3/helpers/createKey';
import findProviderContentFeature from 'app/js/views/v3/helpers/provider/findProviderContentFeature';
import findProviderContentFeatureDirectory from 'app/js/views/v3/helpers/provider/findProviderContentFeatureDirectory';
import shallowEqual from 'app/js/views/v3/helpers/shallowEqual';

const SEVEN_DAYS = 604800;
const EXPERIENCE_SETTINGS_SAVE_TIMEOUT = 1000;
// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const SourceManager = BaseClass.extend({
  mixins: [eventMixin],
  //
  // Init
  initialize(options: any) {
    this.appModel = options.appModel;
    this.debouncedSaveTimeout = null;
  },
  //
  // Control
  start() {
    this.listenTo(this.appModel, {
      ['change:user.change:providers.add']: this.onCloudProviderAdd,
      ['change:user.change:providers.change:isPending']:
        this.onCloudProviderIsPendingChange,
      ['change:user.change:isProvidersPending']:
        this.onCloudProvidersIsPendingChange,
    });
    this.listenTo(this.appModel.get('servers'), {
      ['change:isConnected']: this.onServerIsConnectedChange,
      ['change:isProvidersPending']: this.onServerIsProvidersPendingChange,
      add: this.onServerAdd,
      // Listen for changes to server attributes that result in changes to
      // stored sources. See getSourceForStorage.
      ['change:friendlyName']: this.onServerAttrChanged,
      ['change:sourceTitle']: this.onServerAttrChanged,
    });
    this.listenTo(this.appModel.get('cloudServer'), {
      ['change:isResourcesPending']: this.onCloudReadyChange,
      ['change:hasFailedResources']: this.onCloudReadyChange,
    });
  },
  _getPinnedSources() {
    const userExperienceSettingsModel = this.appModel
      .get('user')
      .get('experienceSettings');
    const sidebarSettings = userExperienceSettingsModel.get('sidebarSettings');
    return sidebarSettings.get('pinnedSources');
  },

  _shouldRemoveHiddenSource(sourceModel: any) {
    const hiddenAt = sourceModel.get('hiddenAt');
    return hiddenAt > 0 && Date.now() - hiddenAt > SEVEN_DAYS;
  },
  _hidePinnedSources(pinnedSourcesToBeHidden: any) {
    pinnedSourcesToBeHidden.forEach((pinnedSourceModel: any) => {
      if (!pinnedSourceModel.get('isHidden')) {
        pinnedSourceModel.set({
          isHidden: true,
          hiddenAt: Date.now(),
        });

        this._debouncedSaveExperienceSettings();
      } else if (this._shouldRemoveHiddenSource(pinnedSourceModel)) {
        pinnedSourceModel.trigger(
          'destroy',
          pinnedSourceModel,
          pinnedSourceModel.collection
        );
        this._debouncedSaveExperienceSettings();
      }
    });
  },
  _hideSourcesWithNoServer() {
    const servers = this.appModel.get('servers');
    const pinnedSourcesToBeHidden = this._getPinnedSources().filter(
      (pinnedSourceModel: any) => {
        return !servers.get(pinnedSourceModel.get('machineIdentifier'));
      }
    );

    this._hidePinnedSources(pinnedSourcesToBeHidden);
  },
  _hideSourcesWithNoProvider(
    serverModel: any,
    providersModel: any,
    isProvidersPopulated: any,
    providersError: any
  ) {
    // We should only hide sources when we know the server is connected and list
    // of providers is populated.
    //
    // If the provider is no longer in the list of providers then we should hide
    // this source.  Examples of this scenario include:
    // - When a user re-creates their Live TV & DVR setup; in this case the
    // original DVR provider is removed.
    // - Unfortunately there is currently a bug in PMS where the Live TV & DVR
    // provider isn't returned during startup of PMS
    // (https://github.com/plexinc/plex-media-server/issues/10410)
    // - An entire online media source is disabled for a user
    if (
      serverModel.get('isConnected') &&
      isProvidersPopulated &&
      !providersError
    ) {
      const serverMachineIdentifier = serverModel.get('machineIdentifier');
      const pinnedSourcesToBeHidden = this._getPinnedSources().filter(
        (pinnedSourceModel: any) => {
          const machineIdentifier = pinnedSourceModel.get('machineIdentifier');
          const providerIdentifier =
            pinnedSourceModel.get('providerIdentifier');
          return (
            machineIdentifier === serverMachineIdentifier &&
            !providersModel.get(providerIdentifier)
          );
        }
      );

      this._hidePinnedSources(pinnedSourcesToBeHidden);
    }
  },
  _hideCloudSourcesWithNoProvider(userModel: any) {
    const serverModel = this.appModel.get('cloudServer');
    this._hideSourcesWithNoProvider(
      serverModel,
      userModel.get('providers'),
      userModel.get('isProvidersPopulated'),
      userModel.get('providersError')
    );
  },
  _hideServerSourcesWithNoProvider(serverModel: any) {
    this._hideSourcesWithNoProvider(
      serverModel,
      serverModel.get('providers'),
      serverModel.get('isProvidersPopulated'),
      serverModel.get('providersError')
    );
  },
  _updateStoredPinnedSources(server: any, provider: any) {
    const pinnedSourcesToBeHidden: any = [];
    this._getPinnedSources().forEach((pinnedSourceModel: any) => {
      const pinnedSource = getSourceForStorage(pinnedSourceModel.toJSON());
      if (
        pinnedSource.machineIdentifier !== server.machineIdentifier ||
        pinnedSource.providerIdentifier !== provider.identifier
      ) {
        return;
      }
      const directory = findProviderContentFeatureDirectory(
        provider,
        pinnedSource.directoryID
      );
      if (!directory) {
        // If the provider is populated and the origin of the source (eg: content
        // directory) no longer exists then it should also be hidden.  Examples of
        // this scenario include:
        // - User deletes a library.
        // - PMS no longer returns a playlists directory once all playlists are
        // deleted
        if (server.isConnected && provider.isPopulated) {
          pinnedSourcesToBeHidden.push(pinnedSourceModel);
        }
        return;
      }
      const sourceTypeObject = getSourceTypeForDirectory(provider, directory);
      const updatedPinnedSource = getSourceForStorage(
        getSource({
          sourceType: sourceTypeObject ? sourceTypeObject.key : null,
          sourceServer: server,
          sourceProvider: provider,
          sourceDirectory: directory,
        })
      );
      if (pinnedSourceModel.get('isHidden')) {
        (updatedPinnedSource as any).isHidden = false;
        (updatedPinnedSource as any).hiddenAt = null;
      }
      if (!shallowEqual(pinnedSource, updatedPinnedSource)) {
        pinnedSourceModel.set(updatedPinnedSource);
        this._debouncedSaveExperienceSettings();
      }
    });
    this._hidePinnedSources(pinnedSourcesToBeHidden);
  },

  _autoPinSources(server: any, provider: any) {
    const userExperienceSettingsModel = this.appModel
      .get('user')
      .get('experienceSettings');
    const autoPinnedProviders = userExperienceSettingsModel.get(
      'autoPinnedProviders'
    );
    const sidebarSettings = userExperienceSettingsModel.get('sidebarSettings');
    const pinnedSources = sidebarSettings.get('pinnedSources');
    const providerKey = createKey(
      server.machineIdentifier,
      provider.identifier
    );
    if (
      !providerIdentifiers.matches(provider.identifier, [
        providerIdentifiers.VOD,
        providerIdentifiers.CLOUD_EPG,
      ]) ||
      autoPinnedProviders.includes(providerKey)
    ) {
      return;
    }
    userExperienceSettingsModel.set({
      autoPinnedProviders: [...autoPinnedProviders, providerKey],
    });
    this._debouncedSaveExperienceSettings();
    const contentFeature = findProviderContentFeature(provider);
    if (
      // User has chance to pin sources during FRE, so we should not be
      // auto-pinning any sources from this provider if setup not completed.
      !sidebarSettings.get('hasCompletedSetup') ||
      !contentFeature ||
      !contentFeature.directories
    ) {
      return;
    }
    const autoPinnedSources = contentFeature.directories.reduce(
      (acc: any, directory: any) => {
        const sourceTypeObject = getSourceTypeForDirectory(provider, directory);
        const sourceKey = getSourceKey(
          sourceTypeObject ? sourceTypeObject.key : null,
          server.machineIdentifier,
          provider.identifier,
          directory.id
        );
        if (sourceTypeObject && !pinnedSources.get(sourceKey)) {
          const source = getSource({
            sourceType: sourceTypeObject ? sourceTypeObject.key : null,
            sourceServer: server,
            sourceProvider: provider,
            sourceDirectory: directory,
          });
          acc.push(getSourceForStorage(source));
        }
        return acc;
      },
      []
    );
    if (autoPinnedSources.length > 0) {
      resolve('appStore').dispatch(
        pinSources({
          sources: autoPinnedSources,
          interaction: false,
        })
      );
    }
  },
  _processSources(server: any, provider: any) {
    if (!provider.isPopulated) {
      return;
    }
    this._updateStoredPinnedSources(server, provider);
    this._autoPinSources(server, provider);
  },
  _debouncedSaveExperienceSettings() {
    // By making use of a timeout we reduce number of settings syncs to plex.tv.
    // This is important during a language change where all cloud providers and
    // servers are re-fetched resulting in a large number of updates.
    const userExperienceSettingsModel = this.appModel
      .get('user')
      .get('experienceSettings');
    userExperienceSettingsModel.set({ isDirty: true });
    if (this.debouncedSaveTimeout) {
      window.clearTimeout(this.debouncedSaveTimeout);
    }
    this.debouncedSaveTimeout = window.setTimeout(() => {
      if (userExperienceSettingsModel.get('isDirty')) {
        resolve('appStore').dispatch(saveExperienceSettings());
      }
    }, EXPERIENCE_SETTINGS_SAVE_TIMEOUT);
  },
  _processCloudProviderChange(providerModel: any) {
    const server = this.appModel.get('cloudServer').toJSON();
    const provider = providerModel.toJSON();
    this._processSources(server, provider);
  },
  _processServerChange(serverModel: any) {
    if (
      !serverModel.get('isConnected') ||
      !serverModel.get('isProvidersPopulated')
    ) {
      return;
    }
    const server = serverModel.toJSON();
    server.providers.forEach((provider: any) =>
      this._processSources(server, provider)
    );
  },
  //
  // Listeners
  onCloudProviderAdd(providerModel: any) {
    this._processCloudProviderChange(providerModel);
  },
  onCloudProviderIsPendingChange(providerModel: any, isPending: any) {
    if (isPending) {
      return;
    }
    this._processCloudProviderChange(providerModel);
  },
  onCloudProvidersIsPendingChange(userModel: any, isProvidersPending: any) {
    if (isProvidersPending) {
      return;
    }
    this._hideCloudSourcesWithNoProvider(userModel);
  },
  onServerAttrChanged(serverModel: any) {
    this._processServerChange(serverModel);
  },
  onServerIsConnectedChange(serverModel: any, isConnected: any) {
    if (isConnected) {
      return;
    }
    this._processServerChange(serverModel);
  },
  onServerIsProvidersPendingChange(serverModel: any, isProvidersPending: any) {
    if (isProvidersPending) {
      return;
    }
    this._hideServerSourcesWithNoProvider(serverModel);
    this._processServerChange(serverModel);
  },
  onServerAdd(serverModel: any) {
    this._processServerChange(serverModel);
  },
  onCloudReadyChange(cloudServerModel: any) {
    if (
      !cloudServerModel.get('isResourcesPending') &&
      !cloudServerModel.get('hasFailedResources')
    ) {
      // If there is no server, only hide the source when all resources have been
      // fetched.  While resources are loading we do not want to assume the
      // server does not exist.
      this._hideSourcesWithNoServer();
    }
  },
});
export default SourceManager;
