import { Provider } from 'app/js/v4/core/providers/providersTypes';

// Rather than trying to handle `provider | undefined` in many places throughout
// the codebase that rely on identifiers and selectors to find provider, use an
// unknown provider object instead of `undefined`.

const unknownProvider: Provider = {
  entityID: '',
  identifier: '',
  metricsIdentifier: '',
  title: '',
  features: [],
  protocols: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

export default unknownProvider;
