/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import t from 'app/js/utils/t';

const whitelistedPrimaryKeySubsets = [
  'all',
  'recentlyAdded',
  'recentlyViewed',
  'recentlyViewedShows',
  'onDeck',
  'newest',
  'folder',
];
const SectionSubsetModel = AbstractModel.extend(
  {
    //
    // Properties
    name: 'SectionSubsetModel',
    context: ['server', 'section'],
    idAttribute: 'key',
    rootPropertyName: 'Directory',
    sessionAttrs: {
      selected: attrTypes.boolean,
    },
    syncedAttrs: {
      key: attrTypes.string,
      title: attrTypes.string,
      secondary: attrTypes.boolean,
    },
    derivedAttrs: {
      // Only allow certain subsets to become primary subsets that are shown
      // in the subset dropdown.
      primary() {
        return (
          !(this as any).get('secondary') &&
          _.contains(whitelistedPrimaryKeySubsets, (this as any).get('key'))
        );
      },
    },
    defaults: {
      selected: false,
    },
    //
    // Control
    augmentResponse(json: any) {
      // Simplify the title for the all subset.
      if (json.key === 'all') {
        json.title = t('All');
      }
      return json;
    },
  },
  {
    //
    // Static
    whitelistedPrimaryKeySubsets,
  }
);
export default SectionSubsetModel;
