/* eslint-plugin-disable @typescript-eslint */

export default function saveSettingsModel(model: any, settings: any) {
  return model
    .save({
      ...settings,
      isSaving: true,
      saveError: null,
    })
    .then(
      () => {
        model.set({ isSaving: false });
      },
      (error: any) => {
        // The error object is provided by the storageAdapter and has no
        // enforced interface. Conservatively cherry-pick a non-localized
        // error message to keep other error members out of the state tree.
        const message = (error && error.message) || '';

        model.set({ isSaving: false, saveError: { message } });
      }
    );
}
