/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { matchPath } from 'react-router-dom';
import t from 'app/js/utils/t';
import history from 'app/js/v4/history';
import routes from 'app/js/v4/routes/routes';
import PlexHeaderImage from 'app/js/views/v3/components/core/PlexHeaderImage';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import SkipSignInMessage from 'app/js/views/v3/components/sign-in/SkipSignInMessage';
import { PRIMARY } from 'app/js/views/v3/helpers/props/kinds';
import { LARGE } from 'app/js/views/v3/helpers/props/sizes';
import styles from './SignInLaunchPagePoll.css';

type SignInLaunchPagePollProps = {
  to?: string;
  code?: string;
  signUp?: boolean;
  isBusy: boolean;
  onSignInPress: (...args: any[]) => any;
  onStillTherePress: (...args: any[]) => any;
  onSkipSignInPress: (...args: any[]) => any;
};

type SignInLaunchPagePollState = any;

class SignInLaunchPagePoll extends Component<
  SignInLaunchPagePollProps,
  SignInLaunchPagePollState
> {
  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isSkipSignInVisible: false,
    };
  }

  //
  // Listeners

  onSwitchToSkipSignInPress = () => {
    this.setState({
      isSkipSignInVisible: true,
    });
  };

  onStillTherePress = () => {
    this.setState({
      isSkipSignInVisible: false,
    });

    this.props.onStillTherePress();
  };

  onSignInPress = () => {
    this.setState({
      isSkipSignInVisible: false,
    });

    this.props.onSignInPress();
  };

  //
  // Render

  render() {
    const { to, code, signUp, isBusy, onSkipSignInPress } = this.props;

    const showTimedOut = !(isBusy || code);
    const showButton = !showTimedOut;
    const isSkipSignInVisible = this.state.isSkipSignInVisible;
    const isFirstRunRoute = matchPath(history.location.pathname, {
      path: routes.serverSetup,
    });

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: (Element | null)[]; className: s... Remove this comment to see the full error message
      <Scroller autoScroll={true} className={styles.page}>
        {isFirstRunRoute && !isSkipSignInVisible ? (
          <Link
            className={styles.switchToSkipSignInLink}
            kind={PRIMARY}
            onPress={this.onSwitchToSkipSignInPress}
          >
            {t("What's this?")}
          </Link>
        ) : null}

        <PlexHeaderImage />

        <div className={styles.container}>
          {isSkipSignInVisible ? <SkipSignInMessage /> : null}

          <div className={styles.section}>
            {showButton ? (
              <SpinnerButton
                className={styles.button}
                isSpinning={!code}
                kind={PRIMARY}
                size={LARGE}
                target="_blank"
                to={to}
                type="submit"
                onPress={this.onSignInPress}
              >
                {signUp ? t('Sign up') : t('Sign in')}
              </SpinnerButton>
            ) : null}

            {showTimedOut ? (
              <Link kind={PRIMARY} onPress={this.onStillTherePress}>
                {t('Are you still there?')}
              </Link>
            ) : null}
          </div>
        </div>

        {isSkipSignInVisible ? (
          <Link
            className={styles.addendumLink}
            kind={PRIMARY}
            onPress={onSkipSignInPress}
          >
            {t('Skip and accept limited functionality')}
          </Link>
        ) : null}
      </Scroller>
    );
  }
}

export default SignInLaunchPagePoll;
