// Based on redux-actions from:
// https://github.com/acdlite/redux-actions/blob/master/src/createAction.js

export interface Action<Payload> {
  type: string;
  payload: Payload;
}

type ActionFunction<R> = (...args: any[]) => R;

function createActionCreator<Payload>(
  type: string,
  actionCreator?: ActionFunction<Payload>
): ActionFunction<Action<Payload>> {
  return function (...args) {
    const payload =
      typeof actionCreator === 'function' ? actionCreator(...args) : args[0];

    return { type, payload };
  };
}

export default createActionCreator;
