import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import { VIDEO } from 'app/js/utils/types/mediaTypes';
import { getLeafType, getMetadataType } from 'app/js/utils/types/metadataTypes';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertFormPolicyView.tpl';

const ConvertFormPolicyView = Marionette.ItemView.extend({
  //
  // Properties
  className: 'convert-policy-container',
  template: tpl,
  ui: {
    policyUnwatchedInput: '#convert-policy-unwatched',
    policyScopeInput: '#convert-policy-scope',
    policyValueInput: '#convert-policy-value',
    policyWarning: '.convert-warning',
  },
  events: {
    'change input[name=policyUnwatched]': 'onPolicyUnwatchedChange',
    'change input[name=policyScope]': 'onPolicyScopeChange',
    'keyup input[name=policyValue]': 'onPolicyValueChange',
    'change input[name=policyValue]': 'onPolicyValueChange',
  },
  //
  // Init
  initialize: function () {
    // Only sync items have a `metadataType`, but we can assume all version
    // generators are videos.
    this.type = getMetadataType(this.model.get('metadataType') || 'movie');
    this.leafMediaType = getLeafType(this.type.typeString).mediaType;
    this.model.setPolicyDefaultsFor(this.leafMediaType);
  },
  //
  // Control
  serializeData: function () {
    const data = {
      isSync: this.options.isSync,
      isDirectory: this.model.get('isDirectory'),
      policy: this.model.get('policy').toJSON(),
    };
    (data as any).showLimitWarning = this.model.shouldShowPolicyWarning(
      this.leafMediaType,
      data.policy.value
    );
    (data as any).showUnwatched =
      data.isDirectory && this.leafMediaType === VIDEO;
    return data;
  },
  updatePolicyValue: function () {
    const policy = this.model.get('policy');
    let policyValue = Math.abs(parseInt(this.ui.policyValueInput.val(), 10));
    // Write the value back after parsing so the input is accurate.
    this.ui.policyValueInput.val(_.isNaN(policyValue) ? '' : policyValue);
    if (_.isNaN(policyValue)) {
      policyValue = 0;
    }
    // A policy with a scope of "all" should not have a value.
    policy.set('value', policy.scope === 'all' ? null : policyValue);
    const showWarning = this.model.shouldShowPolicyWarning(
      this.leafMediaType,
      policyValue
    );
    this.ui.policyWarning.toggleClass('hidden', !showWarning);
  },
  //
  // Listeners
  onPolicyUnwatchedChange: function () {
    const policy = this.model.get('policy');
    policy.set('unwatched', this.ui.policyUnwatchedInput.prop('checked'));
  },
  onPolicyScopeChange: function () {
    const policy = this.model.get('policy');
    const scope = this.ui.policyScopeInput.prop('checked')
      ? this.ui.policyScopeInput.val()
      : 'all';
    policy.set('scope', scope);
    // The value will reset depending on the scope.
    this.updatePolicyValue();
  },
  onPolicyValueChange: function () {
    this.updatePolicyValue();
  },
});
export default ConvertFormPolicyView;
