import React, { ReactElement } from 'react';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import useSelectedSource from 'app/js/v4/features/sources/hooks/useSelectedSource';
import UserPageConnector from 'app/js/views/v3/components/UserPageConnector';
import FullPage from 'app/js/views/v3/components/pages/FullPage';
import SetupPageConnector from 'app/js/views/v3/components/pages/setup/SetupPageConnector';

export default function ServerSetupViewPage(): ReactElement {
  const selectedSource = useSelectedSource();

  const serverEntityID = createEntityID(
    entityTypes.SERVER,
    selectedSource.machineIdentifier
  );

  return (
    <FullPage>
      {/* @ts-expect-error */}
      <UserPageConnector>
        <SetupPageConnector serverEntityID={serverEntityID} />
      </UserPageConnector>
    </FullPage>
  );
}
