/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import NavBarActivityMenuStatusItemConnector from './NavBarActivityMenuStatusItemConnector';
import NavBarEmbeddedServerActivityMenuStatusItemConnector from './NavBarEmbeddedServerActivityMenuStatusItemConnector';
import styles from './NavBarActivityMenu.css';

const menuOptions = {
  placement: 'bottom-end',
};

interface NavBarActivityMenuProps {
  isOpen: boolean;
  target: string;
  embeddedServerEntityID?: string;
  ownedServerEntityIDs: string[];
  onMenuClose: (...args: any[]) => any;
}

function NavBarActivityMenu(props: NavBarActivityMenuProps) {
  const {
    isOpen,
    target,
    embeddedServerEntityID,
    ownedServerEntityIDs,
    onMenuClose,
  } = props;

  //
  // Render

  return (
    <Menu
      className={styles.menu}
      isOpen={isOpen}
      options={menuOptions}
      target={target}
      onMenuClose={onMenuClose}
    >
      {ownedServerEntityIDs.map((serverEntityID) => {
        return (
          <NavBarActivityMenuStatusItemConnector
            key={serverEntityID}
            serverEntityID={serverEntityID}
            onMenuClose={onMenuClose}
          />
        );
      })}
      {embeddedServerEntityID ? (
        <NavBarEmbeddedServerActivityMenuStatusItemConnector
          key={embeddedServerEntityID}
          serverEntityID={embeddedServerEntityID}
          onMenuClose={onMenuClose}
        />
      ) : null}
    </Menu>
  );
}

export default NavBarActivityMenu;
