/* eslint-plugin-disable @typescript-eslint */

import * as messageTypes from 'app/js/utils/pubsub/messageTypes';

function getBaseMessages(notification: any) {
  switch (notification.type) {
    case messageTypes.PREFERENCE:
      return notification.Setting;
    case messageTypes.TIMELINE:
      return notification.TimelineEntry;
    case messageTypes.STATUS:
      return notification.StatusNotification;
    case messageTypes.PROGRESS:
      return notification.ProgressNotification;
    case messageTypes.PLAYING:
      return notification.PlaySessionStateNotification;
    case messageTypes.BACKGROUND_GENERATOR_STATE:
      return notification.BackgroundGeneratorStateNotification;
    case messageTypes.VERSION_STATE:
      return notification.VersionStateNotification;
    case messageTypes.BACKGROUND_PROCESSING_QUEUE:
      return notification.BackgroundProcessingQueueEventNotification;
    case messageTypes.TRANSCODE_START:
    case messageTypes.TRANSCODE_UPDATE:
    case messageTypes.TRANSCODE_END:
      return notification.TranscodeSession;
    case messageTypes.UPDATE_STATE_CHANGE:
      return notification.AutoUpdateNotification;
    case messageTypes.REACHABILITY:
      return notification.ReachabilityNotification;
    case messageTypes.ACCOUNT:
      return notification.AccountUpdateNotification;
    case messageTypes.ACTIVITY:
      return notification.ActivityNotification;
    case messageTypes.GRABBER_DEVICE:
      return notification.MediaGrabberDeviceNotification;
    case messageTypes.PROVIDER_CHANGE:
      return notification.MediaProviderNotification;
    case messageTypes.PROVIDER_CONTENT_CHANGE:
      return notification.MediaProviderContentNotification;
    case messageTypes.LOG:
      return notification.LogSentNotification;
    case messageTypes.PROVIDER_SUBSCRIPTION_CHANGE:
      return notification.MediaProviderSubscriptionNotification;
    case messageTypes.CLIENT_DECISION:
      return notification.ClientDecisionRequestEventNotification;
  }
}

function getMessages(notification: any, acceptedMessageTypes: any) {
  return (
    (acceptedMessageTypes.indexOf(notification.type) === -1
      ? []
      : getBaseMessages(notification)) || []
  );
}

export default getMessages;
