import { PayloadAction } from '@reduxjs/toolkit';
import { Server } from 'app/js/v4/core/servers/serversTypes';

export enum ServerActivityType {
  BackupDatabase = 'general.db.backup',
  CleanBundles = 'general.bundle.clean',
  GrabberGrab = 'grabber.grab',
  GrabberAssimilate = 'grabber.assimilate',
  LibraryDelete = 'library.delete',
  LibraryUpdateSection = 'library.update.section',
  LibraryRefreshItems = 'library.refresh.items',
  MediaGenerateBIF = 'media.generate.bif',
  MediaGenerateChapterThumbs = 'media.generate.chapter.thumbs',
  MediaGenerateIntros = 'media.generate.intros',
  OptimizeDatabase = 'general.db.optimize',
  ProviderDeviceChannelScan = 'provider.device.channelscan',
  ProviderEpgLineupScan = 'provider.epg.lineupscan',
  ProviderEpgLoad = 'provider.epg.load',
  ProviderSubscriptionsProcess = 'provider.subscriptions.process',
  ProviderSubscriptionRefresh = 'provider.subscription.refresh',
  SubtitleDownload = 'subtitle.download',
}

export interface ServerActivitiesState {
  [machineIdentifier: string]: ServerActivity[];
}

export interface ServerActivity {
  uuid: string;
  type: ServerActivityType;
  cancellable: boolean;
  title?: string;
  subtitle?: string;
  userID?: number;
  progress?: number;
  Context: any;
  Response: any;
}

export type ServerActivityAction = PayloadAction<{
  server: Server;
  activity: ServerActivity;
}>;
