/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import Scroller from 'app/js/views/v3/components/core/base/Scroller';
import * as scrollDirections from 'app/js/views/v3/helpers/props/scrollDirections';
import styles from './ModalBody.css';

interface ModalBodyProps {
  children?: React.ReactNode;
  className?: string;
  innerClassName?: string;
  scrollDirection?: any; // TODO: PropTypes.oneOf(scrollDirections.all)
}

function ModalBody({
  children,
  className,
  innerClassName,
  scrollDirection,
  ...otherProps
}: ModalBodyProps) {
  const isScroller = scrollDirection !== scrollDirections.NONE;

  if (!className) {
    className = isScroller ? styles.modalScroller : styles.modalBody;
  }

  return (
    // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: {} | null | undefined; className... Remove this comment to see the full error message
    <Scroller
      autoScroll={true}
      className={className}
      scrollDirection={scrollDirection}
      {...otherProps}
    >
      {isScroller ? <div className={innerClassName}>{children}</div> : children}
    </Scroller>
  );
}

ModalBody.defaultProps = {
  innerClassName: styles.innerModalBody,
};

export default ModalBody;
