/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import dimensionsAdapter from 'app/js/adapters/dimensionsAdapter';
import getMetricsProtocolContext from 'app/js/utils/metrics/getMetricsProtocolContext';
import parseQueryString from 'app/js/utils/parser/parseQueryString';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import splitUrlSearchHashPathAndParams from 'app/js/utils/url/splitUrlSearchHashPathAndParams';

const UTM_REGEX = /^utm_/;

function getCampaignParams() {
  const { searchParams, hashParams } = splitUrlSearchHashPathAndParams(
    window.location.href
  );

  // Campaign params from query string take precedence of hash params
  const params = {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    ...parseQueryString(hashParams),
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    ...parseQueryString(searchParams),
  };

  return Object.keys(params).reduce((acc, key) => {
    if (UTM_REGEX.test(key)) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      acc[key.replace(UTM_REGEX, '')] = params[key];
    }

    return acc;
  }, {});
}

export default function metricsInitializer(options = {}) {
  const metrics = resolve('metrics');
  // @ts-expect-error ts-migrate(2339) FIXME: Property 'context' does not exist on type '{}'.
  const { context = {} } = options;

  // See: https://github.com/plexinc/specs/blob/master/Metrics/Overview.md
  metrics.set({
    deviceIdentifier: dimensionsAdapter.prop('X-Plex-Client-Identifier'),
    context: {
      ...context,
      campaign: getCampaignParams(),
      device: {
        product: dimensionsAdapter.prop('X-Plex-Product'),
        version: dimensionsAdapter.prop('X-Plex-Version'),
        platform: dimensionsAdapter.prop('X-Plex-Platform'),
        platformVersion: dimensionsAdapter.prop('X-Plex-Platform-Version'),
        device: dimensionsAdapter.prop('X-Plex-Device'),
        model: dimensionsAdapter.prop('X-Plex-Model'),
        screenResolution: dimensionsAdapter.prop(
          'X-Plex-Device-Screen-Resolution'
        ),
        protocol: getMetricsProtocolContext(window.location),
      },
    },
  });

  // Metrics are enabled by default until possibly disabled by the client.
  return alwaysResolve(metrics.start());
}
