/* eslint-disable prefer-rest-params, prefer-spread */
/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import populateMixin from 'app/js/mixins/models/populateMixin';
import CollectionCache from 'app/js/utils/cache/CollectionCache';
import parser from 'app/js/utils/parser';
import path from 'app/js/utils/url/path';

// DEPRECATED: Use `AbstractCollection`

const BaseCollection = Backbone.Collection.extend({
  //
  // Properties

  mixins: [populateMixin],
  cache: new CollectionCache(),

  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'models' is declared but its value is never read.
  constructor: function (models: any, options: any) {
    options || (options = {});

    // For all models associated with a higher level models, try to store a
    // reference to those models.
    this.server = options.server;
    this.section = options.section;
    this.channel = options.channel;
    this.device = options.device;
    this.parent = options.parent;
    this.allowInCache = options.allowInCache;
    this.persistInCache = options.persistInCache;
    // Individual models may rely on skipCache on the collection.
    this.skipCache = options.skipCache;

    if (options.url) {
      this.url = options.url;
    }

    if (options.path) {
      this.path = options.path;
    }

    if (options.pageStart != null || options.pageSize != null) {
      this.pageStart = options.pageStart || 0;
      this.pageSize = options.pageSize || 0;
    }

    if (options.populated) {
      this.resetPopulatePromise($.Deferred().resolve().promise());
    }

    if (!options.allowInCache && !options.persistInCache) {
      Backbone.Collection.prototype.constructor.apply(this, arguments);
      return;
    }

    // Options handled by initialize may affect the url used to get a cache
    // key for the collection.
    this.initialize.apply(this, arguments);

    // If this instance is already in the cache, return the cached instance
    // instead of constructing a new instance.
    const cacheKey = this.cache.getItemCacheKey(this);
    let cached = this.cache.get(cacheKey);

    // Treat all instances as disposable. If it's intended for usage the
    // constructor call below will re-invoke `initialize()`.
    this.uninitialize();

    if (options.onlyIfCached) {
      cached || (cached = {});
    }

    if (cached) {
      return cached;
    }

    // Not in the cache, so go ahead and use the original constructor,
    // adding the result to the cache.
    Backbone.Collection.prototype.constructor.apply(this, arguments);

    this.cache.store(this, cacheKey);
  },

  uninitialize: function () {
    this.stopListening();
  },

  //
  // Control

  sync: function () {
    // Whenever the collection is synced, assume the url might have changed,
    // which means we need to update the cache key.
    if (this.allowInCache || this.persistInCache) {
      this.cache.store(this);
    }

    return Backbone.sync.apply(this, arguments);
  },

  syncUrl: function () {
    let url = '';

    if (this.url) {
      url = _.result(this, 'url');
    }

    if (this.path) {
      url = path.join([url, _.result(this, 'path')]);
    }

    // If pagination properties have been set, append the X-Plex pagination
    // params to the end of the querystring.
    if (this.pageStart != null || this.pageSize != null) {
      const pageStart = this.pageStart || 0;
      const pageSize = this.pageSize || 0;

      const pageParams = {};
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      pageParams['X-Plex-Container-Start'] = pageStart;
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      pageParams['X-Plex-Container-Size'] = pageSize;

      url = path.join(url, pageParams);
    }

    return url;
  },

  convert: function (xml: any, element: any) {
    const json = _.isObject(xml) ? xml : parser.parseXML(xml);

    if (!element) {
      return json;
    }

    let elements = json[element];

    if (!elements) {
      return [];
    }

    if (!Array.isArray(elements)) {
      elements = [elements];
    }

    return elements;
  },

  // Override clone to pass in our custom options.
  clone: function (models: any) {
    const options = {
      server: this.server,
      section: this.section,
      parent: this.parent,
      url: this.url,
    };

    // If no models are passed in, use the collection's existing models.
    models || (models = this.models);

    return new this.constructor(models, options);
  },

  cloneDeep: function (models: any) {
    if (!models) {
      models = this.map(function (model: any) {
        return model.clone();
      });
    }

    return this.clone(models);
  },

  clear: function (options: any) {
    options = _.extend({ silent: true }, options);

    // If a request is pending, abort it so the response doesn't come back
    // after we already cleared the collection, defeating our efforts.
    this.abortPendingPopulate();
    this.resetPopulatePromise(null);

    this.reset([], options);
  },
});

// A little tricky, but make sure each subclass gets its own cache. We can't
// override extend without doing it after the initial extend call. The cache
// is what you might call a static variable, so we need to define it on the
// new class' prototype.

BaseCollection.extend = function () {
  const subclass = Backbone.Collection.extend.apply(this, arguments);

  subclass.prototype.cache = new CollectionCache();

  return subclass;
};

export default BaseCollection;
