export default function getUTCDate(date: Date = new Date()): Date {
  // date-fns methods always return a localized date, so we create a new Date
  // object where we add the timezone offset from the provided date.
  // See: https://github.com/date-fns/date-fns/issues/376
  // See: https://github.com/date-fns/date-fns/issues/376#issuecomment-454163253
  return new Date(date.getTime() + date.getTimezoneOffset() * 60 * 1000);
}
