import { useCallback, useState } from 'react';

export default function useModalState(confirmCallback?: () => void): {
  isOpen: boolean;
  onOpen: () => void;
  onClose: () => void;
  onConfirm: () => void;
} {
  const [isOpen, setIsOpen] = useState(false);

  const onOpen = useCallback(() => setIsOpen(true), []);
  const onClose = useCallback(() => setIsOpen(false), []);

  const onConfirm = useCallback(() => {
    setIsOpen(false);

    if (confirmCallback) {
      confirmCallback();
    }
  }, [confirmCallback]);

  return {
    isOpen,
    onOpen,
    onClose,
    onConfirm,
  };
}
