import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import stateMachineMixin from 'app/js/mixins/views/stateMachineMixin';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/alert... Remove this comment to see the full error message
import tpl from 'app/templates/alerts/AlertView.tpl';

const AlertView = Marionette.ItemView.extend({
  mixins: [stateMachineMixin],
  //
  // Properties
  className: 'alert transition-in',
  template: tpl,
  alertDuration: 2000,
  transitionDuration: 500,
  events: {
    mouseenter: 'onMouseEnter',
    mouseleave: 'onMouseLeave',
    'click .close-btn': 'onCloseClick',
  },
  initialState: 'transitionIn',
  states: {
    transitionIn: {
      nextState: 'visible',
      onEnter: function () {
        // Trigger a reflow, which recalculates styles. Otherwise, the
        // browser will simply overwrite our initial styles without
        // transitioning.
        (this as any).$el.triggerReflow();
        (this as any).$el.removeClass('transition-in');
        (this as any).startTransition();
      },
    },
    visible: {
      nextState: 'transitionOut',
      onEnter: function () {
        (this as any).transitionOutTimeout = window.setTimeout(
          (this as any).enterNextState,
          (this as any).alertDuration
        );
      },
    },
    transitionOut: {
      onEnter: function () {
        (this as any).$el.addClass('transition-out');
        (this as any).startTransition();
      },
    },
  },
  //
  // Init
  initialize: function (options: any) {
    this.bindAllWhenRendered(['enterNextState']);
    if (_.isString(options)) {
      options = { message: options };
    }
    this.type = options.type;
    this.message = options.message;
    // Add a state class to the root element if needed. If no type is
    // provided, the default alert style will be used.
    if (this.type) {
      this.$el.addClass('has-' + this.type);
    }
  },
  //
  // Control
  serializeData: function () {
    return {
      type: this.type,
      message: this.message,
    };
  },
  startTransition: function () {
    window.setTimeout(this.enterNextState, this.transitionDuration);
  },
  //
  // Listeners
  onRender: function () {
    // TODO: Use onAttach after upgrading Marionette.
    _.defer(this.enterNextState);
  },
  onStateMachineComplete: function () {
    // If we have cycled through all states, it's time to close the view.
    this.close();
  },
  onMouseEnter: function () {
    window.clearTimeout(this.transitionOutTimeout);
  },
  onMouseLeave: function () {
    // Hide the alert quickly if the user mouses over and then mouses out.
    if (this.currentState === 'visible') {
      window.clearTimeout(this.transitionOutTimeout);
      this.transitionOutTimeout = window.setTimeout(
        this.enterNextState,
        this.alertDuration / 2
      );
    }
  },
  onCloseClick: function (event: any) {
    event.preventDefault();
    if (this.currentState === 'visible') {
      window.clearTimeout(this.transitionOutTimeout);
      this.enterNextState();
    }
  },
});
export default AlertView;
