import createDeepEqualSelector from 'app/js/stores/selectors/createDeepEqualSelector';
import selectAllProviderEntities from 'app/js/stores/selectors/provider/selectAllProviderEntities';
import selectAllServerEntities from 'app/js/stores/selectors/server/selectAllServerEntities';
import selectManageableServerEntityIDs from 'app/js/stores/selectors/server/selectManageableServerEntityIDs';
import getDirectoryUpgrade from 'app/js/utils/server/getDirectoryUpgrade';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import { ContentDirectory } from 'app/js/views/v3/helpers/props/shapes/contentDirectoryShape';
import getProvidersContentDirectories from 'app/js/views/v3/helpers/provider/getProvidersContentDirectories';

export type LibraryUpgradeTuple = [
  machineIdentifier: string,
  serverEntityID: string,
  providerEntityID: string,
  contentDirectory: ContentDirectory
];

const selectAvailableLibraryUpgrades = createDeepEqualSelector(
  selectManageableServerEntityIDs,
  selectAllServerEntities,
  selectAllProviderEntities,
  (serverEntityIDs: string[], serverEntities: any, providerEntities: any) => {
    return serverEntityIDs.reduce<LibraryUpgradeTuple[]>(
      (acc, serverEntityID: string) => {
        const server = serverEntities[serverEntityID];

        const providers: Provider[] = server.providers.map(
          (providerEntityID: string) => providerEntities[providerEntityID]
        );

        const contentDirectories = getProvidersContentDirectories(providers, [
          metadataTypes.MOVIE,
          metadataTypes.SHOW,
          metadataTypes.ARTIST,
        ]);

        const upgradeableTuples = contentDirectories
          .map(([providerEntityID, contentDirectory]) => {
            const upgrade = getDirectoryUpgrade(server, contentDirectory);
            const result: [string, ContentDirectory, boolean] = [
              providerEntityID,
              contentDirectory,
              upgrade.hasPlexMusicUpgrade ||
                upgrade.hasPlexMovieUpgrade ||
                upgrade.hasPlexSeriesUpgrade,
            ];
            return result;
          })
          .filter(([, , hasAvailableUpgrade]) => hasAvailableUpgrade);

        if (upgradeableTuples.length > 0) {
          upgradeableTuples.forEach(([providerEntityID, contentDirectory]) => {
            acc.push([
              server.machineIdentifier,
              serverEntityID,
              providerEntityID,
              contentDirectory,
            ]);
          });
        }

        return acc;
      },
      []
    );
  }
);

export default selectAvailableLibraryUpgrades;
