/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';
import createTypes from 'app/js/utils/types/createTypes';

export const LITTLE_KID = 'little_kid';
export const OLDER_KID = 'older_kid';
export const TEEN = 'teen';
export const UNRESTRICTED = 'unrestricted';

const restrictionProfileTypes = createTypes(
  ['identifier'],
  [
    {
      identifier: LITTLE_KID,
      title: () => t('Younger Kid'),
      defaultUsername: () => t('Kids'),
    },
    {
      identifier: OLDER_KID,
      title: () => t('Older Kid'),
      defaultUsername: () => t('Kids'),
    },
    {
      identifier: TEEN,
      title: () => t('Teen'),
      defaultUsername: () => t('Kids'),
    },
    {
      identifier: UNRESTRICTED,
      title: () => t('None'),
      defaultUsername: () => '',
    },
  ]
);

export const getType = restrictionProfileTypes.get;
export const getAllTypes = restrictionProfileTypes.getAll;
