import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import dispatcher from 'app/js/utils/dispatcher';
import EditSectionModal from 'app/js/views/modals/EditSectionModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/setup/SectionCardListItem.tpl';

const icons = {
  artist: 'music',
  movie: 'film',
  photo: 'picture',
  show: 'display',
};

const SectionCardListItem = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'li',
  className: 'card-tile-list-single-item',
  template: tpl,

  events: {
    'click .edit-section-btn': 'onEditClick',
    'click .delete-section-btn': 'onDeleteClick',
  },

  //
  // Control

  serializeData: function () {
    const server = this.model.server;
    const section = this.model.toJSON();
    const location = section.locations?.[0];
    const pathCount = section.locations ? section.locations.length : 0;

    return {
      section: section,
      serverID: server.id,
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      icon: icons[section.type],
      path: location.path,
      more: pathCount - 1,
    };
  },

  //
  // Bindings

  bindings: {
    '.section-title': {
      modelAttribute: 'title',
    },
  },

  //
  // Listeners

  onEditClick: function (event: any) {
    event.preventDefault();

    const model = this.model;
    const modal = new EditSectionModal({
      model,
      server: model.server,
      isDefaultSection: model.isNew(),
    });

    // Default sections are initially unsaved. If they're saved manually
    // during the setup process, a real section is added to the server's
    // section list, but the default section is not removed, so remove it
    // manually when the modal is dismissed.

    if (model.isNew()) {
      modal.promise.always(() => {
        model.trigger('destroy', model, model.collection);
      });
    }

    dispatcher.trigger('show:modal', modal);
  },

  onDeleteClick: function (event: any) {
    event.preventDefault();

    // We don't need to show a confirm modal when deleting a default section.

    const model = this.model;
    if (model.isNew()) {
      model.trigger('destroy', model, model.collection);
    } else {
      commands.execute('deleteSection', { section: model });
    }
  },
});

export default SectionCardListItem;
