import React, { Component } from 'react';
import t from 'app/js/utils/t';
import TagInjector from 'app/js/views/v3/components/core/TagInjector';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import pmsLogo from 'app/img/pms-logo.svg';
import styles from 'app/js/views/v3/components/pages/empty/InsecureFallbackChoicePage.css';

type InsecureFallbackChoicePageProps = {
  title: string;
  description?: string;
  children: React.ReactNode;
};

class InsecureFallbackChoicePage extends Component<InsecureFallbackChoicePageProps> {
  //
  // Render
  render() {
    const { title, description, children } = this.props;

    return (
      <EmptyPage
        description={
          <>
            <TagInjector
              message={t(
                'Its "Secure connections" setting may be set to disabled, or you may need to adjust some settings on your network. You can {openLink}learn more about secure connections here{closeLink}.'
              )}
              tags={[
                {
                  open: 'openLink',
                  close: 'closeLink',
                  component: Link,
                  to: 'https://support.plex.tv/hc/en-us/articles/206225077-How-to-Use-Secure-Server-Connections',
                  kind: kinds.PRIMARY,
                  target: '_blank',
                },
              ]}
            />
          </>
        }
        iconSrc={pmsLogo}
        title={title}
      >
        {description ? (
          <div className={styles.description}>{description}</div>
        ) : null}

        {children}
      </EmptyPage>
    );
  }
}

export default InsecureFallbackChoicePage;
