/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import paginatedListModelMixin from 'app/js/models/mixins/paginatedListModelMixin';
import * as responseTypes from 'app/js/models/responseTypes';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import replaceMetadataXMLElementNames from 'app/js/utils/parser/replaceMetadataXMLElementNames';
import parser2 from 'app/js/utils/parser2';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import path from 'app/js/utils/url/path';
import MetaModel from './MetaModel';
import MetadataCollection from './MetadataCollection';

const MetadataListModel = AbstractModel.extend({
  mixins: [paginatedListModelMixin],
  //
  // Properties
  name: 'MetadataListModel',
  context: [
    'server',
    'provider',
    'section',
    'metadataItem',
    'identifier',
    'mediaTagPrefix',
    'mediaTagVersion',
    'allowSync',
    'excludeFields',
    'excludeElements',
    'excludeAllLeaves',
    'includeRelated',
    'includeCollections',
    'includeExternalMedia',
    'includeAdvanced',
    'includeMeta',
  ],
  responseContext: [
    'identifier',
    'mediaTagPrefix',
    'mediaTagVersion',
    'allowSync',
  ],
  entityType: entityTypes.METADATA_LIST,
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    return createEntityID(
      this.entityType,
      state.entityKey || state.containerKey || state.key || this.cid,
      (this.provider || this.server).entityID()
    );
  },
  idAttribute: 'containerKey',
  collectionName: 'metadataItems',
  responseType() {
    return getSupportedResponseType(this.server);
  },
  url() {
    const paginatedUrl = this.getPaginatedUrl(
      this.get('containerKey') || this.get('key')
    );
    const params = {
      excludeElements: this.excludeElements ? this.excludeElements : undefined,
      excludeFields: this.excludeFields ? this.excludeFields : undefined,
      excludeAllLeaves: this.excludeAllLeaves ? 1 : undefined,
      includeRelated: this.includeRelated ? 1 : undefined,
      includeCollections: this.includeCollections ? 1 : undefined,
      includeExternalMedia: this.includeExternalMedia ? 1 : undefined,
      includeAdvanced: this.includeAdvanced ? 1 : undefined,
      includeMeta: this.includeMeta ? 1 : undefined,
    };
    return paginatedUrl ? path.join(paginatedUrl, params) : paginatedUrl;
  },
  sessionAttrs: {
    // Prefer `containerKey` instead of `key` since the `key` attribute that
    // exists when fetching children is the `ratingKey`, which would override
    // our valid `key` value. We need `containerKey` to be stable.
    isMetadataList: attrTypes.boolean,
    entityKey: attrTypes.string,
    containerKey: attrTypes.string,
    containerTitle: attrTypes.string,
    currentPageStart: attrTypes.integer,
    currentPageSize: attrTypes.integer,
    populatedRange: attrTypes.object,
    isPending: attrTypes.boolean,
    isPopulated: attrTypes.boolean,
    error: attrTypes.object,
  },
  syncedAttrs: {
    isDirectoryList: attrTypes.boolean,
    isMetaPopulated: attrTypes.boolean,
    key: attrTypes.string,
    librarySectionID: attrTypes.string,
    totalSize: attrTypes.integer,
    viewGroup: attrTypes.string,
    title: attrTypes.string,
    thumb: attrTypes.string,
    art: attrTypes.string,
    banner: attrTypes.string,
    theme: attrTypes.string,
    allowSync: attrTypes.boolean,
  },
  syncedRelations: {
    metadataItems: {
      relationClass: MetadataCollection,
      rootPropertyName: 'Metadata',
      // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'data' implicitly has an 'any' type.
      options(data) {
        return {
          containerKey: data.containerKey || data.key,
        };
      },
      // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'parseOptions' implicitly has an 'any' t... Remove this comment to see the full error message
      setOptions(parseOptions) {
        // `fetchRange` will add these options, which we'll need to provide when
        // setting models for the collection.
        return {
          remove: parseOptions.remove,
          at: parseOptions.at,
        };
      },
    },
    meta: {
      relationClass: MetaModel,
      rootPropertyName: 'Meta',
      deferred: true,
    },
  },
  derivedAttrs: {
    firstItemType() {
      const firstItem = (this as any).getCollection().first();
      // If the list is reset, use the last known value. Avoid `get` since we
      // don't want a listener to its own changes.
      return firstItem
        ? firstItem.get('type')
        : (this as any).attributes.firstItemType;
    },
    firstItemSubtype() {
      const firstItem = (this as any).getCollection().first();
      // If the list is reset, use the last known value. Avoid `get` since we
      // don't want a listener to its own changes.
      return firstItem
        ? firstItem.get('subtype')
        : (this as any).attributes.firstItemSubtype;
    },
    isFirstItemLibraryItem() {
      const firstItem = (this as any).getCollection().first();
      // If the list is reset, use the last known value. Avoid `get` since we
      // don't want a listener to its own changes.
      return firstItem
        ? firstItem.get('isLibraryItem')
        : (this as any).attributes.isFirstItemLibraryItem;
    },
    isChannelsList() {
      const firstItem = (this as any).getCollection().first();
      // If the list is reset, use the last known value. Avoid `get` since we
      // don't want a listener to its own changes.
      return firstItem
        ? firstItem.get('dvrChannel')
        : (this as any).attributes.isChannelsList;
    },
  },
  defaults() {
    return {
      isMetadataList: true,
      isDirectoryList: false,
      populatedRange: {
        start: 0,
        end: 0,
        size: 0,
      },
      isPending: true,
      isPopulated: false,
      isMetaPopulated: false,
      error: null,
    };
  },
  //
  // Init
  // @ts-expect-error ts-migrate(6133) FIXME: 'attributes' is declared but its value is never re... Remove this comment to see the full error message
  initialize(attributes) {
    this._totalSizeAdjustment = 0;
    this.startPopulatedRangeMonitor();
    this.on('change:key change:containerKey', this.onContainerKeyChange, this);
  },
  uninitialize() {
    this.off('change:key change:containerKey', this.onContainerKeyChange, this);
    this.stopListening();
  },
  //
  // Control
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'responseText' implicitly has an 'any' t... Remove this comment to see the full error message
  augmentResponseText(responseText, responseType) {
    // By Folder requests use type-less <Directory> elements inline with
    // metadata items. This means that we need to parse all directories as
    // metadata items instead of just directories with known metadata types.
    const key = this.get('containerKey') || this.get('key');
    const includeDirectories = /\/folder(\?|$)/.test(key);
    return responseType === responseTypes.xml
      ? replaceMetadataXMLElementNames(responseText, { includeDirectories })
      : responseText;
  },
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'response' implicitly has an 'any' type.
  augmentResponse(response, options) {
    // These need to be set before relations are parsed so each metadata item
    // has the proper context on instantiation.
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'contextName' implicitly has an 'any' ty... Remove this comment to see the full error message
    this.responseContext.forEach((contextName) => {
      const context = response[contextName];
      if (context !== undefined) {
        this.setContext(contextName, context);
      }
    });
    const metadataItems = parser2.parseArray(response.Metadata);
    const directoryItems = parser2.parseArray(response.Directory);
    const totalSize = parser2.parseInteger(response.totalSize);
    // The All Leaves (e.g. All Episodes) shortcuts introduce all kinds of
    // problems because their keys and types are not standard.
    if (!this.allowAllLeaves) {
      response.Metadata = _.reject(
        metadataItems,
        function (item) {
          const isAllLeaves = /\/allLeaves$/.test(item.key);
          if (isAllLeaves) {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this._totalSizeAdjustment -= 1;
          }
          return isAllLeaves;
        },
        this
      );
    }
    if (!metadataItems.length && response.Directory) {
      response.Metadata = directoryItems;
      response.isDirectoryList = true;
    }
    if (this.includeMeta) {
      response.isMetaPopulated = true;
    }
    if (totalSize && this._totalSizeAdjustment) {
      response.totalSize = totalSize - this._totalSizeAdjustment;
    }
    // When fetching the library /all endpoint, there is no `title` attribute,
    // but there is a `title1` attribute. Without a list title, we can't provide
    // a suggested title in the sync or optimize modals.
    if (!response.title && response.title1) {
      response.title = response.title1;
    }
    return response;
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
  onContainerKeyChange(model, containerKey) {
    // The `containerKey` will change when a metadata item parses its
    // `childrenKey` and sets the value on its `children` relation.
    const metadataItems = this.get('metadataItems');
    const context = this.syncedRelations.metadataItems.options(this.attributes);
    _.each(context, (value, key) => {
      metadataItems.setContext(key, value);
    });
  },
});
export default MetadataListModel;
