/* eslint-plugin-disable @typescript-eslint */

import parser2 from 'app/js/utils/parser2';

function getAuthXHRErrors(xhr: any) {
  const jsonResponse = xhr && xhr.responseJSON;

  const xmlResponse =
    !jsonResponse &&
    xhr &&
    xhr.responseText &&
    parser2.parseXML(xhr.responseText);

  const errors =
    (jsonResponse && jsonResponse.errors) || (xmlResponse && xmlResponse.error);

  if (!errors) {
    return [];
  }

  return Array.isArray(errors) ? errors : [errors];
}

export default getAuthXHRErrors;
