/* eslint-plugin-disable @typescript-eslint */

import addSearchParams from 'app/js/utils/url/addSearchParams';

export default function getUrlWithCampaignParams(baseUrl: any, context: any) {
  if (!context || !context.campaign) {
    return baseUrl;
  }

  const campaignParams = Object.keys(context.campaign).reduce((acc, key) => {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    acc[`utm_${key}`] = context.campaign[key];
    return acc;
  }, {});

  return addSearchParams(baseUrl, campaignParams);
}
