import $ from 'jquery';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import * as upsellOrigins from 'app/js/utils/constants/upsellOrigins';
import dispatcher from 'app/js/utils/dispatcher';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import t from 'app/js/utils/t';
import PlexPassModal from 'app/js/views/v3/components/user/modals/PlexPassModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setup... Remove this comment to see the full error message
import tpl from 'app/templates/setup/SetupBasicView.tpl';

const SetupBasicView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'setup-basic-container',
  template: tpl,

  ui: {
    modalContainer: '.modal-container',
    connectCheckbox: '#connect',
    publishServerCheckbox: '#PublishServerOnPlexOnlineKey',
    submitBtn: '.submit-btn',
    nextBtn: '.next-btn',
  },

  collectionEvents: {
    reset: 'render',
  },

  events: {
    'change input[id]': 'onInputChange',
    'submit #setup-basic-form': 'onSubmit',
    'change #PublishServerOnPlexOnlineKey': 'onPublishSettingChange',
  },

  //
  // Init

  initialize: function () {
    _.bindAll(this, 'onPlexPassModalClose');

    this.collection = this.model.get(__PLATFORM__).get('settings');
    this.collection.populate({ reset: true });

    // The `FirstRun` pref was removed from PMS in favor of `AcceptedEULA`
    // in version 0.9.15.5.

    if (this.model.isVersionOrNewer('0.9.15.5')) {
      this.changes = {
        PublishServerOnPlexOnlineKey: 1,
      };
    } else {
      this.changes = {
        FirstRun: 0,
        PublishServerOnPlexOnlineKey: 1,
      };
    }

    const superToken = this.model.get('superToken');
    if (superToken) {
      this.changes.PlexOnlineToken = superToken;
    }

    const appModel = resolve('appModel');
    const hasPlexSubscription = appModel.get('user').get('hasPlexSubscription');

    // If the server is eligible for the Intel Plex Pass Lite program, do
    // not show the Plex Pass upsell in this step.

    this.isPlexPassModalOpen =
      !this.model.get('isDesktop') && !hasPlexSubscription;
  },

  //
  // Control

  serializeData: function () {
    const server = this.model.toJSON();
    const friendlyNameSetting = this.collection.get('FriendlyName');
    const publishSetting = this.collection.get('PublishServerOnPlexOnlineKey');

    return {
      server: server,
      friendlyNameSetting: friendlyNameSetting
        ? friendlyNameSetting.toJSON()
        : null,
      publishSetting: publishSetting ? publishSetting.toJSON() : null,
    };
  },

  mountReact: function () {
    if (!__BUNDLED__) {
      return;
    }

    ReactDOM.render(
      <Provider store={resolve('appStore')}>
        <PlexPassModal
          isOpen={this.isPlexPassModalOpen}
          metricsProperties={{
            context: metricsContexts.SETUP_WIZARD,
            firstRun: true,
          }}
          origin={upsellOrigins.SERVER_SETUP}
          onModalClose={this.onPlexPassModalClose}
        />
      </Provider>,
      this.ui.modalContainer[0]
    );
  },

  unmountReact: function () {
    if (__BUNDLED__ && this.isRendered) {
      ReactDOM.unmountComponentAtNode(this.ui.modalContainer[0]);
    }
  },

  submit: function () {
    const submitBtn = this.ui.submitBtn;
    if (submitBtn.hasClass('active')) {
      return;
    }

    submitBtn.addClass('active');

    resolve('metrics').trackClick({
      action: metricsActions.REMOTE_ACCESS,
      page: metricsPages.SETUP_WIZARD,
      pane: 'name',
      firstRun: true,
      value: this.ui.publishServerCheckbox.is(':checked'),
    });

    let promise = this.save();

    promise = promise.done(() => {
      this.ui.nextBtn.click();
    });

    promise.always(function () {
      submitBtn.removeClass('active');
    });
  },

  save() {
    // We only want to get into server-claiming if saving settings worked.
    // Server claiming may have its own failures which should display warnings
    // but shouldn't halt the setup process.
    return this.saveChanges().then(() => this.claimServer());
  },

  saveChanges() {
    const serverChangesPromise = _.isEmpty(this.changes)
      ? $.Deferred().resolve().promise()
      : this.collection.first().save(this.changes);

    return serverChangesPromise.then(null, () => {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem saving these settings.'),
      });
    });
  },

  claimServer() {
    const appModel = resolve('appModel');
    const cloudServer = appModel.get('cloudServer');

    const shouldClaimServer =
      this.model.supports('claimServer') &&
      !this.model.get('isClaimed') &&
      !this.model.get('superToken') &&
      !appModel.get('user').get('anonymous');

    // Succeed if for any reason the server doesn't need to be claimed
    if (!shouldClaimServer) {
      return $.Deferred().resolve().promise();
    }

    return alwaysResolve(
      cloudServer.populate().then(() => {
        return commands
          .execute('signInServer', {
            claim: true,
            cloudAccount: this.model.get(__PLATFORM__).get('cloudAccount'),
          })
          .then(null, () => {
            dispatcher.trigger('show:alert', {
              type: 'error',
              message: t('There was a problem claiming this server.'),
            });
          });
      })
    );
  },

  //
  // Listeners

  onBeforeRender: function () {
    this.unmountReact();
  },

  onRender: function () {
    this.mountReact();
  },

  onClose: function () {
    this.unmountReact();
  },

  onInputChange: function (event: any) {
    const field = event.currentTarget;
    let value = field.value;

    if (field.type === 'checkbox') {
      value = field.checked ? 1 : 0;
    }

    this.changes[field.id] = value;
  },

  onSubmit: function (event: any) {
    event.preventDefault();

    this.submit();
  },

  onPlexPassModalClose: function () {
    this.isPlexPassModalOpen = false;
    this.mountReact();
  },
});

export default SetupBasicView;
