/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const DeviceModel = AbstractModel.extend({
  //
  // Properties

  name: 'DeviceModel',
  context: ['server'],

  rootPropertyName: 'Device',

  url() {
    return '/devices/' + this.id + '.xml';
  },

  syncedAttrs: {
    id: attrTypes.string,
    clientIdentifier: attrTypes.string,
    name: attrTypes.string,
    product: attrTypes.string,
    productVersion: attrTypes.string,
    platform: attrTypes.string,
    platformVersion: attrTypes.string,
    device: attrTypes.string,
    model: attrTypes.string,
    provides: attrTypes.list,
    screenResolution: attrTypes.string,
    screenDensity: attrTypes.string,
    lastSeenAt: attrTypes.integer,
  },

  //
  // Control

  supports(feature: any) {
    return this.get('provides').indexOf(feature) !== -1;
  },
});

export default DeviceModel;
