/* eslint-plugin-disable @typescript-eslint */

import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import routes from 'app/js/v4/routes/routes';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import EmptyPageDescription from 'app/js/views/v3/components/pages/empty/EmptyPageDescription';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';

type ServerUnclaimedPageProps = {
  machineIdentifier?: string;
  friendlyName?: string;
  isPlexAccountRequired: boolean;
  dispatchDismissUnclaimedServer: (...args: any[]) => any;
};

function ServerUnclaimedPage(props: ServerUnclaimedPageProps) {
  const {
    machineIdentifier,
    friendlyName,
    isPlexAccountRequired,
    dispatchDismissUnclaimedServer,
  } = props;

  const history = useHistory();

  const onSignInPress = useCallback(() => {
    history.push(routes.login);
  }, [history]);

  const onClaimPress = useCallback(() => {
    resolve('metrics').trackClick({
      page: metricsPages.UNCLAIMED_SERVER,
      action: metricsActions.CLAIM,
    });
  }, []);

  const onDismissPress = useCallback(() => {
    resolve('metrics').trackClick({
      page: metricsPages.UNCLAIMED_SERVER,
      action: metricsActions.DISMISS,
    });

    dispatchDismissUnclaimedServer({ machineIdentifier });
  }, [machineIdentifier, dispatchDismissUnclaimedServer]);

  return (
    <ErrorPage
      description={t(
        'Claiming this server will associate it with your Plex account. This helps your devices find each other and helps keep your media safe.'
      )}
      title={t('{friendlyName} server is unclaimed and not secure', {
        friendlyName,
      })}
    >
      {isPlexAccountRequired ? (
        <Button kind={kinds.PRIMARY} size={sizes.LARGE} onPress={onSignInPress}>
          {t('Sign in to claim this server')}
        </Button>
      ) : (
        <Button
          kind={kinds.PRIMARY}
          size={sizes.LARGE}
          to={getServerSettingsRoute('settings', 'general')}
          onPress={onClaimPress}
        >
          {t('Claim it now')}
        </Button>
      )}
      <EmptyPageDescription>
        <Link kind={kinds.PRIMARY} size={sizes.SMALL} onPress={onDismissPress}>
          {t("Don't show this again")}
        </Link>
      </EmptyPageDescription>
    </ErrorPage>
  );
}

export default ServerUnclaimedPage;
