import attrTypes from 'app/js/models/attrTypes';
import HomeSettingsModel from 'app/js/models/settings/HomeSettingsModel';
import SidebarSettingsModel from 'app/js/models/settings/SidebarSettingsModel';

export const userExperienceSettingsID = 'experience';

const userExperienceModelAttrs = {
  sessionAttrs: {
    sidebarPreferredSourcesSavedEnteredPath: attrTypes.string,
    isDirty: attrTypes.boolean.isRequired,
    isSaving: attrTypes.boolean.isRequired,
    saveError: attrTypes.object,
  },
  syncedAttrs: {
    updatedAt: attrTypes.integer,
    autoHomeHubsEnabled: attrTypes.boolean,
    shouldSaveLastSourcePivots: attrTypes.boolean.isRequired,
    lastSourcePivots: attrTypes.object.isRequired,
    savedPivotParams: attrTypes.object.isRequired,
    disabledUniversalSearchIdentifiers: attrTypes.object.isRequired,
    autoPinnedProviders: attrTypes.array.isRequired,
    dismissedUpsellModals: attrTypes.array,
  },
  syncedRelations: {
    homeSettings: {
      relationClass: HomeSettingsModel,
    },
    sidebarSettings: {
      relationClass: SidebarSettingsModel,
    },
  },
  defaults: {
    // autoHomeHubsEnabled is set during the settings migration. We check for null
    // to see if it has been migrated already or has been manually set by the user
    autoHomeHubsEnabled: null,
    sidebarPreferredSourcesSavedEnteredPath: null,
    isDirty: false,
    isSaving: false,
    saveError: null,
    shouldSaveLastSourcePivots: true,
    lastSourcePivots: {},
    savedPivotParams: {},
    disabledUniversalSearchIdentifiers: {},
    autoPinnedProviders: [],
    dismissedUpsellModals: [],
  },
};

export default userExperienceModelAttrs;
