/* eslint-plugin-disable @typescript-eslint */

import escapeRegExp from 'app/js/utils/string/escapeRegExp';

function removeUrlParam(url: any, paramName: any) {
  const regExp = new RegExp(
    `(\\?|&)(${escapeRegExp(paramName)}=[^\\?&]+(?:[\\?&]?))`,
    'g'
  );

  return (
    url
      // @ts-expect-error ts-migrate(6133) FIXME: 'full' is declared but its value is never read.
      .replace(regExp, (full: any, delimiter: any) => delimiter)
      .replace(/(\?|&)$/, '')
  );
}

export default removeUrlParam;
