/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function optimizeCommand(options: any) {
  const deferred = $.Deferred();
  const server = options.server;
  const url = '/library/optimize';
  const xhrOptions = _.extend(server.getXHROptions(url), { type: 'PUT' });

  function optimize() {
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(function () {
      dispatcher.trigger('show:alert', {
        type: 'success',
        message: t('The database was optimized.'),
      });

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem optimizing this database.'),
      });

      deferred.reject(xhr);
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr) {
      confirmModal.hide();
    });
  }

  const confirmModal = new ConfirmModal({
    title: t('Optimize Database'),
    message: t(
      "Cleans up the library database and ensures it is operating as quickly as possible. This is especially useful when you've added or removed a large amount of media from the library."
    ),
    confirmLabel: t('Optimize'),
    cancelLabel: t('Cancel'),
    async: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(optimize);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return deferred.promise();
}

export default optimizeCommand;
