import { APIRequestConfig, Headers } from 'app/js/v4/utils/data/api';

function getAllowedHeaders(headers: Headers | undefined): Headers | undefined {
  if (!headers) {
    return headers;
  }

  const {
    ['X-Plex-Username']: forbiddenUsername,
    ['X-Plex-Device-Name']: forbiddenDeviceName,
    ...allowedHeaders
  } = headers;

  return allowedHeaders;
}

export default function omitProviderForbiddenHeadersFilter(
  config: APIRequestConfig
): APIRequestConfig {
  return {
    ...config,
    headers: getAllowedHeaders(config.headers),
  };
}
