/* eslint-plugin-disable @typescript-eslint */

import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import createChainedCallback from 'app/js/views/v3/helpers/createChainedCallback';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';

class MetricsClickTracker extends Component {
  static propTypes = {
    children: PropTypes.element.isRequired,
    action: PropTypes.string.isRequired,
  };
  static contextTypes = {
    metricsPage: PropTypes.string.isRequired,
  };
  //
  // Lifecycle
  shouldComponentUpdate(nextProps: any, nextState: any) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }
  //
  // Listeners
  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onPress = (event: any) => {
    const { children, ...metricsProperties } = this.props;
    const { metricsPage } = this.context;
    resolve('metrics').trackClick({
      ...metricsProperties,
      page: metricsPage,
    });
  };
  //
  // Render
  render() {
    const child = React.Children.only(this.props.children);
    // @ts-expect-error ts-migrate(2769) FIXME: No overload matches this call.
    return React.cloneElement(child, {
      onPress: createChainedCallback(
        this.onPress,
        (child as any).props.onPress
      ),
    });
  }
}
export default MetricsClickTracker;
