import cloneDeep from 'lodash/cloneDeep';
import {
  DVR,
  MediaGrabberDevice,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { MediaContainerResponse } from 'app/js/v4/types/MediaContainerResponse';
import { normalizeAsString } from 'app/js/v4/utils/data/normalizers/normalizeUtils';
import { NormalizerOptions } from 'app/js/v4/utils/data/normalizers/normalizersTypes';
import { normalizeDevice } from './normalizeDevicesResponse';

export interface NormalizeDVRResponseOptions extends NormalizerOptions {
  data: MediaContainerResponse;
}

export default function normalizeDVRResponse({
  data,
}: NormalizeDVRResponseOptions): DVR | undefined {
  // PMS returns this as an array but users can only ever setup a single DVR.
  const dvrResponse = data.Dvr?.[0];

  // We request DVRs when loading the Live TV settings page. We can get back an
  // empty `MediaContainer` (i.e. before the user sets up their first tuner).
  if (!dvrResponse) {
    return;
  }

  const dvr = cloneDeep(dvrResponse) as unknown as DVR;

  normalizeAsString(dvr, 'key');
  dvr.Device = dvrResponse?.Device?.map<MediaGrabberDevice>(normalizeDevice);

  return dvr;
}
