/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

const bindingsMixin = {
  //
  // Control
  bindToModel: function () {
    if (!(this as any).bindings) {
      return;
    }
    _.each(
      _.keys((this as any).bindings),
      function (selector: any) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const binding = this.bindings[selector];
        let model: any;
        // Allow a model string on a specific binding to override the
        // bindingsModel or view model.
        if (binding.model) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          model = this[binding.model];
        } else {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          model = this[this.bindingsModel] || this.model;
        }
        if (!model) {
          return;
        }
        const modelListeners = binding.modelListeners || [
          binding.modelAttribute,
        ];
        _.each(
          modelListeners,
          function (modelListener) {
            const listener = {
              obj: model,
              name: 'change:' + modelListener,
              callback: _.bind(
                // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                this.renderBinding,
                // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                this,
                selector,
                binding,
                model
              ),
            };
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this._bindingListeners || (this._bindingListeners = []);
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this._bindingListeners.push(listener);
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.listenTo(listener.obj, listener.name, listener.callback);
          },
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this
        );
        // Update our element so the view is immediately populated.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.renderBinding(selector, binding, model);
      },
      this
    );
  },
  unbindFromModel: function () {
    if (!(this as any).bindings) {
      return;
    }
    _.each(
      (this as any)._bindingListeners,
      function (listener) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.stopListening(listener.obj, listener.name, listener.callback);
      },
      this
    );
    (this as any)._bindingListeners = null;
  },
  renderBinding: function (selector: any, binding: any, model: any) {
    const $selector = (this as any).$(selector);
    const formatter = binding.formatter;
    const isHtml = !!binding.isHtml;
    const autohide = binding.autohide;
    const hiddenClass = binding.hiddenClass || 'hidden';
    let value = model.get(binding.modelAttribute);
    // A formatter function can be provided that alters the value that we
    // use in the ui.
    if (formatter) {
      value = formatter.call(this, value, $selector);
    }
    // If autohide is set, the element will automatically be given the hide
    // class if the value is falsey or if the autohide function returns a
    // falsey value.
    if (autohide) {
      const hideToggle = _.isFunction(autohide)
        ? autohide.call(this, value)
        : value;
      $selector.toggleClass(hiddenClass, !hideToggle);
    }
    // Only update the element's html if the manual flag isn't set.
    if (!binding.manual) {
      // HTML entities should be escaped before the element is updated.
      if (!isHtml && _.isString(value)) {
        value = _.escape(value);
      }
      $selector.html(value);
    }
  },
};
export default bindingsMixin;
