/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import assert from 'app/js/utils/log/assert';

const controlledPopulateMixin = {
  fetch() {
    assert(
      false,
      'A model or collection with a controlled populate promise cannot be fetched directly'
    );
    return (this as any).populatePromise;
  },
  isPopulating() {
    return (
      !!(this as any).populatePromise &&
      (this as any).populatePromise.state() === 'pending'
    );
  },
  _resetPopulate() {
    if (!this.isPopulating()) {
      (this as any)._populateDeferred = $.Deferred();
      (this as any).populatePromise = (this as any)._populateDeferred.promise();
    }
    return (this as any).populatePromise;
  },
  _resolvePopulate() {
    if ((this as any).populatePromise.state() === 'resolved') {
      return (this as any).populatePromise;
    }
    if ((this as any).populatePromise.state() === 'rejected') {
      this._resetPopulate();
    }
    (this as any)._populateDeferred.resolveWith(this, [this]);
    return (this as any).populatePromise;
  },
  _rejectPopulate() {
    if ((this as any).populatePromise.state() === 'rejected') {
      return (this as any).populatePromise;
    }
    if ((this as any).populatePromise.state() === 'resolved') {
      this._resetPopulate();
    }
    (this as any)._populateDeferred.rejectWith(this, [this]);
    return (this as any).populatePromise;
  },
};
export default controlledPopulateMixin;
