/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import createDeepEqualSelector from 'app/js/stores/selectors/createDeepEqualSelector';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';

export function createStableProvider(provider: any) {
  if (!provider) {
    return null;
  }

  return {
    entityID: provider.entityID,
    id: provider.id,
    parentID: provider.parentID,
    identifier: provider.identifier,
    metricsIdentifier: provider.metricsIdentifier,
    isLibraryProvider: provider.isLibraryProvider,
    isPopulated: !!provider.isPopulated,
    epgSource: provider.epgSource,
    title: provider.title,
    sourceTitle: provider.sourceTitle,
    protocols: provider.protocols.reduce((acc: any, protocol: any) => {
      acc[protocol] = true;
      return acc;
    }, {}),
    features: provider.features.reduce((acc: any, feature: any) => {
      let finalFeature = feature;

      if (feature.type === providerFeatureTypes.ACTIONS) {
        // Create a new object so we don't mutate the feature in the state tree
        let newFeature = {
          type: providerFeatureTypes.ACTIONS,
        };

        newFeature = feature.actions.reduce((acc2: any, action: any) => {
          acc2[action.id] = action;

          return acc2;
        }, newFeature);

        finalFeature = newFeature;
      }

      acc[feature.type] = finalFeature;
      return acc;
    }, {}),
  };
}

// This selector is used frequently in metadata list items. Rather than create a
// unique selector for each component in order for reselect to memoize
// correctly, add a layer of memoization on top of reselect using the entity id
// since there is a small set of stable providers. This allows all metadata list
// items to reuse the same selector.

const memo = {};

function createStableProviderSelector() {
  return createSelector(
    (appState: any, providerEntityID: any) =>
      appState.entities.providers[providerEntityID],
    createStableProvider
  );
}

export default function selectStableProvider(
  appState: any,
  { providerEntityID }: any
) {
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  let selector = memo[providerEntityID];

  if (!selector) {
    selector = createDeepEqualSelector(
      createStableProviderSelector(),
      (provider) => provider
    );

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    memo[providerEntityID] = selector;
  }

  return selector(appState, providerEntityID);
}
