/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import config from 'app/js/models/config';
import ServerModel from 'app/js/models/server/ServerModel';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import getCloudServerConnection from 'app/js/utils/server/getCloudServerConnection';

function initServers() {
  log('[Servers] Initializing...');

  initCloudServer();
  // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
  initStoredServers();
  initConfigServer();
  initBundledServer();
  initLoopbackServer();

  resolve('serverManager').start();
  resolve('primaryServerManager').start();
  resolve('serverConnectionManager').start();
  resolve('companionServerManager').start();
  resolve('providerManager').start();
  resolve('serverUpdateManager').start();

  log('[Servers] Finished initialization');
}

function clearServers() {
  log('[Servers] Clearing...');

  const appModel = resolve('appModel');
  const servers = appModel.get('servers');

  resolve('serverManager').stop();
  resolve('primaryServerManager').stop();
  resolve('serverConnectionManager').stop();
  resolve('serverConnectionTestManager').reset();
  resolve('companionServerManager').stop();
  resolve('providerManager').stop();
  resolve('serverUpdateManager').stop();

  servers.uninitialize();
  servers.reset();
  servers.initialize();

  appModel.unset('primaryServer');
  appModel.unset('companionServer');
  appModel.unset('transientServer');
}

function logInitServer(serverModel: any) {
  const connections = serverModel
    .get('connections')
    .map((connectionModel: any) => connectionModel.get('uri'));

  log(
    '[Servers]',
    'Initialize server',
    serverModel.get('accessToken') ? 'with token,' : 'without token,',
    _.compact([
      serverModel.get('debugName'),
      serverModel.get('machineIdentifier'),
      ...connections,
    ]).join(', ')
  );
}

function initCloudServer() {
  // The cloud server is instantiated in `appModel`, but we need to provide it
  // a connection and add it to the collection.

  const appModel = resolve('appModel');
  const servers = appModel.get('servers');
  const cloudServer = appModel.get('cloudServer');

  cloudServer.addConnection(getCloudServerConnection());

  servers.add(cloudServer);
}

// @ts-expect-error ts-migrate(6133) FIXME: 'accessToken' is declared but its value is never r... Remove this comment to see the full error message
function initStoredServers(accessToken: any) {
  const appModel = resolve('appModel');
  const servers = appModel.get('servers');
  const storedServers = userStorageModel.getActiveServers();

  if (!storedServers) {
    return;
  }

  storedServers.each((storedServer: any) => {
    // Only allow a stored server with at least one connection to be added
    // to the collection. A server with zero connections is invalid.
    if (storedServer.get('connections').length) {
      try {
        const server = new ServerModel(storedServer.toJSON(), {
          parent: appModel,
        });
        const connections = server.get('connections');

        // Eliminate relay connections stored before ServerConnectionManager
        // began omitting relay connections from storage.
        connections.reset(
          connections.filter((connection: any) => !connection.get('relay'))
        );

        // Avoid adding mobile servers and a server which has no connections,
        // such as one with only relay connections.
        if (!server.get('isMobileServer') && connections.length > 0) {
          logInitServer(server);
          servers.add(server);
        }
      } catch (error) {
        // Ignore possible mismatch between cached server data and current model
        log.warn(error);
      }
    }
  });
}

function initConfigServer() {
  // It's possible for server data to be templated in using the global PLEXWEB
  // object. If such data exists, automatically create a server model for it.

  const appModel = resolve('appModel');
  const servers = appModel.get('servers');
  const serverFromConfig = config.primaryServer;

  if (!serverFromConfig) {
    return;
  }

  const configServer = new ServerModel(
    {
      machineIdentifier: serverFromConfig.machineIdentifier,
      friendlyName: serverFromConfig.name,
      isShared: serverFromConfig.shared,
      superToken: serverFromConfig.superToken,
      accessToken: serverFromConfig.accessToken,
      sourceTitle: serverFromConfig.sourceTitle,
    },
    { parent: appModel }
  );

  configServer.addConnection({
    scheme: serverFromConfig.scheme,
    address: serverFromConfig.address,
    port: serverFromConfig.port,
  });

  servers.add(configServer);
}

function initBundledServer() {
  if (!__BUNDLED__) {
    return;
  }

  // If the application is being served from the bundle, we can assume a
  // server exists at the window's host.

  const appModel = resolve('appModel');
  const servers = appModel.get('servers');
  const bundledServer = new ServerModel(
    { isBundled: true },
    { parent: appModel }
  );

  bundledServer.addConnection({
    scheme: window.location.protocol.replace(':', ''),
    address: window.location.hostname,
    port: window.location.port,
    isBundled: true,
  });

  logInitServer(bundledServer);
  servers.add(bundledServer);
}

function initLoopbackServer() {
  if (!config.allowLoopbackDiscovery) {
    return;
  }

  const appModel = resolve('appModel');
  const servers = appModel.get('servers');
  const loopbackServer = new ServerModel({}, { parent: appModel });

  loopbackServer.addConnection({
    scheme: 'http',
    address: '127.0.0.1',
    port: '32400',
  });

  logInitServer(loopbackServer);
  servers.add(loopbackServer);
}

export default function serverInitializer() {
  initServers();

  dispatcher.on('clear:servers', clearServers);
  dispatcher.on('init:servers', initServers);

  return $.Deferred().resolve().promise();
}
