import $ from 'jquery';
import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import AgentCollection from 'app/js/models/server/AgentCollection';
import ExclusiveAgentProxy from 'app/js/models/server/ExclusiveAgentProxy';
import ExclusiveScannerProxy from 'app/js/models/server/ExclusiveScannerProxy';
import ScannerCollection from 'app/js/models/server/ScannerCollection';
import ServerSettingCollection from 'app/js/models/server/ServerSettingCollection';
import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import t from 'app/js/utils/t';
import sectionTypes from 'app/js/utils/types/sectionTypes';
import path from 'app/js/utils/url/path';

const EditSectionBaseView = Marionette.Layout.extend({
  //
  // Properties
  ui: {
    formGroup: '.form-group',
  },
  onlyAfterRender: ['showValidationResult'],
  //
  // Control
  serializeData: function () {
    const data = {};
    if (this.model.isNew()) {
      (data as any).add = true;
    } else {
      (data as any).edit = true;
    }
    (data as any).section = this.model.toJSON();
    return data;
  },
  showValidationResult: function (invalidAttrs: any) {
    // Clear errors
    const formGroup = this.ui.formGroup;
    formGroup.removeClass('has-error');
    // Mark errors
    _.each(invalidAttrs, function (attr) {
      formGroup.filter('.' + attr + '-group').addClass('has-error');
    });
    this.trigger(invalidAttrs.length ? 'invalid' : 'valid');
  },
  createMenuOptions: function (options: any, selectedValue: any) {
    return _.map(options, function (option) {
      return (
        '<option value="' +
        option.value +
        '"' +
        (option.value === selectedValue ? ' selected' : '') +
        '>' +
        option.label +
        '</option>'
      );
    }).join('\n');
  },
  getSectionType: function () {
    if (this.model.sectionType) {
      return this.model.sectionType;
    }
    const sectionKey = this.model.get('type');
    let currentSectionType = sectionTypes.get(sectionKey);
    if (
      sectionKey === 'movie' &&
      this.model.get('agent') === agentIdentifiers.NONE
    ) {
      currentSectionType = sectionTypes.get('videos');
    }
    this.model.sectionType = currentSectionType;
    return currentSectionType;
  },
  setSectionType: function (sectionType: any) {
    this.model.sectionType = sectionType;
  },
  populateAgents: function () {
    const sectionType = this.getSectionType();
    if (!sectionType) {
      return $.Deferred().reject().promise();
    }
    if (!this.model.agents) {
      this.model.agents = new AgentCollection([], {
        server: this.model.server,
        mediaTypeID: sectionType.typeID,
      });
    }
    return this.model.agents.populate().then(function (agents: any) {
      // Discard XML response
      return agents;
    });
  },
  populateScanners: function () {
    const sectionType = this.getSectionType();
    if (!sectionType) {
      return $.Deferred().reject().promise();
    }
    if (!this.model.scanners) {
      this.model.scanners = new ScannerCollection([], {
        server: this.model.server,
        sectionTypeID: sectionType.typeID,
      });
    }
    return this.model.scanners.populate().then(function (scanners: any) {
      // Discard XML response
      return scanners;
    });
  },
  populateAgentsProxy: function () {
    const exclusiveAgents = this.getSectionType().exclusiveAgents;
    const proxy = new ExclusiveAgentProxy(
      this.model.agents,
      exclusiveAgents,
      this.model.get('agent')
    );
    this.model.agentsProxy = proxy;
    return $.Deferred().resolve(proxy).promise();
  },
  populateScannersProxy: function () {
    const exclusiveScanners = this.getSectionType().exclusiveScanners;
    const proxy = new ExclusiveScannerProxy(
      this.model.scanners,
      exclusiveScanners,
      this.model.get('scanner')
    );
    this.model.scannersProxy = proxy;
    return $.Deferred().resolve(proxy).promise();
  },
  populateSectionSettings: function () {
    // If the server doesn't suppose sectionSettings, just resolve
    if (!this.model.server.supports('sectionSettings')) {
      return $.Deferred().resolve().promise();
    }
    if (!this.model.settings) {
      const settings = new ServerSettingCollection([], {
        server: this.model.server,
      });
      // Always pass the agent through so PMS can pass back the correct settings.
      const params = {
        agent: this.model.get('agent'),
      };
      // If the section isn't persisted, request section default settings
      if (this.model.isNew()) {
        const sectionType = this.getSectionType();
        (params as any).type = sectionType.typeID;
        settings.url = path.join(['/library/sections/prefs'], params);
      } else {
        settings.url = path.join(
          ['/library/sections/', this.model.id, '/prefs'],
          params
        );
      }
      this.model.settings = settings;
    }
    return this.model.settings.populate().then(() => {
      const shouldSynthesizeImportFromiTunesSetting = !!(
        this.model.isNew() &&
        this.model.get('type') === 'music' &&
        this.model.server.supports('premiumMusic') &&
        !this.model.settings.findWhere({ id: 'importFromiTunes' }) &&
        this.model.server
          .get(__PLATFORM__)
          .get('settings')
          .findWhere({ id: 'iTunesLibraryXmlPath' })
      );
      if (shouldSynthesizeImportFromiTunesSetting) {
        this.model.settings.unshift({
          type: 'bool',
          id: 'importFromiTunes',
          value: false,
          label: t('Import from iTunes'),
          summary: t(
            'Importing from iTunes will import your music playlists. It will also import your ratings, the date you added the music, as well as play and skip counts for tracks in your library.'
          ),
        });
      }
      // END TODO
      return this.model.settings;
    });
  },
  populateSectionAgentSettings: function () {
    if (!this.model.agentSettings) {
      const agentSettings = new ServerSettingCollection([], {
        server: this.model.server,
        url: path.join(['/:/plugins/', this.model.get('agent'), '/prefs']),
      });
      this.model.agentSettings = agentSettings;
    }
    const promise = this.model.agentSettings.populate();
    return promise;
  },
  onModalClose: function () {
    this.model.scanners = null;
    this.model.scannersProxy = null;
    this.model.agents = null;
    this.model.agentsProxy = null;
    this.model.settings = null;
  },
});
export default EditSectionBaseView;
