/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import { NONE } from 'app/js/utils/constants/player/subtitleStreamIDs';
import formatPlayerTime from 'app/js/utils/dateTime/formatPlayerTime';
import formatBytes from 'app/js/utils/metadata/formatBytes';
import formatResolution from 'app/js/utils/metadata/formatResolution';
import formatVideoLevel from 'app/js/utils/metadata/formatVideoLevel';
import toUpperCase from 'app/js/utils/string/case/toUpperCase';
import formatBoolean from 'app/js/utils/string/format/formatBoolean';
import formatPropertyLabel from 'app/js/utils/string/format/formatPropertyLabel';
import path from 'app/js/utils/url/path';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/MediaInfoModal.tpl';

const hiddenProperties = [
  'id',
  'key',
  'index',
  'videoCodec',
  'audioCodec',
  'audioChannels',
  'default',
  'selected',
  'languageCode',
  'accessible',
  'exists',
  'provider',
  'lines',
  'minLines',
  'follow',
  'requiredBandwidths',
  'deepAnalysisVersion',
  'deepAnalysisDate',
  'Overlay',
];

const mapping = {
  size: formatBytes,
  duration: formatPlayerTime,
  codec: toUpperCase,
  format: toUpperCase,
  container: toUpperCase,
  videoResolution: formatResolution,
  bitrateMode: toUpperCase,
  accessible: formatBoolean,
  exists: formatBoolean,
  optimizedForStreaming: formatBoolean,
  timed: formatBoolean,
  file: path.getLastSegment,
};

const labels = {
  optimizedForStreaming: 'Web Optimized',
  has64bitOffsets: 'Has 64bit Offsets',
  cabac: 'CABAC',
  postURL: 'URL',
  codecID: 'Codec ID',
};

const units = {
  frameRate: ' fps',
  dialogNorm: ' dB',
  bitrate: ' kbps',
  samplingRate: ' Hz',
};

const sanitizeProperties = function (root: any, exempt: any) {
  const sanitized = { properties: [] };

  // Copy properties into an array so we can easily loop through them.

  for (const name in root) {
    if (name === exempt || _.indexOf(hiddenProperties, name) !== -1) {
      continue;
    }

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    let label = labels[name];
    let value = root[name];

    if (name === 'level') {
      value = formatVideoLevel(value, root.codec);
    }

    // If a mapping is specified, store the formatted value.
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    if (mapping[name]) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      value = mapping[name](value);
    }

    if (!value) {
      continue;
    }

    // If units are specified, append them to the value.
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    if (units[name]) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      value += units[name];
    }

    if (!label) {
      label = formatPropertyLabel(name);
    }

    sanitized.properties.push({
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'any' is not assignable to type 'never'.
      name: label,
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'any' is not assignable to type 'never'.
      value: value,
    });
  }

  // Copy the exempt property over to the new object.
  if (exempt && root) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    sanitized[exempt] = root[exempt];
  }

  return sanitized;
};

function mapStreams(streams: any) {
  const filtered = _.reject(streams, function (s) {
    // Filter out "None" subtitles fake stream
    return s.id === NONE;
  });
  const sorted = _.sortBy(filtered, 'streamType');

  return _.map(sorted, function (stream) {
    return sanitizeProperties(stream, 'streamType');
  });
}

function mapMedia(model: any) {
  return _.map(model.get('Media'), function (mediaItem) {
    return _.extend(sanitizeProperties(mediaItem, 'Part'), {
      Part: _.map(mediaItem.Part, function (uPart) {
        return _.extend(sanitizeProperties(uPart, 'Stream'), {
          file: uPart.file,
          exists: uPart.exists !== false,
          accessible: uPart.accessible !== false,
          Stream: mapStreams(uPart.Stream),
        });
      }),
    });
  });
}

const MediaInfoModal = BaseModal.extend({
  //
  // Properties

  name: 'mediaInfoModal',
  className: 'media-info-modal modal modal-lg fade',
  template: tpl,

  ui: {
    scroller: '.modal-body-scroll',
    files: '.files > ul',
    stickyProps: '.media > .props, .part > .props',
  },

  events: _.extend(
    {
      'click .delete-btn': 'onDeleteClick',
    },
    BaseModal.prototype.events
  ),

  modelEvents: {
    'change:Media': 'onMediaChange',
  },

  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  initialize: function (options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);

    this.bindAllWhenRendered('onMediaChange', 'onScroll');

    this.loading = false;
    this.media = mapMedia(this.model);

    // We don't have full metadata in track list, so we need to populate the
    // model in order to show streams

    this.loading = true;

    this.model
      .fetch({
        usePopulatedParams: true,
        data: { checkFiles: 1, asyncCheckFiles: 0 },
      })
      .always(
        this.whenRendered(function () {
          // Update the view even if the Media hasn't changed so we can remove
          // the Loading... message. If Media has changed, `onMediaChange`
          // will handle the update.

          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          if (!this.model.changed.hasOwnProperty('Media')) {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.updateMedia();
          }
        })
      );
  },

  //
  // Control

  serializeData: function () {
    let key = this.model.get('key');
    const appModel = adapters.resolve('appModel');
    const user = appModel.get('user');
    const server = this.model.server;

    if (this.model.get('isLibraryItem')) {
      key = path.join(key, {
        checkFiles: 1,
        includeAllConcerts: 1,
        includeBandwidths: 1,
        includeChapters: 1,
        includeChildren: 1,
        includeConcerts: 1,
        includeExtras: 1,
        includeFields: 1,
        includeGeolocation: 1,
        includeLoudnessRamps: 1,
        includeMarkers: 1,
        includeOnDeck: 1,
        includePopularLeaves: 1,
        includePreferences: 1,
        includeRelated: 1,
        includeRelatedCount: 1,
        includeReviews: 1,
        includeStations: 1,
      });
    }

    return {
      key: key,
      url: this.model.server.resolveUrl(key, { appendToken: true }),
      media: this.media,
      hasFiles: this.hasFiles(),
      hasStreams: this.hasStreams() || this.loading,
      showDeleteFile:
        server.get('isFullOwnedServer') &&
        server.get('allowMediaDeletion') &&
        server.supports('specificMediaDeletion'),
      showFilepath: !server.get('shared') || user.isNinja(),
      loading: this.loading,
    };
  },

  updateMedia: function () {
    this.loading = false;
    this.rerender = true;

    this.ui.scroller.off('scroll', this.onScroll);

    this.media = mapMedia(this.model);
    this.render();
  },

  hasFiles: function () {
    return !!_.chain(this.media)
      .pluck('Part')
      .flatten()
      .pluck('file')
      .compact()
      .size()
      .value();
  },

  hasStreams: function () {
    return !!_.chain(this.media)
      .pluck('Part')
      .flatten()
      .pluck('Stream')
      .flatten()
      .size()
      .value();
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);

    // Scroll event doesn't bubble up so we can't declare it in event delegate hash
    this.ui.scroller.on('scroll', this.onScroll);

    this.$el.tooltip({
      selector: '.files .label',
      placement: 'bottom',
    });
  },

  onClose: function () {
    this.ui.scroller.off('scroll', this.onScroll);
  },

  onMediaChange: function () {
    if (this.isHidden) {
      return;
    }

    if (!this.model.get('Media').length) {
      this.hide();
      return;
    }

    this.updateMedia();
  },

  onDeleteClick: function (event: any) {
    event.preventDefault();

    commands.execute('deleteMediaItem', {
      item: this.model,
      index: $(event.currentTarget).data('media-item'),
    });
  },

  onScroll: function () {
    const scrollTop = this.ui.scroller.scrollTop();
    let mediaSectionOffset = this.ui.scroller.offset().top;

    if (this.ui.files.length) {
      mediaSectionOffset += parseInt(this.ui.files.css('margin-bottom'), 10);
    }

    // Firefox restores scrolling position and produces first `scroll` event
    // before modal finishes its show animation.
    if (mediaSectionOffset < 0) {
      window.setTimeout(this.onScroll, 500);
      return;
    }

    this.ui.stickyProps.each(function () {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const $this = $(this);

      // top value corresponding to "bottom = 0"
      const maxTop = $this.parent().height() - $this.height();
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const unadjustedTop = scrollTop - this.parentNode.offsetTop;
      const top = Math.min(Math.max(unadjustedTop, 0), maxTop);

      if (top && top === unadjustedTop) {
        // Somewhere between top and bottom position within container
        $this.addClass('sticky');
        $this.css('top', mediaSectionOffset);
      } else {
        $this.removeClass('sticky');
        $this.css('top', top);
      }
    });
  },
});

export default MediaInfoModal;
