import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { dismissUnclaimedServer } from 'app/js/stores/actions/appActions';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import selectIsAnonymousUser from 'app/js/stores/selectors/user/selectIsAnonymousUser';
import ServerUnclaimedPage from 'app/js/views/v3/components/pages/ServerUnclaimedPage';

export function createMapStateToProps() {
  return createSelector(
    createUnstableServerSelector(),
    selectIsAnonymousUser,
    (server, isAnonymousUser) => {
      return {
        machineIdentifier: server.machineIdentifier,
        friendlyName: server.friendlyName,
        isPlexAccountRequired: isAnonymousUser,
      };
    }
  );
}

const mapDispatchToProps = {
  dispatchDismissUnclaimedServer: dismissUnclaimedServer,
};

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(ServerUnclaimedPage);
