import BaseCollection from 'app/js/models/BaseCollection';
import BackgroundProcessingListModel from 'app/js/models/library/convert/BackgroundProcessingListModel';
import proxyTypes from 'app/js/utils/constants/proxyTypes';

const BackgroundProcessingListCollection = BaseCollection.extend({
  //
  // Properties

  model: BackgroundProcessingListModel,
  url: '/playlists?type=' + proxyTypes.LOCAL,

  //
  // Control

  parse: function (response: any) {
    return this.convert(response, 'Playlist');
  },
});

export default BackgroundProcessingListCollection;
