/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { useSelector } from 'react-redux';
import {
  IconBack,
  IconHome,
  IconMaximize,
  IconMinimize,
  IconPlexLogo,
} from '@plex/react-chroma-icon';
import { Row } from '@plex/react-chroma-layout';
import { Align } from '@plex/react-chroma-types';
import selectUserLanguage from 'app/js/stores/selectors/user/selectUserLanguage';
import t from 'app/js/utils/t';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import NavBarSignInButton from 'app/js/views/v3/components/navbar/NavBarSignInButton';
import NavBarSignUpButton from 'app/js/views/v3/components/navbar/NavBarSignUpButton';
import createKey from 'app/js/views/v3/helpers/createKey';
import NavBarAccountButton from './NavBarAccountButton';
import NavBarActivityButtonConnector from './NavBarActivityButtonConnector';
import NavBarIconButton from './NavBarIconButton';
import NavBarPlexPassButton from './NavBarPlexPassButton';
import styles from './NavBar.css';

type NavBarProps = {
  className?: string;
  serverEntityID: string;
  hasHistory: boolean;
  userThumb: string;
  isAnonymousUser: boolean;
  canGoPremium?: boolean;
  shouldBypassPlexPassUpsellModal: boolean;
  canEnterFullscreen?: boolean;
  canExitFullscreen?: boolean;
  canNavigateHistory?: boolean;
  shouldRender: boolean;
  onBackPress: (...args: any[]) => any;
  onEnterFullscreen: (...args: any[]) => any;
  onExitFullscreen: (...args: any[]) => any;
};

function NavBar(props: NavBarProps) {
  const {
    serverEntityID,
    hasHistory,
    userThumb,
    canEnterFullscreen,
    canExitFullscreen,
    isAnonymousUser,
    canGoPremium,
    shouldBypassPlexPassUpsellModal,
    canNavigateHistory,
    shouldRender,
    onBackPress,
    onEnterFullscreen,
    onExitFullscreen,
  } = props;

  const userLanguage = useSelector(selectUserLanguage);

  if (!shouldRender) {
    return null;
  }

  // Force re-render when language changes by changing the key. Language can be changed on the web client settings page where this NavBar is visible.
  const key = createKey('NavBar', userLanguage);

  return (
    <div key={key} className={styles.container}>
      <Row
        align={Align.Left}
        grow={true}
        height="100%"
        verticalAlign={Align.Center}
      >
        <NavBarIconButton
          className={styles.firstButton}
          iconComponent={IconHome}
          title={t('Home')}
          // @ts-expect-error
          to="#"
        />

        <Link className={styles.plexLogo} to="#">
          <IconPlexLogo />
        </Link>

        {canNavigateHistory ? (
          <NavBarIconButton
            iconComponent={IconBack}
            isDisabled={!hasHistory}
            title={t('Back')}
            onPress={onBackPress}
          />
        ) : null}
      </Row>
      <div className={styles.right}>
        {canGoPremium ? (
          <NavBarPlexPassButton
            shouldBypassModal={shouldBypassPlexPassUpsellModal}
          />
        ) : null}

        {isAnonymousUser ? (
          <>
            <NavBarSignInButton />
            <NavBarSignUpButton />
          </>
        ) : null}

        {canGoPremium || isAnonymousUser ? (
          <div className={styles.buttonSeparator} />
        ) : null}

        {canExitFullscreen ? (
          <NavBarIconButton
            iconComponent={IconMinimize}
            title={t('Exit Full Screen')}
            onPress={onExitFullscreen}
          />
        ) : null}

        {canEnterFullscreen ? (
          <NavBarIconButton
            iconComponent={IconMaximize}
            title={t('Enter Full Screen')}
            onPress={onEnterFullscreen}
          />
        ) : null}

        {canEnterFullscreen || canExitFullscreen ? (
          <div className={styles.buttonSeparator} />
        ) : null}

        <NavBarActivityButtonConnector />

        <NavBarAccountButton
          className={styles.accountButton}
          serverEntityID={serverEntityID}
          // @ts-expect-error ts-migrate(2322) FIXME: Type 'string | null | undefined' is not assignable... Remove this comment to see the full error message
          userThumb={isAnonymousUser ? null : userThumb}
        />
      </div>
    </div>
  );
}

export default NavBar;
