/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import createAvatarUrl from 'app/js/v4/utils/createAvatarUrl';
import TranscodeImageConnector from 'app/js/views/v3/components/core/images/TranscodeImageConnector';
import styles from './AvatarImg.css';

type AvatarImgProps = {
  height: number;
  width: number;
  serverEntityID: string;
  url?: string;
  className?: string;
};

function AvatarImg(props: AvatarImgProps) {
  const { className, url, width: size, ...otherProps } = props;

  return (
    <div className={className}>
      <TranscodeImageConnector
        {...otherProps}
        url={url && createAvatarUrl(url, size)}
        width={size}
      />
    </div>
  );
}

AvatarImg.defaultProps = {
  className: styles.avatar,
};

export default AvatarImg;
