import { createSelector, OutputParametricSelector } from 'reselect';
import selectAvailableLibraryUpgrades, {
  LibraryUpgradeTuple,
} from 'app/js/stores/selectors/ui/selectAvailableLibraryUpgrades';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

export default function createHasAvailableLibraryUpgradeSelector(): OutputParametricSelector<
  LegacyRootState,
  {
    serverEntityID: string;
  },
  boolean,
  (res1: string, res2: LibraryUpgradeTuple[]) => boolean
> {
  return createSelector(
    (
      _appState: LegacyRootState,
      { serverEntityID }: { serverEntityID: string }
    ) => serverEntityID,
    selectAvailableLibraryUpgrades,
    (targetServerEntityID, availableUpgrades) => {
      return availableUpgrades.some(
        ([, serverEntityID]) => targetServerEntityID === serverEntityID
      );
    }
  );
}
