import { createSlice } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  PlayHistoryFetchFailedAction,
  PlayHistoryFetchRequestedAction,
  PlayHistoryFetchSucceededAction,
  PlayHistoryState,
} from './dashboardPlayHistoryTypes';

const initialState: PlayHistoryState = {
  statistics: [],
  accounts: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const dashboardPlayHistorySlice = createSlice({
  name: 'dashboardPlayHistory',
  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    playHistoryFetchRequested(state, _action: PlayHistoryFetchRequestedAction) {
      markPending(state);
    },

    playHistoryFetchSucceeded(
      state,
      { payload }: PlayHistoryFetchSucceededAction
    ) {
      markPopulated(state);
      state.statistics = payload.data.statistics;
      state.accounts = payload.data.accounts;
    },

    playHistoryFetchFailed(state, { payload }: PlayHistoryFetchFailedAction) {
      markFailed(state, payload.error);
    },
  },
});

export default dashboardPlayHistorySlice;
