/* eslint-plugin-disable @typescript-eslint */
import toHomeHubIdentifier from 'app/js/stores/actions/helpers/toHomeHubIdentifier';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';

export default function migrateCustomPromotedToHubs(
  userExperienceSettings: any
) {
  const homeSettings = userExperienceSettings.homeSettings;

  if (homeSettings && !userExperienceSettings.autoHomeHubsEnabled) {
    homeSettings.hubs = homeSettings.hubs?.reduce((memo: any[], hub: any) => {
      const homeHubIdentifier = toHomeHubIdentifier(hub.hubIdentifier);

      if (
        hub.directoryKey === '/hubs/promoted' &&
        hub.provider.identifier === providerIdentifiers.LIBRARY
      ) {
        if (
          !memo.find(
            (hub_: any) =>
              hub_.hubIdentifier === homeHubIdentifier &&
              hub_.provider?.identifier === hub.provider?.identifier &&
              hub_.server?.machineIdentifier === hub.server?.machineIdentifier
          )
        ) {
          hub.directoryKey = '/hubs';
          hub.hubIdentifier = homeHubIdentifier;
          memo.push(hub);
        }
      } else {
        memo.push(hub);
      }

      return memo;
    }, []);
  }

  return userExperienceSettings;
}
