/* eslint-plugin-disable @typescript-eslint */

import ProviderIntegrationCollection from 'app/js/models/provider/ProviderIntegrationCollection';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createFetchHandler from 'app/js/stores/actions/helpers/createFetchHandler';
import createLocastAccountHelper from 'app/js/stores/actions/provider/createLocastAccount';
import {
  findProviderModel,
  findServerModel,
} from 'app/js/utils/state/modelFinders';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';

//
// Action Types

export const INIT_PROVIDER_INTEGRATIONS =
  'provider/providerIntegrations/initProviderIntegrations';
export const FETCH_PROVIDER_INTEGRATIONS =
  'provider/providerIntegrations/fetchProviderIntegrations';
export const POLL_PROVIDER_INTEGRATIONS =
  'provider/providerIntegrations/pollProviderIntegrations';
export const INTEGRATIONS_ONBOARDING_COMPLETE =
  'provider/providerIntegrations/integrationsOnboardingComplete';
export const CREATE_LOCAST_ACCOUNT =
  'provider/providerIntegrations/createLocastAccount';

//
// Action Creators

export const initProviderIntegrations = createActionCreator(
  INIT_PROVIDER_INTEGRATIONS
);
export const fetchProviderIntegrations = createActionCreator(
  FETCH_PROVIDER_INTEGRATIONS
);
export const pollProviderIntegrations = createActionCreator(
  POLL_PROVIDER_INTEGRATIONS
);
export const integrationsOnboardingComplete = createActionCreator(
  INTEGRATIONS_ONBOARDING_COMPLETE
);
export const createLocastAccount = createActionCreator(CREATE_LOCAST_ACCOUNT);

//
// Helpers

function findProviderIntegrationsModel(appModel: any) {
  return appModel.get('ui').get('providerIntegrations');
}

//
// Action Handlers

export const providerIntegrationsActionHandlers = {
  [INIT_PROVIDER_INTEGRATIONS](appModel: any, payload: any, dispatch: any) {
    findProviderIntegrationsModel(appModel).set({
      isIntegrationsOnboarding: false,
      isIntegrationsPending: false,
      isIntegrationsPopulated: false,
      integrationsError: null,
      integrations: new ProviderIntegrationCollection([], {
        server: findServerModel(appModel, payload),
        provider: findProviderModel(appModel, payload),
      }),
    });

    dispatch(fetchProviderIntegrations());
  },

  [FETCH_PROVIDER_INTEGRATIONS]: createFetchHandler({
    key: 'integrations',
    getModel: findProviderIntegrationsModel,
  }),

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [POLL_PROVIDER_INTEGRATIONS](appModel: any, payload: any, dispatch: any) {
    findProviderIntegrationsModel(appModel).set(
      'isIntegrationsOnboarding',
      true
    );
    window.setTimeout(() => {
      scheduleImmediateUpdate(() => {
        dispatch(fetchProviderIntegrations());
      });
    }, 3000);
  },

  [INTEGRATIONS_ONBOARDING_COMPLETE](appModel: any) {
    findProviderIntegrationsModel(appModel).set(
      'isIntegrationsOnboarding',
      false
    );
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [CREATE_LOCAST_ACCOUNT](appModel: any, payload: any, dispatch: any) {
    findProviderIntegrationsModel(appModel).set(
      'isIntegrationsOnboarding',
      true
    );

    createLocastAccountHelper().then(() => {
      dispatch(pollProviderIntegrations());
    });
  },
};
