/* eslint-plugin-disable @typescript-eslint */

function getMediaIndex(item: any, id: any) {
  // Live TV items don't have IDs on media, if id is undefined
  // return the first media item.

  if (!id) {
    return 0;
  }

  const medias = item.get('Media');

  return medias.findIndex((media: any) => media.id === id);
}

export default getMediaIndex;
