import { SagaIterator } from 'redux-saga';
import { all, call, fork, put, select, takeEvery } from 'redux-saga/effects';
import createServerNowPlayingSessionsSelector from 'app/js/v4/core/serverNowPlayingSessions/selectors/createServerNowPlayingSessionsSelector';
import {
  ServerNowPlayingSessionsChangedAction,
  ServerNowPlayingSessionsFetchRequestedAction,
  ServerNowPlayingSessionsPerServerState,
  ServerNowPlayingSessionsStopRequestedAction,
} from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import normalizeServerNowPlayingSessionsResponse from 'app/js/v4/core/serverNowPlayingSessions/utils/normalizeServerNowPlayingSessionsResponse';
import api from 'app/js/v4/utils/data/api';
import takeLeadingPerID from 'app/js/v4/utils/data/saga/takeLeadingPerID';
import serverNowPlayingSessionsSlice from './serverNowPlayingSessionsSlice';

//
// Workers

export function* fetchSessions({
  payload,
}: ServerNowPlayingSessionsFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const response = yield call(api.get, '/status/sessions', {
      server,
    });

    yield put(
      serverNowPlayingSessionsSlice.actions.fetchSucceeded({
        server,
        data: normalizeServerNowPlayingSessionsResponse(response),
      })
    );
  } catch (error) {
    yield put(
      serverNowPlayingSessionsSlice.actions.fetchFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* maybeFetchForChangedSession({
  payload,
}: ServerNowPlayingSessionsChangedAction): SagaIterator {
  const { server, message } = payload;

  try {
    const serverNowPlayingSessions: ServerNowPlayingSessionsPerServerState =
      yield select(
        createServerNowPlayingSessionsSelector(server.machineIdentifier)
      );

    const existingSession =
      serverNowPlayingSessions.sessionsByKey[message.sessionKey];

    if (existingSession && existingSession.key === message.key) {
      // An existing item is changing, so let the slice handle the update.
    } else {
      // This is a new session or a new item is playing, so fetch sessions again.
      yield put(
        serverNowPlayingSessionsSlice.actions.fetchRequested({ server })
      );
    }
  } catch (error) {
    yield put(
      serverNowPlayingSessionsSlice.actions.stopFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* stopSession({
  payload,
}: ServerNowPlayingSessionsStopRequestedAction): SagaIterator {
  const { server, session, reason } = payload;

  try {
    yield call(api.get, '/status/sessions/terminate', {
      server,
      params: {
        sessionId: session.Session.id,
        reason,
      },
    });
  } catch (error) {
    yield put(
      serverNowPlayingSessionsSlice.actions.stopFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Watchers

function* watchFetchRequested(): SagaIterator {
  yield takeLeadingPerID(
    serverNowPlayingSessionsSlice.actions.fetchRequested,
    fetchSessions,
    ({ payload }) => payload.server.machineIdentifier
  );
}

function* watchSessionChanged(): SagaIterator {
  yield takeLeadingPerID(
    serverNowPlayingSessionsSlice.actions.sessionChanged,
    maybeFetchForChangedSession,
    ({ payload }) => payload.server.machineIdentifier
  );
}

function* watchStopRequested(): SagaIterator {
  yield takeEvery(
    serverNowPlayingSessionsSlice.actions.stopRequested,
    stopSession
  );
}

export default function* serverNowPlayingSessionsSaga(): SagaIterator {
  yield all([
    fork(watchFetchRequested),
    fork(watchSessionChanged),
    fork(watchStopRequested),
  ]);
}
