import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import storageAdapter from 'app/js/adapters/storageAdapter';
import commands from 'app/js/commands/commands';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import AbstractModel from 'app/js/models/AbstractModel';
import addFlexModel from 'app/js/models/addFlexModel';
import attrTypes from 'app/js/models/attrTypes';
import config from 'app/js/models/config';
import controlledPopulateMixin from 'app/js/models/mixins/controlledPopulateMixin';
import ProviderCollection from 'app/js/models/provider/ProviderCollection';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import AdConsentModel from 'app/js/models/user/AdConsentModel';
import GeoIPModel from 'app/js/models/user/GeoIPModel';
import UserFeatureCollection from 'app/js/models/user/UserFeatureCollection';
import * as subscriptionStates from 'app/js/utils/constants/user/subscriptionStates';
import * as subscriptionTypes from 'app/js/utils/constants/user/subscriptionTypes';
import getUserRequestParams from 'app/js/utils/getUserRequestParams';
