/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';

function minDuration(promise: any, delay: any) {
  const deferred = $.Deferred();
  const end = new Date().getTime() + delay;

  let finalize: any;

  promise.then(
    (...args: any[]) => {
      finalize = () => deferred.resolve(...args);
    },
    (...args: any[]) => {
      finalize = () => deferred.reject(...args);
    }
  );

  function startTimeout() {
    const now = new Date().getTime();
    const remaining = Math.max(end - now, 0);
    window.setTimeout(finalize, remaining);
  }

  promise.then(startTimeout, startTimeout);

  return deferred.promise();
}

export default minDuration;
