import isAbsoluteUrl from 'app/js/utils/url/isAbsoluteUrl';
import path from 'app/js/utils/url/path';

function sanitizeUrl(url: string): string | undefined {
  if (__DEV__) {
    return url;
  }

  return /^https:\/\/(?:(\w|-)+\.)*plex\.tv(?:\/|$)/i.test(url)
    ? url
    : undefined;
}

function runtimeUrlParam(param: string): string | undefined {
  const url = new URLSearchParams(window.location.search).get(param);

  return url ? sanitizeUrl(url) : undefined;
}

export function getMyPlexUrl(urlPath?: string): string {
  const root = runtimeUrlParam('myPlexUrl') || 'https://plex.tv';

  return urlPath ? path.join([root, urlPath]) : root;
}

export function getWWWPlexUrl(urlPath?: string): string {
  const root = runtimeUrlParam('wwwPlexUrl') || 'https://www.plex.tv';

  return urlPath ? path.join([root, urlPath]) : root;
}

export function getAuthAppUrl(absolute?: boolean): string {
  const root = runtimeUrlParam('authAppUrl') || __AUTH_APP_URL__;

  const params = {
    myPlexUrl: runtimeUrlParam('myPlexUrl'),
    wwwPlexUrl: runtimeUrlParam('wwwPlexUrl'),
  };

  let url = path.join(root, params, { trailingSlash: true });

  if (absolute && !isAbsoluteUrl(url)) {
    url = path.join([window.location.origin, url]);
  }

  return url;
}

export function getAuthFormUrl(): string {
  const root = runtimeUrlParam('authFormUrl') || __AUTH_FORM_URL__;

  const params = {
    myPlexUrl: runtimeUrlParam('myPlexUrl'),
    wwwPlexUrl: runtimeUrlParam('wwwPlexUrl'),
  };

  return path.join(root, params, { trailingSlash: true });
}
