/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import paginatedListModelMixin from 'app/js/models/mixins/paginatedListModelMixin';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import path from 'app/js/utils/url/path';
import PlaylistCollection from './PlaylistCollection';

const PlaylistListModel = AbstractModel.extend({
  mixins: [paginatedListModelMixin],
  //
  // Properties
  name: 'PlaylistListModel',
  context: ['server', 'section'],
  idAttribute: 'key',
  collectionName: 'playlists',
  responseType() {
    return getSupportedResponseType(this.server);
  },
  url() {
    return this.getPaginatedUrl(_.result(this, 'containerKey'));
  },
  containerKey() {
    const currentType = this.section.get('currentType');
    const currentSort = this.section.get('currentSort');
    const params = {
      // The explicit type=15 is to get a list of all playlists without folders.
      type: 15,
    };
    if (currentSort) {
      (params as any).sort =
        currentSort.id + ':' + (currentSort.get('direction') || 'asc');
    } else {
      (params as any).sort = 'titleSort:asc';
    }
    if (currentType) {
      (params as any).playlistType = currentType;
    }
    if (this.section.get('onlyDumb')) {
      (params as any).smart = 0;
    }
    return path.join('/playlists/all', params);
  },
  sessionAttrs: {
    isPlaylistList: attrTypes.boolean,
    currentPageStart: attrTypes.integer,
    currentPageSize: attrTypes.integer,
    populatedRange: attrTypes.object,
  },
  syncedAttrs: {
    totalSize: attrTypes.integer,
  },
  syncedRelations: {
    playlists: {
      relationClass: PlaylistCollection,
      rootPropertyName: 'Playlist',
      // @ts-expect-error ts-migrate(6133) FIXME: 'data' is declared but its value is never read.
      options(data: any) {
        return {
          containerKey: _.result(this, 'containerKey'),
        };
      },
      setOptions(parseOptions: any) {
        // `fetchRange` will add these options, which we'll need to provide when
        // setting models for the collection.
        return {
          remove: parseOptions.remove,
          at: parseOptions.at,
        };
      },
    },
  },
  defaults() {
    return {
      isPlaylistList: true,
      populatedRange: {
        start: 0,
        end: 0,
        size: 0,
      },
    };
  },
  //
  // Init
  initialize() {
    this.startPopulatedRangeMonitor();
  },
  //
  // Control
  augmentResponse(data: any) {
    // Wrapper is named differently depending on JSON or XML response type.
    // See: https://github.com/plexinc/plex-media-server/issues/7319
    if (data.Metadata) {
      data.Playlist = data.Metadata;
      delete data.Metadata;
    }
    return data;
  },
});
export default PlaylistListModel;
