/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import dispatcher from 'app/js/utils/dispatcher';
import assert from 'app/js/utils/log/assert';

const BaseAppModel = AbstractModel.extend({
  //
  // Properties
  name: 'BaseAppModel',
  isStateTreeRoot: true,
  //
  // Init
  constructor: function () {
    // Entities can be created before the model is initialized as relations are
    // instantiated. Store pending entities and add them to the entities
    // collection after the model is initialized.
    this._pendingEntities = [];
    this.listenTo(dispatcher, 'change:entity', this.onEntityChange);
    // eslint-disable-next-line prefer-rest-params
    AbstractModel.prototype.constructor.apply(this, arguments);
  },
  initialize() {
    this._pendingEntities.forEach(this._addEntity.bind(this));
  },
  //
  // Control
  _addEntity({ type, id, model }: any) {
    const entities = this.get('entities');
    if (__DEV__ && !entities) {
      assert(
        false,
        "Use `createEntitiesModelClass` to create an `entities` session relation in a platform's app model"
      );
    }
    const entitiesCollection = entities.get(type);
    if (__DEV__ && !entitiesCollection) {
      assert(
        false,
        `\`createEntitiesModelClass\` for ${this.name} is missing \`${type}\` relation`
      );
    }
    entitiesCollection.add({ id, model }, { merge: true });
  },
  //
  // Listeners
  onEntityChange(entity: any) {
    if (this.has('entities')) {
      this._addEntity(entity);
    } else {
      this._pendingEntities.push(entity);
    }
  },
});
if (__DEV__) {
  (window as any)._entity = (entityID: any) =>
    (window as any)._appState.entities[entityID.match(/^\w+/)[0]][entityID];
  BaseAppModel.prototype.toState = function () {
    // eslint-disable-next-line prefer-rest-params
    const state = AbstractModel.prototype.toState.apply(this, arguments);
    (window as any)._appState = state;
    (window as any)._appModel = this;
    return state;
  };
}
export default BaseAppModel;
