/* eslint-plugin-disable @typescript-eslint */

export default function createEntityID(type: any, id: any, ...scope: any[]) {
  if (!id) {
    return;
  }

  const parts = [];

  parts.push(type);

  for (let i = 0, len = scope.length; i < len; i++) {
    const scopeID = scope[i];

    if (scopeID) {
      parts.push(scopeID);
    }
  }

  parts.push(id);

  return parts.join('--');
}
