/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as pivotTypes from 'app/js/utils/constants/pivotTypes';

const PivotModel = AbstractModel.extend({
  //
  // Properties

  name: 'PivotModel',
  idAttribute: 'key',

  syncedAttrs: {
    key: attrTypes.string.isRequired,
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 3.
    type: attrTypes.oneOf(pivotTypes.HUB, pivotTypes.LIST, pivotTypes.VIEW)
      .isRequired,
    id: attrTypes.string,
    title: attrTypes.string.isRequired,
    context: attrTypes.string.isRequired,
    requires: attrTypes.string,

    // Used by synthetic pivots to fetch the Types, Filters, and Sorts in place of
    // the Meta element
    libraryKey: attrTypes.string,
  },
});

export default PivotModel;
