import { createSelector } from 'reselect';
import createShallowEqualSelector from 'app/js/stores/selectors/createShallowEqualSelector';

export function createStableServer(server: any) {
  if (!server) {
    return null;
  }

  return {
    entityID: server.entityID,
    machineIdentifier: server.machineIdentifier,
    version: server.version,
    friendlyName: server.friendlyName,
    sourceTitle: server.sourceTitle,
    allowSync: server.allowSync,
    allowMediaDeletion: server.allowMediaDeletion,
    backgroundProcessing: server.backgroundProcessing,
    transcoderVideo: server.transcoderVideo,
    isConnected: server.isConnected,
    isUnavailable: server.isUnavailable,
    isFullServer: server.isFullServer,
    isFullOwnedServer: server.isFullOwnedServer,
    isOutOfDate: server.isOutOfDate,
    isCloud: server.isCloud,
    isShared: server.isShared,
    livetv: server.livetv,
    ownerId: server.ownerId,
    ownerFeatures: server.ownerFeatures,
  };
}

// This selector is used frequently in metadata list items. Rather than create a
// unique selector for each component in order for reselect to memoize
// correctly, add a layer of memoization on top of reselect using the entity id
// since there is a small set of stable servers. This allows all metadata list
// items to reuse the same selector.

const memo = {};

function createStableServerSelector() {
  return createSelector(
    (appState: any, serverEntityID: any) =>
      appState.entities.servers[serverEntityID],
    createStableServer
  );
}

export default function selectStableServer(
  appState: any,
  { serverEntityID }: any
) {
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  let selector = memo[serverEntityID];

  if (!selector) {
    selector = createShallowEqualSelector(
      createStableServerSelector(),
      (server) => server
    );

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    memo[serverEntityID] = selector;
  }

  return selector(appState, serverEntityID);
}
