/* eslint array-bracket-spacing: 0 */

import _ from 'underscore';
import getResolution from 'app/js/utils/metadata/getResolution';
import abstractPlayerQualities from 'app/js/utils/player/abstractPlayerQualities';

const videoPlayerQualities = _.extend({}, abstractPlayerQualities, {
  //
  // Properties

  type: 'video',

  qualities: {
    bitrate: [
      64, 96, 208, 320, 720, 1500, 2000, 3000, 4000, 8000, 10000, 12000, 20000,
    ],
    videoResolution: [
      '220x128',
      '220x128',
      '284x160',
      '420x240',
      '576x320',
      '720x480',
      '1280x720',
      '1280x720',
      '1280x720',
      '1920x1080',
      '1920x1080',
      '1920x1080',
      '1920x1080',
    ],
    videoQuality: [10, 20, 30, 30, 40, 60, 60, 75, 100, 60, 75, 90, 100],
  },

  //
  // Control
  getAvailableQualitiesFor(media: any, transcodeServer: any, options: any) {
    if (!media || !transcodeServer) {
      return [];
    }

    if (!Array.isArray(media)) {
      media = [media];
    }

    if (media.length === 0) {
      return [];
    }

    const canTranscode = transcodeServer.get('transcoderVideo');
    const canRemuxOnly = transcodeServer.get('transcoderVideoRemuxOnly');
    const mediaResolution = this.getMaximumResolution(media);

    // If the video does not specify the resolution for an
    // individual video stream, we can't know when to transcode to a
    // different quality.

    if (!canTranscode || canRemuxOnly || !mediaResolution) {
      return [];
    }

    const maxUploadBitrate = transcodeServer.get('maxUploadBitrate');

    const qualityOptions = _.extend({}, options, {
      comparator: function (i: any) {
        if (maxUploadBitrate && this.qualities.bitrate[i] > maxUploadBitrate) {
          return true;
        }

        const dimensions = this.qualities.videoResolution[i].split('x');
        const qualityResolution = getResolution(dimensions[0], dimensions[1]);

        return qualityResolution > mediaResolution;
      },
    });

    return this.getAvailableQualities(qualityOptions);
  },

  getBestAvailableQuality: function (
    preferredQuality: any,
    availableQualities: any
  ) {
    if (availableQualities.length === 0) {
      return videoPlayerQualities.ORIGINAL;
    }

    if (
      availableQualities.find(
        (quality: any) => quality.index === preferredQuality.index
      )
    ) {
      return preferredQuality.index;
    }

    return _.last(availableQualities).index;
  },

  getNearestIndexToBitrate: function (targetBitrate: any) {
    return this.getNearestIndex(this.qualities.bitrate, targetBitrate);
  },

  getMinimumQuality: function (indexA: any, indexB: any) {
    // ORIGINAL quality index is -1 while others are >= 0. Project ORIGINAL
    // indices to MAX_VALUE to ensure a linear comparison.
    const projectedIndexA =
      indexA === videoPlayerQualities.ORIGINAL ? Number.MAX_VALUE : indexA;
    const projectedIndexB =
      indexB === videoPlayerQualities.ORIGINAL ? Number.MAX_VALUE : indexB;
    return projectedIndexA < projectedIndexB ? indexA : indexB;
  },

  getMaximumQuality: function (indexA: any, indexB: any) {
    return this.getMinimumQuality(indexA, indexB) === indexA ? indexB : indexA;
  },

  getMaximumResolution: function (mediaItems: any) {
    return mediaItems.reduce(function (maxResolution: any, mediaItem: any) {
      if (!mediaItem.width && !mediaItem.height) {
        return maxResolution;
      }

      const resolution = getResolution(mediaItem.width, mediaItem.height);
      return maxResolution === null || resolution > maxResolution
        ? resolution
        : maxResolution;
    }, null);
  },

  isExplicitQuality: function (index: any) {
    return (
      index !== videoPlayerQualities.ORIGINAL &&
      index !== videoPlayerQualities.CONVERT_AUTOMATICALLY
    );
  },
});

export default videoPlayerQualities;
