/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React, { Component } from 'react';
import t from 'app/js/utils/t';
import DisclosureArrowButton from 'app/js/views/v3/components/core/buttons/DisclosureArrowButton';
import AvatarImg from 'app/js/views/v3/components/core/images/AvatarImg';
import TooltipTrigger from 'app/js/views/v3/components/core/tooltips/TooltipTrigger';
import getUniqueElementID from 'app/js/views/v3/helpers/getUniqueElementID';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import NavBarAccountMenuConnector from './NavBarAccountMenuConnector';
import styles from './NavBarAccountButton.css';

interface NavBarAccountButtonProps {
  className?: string;
  tooltipClassName?: string;
  tooltipFocusedClassName?: string;
  serverEntityID: string;
  userThumb?: string;
}

type NavBarAccountButtonState = any;

class NavBarAccountButton extends Component<
  NavBarAccountButtonProps,
  NavBarAccountButtonState
> {
  static defaultProps = {
    className: styles.button,
    tooltipClassName: styles.tooltip,
    tooltipFocusedClassName: styles.tooltipTriggerHasFocus,
  };

  // eslint-disable-next-line react/sort-comp
  _buttonID: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isMenuOpen: false,
    };

    this._buttonID = getUniqueElementID();
  }

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onButtonPress = (event: any) => {
    this.setState({
      isMenuOpen: !this.state.isMenuOpen,
    });
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onMenuClose = (event: any) => {
    this.setState({
      isMenuOpen: false,
    });
  };

  render() {
    const {
      className,
      tooltipClassName,
      tooltipFocusedClassName,
      serverEntityID,
      userThumb,
    } = this.props;

    return (
      <>
        <TooltipTrigger
          className={className}
          // @ts-expect-error ts-migrate(2739) FIXME: Type 'typeof DisclosureArrowButton' is missing the... Remove this comment to see the full error message
          component={DisclosureArrowButton}
          direction={this.state.isMenuOpen ? directions.UP : directions.DOWN}
          id={this._buttonID}
          isSelected={false}
          tooltip={this.state.isMenuOpen ? undefined : t('Account')}
          tooltipClassName={tooltipClassName}
          tooltipFocusedClassName={tooltipFocusedClassName}
          onPress={this.onButtonPress}
        >
          <div className={styles.avatarContainer}>
            <div
              className={classNames(
                styles.overlay,
                this.state.isMenuOpen && styles.isMenuOpen
              )}
            />
            <AvatarImg
              height={30}
              serverEntityID={serverEntityID}
              url={userThumb}
              width={30}
            />
          </div>
        </TooltipTrigger>

        <NavBarAccountMenuConnector
          isOpen={this.state.isMenuOpen}
          target={this._buttonID}
          onMenuClose={this.onMenuClose}
        />
      </>
    );
  }
}

export default NavBarAccountButton;
