/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import InlinePrimaryServerPageConnector from 'app/js/views/v3/components/pages/InlinePrimaryServerPageConnector';
import ServerUnauthorizedErrorPage from 'app/js/views/v3/components/pages/ServerUnauthorizedErrorPage';
import ServerUnavailableErrorPageConnector from 'app/js/views/v3/components/pages/ServerUnavailableErrorPageConnector';
import ServerUnclaimedPageConnector from 'app/js/views/v3/components/pages/ServerUnclaimedPageConnector';
import ServerUpdateRequiredPageConnector from 'app/js/views/v3/components/pages/ServerUpdateRequiredPageConnector';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import InstallingServerUpdatePageConnector from 'app/js/views/v3/components/pages/empty/InstallingServerUpdatePageConnector';
import NotFoundErrorPage from 'app/js/views/v3/components/pages/empty/NotFoundErrorPage';

type ServerPageProps = {
  children: React.ReactNode;
  serverEntityID: string;
  hasServer: boolean;
  showSpinner: boolean;
  machineIdentifier?: string;
  isUnauthorized?: boolean;
  isUnavailable?: boolean;
  isOutOfDate?: boolean;
  isUnclaimed?: boolean;
  isInstallingServerUpdate?: boolean;
  hasHttpsConnections?: boolean;
  isHostedHttps?: boolean;
};

function ServerPage(props: ServerPageProps) {
  const {
    serverEntityID,
    children,
    hasServer,
    showSpinner,
    machineIdentifier,
    isUnauthorized,
    isUnavailable,
    isOutOfDate,
    isUnclaimed,
    isInstallingServerUpdate,
  } = props;

  if (showSpinner) {
    return (
      <EmptyPage>
        <Spinner />
      </EmptyPage>
    );
  }

  if (!hasServer) {
    return <NotFoundErrorPage />;
  }

  if (isInstallingServerUpdate) {
    return (
      <InstallingServerUpdatePageConnector serverEntityID={serverEntityID} />
    );
  }

  if (isUnavailable) {
    return (
      <ServerUnavailableErrorPageConnector serverEntityID={serverEntityID} />
    );
  }

  if (isOutOfDate) {
    return (
      <ServerUpdateRequiredPageConnector serverEntityID={serverEntityID} />
    );
  }

  if (isUnauthorized) {
    return <ServerUnauthorizedErrorPage />;
  }

  if (isUnclaimed) {
    return <ServerUnclaimedPageConnector serverEntityID={serverEntityID} />;
  }

  return (
    // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: ReactNode; machineIdentifier: st... Remove this comment to see the full error message
    <InlinePrimaryServerPageConnector machineIdentifier={machineIdentifier}>
      {children}
    </InlinePrimaryServerPageConnector>
  );
}

export default ServerPage;
