/* eslint-plugin-disable @typescript-eslint */

import ServerConnectionManager from './ServerConnectionManager';
import ServerRetryManager from './ServerRetryManager';

const DesktopServerConnectionManager = ServerConnectionManager.extend({
  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  initialize(options: any) {
    // eslint-disable-next-line prefer-rest-params
    ServerConnectionManager.prototype.initialize.apply(this, arguments);

    this._serverRetryManager = new ServerRetryManager({
      callback: this.retryServerConnections.bind(this),
      logFormat: '[Connections] Retrying connection tests for %s in %s seconds',
    });
  },

  //
  // Control

  stop() {
    // eslint-disable-next-line prefer-rest-params
    ServerConnectionManager.prototype.stop.apply(this, arguments);

    this._serverRetryManager.stopAll();
  },

  startFailedServerRetries(server: any) {
    this._serverRetryManager.start(server);
  },

  stopFailedServerRetries(server: any) {
    this._serverRetryManager.stop(server);
  },
});

export default DesktopServerConnectionManager;
