import _ from 'underscore';

function shortVersion(version: any) {
  const match = /(\d+\.){2,3}\d+/.exec(version);

  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'RegExpExecArray | null' is not a... Remove this comment to see the full error message
  return _.first(match) || version;
}

export default shortVersion;
