/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import SortModel from 'app/js/models/library/section/SortModel';
import { getMetadataType } from 'app/js/utils/types/metadataTypes';
import path from 'app/js/utils/url/path';
import MetadataListModel from './MetadataListModel';

const filterOperators = {
  unviewedLeafCount: '>=',
};

const SectionMetadataListModel = MetadataListModel.extend({
  //
  // Properties

  name: 'SectionMetadataListModel',

  context: [
    'server',
    'section',
    'identifier',
    'mediaTagPrefix',
    'mediaTagVersion',
    'allowSync',
  ],

  url() {
    let segments = [];
    let subsetID;
    let params;

    const folderKey = this.section.get('currentFolderKey');
    if (folderKey) {
      // If we are browsing within a folder, we don't need anything else aside
      // from the folder key.
      segments.push(folderKey);
    } else {
      segments.push(_.result(this.section, 'url'));

      subsetID = this.section.get('currentSubset').id;

      if (this.section.get('allowAdvancedFilters')) {
        // We can sort all items for recently added and recently released
        // instead of limiting ourselves to the actual subset.

        if (subsetID === 'recentlyAdded' || subsetID === 'newest') {
          subsetID = 'all';
        }

        segments.push(subsetID);
        params = this.getParams();
      } else {
        const filters = this.section.get('currentFilters');

        if (filters.length) {
          segments = [filters[0].values[0]];
        } else {
          segments.push(subsetID);
        }
      }
    }

    let url = path.join(segments, params);
    url = this.replaceSpecialFilterOperators(url);

    this.set('containerKey', url);

    return this.server.get('isFullServer') ? this.getPaginatedUrl(url) : url;
  },

  //
  // Control

  getParams() {
    const params = this.getFilterParams();
    const typeID = this.getMetadataTypeID();
    const sort = this.getSort();

    // Do not send a type param with photos since they are browsed by directory.
    if (typeID && typeID !== getMetadataType('photo').id) {
      params.type = typeID;
    }

    if (sort) {
      params.sort = sort;
    }

    // If the section is being scanned, we will want the `metadataState` and
    // `mediaState` attributes immediately.

    if (
      this.section.get('refreshing') &&
      this.server.supports('timelineState')
    ) {
      params.timelineState = 1;
    }

    return params;
  },

  getFilterParams() {
    const filters = this.section.get('currentFilters');
    const params = {};

    // Filters with more than one value should send a comma-delimited list of
    // values.

    _.each(filters, (filter) => {
      if (filter.values.length) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        params[filter.key] = filter.values.map(decodeURIComponent).join(',');
      }
    });

    return params;
  },

  getMetadataTypeID() {
    // Even if the user has show or artist selected for the type, we want to
    // enforce a specific media type when using some of the special subsets.

    const subsetID = this.section.get('currentSubset').id;
    let typeID =
      this.section.get('currentType').id ||
      getMetadataType(this.section.get('type')).id;

    if (/^(onDeck|recentlyAdded|recentlyViewed|newest)$/.test(subsetID)) {
      // Return the episode media type instead of the show type or season type.
      if (
        typeID === getMetadataType('show').id ||
        typeID === getMetadataType('season').id
      ) {
        typeID = getMetadataType('episode').id;
        // Return the album media type instead of the artist type.
      } else if (
        typeID === getMetadataType('artist').id ||
        typeID === getMetadataType('track').id
      ) {
        typeID = getMetadataType('album').id;
      }
    } else if (/^recentlyViewedShows$/.test(subsetID)) {
      typeID = getMetadataType('show').id;
    }

    return typeID;
  },

  getSort() {
    const sort = this.section.get('currentSort');
    const subset = this.section.get('currentSubset').id;

    if (subset === 'recentlyAdded') {
      return 'addedAt:desc';
    } else if (subset === 'newest') {
      return 'originallyAvailableAt:desc';
    } else if (sort) {
      const sortDirection =
        sort.get('direction') || sort.get('defaultDirection');
      return sortDirection === SortModel.directions.DESCENDING
        ? sort.get('descKey')
        : sort.get('key');
    }
  },

  replaceSpecialFilterOperators(url: any) {
    _.each(filterOperators, (operator, filter) => {
      url = url.replace(filter + '=', filter + operator);
    });

    return url;
  },
});

export default SectionMetadataListModel;
