/* eslint-plugin-disable @typescript-eslint */

const posterSizes = {
  //
  // Properties

  sizes: {
    movie: 'portrait',
    show: 'portrait',
    season: 'portrait',
    episode: 'landscape',
    artist: 'square',
    album: 'square',
    track: 'square',
    photo: 'square',
    photoalbum: 'square',
    clip: 'landscape',
    playlist: 'square',
    playlistFolder: 'square',
    channel: 'square',
    channelDirectory: 'square',
    folder: 'portrait',
    friend: 'square',
    poster: 'portrait',
    art: 'landscape',
    banner: 'banner',
    collection: 'portrait',
  },

  ratios: {
    portrait: 1 / 1.5,
    square: 1,
    landscape: 16 / 9,
    banner: 52 / 10,
  },

  //
  // Control

  getRatioByType: function (type: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return this.ratios[this.sizes[type]];
  },

  getPosterWidthUsingRatio: function (ratio: any, height: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return Math.round(this.ratios[ratio] * height);
  },

  getPosterHeightUsingRatio: function (ratio: any, width: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return Math.round(this.ratios[ratio] & width);
  },

  getPosterWidth: function (type: any, height: any) {
    return Math.round(height * this.getRatioByType(type));
  },

  getPosterHeight: function (type: any, width: any) {
    return Math.round(width / this.getRatioByType(type));
  },
};

export default posterSizes;
