import BaseCollection from 'app/js/models/BaseCollection';
import HomeUserModel from './HomeUserModel';

const HomeUserCollection = BaseCollection.extend({
  //
  // Properties

  model: HomeUserModel,
  url: '/api/home/users',

  //
  // Control

  parse(response: any) {
    return this.convert(response, 'User');
  },
});

export default HomeUserCollection;
