import React from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import createServiceAuthUrlSelector from 'app/js/stores/selectors/user/createServiceAuthUrlSelector';

function createMapStateToProps() {
  return createSelector(createServiceAuthUrlSelector(), (serviceAuthUrl) => {
    return {
      serviceAuthUrl,
    };
  });
}

type ServiceAuthUrlConnectorProps = {
  component: React.ReactElement;
  serviceAuthUrl?: string;
};

function ServiceAuthUrlConnector(props: ServiceAuthUrlConnectorProps) {
  const { component: Component, serviceAuthUrl, ...otherProps } = props;

  // @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Component' does not have any con... Remove this comment to see the full error message
  return <Component to={serviceAuthUrl} {...otherProps} />;
}

// Explicitly pass {} as 2nd `connect` arg to avoid injecting `dispatch` prop
export default connect(createMapStateToProps, {})(ServiceAuthUrlConnector);
