/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import storageAdapter from 'app/js/adapters/storageAdapter';
import parseJSON from 'app/js/utils/parser/parseJSON';

export function getJSON(key: any, defaultValue: any) {
  return storageAdapter.get(key).then(
    (storageString: any) => parseJSON(storageString, defaultValue),
    // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
    () => {
      if (defaultValue) {
        return $.Deferred().resolve(defaultValue).promise();
      }
    }
  );
}

export function setJSON(key: any, value: any) {
  return storageAdapter.set(key, JSON.stringify(value));
}
