/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { SIGN_OUT } from 'app/js/utils/constants/metrics/metricsPages';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import PageConnector from 'app/js/views/v3/components/pages/PageConnector';
import PageContent from 'app/js/views/v3/components/pages/PageContent';
import InnerEmptyPage from 'app/js/views/v3/components/pages/empty/InnerEmptyPage';

type SignOutPageProps = {
  isSignedIn: boolean;
  onSignOut: (...args: any[]) => any;
};

type SignOutPageState = any;

class SignOutPage extends Component<SignOutPageProps, SignOutPageState> {
  //
  // Static

  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    return nextProps.isSignedIn && !prevState.isSigningOut
      ? { isSigningOut: true }
      : null;
  }

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.state = { isSigningOut: false };
  }

  componentDidMount() {
    if (this.state.isSigningOut) {
      this.props.onSignOut();
    }
  }

  // @ts-expect-error ts-migrate(6133) FIXME: 'prevProps' is declared but its value is never rea... Remove this comment to see the full error message
  componentDidUpdate(prevProps: SignOutPageProps, prevState: SignOutPageState) {
    if (this.state.isSigningOut && !prevState.isSigningOut) {
      this.props.onSignOut();
    }
  }

  //
  // Render

  render() {
    // The `SignOutView` parents this component in a `FullPage`.
    return (
      <PageConnector metricsPage={SIGN_OUT}>
        <PageContent>
          <InnerEmptyPage>
            <Spinner />
          </InnerEmptyPage>
        </PageContent>
      </PageConnector>
    );
  }
}

export default SignOutPage;
