/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import controlledPopulateMixin from 'app/js/models/mixins/controlledPopulateMixin';
import log from 'app/js/utils/log/log';
import ServerModel from './ServerModel';

// @ts-expect-error ts-migrate(7006) FIXME: Parameter 'servers' implicitly has an 'any' type.
function listServerNames(servers) {
  if (!servers.length) {
    return '(none)';
  }

  const names = _.map(servers, (server) => server.get('debugName'));
  return names.join(', ');
}

const ServerCollection = AbstractCollection.extend({
  mixins: [controlledPopulateMixin],

  //
  // Properties

  name: 'ServerCollection',
  model: ServerModel,

  //
  // Init

  initialize() {
    this._resetPopulate();
  },

  //
  // Control

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'server' implicitly has an 'any' type.
  comparator(server) {
    // 1. Owned servers by friendlyName
    // 2. Shared servers by sourceTitle then friendlyName
    // 3. plex.tv Cloud Server

    const isShared = server.get('isShared');
    const friendlyName = server.get('friendlyName') || '';
    const sourceTitle = server.get('sourceTitle') || '';
    const sortName = isShared ? sourceTitle + friendlyName : friendlyName;

    // `selectServerProviders` and others rely on the collection server order.
    // We generally always want the cloud server listed last when it is used for
    // display purposes, so force a high number here for the comparator and
    // short circuit the name
    if (server.get('isCloud')) {
      return '9';
    }

    return (isShared ? '1' : '0') + sortName.toLowerCase();
  },

  shouldWaitForInitialServers() {
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'server' implicitly has an 'any' type.
    const pendingInitialServers = this.filter((server) => {
      // Wait for unidentified servers until we possibly resolve their identity.
      // Also wait for servers that provide additional server resources.

      const isInitialServer = !server.id || !!server.get('resources');
      return isInitialServer && server.isPopulating();
    });

    if (pendingInitialServers.length) {
      log(
        '[Servers] Waiting for initial servers =',
        listServerNames(pendingInitialServers)
      );
      return true;
    } else {
      return false;
    }
  },

  shouldWaitForResources() {
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'acc' implicitly has an 'any' type.
    const pendingResourceServers = this.reduce((acc, server) => {
      // Wait if an initial cloud server has resolved and its resources have not
      // been resolved yet.

      const isResolved = server.get('isResolved');
      const resources = server.get('resources');
      const hasPendingResources = resources && resources.isPopulating();

      if (isResolved && hasPendingResources) {
        acc.push(server);
      }

      return acc;
    }, []);

    if (pendingResourceServers.length) {
      log(
        '[Servers] Waiting for additional resources from',
        listServerNames(pendingResourceServers)
      );
      return true;
    } else {
      return false;
    }
  },

  shouldWaitForPendingServers() {
    // Only reject the servers promise after all servers are done being tested
    // and we know that there will be no connected servers. If a server is
    // already connected, we know we can resolve immediately.

    if (this.findWhere({ isConnected: true })) {
      return false;
    }

    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'server' implicitly has an 'any' type.
    const pendingServers = this.filter((server) => server.isPopulating());

    if (pendingServers.length) {
      log(
        '[Servers] Waiting for pending servers =',
        listServerNames(pendingServers)
      );
      return true;
    } else {
      return false;
    }
  },

  waitForServers() {
    this._resetPopulate();
  },

  foundAllServers() {
    // After any additional resources have been fetched, resolve the populate
    // promise if at least one server has connected.

    const connectedServers = this.where({ isConnected: true });
    const connectedList = listServerNames(connectedServers);

    log('[Servers] Found all servers =', connectedList);

    if (connectedServers.length) {
      this._resolvePopulate();
    } else {
      this._rejectPopulate();
    }
  },

  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  populateAnyServer(servers = this.models) {
    // If any connected server is required, but not a specific server, populate
    // using whichever server first becomes connected. Allow a set of servers to
    // be passed in to iterate through, but default to the entire collection.

    const deferred = $.Deferred();
    const relevantServers = servers.slice(0);

    // If we already have a connected server, resolve immediately.
    const server = _.find(relevantServers, (currentServer) => {
      // The cloud server (plex.tv) must use `isResolved` in order to ensure the
      // user is authenticated, but media servers can shortcut at `isConnected`,
      // which saves time if connections are being re-tested.

      return currentServer.get('isCloud')
        ? currentServer.get('isResolved')
        : currentServer.get('isConnected');
    });

    if (server) {
      log(
        '[Servers] Populate any server chose %s where servers = %s',
        server.get('debugName'),
        listServerNames(relevantServers)
      );
      return deferred.resolve(server).promise();
    }

    // If all servers are unauthenticated, reject immediately.
    if (this.hasOnlyRejectedServers(relevantServers)) {
      return deferred.reject().promise();
    }

    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'removedServer' implicitly has an 'any' ... Remove this comment to see the full error message
    function onServerRemove(removedServer) {
      // We are listening to events from the entire collection, but
      // `populateAnyServer` may have been called with a subset of servers.
      const removedIndex = relevantServers.indexOf(removedServer);

      if (removedIndex > -1) {
        // If a server is removed from the collection, we should no longer
        // consider it a relevant server.
        relevantServers.splice(removedIndex, 1);

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.hasOnlyRejectedServers(relevantServers)) {
          deferred.reject();
        }
      }
    }

    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'changedServer' implicitly has an 'any' ... Remove this comment to see the full error message
    function onServerResolvedChange(changedServer, isResolved) {
      // We are listening to events from the entire collection, but
      // `populateAnyServer` may have been called with a subset of servers.
      const isRelevantServer = _.contains(relevantServers, changedServer);

      if (isRelevantServer && isResolved) {
        deferred.resolve(changedServer);
      }
    }

    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'changedServer' implicitly has an 'any' ... Remove this comment to see the full error message
    function onServerRejectedChange(changedServer, isRejected) {
      // We are listening to events from the entire collection, but
      // `populateAnyServer` may have been called with a subset of servers.
      const isRelevantServer = _.contains(relevantServers, changedServer);

      if (
        isRelevantServer &&
        isRejected &&
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.hasOnlyRejectedServers(relevantServers)
      ) {
        deferred.reject();
      }
    }

    // Wait for a connectionTestResult change, which will notify if a server
    // authenticates or fails.
    this.on(
      {
        remove: onServerRemove,
        'change:isResolved': onServerResolvedChange,
        'change:isRejected': onServerRejectedChange,
      },
      this
    );

    deferred.always(() => {
      this.off('change:isResolved', onServerResolvedChange, this);
      this.off('change:isRejected', onServerRejectedChange, this);
    });

    return deferred.promise();
  },

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'options' implicitly has an 'any' type.
  populateServer(options) {
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'server' implicitly has an 'any' type.
    return this.findServer(options, true).then((server) => {
      // If a media server is connected, resolve immediately and deal with a
      // newly failed server later in the pipeline. Otherwise wait for the
      // server to populate. The cloud server (plex.tv) must be resolved since
      // `isConnected` does not mean the user is authenticated.

      if (server.get('isConnected') && !server.get('isCloud')) {
        return $.Deferred().resolve(server);
      }

      return server.populate();
    });
  },

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'options' implicitly has an 'any' type.
  findServer(options, requiresPopulate) {
    const isString = _.isString(options);
    // Allow `findServer(id)` to be mapped to the correct options.
    const findOptions = isString ? { machineIdentifier: options } : options;
    const deferred = $.Deferred();

    if (isString) {
      // Short circuit the async activity below when the `findServer` is
      // provided a scalar `options` argument matching the machineIdentifier of
      // a resolved server. Resolve the returned promise after a timeout so the
      // promise from this method always resolves async.
      const server = this.get(findOptions.machineIdentifier);

      if (server && server.get('isResolved')) {
        window.setTimeout(deferred.resolve.bind(deferred, server), 0);
        return deferred.promise();
      }
    }

    const findByID = _.has(findOptions, 'machineIdentifier');
    const changeEvents = _.map(_.keys(findOptions), (key) => 'change:' + key);
    const listenEvents = 'add ' + changeEvents.join(' ');

    // @ts-expect-error ts-migrate(7034) FIXME: Variable 'activeFilterPromise' implicitly has type... Remove this comment to see the full error message
    let activeFilterPromise;

    function onServersChange() {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const filterPromise = this.filterServers(findOptions, requiresPopulate);

      if (filterPromise) {
        // Only let this promise resolve or reject the returned promise if it
        // was the most recently created.
        activeFilterPromise = filterPromise;

        // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'server' implicitly has an 'any' type.
        filterPromise.done((server) => {
          // @ts-expect-error ts-migrate(7005) FIXME: Variable 'activeFilterPromise' implicitly has an '... Remove this comment to see the full error message
          if (activeFilterPromise === filterPromise) {
            deferred.resolve(server);
          }
        });

        filterPromise.fail(() => {
          // @ts-expect-error ts-migrate(7005) FIXME: Variable 'activeFilterPromise' implicitly has an '... Remove this comment to see the full error message
          if (activeFilterPromise === filterPromise) {
            // If not finding a specific identifier, we should not reject until
            // we know that all servers are known since another server that
            // matches the filter may be added.
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            if (findByID || !this.isPopulating()) {
              deferred.reject();
            } else {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.populate().always(onServersChange.bind(this));
            }
          }
        });
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      } else if (!this.isPopulating()) {
        deferred.reject();
      }
    }

    // Kick off a change manually once so we try an initial filter of servers.
    onServersChange.call(this);

    // Wait for a server to change or be added to the collection that matches
    // the criteria.
    this.on(listenEvents, onServersChange, this);

    this.populate().always(() => {
      // If all initial servers have been discovered and we still haven't
      // yielded a promise, we never saw the server we were looking for.
      // @ts-expect-error ts-migrate(7005) FIXME: Variable 'activeFilterPromise' implicitly has an '... Remove this comment to see the full error message
      if (!activeFilterPromise) {
        deferred.reject();
      }
    });

    deferred.always(() => {
      this.off(listenEvents, onServersChange, this);
    });

    return deferred.promise();
  },

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'options' implicitly has an 'any' type.
  filterServers(options, requiresPopulate) {
    const servers = this.where(options);

    if (servers.length) {
      if (requiresPopulate) {
        return this.populateAnyServer(servers);
      } else {
        return $.Deferred().resolve(servers[0]).promise();
      }
    }
  },

  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  hasOnlyRejectedServers(servers = this.models) {
    // Allow a set of servers to be passed in to iterate through, but default to
    // the entire collection.
    return _.every(servers, (server) => server.get('isRejected'));
  },

  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  hasLoopbackServer(servers = this.models) {
    return !!_.find(servers, (server) => {
      const activeConnection = server.get('activeConnection');

      return !!activeConnection && activeConnection.get('isLoopback');
    });
  },
});

export default ServerCollection;
