/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import ConnectionModel from 'app/js/models/server/ConnectionModel';
import BaseClass from 'app/js/utils/BaseClass';
import capabilities from 'app/js/utils/device/capabilities';
import dispatcher from 'app/js/utils/dispatcher';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import parser from 'app/js/utils/parser';
import path from 'app/js/utils/url/path';
import xhrTracker from 'app/js/utils/xhr/xhrTracker';

const SOCKET_PING_DELAY = 30000;
const POLL_TIMEOUT = 30000;

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const CloudServerEventConnection = BaseClass.extend({
  lastModified: null,
  eTag: null,
  mixins: eventMixin,

  //
  // Properties

  url: function () {
    const appModel = adapters.resolve('appModel');
    const user = appModel.get('user');
    const subscriptionPath =
      '/sub/' +
      this.requestType +
      '/' +
      user.id +
      '/' +
      appModel.get('clientID');

    const connection = new ConnectionModel({
      scheme: this.requestType === 'websockets' ? 'wss' : 'https',
      address: 'pubsub.plex.tv',
    });

    const url = this.server.resolveUrl(subscriptionPath, {
      connection: connection,
      appendToken: true,
    });

    return url;
  },

  //
  // Init

  initialize: function (options: any) {
    _.bindAll(
      this,
      'onSocketOpen',
      'onSocketClose',
      'onSocketError',
      'onSocketMessage',
      'onPingTimeout',
      'onPollSuccess',
      'onPollError'
    );

    this.server = options.server;
  },

  //
  // Control

  open: function () {
    if (this.socket || this.pollRequest) {
      this.close();
    }

    if (!capabilities.hasWebSocket) {
      return this.poll();
    }

    this._deferred = $.Deferred();
    this.requestType = 'websockets';
    this.socket = new window.WebSocket(_.result(this, 'url'));

    this.socket.onopen = this.onSocketOpen;
    this.socket.onclose = this.onSocketClose;
    this.socket.onerror = this.onSocketError;
    this.socket.onmessage = this.onSocketMessage;

    return this._deferred.promise();
  },

  close: function () {
    if (this.pollRequest) {
      xhrTracker.abort(this.pollRequest.rid);
      this.pollRequest = null;
    }

    if (this._deferred && this._deferred.state() === 'pending') {
      this._deferred.reject();
    }

    if (this.socket) {
      this.cancelPing();

      this.socket.onopen = null;
      this.socket.onclose = null;
      this.socket.onerror = null;
      this.socket.onmessage = null;
      this.socket.close();
      this.socket = null;
    }
  },

  schedulePing: function () {
    this.cancelPing();
    this._pingTimeoutID = window.setTimeout(
      this.onPingTimeout,
      SOCKET_PING_DELAY
    );
  },

  cancelPing: function () {
    if (this._pingTimeoutID) {
      window.clearTimeout(this._pingTimeoutID);
      this._pingTimeoutID = null;
    }
  },

  poll: function () {
    this.requestType = 'longpolling';
    this.pollUrl = path.join(_.result(this, 'url'), {
      ttl: String(POLL_TIMEOUT / 1000) + 's',
    });

    const xhrOptions = this.server.getXHROptions(this.pollUrl, {
      timeout: POLL_TIMEOUT + 5000,
    });

    xhrOptions.headers['If-Modified-Since'] = this.lastModified
      ? this.lastModified
      : 'Thu, 01 Jan 1970 00:00:00 GMT';

    if (this.eTag) {
      xhrOptions.headers['If-None-Match'] = this.eTag;
    }

    this.pollRequest = $.ajax(xhrOptions)
      .done(this.onPollSuccess)
      .fail(this.onPollError);

    return this.pollRequest.promise();
  },

  handleResponse: function (response: any) {
    if (!response) {
      return;
    }

    const message = parser.parseXML(response);

    if (_.isObject(message)) {
      const commandName = message.command;
      dispatcher.trigger(`cloudServer:${commandName}`, message);
    }
  },

  //
  // Listeners

  onSocketOpen: function (event: any) {
    this.server.set('hasEventConnection', true);
    this.trigger('open', this.server, event);
    this._deferred.resolve(this.server, event);

    this.schedulePing();
  },

  onSocketClose: function (event: any) {
    this.close();

    this.server.set('hasEventConnection', false);
    this.trigger('close', this.server, event);
    this._deferred.reject(this.server, event);
  },

  onSocketError: function (event: any) {
    this.close();

    this.trigger('error', this.server, event);
    this._deferred.reject(this.server, event);
  },

  onSocketMessage: function (event: any) {
    this.handleResponse(event.data);
  },

  onPingTimeout: function () {
    this._pingTimeoutID = null;

    this.socket.send('hello');
    this.schedulePing();
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'textStatus' is declared but its value is never re... Remove this comment to see the full error message
  onPollSuccess: function (response: any, textStatus: any, jqXHR: any) {
    this.trigger('open', this.server, response);

    this.lastModified = jqXHR.getResponseHeader('Last-Modified');
    this.eTag = jqXHR.getResponseHeader('ETag');

    this.handleResponse(response);
    this.poll();
  },

  onPollError: function (xhr: any, textStatus: any) {
    if (textStatus === 'abort') {
      // If the poll request times out because the ttl was not respected for
      // some reason, keep polling.
      this.poll();
    } else if (!this.pollRequest.aborting) {
      this.trigger('error', this.server, xhr);
    }
  },
});

export default CloudServerEventConnection;
