/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectAllProviderEntities from 'app/js/stores/selectors/provider/selectAllProviderEntities';
import createServerFlexSelector from 'app/js/stores/selectors/server/createServerFlexSelector';
import createServerNowPlayingSessionsCountSelector from 'app/js/stores/selectors/server/createServerNowPlayingSessionsCountSelector';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import selectDismissedServerUpdateVersions from 'app/js/stores/selectors/settings/selectDismissedServerUpdateVersions';
import selectServerUpdateDismissedVersion from 'app/js/stores/selectors/settings/selectServerUpdateDismissedVersion';
import createHasAvailableLibraryUpgradeSelector from 'app/js/stores/selectors/ui/createHasAvailableLibraryUpgradeSelector';
import { LIBRARY } from 'app/js/utils/constants/providerIdentifiers';
import isUpdateAvailable from 'app/js/utils/server/isUpdateAvailable';
import NavBarActivityMenuStatusItem from './NavBarActivityMenuStatusItem';

function createSelectServerBackgroundTranscodeJobs() {
  return createSelector(createServerFlexSelector(), (serverFlexModel) =>
    serverFlexModel.backgroundTranscodeJobs
      ? serverFlexModel.backgroundTranscodeJobs
      : []
  );
}

function createSelectServerActivities() {
  return createSelector(createServerFlexSelector(), (serverFlexModel) =>
    serverFlexModel.activities ? serverFlexModel.activities : []
  );
}

function createMapStateToProps() {
  return createSelector(
    createUnstableServerSelector(),
    createServerNowPlayingSessionsCountSelector(),
    createSelectServerBackgroundTranscodeJobs(),
    createSelectServerActivities(),
    createHasAvailableLibraryUpgradeSelector(),
    selectDismissedServerUpdateVersions,
    selectServerUpdateDismissedVersion,
    selectAllProviderEntities,
    (
      server,
      sessionsCount,
      backgroundTranscodeJobs,
      activities,
      hasAvailableLibraryUpgrade,
      dismissedServerUpdateVersions,
      serverUpdateDismissedVersion,
      providers
    ) => {
      return {
        friendlyName: server.friendlyName,
        machineIdentifier: server.machineIdentifier,
        providerEntityID: server.providers
          .map((providerEntityID: string) => providers[providerEntityID])
          .find((provider: any) => provider.identifier === LIBRARY).entityID,
        sessionsCount,
        backgroundTranscodeJobs,
        activities,
        showLibraryUpgradeAvailable: hasAvailableLibraryUpgrade,
        showUpdateAvailable: isUpdateAvailable(
          server,
          dismissedServerUpdateVersions,
          serverUpdateDismissedVersion
        ),
      };
    }
  );
}

const NavBarActivityMenuStatusItemConnector = connect(createMapStateToProps)(
  NavBarActivityMenuStatusItem
);

export default NavBarActivityMenuStatusItemConnector;
