import $ from 'jquery';
import _ from 'underscore';
import BaseModel from 'app/js/models/BaseModel';
import PlayQueueItemCollection from 'app/js/models/library/PlayQueueItemCollection';
import dispatcher from 'app/js/utils/dispatcher';
import parser from 'app/js/utils/parser';
import path from 'app/js/utils/url/path';

const BackgroundProcessingQueueModel = BaseModel.extend({
  //
  // Properties

  idAttribute: 'playQueueID',

  whitelistedAttrs: [
    'playQueueID',
    'playQueueSelectedItemID',
    'playQueueSelectedItemOffset',
    'playQueueSelectedMetadataItemID',
    'playQueueLastAddedItemID',
    'playQueueShuffled',
    'playQueueSourceURI',
    'playQueueTotalCount',
    'playQueueVersion',
    'message',
  ],

  defaults: {
    selectedItem: null,
    mediaType: null,
    owned: true,
    hasUserAdditions: false,
    playQueueVersion: 0,
  },

  metadataElementNames: ['Directory', 'Video', 'Track', 'Photo', 'Hub'],

  url: function () {
    return '/playQueues/' + this.id;
  },

  //
  // Init

  initialize: function () {
    this.listenTo(this, 'change:message', this.onMessageChange);
  },

  //
  // Control

  // Overriding fetch implementation in a way compatible with populateMixin
  _fetch: function () {
    const url = _.result(this, 'url');
    return this._ajax('GET', url);
  },

  convert: function (xml: any) {
    const json = _.isObject(xml) ? xml : parser.parseXML(xml);

    parser.parseBoolean(json, 'playQueueShuffled');

    parser.parseInteger(json, [
      'playQueueSelectedItemOffset',
      'playQueueTotalCount',
      'playQueueVersion',
    ]);

    parser.parseArray(json, 'Metadata');

    return json;
  },

  parse: function (response: any) {
    if (_.isString(response)) {
      response = parser.replaceXMLElementNames(
        response,
        this.metadataElementNames,
        'Metadata'
      );
    }

    const json = this.convert(response);

    // The name of the id attribute is different if the model is fetched
    // individually rather than as part of a collection.
    if (json.id) {
      json.playQueueID = json.id;
    }

    // FIXME: This is hacky, and I'd like to formalize change events when
    // relations are also changing so they can be all be triggered after
    // attributes are set. The reason for the hack is that we filter the up
    // next region by this model's playQueueSelectedItemID and
    // playQueueLastAddedItemID. However, since we have to set either the
    // collection's models or this model's attributes first, the subsequent
    // refiltering on change events will be incorrect momentarily, leading
    // to a glitch. You can reproduce this by performing an action that
    // leads to queue regeneration, like deleting a generator.

    const attributes = _.pick(json, this.whitelistedAttrs);
    this.set(attributes, { silent: true });

    // Record what has changed so we can trigger change events when the
    // collection is ready.
    this.pendingChanges = this.changed;

    this.parseItems(json);

    return attributes;
  },

  parseItems: function (json: any) {
    let collection = this.get('collection');
    const models = json.Metadata;
    const parse = !!models.length;

    const selectedIndex = _.pluck(models, 'playQueueItemID').indexOf(
      json.playQueueSelectedItemID
    );
    const selectedItemOffset = json.playQueueSelectedItemOffset;
    const firstIndex = selectedItemOffset - selectedIndex;

    _.each(models, function (item, index) {
      item.queueIndex = firstIndex + index;
      if (json.identifier) {
        item.identifier = json.identifier;
      }
      if (json.mediaTagPrefix) {
        item.mediaTagPrefix = json.mediaTagPrefix;
      }
      if (json.mediaTagVersion) {
        item.mediaTagVersion = json.mediaTagVersion;
      }
    });

    if (collection) {
      collection.set(models, { parse: parse });
    } else {
      collection = new PlayQueueItemCollection(models, {
        server: this.server,
        parent: this,
        parse: parse,
      });

      this.set('collection', collection);
    }

    collection.totalSize = json.playQueueTotalCount;
    collection.availableRange = {
      start: firstIndex,
      end: firstIndex + collection.length,
    };
  },

  moveItem: function (itemID: any, afterItemID: any) {
    if (itemID === this.get('playQueueSelectedItemID')) {
      return $.Deferred().resolve().promise();
    }

    this.set('hasUserAdditions', true);

    const params = { after: afterItemID || -1 };
    const url = path.join(
      [_.result(this, 'url'), '/items/', itemID, '/move'],
      params
    );

    return this._ajax('PUT', url);
  },

  getUpNextRange: function () {
    const collection = this.get('collection');
    const selectedItem = collection.get(this.get('playQueueSelectedItemID'));
    const selectedIndex = collection.indexOf(selectedItem);
    const startIndex = Math.max(selectedIndex, 0);
    let endIndex = startIndex;

    const lastAddedItemID = this.get('playQueueLastAddedItemID');
    if (lastAddedItemID != null) {
      const lastAddedItem = collection.get(lastAddedItemID);
      const lastAddedIndex = collection.indexOf(lastAddedItem);

      if (lastAddedIndex > startIndex) {
        endIndex = lastAddedIndex;
      }
    }

    return {
      start: startIndex,
      end: endIndex,
    };
  },

  getCurrentItem: function () {
    const selectedItemId = this.get('playQueueSelectedItemID');
    return (
      this.get('collection').findWhere({ playQueueItemID: selectedItemId }) ||
      null
    );
  },

  _ajax: function (type: any, url: any) {
    const xhrOptions = _.extend(this.server.getXHROptions(url), { type: type });
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(
      _.bind(function (response: any) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.set(this.parse(response));

        // This is related to the silent `set` hack above. This set won't
        // actually trigger all the change events that we need.

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (!_.isEmpty(this.pendingChanges)) {
          _.each(
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.pendingChanges,
            function (value, key) {
              // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
              this.trigger('change:' + key, this, value);
            },
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this
          );

          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.trigger('change', this);
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.pendingChanges = null;
        }

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.set('selectedItem', this.getCurrentItem());
      }, this)
    );

    return xhrDeferred.promise();
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
  onMessageChange: function (model: any, message: any) {
    if (message) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: message,
      });
    }
  },
});

export default BackgroundProcessingQueueModel;
