import { useCallback, useMemo, useState } from 'react';

export interface UseDisclosureReturn {
  isOpen: boolean;
  onOpen(): void;
  onClose(): void;
  onToggle(): void;
}

export default function useDisclosure(): UseDisclosureReturn {
  const [isOpen, setIsOpenState] = useState(false);

  const onOpen = useCallback(() => setIsOpenState(true), []);
  const onClose = useCallback(() => setIsOpenState(false), []);
  const onToggle = useCallback(() => setIsOpenState(!isOpen), [isOpen]);

  return useMemo(() => {
    return {
      isOpen,
      onOpen,
      onClose,
      onToggle,
    };
  }, [isOpen, onClose, onOpen, onToggle]);
}
