import { resolve, waitFor } from 'app/js/adapters/adapters';
import localizerMixin from 'app/js/mixins/localizerMixin';
import awaitReady from 'app/js/stores/actions/helpers/awaitReady';
import { fetchProvider } from 'app/js/stores/actions/provider/providerActions';
import { fetchProviders } from 'app/js/stores/actions/serverActions';
import BaseClass from 'app/js/utils/BaseClass';
import whenEach from 'app/js/utils/promise/whenEach';
import path from 'app/js/utils/url/path';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const Localizer = BaseClass.extend({
  mixins: localizerMixin,

  //
  // Init

  constructor: function () {
    this.basePath = path.join([__PUBLIC_PATH__, 'translations']);
    this.translations = {};

    waitFor('appModel').done(this.onAppModelRegistered.bind(this));
  },

  //
  // Control

  _repopulateProviders() {
    const appModel = resolve('appModel');
    const serverModels = appModel.get('servers');

    return whenEach(
      serverModels
        .map((serverModel: any) => {
          if (serverModel.get('isCloud')) {
            const providers = appModel.get('user').get('providers');

            return whenEach(
              providers.map((providerModel: any) => {
                resolve('appStore').dispatch(
                  fetchProvider({
                    providerEntityID: providerModel.entityID(),
                    options: {
                      merge: true,
                    },
                  })
                );

                return awaitReady(providerModel);
              })
            );
          } else if (serverModel.get('isConnected')) {
            resolve('appStore').dispatch(
              fetchProviders({
                serverEntityID: serverModel.entityID(),
                options: {
                  merge: true,
                },
              })
            );

            return awaitReady(serverModel);
          }

          return;
        })
        .filter(Boolean)
    );
  },

  //
  // Listeners

  onAppModelRegistered() {
    if (__DEV__) {
      throw new Error(
        '[Localizer] Missing onAppModelRegistered implementation.'
      );
    }
  },
});

export default Localizer;
