/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { FORUMS_URL } from 'app/js/utils/constants/commonUrls';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import ModalBody from 'app/js/views/v3/components/core/modals/ModalBody';
import ModalContent from 'app/js/views/v3/components/core/modals/ModalContent';
import ModalFooter from 'app/js/views/v3/components/core/modals/ModalFooter';
import ModalHeader from 'app/js/views/v3/components/core/modals/ModalHeader';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from 'app/js/views/v3/components/core/modals/ModalFooter.css';

type UnexpectedErrorModalProps = {
  isOpen: boolean;
  errorMessage?: string;
  onModalClose: (...args: any[]) => any;
};

function UnexpectedErrorModal(props: UnexpectedErrorModalProps) {
  const { isOpen, errorMessage, onModalClose } = props;

  const sentences = [
    errorMessage ? errorMessage : t('There was an unexpected error.'),
    t('Please visit our forums if you continue to experience problems.'),
  ];

  return (
    <Modal
      isOpen={isOpen}
      metricsPage={metricsPages.HOME}
      size={sizes.SMALL}
      onModalClose={onModalClose}
    >
      <ModalContent onModalClose={onModalClose}>
        <ModalHeader>{t('There was an unexpected error')}</ModalHeader>
        <ModalBody>
          {sentences.join(' ')}
          <br />
          <Link
            kind={kinds.PRIMARY}
            target={__EXTERNAL_LINK_TARGET__}
            to={FORUMS_URL}
            onPress={onModalClose}
          >
            {t('Go to Forums')}
          </Link>
        </ModalBody>
        <ModalFooter>
          <Button
            className={styles.button}
            data-autofocus={true}
            kind={kinds.PRIMARY}
            onPress={onModalClose}
          >
            {t('Okay')}
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
}

export default UnexpectedErrorModal;
