/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import durationOf from 'app/js/utils/dateTime/durationOf';

function formatPlayerTime(milliseconds: any) {
  if (milliseconds <= 0 || !_.isNumber(milliseconds)) {
    return '0:00';
  }
  const duration = durationOf(milliseconds);
  const sec = (duration as any).seconds;
  const min = (duration as any).minutes;
  const hr = (duration as any).hours + 24 * (duration as any).days;
  let str = '';
  if (sec < 10) {
    str = ':0' + sec;
  } else {
    str = ':' + sec;
  }
  if (min < 10 && hr > 0) {
    str = '0' + min + str;
  } else {
    str = min + str;
  }
  if (hr > 0) {
    str = hr + ':' + str;
  }
  return str;
}
export default formatPlayerTime;
