/* eslint-plugin-disable @typescript-eslint */

import X2JS from 'x2js';
import log from 'app/js/utils/log/log';

let xmlParser: any;

function parseXML(value: any) {
  const empty = {};

  let result;

  if (!value) {
    log.warn('[Parser] Empty XML source');
    return empty;
  }

  xmlParser =
    xmlParser ||
    new X2JS({
      attributePrefix: '',
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{}' is not assignable to type '"object" | "t... Remove this comment to see the full error message
      emptyNodeForm: {},
      ignoreRoot: true,
    });

  try {
    result = value && xmlParser.xml2js(value);
  } catch (error) {
    log.warn('[Parser] Unable to parse XML: ' + error.message);
  }

  return result || empty;
}

export default parseXML;
