//
// Action Types

export const FETCH_CLOUD_PROVIDERS = 'provider/fetchCloudProviders';
export const POPULATE_CLOUD_PROVIDERS = 'provider/populateCloudProviders';
export const FETCH_PROVIDER = 'providers/fetchProvider';
export const POPULATE_PROVIDER = 'providers/populateProvider';
export const ADD_TO_CATALOG = 'provider/addToCatalog';
export const CLEAR_ADD_TO_CATALOG_ERROR = 'provider/clearAddToCatalogError';
export const SCAN_LIBRARY_FILES = 'provider/scanLibraryFiles';
export const EDIT_LIBRARY = 'provider/editLibrary';
export const ANALYZE_LIBRARY = 'provider/analyzeLibrary';
export const EMPTY_LIBRARY_TRASH = 'provider/emptyLibraryTrash';
export const DELETE_LIBRARY = 'provider/deleteLibrary';
export const UPGRADE_LIBRARY = 'provider/upgradeLibrary';

//
// Action Creators

export const fetchCloudProviders = createActionCreator(FETCH_CLOUD_PROVIDERS);
export const populateCloudProviders = createActionCreator(
  POPULATE_CLOUD_PROVIDERS
);
export const fetchProvider = createActionCreator(
  FETCH_PROVIDER,
  (payload: any) => {
    const { options = {} } = payload;

    return {
      ...payload,
      options: {
        ...options,
        // Disallow headers in the URL for the root provider request on cloud
        // providers only. This allows the provider root endpoint to be cached.
        // See: https://github.com/plexinc/plex-web-client/issues/14201
        allowHeadersInUrl: false,
      },
    };
  }
);
export const populateProvider = createActionCreator(POPULATE_PROVIDER);
export const addToCatalog = createActionCreator(ADD_TO_CATALOG);
export const clearAddToCatalogError = createActionCreator(
  CLEAR_ADD_TO_CATALOG_ERROR
);
export const scanLibraryFiles = createActionCreator(SCAN_LIBRARY_FILES);
export const editLibrary = createActionCreator(EDIT_LIBRARY);
export const emptyLibraryTrash = createActionCreator(EMPTY_LIBRARY_TRASH);
export const analyzeLibrary = createActionCreator(ANALYZE_LIBRARY);
export const deleteLibrary = createActionCreator(DELETE_LIBRARY);
export const upgradeLibrary = createActionCreator(UPGRADE_LIBRARY);
export const refreshAllLibraryMetadata = createActionCreator(
  SCAN_LIBRARY_FILES,
  (payload: any) => {
    return { ...payload, force: true };
  }
);
export const cancelScanLibraryFiles = createActionCreator(
  SCAN_LIBRARY_FILES,
  (payload: any) => {
    return { ...payload, cancel: true };
  }
);

//
// Action Helpers

function getLibraryIDFromContentDirectory(contentDirectoryModel: any) {
  // PMS 1.13.10 and earlier is missing `id` on directories.
  // See: https://github.com/plexinc/plex-media-server/issues/9292
  const directoryKey = contentDirectoryModel.get('key');
  return (
    contentDirectoryModel.get('id') ||
    directoryKey.substr(directoryKey.lastIndexOf('/') + 1)
  );
}

function findContentDirectoryModel(appModel: any, payload: any) {
  const providerModel = findProviderModel(appModel, payload);
  const contentFeatureModel = providerModel
    .get('features')
    .get(providerFeatureTypes.CONTENT);
  const findAttrs = {};
  // Conditionally add key and addKey because otherwise
  // findWhere({ key: undefined, hubKey: 'foo' }) would not find
  // { hubKey: 'foo' }.
  if (payload.contentDirectory.key) {
    (findAttrs as any).key = payload.contentDirectory.key;
  }
  if (payload.contentDirectory.hubKey) {
    (findAttrs as any).hubKey = payload.contentDirectory.hubKey;
  }
  return contentFeatureModel.get('directories').findWhere(findAttrs);
}

function getContentDirectoryKey(
  providerModel: any,
  contentDirectoryModel: any
) {
  const contentFeatureModel = providerModel
    .get('features')
    .get(providerFeatureTypes.CONTENT);
  const contentFeatureKey = contentFeatureModel.get('key');
  // Modern providers just have an `id` on the directory and clients are
  // expected to construct the full `key` using the content feature's `key`.
  let key;
  if (contentFeatureKey) {
    key = path.join([contentFeatureKey, contentDirectoryModel.get('id')]);
  } else {
    key = contentDirectoryModel.get('key');
  }
  return key;
}

function refreshProviders(dispatch: any, serverEntityID: any) {
  dispatch(serverActions.fetchProviders({ serverEntityID }));
  dispatch(serverActions.fetchLibraries({ serverEntityID }));
  dispatch(repopulatePage({ serverEntityID }));
}

//
// Action Handlers

export const providerActionHandlers = {
  [FETCH_CLOUD_PROVIDERS]: createFetchHandler({
    key: 'providers',
    getModel: findUserModel,
  }),

  [POPULATE_CLOUD_PROVIDERS]: createPopulateHandler({
    key: 'providers',
    fetchActionCreator: fetchCloudProviders,
    getModel: findUserModel,
  }),

  [FETCH_PROVIDER]: createFetchHandler({
    getModel: findProviderModel,
  }),

  [POPULATE_PROVIDER]: createPopulateHandler({
    getModel: findProviderModel,
    fetchActionCreator: fetchProvider,
  }),

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [CLEAR_ADD_TO_CATALOG_ERROR](appModel: any, payload: any) {
    const addToCatalogModalModel = appModel.get('ui').get('addToCatalogModal');
    addToCatalogModalModel.set('error', null);
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [ADD_TO_CATALOG](appModel: any, payload: any, dispatch: any) {
    const addToCatalogModalModel = appModel.get('ui').get('addToCatalogModal');
    const providerModel = findProviderModel(appModel, payload);
    const addToCatalogAction = providerModel
      .get('features')
      .get('actions')
      .get('actions')
      .get('addToCatalog');
    const actionKey = addToCatalogAction.get('key');
    scheduleImmediateUpdate(() => {
      addToCatalogModalModel.set({
        isUpdating: true,
        error: null,
      });
    });
    if (payload.data.fileURL) {
      const blobXHR = new window.XMLHttpRequest();
      blobXHR.open('GET', payload.data.fileURL, true);
      blobXHR.responseType = 'blob';
      blobXHR.onload = (event) => {
        if ((event.target as any).status === 200) {
          const file = (event.target as any).response;
          const formData = new window.FormData();
          formData.append('file', file);
          const xhrOptions = providerModel.getXHROptions(actionKey, {
            method: 'POST',
            processData: false,
            contentType: false,
            data: formData,
          });
          $.ajax(xhrOptions).then(
            // @ts-expect-error ts-migrate(6133) FIXME: 'response' is declared but its value is never read... Remove this comment to see the full error message
            (response, status, xhr) => {
              addToCatalogModalModel.set({
                isUpdating: false,
              });
            },
            (xhr) => {
              addToCatalogModalModel.set({
                isUpdating: false,
                error: xhrErrorFilter(xhr),
              });
            }
          );
        }
        // Always revoke the object URL when we are finished so that we clear up
        // the used memory
        window.URL.revokeObjectURL(payload.data.fileURL);
      };
      blobXHR.send();
      return;
    }
    const url = path.join(actionKey, {
      url: payload.data.channelURL,
    });
    const xhrOptions = providerModel.getXHROptions(url, {
      method: 'PUT',
      dataType: 'json',
    });
    const promise = $.ajax(xhrOptions);
    promise.then(
      // @ts-expect-error ts-migrate(6133) FIXME: 'status' is declared but its value is never read.
      (response, status, xhr) => {
        addToCatalogModalModel.set('isUpdating', false);
        const showModel = new MetadataModel(response, {
          server: providerModel.server,
          provider: providerModel,
          parse: true,
        });
        // Some failure conditions cause the provider to return an empty metadata
        // container. Treat this case as an error.
        if (!showModel.get('key')) {
          addToCatalogModalModel.set({
            isUpdating: false,
            error: xhrErrorFilter(xhr),
          });
          return;
        }
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
        const route = getPrePlayRoute(
          providerModel.server.state,
          providerModel.state,
          showModel.get('key')
        );
        // @ts-expect-error ts-migrate(2322) FIXME: Type 'string | undefined' is not assignable to typ... Remove this comment to see the full error message
        window.location.hash = route;
      },
      (xhr) => {
        addToCatalogModalModel.set({
          isUpdating: false,
          error: xhrErrorFilter(xhr),
        });
      }
    );
  },

  [SCAN_LIBRARY_FILES](appModel: any, payload: any, dispatch: any) {
    const { cancel, force, metricsPage } = payload;
    const serverModel = findServerModel(appModel, payload);
    const contentDirectoryModel = findContentDirectoryModel(appModel, payload);
    const options = {
      libraryID: getLibraryIDFromContentDirectory(contentDirectoryModel),
      libraryType: contentDirectoryModel.get('type'),
      cancel,
      force,
      allowMetrics: !!metricsPage,
      metricsProperties: { page: metricsPage },
    };
    contentDirectoryModel.set('refreshing', !cancel);
    refreshLibrary(serverModel, options)
      .fail((xhr) => {
        let errorMessage;
        if (cancel) {
          errorMessage = t('There was an unexpected error canceling the scan.');
        } else if (force) {
          errorMessage =
            xhr.status === 503
              ? t(
                  "An internet connection is required to refresh library metadata. Please check this server's connection and try again."
                )
              : t('There was an unexpected error refreshing library metadata.');
        } else {
          errorMessage =
            xhr.status === 503
              ? t(
                  "An internet connection is required to scan library files. Please check this server's connection and try again."
                )
              : t('There was an unexpected error scanning library files.');
        }
        dispatch(showAlert(errorMessage));
      })
      // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
      .always((xhr) => {
        dispatch(
          serverActions.testServer({
            serverEntityID: payload.serverEntityID,
          })
        );
      });
  },

  [EDIT_LIBRARY](appModel: any, payload: any) {
    const serverModel = findServerModel(appModel, payload);
    const libraries = serverModel.get(__PLATFORM__).get('libraries');
    const contentDirectoryModel = findContentDirectoryModel(appModel, payload);
    libraries.fetch().then(() => {
      const libraryID = getLibraryIDFromContentDirectory(contentDirectoryModel);
      const libraryModel = libraries.get(libraryID);
      dispatcher.trigger(
        'show:modal',
        new EditSectionModal({
          server: serverModel,
          model: libraryModel,
        })
      );
      resolve('metrics').trackClick({
        page: payload.metricsPage,
        action: metricsActions.EDIT_LIBRARY,
        type: libraryModel.get('type'),
        context: metricsContexts.LIBRARY,
      });
    });
  },

  [EMPTY_LIBRARY_TRASH](appModel: any, payload: any, dispatch: any) {
    const serverModel = findServerModel(appModel, payload);
    const providerModel = findProviderModel(appModel, payload);
    const contentDirectoryModel = findContentDirectoryModel(appModel, payload);
    const key = getContentDirectoryKey(providerModel, contentDirectoryModel);
    const url = path.join([key, 'emptyTrash']);
    const xhrOptions = serverModel.getXHROptions(url, { type: 'PUT' });
    $.ajax(xhrOptions)
      .done(() => {
        dispatch(
          showAlert({
            type: alertTypes.SUCCESS,
            message: t('The trash was emptied.'),
          })
        );
      })
      // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
      .fail((xhr) => {
        dispatch(showAlert(t('There was a problem emptying the trash.')));
      });
    resolve('metrics').trackClick({
      page: payload.metricsPage,
      action: metricsActions.EMPTY_TRASH,
      type: contentDirectoryModel.get('type'),
      context: metricsContexts.LIBRARY,
    });
  },

  [ANALYZE_LIBRARY](appModel: any, payload: any, dispatch: any) {
    const serverModel = findServerModel(appModel, payload);
    const providerModel = findProviderModel(appModel, payload);
    const contentDirectoryModel = findContentDirectoryModel(appModel, payload);
    const key = getContentDirectoryKey(providerModel, contentDirectoryModel);
    const url = path.join([key, 'analyze']);
    const xhrOptions = serverModel.getXHROptions(url, { type: 'PUT' });
    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    $.ajax(xhrOptions).fail((xhr) => {
      dispatch(showAlert(t('There was a problem analyzing the library.')));
    });
    resolve('metrics').trackClick({
      page: payload.metricsPage,
      action: metricsActions.ANALYZE_LIBRARY,
      type: contentDirectoryModel.get('type'),
      context: metricsContexts.LIBRARY,
    });
  },

  [DELETE_LIBRARY](appModel: any, payload: any, dispatch: any) {
    const serverModel = findServerModel(appModel, payload);
    const providerModel = findProviderModel(appModel, payload);
    const contentDirectoryModel = findContentDirectoryModel(appModel, payload);
    const key = getContentDirectoryKey(providerModel, contentDirectoryModel);
    const url = path.join(key, { async: 1 });
    const xhrOptions = serverModel.getXHROptions(url, { type: 'DELETE' });
    // Optimistically remove the content directory so the delete is reflected in
    // the UI immediately.
    providerModel
      .get('features')
      .get(providerFeatureTypes.CONTENT)
      .get('directories')
      .remove(contentDirectoryModel);
    $.ajax(xhrOptions).fail(() => {
      // Refresh the provider so our models reflect the real state, ie: reverses
      // the optimistic deletion of the content directory.
      refreshProviders(dispatch, payload.serverEntityID);
      dispatch(showAlert(t('There was a problem deleting the library.')));
    });
    resolve('metrics').trackClick({
      page: payload.metricsPage,
      action: metricsActions.DELETE_LIBRARY,
      type: contentDirectoryModel.get('type'),
      context: metricsContexts.LIBRARY,
    });
  },

  [UPGRADE_LIBRARY](
    appModel: any,
    payload: {
      serverEntityID: string;
      providerEntityID: string;
      contentDirectory?: ContentDirectory;
      options: {
        refreshMetadata: boolean;
      };
    },
    dispatch: any
  ) {
    const serverModel = findServerModel(appModel, payload);
    const providerModel = findProviderModel(appModel, payload);
    const contentDirectoryModel = findContentDirectoryModel(appModel, payload);
    const libraryType = contentDirectoryModel.get('type');

    let options;
    if (libraryType === metadataTypes.MOVIE) {
      options = {
        agent: agentIdentifiers.PLEXMOVIE,
        scanner: scannerIdentifiers.PLEX_MOVIE,
      };
    } else if (libraryType === metadataTypes.ARTIST) {
      options = {
        agent: agentIdentifiers.PLEXMUSIC,
        scanner: scannerIdentifiers.PLEX_MUSIC,
      };
    } else {
      options = {
        agent: agentIdentifiers.PLEXSERIES,
        scanner: scannerIdentifiers.PLEX_SERIES,
      };
    }

    const key = getContentDirectoryKey(providerModel, contentDirectoryModel);
    const url = path.join(key, options);
    const xhrOptions = serverModel.getXHROptions(url, { type: 'PUT' });

    const promise = $.ajax(xhrOptions).then(
      () => {
        refreshProviders(dispatch, payload.serverEntityID);
      },
      () => {
        dispatch(showAlert(t('There was a problem upgrading the library.')));
      }
    );

    if (payload.options.refreshMetadata) {
      promise.then(() => {
        dispatch(
          serverActions.startLibrariesRefresh({
            serverEntityID: payload.serverEntityID,
            cancel: false,
            force: true,
            metricsPage: metricsPages.CONFIRM_UPGRADE_LIBRARY,
          })
        );
      });
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [serverActions.FINISH_ACTIVITY](appModel: any, payload: any, dispatch: any) {
    if (payload.activity.type === activityTypes.LIBRARY_DELETE) {
      // Refresh the providers so content directory changes are accurate.
      refreshProviders(dispatch, payload.serverEntityID);
    }
  },
};
