import { SagaIterator } from 'redux-saga';
import { all, fork } from 'redux-saga/effects';
import dashboardSaga from 'app/js/v4/features/dashboard/dashboardSaga';
import dvrManagementSaga from 'app/js/v4/features/dvrManagement/dvrManagementSaga';
import libraryManagementSaga from 'app/js/v4/features/libraryManagement/libraryManagementSaga';
import liveTVSaga from 'app/js/v4/features/livetv/liveTVSaga';

export default function* featuresSaga(): SagaIterator {
  yield all([
    fork(dashboardSaga),
    fork(dvrManagementSaga),
    fork(libraryManagementSaga),
    fork(liveTVSaga),
  ]);
}
