/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import ServerModel from 'app/js/models/server/ServerModel';
import * as connectionTestStates from 'app/js/utils/constants/server/connectionTestStates';
import { CLOUD_SERVER_ID } from 'app/js/utils/constants/serverIDs';

const { isCloud, ...otherSessionAttrs } = ServerModel.prototype.sessionAttrs;

const TransientServerModel = ServerModel.extend({
  //
  // Properties

  name: 'TransientServerModel',

  cache: null,
  persistInCache: false,

  sessionAttrs: {
    ...otherSessionAttrs,
    isTransient: attrTypes.boolean,
    originalMachineIdentifier: attrTypes.string,
  },

  derivedAttrs: {
    ...ServerModel.prototype.derivedAttrs,
    isCloud() {
      return this.get('originalMachineIdentifier') === CLOUD_SERVER_ID;
    },
  },

  defaults() {
    // eslint-disable-next-line prefer-rest-params
    const defaults = ServerModel.prototype.defaults.apply(this, arguments);

    return {
      ...defaults,
      friendlyName: 'Transient Server',
      isTransient: true,
    };
  },

  updateCacheKey() {
    // Override since transient servers are not cached.
  },

  // Control

  testConnection(connection: any) {
    if (this.get('originalMachineIdentifier') === CLOUD_SERVER_ID) {
      // Treat the transient plex.tv connection as automatically-successful.
      // The providers populate request will act as the real health check.
      connection.set({ testState: connectionTestStates.CONNECTED });
      this.onConnectionTestSuccess(connection);
    } else {
      // eslint-disable-next-line prefer-rest-params
      return ServerModel.prototype.testConnection.apply(this, arguments);
    }
  },
});

export default TransientServerModel;
