/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseClass from 'app/js/utils/BaseClass';
import log from 'app/js/utils/log/log';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ServerRetryManager = BaseClass.extend({
  //
  // Properties

  maximumDelay: 30,

  //
  // Init

  initialize({ callback, logFormat }: any) {
    this._serversBeingRetried = {};
    this.callback = callback;
    this.logFormat = logFormat;
  },

  //
  // Control

  start(server: any) {
    // If we're already retrying this server, don't try again.
    if (this._serversBeingRetried[server.cid]) {
      return;
    }

    if (this.supportsRetry(server)) {
      this._serversBeingRetried[server.cid] = {
        server,
        attempt: 1,
      };

      this._scheduleRetry(server);
    }
  },

  stop(server: any) {
    const retry = this._serversBeingRetried[server.cid];

    if (retry) {
      window.clearTimeout(retry.timeout);
      delete this._serversBeingRetried[server.cid];
    }
  },

  stopAll() {
    _.each(this._serversBeingRetried, (retry, serverCID) => {
      window.clearTimeout(retry.timeout);
      delete this._serversBeingRetried[serverCID];
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  supportsRetry(server: any) {
    return true;
  },

  _scheduleRetry(server: any) {
    const retry = this._serversBeingRetried[server.cid];
    const delay = Math.min(this.maximumDelay / 2, retry.attempt) * 2;

    log(this.logFormat, server.get('debugName'), delay);

    retry.timeout = window.setTimeout(() => {
      this.callback(server);
      retry.attempt++;

      // Always schedule a retry regardless of the result of `retry.action.call()`.
      this._scheduleRetry(server);
    }, delay * 1000);
  },
});

export default ServerRetryManager;
