/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';

function unmatchCommand(options: any) {
  const item = options.item;
  const url = '/library/metadata/' + item.id + '/unmatch';
  const xhrOptions = _.extend(item.server.getXHROptions(url), { type: 'PUT' });

  const deferred = $.ajax(xhrOptions);

  deferred.fail(function () {
    dispatcher.trigger('show:alert', {
      type: 'error',
      message: t('There was a problem unmatching this item.'),
    });
  });

  return deferred.promise();
}

export default unmatchCommand;
