import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import dispatcher from 'app/js/utils/dispatcher';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/alert... Remove this comment to see the full error message
import tpl from 'app/templates/alerts/AlertBarView.tpl';

const ALERT_TYPES = {
  success: true,
  error: true,
  notify: true,
};

const AlertBarView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'alert-bar',
  template: tpl,

  events: {
    'click .close-btn': 'onCloseClick',
  },

  //
  // Init

  initialize: function (options: any) {
    const alert = options.alert;
    this.alert = alert;

    if (alert) {
      const type = alert.type;

      // Since these class names may be user-supplied, only permit known values.
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      if (type && ALERT_TYPES[type]) {
        this.$el.addClass(type);
      }
    }
  },

  //
  // Control

  hide: function () {
    dispatcher.trigger('hide:bar', 'alertbar');
  },

  serializeData: function () {
    return this.alert;
  },

  //
  // Listeners

  onCloseClick: function (event: any) {
    event.preventDefault();
    this.hide();
  },
});

export default AlertBarView;
