/* eslint-plugin-disable @typescript-eslint */

import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';

function normalizeProviderDataIdentifier(
  identifier: any,
  providerIdentifier: any
) {
  const rawProviderIdentifier = providerIdentifier || identifier;
  const isEPG = providerIdentifiers.matches(
    rawProviderIdentifier,
    providerIdentifiers.EPG
  );

  return isEPG ? providerIdentifiers.EPG : rawProviderIdentifier;
}

export default normalizeProviderDataIdentifier;
