/* eslint-plugin-disable @typescript-eslint */

import * as adapters from 'app/js/adapters/adapters';
import config from 'app/js/models/config';
import getUserRequestParams from 'app/js/utils/getUserRequestParams';
import path from 'app/js/utils/url/path';
import getUserAPIPromise from 'app/js/utils/user/getUserAPIPromise';

function authenticateWithServerCommand(options: any) {
  const homeUser = options.homeUser;
  const appModel = adapters.resolve('appModel');
  const user = appModel.get('user');
  const uuID = homeUser.get('uuid') || user.get('uuid');
  const params = getUserRequestParams(user, { pin: options.pin });

  // Send credentials with the request so plex.tv can update its session to
  // reflect the switched to user, if the app is configured to share auth.
  const url = path.join(['/api/v2/home/users/', uuID, '/switch'], params);
  const xhrOptions = homeUser.server.getXHROptions(url, {
    type: 'POST',
    xhrFields: { withCredentials: config.allowSSO },
  });

  return getUserAPIPromise(xhrOptions);
}

export default authenticateWithServerCommand;
