/* eslint-plugin-disable @typescript-eslint */

import config from 'app/js/models/config';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import * as textFormats from 'app/js/utils/constants/textFormats';

function getProviderTextFormat(providerIdentifier: any) {
  if (!config.shouldRequestHTMLFromCloudProvider) {
    return textFormats.PLAIN;
  }

  const htmlProviders = [
    providerIdentifiers.PODCASTS,
    providerIdentifiers.WEBSHOWS,
  ];

  if (providerIdentifiers.matches(providerIdentifier, htmlProviders)) {
    return textFormats.HTML;
  }

  return textFormats.PLAIN;
}

export default getProviderTextFormat;
