/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';

const ActivityModel = AbstractModel.extend({
  //
  // Properties

  name: 'ActivityModel',
  context: ['server'],

  idAttribute: 'uuid',
  rootPropertyName: 'Activity',

  responseType() {
    return getSupportedResponseType(this.server);
  },

  sessionAttrs: {
    synthetic: attrTypes.boolean,
  },

  syncedAttrs: {
    uuid: attrTypes.string,
    userID: attrTypes.integer,
    title: attrTypes.string,
    subtitle: attrTypes.string,
    progress: attrTypes.integer,
    event: attrTypes.string,
    cancellable: attrTypes.boolean,
    type: attrTypes.string,
    Context: attrTypes.object,
    Response: attrTypes.object,
  },

  defaults() {
    return {
      Context: {},
      Response: {},
    };
  },
});

export default ActivityModel;
