//
// Helpers

function trackAction(
  action: any,
  metadataItemModel: any,
  page: any,
  options = {}
) {
  if (!page) {
    return;
  }

  resolve('metrics').trackClick({
    page,
    action,
    type: metadataItemModel.get('type'),
    ...options,
  });
}

//
// Action Handlers

export const metadataActionHandlers = {
  [SELECT_METADATA_ITEM_STREAM](appModel: any, payload: any, dispatch: any) {
    const {
      mediaIndex,
      streamTypeString,
      streamID,
      allParts,
      metricsPage,
      updateLocalOnly,
    } = payload;

    const metadataItemModel = findMetadataItemModel(appModel, payload);

    const options = {
      mediaIndex,
      streamTypeString,
      streamID,
      allParts,
      allowMetrics: !!metricsPage,
      metricsProperties: { page: metricsPage },
      updateLocalOnly,
    };

    selectMetadataItemStreamHelper(metadataItemModel, options).fail((xhr) => {
      if (xhr && xhr.aborting) {
        // The request was aborted because a newer select stream request was made.
        return;
      }

      dispatch(showAlert(t('There was a problem selecting this stream.')));
    });
  },

  [REPOPULATE_METADATA_ITEM](appModel: any, payload: any) {
    const metadataItemModel = tryFindMetadataItemModel(appModel, payload);

    if (metadataItemModel) {
      // Pass the `skipRefresh` param so on-the-fly media analysis does not
      // happen for items while a scan is in progress. This helps to avoid
      // overwhelming low-powered servers.

      metadataItemModel.fetch({
        data: { skipRefresh: 1 },
        usePopulatedParams: true,
      });
    }
  },

  [REMOVE_METADATA_ITEM](appModel: any, payload: any) {
    const metadataItemModel = tryFindMetadataItemModel(appModel, payload);

    if (metadataItemModel) {
      metadataItemModel.trigger(
        'destroy',
        metadataItemModel,
        metadataItemModel.collection
      );
    }
  },

  [TOGGLE_METADATA_ITEM_SAVE](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const providerModel = findProviderModel(appModel, {
      providerEntityID: metadataItemModel.get('providerEntityID'),
    });

    const providerIdentifier = providerModel.get('identifier');
    const isMusicProvider = providerIdentifiers.matches(
      providerIdentifier,
      providerIdentifiers.MUSIC
    );

    let ratingKey = metadataItemModel.get('ratingKey');

    const grandparentRatingKey = metadataItemModel.get('grandparentRatingKey');
    let metadataItemGrandparentModel: any;

    if (grandparentRatingKey && !isMusicProvider) {
      ratingKey = grandparentRatingKey;

      const grandparentKey = metadataItemModel.get('grandparentKey');

      if (grandparentKey) {
        const metadataModelGrandparentEntityID = createEntityID(
          entityTypes.METADATA_ITEM,
          grandparentKey,
          payload.serverEntityID
        );

        metadataItemGrandparentModel = tryFindMetadataItemModel(appModel, {
          metadataItemEntityID: metadataModelGrandparentEntityID,
        });
      }
    }

    const currentSavedStatus = metadataItemModel.get('saved');
    const nextSavedStatus = !currentSavedStatus;
    metadataItemModel.set('saved', nextSavedStatus);

    if (metadataItemGrandparentModel) {
      metadataItemGrandparentModel.set('saved', nextSavedStatus);
    }

    const action = providerModel
      .get('features')
      .get(providerFeatureTypes.ACTIONS)
      .get('actions')
      .get('save');
    const key = nextSavedStatus ? action.get('key') : action.get('reverseKey');
    const url = path.join(key, { ratingKey });
    const xhrOptions = providerModel.getXHROptions(url, {
      type: 'PUT',
    });

    $.ajax(xhrOptions).then(
      () => {
        dispatch(repopulatePage({ serverEntityID: payload.serverEntityID }));
      },
      () => {
        metadataItemModel.set('saved', currentSavedStatus);

        if (metadataItemGrandparentModel) {
          metadataItemGrandparentModel.set('saved', currentSavedStatus);
        }
      }
    );
  },

  [TOGGLE_METADATA_ITEM_PICK](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const providerModel = findProviderModel(appModel, {
      providerEntityID: metadataItemModel.get('providerEntityID'),
    });

    const ratingKey = metadataItemModel.get('ratingKey');
    const currentPicked = metadataItemModel.get('picked');
    const nextPicked = !currentPicked;

    metadataItemModel.set('picked', nextPicked);

    const action = providerModel
      .get('features')
      .get(providerFeatureTypes.ACTIONS)
      .get('actions')
      .get('pick');
    const key = nextPicked ? action.get('key') : action.get('reverseKey');
    const url = path.join(key, { ratingKey });
    const xhrOptions = providerModel.getXHROptions(url, {
      type: 'PUT',
    });

    $.ajax(xhrOptions).then(
      () => {
        dispatch(
          repopulateMetadataItem({
            metadataItemEntityID: payload.metadataItemEntityID,
          })
        );
      },
      (response) => {
        const json = parseXML(response.responseText);
        dispatch(showAlert(json.message));
        metadataItemModel.set('picked', currentPicked);
      }
    );
  },

  [TOGGLE_METADATA_ITEM_WATCHLIST](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const providerModel = findProviderModel(appModel, {
      providerEntityID: metadataItemModel.get('providerEntityID'),
    });

    const ratingKey = metadataItemModel.get('ratingKey');
    const currentWatchlistedAt = metadataItemModel.get('watchlistedAt');
    const nextWatchlistedAt = !currentWatchlistedAt ? Date.now() : undefined;

    metadataItemModel.set('watchlistedAt', nextWatchlistedAt);

    const action = providerModel
      .get('features')
      .get(providerFeatureTypes.ACTIONS)
      .get('actions')
      .get('addToWatchlist');
    const key = nextWatchlistedAt
      ? action.get('key')
      : action.get('reverseKey');
    const url = path.join(key, { ratingKey });
    const xhrOptions = providerModel.getXHROptions(url, {
      type: 'PUT',
    });

    $.ajax(xhrOptions).then(
      () => {
        if (
          payload.metricsPage === metricsPages.SOURCE &&
          payload.metricsProperties.context.includes(metricsContexts.LIBRARY)
        ) {
          dispatch(repopulatePage({ serverEntityID: payload.serverEntityID }));
        } else {
          dispatch(
            repopulateMetadataItem({
              metadataItemEntityID: payload.metadataItemEntityID,
            })
          );
        }
      },
      (response) => {
        const json = parseXML(response.responseText);
        dispatch(showAlert(json.message));
        metadataItemModel.set('watchlistedAt', currentWatchlistedAt);
      }
    );
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [ADD_METADATA_ITEM_TO_LIBRARY](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const destinationServerModel = findServerModel(appModel, payload);
    const providerModel = findProviderModel(appModel, {
      providerEntityID: metadataItemModel.get('providerEntityID'),
    });
    const libraryID = payload.sectionKey.match(/\/library\/sections\/(\w+)/)[1];

    const params = {
      providers: `provider://${providerModel.get('identifier')}`,
      prefs: {
        oneShot: 1,
        remoteMedia: 1,
      },
      params: {
        // We only support adding to music libraries
        libraryType: sectionTypes.get(MUSIC).typeID,
      },
      targetLibrarySectionID: libraryID,
      hints: {
        guid: metadataItemModel.get('guid'),
        ratingKey: metadataItemModel.get('ratingKey'),
        title: metadataItemModel.get('title'),
        parentTitle: metadataItemModel.get('parentTitle'),
        // See https://github.com/plexinc/plex-media-server/issues/9975
        // for why we don't yet send grandparentTitle
      },
      type: getMetadataType(metadataItemModel.get('type')).id,
    };

    const xhrOptions = destinationServerModel.getXHROptions(
      path.join('/media/subscriptions', params),
      {
        type: 'POST',
      }
    );
    $.ajax(xhrOptions);
  },

  [DELETE_METADATA_ITEM_STREAM](appModel: any, payload: any, dispatch: any) {
    const { streamKey, metadataItemEntityID } = payload;

    const metadataItemModel = findMetadataItemModel(appModel, payload);

    scheduleImmediateUpdate(() => {
      metadataItemModel.set('isStreamDeleteInProgress', true);
    });

    const xhrOptions = metadataItemModel.server.getXHROptions(streamKey, {
      type: 'DELETE',
    });

    $.ajax(xhrOptions).then(
      () => {
        metadataItemModel.set('isStreamDeleteInProgress', false);
        dispatch(repopulateMetadataItem({ metadataItemEntityID }));
      },
      // @ts-expect-error ts-migrate(6133) FIXME: 'response' is declared but its value is never read... Remove this comment to see the full error message
      (response) => {
        metadataItemModel.set('isStreamDeleteInProgress', false);
        dispatch(showAlert(t('There was a problem deleting this stream.')));
      }
    );
  },

  [SET_METADATA_ITEM_STREAM_OFFSET](
    appModel: any,
    payload: any,
    dispatch: any
  ) {
    const {
      mediaIndex,
      streamTypeString,
      streamID,
      allParts,
      metricsPage,
      updateLocalOnly,
    } = payload;

    const metadataItemModel = findMetadataItemModel(appModel, payload);

    const options = {
      mediaIndex,
      streamTypeString,
      streamID,
      allParts,
      allowMetrics: !!metricsPage,
      metricsProperties: { page: metricsPage },
      updateLocalOnly,
    };

    selectMetadataItemStreamHelper(metadataItemModel, options).fail((xhr) => {
      if (xhr && xhr.aborting) {
        // The request was aborted because a newer select stream request was made.
        return;
      }

      dispatch(showAlert(t('There was a problem selecting this stream.')));
    });
  },

  [SET_METADATA_ITEM_IS_IN_UNIVERSAL_WATCHLIST](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    metadataItemModel.set('isInUniversalWatchlist', true);
  },

  [REDISPATCH_LEGACY_METADATA_ACTION](
    appModel: any,
    payload: any,
    dispatch: any
  ) {
    const {
      serverModel,
      providerModel,
      itemServerModel,
      itemProviderModel,
      metadataItemModel,
    } = getLegacyActionModels(appModel, payload);

    const legacyType = payload.legacyAction.type;
    const legacyPayload = payload.legacyAction.payload;
    const legacyMetricsProperties = legacyPayload?.metricsProperties || {};

    dispatch({
      type: legacyType,
      payload: {
        ...legacyPayload,
        serverEntityID: serverModel.entityID(),
        providerEntityID: providerModel.entityID(),
        itemServerEntityID: itemServerModel ? itemServerModel.entityID() : null,
        itemProviderEntityID: itemProviderModel
          ? itemProviderModel.entityID()
          : null,
        metadataItemEntityID: metadataItemModel.entityID(),
        metricsPage: payload.metricsPage,
        metricsProperties: {
          ...legacyMetricsProperties,
          ...payload.metricsProperties,
        },
        skipClickMetrics: true,
      },
    });
  },

  [ANALYZE_METADATA_ITEM](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const metricsPage = payload.metricsPage;

    const options = {
      allowMetrics: !!metricsPage,
      metricsProperties: { page: metricsPage },
    };

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    analyzeMetadataItemHelper(metadataItemModel, options).fail((xhr) => {
      dispatch(showAlert(t('There was a problem analyzing this item.')));
    });
  },

  [DOWNLOAD_METADATA_ITEM_VERSION](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const modal = new DownloadVersionModal({
      model: metadataItemModel,
    });

    dispatcher.trigger('show:modal', modal);
  },

  [DELETE_METADATA_ITEM](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const options = payload.options || {};

    const command = metadataItemModel.get('isPlaylist')
      ? 'deletePlaylist'
      : 'deleteItems';

    commands.execute(command, {
      item: metadataItemModel,
      ...options,
    });

    trackAction(metricsActions.DELETE, metadataItemModel, payload.metricsPage);
  },

  [DELETE_METADATA_ITEM_VERSION](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    commands.execute('deleteVersions', { item: metadataItemModel });

    trackAction(metricsActions.DELETE, metadataItemModel, payload.metricsPage);
  },

  [EDIT_METADATA_ITEM]() {
    __DEV__ &&
      window.console.warn(
        'EDIT_METADATA_ITEM action handler requires app-specific implementation'
      );
  },

  [FIX_METADATA_ITEM_MATCH]() {},

  [GET_METADATA_ITEM_INFO](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const modal = new MediaInfoModal({
      model: metadataItemModel,
    });

    dispatcher.trigger('show:modal', modal);

    trackAction(
      metricsActions.GET_INFO,
      metadataItemModel,
      payload.metricsPage
    );
  },

  [UPDATE_METADATA_ITEM_PLAYED_STATUS](
    appModel: any,
    payload: any,
    dispatch: any
  ) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    const promise = updatePlayedStatusHelper(appModel, payload);

    promise.then(
      () => {
        dispatch(repopulatePage({ serverEntityID: payload.serverEntityID }));
      },
      () => {
        const message = payload.played
          ? t('There was an unexpected error marking an item played.')
          : t('There was an unexpected error marking an item unplayed.');

        dispatch(showAlert(message));
      }
    );

    const metricsAction = payload.played
      ? metricsActions.MARK_AS_WATCHED
      : metricsActions.MARK_AS_UNWATCHED;

    trackAction(metricsAction, metadataItemModel, payload.metricsPage);
  },

  [OPTIMIZE_METADATA_ITEM](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const modal = new ConvertModal({
      item: metadataItemModel,
    });

    dispatcher.trigger('show:modal', modal);

    trackAction(
      metricsActions.OPTIMIZE,
      metadataItemModel,
      payload.metricsPage
    );
  },

  [RATE_METADATA_ITEM](appModel: any, payload: any, dispatch: any) {
    const { rating, metricsPage } = payload;

    const providerModel = findProviderModel(appModel, payload);
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    const options = {
      rating,
      allowMetrics: !!metricsPage,
      metricsProperties: { page: metricsPage },
    };

    rateMetadataItemHelper(providerModel, metadataItemModel, options).fail(
      // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
      (xhr) => {
        dispatch(showAlert(t('There was a problem rating this item.')));
      }
    );
  },

  [REFRESH_METADATA_ITEM](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);
    const metricsPage = payload.metricsPage;

    const options = {
      allowMetrics: !!metricsPage,
      metricsProperties: { page: metricsPage },
    };

    refreshMetadataItemHelper(metadataItemModel, options).fail((xhr) => {
      if (xhr.status === 503) {
        dispatcher.trigger(
          'show:modal',
          new LibraryFailureModal({ model: metadataItemModel.server })
        );
      } else {
        dispatch(showAlert(t('There was a problem refreshing this item.')));
      }
    });
  },

  [SPLIT_METADATA_ITEM_APART](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    commands
      .execute('splitApart', { item: metadataItemModel, skipNavigate: true })
      .done(() => {
        dispatch(repopulatePage({ serverEntityID: payload.serverEntityID }));
      });

    trackAction(
      metricsActions.SPLIT_APART,
      metadataItemModel,
      payload.metricsPage
    );
  },

  [UNMATCH_METADATA_ITEM](appModel: any, payload: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    commands.execute('unmatch', { item: metadataItemModel });

    trackAction(metricsActions.UNMATCH, metadataItemModel, payload.metricsPage);
  },

  [CHANGE_METADATA_ITEM_FIELD](appModel: any, payload: any, dispatch: any) {
    const { providerEntityID, metadataItemEntityID, name, value, save } =
      payload;

    const metadataItemModel = findMetadataItemModel(appModel, {
      metadataItemEntityID,
    });

    if (!save) {
      metadataItemModel.set({ [name]: value });
      return;
    }

    const providerModel = findProviderModel(appModel, { providerEntityID });
    const url = path.join(metadataItemModel.get('key'), {
      [name]: value,
      [`${name}.locked`]: 1,
    });
    const xhrOptions = providerModel.getXHROptions(url, { type: 'PUT' });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    $.ajax(xhrOptions).fail((xhr) => {
      dispatch(showAlert(t('There was a problem editing this item.')));
    });

    trackAction(metricsActions.EDIT, metadataItemModel, payload.metricsPage);
  },

  [UPDATE_METADATA_ITEM_REFRESHING_STATE](appModel: any, payload: any) {
    const metadataItemModel = tryFindMetadataItemModel(appModel, payload);

    if (metadataItemModel) {
      metadataItemModel.set({
        metadataState: payload.metadataState,
        mediaState: payload.mediaState,
      });
    }
  },

  [UPDATE_METADATA_SETTINGS](appModel: any, payload: any, dispatch: any) {
    const providerModel = findProviderModel(appModel, payload);
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    const { data } = payload;

    updateMetadataSettingsHelper(providerModel, metadataItemModel, data).then(
      () => {
        dispatch(
          repopulateMetadataItem({
            serverEntityID: payload.serverEntityID,
            metadataItemEntityID: payload.metadataItemEntityID,
          })
        );
      }
    );
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [CLEAR_METADATA_SETTINGS_ERROR](appModel: any, payload: any, dispatch: any) {
    const metadataItemModel = findMetadataItemModel(appModel, payload);

    metadataItemModel.set('settingsUpdateError', null);
  },

  [UPDATE_DOWNLOAD_AVAILABILITY]() {
    __DEV__ &&
      window.console.warn(
        'UPDATE_DOWNLOAD_AVAILABILITY action handler requires app-specific implementation'
      );
  },

  [UPDATE_DOWNLOAD_SUBSCRIPTION_IDS]() {
    __DEV__ &&
      window.console.warn(
        'UPDATE_DOWNLOAD_SUBSCRIPTION_IDS action handler requires app-specific implementation'
      );
  },

  [MODIFY_SMART_PLAYLIST]() {
    __DEV__ &&
      window.console.warn(
        'MODIFY_SMART_PLAYLIST action handler requires app-specific implementation'
      );
  },
};
