/* eslint-plugin-disable @typescript-eslint */

import { AnimatePresence, motion } from 'framer-motion';
import React from 'react';
import PopperPortal from 'app/js/views/v3/components/core/base/PopperPortal';
import cssDurationToSeconds from 'app/js/views/v3/helpers/css/cssDurationToSeconds';
import * as tooltipAttachments from 'app/js/views/v3/helpers/props/tooltipAttachments';
import animations from 'app/styles/variables/animations';
import easings from 'app/styles/variables/easings';
import styles from './Tooltip.css';

const tooltipSpeedSeconds = cssDurationToSeconds(animations.tooltipSpeed);
const tooltipSpeedMilliseconds = tooltipSpeedSeconds * 1000;

const tooltipTransition = {
  opacity: {
    ease: 'easeOut',
    duration: tooltipSpeedSeconds,
  },
  y: {
    ease: easings.motionEasing,
    duration: tooltipSpeedSeconds,
  },
  x: {
    ease: easings.motionEasing,
    duration: tooltipSpeedSeconds,
  },
  scale: {
    ease: easings.motionEasing,
    duration: tooltipSpeedSeconds,
  },
};

function tooltipBeforeY(attachment: string) {
  switch (attachment) {
    case tooltipAttachments.TOP_LEFT:
    case tooltipAttachments.TOP_CENTER:
    case tooltipAttachments.TOP_RIGHT:
      return 5;
    case tooltipAttachments.BOTTOM_LEFT:
    case tooltipAttachments.BOTTOM_CENTER:
    case tooltipAttachments.BOTTOM_RIGHT:
      return -5;
    case tooltipAttachments.MIDDLE_LEFT:
    case tooltipAttachments.MIDDLE_RIGHT:
      return 0;
    default:
      return 0;
  }
}

function tooltipBeforeX(attachment: string) {
  switch (attachment) {
    case tooltipAttachments.TOP_LEFT:
    case tooltipAttachments.TOP_CENTER:
    case tooltipAttachments.TOP_RIGHT:
      return 0;
    case tooltipAttachments.BOTTOM_LEFT:
    case tooltipAttachments.BOTTOM_CENTER:
    case tooltipAttachments.BOTTOM_RIGHT:
      return 0;
    case tooltipAttachments.MIDDLE_LEFT:
      return 5;
    case tooltipAttachments.MIDDLE_RIGHT:
      return -5;
    default:
      return 0;
  }
}

function tooltipExitY(attachment: string) {
  switch (attachment) {
    case tooltipAttachments.TOP_LEFT:
    case tooltipAttachments.TOP_CENTER:
    case tooltipAttachments.TOP_RIGHT:
      return -5;
    case tooltipAttachments.BOTTOM_LEFT:
    case tooltipAttachments.BOTTOM_CENTER:
    case tooltipAttachments.BOTTOM_RIGHT:
      return 5;
    case tooltipAttachments.MIDDLE_LEFT:
    case tooltipAttachments.MIDDLE_RIGHT:
      return 0;
    default:
      return 0;
  }
}

function tooltipExitX(attachment: string) {
  switch (attachment) {
    case tooltipAttachments.TOP_LEFT:
    case tooltipAttachments.TOP_CENTER:
    case tooltipAttachments.TOP_RIGHT:
      return 0;
    case tooltipAttachments.BOTTOM_LEFT:
    case tooltipAttachments.BOTTOM_CENTER:
    case tooltipAttachments.BOTTOM_RIGHT:
      return 0;
    case tooltipAttachments.MIDDLE_LEFT:
      return -5;
    case tooltipAttachments.MIDDLE_RIGHT:
      return 5;
    default:
      return 0;
  }
}

const tooltipContainerVariants = {
  before: (attachment: string) => ({
    opacity: 0,
    y: tooltipBeforeY(attachment),
    x: tooltipBeforeX(attachment),
    scale: 0.95,
    transition: tooltipTransition,
  }),
  enter: {
    opacity: 1,
    y: 0,
    // When a value of 0 is used posed uses a `none` value which breaks the
    // position of the arrow
    x: 0.001,
    scale: 1,
    transition: tooltipTransition,
  },
  exit: (attachment: string) => ({
    opacity: 0,
    y: tooltipExitY(attachment),
    x: tooltipExitX(attachment),
    scale: 0.95,
    transition: tooltipTransition,
  }),
};

const popperOptions = {
  [tooltipAttachments.TOP_LEFT]: {
    placement: 'top-start',
  },
  [tooltipAttachments.TOP_CENTER]: {
    placement: 'top',
  },
  [tooltipAttachments.TOP_RIGHT]: {
    placement: 'top-end',
  },
  [tooltipAttachments.BOTTOM_LEFT]: {
    placement: 'bottom-start',
  },
  [tooltipAttachments.BOTTOM_CENTER]: {
    placement: 'bottom',
  },
  [tooltipAttachments.BOTTOM_RIGHT]: {
    placement: 'bottom-end',
  },
  [tooltipAttachments.MIDDLE_LEFT]: {
    placement: 'left',
  },
  [tooltipAttachments.MIDDLE_RIGHT]: {
    placement: 'right',
  },
};

interface TooltipProps {
  children?: React.ReactNode;
  className?: string;
  target: string;
  attachment?: string;
  options?: any;
  isOpen: boolean;
  onMenuClose?: (...args: any[]) => any;
}

function Tooltip(props: TooltipProps) {
  const {
    children,
    className,
    target,
    attachment,
    options,
    isOpen,
    onMenuClose,
    ...otherProps
  } = props;

  return (
    // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; className: string; targ... Remove this comment to see the full error message
    <PopperPortal
      className={styles.tooltipPortal}
      closeTimeoutMilliseconds={tooltipSpeedMilliseconds}
      isOpen={isOpen}
      // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
      options={options || popperOptions[attachment]}
      target={target}
    >
      <AnimatePresence>
        {isOpen ? (
          <motion.div
            key="container"
            animate="enter"
            // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
            className={className || styles[attachment]}
            custom={attachment}
            exit="exit"
            initial="before"
            variants={tooltipContainerVariants}
            {...otherProps}
          >
            {children}
          </motion.div>
        ) : null}
      </AnimatePresence>
    </PopperPortal>
  );
}

Tooltip.defaultProps = {
  attachment: tooltipAttachments.BOTTOM_CENTER,
};

export default Tooltip;
