/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import config from 'app/js/models/config';
import AbstractStorageModel from 'app/js/models/storage/AbstractStorageModel';
import castAppIDs from 'app/js/utils/constants/castAppIDs';
import debugAdKinds from 'app/js/utils/constants/debugAdKinds';
import debugLevels from 'app/js/utils/constants/debugLevels';

const DeviceSettingsModel = AbstractStorageModel.extend({
  //
  // Properties

  name: 'DeviceSettingsModel',
  storageKey: 'deviceSettings',

  sessionAttrs: {
    isSaving: attrTypes.boolean,
    saveError: attrTypes.object,
  },

  syncedAttrs: {
    chromecastAppID: attrTypes.string,
    chromecastCustomAppID: attrTypes.string,
    sonosCustomBaseURL: attrTypes.string,
    debugAdKind: attrTypes.integer,
    debugLevel: attrTypes.integer,
    debugVideoQualityOverlay: attrTypes.boolean,
    directPlay: attrTypes.boolean,
    directStream: attrTypes.boolean,
    rejectDirectConnections: attrTypes.boolean,
    restorePlayQueue: attrTypes.object,
    shouldSyncSettings: attrTypes.boolean,
    hasDismissedServerDownloadMessage: attrTypes.boolean,
    dismissedUnclaimedServers: attrTypes.object,
    dismissedServerUpdateVersions: attrTypes.object,
    volume: attrTypes.integer,

    // Deprecated. Once dismissedServerUpdateVersions is included in a public
    // bundled PMS release, then after the following public PMS release we can
    // remove serverUpdateDismissedVersion and its usages.
    serverUpdateDismissedVersion: attrTypes.string,
  },

  defaults() {
    return {
      isSaving: false,
      saveError: null,

      chromecastAppID: castAppIDs.PRODUCTION,
      debugAdKind: debugAdKinds.NONE,
      debugLevel: debugLevels.DISABLED,
      debugVideoQualityOverlay: false,
      directPlay: true,
      directStream: true,
      rejectDirectConnections: false,
      restorePlayQueue: null,
      shouldSyncSettings: config.allowSyncSettings,
      hasDismissedServerDownloadMessage: false,
      dismissedUnclaimedServers: {},
      dismissedServerUpdateVersions: {},
      volume: 90,
    };
  },
});

export default DeviceSettingsModel;
