/* eslint-plugin-disable @typescript-eslint */

import BaseCollection from 'app/js/models/BaseCollection';

const PaginatedCollection = BaseCollection.extend({
  //
  // Properties

  pageSize: 10,

  //
  // Control

  pages: function () {
    return Math.max(Math.ceil(this.length / this.pageSize), 1);
  },

  page: function (i: any, options: any) {
    options || (options = {});

    let offset = 0;
    let start;

    if (options.reverse) {
      start = this.length - i * this.pageSize;
    } else {
      start = i * this.pageSize - this.pageSize;
    }

    if (start < 0) {
      offset = start;
      start = 0;
    }

    return this.models.slice(start, start + this.pageSize + offset);
  },
});

export default PaginatedCollection;
