/* eslint-plugin-disable @typescript-eslint */

import cloneDeep from 'lodash/cloneDeep';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import getIdentifiersFromSource from 'app/js/utils/metadata/getIdentifiersFromSource';
import {
  findProviderModel,
  findServerModel,
} from 'app/js/utils/state/modelFinders';

export default function getLegacyActionModels(appModel: any, payload: any) {
  const serverModel = findServerModel(appModel, {
    serverEntityID: payload.server.entityID,
  });
  const providerModel = findProviderModel(appModel, {
    providerEntityID: payload.provider.entityID,
  });
  let itemServerModel;
  let itemProviderModel;
  if (payload.itemServer) {
    itemServerModel = findServerModel(appModel, {
      serverEntityID: payload.itemServer.entityID,
    });
  }
  if (payload.itemProvider) {
    itemProviderModel = findProviderModel(appModel, {
      providerEntityID: payload.itemProvider.entityID,
    });
  }
  const legacyMetadataItem = cloneDeep({
    ...payload.metadataItem,
    ...payload.metadataItemState,
  });
  // V4 metadata items normalize the source to always exist, but V3 actions may
  // get confused by the presence of a source that is the same as the
  // container's. Strip the normalized source in that case.
  if (legacyMetadataItem.source) {
    const {
      serverIdentifier: sourceServerIdentifier,
      providerIdentifier: sourceProviderIdentifier,
    } = getIdentifiersFromSource(legacyMetadataItem.source);
    if (
      sourceServerIdentifier === payload.server.machineIdentifier &&
      sourceProviderIdentifier === payload.provider.identifier
    ) {
      delete legacyMetadataItem.source;
    }
  }
  const metadataItemModel = new MetadataModel(legacyMetadataItem, {
    server: serverModel,
    provider: providerModel,
    identifier: payload.provider.identifier,
    parse: true,
  });
  // Some redispatched actions call `REPOPULATE_METADATA_ITEM` which fetches
  // the item with `usePopulatedParams`. In order to make sure we get back the
  // same data do checks to see if this data was requested, and add them to
  // `_populatedParams` so they are not missing after the fetch.
  if (metadataItemModel._populatedParams == null) {
    metadataItemModel._populatedParams = {};
  }
  const includeParams = {
    includeExternalMedia: 1,
  };
  if (legacyMetadataItem.preferences) {
    (includeParams as any).includePreferences = 1;
  }
  if (legacyMetadataItem.extras) {
    (includeParams as any).includeExtras = 1;
  }
  if (legacyMetadataItem.stations) {
    (includeParams as any).includeStations = 1;
  }
  if (legacyMetadataItem.onDeck) {
    (includeParams as any).includeOnDeck = 1;
  }
  if (legacyMetadataItem.popularLeaves) {
    (includeParams as any).includePopularLeaves = 1;
  }
  // The following are not currently typed in Typescript so we pass though the
  // raw API attribute
  if (legacyMetadataItem.Concert) {
    (includeParams as any).includeConcerts = 1;
  }
  if (legacyMetadataItem.Chapter) {
    (includeParams as any).includeChapters = 1;
  }
  Object.assign(metadataItemModel._populatedParams, includeParams);
  return {
    serverModel,
    providerModel,
    itemServerModel,
    itemProviderModel,
    metadataItemModel,
  };
}
