/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';

function createPIN(appModel: any) {
  const cloudServer = appModel.get('cloudServer');

  return (
    $.ajax(
      cloudServer.getXHROptions('/api/v2/pins', {
        method: 'POST',
        dataType: 'json',
        data: {
          strong: true,
        },
      })
    )
      // Strip XHR from promise resolution so consumers only receive JSON.
      .then((response) => response)
  );
}

export default createPIN;
