/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import commands from 'app/js/commands/commands';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';
import SelectMediaFileModal from 'app/js/views/modals/SelectMediaFileModal';

function deleteMediaItemCommand(options: any) {
  let promise = selectMediaItem(options);

  promise = promise.then(function (mediaItem) {
    const confirmModal = showConfirmation();

    const deletePromise = confirmModal.promise.then(function () {
      return commands
        .execute('deleteFile', { item: options.item, media: mediaItem })
        .always(function () {
          confirmModal.hide();
        });
    });

    return deletePromise;
  });

  return promise;
}

function selectMediaItem(options: any) {
  const deferred = $.Deferred();
  const item = options.item;
  const mediaItems = item.get('Media');

  // If an explicit index is passed in, we don't need to show a modal to
  // select a media item.
  if (options.index in mediaItems) {
    return deferred.resolve(mediaItems[options.index]).promise();
  }

  const mediaModal = new SelectMediaFileModal({ model: item });
  const mediaPromise = mediaModal.promise;

  mediaPromise.done(function (mediaIndex: any) {
    deferred.resolve(mediaItems[mediaIndex]);
  });

  mediaPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', mediaModal);

  return deferred.promise();
}

function showConfirmation() {
  const confirmModal = new ConfirmModal({
    title: t('Delete File'),
    message: t(
      'Are you sure you want to delete this file from your filesystem?'
    ),
    confirmLabel: t('Delete'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  dispatcher.trigger('show:modal', confirmModal);

  return confirmModal;
}

export default deleteMediaItemCommand;
