/* eslint-plugin-disable @typescript-eslint */

import UserExperienceSettingsModel from 'app/js/models/user/UserExperienceSettingsModel';
import {
  DEPRECATED_NONE,
  NORMALIZED,
  UNNORMALIZED,
} from 'app/js/utils/constants/audioBoostLevels';

export default function migrateAudioBoost(userExperienceSettings: any) {
  const { audioBoost } = userExperienceSettings;

  if (audioBoost != null) {
    const nextAudioBoost =
      audioBoost === DEPRECATED_NONE ? NORMALIZED : UNNORMALIZED;

    const defaults = UserExperienceSettingsModel.prototype.defaults();
    const defaultAudioBoost = defaults.audioBoost;

    if (nextAudioBoost !== audioBoost && nextAudioBoost !== defaultAudioBoost) {
      userExperienceSettings.audioBoost = nextAudioBoost;
    }
  }

  return userExperienceSettings;
}
