/* eslint-plugin-disable @typescript-eslint */

import languages from './languages';

// Compares an array of language and region strings against known languages.
// If a language-region combination is unsupported the language-only value is
// examined as well.

const hyphenatedRegExp = /(^[^-]+)-/;

// @ts-expect-error ts-migrate(7023) FIXME: 'closestLanguage' implicitly has return type 'any'... Remove this comment to see the full error message
export default function closestLanguage(searchLanguages: any) {
  searchLanguages = searchLanguages || [];

  for (let i = 0, len = searchLanguages.length; i < len; i++) {
    const code = searchLanguages[i];

    if (code === 'xx' || code === 'xn') {
      continue;
    }

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    if (languages[code]) {
      return code;
    }

    const match = code.match(hyphenatedRegExp);
    // @ts-expect-error ts-migrate(7022) FIXME: 'codeFromMatch' implicitly has type 'any' because ... Remove this comment to see the full error message
    const codeFromMatch = match && closestLanguage([match[1]]);

    if (codeFromMatch) {
      return codeFromMatch;
    }
  }
}
