/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import PaginatedCollection from 'app/js/models/PaginatedCollection';
import ServerAlertModel from 'app/js/models/server/ServerAlertModel';

const ServerAlertCollection = PaginatedCollection.extend({
  //
  // Properties

  model: ServerAlertModel,
  url: '/servers',
  pageSize: 20,
  limit: 1000,

  //
  // Init

  initialize: function () {
    this.startTime = new Date();

    this.listenTo(this.server, 'socket:setting', this.onSettingChange);
    this.listenTo(this.server, 'socket:timeline', this.onTimeline);
    this.listenTo(this.server, 'socket:status', this.onStatus);
    this.listenTo(this.server, 'socket:playing', this.onPlaying);
    this.listenTo(this.server, 'socket:websocket', this.onWebSocket);
  },

  //
  // Control

  // @ts-expect-error ts-migrate(6133) FIXME: 'response' is declared but its value is never read... Remove this comment to see the full error message
  add: function (response: any) {
    // Don't let the size of the collection exceed the limit.
    if (this.length + 1 > this.limit) {
      this.remove(this.at(0));
    }

    // eslint-disable-next-line prefer-rest-params
    PaginatedCollection.prototype.add.apply(this, arguments);
  },

  //
  // Listeners

  onSettingChange: function (event: any) {
    if (event.label) {
      this.add(_.extend({ _type: 'setting' }, event), { parse: true });
    }
  },

  onTimeline: function (event: any) {
    // Do not add the progress timeline events to the collection. They are
    // unimportant and can overwhelm the notifications list.
    if (event.state === 1 || event.sectionID < 0) {
      return;
    }

    this.add(_.extend({ _type: 'timeline' }, event), { parse: true });
  },

  onStatus: function (event: any) {
    // Do not add the 'Scanning' messages to the collection. They are
    // unimportant and can overwhelm the notifications list.
    if (event._elementType === 'ProgressNotification') {
      return;
    }

    this.add(_.extend({ _type: 'status' }, event), { parse: true });
  },

  onPlaying: function (event: any) {
    this.add(_.extend({ _type: 'playing' }, event), { parse: true });
  },

  onWebSocket: function (event: any) {
    this.add(_.extend({ _type: 'websocket' }, event), { parse: true });
  },
});

export default ServerAlertCollection;
