import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { fetchPlans } from 'app/js/stores/actions/userActions';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import { getWWWPlexUrl } from 'app/js/utils/getRuntimeUrl';
import path from 'app/js/utils/url/path';
import PlexPassModalContent from './PlexPassModalContent';

function createMapStateToProps() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { origin }: any) => origin,
    selectUser,
    (origin, user) => {
      const userFlexState = user[__PLATFORM__];
      const subscribeParams = {
        utm_source: 'app.plex.tv',
        utm_medium: 'app',
        utm_campaign: 'PP_in-app-upsell_01',
      };
      if (origin) {
        (subscribeParams as any).utm_content = origin;
      }
      return {
        isSignedIn: user.isSignedIn,
        plans: userFlexState.plans,
        isPlansPending: userFlexState.isPlansPending,
        isPlansPopulated: userFlexState.isPlansPopulated,
        plansError: userFlexState.plansError,
        subscribeUrl: path.join(
          getWWWPlexUrl('/plex-pass/purchase'),
          subscribeParams
        ),
        infoUrl: getWWWPlexUrl('/plex-pass'),
      };
    }
  );
}
type PlexPassModalContentConnectorProps = {
  isSignedIn: boolean;
  fetchPlans: (...args: any[]) => any;
};
class PlexPassModalContentConnector extends Component<PlexPassModalContentConnectorProps> {
  //
  // Lifecycle
  UNSAFE_componentWillMount() {
    if (this.props.isSignedIn) {
      this.props.fetchPlans();
    }
  }
  UNSAFE_componentWillReceiveProps(
    nextProps: PlexPassModalContentConnectorProps
  ) {
    if (
      nextProps.isSignedIn &&
      nextProps.isSignedIn !== this.props.isSignedIn
    ) {
      nextProps.fetchPlans();
    }
  }
  //
  // Render
  render() {
    // @ts-expect-error ts-migrate(2740) FIXME: Type '{ isSignedIn: boolean; fetchPlans: (...args:... Remove this comment to see the full error message
    return <PlexPassModalContent {...this.props} />;
  }
}
export default connect(createMapStateToProps, { fetchPlans })(
  PlexPassModalContentConnector
);
