/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import EmptyPage, {
  EmptyPageProps,
} from 'app/js/views/v3/components/pages/empty/EmptyPage';
import * as scrollDirections from 'app/js/views/v3/helpers/props/scrollDirections';
import styles from 'app/js/views/v3/components/pages/empty/InnerEmptyPage.css';

// An empty page container that adjusts for the `PageHeader` and scrollbar. This
// keeps spinners and content consistently positioned.

type InnerEmptyPageProps = Omit<
  EmptyPageProps,
  'contentClassName' | 'scrollDirection'
>;

function InnerEmptyPage(props: InnerEmptyPageProps) {
  return (
    <EmptyPage
      className={styles.innerEmptyPage}
      contentClassName={styles.innerEmptyPageContent}
      scrollDirection={scrollDirections.NONE}
      {...props}
    />
  );
}

export default InnerEmptyPage;
