/* eslint-plugin-disable @typescript-eslint */

import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import renderComponentToString from 'app/js/utils/renderComponentToString';
import IconAdd from 'app/glyphs/add-560.svg';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/user/UserSelectModalAddUserListItem.tpl';

const UserSelectModalAddUserListItem = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'li',
  className: 'user-select-list-item',
  template: tpl,

  events: {
    'click a': 'onClick',
  },

  //
  // Init

  initialize(options: any) {
    this.disabled = options.disabled;
  },

  //
  // Control

  serializeData() {
    return {
      iconAdd: renderComponentToString(IconAdd, { className: 'add-icon' }),
      disabled: this.disabled,
    };
  },

  //
  // Listeners

  onClick(event: any) {
    event.preventDefault();

    this.trigger('selectUser', this.model, true);
  },
});

export default UserSelectModalAddUserListItem;
