import $ from 'jquery';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import SectionCollection from 'app/js/models/library/section/SectionCollection';
import dispatcher from 'app/js/utils/dispatcher';
import SectionCardList from 'app/js/views/lists/setup/SectionCardList';
import EditSectionModal from 'app/js/views/modals/EditSectionModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setup... Remove this comment to see the full error message
import tpl from 'app/templates/setup/SetupLibraryView.tpl';

const SetupLibraryView = Marionette.Layout.extend({
  //
  // Properties

  className: 'setup-library-container',
  template: tpl,

  regions: {
    list: '.setup-section-list-container',
  },

  events: {
    'click .add-section-btn': 'onAddSectionClick',
  },

  metricsValue: function () {
    return this.collection.size();
  },

  //
  // Init

  initialize: function (options: any) {
    const server = this.model;
    const serverSections = server.get('sections');

    const url = '/services/defaultLibraries';

    const defaultSections = new SectionCollection([], { server, url });

    this.collection = serverSections;

    if (
      this.model.supports('defaultSections') &&
      options.isFirstVisitToSubset
    ) {
      // If the server supports defaultSections, fetch the defaults.
      // After the real sections are populated, append the default sections.
      $.when(defaultSections.populate(), serverSections.populate()).done(() => {
        // Don't suggest default sections if server already has sections.
        if (serverSections.length) {
          return;
        }

        defaultSections.forEach((section: any) => {
          // Set the collection because it is needed when saving the
          // library. We cannot remove the section and then add it in an
          // each loop.

          section.collection = serverSections;
          serverSections.add(section);
        });
      });
    }
  },

  //
  // Listeners

  onRender: function () {
    this.list.show(new SectionCardList({ collection: this.collection }));
  },

  onAddSectionClick: function (event: any) {
    event.preventDefault();

    dispatcher.trigger(
      'show:modal',
      new EditSectionModal({ server: this.model })
    );
  },
});

export default SetupLibraryView;
