import $ from 'jquery';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import xhrTracker from 'app/js/utils/xhr/xhrTracker';

const populateMixin = {
  //
  // Properties
  isPopulating() {
    return (
      !!(this as any).populatePromise &&
      (this as any).populatePromise.state() === 'pending'
    );
  },
  isPopulated() {
    return (
      !!(this as any).populatePromise &&
      (this as any).populatePromise.state() === 'resolved'
    );
  },
  //
  // Control
  // A Deferred object useful for ensuring that the data has been fetched
  // without necessarily refetching it. If the model or collection was
  // previously fetched, populate will return immediately but could have
  // stale data. If fresh data is desired, the plain old fetch method can
  // still be used.
  populate() {
    if ((this as any).populatePromise) {
      return (this as any).populatePromise;
    }
    // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'IArguments' is not assignable to... Remove this comment to see the full error message
    return this.fetch.apply(this, arguments);
  },
  fetch() {
    const deferred = $.Deferred();
    let request: any;
    // Don't allow multiple pending fetch requests at once.
    if (this.isPopulating()) {
      return (this as any).populatePromise;
    }
    // Allow overriding fetch implementation in a way compatible with populateMixin
    // @ts-expect-error ts-migrate(2551) FIXME: Property '_fetch' does not exist on type '{ isPopu... Remove this comment to see the full error message
    if (this._fetch) {
      // @ts-expect-error ts-migrate(2551) FIXME: Property '_fetch' does not exist on type '{ isPopu... Remove this comment to see the full error message
      request = this._fetch.apply(this, arguments);
    } else if (this instanceof Backbone.Collection) {
      request = Backbone.Collection.prototype.fetch.apply(this, arguments);
    } else {
      request = Backbone.Model.prototype.fetch.apply(this, arguments);
    }
    const promise = deferred.promise();
    (promise as any).rid = request.rid;
    (this as any).populatePromise = promise;
    (this as any).failedPopulatePromise = null;
    request.done((response: any) => {
      // Free memory from link to response.
      (this as any).populatePromise = $.Deferred()
        .resolveWith(this, [this])
        .promise();
      // Pass the `responseText` to fetch handler callbacks for proper operation
      // of cached fetched handlers created by `createCachedFetchHandler`.
      deferred.resolveWith(this, [this, response, request]);
    });
    request.fail((xhr: any) => {
      // Reset the populate promise so `populate` attempts to `fetch` again.
      (this as any).populatePromise = null;
      (this as any).failedPopulatePromise = promise;
      deferred.rejectWith(this, [this, xhr]);
    });
    return promise;
  },
  abortPendingPopulate() {
    if (this.isPopulating()) {
      xhrTracker.abort((this as any).populatePromise.rid);
    }
    return (this as any).populatePromise;
  },
  resetPopulatePromise(promise: any) {
    if (!this.isPopulating()) {
      (this as any).populatePromise = promise;
      (this as any).failedPopulatePromise = null;
    }
    return (this as any).populatePromise;
  },
};
export default populateMixin;
