/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import SectionMetadataListModel from 'app/js/models/metadata/SectionMetadataListModel';
import ServerSettingCollection from 'app/js/models/server/ServerSettingCollection';
import ModelCache from 'app/js/utils/cache/ModelCache';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import { getRelatedTypes } from 'app/js/utils/types/metadataTypes';
import sectionTypes from 'app/js/utils/types/sectionTypes';
import subsetTypes from 'app/js/utils/types/subsetTypes';
import path from 'app/js/utils/url/path';
import FilterCollection from './FilterCollection';
import FirstCharacterCollection from './FirstCharacterCollection';
import FirstCharacterModel from './FirstCharacterModel';
import LinearFilterCollection from './LinearFilterCollection';
import LocationCollection from './LocationCollection';
import MediaTypeCollection from './MediaTypeCollection';
import MediaTypeModel from './MediaTypeModel';
import SectionSubsetCollection from './SectionSubsetCollection';
import SectionSubsetModel from './SectionSubsetModel';
import SortCollection from './SortCollection';
import SortModel from './SortModel';

const SectionModel = AbstractModel.extend({
  //
  // Properties
  name: 'SectionModel',
  context: ['server'],
  entityType: entityTypes.LIBRARY,
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    return createEntityID(this.entityType, state.key, this.server.entityID());
  },
  idAttribute: 'key',
  rootPropertyName: 'Directory',
  cache: new ModelCache(),
  persistInCache: true,
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'attributes' implicitly has an 'any' typ... Remove this comment to see the full error message
  cacheKey(attributes) {
    return this.server && this.server.getScopedKey(attributes.key);
  },
  url() {
    if (this.isNew()) {
      return _.result(this, 'urlRoot') || _.result(this.collection, 'url');
    }
    // If the section id has characters other than numbers (e.g. /sync/...),
    // assume the id represents the full path to the section.
    const isFullPath = _.isNaN(parseInt(this.id, 10));
    const url = isFullPath ? this.id : '/library/sections/' + this.id;
    return url;
  },
  sessionAttrs: {
    isSection: attrTypes.boolean,
    isCollectionsPopulated: attrTypes.boolean,
    currentFilters: attrTypes.array,
    currentFolderKey: attrTypes.string,
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
    settings: attrTypes.instanceOf(ServerSettingCollection),
  },
  syncedAttrs: {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 2.
    key: attrTypes.oneOfType(attrTypes.string, attrTypes.integer),
    uuid: attrTypes.string,
    thumb: attrTypes.string,
    art: attrTypes.string,
    createdAt: attrTypes.integer,
    updatedAt: attrTypes.integer,
    allowAdvancedFilters: attrTypes.boolean,
    allowSync: attrTypes.boolean,
    refreshing: attrTypes.boolean,
    name: {
      type: attrTypes.string,
      save: true,
    },
    title: {
      type: attrTypes.string,
      save: true,
    },
    type: {
      type: attrTypes.string,
      save: true,
    },
    agent: {
      type: attrTypes.string,
      save: true,
    },
    scanner: {
      type: attrTypes.string,
      save: true,
    },
    language: {
      type: attrTypes.string,
      save: true,
    },
    importFromiTunes: {
      type: attrTypes.boolean,
      save: true,
    },
    enableAutoPhotoTags: {
      type: attrTypes.boolean,
      save: true,
    },
    downloadMedia: {
      type: attrTypes.integer,
      save: true,
    },
  },
  derivedAttrs: {
    serverEntityID() {
      // The unit test environment doesn't create the state tree universally.
      // The following logic intends to only request an entityID from the server
      // context when the state tree is present. This is similar to checking
      // `config.shouldCreateStateTree` before querying for entityID.
      return (this as any).state ? (this as any).server.entityID() : '';
    },
  },
  syncedRelations: {
    locations() {
      return {
        relationClass: LocationCollection,
        rootPropertyName: 'Location',
        options: { section: this },
        save: true,
      };
    },
  },
  sessionRelations: {
    items() {
      return {
        relationClass: SectionMetadataListModel,
        options: { section: this },
      };
    },
    currentSubset() {
      return {
        relationClass: SectionSubsetModel,
        options: { section: this },
      };
    },
    currentFirstCharacter() {
      return {
        relationClass: FirstCharacterModel,
        deferred: true,
      };
    },
    currentType() {
      return {
        relationClass: MediaTypeModel,
        deferred: true,
      };
    },
    currentSort() {
      return {
        relationClass: SortModel,
        deferred: true,
      };
    },
    types() {
      return {
        relationClass: MediaTypeCollection,
        options: { section: this },
      };
    },
    subsets() {
      return {
        relationClass: SectionSubsetCollection,
        options: { section: this },
      };
    },
    filters() {
      const FilterCollectionClass = (this as any).get('allowAdvancedFilters')
        ? FilterCollection
        : LinearFilterCollection;
      return {
        relationClass: FilterCollectionClass,
        options: { section: this },
      };
    },
    sorts() {
      return {
        relationClass: SortCollection,
        options: { section: this },
      };
    },
    firstCharacters() {
      return {
        relationClass: FirstCharacterCollection,
        options: { section: this },
      };
    },
  },
  defaults() {
    return {
      isSection: true,
      isCollectionsPopulated: false,
      currentType: null,
      currentFilters: [],
      currentSort: null,
      currentFirstCharacter: null,
      currentFolderKey: null,
      currentSubset: subsetTypes.get('all'),
      settings: new ServerSettingCollection([], {
        server: this.server,
        url: () => {
          return path.join(['/library/sections/', this.get('key'), '/prefs']);
        },
      }),
    };
  },
  //
  // Init
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'attrs' implicitly has an 'any' type.
  initialize(attrs) {
    this.updateMediaTypes(this, attrs.type);
    this.set({
      currentType: this.get('types').findWhere({ typeString: attrs.type }),
    });
    this.on('change:type', this.updateMediaTypes, this);
  },
  uninitialize() {
    this.off('change:type', this.updateMediaTypes, this);
    this.stopListening();
  },
  //
  // Control
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'json' implicitly has an 'any' type.
  augmentResponse(json) {
    // We already have an attribute named filters for the filter collection
    // so use a limited attribute instead.
    json.allowAdvancedFilters = !!json.filters;
    delete json.filters;
    return json;
  },
  getSectionType() {
    const type = this.get('type');
    return sectionTypes.get(type);
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'attrs' is declared but its value is never read.
  getSaveParams(attrs) {
    // eslint-disable-next-line prefer-rest-params
    const params = AbstractModel.prototype.getSaveParams.apply(this, arguments);
    if ('type' in params) {
      // Convert ui-differentiated section type keys to
      // server-acceptable equivalents
      const sectionType = sectionTypes.get(params.type);
      if (sectionType) {
        params.type = sectionType.typeString;
      }
    }
    // When creating and updating, the field is 'name' instead of 'title'
    if ('title' in params) {
      params.name = params.title;
      delete params.title;
    }
    if (_.isObject(params.type)) {
      params.type = params.type.typeString;
    }
    if ('Location' in params) {
      params.location = _.pluck(params.Location, 'path');
      delete params.Location;
    }
    return params;
  },
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'models' implicitly has an 'any' type.
  getSyncTitle(models) {
    const title = this.get('title');
    // @ts-expect-error ts-migrate(7034) FIXME: Variable 'titles' implicitly has type 'any[]' in s... Remove this comment to see the full error message
    const titles = [];
    const currentSubset = this.get('currentSubset');
    const currentFilters = this.get('currentFilters');
    // If multiple items have been selected for sync, just return the title
    // of the items instead of looking at the section.
    if (models) {
      _.each(models, (model) => {
        titles.push(model.get('title'));
      });
      // @ts-expect-error ts-migrate(7005) FIXME: Variable 'titles' implicitly has an 'any[]' type.
      return title + ': ' + titles.join(', ');
    }
    // If On Deck or Recently Added is being synced, just return the subset
    // title.
    if (currentSubset.id !== 'all') {
      return title + ': ' + currentSubset.get('title');
    }
    if (!currentFilters.length) {
      return title;
    }
    _.each(currentFilters, function (currentFilter) {
      titles.push(currentFilter.titles.join(', '));
    });
    // @ts-expect-error ts-migrate(7005) FIXME: Variable 'titles' implicitly has an 'any[]' type.
    return title + ': ' + titles.join(',');
  },
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'sectionType' implicitly has an 'any' ty... Remove this comment to see the full error message
  setAgentAndScanner(sectionType, agents, scanners) {
    // Set the agent and scanner to the first available default
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'defaultAgent' implicitly has an 'any' t... Remove this comment to see the full error message
    const agent = sectionType.defaultAgents.find((defaultAgent) =>
      agents.find(
        // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'currentAgent' implicitly has an 'any' t... Remove this comment to see the full error message
        (currentAgent) => currentAgent.get('identifier') === defaultAgent
      )
    );
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'defaultScanner' implicitly has an 'any'... Remove this comment to see the full error message
    const scanner = sectionType.defaultScanners.find((defaultScanner) =>
      scanners.find(
        // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'currentScanner' implicitly has an 'any'... Remove this comment to see the full error message
        (currentScanner) => currentScanner.get('name') === defaultScanner
      )
    );
    this.set({
      agent,
      scanner,
    });
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'section' is declared but its value is never read.
  updateMediaTypes(section, type) {
    // Add any related types to the collection, including the type
    // corresponding to the section type.
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    const relatedTypes = getRelatedTypes(type);
    this.get('types').reset(relatedTypes);
  },
});
export default SectionModel;
