/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import HubCollection from 'app/js/models/metadata/HubCollection';

const DiscoverPageModel = AbstractModel.extend({
  //
  // Properties

  name: 'DiscoverPageModel',

  sessionAttrs: {
    providerEntityID: attrTypes.string,
    hubKey: attrTypes.string,

    isHubsPending: attrTypes.boolean.isRequired,
    isHubsPopulated: attrTypes.boolean.isRequired,
    hubsError: attrTypes.object,
    hubsTitle: attrTypes.string,

    isPromotedHubsPending: attrTypes.boolean.isRequired,
    isPromotedHubsPopulated: attrTypes.boolean.isRequired,
    promotedHubsError: attrTypes.object,

    // Known cloud providers at time of populating promotedHubs
    knownPromotedHubCloudProviderIDs: attrTypes.array.isRequired,
  },

  sessionRelations: {
    hubs: {
      relationClass: HubCollection,
      deferred: true,
    },

    promotedHubs: {
      relationClass: HubCollection,
      deferred: true,
    },
  },

  defaults() {
    return {
      isHubsPending: true,
      isHubsPopulated: false,
      hubsError: null,
      hubsTitle: null,

      isPromotedHubsPending: true,
      isPromotedHubsPopulated: false,
      promotedHubsError: null,

      knownPromotedHubCloudProviderIDs: [],
    };
  },
});

export default DiscoverPageModel;
