import $ from 'jquery';

export default function alwaysResolve<ResolveValue>(
  promise:
    | JQueryPromise<ResolveValue>
    | PromiseLike<ResolveValue>
    | ResolveValue
): JQueryPromise<ResolveValue> {
  const deferred = $.Deferred<ResolveValue>();

  if (typeof promise === 'object' && 'then' in promise) {
    // JQueryPromise and PromiseLike have mis-matched function signatures where
    // jQuery promises have an arbitrary number of resolve values. Since the
    // return type of this helper will always be JQueryPromise this distinction
    // is not relevant so we cast the promise to JQueryPromise and then resolve
    // the value
    (promise as JQueryPromise<ResolveValue>).then(
      deferred.resolve,
      deferred.resolve
    );
  } else {
    deferred.resolve(promise);
  }

  return deferred.promise();
}
