/* eslint-plugin-disable @typescript-eslint */

import Adapter from 'app/js/adapters/Adapter';

// Storage operations should be considered async and should each return a
// promise. Most platforms can use webStorage, but the Chrome app uses the
// chrome.storage api, while the Playstation does not allow localStorage, so
// we need to use cookies.

const volatileStorageAdapter = new Adapter({
  interface: [
    'get', // (`'key name'`) Get the value for a key.
    'set', // (`'key name', value`) Set a value for a key.
    'remove', // (`'key name'`) Remove a key value pair.
    'clear', // () Clear all data.
  ],
});

// Singleton
export default volatileStorageAdapter;
