import { combineReducers } from '@reduxjs/toolkit';
import serverAccountsSlice from 'app/js/v4/core/serverAccounts/serverAccountsSlice';
import serverActivitiesSlice from 'app/js/v4/core/serverActivities/serverActivitiesSlice';
import serverDevicesSlice from 'app/js/v4/core/serverDevices/serverDevicesSlice';
import serverNowPlayingSessionsSlice from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsSlice';
import serversSlice from 'app/js/v4/core/servers/serversSlice';
import userSlice from 'app/js/v4/core/user/userSlice';

const coreReducer = combineReducers({
  user: userSlice.reducer,
  serverAccounts: serverAccountsSlice.reducer,
  serverActivities: serverActivitiesSlice.reducer,
  serverDevices: serverDevicesSlice.reducer,
  serverNowPlayingSessions: serverNowPlayingSessionsSlice.reducer,
  servers: serversSlice.reducer,
});

export default coreReducer;
