/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import parser2 from 'app/js/utils/parser2';

const SEPARATOR = '|';
export const settingsTypes = {
  INT: 'int',
  TEXT: 'text',
  BOOL: 'bool',
  DOUBLE: 'double',
};
function parseValueType(type: any, value: any) {
  switch (type) {
    case settingsTypes.INT:
    case (settingsTypes as any).ENUM:
      return parser2.parseInteger(value);
    case settingsTypes.BOOL:
      return parser2.parseBoolean(value);
    case settingsTypes.DOUBLE:
      return parser2.parseFloat(value);
    default:
      return value;
  }
}
const MediaSettingModel = AbstractModel.extend({
  //
  // Properties
  name: 'MediaSettingModel',
  context: ['server', 'provider'],
  idAttribute: 'id',
  rootPropertyName: 'Setting',
  syncedAttrs: {
    id: attrTypes.string,
    advanced: attrTypes.boolean,
    default: attrTypes.oneOfType(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 3.
      attrTypes.integer,
      attrTypes.string,
      attrTypes.boolean
    ),
    hidden: attrTypes.boolean,
    group: attrTypes.string,
    label: attrTypes.string,
    summary: attrTypes.string,
    type: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 5.
      settingsTypes.INT,
      settingsTypes.TEXT,
      settingsTypes.BOOL,
      (settingsTypes as any).ENUM,
      settingsTypes.DOUBLE
    ),
    value: attrTypes.oneOfType(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 4.
      attrTypes.integer,
      attrTypes.float,
      attrTypes.string,
      attrTypes.boolean
    ),
    min: attrTypes.float,
    max: attrTypes.float,
    step: attrTypes.float,
    enumValues: attrTypes.array,
  },
  //
  // Control
  augmentResponse(json: any) {
    json.default = parseValueType(json.type, json.default);
    json.value = parseValueType(json.type, json.value);
    if (json.enumValues) {
      json.enumValues = json.enumValues
        .split(SEPARATOR)
        .map((valueStr: any) => {
          const [value, label = value] = valueStr.split(':');
          return {
            label: decodeURIComponent(label),
            value: parseValueType(json.type, value),
          };
        });
    }
    return json;
  },
  parseAndSetValue(value: any) {
    this.set('value', parseValueType(this.get('type'), value));
  },
});
export default MediaSettingModel;
