/* eslint-plugin-disable @typescript-eslint */

import parseQueryString from 'app/js/utils/parser/parseQueryString';
import stringifyQueryString from 'app/js/utils/parser/stringifyQueryString';
import splitUrlSearchHashPathAndParams from 'app/js/utils/url/splitUrlSearchHashPathAndParams';

function addSearchParams(url: any, params: any) {
  const { base, searchParams, hashPath, hashParams } =
    splitUrlSearchHashPathAndParams(url);
  // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
  const parsedParams = parseQueryString(searchParams);

  const stringifiedParams = stringifyQueryString({
    ...parsedParams,
    ...params,
  });

  const hashParts = (hashPath || hashParams ? '#' : '') + hashPath + hashParams;

  return `${base}?${stringifiedParams}${hashParts}`;
}

export default addSearchParams;
