/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import config from 'app/js/models/config';
import createImageTranscodeUrlSelector from 'app/js/stores/selectors/createImageTranscodeUrlSelector';
import selectStableProvider from 'app/js/stores/selectors/provider/selectStableProvider';
import selectStableServer from 'app/js/stores/selectors/server/selectStableServer';
import TranscodeImage from 'app/js/views/v3/components/core/images/TranscodeImage';

// All requests from the Qt Desktop client to the nano host should include the
// X-Plex-Account-ID: 1 header. Specifically this header ensures that poster
// requests for playlist composites of downloaded playlists work correctly.
const options = config.allowSyncDownload
  ? { urlParams: { 'X-Plex-Account-ID': 1 } }
  : null;

function createMapStateToProps() {
  return createSelector(
    selectStableServer,
    selectStableProvider,
    createImageTranscodeUrlSelector(options),
    (server, provider, transcodeUrl) => {
      return {
        url: transcodeUrl,
        // The size parameter is only supported by the provider cloud transcoder
        // currently, not the plex.tv or PMS transcoders.
        supportsSize: !!(server && server.isCloud && provider),
      };
    }
  );
}

const TranscodeImageConnector = connect(createMapStateToProps)(TranscodeImage);

export default TranscodeImageConnector;
