/* eslint-plugin-disable @typescript-eslint */

function replaceXMLElementNames(xml: any, elementNames: any, replacement: any) {
  if (!Array.isArray(elementNames)) {
    elementNames = [elementNames];
  }

  const joinedNames = elementNames.join('|');
  const openRegex = new RegExp(`<(${joinedNames})`, 'g');
  const closeRegex = new RegExp(`</(${joinedNames})>`, 'g');

  return xml
    .replace(openRegex, `<${replacement} elementName="$1"`)
    .replace(closeRegex, `</${replacement}>`);
}

export default replaceXMLElementNames;
