/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import getStreams from './getStreams';

function selectStream(media: any, streamType: any, id: any) {
  if (media) {
    const streams = getStreams(media, { streamType: streamType.id });

    _.each(streams, (stream) => {
      stream.selected = stream.id === id;
    });
  }
}

export default selectStream;
