import { createSelector, Selector } from 'reselect';
import selectManageableServerEntityIDs from 'app/js/stores/selectors/server/selectManageableServerEntityIDs';
import selectServers from 'app/js/stores/selectors/server/selectServers';
import { Server, UnstableServer } from 'app/js/v4/core/servers/serversTypes';
import unknownServer from 'app/js/v4/core/servers/utils/unknownServer';
import { LegacyRootState } from 'app/js/v4/root/rootTypes';

function getManageableServer(
  primaryServerEntityID: string,
  manageableServerEntityIDs: string[]
): string | null {
  if (manageableServerEntityIDs.includes(primaryServerEntityID)) {
    return primaryServerEntityID;
  }

  if (manageableServerEntityIDs) {
    return manageableServerEntityIDs[0];
  }

  return null;
}

const selectServer: Selector<LegacyRootState, Server & UnstableServer> =
  createSelector(
    selectServers,
    selectManageableServerEntityIDs,
    (appState: any) => appState.primaryServer,
    (servers, manageableServers, primaryServer) => {
      const serverEntityID = getManageableServer(
        primaryServer,
        manageableServers
      );

      return (
        servers.find(
          (server: Server & UnstableServer) =>
            server.entityID === serverEntityID
        ) || unknownServer
      );
    }
  );

export default selectServer;
