/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import DVRCollection from 'app/js/models/provider/livetv/dvr/DVRCollection';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createActionHandler from 'app/js/stores/actions/createActionHandler';
import createFetchHandler from 'app/js/stores/actions/helpers/createFetchHandler';
import { findServerModel } from 'app/js/utils/state/modelFinders';
import path from 'app/js/utils/url/path';

//
// Action Types

export const INIT_DVR_SETTINGS_UI = 'ui/dvrSettings/initDVRSettingsUI';
export const CLEAR_DVR_SETTINGS_UI = 'ui/dvrSettings/clearDVRSettingsUI';
export const FETCH_DVRS = 'ui/dvrSettings/fetchDVRs';
export const RELOAD_GUIDE = 'ui/dvrSettings/reloadGuide';
export const CANCEL_RELOAD_GUIDE = 'ui/dvrSettings/cancelReloadGuide';

//
// Action Creators

export const initDVRSettingsUI = createActionCreator(INIT_DVR_SETTINGS_UI);
export const clearDVRSettingsUI = createActionCreator(CLEAR_DVR_SETTINGS_UI);
export const fetchDVRs = createActionCreator(FETCH_DVRS);
export const reloadGuide = createActionCreator(RELOAD_GUIDE);
export const cancelReloadGuide = createActionCreator(CANCEL_RELOAD_GUIDE);

//
// Helpers

function findDVRSettingsUIModel(appModel: any) {
  return appModel.get('ui').get('dvrSettingsUI');
}

function sendReloadGuideRequest(appModel: any, payload: any, method: any) {
  const server = findServerModel(appModel, payload);
  const url = path.join(['/livetv/dvrs', payload.dvrID, 'reloadGuide']);
  const xhrOptions = server.getXHROptions(url, { method });
  $.ajax(xhrOptions);
}

//
// Action Handlers

export const dvrSettingsUIActionHandlers = createActionHandler({
  [FETCH_DVRS]: createFetchHandler({
    key: 'dvrs',
    getModel: findDVRSettingsUIModel,
  }),

  [INIT_DVR_SETTINGS_UI](appModel: any, payload: any, dispatch: any) {
    const dvrSettingsUIModel = findDVRSettingsUIModel(appModel);
    const currentDVRs = dvrSettingsUIModel.get('dvr');
    const server = findServerModel(appModel, payload);
    const machineIdentifier = server.get('machineIdentifier');
    // Only create a new `DVRCollection` if one does not already exist for that
    // server. Otherwise, we can reset and reuse the existing collection.
    const attrs = {
      isUIPopulated: true,
    };
    if (
      !currentDVRs ||
      currentDVRs.server.get('machineIdentifier') !== machineIdentifier
    ) {
      (attrs as any).dvrs = new DVRCollection([], {
        server,
      });
    }
    dvrSettingsUIModel.set(attrs);
    if (server.hasOwnerFeature('dvr') || server.hasOwnerFeature('livetv')) {
      dispatch(fetchDVRs(payload));
    }
  },

  [CLEAR_DVR_SETTINGS_UI](appModel: any) {
    const dvrSettingsUIModel = findDVRSettingsUIModel(appModel);
    dvrSettingsUIModel.set('isUIPopulated', false);
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [RELOAD_GUIDE](appModel: any, payload: any, dispatch: any) {
    sendReloadGuideRequest(appModel, payload, 'POST');
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [CANCEL_RELOAD_GUIDE](appModel: any, payload: any, dispatch: any) {
    sendReloadGuideRequest(appModel, payload, 'DELETE');
  },
});
