/* eslint-plugin-disable @typescript-eslint */

import { getMyPlexUrl } from 'app/js/utils/getRuntimeUrl';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import getUserAPIPromise from 'app/js/utils/user/getUserAPIPromise';

function signOut(options: any) {
  return alwaysResolve(
    getUserAPIPromise({
      url: getMyPlexUrl('/api/v2/users/signout'),
      headers: {
        'X-Plex-Token': options.accessToken,
      },
      type: 'DELETE',
      xhrFields: { withCredentials: __HOSTED__ },
    })
  );
}

export default signOut;
