/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import addFlexModel from 'app/js/models/addFlexModel';
import attrTypes from 'app/js/models/attrTypes';
import ServerModel from 'app/js/models/server/ServerModel';
import { getMyPlexUrl } from 'app/js/utils/getRuntimeUrl';
import log from 'app/js/utils/log/log';
import whenFirstSucceed from 'app/js/utils/promise/whenFirstSucceed';
import path from 'app/js/utils/url/path';
import ConnectionCollection from './ConnectionCollection';
import ConnectionModel from './ConnectionModel';
import ResourceCollection from './ResourceCollection';

const { connectionTestResults, promiseStates } = ServerModel;

const CloudServerModel = ServerModel.extend({
  //
  // Properties

  name: 'CloudServerModel',
  connectionTestUrl: getMyPlexUrl('/monitoring/health'),

  sessionAttrs: {
    connectionTestResult: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 4.
      connectionTestResults.PENDING,
      connectionTestResults.CONNECTED,
      connectionTestResults.UNAVAILABLE,
      connectionTestResults.UNAUTHORIZED
    ).isRequired,

    promiseState: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 3.
      promiseStates.PENDING,
      promiseStates.RESOLVED,
      promiseStates.REJECTED
    ).isRequired,

    isShared: attrTypes.boolean.isRequired,
    isBundled: attrTypes.boolean.isRequired,
    isCloud: attrTypes.boolean.isRequired,
    isMerged: attrTypes.boolean.isRequired,
    isDownForMaintenance: attrTypes.boolean.isRequired,
    isResourcesPending: attrTypes.boolean.isRequired,
    isResourcesPopulated: attrTypes.boolean.isRequired,
    hasFailedResources: attrTypes.boolean.isRequired,

    accessToken: attrTypes.string,

    baseUrl: attrTypes.string.isRequired,
    plexPassUrl: attrTypes.string.isRequired,
    appsUrl: attrTypes.string.isRequired,

    hasEventConnection: attrTypes.boolean,
  },

  sessionRelations: {
    activeConnection: {
      relationClass: ConnectionModel,
      deferred: true,
    },

    connections: {
      relationClass: ConnectionCollection,
      options() {
        return { server: this };
      },
    },

    resources: {
      relationClass: ResourceCollection,
      options() {
        return { server: this };
      },
    },
  },

  defaults() {
    const baseUrl = getMyPlexUrl();

    return {
      connectionTestResult: connectionTestResults.PENDING,
      promiseState: promiseStates.PENDING,

      isShared: false,
      isBundled: false,
      isCloud: true,
      isMerged: false,
      isDownForMaintenance: false,
      isResourcesPending: false,
      isResourcesPopulated: false,
      hasFailedResources: false,
      friendlyName: 'plex.tv',

      baseUrl: baseUrl,
      plexPassUrl: path.join([baseUrl, '/subscription/about']),
      appsUrl: path.join([baseUrl, '/downloads']),
      hasEventConnection: false,
    };
  },

  //
  // Control

  testAllConnections() {
    // Override so the populate promise is not reset by `ServerModel`. A cloud
    // server's populate promise is determined by whether the user is
    // authenticated.
    const connections = this.get('connections');

    log(
      '[Connections] Testing all %s connection(s) for %s',
      connections.length,
      this.get('debugName')
    );

    const promises = connections.map((connection: any) =>
      this.testConnection(connection)
    );

    // We want the promise to resolve when any of the connection promises
    // resolve and only reject when all of the returned promises reject.
    return whenFirstSucceed(promises);
  },

  testConnection(connection: any) {
    // Override so a different url can be passed for testing and so the populate
    // promise is not reset by `ServerModel`. A cloud server's populate promise
    // is determined by whether the user is authenticated.

    const options = {
      server: this,
      connection: connection,
      url: this.connectionTestUrl,
    };

    const promise = commands
      .execute('testServerConnection', options)
      .done(this.onConnectionTestSuccess.bind(this))
      .fail(this.onConnectionTestError.bind(this));

    return promise;
  },

  abortConnectionTest(connection: any) {
    const options = {
      server: this,
      connection: connection,
      url: this.connectionTestUrl,
    };

    commands.execute('abortServerConnectionTest', options);
  },

  deauthenticate() {
    // The connection test result should not change when the server is
    // deauthenticated, but `ServerModel` assumes the server is unavailable or
    // unauthorized when it is rejected.

    this.get('resources').clear();

    this._reject({
      connectionTestResult: this.get('connectionTestResult'),
      accessToken: null,
    });
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'connection' is declared but its value is never re... Remove this comment to see the full error message
  onConnectionTestSuccess(connection: any) {
    // Do not resolve the populate promise until the server is authenticated.
    // This ensures that the cloud server can be populated and it will not
    // resolve until the user is signed in.

    const bestConnection = this.get('connections').getBestConnection();

    log.info(
      '[Connections] Active connection to %s is %s',
      this.get('debugName'),
      bestConnection.id
    );

    // Capture change events to ensure that a change event for
    // `connectionTestResult` is not triggered before the derived attributes
    // reflect the new state of the server.

    this.startCapturingChangeEvents();

    this.set({
      isDownForMaintenance: false,
      connectionTestResult: connectionTestResults.CONNECTED,
      activeConnection: bestConnection,
    });

    this.releaseCapturedChangeEvents();
  },

  onConnectionTestError(connection: any, xhr: any) {
    if (connection.get('isAborted')) {
      return;
    }

    if (this.get('connections').hasOnlyFailedConnections()) {
      this._reject({
        isDownForMaintenance: xhr.status === 503,
      });
    }
  },
});

addFlexModel(CloudServerModel, function () {
  return {
    relationClass: resolve('CloudServerFlexModel'),
    options: {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      server: this,
    },
  };
});

export default CloudServerModel;
