import * as adapters from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import * as restrictionProfileTypes from 'app/js/utils/constants/restrictionProfileTypes';
import * as features from 'app/js/utils/constants/user/features';
import t from 'app/js/utils/t';
import { userHasFeature } from 'app/js/v4/utils/features';
import GravatarImage from 'app/js/views/images/GravatarImage';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/user/UserSelectModalUserListItem.tpl';

const UserSelectModalUserListItem = Marionette.ItemView.extend({
  //
  // Properties
  tagName: 'li',
  className: 'user-select-list-item',
  template: tpl,
  ui: {
    container: '.user-select-container',
    poster: '.user-poster',
  },
  events: {
    'click a': 'onClick',
  },
  //
  // Init
  initialize: function (options: any) {
    this.disabled = options.disabled;
    this.posterImage = new GravatarImage({
      model: this.model,
      type: 'friend',
      width: 80,
    });
    const appModel = adapters.resolve('appModel');
    const primaryServer = appModel.get('primaryServer');
    if (primaryServer?.supports('imageFilters')) {
      this.backgroundImage = new GravatarImage({
        model: this.model,
        type: 'friend',
        width: 120,
        background: true,
        filters: {
          blur: 20,
          opacity: 50,
          background: '000000',
        },
      });
      // Do not show a placeholder image for the background.
      this.backgroundImage.placeholderClass = false;
    }
  },
  //
  // Control
  serializeData: function () {
    return {
      disabled: this.disabled,
    };
  },
  //
  // Bindings
  bindings: {
    '.username': {
      modelAttribute: 'title',
    },
    '.managed-title': {
      modelAttribute: 'restrictionProfile',
      autohide: true,
      formatter(restrictionProfile: any) {
        const hasHomeLibraryAccessLabels = userHasFeature(
          features.HOME_LIBRARY_ACCESS_LABELS
        );
        const hasCommunityFeatures = userHasFeature(
          features.ALLOW_COMMUNITY_FEATURES
        );

        // We only want to show this label with the legacy styles when we don't have the feature flag on.
        if (hasCommunityFeatures) {
          return;
        }

        const managedLabel = hasHomeLibraryAccessLabels
          ? t('Managed Account')
          : t('Managed User');

        const username = (this as any).model.get('username');
        const title = (this as any).model.get('title');

        if (username && title !== username) {
          return username;
        }

        if (
          (this as any).model.get('restricted') &&
          !(this as any).model.get('guest')
        ) {
          return restrictionProfile
            ? restrictionProfileTypes.getType(restrictionProfile).title
            : managedLabel;
        }
      },
    },
    '.managed-title-community': {
      modelAttribute: 'restrictionProfile',
      autohide: true,
      formatter(restrictionProfile: any) {
        const hasCommunityFeatures = userHasFeature(
          features.ALLOW_COMMUNITY_FEATURES
        );

        // We only want to show this label with new styles when have the feature flag on.
        if (!hasCommunityFeatures) {
          return;
        }

        const username = (this as any).model.get('username');
        const title = (this as any).model.get('title');

        if (username && title !== username) {
          return username;
        }

        if (
          (this as any).model.get('restricted') &&
          !(this as any).model.get('guest')
        ) {
          return restrictionProfile
            ? restrictionProfileTypes.getType(restrictionProfile).title
            : t('Managed Account');
        }
      },
    },
    '.protected-icon': {
      modelAttribute: 'protected',
      autohide: true,
      manual: true,
    },
    '.admin-icon': {
      modelAttribute: 'admin',
      autohide: true,
      manual: true,
    },
  },
  //
  // Listeners
  onRender: function () {
    this.posterImage.setElement(this.ui.poster);
    if (this.backgroundImage) {
      this.backgroundImage.setElement(this.ui.container);
    }
  },
  onClose: function () {
    // Manually close the images because we aren't actually showing them so
    // they won't be cleaned up automatically.
    this.posterImage.close();
    if (this.backgroundImage) {
      this.backgroundImage.close();
    }
  },
  onClick: function (event: any) {
    event.preventDefault();
    this.trigger('selectUser', this.model);
  },
});
export default UserSelectModalUserListItem;
