/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import storageAdapter from 'app/js/adapters/storageAdapter';
import UserManager from 'app/js/managers/UserManager';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';

export default function userInitializer() {
  let promise = alwaysResolve(userStorageModel.populate());

  promise = promise.then(() => storageAdapter.get('myPlexAccessToken'));

  promise = promise.then(
    (myPlexAccessToken) => {
      const user = resolve('appModel').get('user');
      const storedUsers = userStorageModel.get('users');
      const storedUser = storedUsers.findWhere({
        authToken: myPlexAccessToken,
      });

      if (storedUser) {
        user.activateStoredUser(storedUser.id);
        storedUser.copyToModel(user);
      }
    },
    () => {
      // Activate the anonymous user if there is no stored token.
      const user = resolve('appModel').get('user');
      user.activateStoredUser(0);

      const storedUser = userStorageModel.getActiveUser();

      if (storedUser) {
        storedUser.copyToModel(user);
      }
    }
  );

  return alwaysResolve(promise).then(() => {
    const user = resolve('appModel').get('user');

    const userManager = new UserManager(user);
    userManager.start();
  });
}
