import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import CollectionCache from 'app/js/utils/cache/CollectionCache';
import AgentModel from './AgentModel';

const AgentCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'AgentCollection',
  context: ['server', 'mediaTypeID', 'primaryAgentID', 'relatedAgentID'],

  model: AgentModel,

  cache: new CollectionCache(),
  cacheKey: function () {
    const url = _.result(this, 'url');

    return this.server?.getScopedKey(url);
  },

  url: function () {
    const path = '/system/agents';

    if (this.primaryAgentID) {
      return (
        path +
        '/contributors?mediaType=' +
        this.mediaTypeID +
        '&primaryAgent=' +
        this.primaryAgentID
      );
    } else if (this.relatedAgentID) {
      return path + '/' + this.relatedAgentID + '/config/' + this.mediaTypeID;
    } else if (this.mediaTypeID) {
      return path + '?mediaType=' + this.mediaTypeID;
    } else {
      return path;
    }
  },

  //
  // Control

  hasExclusiveAgent: function (exclusiveAgents: any) {
    return !!_.intersection(this.pluck('identifier'), exclusiveAgents).length;
  },
});

export default AgentCollection;
