/* eslint-plugin-disable @typescript-eslint */

import userStorageModel from 'app/js/models/storage/userStorageModel';
import * as jsonStorage from 'app/js/utils/storage/jsonStorage';

// Previously experienceSettings were per device.  Now they are per user. So all
// users must have these settings applied to their experienceSettings.

function migrateExperienceSettingsToAllStoredUsers(experienceSettings: any) {
  // Settings are marked as dirty so they are updated on plex.tv during
  // application start up.
  experienceSettings.isDirty = true;

  // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
  return jsonStorage.getJSON(userStorageModel.storageKey).then((json: any) => {
    if (!json.users) {
      return;
    }

    json.users.forEach((user: any) => {
      user.experienceSettings = {
        ...user.experienceSettings,
        ...experienceSettings,
      };
    });

    return jsonStorage.setJSON(userStorageModel.storageKey, json);
  });
}

export default migrateExperienceSettingsToAllStoredUsers;
