/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import BaseClass from 'app/js/utils/BaseClass';
import log from 'app/js/utils/log/log';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const AbstractStorage = BaseClass.extend({
  //
  // Control
  getStorage() {
    // Abstract function
  },
  get(key: any) {
    return this._catchStorageError((storage: any) => {
      // Value resolution rules are shared with between storage modules.
      // See: https://github.com/plexinc/plex-web-client/issues/1645
      const PLEXWEB = (window as any).PLEXWEB;
      const value =
        PLEXWEB && key in PLEXWEB ? PLEXWEB[key] : storage.getItem(key);
      if (value == null) {
        throw new Error(`${key} is not in ${this.name}`);
      }
      return value;
    });
  },
  set(key: any, value: any) {
    return this._catchStorageError((storage: any) => {
      if (!key) {
        throw new Error(`Unable to set value in ${this.name} without a key`);
      }
      if (value == null) {
        throw new Error(
          `Unable to set null or undefined value for ${key} in ${this.name}. Use the remove function.`
        );
      }
      return storage.setItem(key, value);
    });
  },
  remove(key: any) {
    return this._catchStorageError((storage: any) => {
      return storage.removeItem(key);
    });
  },
  clear() {
    return this._catchStorageError((storage: any) => {
      return storage.clear();
    });
  },
  // Accessing session storage can result in errors if we run out of storage
  // space or the browser is in a private browsing mode that disables session
  // storage. Wrap all storage access in a try catch to prevent these errors
  // from breaking the application.
  _catchStorageError(accessFn: any) {
    const deferred = $.Deferred();
    try {
      deferred.resolve(accessFn(this.getStorage()));
    } catch (error) {
      log.warn(error.message);
      deferred.reject(error);
    }
    return deferred.promise();
  },
});
export default AbstractStorage;
