/* eslint-plugin-disable @typescript-eslint */

import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import t from 'app/js/utils/t';
import routes from 'app/js/v4/routes/routes';
import NavBarButton from 'app/js/views/v3/components/navbar/NavBarButton';
import styles from './NavBarSignInButton.css';

function NavBarSignInButton() {
  const history = useHistory();

  const onSignInPress = useCallback(() => {
    history.push(routes.login);
  }, [history]);

  return (
    <NavBarButton
      className={styles.signInButton}
      data-testid="signInButton"
      onPress={onSignInPress}
    >
      {t('Sign In')}
    </NavBarButton>
  );
}

export default NavBarSignInButton;
