import React, { Component, ReactElement } from 'react';
import * as Sentry from '@sentry/browser';
import log from 'app/js/utils/log/log';
import UnexpectedErrorPageContent from 'app/js/v4/core/ui/pages/components/empty/UnexpectedErrorPageContent';

interface PageErrorBoundaryState {
  hasError: boolean;
}

class PageErrorBoundary extends Component<object, PageErrorBoundaryState> {
  public static getDerivedStateFromError(): PageErrorBoundaryState {
    return { hasError: true };
  }

  public constructor(props: object, context: object) {
    super(props, context);

    this.state = { hasError: false };
  }

  public componentDidCatch(error: Error, errorInfo: React.ErrorInfo): void {
    log.error(error);

    // See: https://docs.sentry.io/platforms/javascript/enriching-events/context/#passing-context-directly
    const context = { extra: { componentStack: errorInfo.componentStack } };

    Sentry.captureException(error, context);
  }

  public render(): ReactElement {
    return this.state.hasError ? (
      <UnexpectedErrorPageContent />
    ) : (
      <>{this.props.children}</>
    );
  }
}

export default PageErrorBoundary;
