/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createActionHandler from 'app/js/stores/actions/createActionHandler';
import { findServerModel } from 'app/js/utils/state/modelFinders';
import path from 'app/js/utils/url/path';
import xhrErrorFilter from 'app/js/utils/xhr/xhrErrorFilter';

//
// Action Types

export const SAVE_DVR_SETTINGS = 'SAVE_DVR_SETTINGS';
export const CLEAR_DVR_SETTINGS_SAVE_ERROR = 'CLEAR_DVR_SETTINGS_SAVE_ERROR';
export const SAVE_DEVICE_SETTINGS = 'SAVE_DEVICE_SETTINGS';
export const SAVE_DEVICE_TITLE = 'SAVE_DEVICE_TITLE';
export const CLEAR_DEVICE_SETTINGS_SAVE_ERROR =
  'CLEAR_DEVICE_SETTINGS_SAVE_ERROR';
export const SET_DVR_DEVICE_ENABLED = 'SET_DVR_DEVICE_ENABLED';

//
// Action Creators

export const saveDVRSettings = createActionCreator(SAVE_DVR_SETTINGS);
export const clearDVRSettingsSaveError = createActionCreator(
  CLEAR_DVR_SETTINGS_SAVE_ERROR
);
export const saveDeviceSettings = createActionCreator(SAVE_DEVICE_SETTINGS);
export const saveDeviceTitle = createActionCreator(SAVE_DEVICE_TITLE);
export const clearDeviceSettingsSaveError = createActionCreator(
  CLEAR_DEVICE_SETTINGS_SAVE_ERROR
);
export const setDVRDeviceEnabled = createActionCreator(SET_DVR_DEVICE_ENABLED);

//
// Helpers

function findDVRSetupModel(appModel: any) {
  return appModel.get('ui').get('dvrSettingsUI').get('dvrSetup');
}

function findDVRs(appModel: any) {
  return appModel.get('ui').get('dvrSettingsUI').get('dvrs');
}

function findDVR(appModel: any, payload: any) {
  const dvrs = findDVRs(appModel);
  return dvrs.get(payload.dvrID);
}

function getDevice(appModel: any, payload: any) {
  const dvrSetupModel = findDVRSetupModel(appModel);
  const deviceID = payload.deviceID;
  // Try to find a device using the dvrID and deviceID,
  // if that fails look for the device in the `dvrSetupModel`
  // because we may be looking for a device that is being
  // added to a DVR. If we still haven't found a device
  // return the selected device.
  if (payload.dvrID && deviceID) {
    const dvrDevice = findDVR(appModel, payload).get('devices').get(deviceID);
    if (dvrDevice) {
      return dvrDevice;
    }
  }
  if (deviceID) {
    const device = dvrSetupModel.get('devices').get(deviceID);
    if (device) {
      return device;
    }
  }
  return dvrSetupModel.get('selectedDevice');
}

//
// Action Handlers

export const dvrSetupActionHandlers = createActionHandler({
  [SAVE_DVR_SETTINGS](appModel: any, payload: any) {
    const server = findServerModel(appModel, payload);
    const dvr = findDVR(appModel, payload);
    const prefs = payload.prefs;
    const url = path.join(['/livetv/dvrs', payload.dvrID, 'prefs'], prefs);
    dvr.set('isSaving', true);
    const xhrOptions = server.getXHROptions(url, { type: 'PUT' });
    const promise = $.ajax(xhrOptions);
    promise.done(() => {
      const dvrPrefs = dvr.get('prefs');
      Object.keys(prefs).forEach((pref) => {
        dvrPrefs.get(pref).set('value', prefs[pref]);
      });
      dvr.set({
        isSaving: false,
        saveError: null,
      });
    });
    promise.fail((xhr) => {
      dvr.set({
        isSaving: false,
        saveError: xhrErrorFilter(xhr),
      });
    });
  },

  [CLEAR_DVR_SETTINGS_SAVE_ERROR](appModel: any, payload: any) {
    const dvr = findDVR(appModel, payload);
    dvr.set('saveError', null);
  },

  [SAVE_DEVICE_SETTINGS](appModel: any, payload: any) {
    const server = findServerModel(appModel, payload);
    const device = getDevice(appModel, payload);
    const prefs = payload.prefs;
    const url = path.join(
      ['/media/grabbers/devices', payload.deviceID, 'prefs'],
      prefs
    );
    device.set('isSaving', true);
    const xhrOptions = server.getXHROptions(url, { type: 'PUT' });
    const promise = $.ajax(xhrOptions);
    promise.done(() => {
      const devicePrefs = device.get('prefs');
      Object.keys(prefs).forEach((pref) => {
        devicePrefs.get(pref).set('value', prefs[pref]);
      });
      device.set({
        isSaving: false,
        saveError: null,
      });
    });
    promise.fail((xhr) => {
      device.set({
        isSaving: false,
        saveError: xhrErrorFilter(xhr),
      });
    });
  },

  [SAVE_DEVICE_TITLE](appModel: any, payload: any) {
    const { title } = payload;
    const server = findServerModel(appModel, payload);
    const device = getDevice(appModel, payload);
    // `enabled` is always required by this endpoint. Because this action is
    // only dispatched by a user setting the title on a configured device, we
    // can assume the device is enabled and should remain enabled.
    const url = path.join(['/media/grabbers/devices', payload.deviceID], {
      title,
      enabled: 1,
    });
    const xhrOptions = server.getXHROptions(url, { type: 'PUT' });
    const promise = $.ajax(xhrOptions);

    promise.then(() => {
      device.set('title', title);
    });
  },

  [CLEAR_DEVICE_SETTINGS_SAVE_ERROR](appModel: any, payload: any) {
    const device = getDevice(appModel, payload);
    device.set('saveError', null);
  },
  [SET_DVR_DEVICE_ENABLED](appModel: any, payload: any) {
    const server = findServerModel(appModel, payload);
    const enabled = payload.enabled;
    const url = path.join(['/media/grabbers/devices', payload.deviceID], {
      enabled: enabled ? 1 : 0,
    });
    const xhrOptions = server.getXHROptions(url, { type: 'PUT' });
    const promise = $.ajax(xhrOptions).promise();
    promise.done(() => {
      const dvrSetupModel = findDVRSetupModel(appModel);
      if (dvrSetupModel) {
        const device = getDevice(appModel, payload);
        if (device) {
          device.set('status', enabled ? 'enabled' : 'disabled');
        }
      }
    });
  },
});
