/* eslint-plugin-disable @typescript-eslint */

import getProviderRouteFromIdentifiers from 'app/js/views/v3/helpers/routes/getProviderRouteFromIdentifiers';

export default function getProviderRoute(
  server: any,
  provider: any,
  params: any,
  path: any
) {
  return getProviderRouteFromIdentifiers(
    server.isCloud,
    server.machineIdentifier,
    provider.identifier,
    params,
    path
  );
}
