import $ from 'jquery';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/secti... Remove this comment to see the full error message
import tpl from 'app/templates/section/edit/EditSectionSettingView.tpl';

const EditSectionSettingView = Marionette.ItemView.extend({
  //
  // Properties

  template: tpl,

  events: {
    'change [name]': 'onEditSectionSettingInputChange',
  },

  //
  // Control

  serializeData: function () {
    return { settings: this.collection.toJSON(), edit: !this.model.isNew() };
  },

  //
  // Listeners

  onEditSectionSettingInputChange: function (event: any) {
    const $target = $(event.currentTarget);
    const name = $target.attr('name');

    // The import from iTunes setting is synthetic and needs to be sent with
    // the POST to create a section.
    if (name === 'importFromiTunes') {
      this.model.set(name, true);
      return;
    }

    const value = $target.is(':checkbox')
      ? $target.prop('checked')
      : $target.val();

    const setting = this.collection.findWhere({ id: name });
    setting.set('value', value);
  },
});

export default EditSectionSettingView;
