import React, { ReactElement, Suspense } from 'react';
import { generatePath, Redirect, Route, Switch } from 'react-router-dom';
import PageErrorBoundary from 'app/js/v4/core/ui/pages/components/PageErrorBoundary';
import EmptyPageContent from 'app/js/v4/core/ui/pages/components/empty/EmptyPageContent';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import FullPageBackground from 'app/js/v4/features/background/FullPageBackground';
import useNavigationHistory from 'app/js/v4/features/navigationHistory/hooks/useNavigationHistory';
import ServerAwaitPage from 'app/js/v4/features/serverSettings/ServerAwaitPage';
import ServerSetupViewPage from 'app/js/v4/features/serverSetup/components/ServerSetupViewPage';
import routes from 'app/js/v4/routes/routes';
import NavBarConnector from 'app/js/views/v3/components/navbar/NavBarConnector';
import FullPage from 'app/js/views/v3/components/pages/FullPage';
import LoginPage from 'app/js/views/v3/components/pages/LoginPage';
import NotFoundErrorPage from 'app/js/views/v3/components/pages/empty/NotFoundErrorPage';
import SignInLaunchPageConnector from 'app/js/views/v3/components/pages/signIn/SignInLaunchPageConnector';
import SignOutPageConnector from 'app/js/views/v3/components/pages/signOut/SignOutPageConnector';
import useGarbageCollectEntities from './utils/hooks/useGarbageCollectEntities';

const InfoViewPage = React.lazy(
  () =>
    import(
      /* webpackPrefetch: true */
      'app/js/v4/features/info/components/InfoViewPage'
    )
);
const LogsViewPage = React.lazy(
  () =>
    import(
      /* webpackPrefetch: true */
      'app/js/v4/features/logs/components/LogsViewPage'
    )
);
const ServerUpdateViewPage = React.lazy(
  () =>
    import(
      /* webpackPrefetch: true */
      'app/js/v4/features/serverUpdate/components/ServerUpdateViewPage'
    )
);
const ServerSettingsViewPage = React.lazy(
  () =>
    import(
      /* webpackPrefetch: true */
      'app/js/v4/features/serverSettings/components/ServerSettingsViewPage'
    )
);

export const CONTENT_CLASS_NAMES = 'scroll-container dark-scrollbar';

export default function App(): ReactElement {
  useGarbageCollectEntities();
  useNavigationHistory();

  const serverDashboardRoute = generatePath(routes.serverSettings, {
    settingsSection: 'status',
    settingsGroup: 'server-dashboard',
  });

  return (
    <>
      <FullPageBackground />

      <PageErrorBoundary>
        <Suspense
          fallback={
            <FullPage>
              <EmptyPageContent>
                <Spinner />
              </EmptyPageContent>
            </FullPage>
          }
        >
          <Switch>
            <Redirect
              exact={true}
              from={routes.home}
              to={serverDashboardRoute}
            />

            <Redirect
              exact={true}
              from={routes.serverSettingsPMS}
              to={serverDashboardRoute}
            />

            <Route
              component={ServerSetupViewPage}
              exact={true}
              path={routes.serverSetup}
            />

            <Route>
              <div>
                <NavBarConnector />
              </div>

              <div className={CONTENT_CLASS_NAMES}>
                {/* This error boundary and suspense spinner ensures navbar is displayed at all times */}
                <PageErrorBoundary>
                  <Suspense
                    fallback={
                      <FullPage>
                        <EmptyPageContent>
                          <Spinner />
                        </EmptyPageContent>
                      </FullPage>
                    }
                  >
                    <Switch>
                      <Route
                        component={ServerAwaitPage}
                        exact={true}
                        path={routes.serverAwait}
                      />

                      <Route exact={true} path={routes.logout}>
                        <FullPage>
                          <SignOutPageConnector />
                        </FullPage>
                      </Route>

                      <Route exact={true} path={routes.login}>
                        <FullPage>
                          <LoginPage />
                        </FullPage>
                      </Route>

                      <Route exact={true} path={routes.signUp}>
                        <FullPage>
                          {/* @ts-expect-error FIXME: Remove this comment to see the full error message */}
                          <SignInLaunchPageConnector signUp={true} />
                        </FullPage>
                      </Route>

                      <Route
                        component={InfoViewPage}
                        exact={true}
                        path={routes.info}
                      />

                      <Route
                        component={LogsViewPage}
                        exact={true}
                        path={routes.logs}
                      />

                      <Route
                        component={ServerUpdateViewPage}
                        exact={true}
                        path={routes.serverUpdate}
                      />

                      <Route
                        component={ServerSettingsViewPage}
                        exact={true}
                        path={routes.serverSettings}
                      />

                      <Route component={NotFoundErrorPage} />
                    </Switch>
                  </Suspense>
                </PageErrorBoundary>
              </div>
            </Route>
          </Switch>
        </Suspense>
      </PageErrorBoundary>
    </>
  );
}
