/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import log from 'app/js/utils/log/log';

function updateSessionLastInteractionTime() {
  const now = Date.now();
  const sessionStats = resolve('sessionStats');

  log.debug('[Metrics] Updating session last interaction time');
  sessionStats.save('lastInteractionTime', now);
}

export default updateSessionLastInteractionTime;
