/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';

function fetchPIN(appModel: any, pinID: any) {
  const cloudServer = appModel.get('cloudServer');

  return (
    $.ajax(
      cloudServer.getXHROptions(`/api/v2/pins/${pinID}`, {
        dataType: 'json',
      })
    )
      .promise()
      // Strip XHR from promise resolution so consumers only receive JSON.
      .then((response) => response)
  );
}

export default fetchPIN;
