/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { saveDeviceSettings } from 'app/js/stores/actions/appActions';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import * as serverUpdateStates from 'app/js/utils/constants/serverUpdateStates';
import dispatcher from 'app/js/utils/dispatcher';
import {
  findServerFlexModel,
  findServerModel,
} from 'app/js/utils/state/modelFinders';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';
//
// Action Types
export const FETCH_SERVER_UPDATE_STATUS =
  'serverUpdate/fetchServerUpdateStatus';
export const CHECK_FOR_SERVER_UPDATE = 'serverUpdate/checkForServerUpdate';
export const DISMISS_SERVER_UPDATE = 'serverUpdate/dismissServerUpdate';
export const DOWNLOAD_SERVER_UPDATE = 'serverUpdate/downloadServerUpdate';
export const INSTALL_SERVER_UPDATE = 'serverUpdate/installServerUpdate';
//
// Helpers
function updaterCheckRequest(serverModel: any, options: any) {
  // isChecking should only be set false when caller knows there most likely
  // won't be a WebSocket event received, but still needs to initiate the check.
  const { download, initialState, isChecking = true } = options;
  const params = { download: download ? '1' : '0' };
  const url = path.join('/updater/check', params);
  const xhrOptions = serverModel.getXHROptions(url, { type: 'PUT' });
  const update = serverModel.get(__PLATFORM__).get('update');
  const attrs = { isChecking };
  if (initialState != null) {
    (attrs as any).state = initialState;
  }
  update.set(attrs);
  // https://github.com/plexinc/specs/blob/master/Server/Auto%20Update.md#updatercheck
  // The updater check request returns 200 when completed, but the result of the
  // check will be transmitted via WebSockets when available. So in the success
  // case here we do not set isChecking false, since the 'check' isn't complete
  // until the WebSocket event received.
  // @ts-expect-error ts-migrate(2769) FIXME: No overloads match this call.
  $.ajax(xhrOptions).then(null, () => {
    update.set({ isChecking: false, state: serverUpdateStates.ERROR });
  });
}
function getUpdaterApplyRequest(serverModel: any, params: any) {
  const url = path.join(['/updater/apply'], params);
  const xhrOptions = serverModel.getXHROptions(url, { type: 'PUT' });
  return $.ajax(xhrOptions);
}
function install(serverModel: any) {
  const update = serverModel.get(__PLATFORM__).get('update');
  // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
  getUpdaterApplyRequest(serverModel).then(
    () => {
      update.set('state', serverUpdateStates.INSTALLING);
      serverModel.set('isInstallingServerUpdate', true);
    },
    () => {
      update.set('state', serverUpdateStates.ERROR);
      serverModel.set('isInstallingServerUpdate', false);
    }
  );
}
function dismissServerUpdateVersion(
  appModel: any,
  dispatch: any,
  serverModel: any,
  version: any
) {
  const deviceSettingsModel = appModel.get('deviceSettings');
  const dismissedServerUpdateVersions = deviceSettingsModel.get(
    'dismissedServerUpdateVersions'
  );
  dispatch(
    saveDeviceSettings({
      dismissedServerUpdateVersions: {
        ...dismissedServerUpdateVersions,
        [serverModel.get('machineIdentifier')]: version,
      },
    })
  );
}
//
// Action Creators
export const fetchServerUpdateStatus = createActionCreator(
  FETCH_SERVER_UPDATE_STATUS
);
export const checkForServerUpdate = createActionCreator(
  CHECK_FOR_SERVER_UPDATE
);
export const dismissServerUpdate = createActionCreator(DISMISS_SERVER_UPDATE);
export const downloadServerUpdate = createActionCreator(DOWNLOAD_SERVER_UPDATE);
export const installServerUpdate = createActionCreator(INSTALL_SERVER_UPDATE);
//
// Action Handlers
export const serverUpdateActionHandlers = {
  [FETCH_SERVER_UPDATE_STATUS](appModel: any, payload: any) {
    const serverModel = findServerModel(appModel, payload);
    const serverFlexModel = findServerFlexModel(appModel, payload);
    const update = serverFlexModel.get('update');
    if (
      !serverModel.get('isFullOwnedServer') ||
      !serverModel.get('updater') ||
      serverModel.get('isOutOfDate') ||
      update.get('isChecking')
    ) {
      return;
    }
    update.set({ isChecking: true });
    update.fetch().then(
      () => {
        const state = update.get('state');
        // We don't want to set isChecking true when we suspect there will be no
        // socket events from the updater check. Without this isChecking would
        // remain true and the ServerSettingsUpdateView would be stuck in the
        // checking state.
        const isChecking =
          state === serverUpdateStates.DONE ||
          state === serverUpdateStates.ERROR ||
          state === serverUpdateStates.NONE;
        updaterCheckRequest(serverModel, { isChecking });
      },
      () => {
        update.set({ isChecking: false });
      }
    );
  },
  [CHECK_FOR_SERVER_UPDATE](appModel: any, payload: any) {
    const { download, initialState } = payload;
    const serverModel = findServerModel(appModel, payload);
    updaterCheckRequest(serverModel, { download, initialState });
  },
  [DISMISS_SERVER_UPDATE](appModel: any, payload: any, dispatch: any) {
    const serverModel = findServerModel(appModel, payload);
    const serverFlexModel = findServerFlexModel(appModel, payload);
    const update = serverFlexModel.get('update');
    // Skip via API is only available if the server supports auto install
    if (update.get('canInstall')) {
      getUpdaterApplyRequest(serverModel, { skip: 1 }).then(
        () => {
          dispatch(fetchServerUpdateStatus(payload));
        },
        () => {
          // Fallback to local storage when skip request fails.
          dismissServerUpdateVersion(
            appModel,
            dispatch,
            serverModel,
            payload.version
          );
        }
      );
    } else {
      dismissServerUpdateVersion(
        appModel,
        dispatch,
        serverModel,
        payload.version
      );
    }
  },
  [DOWNLOAD_SERVER_UPDATE](appModel: any, payload: any) {
    const serverModel = findServerModel(appModel, payload);
    updaterCheckRequest(serverModel, {
      download: true,
      initialState: serverUpdateStates.DOWNLOADING,
    });
  },
  [INSTALL_SERVER_UPDATE](appModel: any, payload: any) {
    const serverModel = findServerModel(appModel, payload);
    if (serverModel.get('activeConnection').get('isLoopback')) {
      install(serverModel);
    } else {
      const confirmModal = new ConfirmModal({
        title: t('Remote Installation'),
        confirmLabel: t('Continue'),
        cancelLabel: t('Cancel'),
        message: t(
          'The installer may display dialogs that require input. Make sure you have desktop access to the computer running Plex Media Server before continuing.'
        ),
      });
      dispatcher.trigger('show:modal', confirmModal);
      confirmModal.promise.then(() => install(serverModel));
    }
  },
};
