/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import ProviderIntegrationCollection from 'app/js/models/provider/ProviderIntegrationCollection';

const ProviderIntegrationsModel = AbstractModel.extend({
  //
  // Properties

  name: 'ProviderIntegrationsModel',

  sessionAttrs: {
    isIntegrationsOnboarding: attrTypes.boolean.isRequired,
    isIntegrationsPending: attrTypes.boolean.isRequired,
    isIntegrationsPopulated: attrTypes.boolean.isRequired,
    integrationsError: attrTypes.object,
  },

  sessionRelations: {
    integrations: {
      relationClass: ProviderIntegrationCollection,
      deferred: true,
    },
  },

  defaults: {
    isIntegrationsOnboarding: false,
    isIntegrationsPending: true,
    isIntegrationsPopulated: false,
  },
});

export default ProviderIntegrationsModel;
