/* eslint-plugin-disable @typescript-eslint */

import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';

export default function synthesizeDirectoryID(directoryKey: any) {
  const directoryKeyParts = directoryKey.split('/');
  // Discard the first item in the array, which will be an empty string
  const firstPart = directoryKeyParts.shift();

  // If the firstPart is an empty string that means we got a `/` as the first
  // item in the array, and this source has not been migrated yet
  if (firstPart === '') {
    const secondPart = directoryKeyParts[0];
    const lastPart = directoryKeyParts[directoryKeyParts.length - 1];

    let directoryID;

    // The directoryID of the EPG directory is the ID of the DVR, whcih will be
    // in the first segment
    if (providerIdentifiers.matches(secondPart, providerIdentifiers.EPG)) {
      directoryID = secondPart;
    } else if (lastPart) {
      directoryID = lastPart;
    }

    return directoryID;
  }

  return directoryKey;
}
