/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import DeviceSettingsModel from 'app/js/models/settings/DeviceSettingsModel';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import * as jsonStorage from 'app/js/utils/storage/jsonStorage';

const movedAttrs = ['debugVideoQualityOverlay', 'restorePlayQueue', 'volume'];

// Handles the migration of attributes from appModel.experienceSettings to
// appModel.deviceSettings
//
// This migration assumes that appModel.experience will be deleted by a later
// migration.

function migrateExperienceSettingsToDeviceSettings() {
  const deviceSettingsStorageKey = DeviceSettingsModel.prototype.storageKey;

  const promise = jsonStorage
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    .getJSON('experience')
    .then((existingExperienceSettings: any) => {
      const migratedDeviceSettings = _.pick(
        existingExperienceSettings,
        movedAttrs
      );

      if (Object.keys(migratedDeviceSettings).length === 0) {
        return;
      }

      return jsonStorage
        .getJSON(deviceSettingsStorageKey, {})
        .then((originalDeviceSettings: any) => {
          const updatedDeviceSettings = {
            ...originalDeviceSettings,
            ...migratedDeviceSettings,
          };

          return jsonStorage.setJSON(
            deviceSettingsStorageKey,
            updatedDeviceSettings
          );
        });
    });

  return alwaysResolve(promise);
}

export default migrateExperienceSettingsToDeviceSettings;
