/* eslint-plugin-disable @typescript-eslint */

function acceptEULA(server: any) {
  const serverFlexModel = server.get(__PLATFORM__);
  const settingsModel = serverFlexModel.get('settings');

  return settingsModel
    .populate()
    .then((settings: any) =>
      settings.get('AcceptedEULA').save({ AcceptedEULA: true })
    );
}

export default acceptEULA;
