/* eslint-plugin-disable @typescript-eslint */

import BaseClass from 'app/js/utils/BaseClass';
import capabilities from 'app/js/utils/device/capabilities';
import dispatcher from 'app/js/utils/dispatcher';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const AbstractReporter = BaseClass.extend({
  mixins: eventMixin,
  //
  // Properties
  name: 'AbstractReporter',
  enabled: false,
  destroyed: false,
  _suspended: false,
  //
  // Init
  initialize(options = {}) {
    if ((options as any).enabled !== false) {
      this.enable();
    }
  },
  //
  // Control
  hasSupport() {
    return true;
  },
  enable() {
    if (this.enabled || this.destroyed || !this.hasSupport()) {
      return;
    }
    this.enabled = true;
    this.listenTo(dispatcher, {
      'log:message': this.onLog,
      'log:group': this.onLog,
      'log:groupCollapsed': this.onLog,
      'log:groupEnd': this.onLog,
    });
    this.onEnabled();
  },
  disable() {
    if (!this.enabled) {
      return;
    }
    this.enabled = false;
    this.stopListening();
    this.onDisabled();
  },
  destroy() {
    if (this.destroyed) {
      return;
    }
    this.disable();
    this.destroyed = true;
    this.onDestroyed();
  },
  // Suspends accepting new log messages. Doesn't suspend actual reporting of
  // previously received log messages.
  _suspend() {
    if (this._suspended) {
      return;
    }
    this._suspended = true;
    this.onSuspended();
  },
  _resume() {
    if (!this._suspended) {
      return;
    }
    this._suspended = false;
    this.onResumed();
  },
  //
  // Listeners
  // Subclasses should implement the specific log type event handlers below.
  // This method serves a single gateway for the abstract to avoid reentrancy.
  onLog(log: any) {
    if (this._suspended) {
      if (capabilities.hasConsole) {
        window.console.warn(
          '[Logging] Ignored log reentrancy:',
          JSON.stringify(log)
        );
      }
      return;
    }
    // Manually suspend and resume reporting to avoid maximum callstack death by
    // reentrancy triggered when dependencies call `log` methods while a
    // previous log is processing.
    this._suspend();
    switch (log.type) {
      case 'log:message':
        this.onLogMessage(log);
        break;
      case 'log:group':
        this.onLogGroup(log);
        break;
      case 'log:groupCollapsed':
        this.onLogGroupCollapsed(log);
        break;
      case 'log:groupEnd':
        this.onLogGroupEnd(log);
        break;
    }
    this._resume();
  },
  // Logging event listeners subclasses are expected to implement.
  onLogMessage() {},
  onLogGroup() {},
  onLogGroupCollapsed() {
    // A collapsed group only makes sense in a browser.
    // Call `onLogGroup` if `onLogGroupCollapsed` is not defined.
    // eslint-disable-next-line prefer-rest-params, prefer-spread
    this.onLogGroup.apply(this, arguments);
  },
  onLogGroupEnd() {},
  // Event style extension points make it easy for subclasses to respond to
  // changes without overriding base methods.
  onEnabled() {},
  onDisabled() {},
  onResumed() {},
  onSuspended() {},
  onDestroyed() {},
});
export default AbstractReporter;
