/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import {
  changeCompanionServer,
  removeCompanionServer,
} from 'app/js/stores/actions/appActions';
import BaseClass from 'app/js/utils/BaseClass';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';

function isServerSupported(server: any) {
  return !!(
    server &&
    !server.get('isCloud') &&
    server.get('platform') !== 'iOS' &&
    server.supports('companionProxy')
  );
}

function isConnectionSupported(connection: any) {
  return !!(
    connection &&
    connection.get('isPrivate') &&
    connection.get('isConnected')
  );
}

function isEligibleForCompanion(server: any) {
  return !!(
    isServerSupported(server) &&
    isConnectionSupported(server.get('activeConnection'))
  );
}

function isLocallyOwned(server: any) {
  return !!(isEligibleForCompanion(server) && !server.get('isShared'));
}

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const CompanionServerManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Properties

  name: 'CompanionServerManager',

  //
  // Init

  initialize(options: any) {
    this.appModel = options.appModel;
    this.servers = this.appModel.get('servers');

    this._throttlePickCompanionServer = _.throttle(
      this._pickCompanionServer,
      2000
    );
  },

  //
  // Control

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  start(options: any) {
    this.stop();

    this.listenTo(
      this.servers,
      'change:connected change:activeConnection update',
      this._throttlePickCompanionServer
    );
    this.started = true;

    this._pickCompanionServer();
  },

  stop() {
    this.stopListening();
    this.started = false;
  },

  _pickCompanionServer() {
    if (!this.started) {
      return;
    }

    // We prefer not to switch companion servers if the current one is good enough
    if (
      this.companionServer &&
      isEligibleForCompanion(this.companionServer) &&
      this.servers.contains(this.companionServer)
    ) {
      return;
    }

    // Try and find a local AND owned server and, if nothing is found, fallback
    // to local and shared.
    this.companionServer =
      this.servers.find(isLocallyOwned) ||
      this.servers.find(isEligibleForCompanion);

    if (this.companionServer) {
      resolve('appStore').dispatch(
        changeCompanionServer({
          serverEntityID: this.companionServer.entityID(),
        })
      );

      log.info(
        '[Companion] Found companion',
        this.companionServer.get('debugName')
      );
    } else {
      resolve('appStore').dispatch(removeCompanionServer());
      log.warn('[Companion] No companion found');
    }
  },
});

export default CompanionServerManager;
