import { FunctionComponent } from 'react';
import {
  IconBook,
  IconCamera,
  IconGames,
  IconMovie,
  IconMusic,
  IconProps,
  IconTv,
  IconVideo,
} from '@plex/react-chroma-icon';
import {
  AUTHOR,
  GAMES,
  MOVIES,
  MUSIC,
  PHOTOS,
  TV,
  VIDEOS,
} from 'app/js/utils/types/sectionTypes';

function getSectionTypeIcon(
  typeKey: string
): FunctionComponent<IconProps> | undefined {
  switch (typeKey) {
    case MOVIES:
      return IconMovie;
    case MUSIC:
      return IconMusic;
    case PHOTOS:
      return IconCamera;
    case TV:
      return IconTv;
    case VIDEOS:
      return IconVideo;
    case GAMES:
      return IconGames;
    case AUTHOR:
      return IconBook;
  }

  return undefined;
}

export default getSectionTypeIcon;
