/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import dimensionsAdapter from 'app/js/adapters/dimensionsAdapter';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import getServerSettingsRoute from 'app/js/views/v3/helpers/routes/getServerSettingsRoute';

type ServerUpdateRequiredPageProps = {
  machineIdentifier?: string;
  friendlyName?: string;
  activeConnectionUri?: string;
  shouldUseNewSettingsRoutes?: boolean;
  isShared?: boolean;
};

function ServerUpdateRequiredPage(props: ServerUpdateRequiredPageProps) {
  const {
    friendlyName,
    activeConnectionUri,
    shouldUseNewSettingsRoutes,
    isShared,
  } = props;

  let description;

  if (friendlyName) {
    description = t(
      'The server {1} is out of date and cannot be used.',
      friendlyName
    );
  }

  description =
    description +
    ' ' +
    t(
      'This version of {productName} requires Plex Media Server {minServerVersion} or higher.',
      {
        productName: dimensionsAdapter.prop('X-Plex-Product'),
        minServerVersion: __MIN_SERVER_VERSION__,
      }
    );

  let route = '/web/index.html';
  if (!isShared) {
    route += shouldUseNewSettingsRoutes
      ? getServerSettingsRoute('settings', 'general')
      : '#!/settings/server';
  }

  return (
    <ErrorPage description={description} title="Update your Plex Media Server">
      <Button
        kind={kinds.PRIMARY}
        size={sizes.LARGE}
        target={__EXTERNAL_LINK_TARGET__}
        to={path.join([activeConnectionUri, route])}
      >
        {t('Open server directly')}
      </Button>
    </ErrorPage>
  );
}

export default ServerUpdateRequiredPage;
