/* eslint-plugin-disable @typescript-eslint */
/* eslint-disable prefer-rest-params */

import _ from 'underscore';
import parser2 from 'app/js/utils/parser2';

const coreTypes = ['boolean', 'integer', 'float', 'string', 'object', 'array'];

// @ts-expect-error ts-migrate(6133) FIXME: 'value' is declared but its value is never read.
function isAny(value: any) {
  return true;
}

function isObject(value: any) {
  return _.isObject(value) && !Array.isArray(value);
}

function createAttrType(typeName: any, options: any) {
  options || (options = {});

  const type = _.defaults(options, {
    typeName: typeName,
    parse: _.bind(parser2.parseType, parser2, typeName),
    stringify: _.bind(parser2.stringifyType, parser2, typeName),
  });

  type.validate = options.validate ? createValidator(options.validate) : isAny;

  // Add an `isRequired` property to the type, which returns the same type,
  // but disallows null or undefined as a valid value.
  type.isRequired = {
    ...type,
    validate: (value: any) => (value == null ? false : type.validate(value)),
    typeName: `${type.typeName}<required>`,
  };

  return type;
}

function createValidator(validator: any) {
  return function (value: any) {
    // By default, an attribute is valid if it's null or undefined.
    if (value == null) {
      return true;
    }

    return validator(value);
  };
}

const attrTypes = {
  // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
  any: createAttrType('any'),
  boolean: createAttrType('boolean', { validate: _.isBoolean }),
  integer: createAttrType('integer', { validate: Number.isInteger }),
  float: createAttrType('float', { validate: _.isNumber }),
  string: createAttrType('string', { validate: _.isString }),
  object: createAttrType('object', { validate: isObject }),
  array: createAttrType('array', { validate: Array.isArray }),
  list: createAttrType('list', { validate: Array.isArray }),
  encodedString: createAttrType('encodedString', { validate: _.isString }),
  queryString: createAttrType('queryString', { validate: isObject }),

  instanceOf: function () {
    const classes = _.toArray(arguments);

    return createAttrType('instanceOf', {
      validate: function (value: any) {
        return _.some(classes, function (klass) {
          return value instanceof klass;
        });
      },
    });
  },

  oneOf: function () {
    const possibleValues = _.toArray(arguments);

    return createAttrType('oneOf', {
      validate: function (value: any) {
        return _.contains(possibleValues, value);
      },
    });
  },

  oneOfType: function () {
    const types = _.toArray(arguments);

    return createAttrType('oneOfType', {
      validate: function (value: any) {
        return _.some(types, function (type) {
          return type.validate(value);
        });
      },
    });
  },

  custom: function (validator: any) {
    return createAttrType('custom', {
      validate: validator,
    });
  },

  // @ts-expect-error ts-migrate(7023) FIXME: 'getAttrTypeFor' implicitly has return type 'any' ... Remove this comment to see the full error message
  getAttrTypeFor: function (value: any) {
    if (value == null) {
      return attrTypes.any;
    }

    // If the value validates as one of the core types, use that attribute
    // type. Otherwise, use the wildcard type.

    for (let i = 0, len = coreTypes.length; i < len; i++) {
      const type = coreTypes[i];
      // @ts-expect-error ts-migrate(7022) FIXME: 'attrType' implicitly has type 'any' because it do... Remove this comment to see the full error message
      const attrType = attrTypes[type];

      if (attrType.validate(value)) {
        return attrType;
      }
    }

    return attrTypes.any;
  },
};

export default attrTypes;
