import { combineReducers } from '@reduxjs/toolkit';
import dashboardReducer from 'app/js/v4/features/dashboard/dashboardReducer';
import dvrManagementReducer from 'app/js/v4/features/dvrManagement/dvrManagementReducer';
import libraryManagementSlice from 'app/js/v4/features/libraryManagement/libraryManagementSlice';
import liveTVSlice from 'app/js/v4/features/livetv/liveTVSlice';

const featuresReducer = combineReducers({
  dashboard: dashboardReducer,
  dvrManagement: dvrManagementReducer,
  libraryManagement: libraryManagementSlice.reducer,
  livetv: liveTVSlice.reducer,
});

export default featuresReducer;
