/* eslint-plugin-disable @typescript-eslint */

import * as timelineStates from 'app/js/utils/constants/player/timelineStates';

export default function getTimelineState(playerModel: any) {
  if (!playerModel.get('isOpen')) {
    return timelineStates.STOPPED;
  }

  if (playerModel.get('hasError')) {
    return timelineStates.ERROR;
  }

  if (playerModel.get('isPaused')) {
    return timelineStates.PAUSED;
  }

  if (playerModel.get('isBuffering')) {
    return timelineStates.BUFFERING;
  }

  return timelineStates.PLAYING;
}
