/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import config from 'app/js/models/config';
import BaseClass from 'app/js/utils/BaseClass';
import log from 'app/js/utils/log/log';
import xhrTracker from 'app/js/utils/xhr/xhrTracker';

function getMatchingTestComparator(server: any, connection: any, url: any) {
  return function matchingTest(existing: any) {
    // Companion transient servers could share the same server ID but have different tokens,
    // which means connection tests can't be reused and they need to be tested independently and,
    // because of that we can't rely on comparing server IDs alone and need to actually compare instances
    // to make sure the servers are actually the same.
    return (
      existing.server === server &&
      existing.connection.id === connection.id &&
      existing.url === url
    );
  };
}

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ServerConnectionTestManager = BaseClass.extend({
  //
  // Init

  initialize(options: any) {
    this.appModel = options.appModel;
    this.queued = [];
    this.executing = [];
    this.maximumConcurrent = config.maximumConcurrentConnectionTests;
    this.throttledStartProcessing = _.throttle(
      this._startProcessing.bind(this),
      50,
      { leading: false }
    );
  },

  //
  // Control

  queueConnectionTest({ server, connection, url }: any) {
    const previous = this._queuedOrExecuting(server, connection, url);

    if (previous) {
      return previous.deferred.promise();
    }

    const deferred = $.Deferred();

    this.queued.push({
      server,
      connection,
      url,
      deferred,
    });

    this._sortQueue();

    // Throttle calls to startProcessing to avoid testing connections
    // before we have a chance to sort them.
    // `leading: false` prevents from calling startProcessing immediately.
    this.throttledStartProcessing();

    return deferred.promise();
  },

  abortConnectionTest({ server, connection, url }: any) {
    this._abortCurrentTest(server, connection, url);
    this._removeQueuedOrExecuting(server, connection, url);

    return $.Deferred().resolve().promise();
  },

  reset() {
    this.queued.length = 0;

    while (this.executing.length > 0) {
      this.abortConnectionTest(this.executing[0]);
    }
  },

  _sortQueue() {
    const lastPrimaryServerID = this.appModel
      .get('user')
      .get('lastPrimaryServerID');

    this.queued.sort((a: any, b: any) => {
      const serverA = a.server;
      const serverB = b.server;
      const connectionA = a.connection;
      const connectionB = b.connection;

      const manualConnectionA = this._isManualConnection(connectionA);
      const manualConnectionB = this._isManualConnection(connectionB);
      const matchingLocationA = this._isMatchingLocation(serverA, connectionA);
      const matchingLocationB = this._isMatchingLocation(serverB, connectionB);

      if (
        serverA.id === lastPrimaryServerID &&
        serverB.id !== lastPrimaryServerID
      ) {
        return -1;
      }
      if (
        serverB.id === lastPrimaryServerID &&
        serverA.id !== lastPrimaryServerID
      ) {
        return 1;
      }

      if (serverA.get('isCloud') && !serverB.get('isCloud')) {
        return -1;
      }
      if (serverB.get('isCloud') && !serverA.get('isCloud')) {
        return 1;
      }

      if (!connectionA.get('relay') && connectionB.get('relay')) {
        return -1;
      }
      if (!connectionB.get('relay') && connectionA.get('relay')) {
        return 1;
      }

      if (!connectionA.get('isConnected') && connectionB.get('isConnected')) {
        return -1;
      }
      if (!connectionB.get('isConnected') && connectionA.get('isConnected')) {
        return 1;
      }

      if (!serverA.get('isShared') && serverB.get('isShared')) {
        return -1;
      }
      if (!serverB.get('isShared') && serverA.get('isShared')) {
        return 1;
      }

      if (serverA.get('isFullServer') && !serverB.get('isFullServer')) {
        return -1;
      }
      if (serverB.get('isFullServer') && !serverA.get('isFullServer')) {
        return 1;
      }

      if (manualConnectionA && !manualConnectionB) {
        return -1;
      }
      if (manualConnectionB && !manualConnectionA) {
        return 1;
      }

      if (connectionA.get('isBundled') && !connectionB.get('isBundled')) {
        return -1;
      }
      if (connectionB.get('isBundled') && !connectionA.get('isBundled')) {
        return 1;
      }

      if (matchingLocationA && !matchingLocationB) {
        return -1;
      }
      if (matchingLocationB && !matchingLocationA) {
        return 1;
      }

      return 0;
    });
  },

  _startProcessing() {
    _.times(
      this.maximumConcurrent - this.executing.length,
      this._process.bind(this)
    );
  },

  _process() {
    if (this.executing.length >= this.maximumConcurrent) {
      return;
    }

    if (!this.queued.length) {
      return;
    }

    const connectionTest = this.queued.shift();
    const { server, connection, url } = connectionTest;

    // If there is a pending request still from a previous test, abort that
    // request before we kick off a new one.
    this._abortCurrentTest(server, connection, url);

    const testServerConnection = resolve('testServerConnection');
    const promise = testServerConnection({ server, connection, url });
    this.executing.push(connectionTest);

    promise.done((connection_: any, attributes: any) => {
      connectionTest.deferred.resolve(connection_, attributes);
    });

    promise.fail((connection_: any, xhr: any) => {
      connectionTest.deferred.reject(connection_, xhr);
    });

    promise.always(() => {
      const index = this.executing.indexOf(connectionTest);

      this.executing.splice(index, 1);
      this._process();
    });
  },

  _abortCurrentTest(server: any, connection: any, connectionTestUrl: any) {
    if (!connection.get('currentTest')) {
      return;
    }

    const currentTests = connection.get('currentTest');

    currentTests.forEach((currentTest: any) => {
      const url = server.resolveUrl(connectionTestUrl || currentTest.url, {
        connection: connection,
      });

      if (currentTest && currentTest.state() === 'pending') {
        log(
          '[Connections] Aborting pending connection test for %s at %s',
          server.get('debugName'),
          url
        );
        xhrTracker.abort(currentTest.rid);
        currentTest.abort();
      }
    });
  },

  _isManualConnection(connection: any) {
    return connection.get('sources').some((source: any) => {
      return source.id.indexOf('manualServer') === 0;
    });
  },

  _isMatchingLocation(server: any, connection: any) {
    const publicAddressMatches = server.get('publicAddressMatches');
    const isPrivate = connection.get('isPrivate');

    return (
      (publicAddressMatches && isPrivate) ||
      (!publicAddressMatches && !isPrivate)
    );
  },

  _queuedOrExecuting(server: any, connection: any, url: any) {
    const comparator = getMatchingTestComparator(server, connection, url);

    return (
      _.find(this.executing, comparator) || _.find(this.queued, comparator)
    );
  },

  _removeQueuedOrExecuting(server: any, connection: any, url: any) {
    const comparator = getMatchingTestComparator(server, connection, url);
    const executingIndex = _.findIndex(this.executing, comparator);
    const queuedIndex = _.findIndex(this.queued, comparator);

    if (executingIndex >= 0) {
      this.executing.splice(executingIndex, 1);
    }

    if (queuedIndex >= 0) {
      this.queued.splice(queuedIndex, 1);
    }
  },
});

export default ServerConnectionTestManager;
