/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { createPopper } from '@popperjs/core';
import assert from 'app/js/utils/log/assert';
import Portal from 'app/js/views/v3/components/core/base/Portal';
// @ts-expect-error ts-migrate(6133) FIXME: 'forwardedRefShape' is declared but its value is n... Remove this comment to see the full error message
import forwardedRefShape from 'app/js/views/v3/helpers/props/shapes/forwardedRefShape';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';

/*
(ts-migrate) TODO: Migrate the remaining prop types
...Portal.propTypes
*/
interface PopperPortalProps {
  target: string;
  options?: any;
  // @ts-expect-error ts-migrate(2749) FIXME: 'forwardedRefShape' refers to a value, but is bein... Remove this comment to see the full error message
  forwardedRef?: forwardedRefShape;
  onOpen?: (...args: any[]) => any;
  onClose?: (...args: any[]) => any;
}

class PopperPortal extends Component<PopperPortalProps> {
  static defaultProps: any;

  // eslint-disable-next-line react/sort-comp
  _popper: any;
  _ref: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this._ref = props.forwardedRef || React.createRef();
    this._popper = null;
  }

  shouldComponentUpdate(nextProps: PopperPortalProps, nextState: any) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  componentDidUpdate(prevProps: PopperPortalProps) {
    if (this._popper) {
      // Make popper recalculate offset if the component changes
      this._popper.update();
    }

    if (__DEV__) {
      assert(
        this.props.forwardedRef === prevProps.forwardedRef,
        'The `forwardedRef` prop provided to `PopperPortal` should not change'
      );
    }
  }

  componentWillUnmount() {
    this._close();
  }

  //
  // Control

  _open() {
    const { target, options } = this.props;

    const targetElement =
      document.getElementById(target) || document.getElementsByName(target)[0];
    this._popper = createPopper(
      targetElement,
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Element | Text | null' is not as... Remove this comment to see the full error message
      ReactDOM.findDOMNode(this._ref.current),
      options
    );

    // Workaround to ensure the popper has the correct position.
    this._popper.update();
  }

  _close() {
    if (this._popper) {
      this._popper.destroy();
      this._popper = null;
    }
  }

  //
  // Listeners

  onOpen = () => {
    this._open();

    this.props.onOpen?.();
  };

  onClose = () => {
    this._close();

    this.props.onClose?.();
  };

  //
  // Render

  render() {
    return (
      <Portal
        {...this.props}
        ref={this._ref}
        // @ts-expect-error ts-migrate(2322) FIXME: Type '{ ref: any; onOpen: () => void; onClose: () ... Remove this comment to see the full error message
        onClose={this.onClose}
        onOpen={this.onOpen}
      />
    );
  }
}

export const defaultProps = {
  options: {
    placement: 'bottom-start',
  },
};

PopperPortal.defaultProps = defaultProps;

export default React.forwardRef((props, ref) => {
  return <PopperPortal {...props} forwardedRef={ref} />;
});
