import { combineReducers } from '@reduxjs/toolkit';
import dashboardBandwidthSlice from './dashboardBandwidthSlice';
import dashboardPlayHistorySlice from './dashboardPlayHistorySlice';
import dashboardResourcesSlice from './dashboardResourcesSlice';
import dashboardTopPlayedSlice from './dashboardTopPlayedSlice';
import dashboardTopUsersSlice from './dashboardTopUsersSlice';

const dashboardReducer = combineReducers({
  bandwidth: dashboardBandwidthSlice.reducer,
  playHistory: dashboardPlayHistorySlice.reducer,
  resources: dashboardResourcesSlice.reducer,
  topPlayed: dashboardTopPlayedSlice.reducer,
  topUsers: dashboardTopUsersSlice.reducer,
});

export default dashboardReducer;
