/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const defaultStartOffset = -1;
const defaultEndOffset = -1;

const LyricsSpanModel = AbstractModel.extend({
  //
  // Properties

  name: 'LyricsSpanModel',
  context: ['server', 'section'],

  sessionAttrs: {
    active: attrTypes.boolean,
  },

  syncedAttrs: {
    startOffset: attrTypes.integer,
    endOffset: attrTypes.integer,
    text: attrTypes.string,
  },

  defaults: {
    startOffset: defaultStartOffset,
    endOffset: defaultEndOffset,
  },

  //
  // Control

  isPositionWithin(position: any) {
    const startOffset = this.get('startOffset');
    const endOffset = this.get('endOffset');

    if (startOffset === defaultStartOffset) {
      return false;
    }

    const isAfterStart = position >= startOffset;
    const isBeforeEnd = position <= endOffset || endOffset === defaultEndOffset;

    return isAfterStart && isBeforeEnd;
  },
});

export default LyricsSpanModel;
