import { PayloadAction } from '@reduxjs/toolkit';
import { ServerActivity } from 'app/js/v4/core/serverActivities/serverActivitiesTypes';
import { ServerSetting } from 'app/js/v4/core/serverSettings/serverSettingsTypes';

export type ServersState = object;

export enum ServerPlatform {
  Windows = 'Windows',
  Android = 'Android',
  Linux = 'Linux',
  FreeBSD = 'FreeBSD',
  MacOSX = 'MacOSX',
  IOS = 'iOS',
}

export interface Server {
  entityID: string;
  machineIdentifier: string;
  accessToken?: string;
  uri?: string;
  version?: string;
  platform?: ServerPlatform;
  friendlyName: string;
  sourceTitle?: string;
  countryCode?: string;
  allowSync: boolean;
  allowMediaDeletion: boolean;
  backgroundProcessing: boolean;
  transcoderVideo: boolean;
  isCloud: boolean;
  isEmbedded: boolean;
  isFullServer: boolean;
  isFullOwnedServer: boolean;
  isOutOfDate: boolean;
  isUnavailable: boolean;
  isUnauthorized: boolean;
  isConnected: boolean;
  isShared: boolean;
  isTransient: boolean;
  livetv?: number;
  connectionTestResult: ConnectionTestResult;
  ownerId?: number;
  ownerFeatures?: string[];
}

export interface UnstableServer {
  entityID: string;
  machineIdentifier: string;
  accessToken?: string;
  version?: string;
  platform?: ServerPlatform;
  friendlyName: string;
  sourceTitle?: string;
  countryCode?: string;
  allowSync: boolean;
  allowMediaDeletion: boolean;
  backgroundProcessing: boolean;
  transcoderVideo: boolean;
  isClaimed?: boolean;
  isCloud: boolean;
  isEmbedded: boolean;
  isFullServer: boolean;
  isFullOwnedServer: boolean;
  isOutOfDate: boolean;
  isShared: boolean;
  isUnavailable: boolean;
  isUnauthorized: boolean;
  isConnected: boolean;
  isTransient: boolean;
  livetv?: number;
  ownerId?: number;
  ownerFeatures?: string[];
  activeConnection?: { uri: string };
  connectionTestResult: ConnectionTestResult;
  providers: string[];
  desktop?: {
    activities: ServerActivity[];
    settings: ServerSetting[];
  };
}

export type ServerConnectedAction = PayloadAction<{
  server: Server;
}>;

export type ServerLibraryScanUpdatedAction = PayloadAction<{
  server: Server;
}>;

export enum ConnectionTestResult {
  Pending = 'pending',
  Connected = 'connected',
  Unavailable = 'unavailable',
  Unauthorized = 'unauthorized',
}
