/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import config from 'app/js/models/config';
import ImageCache from 'app/js/utils/cache/ImageCache';
import getPixelRatio from 'app/js/utils/constants/getPixelRatio';
import capabilities from 'app/js/utils/device/capabilities';
import transcoder from 'app/js/utils/transcoder';

// From: http://css-tricks.com/snippets/html/base64-encode-of-1x1px-transparent-gif/
const placeholderSrc =
  'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7';

const BaseImage = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'img',
  background: false,
  backgroundSize: '',

  cache: new ImageCache(),

  //
  // Init

  initialize: function (options: any) {
    options || (options = {});

    _.bindAll(this, 'onLoad', 'onError', 'loadImage');

    this.url = options.url;
    this.server = options.server;
    this.placeholder = options.placeholder;
    this.placeholderClass = options.placeholderClass;
    this.modelAttribute = options.modelAttribute;
    this.ignoreAttributeChanges = options.ignoreAttributeChanges;
    this.exposeUrl = options.exposeUrl;
    this.resetOnError = options.resetOnError;

    this.emptyImage = options.explicitEmpty
      ? 'url(' + placeholderSrc + ')'
      : '';

    if (options.background) {
      this.background = options.background;
    }

    if (options.backgroundSize) {
      this.backgroundSize = options.backgroundSize;
    }

    if (options.format) {
      this.format = options.format;
    }

    // 0 to 100: JPEG quality default: 75
    if (options.quality) {
      this.quality = options.quality;
    }

    if (_.isFunction(options.getUrl)) {
      this.getUrl = options.getUrl;
    }

    this.allowBaseImageBlobs = config.allowBaseImageBlobs;
    this.allowBaseImagePreloading = config.allowBaseImagePreloading;

    this.setup(options);
  },

  setup: function (options: any) {
    this.setDimensions(options);
    this.setFilters(options.filters);
    this.setModel(options.model, options);
  },

  setDimensions: function (options: any) {
    if (options.width && options.height) {
      this.transcode = true;
      this.width = options.width;
      this.height = options.height;
    }
  },

  // @param {Object} filterOptions
  // @param {Number} filterOptions.blur         0 to 100: blur radius applied to composed image
  // @param {String} filterOptions.background   '000000' to 'ffffff': solid color composed behind image
  // @param {Number} filterOptions.opacity      0 to 100: transparency applied to image when composed with "background" color
  // @param {Number} filterOptions.saturation   0 to 100: saturation of image when composed with "background" color
  setFilters: function (filterOptions: any) {
    this.filters = filterOptions || undefined;
  },

  hasFilters: function () {
    return this.filters !== undefined;
  },

  setModel: function (model: any, options: any) {
    options || (options = {});

    // Clean up any listeners in case we are changing models.
    this.stopListening();

    // Clear up previous state
    this.src = null;

    this.model = model;

    if (!model) {
      return;
    }

    if (!this.server) {
      this.server = model.server;
    }

    // If a url was passed in explicitly, don't go any further.
    if (options.url) {
      return;
    }

    if (this.modelAttribute) {
      this.url = this.getUrl(model.get(this.modelAttribute));

      if (!this.ignoreAttributeChanges) {
        this.listenTo(model, 'change:' + this.modelAttribute, this.onChange);
      }
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: '$el' is declared but its value is never read.
  setElement: function ($el: any) {
    // eslint-disable-next-line prefer-rest-params
    Marionette.ItemView.prototype.setElement.apply(this, arguments);

    // If the element changes, we need to show the image on the new element.
    this.render({ force: true });
  },

  //
  // Control
  getUrl: function (url: any) {
    // This lets us override the url used by the onChange listener.
    return url;
  },

  getTranscodeUrl: function (url: any) {
    if (!this.server || !this.transcode) {
      return url;
    }

    const appModel = adapters.resolve('appModel');
    const pixelRatio = getPixelRatio();

    return transcoder.image(this.server, url, {
      fallbackServer: appModel.get('primaryServer'),
      width: this.width * pixelRatio,
      height: this.height * pixelRatio,
      minSize: this.minSize,
      filters: this.filters,
      format: this.format,
      quality: this.quality,
    });
  },

  render: function (options: any) {
    options || (options = {});

    if (!this.url && !this.placeholder && !this.placeholderClass) {
      return this;
    }

    if (this.src) {
      // If the image has already been loaded but the force flag is set,
      // update the image again because the element might have changed.
      if (options.force) {
        this.showImage(this.src);
      }

      return this;
    }

    // First set the placeholder image if there is one, then load the actual
    // image to replace the placeholder if successful.

    if (!this.isPlaceholderRendered || !this.url) {
      this.showPlaceholder();
    }

    if (this.url) {
      this.load(this.url, this.transcode).then(
        this.onLoad,
        this.onError,
        this.onLoad
      );
    }

    return this;
  },

  showPlaceholder: function () {
    this.isPlaceholderRendered = true;

    this.$el.removeClass('loaded');

    if (this.placeholderClass) {
      this.$el.addClass(this.placeholderClass);
      this.$el.css('background-image', this.emptyImage);
      return;
    }

    if (!this.placeholder) {
      this.clearImage();
      return;
    }

    this.showImage(this.placeholder);
  },

  clearImage: function () {
    if (this.background) {
      // If you're not using a placeholder and you're rendering to
      // background then clear the background image.
      this.$el.css('background-image', this.emptyImage);
    } else {
      // Use a transparent image to prevent the default white border that
      // is used in some browsers for empty images.
      this.showImage(placeholderSrc);
    }
  },

  showImage: function (src: any) {
    if (this.background) {
      this.showBackgroundImage(src);
      return;
    }

    // IE will occasionally add width and height attributes when changing
    // the src of an image.
    this.$el.attr('src', src).removeAttr('width').removeAttr('height');
  },

  showBackgroundImage: function (src: any) {
    this.$el.css('background-image', 'url(' + src + ')');
  },

  load: function (url: any) {
    if (!url) {
      return $.Deferred().reject().promise();
    }

    url = this.getTranscodeUrl(url);

    // Expose the url used for the image as a data attribute so third
    // parties have a way of getting the source image.
    if (this.exposeUrl) {
      this.$el.attr('data-image-url', url);
    }

    if (
      this.allowBaseImageBlobs &&
      capabilities.hasBlob &&
      capabilities.hasObjectUrl
    ) {
      let promise = this.loadBlob(url);

      // Still use loadImage to preload the image so there is no flicker
      // between the placeholder and the blob image.
      promise = promise.then(this.loadImage, null, this.loadImage);

      return promise;
    } else if (!this.allowBaseImagePreloading) {
      return $.Deferred().resolve(url).promise();
    } else {
      return this.loadImage(url);
    }
  },

  loadBlob: function (url: any) {
    const deferred = $.Deferred();

    // Cancel the current request if it is still waiting.
    this.cancel();

    // First try to pull the image or a placeholder from the cache.
    const cachedBlobUrl = this.cache.getBlobUrl(url);

    if (cachedBlobUrl) {
      deferred.resolve(cachedBlobUrl);
      return deferred.promise();
    } else {
      const cachedPlaceholder = this.cache.getPlaceholder(url);
      if (cachedPlaceholder) {
        deferred.notify(cachedPlaceholder, true);
      }
    }

    // Request the image data and create a Blob which is stored by the
    // browser and then referenced with an object url.

    const xhr = new window.XMLHttpRequest();
    xhr.open('GET', url, true);
    xhr.responseType = 'arraybuffer';

    // Keep a reference to the request so we can abort if necessary.
    this.xhr = xhr;

    xhr.onreadystatechange = _.bind(function () {
      if (xhr.readyState === 4) {
        xhr.onreadystatechange = null;
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.xhr = null;

        if (xhr.status === 200) {
          const blob = new window.Blob([xhr.response], { type: 'image/jpeg' });
          const blobUrl = window.URL.createObjectURL(blob);

          // Store the blobUrl in the image cache so it can be used again
          // or revoked as needed.
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.cache.store({ url: url, blobUrl: blobUrl });

          deferred.resolve(blobUrl);
        } else {
          deferred.reject();
        }
      }
    }, this);

    xhr.send();

    return deferred.promise();
  },

  loadImage: function (url: any, placeholder: any) {
    const deferred = $.Deferred();
    const img = new window.Image();

    img.onload = function () {
      img.onload = img.onabort = img.onerror = null;
      placeholder
        ? deferred.notify(url, img.width, img.height)
        : deferred.resolve(url, img.width, img.height);
    };

    img.onabort = img.onerror = function () {
      img.onload = img.onabort = img.onerror = null;
      deferred.reject();
    };

    img.src = url;

    return deferred.promise();
  },

  cancel: function () {
    if (!this.xhr) {
      return;
    }

    this.xhr.onreadystatechange = null;
    this.xhr.abort();
    this.xhr = null;
  },

  //
  // Listeners

  onLoad: function (src: any, width: any, height: any) {
    if (this.backgroundSize) {
      // Determine handling of background images larger than the element size.
      const value =
        (width && width > this.width) || (height && height > this.height)
          ? this.backgroundSize
          : 'auto';
      this.$el[0].style.backgroundSize = value;
    }

    this.src = src;

    if (this.placeholderClass) {
      this.$el.removeClass(this.placeholderClass);
    }

    this.$el.addClass('loaded');

    this.showImage(src);
    this.trigger('load', src, width, height);
  },

  onError: function () {
    if (this.resetOnError) {
      this.showPlaceholder();
    }
    this.trigger('error');
  },

  onClose: function () {
    this.cancel();
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
  onChange: function (model: any, url: any) {
    url = this.getUrl(url);

    if (this.url === url) {
      return this;
    }

    this.url = url;
    this.src = null;

    this.render();
  },
});

export default BaseImage;
