import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import createServerFlexSelector from 'app/js/stores/selectors/server/createServerFlexSelector';
import activityTypeWhitelist from 'app/js/utils/activityTypeWhitelist';
import NavBarEmbeddedServerActivityMenuStatusItem from './NavBarEmbeddedServerActivityMenuStatusItem';

function createMapStateToProps() {
  return createSelector(createServerFlexSelector(), (serverFlexModel) => {
    const activities =
      serverFlexModel && serverFlexModel.activities
        ? serverFlexModel.activities.filter(
            // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
            (activity: any) => activityTypeWhitelist[activity.type]
          )
        : [];

    return { activities };
  });
}

const NavBarEmbeddedServerActivityMenuStatusItemConnector = connect(
  createMapStateToProps
)(NavBarEmbeddedServerActivityMenuStatusItem);

export default NavBarEmbeddedServerActivityMenuStatusItemConnector;
