/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import { motion } from 'framer-motion';
import React from 'react';
import colors from 'app/js/views/v3/styles/variables/colors';
import styles from './CircularProgressBar.css';

export interface CircularProgressBarProps {
  className?: string;
  containerClassName?: string;
  backgroundClassName?: string;
  textClassName?: string;
  size?: number;
  progress: number;
  progressText?: string | number;
  lineWidth?: number;
  strokeColor?: string;
  trackColor?: string;
  showIndeterminate?: boolean;
  showProgressText?: boolean;
  smoothProgressDuration?: number;
  easing?: 'linear' | 'easeOut';
}

function CircularProgressBar(props: CircularProgressBarProps) {
  const {
    className = styles.circularProgressBar,
    containerClassName = styles.circularProgressBarContainer,
    backgroundClassName = styles.circularProgressBarBackground,
    textClassName = styles.circularProgressBarText,
    size = 60,
    lineWidth = 5,
    strokeColor = colors.primaryColor,
    trackColor,
    progress,
    progressText = `${Math.round(progress)}%`,
    easing = 'easeOut',
    showProgressText = true,
    showIndeterminate,
    smoothProgressDuration = 1000,
  } = props;

  const duration = smoothProgressDuration / 1000;

  const finalProgress = showIndeterminate ? 50 : progress;
  const finalProgressText = showIndeterminate ? null : progressText;

  const center = size / 2;
  const radius = center - lineWidth;
  const circumference = Math.PI * (radius * 2);
  const sizeInPixels = size + 'px';
  const backgroundSizeInPixels = size - lineWidth * 3 + 'px';
  const strokeDashoffset = ((100 - finalProgress) / 100) * circumference;

  return (
    <div
      className={containerClassName}
      style={{
        width: sizeInPixels,
        height: sizeInPixels,
        lineHeight: sizeInPixels,
      }}
    >
      <svg
        className={classNames(
          className,
          showIndeterminate && styles.indeterminate
        )}
        height={size}
        version="1.1"
        width={size}
        xmlns="http://www.w3.org/2000/svg"
      >
        {trackColor ? (
          <circle
            cx={center}
            cy={center}
            fill="transparent"
            r={radius}
            style={{
              strokeWidth: lineWidth,
              stroke: trackColor,
            }}
          />
        ) : null}
        <motion.circle
          animate={{
            strokeDashoffset,
          }}
          cx={center}
          cy={center}
          fill="transparent"
          initial={true}
          r={radius}
          strokeDasharray={circumference}
          strokeDashoffset={strokeDashoffset}
          style={{
            strokeWidth: lineWidth,
            stroke: strokeColor,
          }}
          transition={{ duration, ease: easing }}
        />
      </svg>

      <div
        className={backgroundClassName}
        style={{
          width: backgroundSizeInPixels,
          height: backgroundSizeInPixels,
        }}
      />

      {showProgressText && finalProgressText ? (
        <div className={textClassName}>{finalProgressText}</div>
      ) : null}
    </div>
  );
}

export default CircularProgressBar;
