import localizerAdapter from 'app/js/adapters/localizerAdapter';
import { InterpolationValue } from 'app/js/utils/TranslationTypes';

export default function t(
  translation: string,
  valuesTemplate?: InterpolationValue | { [key: string]: InterpolationValue },
  ...args: InterpolationValue[]
): string {
  return localizerAdapter.t(translation, valuesTemplate, ...args);
}

export function tEscape(
  translation: string,
  valuesTemplate?: InterpolationValue | { [key: string]: InterpolationValue },
  ...args: InterpolationValue[]
): string {
  return localizerAdapter.tEscape(translation, valuesTemplate, ...args);
}
