/* eslint-plugin-disable @typescript-eslint */

import { AnimatePresence, motion } from 'framer-motion';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import * as keyCodes from 'app/js/utils/constants/keyCodes';
import Portal from 'app/js/views/v3/components/core/base/Portal';
import MetricsPageTracker from 'app/js/views/v3/components/core/metrics/MetricsPageTracker';
import cssDurationToSeconds from 'app/js/views/v3/helpers/css/cssDurationToSeconds';
import * as focusManager from 'app/js/views/v3/helpers/focus/focusManager';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import shouldPureComponentUpdate from 'app/js/views/v3/helpers/shouldPureComponentUpdate';
import animations from 'app/styles/variables/animations';
import easings from 'app/styles/variables/easings';
import styles from './Modal.css';

const modalSpeedSeconds = cssDurationToSeconds(animations.modalSpeed);
const modalSpeedMilliseconds = modalSpeedSeconds * 1000;

const opacityTransition = {
  ease: 'easeOut',
  duration: modalSpeedSeconds,
};

const scaleTransition = {
  ease: easings.motionEasing,
  duration: modalSpeedSeconds,
};

const backdropTransition = {
  opacity: opacityTransition,
};

const backdropVariants = {
  enter: {
    opacity: 1,
    transition: backdropTransition,
  },
  exit: {
    opacity: 0,
    transition: backdropTransition,
  },
};

const containerTransition = {
  scale: scaleTransition,
  opacity: opacityTransition,
};

const modalContainerVariants = {
  enter: {
    opacity: 1,
    scale: 1,
    transition: containerTransition,
  },
  exit: {
    opacity: 0,
    scale: 0.95,
    transition: containerTransition,
  },
};

interface ModalProps {
  className?: string;
  backdropClassName?: string;
  containerClassName?: string;
  isOpen: boolean;
  metricsPage: string;
  metricsProperties?: any;
  onModalClose: () => void;
  preventCloseOnBackdropPress?: boolean;
  size?: typeof sizes[keyof typeof sizes];
}

class Modal extends Component<ModalProps> {
  static defaultProps = {
    backdropClassName: styles.modalBackdrop,
    containerClassName: styles.modalContainer,
    size: sizes.MEDIUM,
    preventCloseOnBackdropPress: false,
  };

  // eslint-disable-next-line react/sort-comp
  _autoFocusTimeoutID: any;
  _isBackdropPressed: any;

  //
  // Lifecycle

  componentDidMount() {
    if (this.props.isOpen) {
      this._openModal();
    }
  }

  shouldComponentUpdate(nextProps: ModalProps, nextState: any) {
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    return shouldPureComponentUpdate(this, nextProps, nextState);
  }

  componentDidUpdate(prevProps: ModalProps) {
    if (!prevProps.isOpen && this.props.isOpen) {
      this._openModal();
    } else if (prevProps.isOpen && !this.props.isOpen) {
      this._closeModal();
    }
  }

  componentWillUnmount() {
    if (this.props.isOpen) {
      this._closeModal();
    }
  }

  //
  // Control

  _openModal() {
    focusManager.startScopedFocus(ReactDOM.findDOMNode(this.refs.modal));

    this._autoFocusTimeoutID = window.setTimeout(
      focusManager.scopeAutoFocus,
      modalSpeedMilliseconds
    );

    document.addEventListener('keydown', this.onKeyDown);
  }

  _closeModal() {
    window.clearTimeout(this._autoFocusTimeoutID);

    document.removeEventListener('keydown', this.onKeyDown);
  }

  _isBackdropTarget(event: any) {
    const targetElement = this._findEventTarget(event);

    if (targetElement) {
      const modalElement = ReactDOM.findDOMNode(this.refs.modal);

      return !modalElement || !modalElement.contains(targetElement);
    }

    return false;
  }

  _findEventTarget(event: any) {
    const changedTouches = event.changedTouches;

    if (!changedTouches) {
      return event.target;
    }

    if (changedTouches.length === 1) {
      const touch = changedTouches[0];

      return document.elementFromPoint(touch.clientX, touch.clientY);
    }
  }

  //
  // Listeners

  onBackdropBeginPress = (event: any) => {
    this._isBackdropPressed = this._isBackdropTarget(event);
  };

  onBackdropEndPress = (event: any) => {
    if (
      !this.props.preventCloseOnBackdropPress &&
      this._isBackdropPressed &&
      this._isBackdropTarget(event)
    ) {
      this.props.onModalClose();
    }

    this._isBackdropPressed = false;
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onTouchMove = (event: any) => {
    this._isBackdropPressed = false;
  };

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onTouchCancel = (event: any) => {
    this._isBackdropPressed = false;
  };

  onKeyDown = (event: any) => {
    const keyCode = event.keyCode;

    if (keyCode === keyCodes.TAB) {
      focusManager.scopeTabEvent(event);
    } else if (keyCode === keyCodes.ESCAPE) {
      event.preventDefault();
      event.stopPropagation();

      this.props.onModalClose();

      // Since the user used the keyboard to close the modal, attempt to
      // automatically restore the previously focused element to retain the
      // focus position.

      focusManager.restorePreviousFocus();
    }
  };

  //
  // Render

  render() {
    const {
      className,
      backdropClassName,
      containerClassName,
      children,
      size,
      isOpen,
      metricsPage,
      metricsProperties,
    } = this.props;

    return (
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ children: Element; isOpen: boolean; closeT... Remove this comment to see the full error message
      <Portal
        className={containerClassName}
        closeTimeoutMilliseconds={modalSpeedMilliseconds}
        isOpen={isOpen}
      >
        <AnimatePresence>
          {isOpen ? (
            <motion.div
              key="backdrop"
              animate="enter"
              className={backdropClassName}
              exit="exit"
              initial="exit"
              variants={backdropVariants}
              onMouseDown={this.onBackdropBeginPress}
              onMouseUp={this.onBackdropEndPress}
              onTouchCancel={this.onTouchCancel}
              onTouchEnd={this.onBackdropEndPress}
              onTouchMove={this.onTouchMove}
              onTouchStart={this.onBackdropBeginPress}
            >
              <motion.div
                key="modal"
                // @ts-expect-error ts-migrate(2322) FIXME: Type 'string' is not assignable to type '((instanc... Remove this comment to see the full error message
                ref="modal"
                animate="enter"
                className={className || styles[size!]}
                exit="exit"
                initial="exit"
                variants={modalContainerVariants}
              >
                <MetricsPageTracker {...metricsProperties} page={metricsPage}>
                  {children}
                </MetricsPageTracker>
              </motion.div>
            </motion.div>
          ) : null}
        </AnimatePresence>
      </Portal>
    );
  }
}

export default Modal;
