import * as connectionTestStates from 'app/js/utils/constants/server/connectionTestStates';
import capabilities from 'app/js/utils/device/capabilities';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import log from 'app/js/utils/log/log';
