export default function testServerConnection(options: any) {
  const { server, connection } = options;
  const url = server.resolveUrl(options.url || '/media/providers', {
    connection,
  });
  const appModel = resolve('appModel');
  const settings = appModel.get('settings');
  const deviceSettingsModel = appModel.get('deviceSettings');
  // Before starting the connection test check if it should be
  // rejected for the reasons list below.
  // The app doesn't allow https connections to anything except plex.tv and the
  // connection is https
  if (
    !config.allowHttpsMediaServerConnections &&
    connection.get('isHttps') &&
    !server.get('isCloud')
  ) {
    return rejectSecureConnectionTest(url, options);
  }
  // The user prefers insecure connections for local connections
  // and a local connection is being tested
  const preferInsecureConnections =
    settings && settings.get('preferInsecureConnections');
  // The server does not require secure connections and prefers insecure
  // connections for all connections (except plex.tv).
  if (
    !server.get('httpsRequired') &&
    !server.get('isCloud') &&
    connection.get('isHttps') &&
    preferInsecureConnections === preferInsecureConnectionsOptions.ALWAYS
  ) {
    return rejectSecureConnectionTest(url, options);
  }
  // The server does not require secure connections, prefers insecure
  // connections for local connections and a local connection is being
  // tested for a local server.
  if (
    !server.get('httpsRequired') &&
    connection.get('isHttps') &&
    preferInsecureConnections ===
      preferInsecureConnectionsOptions.SAME_NETWORK &&
    connection.get('isPrivate') &&
    server.get('publicAddressMatches')
  ) {
    return rejectSecureLocalConnectionTest(url, options);
  }
  // The app is hosted over https, mixed content is not allowed and the
  // connection is http.
  if (
    !config.allowMixedContent &&
    capabilities.isHostedHttps &&
    !connection.get('isHttps')
  ) {
    return rejectMixedContentTest(url, options);
  }
  // Not all platforms have deviceSettings, so need to check if they exist first
  const rejectDirectConnectionsSetting =
    deviceSettingsModel && deviceSettingsModel.get('rejectDirectConnections');
  // The Debug setting `Reject Direct Connections` is enabled.
  if (
    !server.get('isCloud') &&
    !connection.get('relay') &&
    (rejectDirectConnectionsSetting || config.rejectDirectConnections)
  ) {
    return rejectDirectConnectionTest(url, options);
  }
  return sendConnectionTest(url, options);
}
