/* eslint-plugin-disable @typescript-eslint */

export const CLOUD_EPG = 'tv.plex.provider.epg';
export const EPG = 'tv.plex.providers.epg';
export const XMLTV = 'tv.plex.providers.epg.xmltv';
export const LIBRARY = 'com.plexapp.plugins.library';
export const LIBRARY_SYNC = 'com.plexapp.plugins.library.sync';
export const METADATA = 'tv.plex.provider.metadata';
export const MUSIC = 'tv.plex.provider.music';
export const PODCASTS = 'tv.plex.provider.podcasts';
export const VOD = 'tv.plex.provider.vod';
export const WEBSHOWS = 'tv.plex.provider.webshows';
export const DISCOVER = 'tv.plex.provider.discover';
export const HOME_SYNTHETIC = 'synthetic.provider.home';

export const ALL = [
  CLOUD_EPG,
  EPG,
  XMLTV,
  LIBRARY,
  LIBRARY_SYNC,
  METADATA,
  MUSIC,
  PODCASTS,
  VOD,
  WEBSHOWS,
  DISCOVER,
  HOME_SYNTHETIC,
];

export function matches(identifier: any, providerIdentifiers: any) {
  if (!identifier) {
    return false;
  }

  if (!Array.isArray(providerIdentifiers)) {
    providerIdentifiers = [providerIdentifiers];
  }

  return providerIdentifiers.some(
    (providerIdentifier: any) => identifier.indexOf(providerIdentifier) !== -1
  );
}
