import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const NeighborhoodDeviceModel = AbstractModel.extend({
  //
  // Properties

  name: 'NeighborhoodDeviceModel',
  context: ['server'],

  idAttribute: 'uuid',
  rootPropertyName: 'Device',

  syncedAttrs: {
    uuid: attrTypes.string.isRequired,
    address: attrTypes.string.isRequired,
    title: attrTypes.string,
    manufacturer: attrTypes.string,
    model: attrTypes.string,
    modelNumber: attrTypes.string,
    serialNumber: attrTypes.string,
  },
});

export default NeighborhoodDeviceModel;
