/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

function getPartIndex(media: any, key: any) {
  // Use a key to find the part since not all channel content provides
  // parts with an id.

  const parts = media.Part;
  const partIndex = _.indexOf(parts, _.findWhere(parts, { key }));

  return partIndex;
}

export default getPartIndex;
