import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import base64ImageStringToBlob from 'app/js/utils/images/base64ImageStringToBlob';

export default function updateAvatar(avatar: any, userID: any) {
  const appModel = resolve('appModel');
  const cloudServerModel = appModel.get('cloudServer');

  const avatarBlob = base64ImageStringToBlob(avatar);

  const formData = new window.FormData();
  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Blob | null' is not assignable t... Remove this comment to see the full error message
  formData.append('avatar', avatarBlob);

  if (userID) {
    formData.append('userId', userID);
  }

  const xhrOptions = cloudServerModel.getXHROptions('/api/v2/user', {
    type: 'PUT',
    processData: false,
    contentType: false,
    data: formData,
  });

  return $.ajax(xhrOptions);
}
