/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import * as providerActionIDs from 'app/js/utils/constants/providerActionIDs';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as features from 'app/js/utils/constants/user/features';
import ProviderFeatureModel from './ProviderFeatureModel';

const ProviderFeatureCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ProviderFeatureCollection',
  context: ['server', 'provider'],

  model: ProviderFeatureModel,

  modelId(attrs: any) {
    return attrs.type === providerFeatureTypes.ACTION ? attrs.id : attrs.type;
  },

  augmentResponse(response: any) {
    // Check the response for an `actions` feature. If no such feature exists
    // manually create it. With a missing `actions` feature it will break parts
    // of the app that expect this feature to exist. LegacyLibraryProviderModel
    // Also manually adds actions.
    let actionsFeature = response.find(
      (feature: any) => feature.type === providerFeatureTypes.ACTIONS
    );

    if (actionsFeature != null && !Array.isArray(actionsFeature.Action)) {
      actionsFeature.Action = actionsFeature.Action
        ? [actionsFeature.Action]
        : [];
    }

    if (actionsFeature == null) {
      actionsFeature = { Action: [], type: providerFeatureTypes.ACTIONS };
      response.push(actionsFeature);
    }

    response.forEach((feature: any) => {
      if (
        feature.type === providerFeatureTypes.TIMELINE &&
        (feature.scrobbleKey != null || feature.unscrobbleKey != null)
      ) {
        const { scrobbleKey: key, unscrobbleKey: reverseKey } = feature;

        actionsFeature.Action.push({
          id: providerActionIDs.SCROBBLE,
          key,
          reverseKey,
        });

        delete feature.scrobbleKey;
        delete feature.unscrobbleKey;
      }

      if (feature.type === providerFeatureTypes.RATE) {
        const { key } = feature;

        actionsFeature.Action.push({
          id: providerActionIDs.RATE,
          key,
        });

        delete feature.key;
      }

      if (
        feature.type === providerFeatureTypes.ACTION &&
        (feature.key != null || feature.reverseKey != null)
      ) {
        const { key, reverseKey } = feature;

        actionsFeature.Action.push({
          id: feature.id,
          key,
          reverseKey,
        });

        delete feature.key;
        delete feature.reverseKey;
      }

      // In the future the API will return `requires` attribute on Pivot, Action
      // . The attribute value will be the short name of the feature flag. See
      // https://github.com/plexinc/api/pull/276 &
      // https://github.com/plexinc/my-plex/issues/5303
      //
      // Once API changes are made and we use the correct FF short name, this
      // code can be deleted.
      if (feature.type === providerFeatureTypes.ACTIONS) {
        feature.Action.forEach((action: any) => {
          if (
            action.id === providerActionIDs.ADD_TO_WATCHLIST ||
            action.id === providerActionIDs.ADD_TO_CATALOG ||
            action.id === providerActionIDs.SAVE
          ) {
            action.requires = features.NON_ANONYMOUS_ACCOUNT;
          }
        });
      }

      if (feature.type === providerFeatureTypes.SETTINGS) {
        feature.requires = features.NON_ANONYMOUS_ACCOUNT;
      }
    });

    return response;
  },
});

export default ProviderFeatureCollection;
