/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as serverUpdateStates from 'app/js/utils/constants/serverUpdateStates';

const ServerUpdateModel = AbstractModel.extend({
  //
  // Properties

  context: ['server'],
  url: '/updater/status',

  sessionAttrs: {
    isChecking: attrTypes.boolean.isRequired,
  },

  syncedAttrs: {
    status: attrTypes.integer,
    canInstall: attrTypes.boolean,
    downloadURL: attrTypes.string,

    // Release
    state: attrTypes.string,
    progress: attrTypes.integer,
    version: attrTypes.string,
    error: attrTypes.string,
    added: attrTypes.string,
    fixed: attrTypes.string,
  },

  defaults: {
    // Only defaults for Release are needed to reset missing attributes.
    state: serverUpdateStates.NONE,
    status: 0,
    progress: 0,
    version: null,
    error: '',
    added: null,
    fixed: null,
    isChecking: false,
  },

  //
  // Control

  augmentResponse(response: any) {
    const { Release, ...otherAttrs } = response;

    if (Release) {
      response = { ...otherAttrs, ...Release };
    }

    if (response.status !== '0') {
      response.state = serverUpdateStates.ERROR;
    }

    return response;
  },
});

export default ServerUpdateModel;
