/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import commands from 'app/js/commands/commands';

function reconnectServerCommand(options: any) {
  const cloudAccount = options.cloudAccount;

  const connectOptions = {
    cloudAccount: cloudAccount,
    value: false,
  };

  let promise = commands.execute('connectServer', connectOptions);

  promise = promise.then(function () {
    connectOptions.value = true;

    return commands.execute('connectServer', connectOptions);
  });

  // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
  promise = promise.then(function () {
    if (cloudAccount.get('mappingError')) {
      return $.Deferred().reject().promise();
    }
  });

  return promise;
}

export default reconnectServerCommand;
