/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import commands from 'app/js/commands/commands';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deletePromises(items: any, skipRedirect: any) {
  return _.map(items, function (item) {
    return commands.execute('deleteItem', {
      item,
      skipRedirect,
    });
  });
}
function deleteItemsCommand(options: any) {
  let items = options.items;
  let item = options.item;
  const skipConfirmation = options.skipConfirmation;
  const skipRedirect = options.skipRedirect;
  // Ensure we have an items array and a specific item to work with.
  if (!items) {
    items = [item];
  }
  if (!item) {
    item = _.first(items);
  }
  // If this item has multiple media items, allow the user to pick which
  // media item to delete.
  const mediaItems = item.get('Media');
  if (
    items.length === 1 &&
    mediaItems.length > 1 &&
    !item.get('remoteMedia') &&
    item.server.supports('specificMediaDeletion')
  ) {
    return commands.execute('deleteMediaItem', { item: item });
  }
  let promise;
  if (skipConfirmation) {
    // eslint-disable-next-line prefer-spread
    promise = $.when.apply($, deletePromises(items, skipRedirect));
  } else {
    const confirmModal = showConfirmation({ item: item, items: items });
    const confirmPromise = confirmModal.promise;
    promise = confirmPromise.then(function () {
      // eslint-disable-next-line prefer-spread
      return $.when
        .apply($, deletePromises(items, skipRedirect))
        .always(function () {
          confirmModal.hide();
        });
    });
  }
  return promise;
}
function showConfirmation(options: any) {
  const item = options.item;
  const items = options.items;
  const isMultiple = items.length > 1;
  const isRemote = item.get('remoteMedia');
  const type = item.get('type');
  const metadataType = metadataTypes.getMetadataType(type);
  let typeLabel;
  let message;
  if (isRemote && !isMultiple) {
    typeLabel = metadataType.title;
    message = t('Are you sure you want to remove {1}?', item.get('title'));
  } else if (isMultiple && items.every((i: any) => i.get('remoteMedia'))) {
    typeLabel = metadataType.plural;
    message = t('Are you sure you want to remove {1} items?', items.length);
  } else if (type === metadataTypes.COLLECTION) {
    typeLabel = metadataType.title;
    message = t(
      'Are you sure you want to delete {1}? This cannot be undone.',
      item.get('title')
    );
  } else if (isMultiple) {
    typeLabel = metadataType.plural;
    message = t(
      'Are you sure you want to delete {1} items from your filesystem?',
      items.length
    );
  } else {
    typeLabel = metadataType.title;
    message = t(
      'Are you sure you want to delete this item from your filesystem?'
    );
  }
  const modalOptions = {
    title: isRemote
      ? t('Remove {typeLabel}', { typeLabel })
      : t('Delete {typeLabel}', { typeLabel }),
    message: message,
    confirmLabel: isRemote ? t('Remove') : t('Delete'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  };
  // If we are deleting multiple items or a composite item, show an
  // additional confirmation step to be cautious.
  if (isMultiple || metadataTypes.isParentType(type)) {
    (modalOptions as any).requireDoubleConfirm = true;
    (modalOptions as any).doubleConfirmMessage = isRemote
      ? t('Multiple files may be removed.')
      : t('Multiple items may be deleted.');
    (modalOptions as any).doubleConfirmLabel = t("Yes, I'm sure");
  }
  const confirmModal = new ConfirmModal(modalOptions);
  dispatcher.trigger('show:modal', confirmModal);
  return confirmModal;
}
export default deleteItemsCommand;
