/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';

export default function isEmbeddedServer(server: any) {
  const embeddedServer = resolve('appModel').get('embeddedServer');

  return (
    !!server &&
    !!embeddedServer &&
    server.machineIdentifier === embeddedServer.get('machineIdentifier')
  );
}
