/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import ProviderEPGChannelCollection from 'app/js/models/provider/ProviderEPGChannelCollection';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createFetchHandler from 'app/js/stores/actions/helpers/createFetchHandler';
import createPopulateHandler from 'app/js/stores/actions/helpers/createPopulateHandler';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as providerSettingIDs from 'app/js/utils/constants/providerSettingIDs';
import * as providerSettingTypes from 'app/js/utils/constants/providerSettingTypes';
import { findProviderModel } from 'app/js/utils/state/modelFinders';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import path from 'app/js/utils/url/path';
import xhrErrorFilter from 'app/js/utils/xhr/xhrErrorFilter';

//
// Action Types

export const FETCH_PROVIDER_SETTINGS =
  'provider/providerSettings/fetchProviderSettings';
export const FETCH_PROVIDER_SETTING_SELECT_OPTIONS =
  'provider/providerSettings/fetchProviderSettingSelectOptions';
export const CLEAR_PROVIDER_SETTING_FETCH_ERROR =
  'provider/provderSettings/clearProviderSettingFetchError';
export const FETCH_PROVIDER_SETTING_LOCATION_OPTIONS =
  'provider/providerSettings/fetchProviderSettingLocationOptions';
export const POPULATE_PROVIDER_SETTING_LOCATION_OPTIONS =
  'provider/providerSettings/populateProviderSettingLocationOptions';
export const FETCH_PROVIDER_SETTING_LINEUP_CHANNELS =
  'provider/providerSettings/fetchProviderSettingLineupChannels';
export const POPULATE_PROVIDER_SETTING_LINEUP_CHANNELS =
  'provider/providerSettings/populateProviderSettingLineupChannels';
export const FETCH_PROVIDER_SETTING_RECENT_CHANNELS_IN_GRID =
  'provider/providerSettings/fetchProviderSettingRecentChannelsInGrid';
export const POPULATE_PROVIDER_SETTING_RECENT_CHANNELS_IN_GRID =
  'provider/providerSettings/populateProviderSettingRecentChannelsInGrid';
export const UPDATE_PROVIDER_SETTING =
  'provider/providerSettings/updateProviderSetting';
export const CLEAR_PROVIDER_SETTING_UPDATE_ERROR =
  'provider/providerSettings/clearProviderSettingUpdateError';
export const POPULATE_PROVIDER_SETTING_OPTIONS =
  'provider/providerSettings/populateProviderSettingOptions';

//
// Action Creators

export const fetchProviderSettings = createActionCreator(
  FETCH_PROVIDER_SETTINGS
);
export const fetchProviderSettingSelectOptions = createActionCreator(
  FETCH_PROVIDER_SETTING_SELECT_OPTIONS
);
export const clearProviderSettingFetchError = createActionCreator(
  CLEAR_PROVIDER_SETTING_FETCH_ERROR
);
export const fetchProviderSettingLocationOptions = createActionCreator(
  FETCH_PROVIDER_SETTING_LOCATION_OPTIONS
);
export const populateProviderSettingLocationOptions = createActionCreator(
  POPULATE_PROVIDER_SETTING_LOCATION_OPTIONS
);
export const fetchProviderSettingLineupChannels = createActionCreator(
  FETCH_PROVIDER_SETTING_LINEUP_CHANNELS
);
export const populateProviderSettingLineupChannels = createActionCreator(
  POPULATE_PROVIDER_SETTING_LINEUP_CHANNELS
);
export const fetchProviderSettingRecentChannelsInGrid = createActionCreator(
  FETCH_PROVIDER_SETTING_RECENT_CHANNELS_IN_GRID
);
export const populateProviderSettingRecentChannelsInGrid = createActionCreator(
  POPULATE_PROVIDER_SETTING_RECENT_CHANNELS_IN_GRID
);
export const updateProviderSetting = createActionCreator(
  UPDATE_PROVIDER_SETTING
);
export const clearProviderSettingUpdateError = createActionCreator(
  CLEAR_PROVIDER_SETTING_UPDATE_ERROR
);
export const populateProviderSettingOptions = createActionCreator(
  POPULATE_PROVIDER_SETTING_OPTIONS
);

//
// Helpers

function findSettingsFeatureModel(appModel: any, payload: any) {
  return findProviderModel(appModel, payload)
    .get('features')
    .get(providerFeatureTypes.SETTINGS);
}

function findProviderSettingByID(appModel: any, payload: any) {
  return findSettingsFeatureModel(appModel, payload)
    .get('settings')
    .get(payload.settingID);
}

function updateSettingModel(settingModel: any, values: any) {
  switch (settingModel.get('type')) {
    case providerSettingTypes.LINEUP: {
      const oldLineup = settingModel.get('lineup');
      const newLineup = new ProviderEPGChannelCollection(
        values.map((channel: any) => {
          const channelModel = oldLineup.findWhere({ id: channel.id });

          channelModel.set({ hidden: channel.hidden });

          return channelModel;
        })
      );

      settingModel.set('lineup', newLineup);
      break;
    }

    case providerSettingTypes.LOCATION: {
      const country = settingModel.get('locations').get(values.country);

      settingModel.set({
        country: values.country,
        countryLabel: country.get('title'),
        postalCode: values.postalCode,
      });
      break;
    }

    case providerSettingTypes.SELECT:
      _.each(values, (value, key) => {
        settingModel.get('options').get(key).set({ selected: value });
      });
      settingModel.set(
        'selectedCount',
        _.values(values).filter((value) => value).length
      );
      break;

    default:
      settingModel.set('value', values[settingModel.id]);
  }
}

function getUpdatePromise(settingModel: any, values: any, providerModel: any) {
  // @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
  const parsedValues = _.mapObject(values, (value, key) => {
    if (value === true) {
      return '1';
    } else if (value === false) {
      return '0';
    }

    return value;
  });

  const url = path.join(_.result(settingModel, 'url'), { prefs: parsedValues });
  const xhrOptions = providerModel.getXHROptions(url, { type: 'PUT' });

  return $.ajax(xhrOptions).then(() => {
    updateSettingModel(settingModel, values);
  });
}

//
// Action Handlers

export const providerSettingsActionHandlers = {
  [FETCH_PROVIDER_SETTINGS]: createFetchHandler({
    key: 'settings',
    getModel: findSettingsFeatureModel,
  }),

  [FETCH_PROVIDER_SETTING_SELECT_OPTIONS]: createFetchHandler({
    key: 'options',
    getModel: findProviderSettingByID,
  }),

  [FETCH_PROVIDER_SETTING_LOCATION_OPTIONS]: createFetchHandler({
    key: 'locations',
    getModel: findProviderSettingByID,
  }),

  [POPULATE_PROVIDER_SETTING_LOCATION_OPTIONS]: createPopulateHandler({
    key: 'locations',
    fetchActionCreator: fetchProviderSettingLocationOptions,
    getModel: findProviderSettingByID,
  }),

  [FETCH_PROVIDER_SETTING_LINEUP_CHANNELS]: createFetchHandler({
    key: 'lineup',
    getModel: findProviderSettingByID,
  }),

  [POPULATE_PROVIDER_SETTING_LINEUP_CHANNELS]: createPopulateHandler({
    key: 'lineup',
    fetchActionCreator: fetchProviderSettingLineupChannels,
    getModel: findProviderSettingByID,
  }),

  [FETCH_PROVIDER_SETTING_RECENT_CHANNELS_IN_GRID]: createFetchHandler({
    key: 'recentChannelsInGrid',
    getModel: findProviderSettingByID,
  }),

  [POPULATE_PROVIDER_SETTING_RECENT_CHANNELS_IN_GRID]: createPopulateHandler({
    key: 'recentChannelsInGrid',
    fetchActionCreator: fetchProviderSettingRecentChannelsInGrid,
    getModel: findProviderSettingByID,
  }),

  [UPDATE_PROVIDER_SETTING](appModel: any, payload: any) {
    const { settingID, values } = payload;

    const providerModel = findProviderModel(appModel, payload);
    const settingsFeature = providerModel
      .get('features')
      .get(providerFeatureTypes.SETTINGS);
    const providerSettingsPage = appModel.get('ui').get('providerSettingsPage');
    const settingModel = settingsFeature.get('settings').get(settingID);

    providerSettingsPage.set({
      isUpdatePending: true,
      updateError: null,
    });

    const updatePromises = [];
    if (settingModel.get('type') === providerSettingTypes.GROUP) {
      settingModel.get('settings').forEach((groupedSetting: any) => {
        const groupedSettingValues = {
          [groupedSetting.id]: values[groupedSetting.id],
        };

        updatePromises.push(
          getUpdatePromise(groupedSetting, groupedSettingValues, providerModel)
        );
      });
    } else if (settingModel.get('type') === providerSettingTypes.LINEUP) {
      const url = _.result(settingModel, 'url');
      const xhrOptions = providerModel.getXHROptions(url, {
        type: 'PUT',
        contentType: 'application/json',
        data: JSON.stringify(values),
      });
      const promise = $.ajax(xhrOptions).then(() => {
        updateSettingModel(settingModel, values);
      });

      updatePromises.push(promise);
    } else if (settingModel.get('type') === providerSettingTypes.INT) {
      Object.keys(values).forEach((key) => {
        values[key] = parseInt(values[key], 10);
      });

      updatePromises.push(
        getUpdatePromise(settingModel, values, providerModel)
      );
    } else {
      updatePromises.push(
        getUpdatePromise(settingModel, values, providerModel)
      );
    }

    $.when(...updatePromises).then(
      () => {
        providerSettingsPage.set({
          isUpdatePending: false,
          updateError: null,
        });
      },
      (xhr) => {
        providerSettingsPage.set({
          isUpdatePending: false,
          updateError: xhrErrorFilter(xhr),
        });
      }
    );
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [CLEAR_PROVIDER_SETTING_UPDATE_ERROR](appModel: any, payload: any) {
    const providerSettingsPage = appModel.get('ui').get('providerSettingsPage');

    providerSettingsPage.set('updateError', null);
  },

  [CLEAR_PROVIDER_SETTING_FETCH_ERROR](appModel: any, payload: any) {
    const setting = findProviderSettingByID(appModel, payload);

    setting.set({
      optionsError: null,
      locationsError: null,
    });
  },

  [POPULATE_PROVIDER_SETTING_OPTIONS](
    appModel: any,
    payload: any,
    dispatch: any
  ) {
    const setting = findProviderSettingByID(appModel, payload);
    const settingType = setting.get('type');

    if (settingType === providerSettingTypes.SELECT) {
      scheduleImmediateUpdate(() => {
        dispatch(fetchProviderSettingSelectOptions(payload));
      });
    } else if (settingType === providerSettingTypes.LOCATION) {
      dispatch(populateProviderSettingLocationOptions(payload));
    } else if (settingType === providerSettingTypes.LINEUP) {
      const settingID = setting.get('id');

      if (settingID === providerSettingIDs.LINEUP) {
        dispatch(populateProviderSettingLineupChannels(payload));
      } else if (settingID === providerSettingIDs.RECENT_CHANNELS_IN_GRID) {
        dispatch(populateProviderSettingRecentChannelsInGrid(payload));
      }
    }
  },
};
