import { createSlice } from '@reduxjs/toolkit';
import t from 'app/js/utils/t';
import serverActivitiesSlice from 'app/js/v4/core/serverActivities/serverActivitiesSlice';
import {
  ServerActivityAction,
  ServerActivityType,
} from 'app/js/v4/core/serverActivities/serverActivitiesTypes';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  AddDVRDeviceFailedAction,
  AddDVRDeviceRequestedAction,
  AddDVRDeviceSucceededAction,
  DeviceAddFailedAction,
  DeviceAddRequestedAction,
  DeviceAddSucceededAction,
  DeviceChannelMappingsSaveFailedAction,
  DeviceChannelMappingsSaveRequestedAction,
  DeviceChannelMappingsSaveSucceededAction,
  DeviceChannelsCancelScanFailedAction,
  DeviceChannelsCancelScanRequestedAction,
  DeviceChannelsFetchFailedAction,
  DeviceChannelsFetchRequestedAction,
  DeviceChannelsFetchSucceededAction,
  DeviceChannelsScanFailedAction,
  DeviceChannelsScanRequestedAction,
  DeviceChannelsState,
  DeviceEnableFailedAction,
  DeviceEnableRequestedAction,
  DeviceEnableSucceededAction,
  DevicesDiscoverFailedAction,
  DevicesDiscoverRequestedAction,
  DevicesDiscoverSucceededAction,
  DeviceSelectedAction,
  DevicesFetchFailedAction,
  DevicesFetchRequestedAction,
  DevicesFetchSucceededAction,
  DeviceSourceSelectedAction,
  DevicesState,
  DVRDeleteFailedAction,
  DVRDeleteRequestedAction,
  DVRDeleteSucceededAction,
  DVRFetchFailedAction,
  DVRFetchRequestedAction,
  DVRFetchSucceededAction,
  DVRSaveFailedAction,
  DVRSaveRequestedAction,
  DVRSaveSucceededAction,
  DVRSetupErrorAddedAction,
  DVRSetupErrorRemovedAction,
  DVRSetupState,
  MediaGrabberDeviceState,
  RemoveDVRDeviceFailedAction,
  RemoveDVRDeviceRequestedAction,
  RemoveDVRDeviceSucceededAction,
  ScanNetworksAddedAction,
  ScanPrefsAddedAction,
  SelectedDVRState,
} from './dvrManagementTypes';

export const initialDeviceChannelsState: DeviceChannelsState = {
  channels: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const initialSelectedDVRState: SelectedDVRState = {
  dvr: undefined,
  isPending: false,
  isPopulated: false,
  error: null,
};

const initialDevicesState: DevicesState = {
  mediaGrabberDevices: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

export const initialState: DVRSetupState = {
  devices: initialDevicesState,
  epgDownloadActivityID: undefined,
  selectedDevice: undefined,
  selectedDVR: initialSelectedDVRState,
  channelsByDeviceKey: {},
  scanPrefs: [],
  scanNetworks: [],
  hasScannedChannels: false,
  isSavingDVR: false,
  isDeletingDVR: false,
  isAddingDevice: false,
  isDiscoveringDevices: false,
  isSavingChannelMappings: false,
  isCancellingChannelScan: false,
  isCancellingReloadGuide: false,
  channelMappingsSaveError: null,
  setupErrorMessage: null,
};

function channelScanActivityReducer(
  state: DVRSetupState,
  { payload }: ServerActivityAction
): void {
  const { activity } = payload;

  if (activity.type !== ServerActivityType.ProviderDeviceChannelScan) {
    return;
  }

  // A network provides additional information to the scanner.
  // If we receive `Network` on the response store it so an option can be
  // presented to the user.
  const networks = activity?.Response?.Network;

  if (networks) {
    state.hasScannedChannels = false;
    state.scanNetworks = networks;
  } else {
    const isFinished = activity?.progress === 100;

    state.hasScannedChannels = isFinished;

    if (state.isCancellingChannelScan && isFinished) {
      state.isCancellingChannelScan = false;
    }
  }
}

const dvrSetupSlice = createSlice({
  name: 'dvrSetup',

  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    setupReset(state) {
      // Selectively reset state to avoid unnecessary data fetching.
      state.devices = initialDevicesState;
      state.selectedDevice = undefined;
      state.hasScannedChannels = false;
      state.isSavingDVR = false;
      state.isAddingDevice = false;
      state.isDiscoveringDevices = false;
      state.isSavingChannelMappings = false;
      state.channelMappingsSaveError = null;
      state.setupErrorMessage = null;
    },

    setupErrorMessageAdded(state, { payload }: DVRSetupErrorAddedAction) {
      state.setupErrorMessage = payload.message;
    },

    setupErrorMessageRemoved(state, _action: DVRSetupErrorRemovedAction) {
      state.setupErrorMessage = null;
    },

    devicesDiscoverRequested(state, _action: DevicesDiscoverRequestedAction) {
      state.isDiscoveringDevices = true;
    },

    devicesDiscoverSucceeded(
      state,
      { payload }: DevicesDiscoverSucceededAction
    ) {
      state.isDiscoveringDevices = false;

      const { mediaGrabberDevices } = state.devices;

      for (let index = 0; index < payload.data.length; index++) {
        const device = payload.data[index];
        const existingDevice = mediaGrabberDevices.find(
          (mediaGrabberDevice) => device.uuid === mediaGrabberDevice.uuid
        );

        if (existingDevice) {
          // Keep most attributes from existing device in state. Doing so
          // prevents losing changes made by user in the device step.
          existingDevice.state = device.state;
          existingDevice.status = device.status;
          existingDevice.parentID = device.parentID;
        } else {
          mediaGrabberDevices.push(device);
        }
      }
    },

    devicesDiscoverFailed(state, _action: DevicesDiscoverFailedAction) {
      state.isDiscoveringDevices = false;
    },

    devicesFetchRequested({ devices }, _action: DevicesFetchRequestedAction) {
      markPending(devices);
    },

    devicesFetchSucceeded(
      { devices },
      { payload }: DevicesFetchSucceededAction
    ) {
      markPopulated(devices);
      devices.mediaGrabberDevices = payload.data;
    },

    devicesFetchFailed({ devices }, { payload }: DevicesFetchFailedAction) {
      markFailed(devices, payload.error);
    },

    deviceChannelsFetchRequested(
      state,
      { payload }: DeviceChannelsFetchRequestedAction
    ) {
      let deviceChannels = state.channelsByDeviceKey[payload.deviceKey];

      if (!deviceChannels) {
        deviceChannels = { ...initialDeviceChannelsState };
        state.channelsByDeviceKey[payload.deviceKey] = deviceChannels;
      } else {
        deviceChannels.channels = [];
        deviceChannels.isPopulated = false;
      }

      state.setupErrorMessage = null;
      markPending(deviceChannels);
    },

    deviceChannelsFetchSucceeded(
      state,
      { payload }: DeviceChannelsFetchSucceededAction
    ) {
      const deviceChannels = state.channelsByDeviceKey[payload.deviceKey];

      markPopulated(deviceChannels);
      deviceChannels.channels = payload.data;
    },

    deviceChannelsFetchFailed(
      state,
      { payload }: DeviceChannelsFetchFailedAction
    ) {
      let deviceChannels = state.channelsByDeviceKey[payload.deviceKey];

      if (!deviceChannels) {
        deviceChannels = { ...initialDeviceChannelsState };
        state.channelsByDeviceKey[payload.deviceKey] = deviceChannels;
      }

      markFailed(deviceChannels, payload.error);
    },

    deviceChannelsScanRequested(
      state,
      _action: DeviceChannelsScanRequestedAction
    ) {
      state.hasScannedChannels = false;
    },

    deviceChannelsScanFailed(state, _action: DeviceChannelsScanFailedAction) {
      state.hasScannedChannels = true;
    },

    deviceChannelsCancelScanRequested(
      state,
      _action: DeviceChannelsCancelScanRequestedAction
    ) {
      state.isCancellingChannelScan = true;
    },

    deviceChannelsCancelScanFailed(
      state,
      _action: DeviceChannelsCancelScanFailedAction
    ) {
      state.isCancellingChannelScan = false;
    },

    deviceSelected(state, { payload }: DeviceSelectedAction) {
      const mediaGrabberDevice = state.devices.mediaGrabberDevices.find(
        (device) => device.key === payload.deviceKey
      );

      if (mediaGrabberDevice) {
        state.selectedDevice = mediaGrabberDevice;
      }
    },

    deviceSourceSelected({ devices }, { payload }: DeviceSourceSelectedAction) {
      const mediaGrabberDevice = devices.mediaGrabberDevices.find(
        (device) => device.key === payload.deviceKey
      );

      if (mediaGrabberDevice) {
        mediaGrabberDevice.source = payload.source;
      }
    },

    deviceChannelMappingsSaveRequested(
      state,
      _action: DeviceChannelMappingsSaveRequestedAction
    ) {
      state.isSavingChannelMappings = true;
      state.channelMappingsSaveError = null;
    },

    deviceChannelMappingsSaveSucceeded(
      state,
      _action: DeviceChannelMappingsSaveSucceededAction
    ) {
      state.isSavingChannelMappings = false;
      state.channelMappingsSaveError = null;
    },

    deviceChannelMappingsSaveFailed(
      state,
      { payload }: DeviceChannelMappingsSaveFailedAction
    ) {
      state.isSavingChannelMappings = false;
      state.channelMappingsSaveError = payload.error;
    },

    deviceEnableRequested(_state, _action: DeviceEnableRequestedAction) {
      // Handled by saga, no state change.
    },

    deviceEnableSucceeded(state, { payload }: DeviceEnableSucceededAction) {
      const mediaGrabberDevice = state.devices.mediaGrabberDevices.find(
        (device) => device.key === payload.deviceKey
      );

      if (mediaGrabberDevice) {
        mediaGrabberDevice.state = payload.enabled
          ? MediaGrabberDeviceState.Enabled
          : MediaGrabberDeviceState.Disabled;
        mediaGrabberDevice.title = payload.title;
      }
    },

    deviceEnableFailed(_state, _action: DeviceEnableFailedAction) {
      // Handled by saga, no state change.
    },

    deviceAddRequested(state, _action: DeviceAddRequestedAction) {
      state.isAddingDevice = true;
    },

    deviceAddSucceeded(state, { payload }: DeviceAddSucceededAction) {
      state.setupErrorMessage = null;
      state.isAddingDevice = false;

      const mediaGrabberDevice = state.devices.mediaGrabberDevices.find(
        (device) => device.key === payload.data.key
      );

      if (!mediaGrabberDevice) {
        state.devices.mediaGrabberDevices.push(payload.data);
      } else {
        Object.assign(mediaGrabberDevice, payload.data);
      }
    },

    deviceAddFailed(state, _action: DeviceAddFailedAction) {
      state.isAddingDevice = false;
    },

    //
    // DVR

    dvrFetchRequested(state, _action: DVRFetchRequestedAction) {
      state.setupErrorMessage = null;
      markPending(state.selectedDVR);
    },

    dvrFetchSucceeded(
      { selectedDVR, devices },
      { payload }: DVRFetchSucceededAction
    ) {
      const { dvr } = payload;
      markPopulated(selectedDVR);
      selectedDVR.dvr = dvr;

      if (dvr?.Device) {
        markPopulated(devices);
        devices.mediaGrabberDevices = dvr.Device;
      }
    },

    dvrFetchFailed(state, { payload }: DVRFetchFailedAction) {
      markFailed(state.selectedDVR, payload.error);
      state.setupErrorMessage = t(
        'There was a problem fetching your DVR. Please try again later.'
      );
    },

    dvrSaveRequested(state, _action: DVRSaveRequestedAction) {
      state.setupErrorMessage = null;
      state.isSavingDVR = true;
    },

    dvrSaveSucceeded(state, { payload }: DVRSaveSucceededAction) {
      markPopulated(state.selectedDVR);
      state.epgDownloadActivityID = payload.activityUUID;
      state.isSavingDVR = false;
      state.selectedDVR.dvr = payload.dvr;
    },

    dvrSaveFailed(state, { payload }: DVRSaveFailedAction) {
      markFailed(state.selectedDVR, payload.error);
      state.isSavingDVR = false;
    },

    dvrDeleteRequested(state, _action: DVRDeleteRequestedAction) {
      state.isDeletingDVR = true;
    },

    dvrDeleteSucceeded(state, _action: DVRDeleteSucceededAction) {
      state.selectedDVR = initialSelectedDVRState;
      state.isDeletingDVR = false;
    },

    dvrDeleteFailed(state, _action: DVRDeleteFailedAction) {
      state.isDeletingDVR = false;
    },

    addDVRDeviceRequested(state, _action: AddDVRDeviceRequestedAction) {
      state.setupErrorMessage = null;
      state.isSavingDVR = true;
    },

    addDVRDeviceSucceeded(state, { payload }: AddDVRDeviceSucceededAction) {
      markPopulated(state.selectedDVR);
      state.epgDownloadActivityID = payload.activityUUID;
      state.isSavingDVR = false;
      state.selectedDVR.dvr = payload.dvr;
    },

    addDVRDeviceFailed(state, { payload }: AddDVRDeviceFailedAction) {
      markFailed(state.selectedDVR, payload.error);
      state.isSavingDVR = false;
    },

    removeDVRDeviceRequested(_state, _action: RemoveDVRDeviceRequestedAction) {
      // Handled by saga, no state change.
    },

    removeDVRDeviceSucceeded(state, action: RemoveDVRDeviceSucceededAction) {
      if (state.selectedDVR?.dvr?.Device) {
        state.selectedDVR.dvr.Device = state.selectedDVR.dvr.Device.filter(
          (device) => device.key !== action.payload.deviceKey
        );
      }

      state.devices.mediaGrabberDevices =
        state.devices.mediaGrabberDevices.filter(
          (device) => device.key !== action.payload.deviceKey
        );
    },

    removeDVRDeviceFailed(_state, _action: RemoveDVRDeviceFailedAction) {
      // Handled by saga, no state change.
    },

    cancelReloadGuide(state) {
      state.isCancellingReloadGuide = true;
    },

    scanPrefsAndNetworksReset(state) {
      state.scanPrefs = [];
      state.scanNetworks = [];
    },

    scanPrefsAdded(state, { payload }: ScanPrefsAddedAction) {
      state.scanPrefs = payload.settings;
    },

    scanNetworksAdded(state, { payload }: ScanNetworksAddedAction) {
      state.scanNetworks = payload.networks;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(
        serverActivitiesSlice.actions.activityStarted,
        channelScanActivityReducer
      )
      .addCase(
        serverActivitiesSlice.actions.activityUpdated,
        channelScanActivityReducer
      )
      .addCase(
        serverActivitiesSlice.actions.activityFinished,
        (state, action) => {
          if (
            action.payload.activity.type === ServerActivityType.ProviderEpgLoad
          ) {
            state.isCancellingReloadGuide = false;
            state.epgDownloadActivityID = undefined;
          } else {
            channelScanActivityReducer(state, action);
          }
        }
      );
  },
});

export default dvrSetupSlice;
