/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import * as metricsEvents from 'app/js/utils/constants/metrics/metricsEvents';
import dispatcher from 'app/js/utils/dispatcher';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';

function switchUserCommand(options = {}) {
  // If plex.tv is available, always use the server to authenticate.
  // Otherwise, try to use the hash from storage.
  const user = resolve('appModel').get('user');
  const isUserSignedIn = user.get('signedIn');
  const isDifferentUser = user.id !== (options as any).homeUser.id;
  const allowGuestLock = (options as any).allowGuestLock;
  const isRemoteUser = !!(options as any).isRemoteUser;
  const shouldSwitchUser = isDifferentUser || allowGuestLock;
  let authPromise = isUserSignedIn
    ? commands.execute('authenticateWithServer', options)
    : commands.execute('authenticateWithHash', options);
  if (isUserSignedIn && shouldSwitchUser) {
    authPromise = authPromise.then((response: any) => {
      return switchUser(response, options);
    });
  } else {
    authPromise.done(() => {
      const attributes = {
        isRemote: isRemoteUser,
      };
      user.set(attributes);
    });
  }
  authPromise.done(() => {
    navigateToRoute('home');
  });
  return authPromise;
}
function switchUser(response: any, options: any) {
  // After the current user has been completely signed out, switch to
  // the new user with the response from our initial request.
  const user = resolve('appModel').get('user');
  let promise = commands.execute('resetUser');
  promise = promise.then(() => {
    // Activate the stored user before resetting the servers and providers so
    // the stored user model and `lastPrimaryServerID` is available to each
    // initializer.
    user.activateStoredUser(options.homeUser.id);
    const storedUser = userStorageModel.getActiveUser();
    if (storedUser) {
      storedUser.copyToModel(user);
    }
    dispatcher.trigger('init:servers');
    const attributes = {};
    return user.signIn(response, attributes);
  });
  promise.done(() => {
    resolve('metrics').trackEvent({
      event: metricsEvents.CLIENT_SWITCH_USER,
      interaction: true,
    });
  });
  return promise;
}
export default switchUserCommand;
