import $ from 'jquery';
import _ from 'underscore';
import PlaylistModel from 'app/js/models/library/PlaylistModel';
import VersionGeneratorCollection from 'app/js/models/library/convert/VersionGeneratorCollection';
import path from 'app/js/utils/url/path';

const BackgroundProcessingListModel = PlaylistModel.extend({
  //
  // Properties

  defaults: function () {
    return {
      children: new VersionGeneratorCollection([], {
        server: this.server,
        parent: this,
      }),
    };
  },

  //
  // Properties

  findItem: function (id: any) {
    return this.get('children').get(id);
  },

  addContent: function (item: any) {
    const segments = [_.result(this, 'url'), 'items', item.id];

    const url = path.join(segments, item.getSaveParams());
    const xhrOptions = _.extend(item.server.getXHROptions(url), {
      type: 'PUT',
    });
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.then(
      _.bind(function (response: any) {
        // Both the metadata of the playlist (returned in response) and the
        // collection of items will have changed on the server
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.set(this.parse(response));
      }, this)
    );

    return xhrDeferred.promise();
  },
});

export default BackgroundProcessingListModel;
