/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import * as Sentry from '@sentry/browser';
import * as adapters from 'app/js/adapters/adapters';
import ConnectionModel from 'app/js/models/server/ConnectionModel';
import {
  eventConnectionClose,
  eventConnectionOpen,
} from 'app/js/stores/actions/serverActions';
import BaseClass from 'app/js/utils/BaseClass';
import * as serverUpdateStates from 'app/js/utils/constants/serverUpdateStates';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import getMessages from 'app/js/utils/pubsub/getMessages';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const BaseMediaServerEventConnection = BaseClass.extend({
  mixins: eventMixin,

  //
  // Properties

  url() {
    const activeConnection = this.server.get('activeConnection');

    if (!activeConnection) {
      return;
    }

    const socketConnectionUri = activeConnection.id.replace(/^http/, 'ws');

    // If the the app is being served over https we want to use wss://
    // instead of ws:// to prevent warnings about insecure connections.
    const url = this.server.resolveUrl('/:/websockets/notifications', {
      connection: new ConnectionModel({ uri: socketConnectionUri }),
      appendToken: true,
      appendHeaders: this.appendHeaders,
    });

    return url;
  },

  //
  // Init

  initialize(options: any) {
    _.bindAll(
      this,
      'onOpen',
      'onClose',
      'onError',
      'onMessage',
      'getMessageTypes'
    );

    this.server = options.server;
    this.appendHeaders = options.appendHeaders || false;
  },

  //
  // Control

  open() {
    if (this.socket) {
      this.close();
    }

    this._deferred = $.Deferred();
    const url = _.result(this, 'url');

    if (!url) {
      return this._deferred.reject().promise();
    }

    this.socket = new window.WebSocket(url);
    this.socket.onopen = this.onOpen;
    this.socket.onclose = this.onClose;
    this.socket.onerror = this.onError;
    this.socket.onmessage = this.onMessage;

    return this._deferred.promise();
  },

  close() {
    if (!this.socket) {
      return;
    }

    this.socket.onopen = null;
    this.socket.onclose = null;
    this.socket.onerror = null;
    this.socket.onmessage = null;
    this.socket.close();
    this.socket = null;

    if (this._deferred && this._deferred.state() === 'pending') {
      this._deferred.reject();
    }
  },

  testConnection() {
    if (!this.server.get('connected')) {
      return;
    }

    // If this server is in the process of updating, we should unset
    // `connected` immediately since we expected it to go offline.

    const update = this.server.get(__PLATFORM__).get('update');
    if (update && update.get('state') === serverUpdateStates.INSTALLING) {
      this.server.set('connected', null);
    }

    this.server.trigger('error:connection', this.server);
  },

  //
  // Listeners

  onOpen(event: any) {
    const appStore = adapters.resolve('appStore');
    const serverEntityID = this.server.entityID();

    if (serverEntityID) {
      appStore.dispatch(eventConnectionOpen({ serverEntityID, event }));
    }

    this.server.trigger('socket:websocket', { opened: true });

    this.trigger('open', this.server, event);
    this._deferred.resolve(this.server, event);
  },

  onClose(event: any) {
    this.close();

    const appStore = adapters.resolve('appStore');
    const serverEntityID = this.server.entityID();

    if (serverEntityID) {
      appStore.dispatch(eventConnectionClose({ serverEntityID, event }));
    }

    this.server.trigger('socket:websocket', { closed: true });

    this.trigger('close', this.server, event);
    this._deferred.reject(this.server, event);

    // We should test the server's connections after a request fails.
    this.testConnection();
  },

  onError(event: any) {
    this.close();

    this.server.trigger('socket:websocket', { error: true });

    this.trigger('error', this.server, event);
    this._deferred.reject(this.server, event);

    // We should test the server's connections after a request fails.
    this.testConnection();
  },

  onMessage(event: any) {
    let data;

    // Catch errors from poorly formatted JSON.
    try {
      data = JSON.parse(event.data);
    } catch (error) {
      log.warn('[Connections] Error formatting message:\n%s', event.data);
      return;
    }

    const isLegacyJSON = !!data._elementType;
    const notification = isLegacyJSON
      ? _.first(data._children)
      : data.NotificationContainer;

    if (notification) {
      const type = isLegacyJSON ? data.type : notification.type;
      const handler = this[type];

      let messages;

      if (isLegacyJSON) {
        messages = Array.isArray(notification) ? notification : [notification];
      } else {
        messages = getMessages(notification, this.getMessageTypes());
      }

      messages.forEach((message: any) => {
        try {
          this.trigger('message', message);
        } catch (error) {
          log.error('[Connections] Error when triggering', message, error);
          Sentry.captureException(error, { extra: { message } });
        }

        if (_.isFunction(handler)) {
          try {
            handler.call(this, message);
          } catch (error) {
            log.error('[Connections] Error when handling', message, error);
            Sentry.captureException(error, { extra: { message } });
          }
        }
      });
    }
  },
});

export default BaseMediaServerEventConnection;
