/* eslint-plugin-disable @typescript-eslint */

import migrateUserExperienceSettings from 'app/js/initializers/migrations/migrateUserExperienceSettings';
import attrTypes from 'app/js/models/attrTypes';
import AbstractStorageModel from 'app/js/models/storage/AbstractStorageModel';
import * as audioBoostLevels from 'app/js/utils/constants/audioBoostLevels';
import httpFallbackOptions from 'app/js/utils/constants/httpFallbackOptions';
import subtitleColors from 'app/js/utils/constants/subtitleColors';
import subtitlePositions from 'app/js/utils/constants/subtitlePositions';
import subtitlesBurnLevels from 'app/js/utils/constants/subtitlesBurnLevels';
import getDefaultLanguage from 'app/js/utils/language/getDefaultLanguage';
import log from 'app/js/utils/log/log';
import parseInteger from 'app/js/utils/parser/parseInteger';
import abstractPlayerQualities from 'app/js/utils/player/abstractPlayerQualities';
import * as metadataContexts from 'app/js/views/v3/helpers/props/metadataContext';
import * as metadataListStyles from 'app/js/views/v3/helpers/props/metadataListStyle';
import * as recordingScheduleViewTypes from 'app/js/views/v3/helpers/props/recordingScheduleViewTypes';
import ReminderCollection from './ReminderCollection';
import userExperienceModelAttrs from './userExperienceModelAttrs';

export const localQualityDefault = abstractPlayerQualities.ORIGINAL;
export const remoteQualityDefault = 6;

// schemaVersion represents the version of the structure of the stored
// settings. It is critical when retrieving settings from plex.tv and the sync
// settings feature.
//
// schemaVersion must be incremented when:
// - add to syncedAttrs or syncedRelations
// - remove from syncedAttrs or syncedRelations
// - change the way a syncedAttrs or syncedRelations is used.  Which includes
//   when we change the expected structure of an attr / relation of type
//   `attrTypes.object`
//
// We do not *need* to increment schemaVersion when:
// - making changes to sessionAttrs or sessionRelations
// - making changes to code like augmentResponse etc.
// - making changes to defaults
//
// schemaVersion increment checklist:
// - Add to migrateUserExperienceSettings.schemaVersionMigrations
// - Manually increment schemaVersion
const schemaVersion = 6;

const UserExperienceSettingsModel = AbstractStorageModel.extend(
  {
    //
    // Properties

    name: 'UserExperienceSettingsModel',

    sessionAttrs: {
      ...userExperienceModelAttrs.sessionAttrs,
      isRemoteSchemaVersionNewer: attrTypes.boolean,
    },

    syncedAttrs: {
      ...userExperienceModelAttrs.syncedAttrs,

      // General
      schemaVersion: attrTypes.integer,
      language: attrTypes.string,
      playThemeMusic: attrTypes.boolean,
      companion: attrTypes.boolean,
      allowHttpFallback: attrTypes.string,
      timeFormat: attrTypes.string,
      dismissedWhatsNewFeatures: attrTypes.object,

      // Quality
      autoAdjustQuality: attrTypes.boolean,
      remoteSmallVideoAtOriginalQuality: attrTypes.boolean,
      useHomeStreamingRecommendedSettings: attrTypes.boolean,
      localQuality: attrTypes.integer,
      remoteQuality: attrTypes.integer,

      // Player
      audioBoost: attrTypes.integer,
      subtitleColor: attrTypes.string,
      subtitlePosition: attrTypes.string,
      subtitleSize: attrTypes.integer,
      subtitlesBurnLevel: attrTypes.string,
      extrasPrefixCount: attrTypes.integer,
      audioForceMultiChannel: attrTypes.boolean,
      forceTranscodeProtocolHLS: attrTypes.boolean,

      // Inline
      autoPlay: attrTypes.boolean,
      calendarViewType: attrTypes.string,
      columnAdjustment: attrTypes.integer,
      columnWidthMultiplier: attrTypes.float,
      dashboardNowPlayingShowDetails: attrTypes.boolean,
      hasDismissedDVRUpsell: attrTypes.boolean,
      hasDismissedServerDashboardUpsell: attrTypes.boolean,
      hasDismissedHubPromotionWarning: attrTypes.boolean,
      isSidebarCollapsed: attrTypes.boolean,
      libraryTableColumns: attrTypes.object,
      listStyles: attrTypes.object,
      prePlayListStyles: attrTypes.object,
      directoryListStyles: attrTypes.object,
      directoryTableColumns: attrTypes.object,
      savedDirectoryListKeys: attrTypes.object,
      relayReminders: attrTypes.object,
      showAdvancedSettings: attrTypes.boolean,
      showPrePlayArtwork: attrTypes.boolean,

      // Downloads
      musicDownloadQuality: attrTypes.integer,
      videoDownloadQuality: attrTypes.integer,

      // Search
      recentSearches: attrTypes.array,

      // Unused attributes
      // The following attributes are no longer used and only
      // kept to avoid unnecessarily bumping schema version
      searchAllServers: attrTypes.boolean,
      libraryRoutes: attrTypes.object,
      hasDismissedNewsSunsetBanner: attrTypes.boolean,
      hasDismissedCustomHomeSunset: attrTypes.boolean,
    },

    syncedRelations: {
      ...userExperienceModelAttrs.syncedRelations,

      reminders: {
        relationClass: ReminderCollection,
      },
    },

    defaults() {
      return {
        ...userExperienceModelAttrs.defaults,

        // General
        isRemoteSchemaVersionNewer: false,
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
        language: getDefaultLanguage(),
        playThemeMusic: false,
        companion: true,
        allowHttpFallback: httpFallbackOptions.NEVER,
        timeFormat: 'h:mma',
        dismissedWhatsNewFeatures: {},

        // Quality
        autoAdjustQuality: false,
        remoteSmallVideoAtOriginalQuality: true,
        useHomeStreamingRecommendedSettings: true,
        localQuality: localQualityDefault,
        remoteQuality: remoteQualityDefault,

        // Player
        audioBoost: audioBoostLevels.NORMALIZED,
        subtitleColor: subtitleColors.WHITE,
        subtitlePosition: subtitlePositions.BOTTOM,
        subtitleSize: 100,
        subtitlesBurnLevel: subtitlesBurnLevels.AUTO,
        extrasPrefixCount: 0,
        audioForceMultiChannel: false,
        forceTranscodeProtocolHLS: false,

        // Inline
        autoPlay: true,
        calendarViewType: recordingScheduleViewTypes.CALENDAR,
        columnAdjustment: 0,
        columnWidthMultiplier: 1.5,
        dashboardNowPlayingShowDetails: false,
        hasDismissedServerDashboardUpsell: false,
        isSidebarCollapsed: false,
        libraryTableColumns: {},
        listStyles: {
          [metadataContexts.LIBRARY]: {
            episode: metadataListStyles.TABLE,
            track: metadataListStyles.TABLE,
          },
          [metadataContexts.PRE_PLAY]: {
            track: metadataListStyles.TABLE,
          },
        },
        prePlayListStyles: {},
        directoryListStyles: {},
        directoryTableColumns: {},
        savedDirectoryListKeys: {},
        relayReminders: {},
        showAdvancedSettings: false,
        showPrePlayArtwork: false,

        // Downloads
        musicDownloadQuality: abstractPlayerQualities.ORIGINAL,
        videoDownloadQuality: abstractPlayerQualities.ORIGINAL,

        // Search
        recentSearches: [],

        // Unused attributes
        searchAllServers: false,
        libraryRoutes: {},
        hasDismissedNewsSunsetBanner: false,
        hasDismissedCustomHomeSunset: false,
      };
    },

    //
    // Init

    initialize() {
      // We want schemaVersion to be included in the toJSON response.  So set it
      // in the initialise and not as a default.
      this.set({ schemaVersion: UserExperienceSettingsModel.schemaVersion });
    },

    //
    // Control

    augmentResponse(response: any) {
      if (response.schemaVersion > UserExperienceSettingsModel.schemaVersion) {
        // We can't use the response.  It could contain unknown attrs and break
        // the app.  From this point we cannot share sync settings with other
        // devices.
        log.warn(
          `[Settings] Received settings with schemaVersion of ${response.schemaVersion}; expected ${UserExperienceSettingsModel.schemaVersion} or less.  Settings sharing is now disabled.`
        );

        return {
          ...this.toJSON(),
          isRemoteSchemaVersionNewer: true,
        };
      }

      response = migrateUserExperienceSettings(response);

      const isDirty = this.get('isDirty');
      const updatedAt = this.get('updatedAt');
      const responseUpdatedAt = parseInteger(response.updatedAt);

      const isModelNewerVersion =
        updatedAt && responseUpdatedAt && updatedAt > responseUpdatedAt;

      const isResponseNewerVersion =
        updatedAt && responseUpdatedAt && updatedAt < responseUpdatedAt;

      // Prefer our local values when they have not yet been synced successfully
      // and it is same version as what is returned from server.
      //
      // It is possible that this model is populated from the server before we
      // restore the stored settings.  In which case we prefer the values from
      // this model.
      if ((isDirty && updatedAt === responseUpdatedAt) || isModelNewerVersion) {
        return this.toJSON();
      }

      if (isDirty && isResponseNewerVersion) {
        response.isDirty = false;
      }

      // Response version is known.  We can share sync settings now.
      response.isRemoteSchemaVersionNewer = false;

      return response;
    },
  },
  {
    //
    // Static

    schemaVersion,
  }
);

export default UserExperienceSettingsModel;
