/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';

function analyzeItemsCommand(options: any) {
  const items = options.items;

  // eslint-disable-next-line prefer-spread
  return $.when.apply(
    $,
    _.map(items, function (item) {
      const url = '/library/metadata/' + item.id + '/analyze';
      const xhrOptions = _.extend(item.server.getXHROptions(url), {
        type: 'PUT',
      });

      return $.ajax(xhrOptions).promise();
    })
  );
}

export default analyzeItemsCommand;
