import { useRouteMatch } from 'react-router-dom';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import { CLOUD_SERVER_ID } from 'app/js/utils/constants/serverIDs';
import unknownProvider from 'app/js/v4/core/providers/utils/unknownProvider';
import unknownServer from 'app/js/v4/core/servers/utils/unknownServer';
import routes from 'app/js/v4/routes/routes';

interface ServerProviderRouteParams {
  machineIdentifier?: string;
  providerIdentifier?: string;
}

export interface SelectedSource {
  machineIdentifier: string;
  providerIdentifier: string;
}

function useSelectedSource(): SelectedSource {
  // The order of routes is significant, especially when routes contain optional
  // parts. First one matched is used. eg: /foo/:bar? should be before /foo.
  const routeMatch = useRouteMatch({
    path: [routes.serverSetup],
    exact: true,
  });

  const params: ServerProviderRouteParams | undefined = routeMatch?.params;
  const machineIdentifier = params?.machineIdentifier;
  const providerIdentifier = params?.providerIdentifier;

  if (!machineIdentifier && !providerIdentifier) {
    return {
      machineIdentifier: unknownServer.machineIdentifier,
      providerIdentifier: unknownProvider.identifier,
    };
  }

  return {
    machineIdentifier: machineIdentifier || CLOUD_SERVER_ID,
    providerIdentifier: providerIdentifier
      ? decodeURIComponent(providerIdentifier)
      : providerIdentifiers.LIBRARY,
  };
}
export default useSelectedSource;
