/* eslint-plugin-disable @typescript-eslint */

import * as adapters from 'app/js/adapters/adapters';
import { fetchServerUpdateStatus } from 'app/js/stores/actions/serverUpdateActions';
import BaseClass from 'app/js/utils/BaseClass';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ServerUpdateManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize(options: any) {
    this.appModel = options.appModel;
  },

  //
  // Control

  start() {
    this.stop();

    this.listenTo(this.appModel.get('servers'), {
      ['change:isConnected']: this.onServerIsConnectedChange,

      // Once install completes we need to start checking for server updates, or
      // else the state will remain in `downloaded` since that is the last
      // message we receive. During install on Windows the server isConnected
      // doesn't change so we must listen to isInstallingServerUpdate.
      ['change:isInstallingServerUpdate']:
        this.onIsInstallingServerUpdateChange,
    });
  },

  stop() {
    this.stopListening();
  },

  //
  // Listeners

  onServerIsConnectedChange(serverModel: any, isConnected: any) {
    if (isConnected) {
      adapters.resolve('appStore').dispatch(
        fetchServerUpdateStatus({
          serverEntityID: serverModel.entityID(),
        })
      );
    }
  },

  onIsInstallingServerUpdateChange(
    serverModel: any,
    isInstallingServerUpdate: any
  ) {
    if (!isInstallingServerUpdate) {
      adapters.resolve('appStore').dispatch(
        fetchServerUpdateStatus({
          serverEntityID: serverModel.entityID(),
        })
      );
    }
  },
});

export default ServerUpdateManager;
