/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import log from 'app/js/utils/log/log';
import getQualityAttribute from './getQualityAttribute';

const abstractPlayerQualities = {
  //
  // Properties
  ORIGINAL: -1,
  CONVERT_AUTOMATICALLY: -2,
  qualities: {},
  //
  // Control
  getQualityByIndex: function (qualityIndex: any) {
    if (qualityIndex === this.CONVERT_AUTOMATICALLY) {
      return { index: this.CONVERT_AUTOMATICALLY };
    }
    // The "Original" quality setting has an index of this.ORIGINAL.
    if (qualityIndex < 0) {
      return { index: this.ORIGINAL };
    }
    const quality = { index: qualityIndex };
    _.each(
      this.qualities,
      // @ts-expect-error ts-migrate(6133) FIXME: 'value' is declared but its value is never read.
      function (value, key) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        quality[key] = _.result(this.qualities, key)[qualityIndex];
      },
      this
    );
    return quality;
  },
  getQualityCount: function () {
    const key = _.first(_.keys(this.qualities));
    // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'string | undefined' is not assig... Remove this comment to see the full error message
    return _.result(this.qualities, key).length;
  },
  getQualityAttribute: function (item: any) {
    const name = getQualityAttribute(
      item,
      item.server.get('isPrivateConnection')
    );
    // Keep the video quality attribute names the same for backwards
    // compatibility, but prefix the names for music and photos so they
    // don't collide.
    return (this as any).type !== 'video'
      ? (this as any).type + '!' + name
      : name;
  },
  getSyncQualityAttribute: function () {
    return 'syncQuality!' + (this as any).type;
  },
  getCurrentQuality: function (
    item: any,
    settings: any,
    onSettingsQualityIndexDoesNotExist: any
  ) {
    const qualityAttribute = this.getQualityAttribute(item);
    let qualityIndex = settings.get(qualityAttribute);
    // Make sure the quality setting is within reasonable bounds.
    if (qualityIndex >= this.getQualityCount()) {
      log.warn('Quality index ' + qualityIndex + ' is out of bounds');
      qualityIndex = _.result(settings, 'defaults')[qualityAttribute];
      onSettingsQualityIndexDoesNotExist({ [qualityAttribute]: qualityIndex });
    }
    return _.extend(this.getQualityByIndex(qualityIndex), {
      attribute: qualityAttribute,
    });
  },
  getAvailableQualities: function (options: any) {
    options || (options = {});
    const lowestQualityIndex = options.lowestQualityIndex;
    let highestQualityIndex = options.highestQualityIndex;
    const comparator = options.comparator;
    const availableQualities = [];
    // TV callers define a highestQualityIndex argument with this.ORIGINAL
    // value. Web callers don't define highestQualityIndex and treat
    // an undefined highestQualityIndex as equaling this.ORIGINAL.
    if (highestQualityIndex === this.ORIGINAL) {
      highestQualityIndex = undefined;
    }
    for (let i = 0, len = this.getQualityCount(); i < len; i++) {
      if (lowestQualityIndex && i < lowestQualityIndex) {
        continue;
      }
      if (highestQualityIndex && i > highestQualityIndex) {
        return availableQualities;
      }
      if (comparator && comparator.call(this, i)) {
        return availableQualities;
      }
      availableQualities.push(this.getQualityByIndex(i));
    }
    return availableQualities;
  },
  getResolutionFromSize: function (size: any) {
    // Format: 'widthxheight'
    const dimensions = size.split('x');
    const height = parseInt(dimensions[1], 10);
    return this.getResolutionFromHeight(height);
  },
  getResolutionFromHeight: function (height: any) {
    // Values are derived from Plex Media Server's MediaItem.cpp
    if (height > 2560) {
      return '8k';
    } else if (height > 1100) {
      return '4k';
    } else if (height > 720) {
      return '1080';
    } else if (height > 576) {
      return '720';
    } else if (height > 480) {
      return '576';
    } else if (height > 360) {
      return '480';
    } else {
      return 'sd';
    }
  },
  getHeightFromResolution: function (resolution: any) {
    switch (resolution) {
      case '8k':
        return 5120;
      case '4k':
        return 2560;
      case '1080':
        return 1080;
      case '720':
        return 720;
      case '576':
        return 576;
      case '480':
        return 480;
      case 'sd':
        return 360;
      default:
        return 360;
    }
  },
  // Returns the quality index equal to or closest to the target value
  // without going over.
  getNearestIndex: function (collection: any, target: any) {
    if (!target) {
      return this.ORIGINAL;
    }
    let qualityIndex = collection.indexOf(target);
    if (qualityIndex < 0) {
      qualityIndex = 0;
      for (let i = 0, len = collection.length; i < len; i++) {
        const value = collection[i];
        if (value > target) {
          break;
        }
        qualityIndex = i;
      }
    }
    return qualityIndex;
  },
};
export default abstractPlayerQualities;
