/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import storageAdapter from 'app/js/adapters/storageAdapter';
import commands from 'app/js/commands/commands';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import getAuthXHRErrors from 'app/js/stores/actions/helpers/user/getAuthXHRErrors';
import getSignInErrorMessage from 'app/js/stores/actions/helpers/user/getSignInErrorMessage';
import dispatcher from 'app/js/utils/dispatcher';

//
// Action Types

export const SIGN_IN_WITH_AUTH_TOKEN = 'user/signIn/signInWithAuthToken';

//
// Action Creators

export const signInWithAuthToken = createActionCreator(SIGN_IN_WITH_AUTH_TOKEN);

//
// Helpers

function signIn(appModel: any, { authToken, onSignInSuccess }: any) {
  const cloudServer = appModel.get('cloudServer');
  const userModel = appModel.get('user');

  return $.ajax(
    cloudServer.getXHROptions(_.result(userModel, 'url'), {
      headers: { 'X-Plex-Token': authToken },
    })
  )
    .then((response) => userModel.signIn(response))
    .then(() => {
      if (onSignInSuccess) {
        onSignInSuccess();
      }
    })
    .fail((xhr) => {
      const errors = getAuthXHRErrors(xhr);
      dispatcher.trigger('show:alert', {
        message: getSignInErrorMessage(errors),
      });
    });
}

//
// Action Handlers

export const signInLaunchActionHandlers = {
  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [SIGN_IN_WITH_AUTH_TOKEN](appModel: any, payload: any, dispatch: any) {
    const { authToken, onSignInSuccess } = payload;
    const userModel = appModel.get('user');

    if (
      userModel.get('isSignedIn') &&
      userModel.get('authToken') !== authToken
    ) {
      // Refresh to use the token and sign in the user in.
      commands
        .execute('resetUser')
        .then(() => storageAdapter.set('myPlexAccessToken', authToken))
        .then(() => window.location.reload());
    } else {
      signIn(appModel, { authToken, onSignInSuccess });
    }
  },
};
