import { SagaIterator } from 'redux-saga';
import {
  all,
  call,
  cancel,
  fork,
  put,
  take,
  takeLatest,
  takeLeading,
} from 'redux-saga/effects';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import { MetadataType } from 'app/js/v4/core/metadata/metadataTypes';
import getServerUTCTime from 'app/js/v4/features/dashboard/utils/getServerUTCTime';
import api from 'app/js/v4/utils/data/api';
import { getTimePeriodTimespan, TimePeriod } from 'app/js/v4/utils/timePeriods';
import dashboardBandwidthSlice from './dashboardBandwidthSlice';
import { BandwidthFetchRequestedAction } from './dashboardBandwidthTypes';
import dashboardPlayHistorySlice from './dashboardPlayHistorySlice';
import { PlayHistoryFetchRequestedAction } from './dashboardPlayHistoryTypes';
import dashboardResourcesSlice from './dashboardResourcesSlice';
import { ResourcesFetchRequestedAction } from './dashboardResourcesTypes';
import dashboardTopPlayedSlice from './dashboardTopPlayedSlice';
import { TopPlayedFetchRequestedAction } from './dashboardTopPlayedTypes';
import dashboardTopUsersSlice from './dashboardTopUsersSlice';
import { TopUsersFetchRequestedAction } from './dashboardTopUsersTypes';

//
// Workers

export function* fetchBandwidth({
  payload,
}: BandwidthFetchRequestedAction): SagaIterator {
  try {
    const requestTimestampMilliseconds = Date.now();

    const response = yield call(api.get, '/statistics/bandwidth', {
      server: payload.server,
      params: {
        timespan: payload.timespan,
        'at>': payload.after,
      },
    });
    const container = response.data.MediaContainer;
    const date = response.headers.Date;

    // Only PMS 1.15.1 allows us to access the `Date` response header. If we
    // do not have access then fall back to our timestamp.
    const responseTimestampMilliseconds = date
      ? getServerUTCTime(date)
      : requestTimestampMilliseconds;

    yield put(
      dashboardBandwidthSlice.actions.bandwidthFetchSucceeded({
        data: container?.StatisticsBandwidth ?? [],
        requestTimestampMilliseconds,
        responseTimestampMilliseconds,
      })
    );
  } catch (error) {
    yield put(
      dashboardBandwidthSlice.actions.bandwidthFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchResources({
  payload,
}: ResourcesFetchRequestedAction): SagaIterator {
  try {
    const requestTimestampMilliseconds = Date.now();

    const response = yield call(api.get, '/statistics/resources', {
      server: payload.server,
      params: {
        timespan: getTimePeriodTimespan(TimePeriod.RealTime),
      },
    });
    const container = response.data.MediaContainer;

    yield put(
      dashboardResourcesSlice.actions.resourcesFetchSucceeded({
        data: container?.StatisticsResources ?? [],
        requestTimestampMilliseconds,
      })
    );
  } catch (error) {
    yield put(
      dashboardResourcesSlice.actions.resourcesFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchTopUsers({
  payload,
}: TopUsersFetchRequestedAction): SagaIterator {
  try {
    const response = yield call(fetchMediaStatistics, payload);
    const container = response.data.MediaContainer;

    yield put(
      dashboardTopUsersSlice.actions.topUsersFetchSucceeded({
        data: {
          statistics: container?.StatisticsMedia ?? [],
          accounts: container?.Account ?? [],
        },
      })
    );
  } catch (error) {
    yield put(
      dashboardTopUsersSlice.actions.topUsersFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchPlayHistory({
  payload,
}: PlayHistoryFetchRequestedAction): SagaIterator {
  try {
    const response = yield call(fetchMediaStatistics, payload);
    const container = response.data.MediaContainer;

    yield put(
      dashboardPlayHistorySlice.actions.playHistoryFetchSucceeded({
        data: {
          statistics: container?.StatisticsMedia ?? [],
          accounts: container?.Account ?? [],
        },
      })
    );
  } catch (error) {
    yield put(
      dashboardPlayHistorySlice.actions.playHistoryFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchTopPlayed({
  payload,
}: TopPlayedFetchRequestedAction): SagaIterator {
  try {
    const [moviesResponse, tvResponse, musicResponse, gameResponse] = yield all(
      [
        call(fetchLibraryTopPlayed, payload, MetadataType.Movie),
        call(fetchLibraryTopPlayed, payload, MetadataType.Show),
        call(fetchLibraryTopPlayed, payload, MetadataType.Artist),
        call(fetchLibraryTopPlayed, payload, MetadataType.Game),
      ]
    );

    yield put(
      dashboardTopPlayedSlice.actions.topPlayedFetchSucceeded({
        data: {
          moviesStatistics: moviesResponse.data.MediaContainer?.Metadata ?? [],
          tvStatistics: tvResponse.data.MediaContainer?.Metadata ?? [],
          musicStatistics: musicResponse.data.MediaContainer?.Metadata ?? [],
          gameStatistics: gameResponse.data.MediaContainer?.Metadata ?? [],
        },
      })
    );
  } catch (error) {
    yield put(
      dashboardTopPlayedSlice.actions.topPlayedFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Helper Workers

function* fetchMediaStatistics(
  payload:
    | PlayHistoryFetchRequestedAction['payload']
    | TopUsersFetchRequestedAction['payload']
): SagaIterator {
  return yield call(api.get, '/statistics/media', {
    server: payload.server,
    params: {
      timespan: payload.timespan,
      'at>': payload.after,
    },
  });
}

function* fetchLibraryTopPlayed(
  payload: TopPlayedFetchRequestedAction['payload'],
  metadataType: MetadataType
): SagaIterator {
  return yield call(api.get, '/library/all/top', {
    server: payload.server,
    params: {
      accountID: payload.accountID,
      type: metadataTypes.getMetadataType(metadataType).id,
      'viewedAt>': payload.viewedAt,
      limit: payload.limit,
    },
  });
}

//
// Watchers

function* watchBandwidthFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLatest(
      dashboardBandwidthSlice.actions.bandwidthFetchRequested,
      fetchBandwidth
    );

    yield take([
      dashboardBandwidthSlice.actions.reset,
      dashboardBandwidthSlice.actions.bandwidthAbortRequested,
    ]);
    yield cancel(task);
  }
}

function* watchResourcesFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLatest(
      dashboardResourcesSlice.actions.resourcesFetchRequested,
      fetchResources
    );

    yield take(dashboardResourcesSlice.actions.reset);
    yield cancel(task);
  }
}

function* watchTopUsersFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      dashboardTopUsersSlice.actions.topUsersFetchRequested,
      fetchTopUsers
    );

    yield take(dashboardTopUsersSlice.actions.reset);
    yield cancel(task);
  }
}

function* watchPlayHistoryFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      dashboardPlayHistorySlice.actions.playHistoryFetchRequested,
      fetchPlayHistory
    );

    yield take(dashboardPlayHistorySlice.actions.reset);
    yield cancel(task);
  }
}

function* watchTopPlayedFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      dashboardTopPlayedSlice.actions.topPlayedFetchRequested,
      fetchTopPlayed
    );

    yield take(dashboardTopPlayedSlice.actions.reset);
    yield cancel(task);
  }
}

export default function* dashboardSaga(): SagaIterator {
  yield all([
    fork(watchBandwidthFetchRequested),
    fork(watchResourcesFetchRequested),
    fork(watchTopUsersFetchRequested),
    fork(watchPlayHistoryFetchRequested),
    fork(watchTopPlayedFetchRequested),
  ]);
}
