/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const TranscodeSessionModel = AbstractModel.extend({
  //
  // Properties

  name: 'TranscodeSessionModel',
  context: ['server'],

  url() {
    return '/transcode/sessions/' + this.id;
  },

  syncedAttrs: {
    key: attrTypes.string,
    progress: attrTypes.float,
    throttled: attrTypes.boolean,
    speed: attrTypes.float,
    duration: attrTypes.integer,
    videoDecision: attrTypes.string, // (copy|transcode)
    audioDecision: attrTypes.string, // (copy|transcode)
    protocol: attrTypes.string,
    container: attrTypes.string,
    videoCodec: attrTypes.string,
    audioCodec: attrTypes.string,
    audioChannels: attrTypes.integer,
    width: attrTypes.string,
    height: attrTypes.string,
    transcodeHwDecoding: attrTypes.string,
    transcodeHwEncoding: attrTypes.string,
    timeStamp: attrTypes.integer,
    minOffsetAvailable: attrTypes.integer,
    maxOffsetAvailable: attrTypes.integer,
  },
});

export default TranscodeSessionModel;
