import $ from 'jquery';
import * as Sentry from '@sentry/browser';
import dimensionsAdapter from 'app/js/adapters/dimensionsAdapter';
import escapeRegExp from 'app/js/utils/string/escapeRegExp';

const dimensionKeys = [
  'X-Plex-Device',
  'X-Plex-Model',
  'X-Plex-Platform',
  'X-Plex-Platform-Version',
  'X-Plex-Product',
  'X-Plex-Version',
];

export default function errorTrackerInitializer(
  options: Record<string, string> = {},
  tags: Record<string, string> = {}
  // Use JQueryPromise<any> even though `void` is correct in order to make `alwaysResolve` usage correct which is currently untyped
): JQueryPromise<any> {
  // Sentry hooks into the global console scope which causes the "file" which
  // logs it to all come from the same place. This is undesirable during
  // development where developers may want to see the file which triggered the
  // log. During development we completely disable Sentry.
  if (__DEV__) {
    return $.Deferred().resolve().promise();
  }

  const isAppScriptRegExp = new RegExp(escapeRegExp(__ERROR_FILE_SIGIL__));

  // Configures the sample rate for error events, in the range of 0.0 to 1.0.
  // The default is 1.0 which means that 100% of error events are sent. If set
  // to 0.1 only 10% of error events will be sent. Events are picked randomly.
  // See: https://docs.sentry.io/platforms/javascript/configuration/filtering/
  const sampleRate = __BUNDLED__ ? 0.05 : 1.0;

  const finalOptions = {
    allowUrls: [isAppScriptRegExp],
    debug: __DEV__,
    dsn: __ERROR_TRACKER_ID__,
    // This value is also provided to the Sentry CLI plugin.
    environment: __SENTRY_ENVIRONMENT__,
    ignoreErrors: [
      // Chrome's ResizeObserver implementation may throw an error caught by
      // window.onerror when all observer notifications cannot be dispatched
      // within a single frame. It's benign and should not be an error.
      // See: https://github.com/w3c/csswg-drafts/issues/5023
      'ResizeObserver',
      // Ignore a high-volume error not originating in Plex code:
      // See: https://github.com/plexinc/plex-web-client/issues/13067
      'songId',
      // Unhandled Promise Rejections appear to be happening from some third
      // party libraries and can flood sentry
      // See: https://github.com/plexinc/plex-web-client/issues/18035
      /^Non-Error promise rejection captured/,
      // Promises that deal with localStorage can fail causing
      // unhandledrejection events which are caught and reported by sentry.
      // These errors are harmless and noise so filter ignore them
      /^SecurityError: The operation is insecure/,
    ],
    integrations: [
      // Override only the Breadcrumbs integration; other integrations are
      // unaffected. All breadcrumb types are enabled by default. The
      // integrations below are opted-out due to potential privacy concerns.
      new Sentry.Integrations.Breadcrumbs({ fetch: false, xhr: false }),
    ],
    release: __SENTRY_RELEASE__,
    sampleRate,
    version: __VERSION__,
    ...options,
  };

  const finalTags = dimensionKeys.reduce((acc, key) => {
    if (!acc.hasOwnProperty(key)) {
      acc[key] = dimensionsAdapter.prop(key);
    }

    return acc;
  }, tags);

  Sentry.init(finalOptions);

  Object.entries(finalTags).forEach(([key, value]) => {
    Sentry.setTag(key, value);
  });

  return $.Deferred().resolve().promise();
}
