import qs, { IParseOptions, IStringifyOptions } from 'qs';
import URI from 'urijs';

export interface ParsedParams {
  [key: string]: unknown;
}

export interface ParsedURI {
  isAbsolute: boolean;
  hash: string;
  host: string;
  hostname: string;
  href: string;
  origin: string;
  params: ParsedParams;
  pathname: string;
  port: string;
  protocol: string;
  search: string;
}

function sanitizeSegment(segment: string): string {
  return String(segment).replace(/^\/|\/$/g, '');
}

const PATH_PARAMS_HASH_REGEX = /^([^?#]*)(?:\?([^#]*))?(#.*)?/;
const PATH_PARAMS_REGEX = /([^?]*)\??(.*)()/;

function getLastSegment(segmentedPath?: string): string {
  if (segmentedPath) {
    const match = /[^/\\]+$/.exec(segmentedPath);
    const segment = match?.[0];

    return segment || segmentedPath;
  }

  return '';
}

function stringifyParams(params: object, options?: IStringifyOptions): string {
  // Plex Media Server does not accept brackets for arrays in the query
  // string.
  // See: https://github.com/plexinc/plex-media-server/issues/3081

  return qs.stringify(params, {
    arrayFormat: 'repeat',
    ...options,
  });
}

function parseParams(
  queryString: string,
  options?: IParseOptions
): ParsedParams {
  return qs.parse(queryString, options);
}

function parseUri(uri: string, options?: IParseOptions): ParsedURI {
  const parts = URI.parse(uri);

  const protocol = parts.protocol ? `${parts.protocol}:` : '';
  const port = parts.port ? String(parts.port) : '';
  const host = parts.hostname
    ? (port && `${parts.hostname}:${port}`) || parts.hostname
    : '';
  const hostname = parts.hostname || '';
  const hash = parts.fragment ? `#${parts.fragment}` : '';
  const href = uri;
  const origin = protocol && host ? `${protocol}//${host}` : '';
  const pathname = parts.path || '';
  const search = parts.query ? `?${parts.query}` : '';
  const params = parts.query ? parseParams(parts.query, options) : {};

  return {
    isAbsolute: /^[\w:]*\/\//.test(uri),
    hash,
    host,
    hostname,
    href,
    origin,
    params,
    pathname,
    port,
    protocol,
    search,
  };
}

function parseUrl(url: string): ParsedURI {
  const properties = { ...parseUri(url) };

  // URL parsing inserts fallback values when the parsed string does not contain
  // the related value. You can see this in action by running the gist below
  // with different `href` inputs.
  // See: https://gist.github.com/mseeley/f501b72c3dbee1bc25e5757ece7c16d0

  const isFile = /file/.test(properties.protocol);

  if (!properties.protocol) {
    properties.protocol = window.location.protocol;
  }

  if (!isFile) {
    if (!properties.host) {
      properties.host = window.location.host;
    }

    if (!properties.hostname) {
      properties.hostname = window.location.hostname;
    }

    if (!properties.port) {
      properties.port = window.location.port;
    }
  }

  if (!properties.origin) {
    properties.origin = isFile
      ? `${properties.protocol}//`
      : `${properties.protocol}//${properties.host}`;
  }

  return properties;
}

function join(
  segments: (string | null | undefined)[] | string,
  params?: object | string,
  options?: {
    recognizeHash?: boolean;
    parseParams?: boolean;
    stringifyOptions?: IStringifyOptions;
    trailingSlash?: boolean;
  }
): string {
  // Normalize segments to an array of truthy strings.
  if (typeof segments === 'string') {
    segments = [segments];
  }

  segments = segments.filter((segment: any) => segment);

  // Once segments are normalized, go through splitting them into parts;
  // either path+params+hash, or just path+params.
  const recognizeHash = options?.recognizeHash;
  const regex = recognizeHash ? PATH_PARAMS_HASH_REGEX : PATH_PARAMS_REGEX;

  // Walk the segments, building up path, params, and hash parts.
  const traversal = segments.reduce(
    (acc: any, segment: any) => {
      const match = sanitizeSegment(segment).match(regex);

      if (!match) {
        return acc;
      }

      const [, path, segmentParams, hash] = match;

      if (path) {
        acc.path = acc.path ? acc.path + '/' + path : path;
      }

      if (segmentParams) {
        acc.params = acc.params
          ? acc.params + '&' + segmentParams
          : segmentParams;
      }

      if (hash) {
        acc.hash = hash;
      }

      return acc;
    },
    {
      path: '',
      params: '',
      hash: '',
    }
  );

  let stringifiedParams: string | undefined;
  if (options?.parseParams) {
    // Parse the built-up string params and merge the new params in.
    stringifiedParams = stringifyParams(
      Object.assign(
        parseParams(traversal.params),
        typeof params === 'string' ? parseParams(params) : params
      )
    );
  } else if (traversal.params || params) {
    if (traversal.params) {
      stringifiedParams = traversal.params;
    }

    if (params) {
      const newParams =
        typeof params === 'string'
          ? params
          : stringifyParams(params, options?.stringifyOptions);

      if (newParams) {
        stringifiedParams = stringifiedParams
          ? `${stringifiedParams}&${newParams}`
          : newParams;
      }
    }
  }

  // Check if the first segment began with a slash before sanitization,
  // and preserve it if so.
  const leadingSlash =
    segments.length && segments[0]?.charAt(0) === '/' ? '/' : '';

  const path = traversal.path;

  // Only add a trailing slash if one is not already present.
  const trailingSlash =
    options?.trailingSlash && path.length && path[path.length - 1] !== '/'
      ? '/'
      : '';

  return (
    leadingSlash +
    path +
    trailingSlash +
    (stringifiedParams ? '?' + stringifiedParams : '') +
    traversal.hash
  );
}

const path = {
  getLastSegment,
  stringifyParams,
  parseParams,
  parseUrl,
  parseUri,
  join,
};

export default path;
