/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import { CONTENT_CLASS_NAMES } from 'app/js/v4/App';
import useLocationSearchParams from 'app/js/v4/core/ui/hooks/useLocationSearchParams';
import parseString from 'app/js/v4/utils/parser/parseString';
import SetupView from 'app/js/views/setup/SetupView';
import MarionetteComponent from 'app/js/views/v3/components/MarionetteComponent';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import ServerPageConnector from 'app/js/views/v3/components/pages/ServerPageConnector';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import AcceptEULAPageConnector from 'app/js/views/v3/components/pages/setup/AcceptEULAPageConnector';
import createKey from 'app/js/views/v3/helpers/createKey';

type SetupPageProps = {
  serverEntityID?: string;
  isWaitingForPrimaryServer: boolean;
};

function SetupPage(props: SetupPageProps) {
  const { serverEntityID, isWaitingForPrimaryServer } = props;
  const params = useLocationSearchParams();
  const forwardUrl = parseString(params.forwardUrl);

  if (!serverEntityID) {
    return isWaitingForPrimaryServer ? (
      <EmptyPage>
        <Spinner />
      </EmptyPage>
    ) : (
      <ErrorPage
        description={t(
          "The server you're trying to access doesn't seem to exist."
        )}
        title={t('Media server not found')}
      />
    );
  }

  return (
    <ServerPageConnector
      hideUnclaimedPage={true}
      serverEntityID={serverEntityID}
    >
      {forwardUrl ? (
        <AcceptEULAPageConnector
          forwardUrl={forwardUrl}
          serverEntityID={serverEntityID}
        />
      ) : (
        <MarionetteComponent
          key={createKey('SetupPage', serverEntityID)}
          className={CONTENT_CLASS_NAMES}
          view={SetupView}
          viewOptions={{ serverEntityID, subset: 'setupBasic' }}
        />
      )}
    </ServerPageConnector>
  );
}

export default SetupPage;
