/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import log from 'app/js/utils/log/log';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import resolveMediaFromOrigin from './resolveMediaFromOrigin';

export function fetchMetadataItemModel(
  metadataItemModel: any,
  options: any = {}
) {
  const { itemProvider } = options;
  const data = {
    includeConcerts: 1,
    includeExtras: 1,
    includeOnDeck: 1,
    includePopularLeaves: 1,
    includePreferences: 1,
    includeChapters: 1,
    includeStations: 1,
    includeMarkers: 1,
    includeExternalMedia: 1,
    asyncAugmentMetadata: 1,
  };
  if (metadataItemModel.get('type') === metadataTypes.TRACK) {
    (data as any).includeRelated = 1;
  }
  if (
    !options.forceSyncCheckFiles &&
    metadataItemModel.server.supports('asyncCheckFiles')
  ) {
    (data as any).asyncCheckFiles = 1;
    (data as any).asyncRefreshAnalysis = 1;
    (data as any).asyncRefreshLocalMediaAgent = 1;
  } else {
    (data as any).checkFiles = 1;
  }
  // If the item is from a different provider to the context then we need to
  // fetch from the source but merge with the context provided model to
  // preserve attributes such as `source` and `attribution`.
  const provider = itemProvider || metadataItemModel.provider;
  const responseType = getSupportedResponseType(provider.server);
  const url = provider.resolveUrl(metadataItemModel.url(), {
    appendHeaders: true,
  });
  const xhrOptions = provider.getXHROptions(url, {
    dataType: responseType.typeName,
    data,
    merge: true,
  });
  return metadataItemModel.fetch(xhrOptions).done(() => {
    metadataItemModel.set('isPreFetched', true);
  });
}

/**
 * @param metadataItemModel {Object} The model to fetch
 * @param options.forceFetch {Boolean} When true; will fetch the metadata item even when item is pre-fetched
 * @param options.forceSyncCheckFiles {Boolean}
 * @param options.itemProvider {Object} ProviderModel override for augmented items
 * @returns {Promise}
 */
export default function fetchPrePlayMetadataItem(
  metadataItemModel: any,
  options = {}
) {
  let promise =
    (options as any).forceFetch || !metadataItemModel.get('isPreFetched')
      ? fetchMetadataItemModel(metadataItemModel, options)
      : $.Deferred().resolve().promise();
  // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
  promise = promise.then(() => {
    const Media = metadataItemModel.get('Media');
    const indirect = metadataItemModel.get('indirect');
    // Accept any non-empty Media that the provider has included up front.
    if (indirect && (!Media || !Media.length)) {
      const innerPromise = resolveMediaFromOrigin(
        resolve('appModel'),
        metadataItemModel
      ).then((NextMedia) => {
        metadataItemModel.set({ Media: NextMedia });
      });
      // @ts-expect-error ts-migrate(2769) FIXME: No overloads match this call.
      innerPromise.then(null, (error) => {
        log.error('[Metadata] %s', (error as any).message);
      });
      return innerPromise;
    }
  });
  return promise;
}
