/* eslint-plugin-disable @typescript-eslint */

/**
 * @param {MetadataModel|MetadataItem} metadataItemOrModel
 * @param {boolean} isPrivateConnection
 * @returns {boolean} true if item is considered local
 */
export default function isLocalMetadataItem(
  metadataItemOrModel: any,
  isPrivateConnection: any
) {
  const metadataItem = metadataItemOrModel.toJSON
    ? metadataItemOrModel.toJSON()
    : metadataItemOrModel;

  const { isExtra, guid } = metadataItem;

  const isOnlineExtra = isExtra && !/^file:\/\//.test(guid);

  return !isOnlineExtra && isPrivateConnection;
}
