/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as connectionTestStates from 'app/js/utils/constants/server/connectionTestStates';
import assert from 'app/js/utils/log/assert';
import getIpAddressFromPlexDirectAddress from 'app/js/utils/url/getIpAddressFromPlexDirectAddress';
import * as ip from 'app/js/utils/url/ip';
import path from 'app/js/utils/url/path';
import ConnectionSourceCollection from './ConnectionSourceCollection';

const requiredBoolean = attrTypes.boolean.isRequired;
const ConnectionModel = AbstractModel.extend({
  //
  // Properties
  name: 'ConnectionModel',
  context: ['server'],
  idAttribute: 'uri',
  sessionAttrs: {
    // The currentTest attribute should be an object that allows the test to be
    // aborted (e.g. jqXHR from $.ajax).
    currentTest: attrTypes.custom(
      (value: any) =>
        Array.isArray(value) && value.every((v) => _.isFunction(v.abort))
    ),
    testState: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 6.
      connectionTestStates.UNTESTED,
      connectionTestStates.PENDING,
      connectionTestStates.CONNECTED,
      connectionTestStates.UNAUTHORIZED,
      connectionTestStates.UNAVAILABLE,
      connectionTestStates.ABORTED
    ).isRequired,
    isBundled: requiredBoolean,
    isFallback: requiredBoolean,
  },
  sessionRelations: {
    sources: {
      relationClass: ConnectionSourceCollection,
    },
  },
  syncedAttrs: {
    uri: attrTypes.string,
    scheme: attrTypes.string,
    address: attrTypes.string,
    port: attrTypes.string,
    relay: attrTypes.boolean,
  },
  derivedAttrs: {
    isUntested() {
      return (this as any).get('testState') === connectionTestStates.UNTESTED;
    },
    isPending() {
      return (this as any).get('testState') === connectionTestStates.PENDING;
    },
    isConnected() {
      return (this as any).get('testState') === connectionTestStates.CONNECTED;
    },
    isUnauthorized() {
      return (
        (this as any).get('testState') === connectionTestStates.UNAUTHORIZED
      );
    },
    isUnavailable() {
      return (
        (this as any).get('testState') === connectionTestStates.UNAVAILABLE
      );
    },
    isFailed() {
      return (
        (this as any).get('isUnauthorized') ||
        (this as any).get('isUnavailable')
      );
    },
    isAborted() {
      return (this as any).get('testState') === connectionTestStates.ABORTED;
    },
    isLoopback() {
      const address = (this as any).get('address');
      return address === 'localhost' || ip.isLoopbackAddress(address);
    },
    isPrivate() {
      return (
        (this as any).get('isLoopback') ||
        ip.isPrivateAddress((this as any).get('address'))
      );
    },
    isHttps() {
      return (this as any).get('scheme') === 'https';
    },
    isSecure() {
      return (this as any).get('isLoopback') || (this as any).get('isHttps');
    },
    isPlexDirect() {
      return /\.plex\.direct:/.test((this as any).get('uri'));
    },
  },
  defaults: {
    testState: connectionTestStates.UNTESTED,
    isBundled: false,
    isFallback: false,
    relay: false,
  },
  url() {
    return this.get('uri');
  },
  //
  // Init
  constructor: function (attrs: any, options: any) {
    const clonedAttrs = _.clone(attrs);
    // Ensure that a uri exists since it is used for the connection id and url.
    if (_.isObject(attrs) && !attrs.uri) {
      assert(
        attrs.scheme && attrs.address,
        `A server connection requires either a uri or a scheme and address. ${attrs.scheme}://${attrs.address}`
      );
      clonedAttrs.uri = `${attrs.scheme}://${attrs.address}`;
      if (attrs.port) {
        clonedAttrs.uri += ':' + attrs.port;
      }
    }
    AbstractModel.prototype.constructor.call(this, clonedAttrs, options);
  },
  initialize() {
    this.listenTo(this.get('sources'), 'remove reset', this.onSourcesChange);
  },
  //
  // Control
  augmentResponse(response: any) {
    // Connections from `/api/resources` use the attribute name "protocol"
    // instead of "scheme".
    const protocol = response.protocol;
    if (protocol) {
      return _.extend({}, response, { scheme: protocol });
    }
    return response;
  },
  getAddressFromUri() {
    return path.parseUrl(this.get('uri')).hostname;
  },
  getIpAddressFromPlexDirectAddress() {
    return getIpAddressFromPlexDirectAddress(this.get('address'));
  },
  getLoopbackAttributes() {
    const loopbackConnection = {
      scheme: 'http',
      address: '127.0.0.1',
      port: this.get('port'),
    };
    return loopbackConnection;
  },
  hasSource(sourceID: any) {
    return this.get('sources').some((source: any) => {
      return sourceID && source.get('id') === sourceID;
    });
  },
  //
  // Listeners
  onSourcesChange() {
    // A connection should not exist without any sources. If all sources are
    // removed, destroy the connection.
    if (!this.get('sources').length) {
      this.trigger('destroy', this, this.collection);
    }
  },
});
export default ConnectionModel;
