/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import ProviderModel from 'app/js/models/provider/ProviderModel';
import ServerModel from 'app/js/models/server/ServerModel';

const BackgroundModel = AbstractModel.extend({
  //
  // Properties

  name: 'BackgroundModel',

  sessionAttrs: {
    imageUrl: attrTypes.string,
    themeMusicUrl: attrTypes.string,
    allowBlur: attrTypes.boolean,
    isBackgroundPending: attrTypes.boolean,
  },

  sessionRelations: {
    server: {
      relationClass: ServerModel,
      deferred: true,
    },
    provider: {
      relationClass: ProviderModel,
      deferred: true,
    },
  },

  defaults: {
    allowBlur: true,
    isBackgroundPending: false,
  },
});

export default BackgroundModel;
