import { createSlice } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  ChannelsGridSetStartTimeAction,
  ChannelsGridSetViewportSizeAction,
  ChannelsGridState,
  GuideChannelsFetchFailedAction,
  GuideChannelsFetchRequestedAction,
  GuideChannelsFetchSucceededAction,
  LiveTVState,
} from './liveTVTypes';

const HALF_HOUR_IN_SECONDS = 30 * 60;
const MIN_CHANNEL_CELLS = 1;
const PAST_AIRINGS_DURATION_IN_SECONDS = HALF_HOUR_IN_SECONDS * 6;

export const initialState: LiveTVState = {
  guideChannels: {
    channels: [],
    isPending: false,
    isPopulated: false,
    error: null,
  },

  channelsGrid: {
    startTime: 0,
    visibleColumns: 1,
    columnTimes: [],
    columnWidth: 0,
    viewportWidth: 0,
    viewportHeight: 0,
    airedProgress: 0,
  },
};

function getColumnTimes(
  startTime: number,
  visibleColumns: number
): ChannelsGridState['columnTimes'] {
  const roundedStartTime =
    Math.floor(startTime / HALF_HOUR_IN_SECONDS) * HALF_HOUR_IN_SECONDS;
  const columnTimes = [];

  // Add an extra frame for the end time, so use <= for comparison here
  for (let i = 0; i <= visibleColumns; i++) {
    columnTimes.push(roundedStartTime + i * HALF_HOUR_IN_SECONDS);
  }

  return columnTimes;
}

const liveTVSlice = createSlice({
  name: 'livetv',

  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    //
    // Channels Grid

    channelsGridResetStartTime({ channelsGrid }) {
      const startTime = Math.floor(Date.now() / 1000);
      const columnTimes = getColumnTimes(
        startTime,
        channelsGrid.visibleColumns
      );

      channelsGrid.startTime = startTime;
      channelsGrid.columnTimes = columnTimes;
    },

    channelsGridSetStartTime(
      { channelsGrid },
      { payload }: ChannelsGridSetStartTimeAction
    ) {
      const columnTimes = getColumnTimes(
        payload.startTime,
        channelsGrid.visibleColumns
      );

      channelsGrid.startTime = payload.startTime;
      channelsGrid.columnTimes = columnTimes;
    },

    channelsGridSetViewportSize(
      { channelsGrid },
      { payload }: ChannelsGridSetViewportSizeAction
    ) {
      const {
        width = 1,
        height = 1,
        overflowWidth = 0,
        channelWidth = 1,
        channelTitleWidth = 0,
      } = payload;

      const viewportWidth = Math.floor(width + overflowWidth);
      const visibleColumns =
        Math.max(
          Math.floor((width - channelTitleWidth) / channelWidth),
          MIN_CHANNEL_CELLS
        ) + 1;
      const columnWidth = Math.floor(
        (viewportWidth - channelTitleWidth) / visibleColumns
      );
      const columnTimes = getColumnTimes(
        channelsGrid.startTime,
        visibleColumns
      );

      channelsGrid.visibleColumns = visibleColumns;
      channelsGrid.columnTimes = columnTimes;
      channelsGrid.columnWidth = columnWidth;
      channelsGrid.viewportWidth = viewportWidth;
      channelsGrid.viewportHeight = Math.floor(height);
    },

    channelsGridUpdateAiredProgress({ channelsGrid }) {
      const { columnTimes } = channelsGrid;
      const startTime = columnTimes[0];
      const endTime = columnTimes[columnTimes.length - 1];
      const duration = endTime - startTime;
      const now = Math.floor(Date.now() / 1000);
      const airedProgress = Math.min(
        100,
        Math.max(0, ((now - startTime) / duration) * 100)
      );

      channelsGrid.airedProgress = airedProgress;
    },

    channelsGridNavigateTimeForward({ channelsGrid }) {
      const nextStartTime = channelsGrid.columnTimes[0] + HALF_HOUR_IN_SECONDS;
      const columnTimes = getColumnTimes(
        nextStartTime,
        channelsGrid.visibleColumns
      );

      channelsGrid.startTime = nextStartTime;
      channelsGrid.columnTimes = columnTimes;
    },

    channelsGridNavigateTimeBack({ channelsGrid }) {
      const nextStartTime = channelsGrid.columnTimes[0] - HALF_HOUR_IN_SECONDS;
      const earliestAiringTime =
        Math.floor(Date.now() / 1000) - PAST_AIRINGS_DURATION_IN_SECONDS;

      if (nextStartTime + HALF_HOUR_IN_SECONDS > earliestAiringTime) {
        const columnTimes = getColumnTimes(
          nextStartTime,
          channelsGrid.visibleColumns
        );

        channelsGrid.startTime = nextStartTime;
        channelsGrid.columnTimes = columnTimes;
      }
    },

    //
    // Guide Channels

    guideChannelsFetchRequested(
      { guideChannels },
      _action: GuideChannelsFetchRequestedAction
    ) {
      markPending(guideChannels);
    },

    guideChannelsFetchSucceeded(
      { guideChannels },
      { payload }: GuideChannelsFetchSucceededAction
    ) {
      markPopulated(guideChannels);
      guideChannels.channels = payload.data;
    },

    guideChannelsFetchFailed(
      { guideChannels },
      { payload }: GuideChannelsFetchFailedAction
    ) {
      markFailed(guideChannels, payload.error);
    },
  },
});

export default liveTVSlice;
