import { SagaIterator } from 'redux-saga';
import { all, fork } from 'redux-saga/effects';
import dvrSetupSaga from './dvrSetupSaga';
import epgSettingsSaga from './epgSettingsSaga';

export default function* dvrManagementSaga(): SagaIterator {
  yield all([fork(dvrSetupSaga), fork(epgSettingsSaga)]);
}
