/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import ProviderEPGChannelModel from 'app/js/models/provider/ProviderEPGChannelModel';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as providerSettingIDs from 'app/js/utils/constants/providerSettingIDs';

const ProviderEPGRecentChannelCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ProviderEPGChannelCollection',
  context: ['server', 'provider'],
  model: ProviderEPGChannelModel,
  url() {
    const lineupSetting = this.provider
      .get('features')
      .get(providerFeatureTypes.SETTINGS)
      .get('settings')
      .get(providerSettingIDs.RECENT_CHANNELS_IN_GRID);

    return lineupSetting.get('key');
  },
});

export default ProviderEPGRecentChannelCollection;
