/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function analyzeSectionCommand(options: any) {
  const deferred = $.Deferred();
  const section = options.section;
  const url = '/library/sections/' + section.id + '/analyze';
  const xhrOptions = _.extend(section.server.getXHROptions(url), {
    type: 'PUT',
  });

  function analyzeSection() {
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(function () {
      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem analyzing the library.'),
      });

      deferred.reject(xhr);
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr) {
      confirmModal.hide();
    });
  }

  const confirmModal = new ConfirmModal({
    title: t('Analyze Library'),
    message: t(
      'Analyzes all the media files in the library. The analysis captures information about the media which helps Plex decide how to optimally play back the media.'
    ),
    confirmLabel: t('Analyze'),
    cancelLabel: t('Cancel'),
    async: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(analyzeSection);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return deferred.promise();
}

export default analyzeSectionCommand;
