/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import { navigateBack } from 'app/js/stores/actions/appActions';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deletePlaylistCommand(options: any) {
  const deferred = $.Deferred();
  const isAlbum = options.item.get('playlistType') === 'photo';

  function deletePlaylist() {
    const xhrDeferred = options.item.destroy();

    xhrDeferred.done(function () {
      if (!options.skipRedirect) {
        resolve('appStore').dispatch(navigateBack());
      }

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr: any) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: isAlbum
          ? t('There was a problem deleting the album.')
          : t('There was a problem deleting the playlist.'),
      });

      deferred.reject(xhr);
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr: any) {
      confirmModal.hide();
    });
  }

  const confirmModal = new ConfirmModal({
    title: isAlbum ? t('Delete Album') : t('Delete Playlist'),
    message: t(
      'Are you sure you want to delete {1}? This cannot be undone.',
      options.item.get('title')
    ),
    confirmLabel: t('Delete'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(deletePlaylist);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return deferred.promise();
}

export default deletePlaylistCommand;
