/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const FirstCharacterModel = AbstractModel.extend({
  //
  // Properties

  name: 'FirstCharacterModel',
  context: ['server', 'provider', 'section'],

  idAttribute: 'key',
  rootPropertyName: 'Directory',

  syncedAttrs: {
    key: attrTypes.string,
    title: attrTypes.string,
    size: attrTypes.integer,
  },

  urlRoot() {
    return `/library/sections/${this.section.id}/firstCharacter`;
  },
});

export default FirstCharacterModel;
