/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { readStoredPIN } from 'app/js/stores/actions/user/authCodeExchangeActions';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import RedirectToConnector from 'app/js/views/v3/components/pages/serverSettings/RedirectToConnector';
import ServiceAuthUrlConnector from 'app/js/views/v3/components/pages/serverSettings/ServiceAuthUrlConnector';

const mapDispatchToProps = {
  dispatchReadStoredPIN: readStoredPIN,
};

type SignInLaunchPageRedirectConnectorProps = {
  pinID?: number;
  dispatchReadStoredPIN: (...args: any[]) => any;
};

class SignInLaunchPageRedirectConnector extends Component<SignInLaunchPageRedirectConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this.props.dispatchReadStoredPIN({ shouldCreatePIN: true });
  }

  //
  // Render

  render() {
    const { pinID, dispatchReadStoredPIN, ...otherProps } = this.props;

    if (pinID) {
      return (
        <ServiceAuthUrlConnector
          component={RedirectToConnector}
          pinID={pinID}
          replace={true}
          {...otherProps}
        />
      );
    }

    return (
      <EmptyPage>
        <Spinner />
      </EmptyPage>
    );
  }
}

export default connect(
  selectAuthCodeExchange,
  mapDispatchToProps
)(SignInLaunchPageRedirectConnector);
