/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import Cache from 'app/js/utils/cache/Cache';

const ModelCache = Cache.extend({
  //
  // Properties

  maxCycles: 5,
  maxSize: 100,

  //
  // Control

  getItemCacheKey: function (model: any, attributes: any) {
    if (_.isFunction(model.cacheKey)) {
      attributes || (attributes = model.toJSON());

      return model.cacheKey(attributes);
    }
  },
});

export default ModelCache;
