export enum MetadataType {
  Movie = 'movie',
  Show = 'show',
  Season = 'season',
  Episode = 'episode',
  Person = 'person',
  Artist = 'artist',
  Album = 'album',
  Track = 'track',
  Clip = 'clip',
  Photo = 'photo',
  Game = 'game',
  PhotoAlbum = 'photoalbum',
  Playlist = 'playlist',
  Collection = 'collection',
  Directory = 'directory',
  // Directory elements may contain a key that returns a pivot, or a Live TV
  // channel. We treat Directory and Metadata elements the same, so allow for
  // these types to exist on MetadataType
  Channel = 'channel',
  Hub = 'hub',
  List = 'list',
  Author = 'author',
  Audiobook = 'audiobook',
  Chapter = 'chapter',
}

export enum MetadataNumericType {
  Movie = 1,
  Show = 2,
  Season = 3,
  Episode = 4,
  Person = 7,
  Artist = 8,
  Album = 9,
  Track = 10,
  Clip = 12,
  Photo = 13,
  PhotoAlbum = 14,
  Playlist = 15,
  Collection = 18,
  Game = 19,
  Author = 20,
  Audiobook = 21,
  Chapter = 22,
}

export enum MetadataSubtype {
  Podcast = 'podcast',
  Webshow = 'webshow',
  Movie = 'movie',
  Show = 'show',
  Season = 'season',
  Episode = 'episode',
  Artist = 'artist',
  Album = 'album',
  Track = 'track',
  Trailer = 'trailer',
  DeletedScene = 'deletedScene',
  Interview = 'interview',
  MusicVideo = 'musicVideo',
  BehindTheScenes = 'behindTheScenes',
  SceneOrSample = 'sceneOrSample',
  LiveMusicVideo = 'liveMusicVideo',
  LyricMusicVideo = 'lyricMusicVideo',
  Concert = 'concert',
  Featurette = 'featurette',
  Short = 'short',
  Other = 'other',
  // Photo and Clip subtypes were added in recent (1.20.3+) PMS versions that
  // help identify clips and a photo library and movies in an Other Videos
  // library respectively. We augment these types during normalization for older
  // PMS versions
  Photo = 'photo',
  Clip = 'clip',
  Author = 'author',
  Audiobook = 'audiobook',
  Chapter = 'chapter',
}

