export interface MetadataItem
  extends Omit<
      MetadataItemResponse,
      | keyof MetadataItemRequired
      | keyof MetadataItemState
      | 'Context'
      | 'Extras'
      | 'OnDeck'
      | 'PopularLeaves'
      | 'Stations'
    >,
    MetadataItemRequired {
  // Properties added during normalization.
  sourceURI: string;
  listItemKey: string;
  liveTVChannel?: LiveTVChannel;
  isPlaylist: boolean;
  isLiveTVChannel: boolean;
  isExtra: boolean;
  isFolder: boolean;
  isUnreleased: boolean;
  stations?: MetadataItem[];
  extras?: MetadataItem[];
  popularLeaves?: {
    key: string;
    items: MetadataItem[];
  };
  onDeck?: MetadataItem;
  displayFields?: DisplayFieldName[];
  displayImage?: DisplayImageType;
}

interface MetadataItemRequired {
  userState: boolean;
  source: string;
}

