/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { discoverServers } from 'app/js/stores/actions/appActions';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createFetchHandler from 'app/js/stores/actions/helpers/createFetchHandler';
import minDuration from 'app/js/utils/promise/minDuration';

//
// Action Types

export const FETCH_OAUTH_PROVIDERS = 'oauth/fetchOauthProviders';
export const UNLINK_OAUTH_PROVIDER = 'oauth/unlinkOauthProvider';

//
// Action Creators

export const fetchOauthProviders = createActionCreator(FETCH_OAUTH_PROVIDERS);
export const unlinkOauthProvider = createActionCreator(UNLINK_OAUTH_PROVIDER);

//
// Action Handlers

export const oauthActionHandlers = {
  [FETCH_OAUTH_PROVIDERS]: createFetchHandler({
    key: 'oauthProviders',
    getModel: (appModel: any) => appModel.get('cloudServer').get(__PLATFORM__),
  }),

  [UNLINK_OAUTH_PROVIDER](appModel: any, payload: any, dispatch: any) {
    const cloudServer = appModel.get('cloudServer');
    const model = cloudServer
      .get(__PLATFORM__)
      .get('oauthProviders')
      .findWhere(payload);

    model.set('isUnlinking', true);

    // Ensure we have the plex.tv access token.
    const promise = cloudServer
      .populate()
      .then(() =>
        $.ajax(cloudServer.getXHROptions(payload.url, { type: 'DELETE' }))
      );

    minDuration(promise, 1000)
      .done(() => {
        model.set('unlinkError', null);
      })
      .fail((error) => {
        model.set('unlinkError', error);
      })
      .always(() => {
        dispatch(discoverServers());
        dispatch(fetchOauthProviders());
        model.set('isUnlinking', false);
      });
  },
};
