/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

export default function preserveMergeStrategy(
  // @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
  key: any,
  previousValue: any,
  nextValue: any
) {
  // When preserving attributes, do not allow values to be unset and do not
  // allow objects or arrays to be overwritten. Objects and arrays may have an
  // incomplete set of data.

  if (nextValue === undefined) {
    return previousValue;
  }

  if (_.isObject(previousValue)) {
    return previousValue;
  }

  return nextValue;
}
