import { SagaIterator } from 'redux-saga';
import { all, fork } from 'redux-saga/effects';
import coreSaga from 'app/js/v4/core/coreSaga';
import featuresSaga from 'app/js/v4/features/featuresSaga';

export default function* rootSaga(): SagaIterator {
  yield all([fork(coreSaga), fork(featuresSaga)]);
}
