/* eslint-plugin-disable @typescript-eslint */

import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import path from 'app/js/utils/url/path';

export const MEDIA_ROUTE_PREFIX = 'media';

export default function getProviderRouteFromIdentifiers(
  isCloud: any,
  machineIdentifier: any,
  providerIdentifier: any,
  params: any = {},
  pathSuffix: any
) {
  if (providerIdentifier === providerIdentifiers.HOME_SYNTHETIC) {
    return params.pivot ? path.join('#!', { pivot: params.pivot }) : '#!';
  }

  const parts = ['#!', MEDIA_ROUTE_PREFIX];

  if (!isCloud) {
    parts.push(encodeURIComponent(machineIdentifier));
  }

  parts.push(encodeURIComponent(providerIdentifier), pathSuffix);

  return path.join(parts, params);
}
