/* eslint-plugin-disable @typescript-eslint */

export default function getIpAddressFromPlexDirectAddress(address: any) {
  if (/\.plex\.direct$/.test(address)) {
    const delimiter = address.indexOf('.');

    if (delimiter > 0) {
      address = address.substring(0, delimiter);
      address = address.replace(/-/g, '.');
    }
  }

  return address;
}
