import React, { FunctionComponent, ReactElement } from 'react';
import assert from 'app/js/utils/log/assert';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import TooltipTrigger, {
  TooltipTriggerProps,
} from 'app/js/views/v3/components/core/tooltips/TooltipTrigger';
import styles from './NavBarIconButton.css';

type NavBarIconButtonProps = Omit<TooltipTriggerProps, 'component'> & {
  className?: string;
  tooltipClassName?: string;
  tooltipFocusedClassName?: string;
  labelClassName?: string;
  iconComponent: FunctionComponent<any>;
  isMenuOpen?: boolean;
  title?: string;
  ariaLabel?: string;
  label?: string;
  iconProps?: any;
};

function NavBarIconButton(props: NavBarIconButtonProps): ReactElement {
  const {
    className,
    tooltipClassName,
    tooltipFocusedClassName,
    labelClassName,
    iconComponent: IconComponent,
    title,
    ariaLabel,
    label,
    iconProps,
    ...otherProps
  } = props;

  const finalAriaLabel = title || ariaLabel;

  __DEV__ &&
    assert(
      finalAriaLabel,
      'NavBarIconButton requires an `ariaLabel` or `title` string prop.'
    );

  return (
    <TooltipTrigger
      aria-label={finalAriaLabel}
      className={className}
      // @ts-expect-error ts-migrate(2739) FIXME: Type 'typeof Link' is missing the following proper... Remove this comment to see the full error message
      component={Link}
      tooltip={title}
      tooltipClassName={tooltipClassName}
      tooltipFocusedClassName={tooltipFocusedClassName}
      {...otherProps}
    >
      <IconComponent {...iconProps} />
      {label ? <span className={labelClassName}>{label}</span> : null}
    </TooltipTrigger>
  );
}

NavBarIconButton.defaultProps = {
  className: styles.button,
  tooltipClassName: styles.tooltip,
  tooltipFocusedClassName: styles.tooltipTriggerHasFocus,
};

export default NavBarIconButton;
