import React, { ReactElement, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { discoverServers } from 'app/js/stores/actions/appActions';
import t from 'app/js/utils/t';
import selectServer from 'app/js/v4/core/servers/selectors/selectServer';
import EmptyPageContent from 'app/js/v4/core/ui/pages/components/empty/EmptyPageContent';
import ErrorPageContent from 'app/js/v4/core/ui/pages/components/empty/ErrorPageContent';
import Spinner from 'app/js/v4/core/ui/spinners/components/Spinner';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import UserPageConnector from 'app/js/views/v3/components/UserPageConnector';
import FullPage from 'app/js/views/v3/components/pages/FullPage';

export default function ServerAwaitPage(): ReactElement {
  const [isExpired, setIsExpired] = useState(false);
  const dispatch = useDispatch();

  const server = useSelector(selectServer);

  const acceptedEULASetting = server.desktop?.settings.find((setting) => {
    return setting.id === 'AcceptedEULA';
  });

  //
  // Effects

  useEffect(() => {
    const intervalID = window.setInterval(() => {
      dispatch(discoverServers());
    }, 2_000);

    return () => {
      window.clearInterval(intervalID);
    };
  }, [dispatch]);

  useEffect(() => {
    if (
      acceptedEULASetting &&
      server.isFullOwnedServer &&
      server.isConnected &&
      server.isClaimed
    ) {
      if (acceptedEULASetting.value) {
        navigateToRoute('home');
      } else {
        navigateToRoute('serverSetup', {
          machineIdentifier: server.machineIdentifier,
        });
      }
    }
  }, [
    acceptedEULASetting,
    server.isClaimed,
    server.isConnected,
    server.isFullOwnedServer,
    server.machineIdentifier,
  ]);

  useEffect(() => {
    const timeoutID = window.setTimeout(() => {
      setIsExpired(true);
    }, 300_000);

    return () => {
      window.clearTimeout(timeoutID);
    };
  }, []);

  //
  // Render

  return (
    <FullPage>
      {/* @ts-expect-error */}
      <UserPageConnector>
        {!isExpired ? (
          <EmptyPageContent description={t('Initializing Plex Media Server…')}>
            <Spinner />
          </EmptyPageContent>
        ) : (
          <ErrorPageContent>
            {t(
              'Something went wrong. Make sure the server is available and try again.'
            )}
          </ErrorPageContent>
        )}
      </UserPageConnector>
    </FullPage>
  );
}
