/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectUserFeaturesByUUID from './selectUserFeaturesByUUID';

const memo = {};

function createUserFeatureSelector(uuid: string): (appState: any) => boolean {
  const selector =
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    memo[uuid] ||
    createSelector(
      selectUserFeaturesByUUID,
      (userFeaturesByUUID) => !!userFeaturesByUUID[uuid]
    );

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  memo[uuid] = selector;

  return selector;
}

export default createUserFeatureSelector;
