import { createSlice } from '@reduxjs/toolkit';
import cleanXMLTVPath from 'app/js/utils/cleanXMLTVPath';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import random from 'app/js/utils/string/random';
import serverActivitiesSlice from 'app/js/v4/core/serverActivities/serverActivitiesSlice';
import { ServerActivityType } from 'app/js/v4/core/serverActivities/serverActivitiesTypes';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  BestEPGLineupSearchFailedAction,
  BestEPGLineupSearchRequestedAction,
  BestEPGLineupSearchSucceededAction,
  ChannelMappingChangedAction,
  ChannelMappingsFetchFailedAction,
  ChannelMappingsFetchRequestedAction,
  ChannelMappingsFetchSucceededAction,
  ChannelMappingsState,
  EPGCountriesFetchFailedAction,
  EPGCountriesFetchRequestedAction,
  EPGCountriesFetchSucceededAction,
  EPGCountryRegionSelectedAction,
  EPGCountryRegionsFetchFailedAction,
  EPGCountryRegionsFetchRequestedAction,
  EPGCountryRegionsFetchSucceededAction,
  EPGCountrySelectedAction,
  EPGLanguageSelectedAction,
  EPGLanguagesFetchFailedAction,
  EPGLanguagesFetchRequestedAction,
  EPGLanguagesFetchSucceededAction,
  EPGLineup,
  EPGLineupGroupFetchFailedAction,
  EPGLineupGroupFetchRequestedAction,
  EPGLineupGroupFetchSucceededAction,
  EPGLineupType,
  EPGPostalCodeChangedAction,
  EPGSettingsState,
  LineupChannelsState,
  LineupGroupsState,
  SelectedDVRLineupChannelsFetchFailedAction,
  SelectedDVRLineupChannelsFetchRequestedAction,
  SelectedDVRLineupChannelsFetchSucceededAction,
  SelectedEPGLineupGroup,
  SelectedEPGLineupGroupAddRequestedAction,
  SelectedEPGLineupGroupAddSucceededAction,
  SelectedEPGLineupGroupRemovedAction,
  SelectedLineupSelectedAction,
  XMLTVSelectedEPGLineupGroupAddedAction,
} from './dvrManagementTypes';
import dvrSetupSlice from './dvrSetupSlice';

export const initialChannelMappingsState: ChannelMappingsState = {
  mappings: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

const initialLineupChannelsState: LineupChannelsState = {
  channelsByLineupUUID: {},
  isPending: false,
  isPopulated: false,
  error: null,
};

const initialLineupGroupsState: LineupGroupsState = {
  epgLineupGroups: [],
  isPending: false,
  isPopulated: false,
  error: null,
};

export const initialState: EPGSettingsState = {
  channelMappings: initialChannelMappingsState,

  lineupChannels: initialLineupChannelsState,

  countries: {
    epgCountries: [],
    isPending: false,
    isPopulated: false,
    error: null,
  },

  languages: {
    epgLanguages: [],
    isPending: false,
    isPopulated: false,
    error: null,
  },

  regions: {
    epgCountryRegions: [],
    isPending: false,
    isPopulated: false,
    error: null,
  },

  lineupGroups: initialLineupGroupsState,

  selectedCountry: undefined,
  selectedLanguage: undefined,
  selectedRegion: undefined,
  selectedLineupGroups: [],
  postalCode: '',
};

const epgSettingsSlice = createSlice({
  name: 'epgSettings',

  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    //
    // Channel Mapping

    channelMappingsFetchRequested(
      { lineupChannels, channelMappings },
      _action: ChannelMappingsFetchRequestedAction
    ) {
      markPending(lineupChannels);
      markPending(channelMappings);
    },

    channelMappingsFetchSucceeded(
      { channelMappings, lineupChannels, selectedLineupGroups },
      { payload }: ChannelMappingsFetchSucceededAction
    ) {
      channelMappings.mappings = payload.channelMappings;
      markPopulated(channelMappings);

      payload.lineupChannels.forEach(({ uuid, Channel }) => {
        lineupChannels.channelsByLineupUUID[uuid] = Channel ?? [];
      });
      markPopulated(lineupChannels);

      selectedLineupGroups.forEach((lineupGroup) => {
        lineupGroup.isChannelMappingsFetched = true;
      });
    },

    channelMappingsFetchFailed(
      { channelMappings, lineupChannels },
      { payload }: ChannelMappingsFetchFailedAction
    ) {
      markFailed(channelMappings, payload.error);
      markFailed(lineupChannels, payload.error);
    },

    channelMappingChanged(
      { channelMappings },
      { payload }: ChannelMappingChangedAction
    ) {
      const { index, ...channelMappingAttributes } = payload;

      const mapping = channelMappings.mappings[index];

      Object.assign(mapping, channelMappingAttributes);
    },

    //
    // Lineups

    epgLineupGroupFetchRequested(
      { lineupGroups },
      _action: EPGLineupGroupFetchRequestedAction
    ) {
      markPending(lineupGroups);
    },

    epgLineupGroupFetchSucceeded(
      { lineupGroups },
      { payload }: EPGLineupGroupFetchSucceededAction
    ) {
      markPopulated(lineupGroups);

      lineupGroups.epgLineupGroups.push({
        uuid: payload.data.uuid,
        epgLineups: payload.data.epgLineups,
      });
    },

    epgLineupGroupFetchFailed(
      { lineupGroups },
      { payload }: EPGLineupGroupFetchFailedAction
    ) {
      markFailed(lineupGroups, payload.error);
    },

    bestEPGLineupSearchRequested(
      _state,
      _action: BestEPGLineupSearchRequestedAction
    ) {
      // Handled by saga, no state change.
    },

    bestEPGLineupSearchSucceeded(
      { selectedLineupGroups },
      { payload }: BestEPGLineupSearchSucceededAction
    ) {
      const lineupGroup = selectedLineupGroups.find(
        (epgLineupGroup) => epgLineupGroup.uuid === payload.lineupGroup.uuid
      );

      if (lineupGroup) {
        lineupGroup.activityUUID = payload.data;
        lineupGroup.isSelectedLineupPending = true;
      }
    },

    bestEPGLineupSearchFailed(
      _state,
      _action: BestEPGLineupSearchFailedAction
    ) {
      // Handled by saga, no state change.
    },

    selectedLineupSelected(
      { selectedLineupGroups },
      { payload }: SelectedLineupSelectedAction
    ) {
      const lineupGroup = selectedLineupGroups.find(
        (epgLineupGroup) => epgLineupGroup.uuid === payload.lineupGroupUUID
      );

      if (lineupGroup) {
        const selectedLineup = lineupGroup.epgLineups.find(
          (epgLineup) => epgLineup.uuid === payload.selectedLineupUUID
        );

        lineupGroup.selectedLineup = selectedLineup;
      }
    },

    selectedEPGLineupGroupAddRequested(
      _state,
      _action: SelectedEPGLineupGroupAddRequestedAction
    ) {
      // Handled by saga, no state change.
    },

    selectedEPGLineupGroupAddSucceeded(
      { selectedLineupGroups },
      { payload }: SelectedEPGLineupGroupAddSucceededAction
    ) {
      selectedLineupGroups.push({
        ...payload.lineupGroup,
        activityUUID: undefined,
        selectedLineup: undefined,
        isChannelMappingsFetched: false,
        isSelectedLineupPending: false,
        isSelectedLineupPopulated: false,
      });
    },

    selectedEPGLineupGroupRemoved(
      state,
      { payload }: SelectedEPGLineupGroupRemovedAction
    ) {
      state.selectedLineupGroups = state.selectedLineupGroups.filter(
        (epgLineupGroup) => epgLineupGroup.uuid !== payload.lineupGroupUUID
      );
    },

    xmltvSelectedEPGLineupGroupAdded(
      { selectedLineupGroups },
      { payload }: XMLTVSelectedEPGLineupGroupAddedAction
    ) {
      const { path, title } = payload;
      const cleanPath = cleanXMLTVPath(path);
      const lineupUUID = `lineup://${
        providerIdentifiers.XMLTV
      }/${cleanPath}#${encodeURIComponent(title)}`;

      const syntheticLineup: EPGLineup = {
        uuid: lineupUUID,
        title,
        lineupType: EPGLineupType.Virtual,
      };

      const syntheticLineupGroup: SelectedEPGLineupGroup = {
        uuid: `xmltv-${random()}`,
        epgLineups: [syntheticLineup],
        activityUUID: undefined,
        selectedLineup: syntheticLineup,
        isChannelMappingsFetched: false,
        isSelectedLineupPending: false,
        isSelectedLineupPopulated: true,
      };

      selectedLineupGroups.push(syntheticLineupGroup);
    },

    selectedDVRLineupChannelsFetchRequested(
      { lineupChannels },
      _action: SelectedDVRLineupChannelsFetchRequestedAction
    ): void {
      markPending(lineupChannels);
    },

    selectedDVRLineupChannelsFetchSucceeded(
      { lineupChannels },
      { payload }: SelectedDVRLineupChannelsFetchSucceededAction
    ): void {
      payload.lineupChannels.forEach(({ uuid, Channel }) => {
        lineupChannels.channelsByLineupUUID[uuid] = Channel ?? [];
      });
      markPopulated(lineupChannels);
    },

    selectedDVRLineupChannelsFetchFailed(
      { lineupChannels },
      { payload }: SelectedDVRLineupChannelsFetchFailedAction
    ) {
      markFailed(lineupChannels, payload.error);
    },

    //
    // Countries

    epgCountriesFetchRequested(
      { countries },
      _action: EPGCountriesFetchRequestedAction
    ) {
      markPending(countries);
    },

    epgCountriesFetchSucceeded(
      { countries },
      { payload }: EPGCountriesFetchSucceededAction
    ) {
      markPopulated(countries);
      countries.epgCountries = payload.data;
    },

    epgCountriesFetchFailed(
      { countries },
      { payload }: EPGCountriesFetchFailedAction
    ) {
      markFailed(countries, payload.error);
    },

    epgCountrySelected(state, { payload }: EPGCountrySelectedAction) {
      const selectedCountry = state.countries.epgCountries.find(
        (country) => country.code === payload.countryCode
      );

      state.selectedCountry = selectedCountry;
      state.selectedRegion = undefined;
      state.postalCode = undefined;
    },

    //
    // Country Regions

    epgCountryRegionSelected(
      state,
      { payload }: EPGCountryRegionSelectedAction
    ) {
      const selectedRegion = state.regions.epgCountryRegions.find(
        (region) => region.key === payload.regionKey
      );

      state.selectedRegion = selectedRegion;
    },

    epgCountryRegionsFetchRequested(
      { regions },
      _action: EPGCountryRegionsFetchRequestedAction
    ) {
      markPending(regions);
    },

    epgCountryRegionsFetchSucceeded(
      { regions },
      { payload }: EPGCountryRegionsFetchSucceededAction
    ) {
      markPopulated(regions);
      regions.epgCountryRegions = payload.data;
    },

    epgCountryRegionsFetchFailed(
      { regions },
      { payload }: EPGCountryRegionsFetchFailedAction
    ) {
      markFailed(regions, payload.error);
    },

    //
    // Languages

    epgLanguagesFetchRequested(
      { languages },
      _action: EPGLanguagesFetchRequestedAction
    ) {
      markPending(languages);
    },

    epgLanguagesFetchSucceeded(
      { languages },
      { payload }: EPGLanguagesFetchSucceededAction
    ) {
      markPopulated(languages);
      languages.epgLanguages = payload.data;
    },

    epgLanguagesFetchFailed(
      { languages },
      { payload }: EPGLanguagesFetchFailedAction
    ) {
      markFailed(languages, payload.error);
    },

    epgLanguageSelected(state, { payload }: EPGLanguageSelectedAction) {
      const selectedLanguage = state.languages.epgLanguages.find(
        (language) => language.code === payload.languageCode
      );

      state.selectedLanguage = selectedLanguage;
    },

    //
    // Postal Code

    epgPostalCodeChanged(state, { payload }: EPGPostalCodeChangedAction) {
      state.postalCode = payload.postalCode;
    },
  },

  extraReducers(builder) {
    builder
      .addCase(dvrSetupSlice.actions.setupReset, (state) => {
        // Selectively reset state to avoid unnecessary data fetching.
        state.channelMappings = initialChannelMappingsState;
        state.lineupChannels = initialLineupChannelsState;
        state.lineupGroups = initialLineupGroupsState;
        state.selectedRegion = undefined;
        state.selectedLineupGroups = [];
        state.postalCode = '';
      })

      .addCase(
        serverActivitiesSlice.actions.activityFinished,
        (state, { payload }) => {
          const { activity } = payload;

          if (activity.type === ServerActivityType.ProviderEpgLineupScan) {
            const lineupGroup = state.selectedLineupGroups.find(
              (epgLineupGroup) => epgLineupGroup.activityUUID === activity.uuid
            );

            if (lineupGroup) {
              const selectedLineup = lineupGroup.epgLineups.find(
                (epgLineup) => epgLineup.uuid === activity.Response.lineup
              );

              lineupGroup.activityUUID = undefined;
              lineupGroup.selectedLineup = selectedLineup;
              lineupGroup.isSelectedLineupPending = false;
              lineupGroup.isSelectedLineupPopulated = true;
            }
          }
        }
      );
  },
});

export default epgSettingsSlice;
