/* eslint-plugin-disable @typescript-eslint */

import migrateCustomPromotedToHubs from 'app/js/initializers/migrations/migrateCustomPromotedToHubs';
import UserExperienceSettingsModel from 'app/js/models/user/UserExperienceSettingsModel';
import assert from 'app/js/utils/log/assert';
import log from 'app/js/utils/log/log';
import migrateAudioBoost from './migrateAudioBoost';
import migrateDirectoryKeyToID from './migrateDirectoryKeyToID';
import migrateRelayReminders from './migrateRelayReminders';
import migrateSourceHiddenAt from './migrateSourceHiddenAt';
import migrateToAutoHome from './migrateToAutoHome';

const schemaVersionMigrations = {
  1: versionBumpOnlyMigration,
  2: migrateDirectoryKeyToID,
  3: versionBumpOnlyMigration,
  4: migrateRelayReminders,
  5: versionBumpOnlyMigration,
  6: versionBumpOnlyMigration,
};
// List of migrations to run after all schemaVersionMigrations have already run.
const migrations = [
  migrateToAutoHome,
  migrateSourceHiddenAt,
  migrateAudioBoost,
  migrateCustomPromotedToHubs,
];
// A migration that results in only the schemaVersion being set
function versionBumpOnlyMigration(userExperienceSettings: any) {
  return userExperienceSettings;
}
export default function migrateUserExperienceSettings(
  userExperienceSettings = {}
) {
  if (__DEV__) {
    Array.from({
      length: Math.max(
        UserExperienceSettingsModel.schemaVersion,
        ...Object.keys(schemaVersionMigrations).map((key) => parseInt(key, 10))
      ),
    })
      // @ts-expect-error
      .map((_, i) => schemaVersionMigrations[i + 1])
      .forEach((migration, i) => {
        assert(
          i < UserExperienceSettingsModel.schemaVersion,
          `Schema migration ${
            i + 1
          } greater than UserExperienceSettingsModel.schemaVersion ${
            UserExperienceSettingsModel.schemaVersion
          }`
        );
        assert(migration, `Missing expected schema migration version ${i + 1}`);
      });
  }

  // Default to zero as the settings could pre-date our usage of schemaVersion.
  let schemaVersion = (userExperienceSettings as any).schemaVersion || 0;
  while (schemaVersion < UserExperienceSettingsModel.schemaVersion) {
    (userExperienceSettings as any).schemaVersion = ++schemaVersion;
    // @ts-expect-error
    schemaVersionMigrations[schemaVersion](userExperienceSettings);
  }
  migrations.forEach((migration, i) => {
    try {
      migration(userExperienceSettings);
    } catch (error) {
      log.error('[User] Setting migration[%d] error', i, error);
    }
  });
  return userExperienceSettings;
}
