/* eslint-plugin-disable @typescript-eslint */

const propertyWhitelist = ['page', 'type', 'style', 'mode'];

export default function getPerformanceMeasureName(
  eventName: any,
  metricsProperties: any
) {
  let measureName = eventName;

  for (let i = 0; i < propertyWhitelist.length; i++) {
    const value = metricsProperties[propertyWhitelist[i]];
    if (value != null) {
      measureName = `${measureName}:${value}`;
    }
  }

  return measureName;
}
