import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import alerts from 'app/js/models/alerts';
import BaseModal from 'app/js/views/modals/BaseModal';
import UserSelectModalAlertBarView from 'app/js/views/modals/user/UserSelectModalAlertBarView';
import UserSelectModalPINPane from 'app/js/views/modals/user/UserSelectModalPINPane';
import UserSelectModalUsersPane from 'app/js/views/modals/user/UserSelectModalUsersPane';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/user/UserSelectModal.tpl';

const UserSelectModal = BaseModal.extend({
  //
  // Properties

  name: 'userSelectModal',
  className: 'user-select-modal modal modal-transparent fade',
  template: tpl,
  transparent: true,

  panes: {
    users: UserSelectModalUsersPane,
    pin: UserSelectModalPINPane,
  },

  regions: {
    content: '.modal-content',
    alertBar: '.modal-alert',
  },

  //
  // Init

  initialize: function (options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);

    const appModel = adapters.resolve('appModel');

    this.options = options;
    this.isForced = options.isForced;
    this.user = appModel.get('user');

    this.promise = $.Deferred();
  },

  showPane: function (paneType: any, options: any) {
    if (this.currentPane) {
      this.stopListening(this.currentPane);
    }

    const PaneConstructor = this.panes[paneType];
    const pane = new PaneConstructor(_.extend({}, this.options, options));

    this.listenTo(pane, {
      'change:pane': this.onChangePane,
      resolve: this.onResolve,
      reject: this.onReject,
    });

    this.currentPane = pane;
    this.content.show(pane);

    return pane;
  },

  showAlertBar: function () {
    const alert = alerts[0];
    alerts.length = 0;
    this.alertBar.show(
      new UserSelectModalAlertBarView({
        alert,
      })
    );
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);

    if (this.model) {
      this.showPane('pin', { focusDelay: this.focusDelay });
    } else {
      this.showPane('users');
    }

    this.showAlertBar();
  },

  onHide: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onHide.apply(this, arguments);

    if (this.promise.state() === 'pending') {
      this.promise.reject();
    }
  },

  onChangePane: function (paneType: any, options: any) {
    this.showPane(paneType, options);
  },

  onResolve: function (pin: any) {
    this.promise.resolve(pin);
    this.hide();
  },

  onReject: function () {
    this.promise.reject();
    this.hide();
  },
});

export default UserSelectModal;
