/* eslint-plugin-disable @typescript-eslint */

// Only one model in the collection can have a truthy value for this attribute.
const exclusiveBoolAttrMixin = {
  exclusiveBoolAttr(attr: any) {
    (this as any).listenTo(
      this,
      'change:' + attr,
      (changedModel: any, value: any) => {
        if (!value) {
          return;
        }
        (this as any).each((model: any) => {
          model.set(attr, model === changedModel ? value : false);
        });
      }
    );
  },
};
export default exclusiveBoolAttrMixin;
