/* eslint-plugin-disable @typescript-eslint */

import capabilities from 'app/js/utils/device/capabilities';
import AbstractReporter from 'app/js/utils/log/AbstractReporter';
import log from 'app/js/utils/log/log';

const LOG_METHODS = {
  [(log as any).LEVEL_LOG]: 'log',
  [(log as any).LEVEL_DEBUG]: 'debug',
  [(log as any).LEVEL_INFO]: 'info',
  [(log as any).LEVEL_WARN]: 'warn',
  [(log as any).LEVEL_ERROR]: 'error',
};
const ConsoleReporter = AbstractReporter.extend({
  //
  // Properties
  name: 'ConsoleReporter',
  //
  // Control
  hasSupport() {
    return capabilities.hasConsole;
  },
  //
  // Listeners
  onLogMessage(data: any) {
    const message = data.message;
    const method = LOG_METHODS[data.level];
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const logFn = window.console[method] || window.console.log;
    logFn.call(window.console, message);
  },
  onLogGroup(data: any) {
    // See: https://github.com/plexinc/plex-web-client/issues/4062
    const logFn = window.console.group || window.console.log;
    logFn.call(window.console, data.message);
  },
  onLogGroupCollapsed(data: any) {
    // See: https://github.com/plexinc/plex-web-client/issues/4062
    const logFn = window.console.groupCollapsed || window.console.log;
    logFn.call(window.console, data.message);
  },
  onLogGroupEnd() {
    // See: https://github.com/plexinc/plex-web-client/issues/4062
    if (window.console.groupEnd) {
      window.console.groupEnd();
    }
  },
});
export default ConsoleReporter;
