/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function cleanBundlesCommand(options: any) {
  const deferred = $.Deferred();
  const server = options.server;
  const url = '/library/clean/bundles';
  const xhrOptions = _.extend(server.getXHROptions(url), { type: 'PUT' });

  function optimize() {
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(function () {
      dispatcher.trigger('show:alert', {
        type: 'success',
        message: t('Unused bundles are being removed.'),
      });

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem removing unused bundles.'),
      });

      deferred.reject(xhr);
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr) {
      confirmModal.hide();
    });
  }

  const confirmModal = new ConfirmModal({
    title: t('Clean Bundles'),
    message: t(
      'The media server stores information about your media to disk, as well as posters, artwork, and other media resources from the internet. Running this task deletes any bundles which are no longer in use.'
    ),
    confirmLabel: t('Clean'),
    cancelLabel: t('Cancel'),
    async: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(optimize);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return deferred.promise();
}

export default cleanBundlesCommand;
