/* eslint-plugin-disable @typescript-eslint */

import AjaxQueue from 'app/js/utils/AjaxQueue';
import { METRICS_DEVELOPMENT_URL } from 'app/js/utils/constants/commonUrls';
import assert from 'app/js/utils/log/assert';
import path from 'app/js/utils/url/path';

export default function createPlexMetricsReporter() {
  const queue = new AjaxQueue();

  // The production baseURL is returned by plex.tv's /api/v2/user/privacy
  // resource. The same resource doesn't provide a development URL.
  let baseUrl = __DEV__ ? METRICS_DEVELOPMENT_URL : null;

  return {
    setBaseUrl(url: any) {
      baseUrl = url;
    },

    trackEvent(event: any) {
      if (!baseUrl) {
        assert(
          false,
          'Network metrics reporters requires a base URL to be set before tracking events'
        );
        return;
      }

      const url = path.join([baseUrl, 'collect/event']);
      const data = JSON.stringify(event);

      // The sendBeacon method is supported in all target environments but may
      // be disabled by a user's privacy settings.
      // See: https://github.com/plexinc/plex-web-client/pull/18550
      if (window.navigator.sendBeacon) {
        window.navigator.sendBeacon(url, data);
      } else {
        queue.send({
          type: 'POST',
          url,
          // Use `text/plain` to avoid the OPTIONS preflight request. The metrics
          // server forces parsing of content as JSON.
          contentType: 'text/plain',
          data,
        });
      }
    },
  };
}
