/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

// BaseClass is a class that can be extended, which allows for the easy
// implementation of inheritance and mixins using the prototype.

// Derived from the extend method used by Backbone.
const extend = function (protoProps: any, staticProps: any) {
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  const self = this;
  let child: any;

  // The constructor function for the new subclass is either defined by you
  // (the "constructor" property in your `extend` definition), or defaulted
  // by us to simply call the self's constructor.
  if (protoProps && _.has(protoProps, 'constructor')) {
    child = protoProps.constructor;

    if (__DEV__ && !/^function\s*\w*\(/.test(child.toString())) {
      // See: https://github.com/plexinc/plex-web-client/pull/13366#issuecomment-461696398
      throw new Error(
        'BaseClass constructor must be constructable; use long-hand declaration `constructor: function (…`'
      );
    }
  } else {
    child = function () {
      // Opera has some problems returning from a constructor when Dragonfly isn't running.
      // The || null seems to  be sufficient to stop it misbehaving. Found by ExtJS team.
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      // eslint-disable-next-line prefer-rest-params
      return self.apply(this, arguments) || null;
    };
  }

  // Add static properties to the constructor function, if supplied.
  _.extend(child, self, staticProps);

  // Set the prototype chain to inherit from `self`, without calling
  // `self`'s constructor function.
  const Surrogate = function () {
    // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
    this.constructor = child;
  };
  Surrogate.prototype = self.prototype;
  // @ts-expect-error ts-migrate(7009) FIXME: 'new' expression, whose target lacks a construct s... Remove this comment to see the full error message
  child.prototype = new Surrogate();

  if (protoProps && protoProps.hasOwnProperty('mixins')) {
    let mixins = protoProps.mixins;

    // Remove the mixins so they are not added to the prototype as
    // a mixins property.
    delete protoProps.mixins;

    // Convert mixins to an array if it is not already, so we can loop over
    // each mixin and add their properties to the prototype.
    if (!Array.isArray(mixins)) {
      mixins = [mixins];
    }

    _.each(mixins, function (mixin) {
      _.extend(child.prototype, mixin);
    });
  }

  // Add prototype properties (instance properties) to the subclass,
  // if supplied.
  if (protoProps) {
    _.extend(child.prototype, protoProps);
  }

  // Set a convenience property in case the self's prototype is needed
  // later.
  child.__super__ = self.prototype;

  return child;
};

function BaseClass() {
  // If an initialize function is provided, call it after the constructor
  // with the arguments passed to the constructor.
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  if (_.isFunction(this.initialize)) {
    // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
    // eslint-disable-next-line prefer-rest-params, prefer-spread
    this.initialize.apply(this, arguments);
  }
}

BaseClass.extend = extend;

export default BaseClass;
