import AbstractCollection from 'app/js/models/AbstractCollection';
import path from 'app/js/utils/url/path';
import FilePathModel from './FilePathModel';

const FilePathCollection = AbstractCollection.extend({
  //
  // Properties
  name: 'FilePathCollection',
  context: ['server', 'includeFiles'],
  model: FilePathModel,
  url() {
    const params = {};
    if (this.includeFiles) {
      (params as any).includeFiles = 1;
    }
    return path.join(['/services/browse'], params);
  },
});
export default FilePathCollection;
