/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import path from 'app/js/utils/url/path';

const FilePathModel = AbstractModel.extend({
  //
  // Properties
  name: 'FilePathModel',
  context: ['server', 'includeFiles'],
  idAttribute: 'key',
  rootPropertyName: 'Path',
  sessionAttrs: {
    isBackShortcut: attrTypes.boolean,
  },
  sessionRelations: {
    children: function () {
      const params = {};
      if ((this as any).includeFiles) {
        (params as any).includeFiles = 1;
      }
      return {
        relationClass: require('./FilePathListModel').default,
        options: { url: path.join([(this as any).id], params) },
      };
    },
  },
  syncedAttrs: {
    key: attrTypes.string,
    title: attrTypes.string,
    path: attrTypes.string,
    home: attrTypes.boolean,
    network: attrTypes.boolean,
  },
});
export default FilePathModel;
