import { combineReducers, createAction, Reducer } from '@reduxjs/toolkit';
import coreReducer from 'app/js/v4/core/coreReducer';
import featuresReducer from 'app/js/v4/features/featuresReducer';

const v4Reducer = combineReducers({
  core: coreReducer,
  features: featuresReducer,
});

export const resetV4 = createAction('v4/reset');

const rootReducer: Reducer<ReturnType<typeof v4Reducer>> = (state, action) => {
  if (action.type === resetV4.type) {
    state = undefined;
  }

  return v4Reducer(state, action);
};

export default rootReducer;
