/* eslint-plugin-disable @typescript-eslint */

import config from 'app/js/models/config';

export default function addFlexModel(ModelClass: any, definition: any) {
  if (config.shouldCreateFlexModels) {
    ModelClass.prototype.sessionRelations = {
      ...ModelClass.prototype.sessionRelations,
      [__PLATFORM__]: definition,
    };
  }

  return ModelClass;
}

export function removeFlexModel(ModelClass: any) {
  if (ModelClass.prototype.sessionRelations[__PLATFORM__]) {
    delete ModelClass.prototype.sessionRelations[__PLATFORM__];
  }

  return ModelClass;
}
