/* eslint-plugin-disable @typescript-eslint */

// From: https://gist.github.com/jed/982883#gistcomment-55248

const template = '00000000-0000-4000-8000-000000000000';
const match = /0/g;
const replacer = () => (0 | (Math.random() * 16)).toString(16);

function uuidV4() {
  return template.replace(match, replacer);
}

export default uuidV4;
