/* eslint-plugin-disable @typescript-eslint */

import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createActionHandler from 'app/js/stores/actions/createActionHandler';
import {
  findProviderModel,
  findServerModel,
} from 'app/js/utils/state/modelFinders';
import * as types from '../actionTypes';

//
// Action Creators

export const changeBackground = createActionCreator(types.CHANGE_BACKGROUND);
export const changeThemeMusic = createActionCreator(types.CHANGE_THEME_MUSIC);
export const resetBackground = createActionCreator(types.RESET_BACKGROUND);

//
// Action Handlers

function findBackgroundModel(appModel: any) {
  return appModel.get('ui').get('background');
}

export const backgroundActionHandlers = createActionHandler({
  [types.CHANGE_BACKGROUND](appModel: any, payload: any = {}) {
    const { imageUrl, allowBlur = true } = payload;
    const backgroundModel = findBackgroundModel(appModel);

    backgroundModel.set({
      server: findServerModel(appModel, payload),
      provider: findProviderModel(appModel, payload),
      imageUrl,
      allowBlur,
      isBackgroundPending: false,
    });
  },

  [types.CHANGE_THEME_MUSIC](appModel: any, payload: any) {
    const { theme } = payload;

    const backgroundModel = findBackgroundModel(appModel);
    const serverModel = findServerModel(appModel, payload);

    const themeMusicUrl =
      serverModel && theme
        ? serverModel.resolveUrl(theme, { appendHeaders: true })
        : undefined;

    backgroundModel.set('themeMusicUrl', themeMusicUrl);
  },

  [types.RESET_BACKGROUND](appModel: any, payload: any) {
    const backgroundModel = findBackgroundModel(appModel);

    backgroundModel.restoreDefaults({
      isBackgroundPending: payload.isBackgroundPending,
    });
  },
});
