/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import assert from 'app/js/utils/log/assert';
import createControlledFetchHandler from './createControlledFetchHandler';

export default function createFetchHandler(options: any) {
  const { key, getModel } = options;

  return createControlledFetchHandler({
    ...options,
    fetch(appModel: any, payload: any) {
      const model = getModel(appModel, payload);
      const instance = key ? model.get(key) : model;

      if (!instance) {
        __DEV__ && assert(instance, 'Instance unavailable, cannot fetch');

        return $.Deferred().reject({ status: 0 }).promise();
      }

      return (
        instance
          .fetch(payload.options)
          // @ts-expect-error ts-migrate(6133) FIXME: 'instance_' is declared but its value is never rea... Remove this comment to see the full error message
          .then(null, (instance_: any, xhr: any) =>
            $.Deferred().reject(xhr).promise()
          )
      );
    },
  });
}
