/* eslint-plugin-disable @typescript-eslint */

import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import browser from 'app/js/utils/device/browser';

const DebugModel = Backbone.Model.extend({
  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'attributes' is declared but its value is never re... Remove this comment to see the full error message
  initialize: function (attributes: any, options: any) {
    this.set('host', window.location.host);
    this.set('userAgent', window.navigator.userAgent);
    this.set('browser', browser);
  },

  //
  // Control

  toJSON: function () {
    const appModel = this.parent;
    const primary = appModel.get('primaryServer');
    const user = appModel.get('user');

    this.set('version', __VERSION__);

    if (primary) {
      if (primary.get('activeConnection')) {
        this.set('primaryServerUrl', primary.url());
      }

      this.set('primaryServerAuthToken', !!primary.get('accessToken'));
      this.set('primaryServerVersion', primary.get('versionFull'));
    }

    this.set('username', user.get('username'));
    this.set('cloudUrl', user.server.get('connections'));

    // eslint-disable-next-line prefer-rest-params
    return Backbone.Model.prototype.toJSON.apply(this, arguments);
  },
});

// Singleton
export default new DebugModel();
