/* eslint-plugin-disable @typescript-eslint */

let i = 0;

// `getUniqueElementID` returns HTML 4.0 compliant element IDs.
// See: http://stackoverflow.com/a/79022

function getUniqueElementID() {
  return `id-${i++}`;
}

if (__TEST__) {
  getUniqueElementID.reset = () => {
    i = 0;
  };
}

export default getUniqueElementID;
