import $ from 'jquery';
import BaseClass from 'app/js/utils/BaseClass';
import dispatcher from 'app/js/utils/dispatcher';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import AlertView from 'app/js/views/alerts/AlertView';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const AlertManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize() {
    this.openAlerts = {};
  },

  //
  // Control

  start() {
    this.listenTo(dispatcher, {
      'show:alert': this.showAlert,
    });
  },

  //
  // Listeners

  showAlert: function (options: any) {
    const message = options.message;

    if (this.openAlerts[message]) {
      return;
    }

    const alert = new AlertView(options);
    this.openAlerts[message] = true;
    this.listenToOnce(alert, 'close', function () {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      delete this.openAlerts[message];
    });

    $('body').append(alert.render().el);
  },
});

export default AlertManager;
