/* eslint-plugin-disable @typescript-eslint */

import parser2 from 'app/js/utils/parser2';

export default function getErrorMessageFromXHR(
  xhr: any,
  errorMessage: any,
  messageProperty = 'message'
) {
  if (!xhr) {
    return errorMessage;
  }

  const error = parser2.parseXML(xhr.responseText);

  if (error && error[messageProperty]) {
    return error[messageProperty];
  }

  return errorMessage;
}
