/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import streamTypes from 'app/js/utils/types/streamTypes';
import getStreams from './getStreams';

function getSelectedVideo(media: any) {
  // Video streams don't actually have a selected flag, so just grab the first
  // video stream.
  const streamType = streamTypes.get('video').id;
  const selectedStreams = getStreams(media, { streamType });

  return _.first(selectedStreams);
}

export default getSelectedVideo;
