import { createSelector } from 'reselect';
import selectDeviceSettings from 'app/js/stores/selectors/settings/selectDeviceSettings';

const selectDeviceSettingsVolume = createSelector(
  selectDeviceSettings,
  (deviceSettings) => {
    return deviceSettings.volume;
  }
);

export default selectDeviceSettingsVolume;
