/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import getServerErrorMessage, {
  ErrorContexts,
} from 'app/js/utils/getServerErrorMessage';
import t from 'app/js/utils/t';

export default function getSignInErrorMessage(
  errors: any,
  context?: ErrorContexts
) {
  const error = errors && errors[0];

  if (error) {
    return _.isObject(error) ? getServerErrorMessage(error, context) : error;
  } else {
    return t('There was a problem signing in to Plex.');
  }
}
