import _ from 'underscore';
import BaseModel from 'app/js/models/BaseModel';
import parser from 'app/js/utils/parser';

const VersionClientProfileModel = BaseModel.extend({
  //
  // Properties

  idAttribute: 'title',

  whitelistedAttrs: ['title', 'selected'],

  //
  // Control

  convert: function (xml: any) {
    const json = _.isObject(xml) ? xml : parser.parseXML(xml);

    parser.parseBoolean(json, 'selected');

    return json;
  },

  parse: function (response: any) {
    let json = this.convert(response);

    json = _.pick(json, this.whitelistedAttrs);

    return json;
  },
});

export default VersionClientProfileModel;
