/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

function getStreams(media: any, options: any) {
  const streams: any = [];

  if (media) {
    const part = _.first(media.Part);

    if (part && part.Stream) {
      // eslint-disable-next-line prefer-spread
      streams.push.apply(streams, _.where(part.Stream, options));
    }
  }

  return streams;
}

export default getStreams;
