/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { setPrimaryServer } from 'app/js/stores/actions/appActions';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';

type InlinePrimaryServerPageConnectorProps = {
  machineIdentifier?: string;
  dispatchSetPrimaryServer: (...args: any[]) => any;
};

class InlinePrimaryServerPageConnector extends Component<InlinePrimaryServerPageConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this._populate(this.props);
  }

  componentDidUpdate(prevProps: InlinePrimaryServerPageConnectorProps) {
    const { machineIdentifier } = this.props;

    if (
      machineIdentifier &&
      machineIdentifier !== prevProps.machineIdentifier
    ) {
      this._populate(this.props);
    }
  }

  //
  // Control

  _populate(props: any) {
    const { machineIdentifier, dispatchSetPrimaryServer } = props;

    if (machineIdentifier) {
      scheduleImmediateUpdate(() => {
        dispatchSetPrimaryServer({ machineIdentifier });
      });
    }
  }

  //
  // Render

  render() {
    return <>{this.props.children}</>;
  }
}

const mapDispatchToProps = {
  dispatchSetPrimaryServer: setPrimaryServer,
};

export default connect(
  null,
  mapDispatchToProps
)(InlinePrimaryServerPageConnector);
