import { createSlice } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  BandwidthFetchFailedAction,
  BandwidthFetchRequestedAction,
  BandwidthFetchSucceededAction,
  BandwidthState,
} from './dashboardBandwidthTypes';

const initialState: BandwidthState = {
  statistics: [],
  isPending: false,
  isPopulated: false,
  error: null,
  requestTimestampMilliseconds: null,
  responseTimestampMilliseconds: null,
};

const dashboardBandwidthSlice = createSlice({
  name: 'dashboardBandwidth',
  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    bandwidthFetchRequested(state, _action: BandwidthFetchRequestedAction) {
      markPending(state);
    },

    bandwidthFetchSucceeded(state, { payload }: BandwidthFetchSucceededAction) {
      markPopulated(state);
      state.statistics = payload.data;
      state.requestTimestampMilliseconds = payload.requestTimestampMilliseconds;
      state.responseTimestampMilliseconds =
        payload.responseTimestampMilliseconds;
    },

    bandwidthFetchFailed(state, { payload }: BandwidthFetchFailedAction) {
      markFailed(state, payload.error);
      state.requestTimestampMilliseconds = null;
      state.responseTimestampMilliseconds = null;
    },

    bandwidthAbortRequested() {
      // Handled by saga, no state change.
    },
  },
});

export default dashboardBandwidthSlice;
