/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { populateUserFeatures } from 'app/js/stores/actions/userActions';
import selectCloudServer from 'app/js/stores/selectors/server/selectCloudServer';
import selectServers from 'app/js/stores/selectors/server/selectServers';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import UserPage from 'app/js/views/v3/components/UserPage';

const selectIsWaitingForDownloads = createSelector(
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.downloads,
  (downloads) => {
    return downloads
      ? !downloads.isSubscriptionsPopulated && !downloads.subscriptionsError
      : false;
  }
);

const selectHasDownloads = createSelector(
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.downloads,
  (downloads) => {
    return !!(
      downloads &&
      downloads.subscriptions &&
      downloads.subscriptions.length > 0
    );
  }
);

const selectIsAnyServerAuthenticated = createSelector(
  selectServers,
  (servers) => {
    return servers.some((server: any) => {
      // The cloud server (plex.tv) must use `isResolved` in order to ensure the
      // user is authenticated, but media servers can shortcut at `isConnected`,
      // which saves time if connections are being re-tested. The presence of an
      // embedded server is not enough to consider user authenticated.

      return (
        (server.isCloud && server.isResolved) ||
        (!server.isCloud && server.isConnected)
      );
    });
  }
);

const mapStateToProps = createSelector(
  (appState) => appState.isWaitingForServers,
  selectIsWaitingForDownloads,
  selectIsAnyServerAuthenticated,
  selectHasDownloads,
  selectUser,
  selectCloudServer,
  (
    isWaitingForServers,
    isWaitingForDownloads,
    isAnyServerAuthenticated,
    hasDownloads,
    user,
    cloudServer
  ) => {
    return {
      // When there is an embedded server but no media it complicates sign-in
      // prompt presentation in the Desktop app. So let the user in if there is
      // any downloads, which means we also need to wait for downloads to
      // populate.
      isWaitingForServers: isWaitingForServers || isWaitingForDownloads,
      isAnyServerAuthenticated: isAnyServerAuthenticated || hasDownloads,

      isWaitingForCloudServer:
        !cloudServer.isResolved && !cloudServer.isRejected,
      isCloudServerUnauthorized: cloudServer.isUnauthorized,
      isCloudServerConnected: cloudServer.isConnected,
      isCloudServerUnavailable: cloudServer.isUnavailable,
      isCloudServerDownForMaintenance: cloudServer.isDownForMaintenance,
      hasAttemptedSignIn: user.hasAttemptedSignIn,
      isUserFeaturesPopulated: user.isFeaturesPopulated,
      isUserFeaturesError: !!user.featuresError,
    };
  }
);

const mapDispatchToProps = {
  onPopulateUserFeatures: populateUserFeatures,
};

// @ts-expect-error
export default connect(mapStateToProps, mapDispatchToProps)(UserPage);
