/* eslint-plugin-disable @typescript-eslint */

import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import * as providerProtocols from 'app/js/utils/constants/providerProtocols';
import * as sourceTypes from 'app/js/utils/types/sourceTypes';

function isDirectoryForSourceType(
  sourceTypeObject: any,
  provider: any,
  contentDirectory: any
) {
  const { key: sourceType, types = {} } = sourceTypeObject;

  // The `protocols` attribute on the provider model might be an array or an
  // object depending on the source.
  const isLiveTVProvider = Array.isArray(provider.protocols)
    ? provider.protocols.includes(providerProtocols.LIVE_TV)
    : provider.protocols[providerProtocols.LIVE_TV];

  const isDVRSourceType = sourceType === sourceTypes.DVR;
  const isDiscoverSourceType = sourceType === sourceTypes.DISCOVER;

  if (isLiveTVProvider) {
    return isDVRSourceType;
  }

  if (
    providerIdentifiers.matches(
      provider.identifier,
      providerIdentifiers.DISCOVER
    )
  ) {
    return isDiscoverSourceType;
  }

  // DVR and Discover have no types to compare to the content directory.
  if (isDVRSourceType || isDiscoverSourceType) {
    return false;
  }

  const hasNoneAgent = contentDirectory.agent === agentIdentifiers.NONE;
  if (
    (sourceType === sourceTypes.VIDEOS && !hasNoneAgent) ||
    (sourceType === sourceTypes.MOVIES && hasNoneAgent)
  ) {
    return false;
  }

  const { type, subtype } = types;
  return (
    contentDirectory.type === type &&
    ((subtype == null && contentDirectory.subtype == null) ||
      (subtype != null && contentDirectory.subtype === subtype))
  );
}

export default isDirectoryForSourceType;
