/* eslint-plugin-disable @typescript-eslint */

import React, { PropsWithChildren } from 'react';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import IconRemove from 'app/glyphs/remove-560.svg';
import styles from './ModalContent.css';

interface ModalContentProps {
  className?: string;
  iconComponent?: React.ReactElement;
  onModalClose: (...args: any[]) => any;
}

function ModalContent(props: PropsWithChildren<ModalContentProps>) {
  const {
    className = styles.modalContent,
    children,
    iconComponent: Icon = IconRemove,
    onModalClose,
    ...otherProps
  } = props;

  return (
    <div className={className} {...otherProps}>
      <Link className={styles.closeButton} onPress={onModalClose}>
        {/* @ts-expect-error ts-migrate(2604) FIXME: JSX element type 'Icon' does not have any construc... Remove this comment to see the full error message */}
        {Icon ? <Icon /> : null}
      </Link>

      {children}
    </div>
  );
}

export default ModalContent;
