import $ from 'jquery';
import _ from 'underscore';
import commands from 'app/js/commands/commands';
import EditSectionAgentSettingsView from 'app/js/views/editsection/EditSectionAgentSettingsView';
import EditSectionBaseView from 'app/js/views/editsection/EditSectionBaseView';
import EditSectionSettingView from 'app/js/views/editsection/EditSectionSettingView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/secti... Remove this comment to see the full error message
import tpl from 'app/templates/section/edit/EditSectionAdvancedView.tpl';

const EditSectionAdvancedView = EditSectionBaseView.extend({
  //
  // Properties

  template: tpl,

  ui: _.extend(
    {
      sectionAgent: '#edit-section-agent',
      sectionScanner: '#edit-section-scanner',
      thumbnailsCheckbox: 'input[name="enableBIFGeneration"]',
      agentGroup: '.agent-group',
      scannerGroup: '.scanner-group',
    },
    EditSectionBaseView.prototype.ui
  ),

  events: {
    'change #edit-section-agent': 'onEditSectionAgentChange',
    'change #edit-section-scanner': 'onEditSectionScannerChange',
    'click .delete-thumbnails-btn': 'onThumbnailsDeleteClick',
  },

  regions: {
    sectionSettingsRegion: '.section-settings-region',
    agentSettingsRegion: '.section-agent-settings-region',
  },

  onlyAfterRender: [
    'renderSectionAgent',
    'renderSectionScanner',
    'renderSectionSettings',
    'renderSectionAgentSettings',
  ].concat(EditSectionBaseView.prototype.onlyAfterRender),

  //
  // Init

  initialize: function () {
    this.populateCollections();
  },

  //
  // Control

  onRender: function () {
    this.model.agents.populate();
    this.model.scanners.populate();
  },

  validate: function (model: any) {
    const invalidAttrs = [];

    if (!model.get('agent')) {
      invalidAttrs.push('agent');
    }

    if (!model.get('scanner')) {
      invalidAttrs.push('scanner');
    }

    return invalidAttrs;
  },

  renderSectionSettings: function () {
    const view = new EditSectionSettingView({
      model: this.model,
      collection: this.model.settings,
    });
    this.sectionSettingsRegion.show(view);
  },

  renderSectionAgentSettings: function () {
    const view = new EditSectionAgentSettingsView({
      model: this.model,
      collection: this.model.agentSettings,
    });
    this.agentSettingsRegion.show(view);
  },

  populateCollections: function () {
    const populateAgents = this.populateAgents();
    const populateScanners = this.populateScanners();
    const populateSectionSettings = this.populateSectionSettings();
    const populateSectionAgentSettings = this.populateSectionAgentSettings();

    this.populateAgentsProxy();
    this.populateScannersProxy();

    populateAgents.then(_.bind(this.onAgentsPopulate, this));
    populateScanners.then(_.bind(this.onScannersPopulate, this));
    populateSectionSettings.then(_.bind(this.renderSectionSettings, this));
    populateSectionAgentSettings.then(
      _.bind(this.renderSectionAgentSettings, this)
    );

    return $.when(
      populateAgents,
      populateScanners,
      populateSectionSettings,
      populateSectionAgentSettings
    ).then(_.bind(this.validate, this, this.model));
  },

  renderSectionAgent: function () {
    const agents = this.model.agentsProxy;
    const html = this.createMenuOptions(
      agents.map(function (agent: any) {
        return {
          label: agent.get('name'),
          value: agent.get('identifier'),
        };
      }),
      this.model.get('agent')
    );

    this.ui.sectionAgent.html(html);

    if (agents.length > 1) {
      this.ui.agentGroup.removeClass('hidden');
    }
  },

  renderSectionScanner: function () {
    const scanners = this.model.scannersProxy;
    const html = this.createMenuOptions(
      scanners.map(function (scanner: any) {
        return {
          label: scanner.get('name'),
          value: scanner.get('name'),
        };
      }),
      this.model.get('scanner')
    );

    this.ui.sectionScanner.html(html);

    if (scanners.length > 1) {
      this.ui.scannerGroup.removeClass('hidden');
    }
  },

  updateSectionAgent: function (selectedAgent: string): void {
    const selectedScanner = this.model.get('scanner');

    this.model.set('agent', selectedAgent);
    this.agentSettingsRegion.close();

    const agent = this.model.agents.get(selectedAgent);
    if (agent) {
      // If the selected language is no longer available with the new agent,
      // set it to the server provided default or the first available language.
      const agentLanguages = agent.get('languages');
      const currentLanguage = this.model.get('language');
      const sectionType = this.getSectionType();

      if (!agentLanguages.get(currentLanguage)) {
        let defaultLanguage = agentLanguages.findWhere({ default: true });

        // If PMS didn't return a default and our currently selected language is
        // of the hyphenated variant (e.g. "en-US") then just try with the language part.
        if (!defaultLanguage && currentLanguage.search('-') === 2) {
          defaultLanguage = agentLanguages.get(currentLanguage.substr(0, 2));
        }

        // Always make sure we have a fallback.
        if (!defaultLanguage) {
          defaultLanguage =
            agentLanguages.get(sectionType.defaultLanguage) ||
            agentLanguages.first();
        }

        this.model.set('language', defaultLanguage.get('code'));
      }

      // Reset to null so settings for the new agent are fetched.
      this.model.agentSettings = null;
      this.populateSectionAgentSettings().then(
        _.bind(this.renderSectionAgentSettings, this)
      );

      // Also ensure we pull in the section settings specific to the agent.
      this.model.settings = null;
      this.populateSectionSettings().then(
        _.bind(this.renderSectionSettings, this)
      );

      if (selectedAgent === sectionType.mutual.agent) {
        this.model.set('scanner', sectionType.mutual.scanner);
        this.renderSectionScanner();
      } else if (this.model.get('scanner') === sectionType.mutual.scanner) {
        const scanner = this.model.scanners.find(
          (currentScanner: any) =>
            currentScanner.get('name') !== selectedScanner
        );
        this.model.set('scanner', scanner.get('name'));
        this.renderSectionScanner();
      }
    }
    this.showValidationResult(this.validate(this.model));
  },

  //
  // Listeners

  onEditSectionAgentChange: function (event: any) {
    const $target = $(event.currentTarget);
    const selectedAgent = $target.val();

    this.updateSectionAgent(selectedAgent);
  },

  onEditSectionScannerChange: function (event: any) {
    const $target = $(event.currentTarget);
    const selectedScanner = $target.val();
    const selectedAgent = this.model.get('agent');
    const sectionType = this.getSectionType();

    this.model.set('scanner', selectedScanner);

    if (selectedScanner === sectionType.mutual.scanner) {
      this.updateSectionAgent(sectionType.mutual.agent);

      this.renderSectionAgent();
    } else if (this.model.get('agent') === sectionType.mutual.agent) {
      const agent = this.model.agents.find(
        (currentAgent: any) => currentAgent.get('identifier') !== selectedAgent
      );

      this.updateSectionAgent(agent.get('identifier'));

      this.renderSectionAgent();
    }

    this.showValidationResult(this.validate(this.model));
  },

  onAgentsPopulate: function () {
    this.renderSectionAgent();
  },

  onScannersPopulate: function () {
    this.renderSectionScanner();
  },

  onThumbnailsDeleteClick: function (event: any) {
    event.preventDefault();

    const promise = commands.execute('deleteSectionThumbnails', {
      section: this.model,
    });

    promise.done(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.thumbnailsCheckbox.prop('checked', false);
      })
    );
  },
});

export default EditSectionAdvancedView;
