/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function emptyTrashCommand(options: any) {
  const deferred = $.Deferred();
  const section = options.section;
  const server = section ? section.server : options.server;
  const sectionIDs: any = [];
  const requests: any = [];

  if (section) {
    sectionIDs.push(section.id);
  } else {
    const sections = server.get('sections');

    sections.each(function (currentSection: any) {
      sectionIDs.push(currentSection.id);
    });
  }

  function emptyTrash() {
    // There is not an endpoint for emptying the trash for all library
    // sections, so instead loop through and make each individual request
    // to empty the trash for all sections.

    _.each(sectionIDs, function (sectionID) {
      const url = '/library/sections/' + sectionID + '/emptyTrash';
      const xhrOptions = _.extend(server.getXHROptions(url), { type: 'PUT' });

      requests.push($.ajax(xhrOptions));
    });

    // eslint-disable-next-line prefer-spread
    const xhrDeferred = $.when.apply($, requests);

    xhrDeferred.done(function () {
      dispatcher.trigger('show:alert', {
        type: 'success',
        message: t('The trash was emptied.'),
      });

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem emptying the trash.'),
      });

      deferred.reject(xhr);
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr) {
      confirmModal.hide();
    });
  }

  const confirmModal = new ConfirmModal({
    title: t('Empty Trash'),
    message: t(
      "By default the media server doesn't remove information for media right away. This helps if a drive is temporarily disconnected. When you empty the trash for a section, all information about missing media is deleted."
    ),
    confirmLabel: t('Empty'),
    cancelLabel: t('Cancel'),
    async: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(emptyTrash);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return deferred.promise();
}

export default emptyTrashCommand;
