  allowMediaDeletion: false,
  backgroundProcessing: false,
  transcoderVideo: false,
  isCloud: false,
  isEmbedded: false,
  isFullServer: false,
  isFullOwnedServer: false,
  isOutOfDate: false,
  isShared: false,
  isUnavailable: false,
  isUnauthorized: false,
  isConnected: false,
  isTransient: false,
  connectionTestResult: ConnectionTestResult.Pending,
  ownerFeatures: [],
  providers: [],
