/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import * as directions from 'app/js/views/v3/helpers/props/directions';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './DisclosureArrow.css';

interface DisclosureArrowProps {
  className?: string;
  direction?: any; // TODO: PropTypes.oneOf(directions.all)
  kind?: any; // TODO: PropTypes.oneOf(kinds.all)
  size?: any; // TODO: PropTypes.oneOf(sizes.all)
  isSelected?: boolean;
}

function DisclosureArrow({
  className,
  kind,
  size,
  direction,
  isSelected,
}: DisclosureArrowProps) {
  return (
    <span
      className={classNames(
        className,
        styles[direction],
        styles[kind],
        styles[size],
        isSelected && styles.isSelected
      )}
    />
  );
}

DisclosureArrow.defaultProps = {
  className: styles.disclosureArrow,
  direction: directions.DOWN,
  kind: kinds.DEFAULT,
  size: sizes.MEDIUM,
  isSelected: false,
};

export default DisclosureArrow;
