import { createSlice } from '@reduxjs/toolkit';
import serversSlice from 'app/js/v4/core/servers/serversSlice';
import {
  ServerActivitiesState,
  ServerActivityAction,
} from './serverActivitiesTypes';

const initialState: ServerActivitiesState = {};

const serverActivitiesSlice = createSlice({
  name: 'serverActivities',
  initialState,

  reducers: {
    activityStarted(_state, _action: ServerActivityAction) {
      // Handled by other slices for now, no state change.
    },

    activityUpdated(_state, _action: ServerActivityAction) {
      // Handled by other slices for now, no state change.
    },

    activityFinished(_state, _action: ServerActivityAction) {
      // Handled by other slices for now, no state change.
    },
  },

  extraReducers: (builder) => {
    builder.addCase(serversSlice.actions.connected, (state, { payload }) => {
      const { server } = payload;

      if (!state[server.machineIdentifier]) {
        state[server.machineIdentifier] = [];
      }
    });
  },
});

export default serverActivitiesSlice;
