/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { resolve } from 'app/js/adapters/adapters';
import { navigateBack } from 'app/js/stores/actions/appActions';
import selectAllSourcesSidebarServers from 'app/js/stores/selectors/server/selectAllSourcesSidebarServers';
import selectCloudServer from 'app/js/stores/selectors/server/selectCloudServer';
import createUserFeatureSelector from 'app/js/stores/selectors/user/createUserFeatureSelector';
import selectUser from 'app/js/stores/selectors/user/selectUser';
import { CLOUD_SERVER_ID } from 'app/js/utils/constants/serverIDs';
import * as features from 'app/js/utils/constants/user/features';
import log from 'app/js/utils/log/log';
import { isIOS } from 'app/js/utils/predicates/device';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import ErrorNavBar from 'app/js/views/v3/components/navbar/ErrorNavBar';
import NavBar from './NavBar';

const hasManualHistoryNavigation = isIOS();

const selectIsFullscreen = createSelector(
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.isFullscreen,
  (isFullscreen) => isFullscreen
);

const selectHasDownloads = createSelector(
  // The downloads relation is not available on every platform. While the
  // subscriptions relation is deferred.
  // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
  (appState) => appState.downloads && appState.downloads.subscriptions,
  (subscriptions) => (subscriptions ? subscriptions.length > 0 : false)
);

const selectHasConnectedSidebarSource = createSelector(
  selectAllSourcesSidebarServers,
  (servers) => servers.some((server: any) => server.isConnected)
);

const createShouldRenderSelector = () => {
  return createSelector(
    selectCloudServer,
    selectHasDownloads,
    selectHasConnectedSidebarSource,
    (cloudServer, hasDownloads, hasConnectedSidebarSource) => {
      if (
        // Assume we're racing startup. Multiple navbar components throw if a
        // cloudServer is not present.
        !cloudServer
      ) {
        return false;
      }

      return (
        cloudServer.isResolved || hasDownloads || hasConnectedSidebarSource
      );
    }
  );
};

function createMapStateToProps() {
  return createSelector(
    // @ts-expect-error ts-migrate(2571) FIXME: Object is of type 'unknown'.
    (appState) => appState.history,
    selectUser,
    selectIsFullscreen,
    createShouldRenderSelector(),
    createUserFeatureSelector(features.BYPASS_PLEX_PASS_UPSELL_MODAL),

    (
      history,
      user,
      isFullscreen,
      shouldRender,
      shouldBypassPlexPassUpsellModal
    ) => {
      const serverEntityID = createEntityID(
        entityTypes.SERVER,
        CLOUD_SERVER_ID
      );

      return {
        serverEntityID,
        hasHistory: history.length > 1,
        userThumb: user.thumb,
        isFullscreen,
        isAnonymousUser: !!user.anonymous,
        canGoPremium: user.canGoPremium,
        shouldBypassPlexPassUpsellModal,
        canNavigateHistory: hasManualHistoryNavigation,
        shouldRender,
      };
    }
  );
}

// @ts-expect-error ts-migrate(6133) FIXME: 'props' is declared but its value is never read.
function createMapDispatchToProps(dispatch: any, props: any) {
  return {
    // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
    onBackPress(event: any) {
      dispatch(navigateBack());
    },
    // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
    onEnterFullscreen(event: any) {
      resolve('fullscreenManager').enter();
    },
    // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
    onExitFullscreen(event: any) {
      resolve('fullscreenManager').exit();
    },
  };
}

const UnsafeNavBarConnector = connect(
  createMapStateToProps,
  createMapDispatchToProps
  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type '(props: NavBarProps) => Element ... Remove this comment to see the full error message
)(NavBar);

type NavBarConnectorState = any;

class NavBarConnector extends Component<{}, NavBarConnectorState> {
  //
  // Static

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  //
  // Lifecycle

  constructor(props: {}) {
    super(props);
    this.state = { hasError: false };
  }

  componentDidCatch(error: any) {
    log.error(error);
  }

  //
  // Render

  render() {
    return this.state.hasError ? <ErrorNavBar /> : <UnsafeNavBarConnector />;
  }
}

export default NavBarConnector;
