/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import useEffectOnce from 'app/js/views/v3/helpers/hooks/useEffectOnce';

type AcceptEULAPageProps = {
  isSettingsPopulated?: boolean;
  settingsError?: any;
  hasAcceptedEULA: boolean;
  isAcceptedEULASaving: boolean;
  onPopulateSettings: (...args: any[]) => any;
  onAcceptEULA: (...args: any[]) => any;
  onSkipSignIn: (...args: any[]) => any;
};

function AcceptEULAPage(props: AcceptEULAPageProps) {
  const {
    isSettingsPopulated,
    settingsError,
    hasAcceptedEULA,
    isAcceptedEULASaving,
    onPopulateSettings,
    onAcceptEULA,
    onSkipSignIn,
  } = props;

  useEffectOnce(() => {
    onPopulateSettings();
  });

  useEffectOnce(() => {
    onAcceptEULA();
  }, isSettingsPopulated && !hasAcceptedEULA);

  useEffectOnce(() => {
    onSkipSignIn();
  }, hasAcceptedEULA && !isAcceptedEULASaving);

  return settingsError ? (
    <ErrorPage
      description={t(
        "The server you're trying to access doesn't seem to exist."
      )}
      title={t('Media server not found')}
    />
  ) : (
    <EmptyPage>
      <Spinner />
    </EmptyPage>
  );
}

export default AcceptEULAPage;
