import * as responseTypes from 'app/js/models/responseTypes';

function isJSONSupported(server: any) {
  if (!server) {
    return false;
  }

  // Media providers support JSON similar to media servers.
  if (server.get('isCloud')) {
    return true;
  }

  // Only allow JSON with full media servers. Nano servers support JSON, but
  // mobile clients have proxies in front of the nano servers, which may not
  // support JSON.
  return (
    server.supports('json') &&
    !server.get('onlyXML') &&
    !server.get('isMobileServer')
  );
}

export default function getSupportedResponseType(server: any) {
  return isJSONSupported(server) ? responseTypes.json : responseTypes.xml;
}
