import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertSuccessView.tpl';

const ConvertSuccessView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'modal-body',
  template: tpl,

  events: {
    'click .status-link': 'onStatusClick',
  },

  //
  // Listeners

  onStatusClick: function (event: any) {
    event.preventDefault();

    this.triggerMethod('request:nextState');

    navigateToRoute('serverSettings', {
      machineIdentifier: this.options.server.id,
      settingsSection: 'status',
      settingsGroup: 'conversion',
    });
  },
});

export default ConvertSuccessView;
