/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectCloudServer from 'app/js/stores/selectors/server/selectCloudServer';
import SetupPage from 'app/js/views/v3/components/pages/setup/SetupPage';

function createMapStateToProps() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { serverEntityID }: any) => serverEntityID,
    (appState) => appState.primaryServer,
    (appState) => appState.isWaitingForPrimaryServer,
    selectCloudServer,
    (serverEntityID, primaryServerEntityID, isWaitingForPrimaryServer) => {
      return {
        serverEntityID: serverEntityID || primaryServerEntityID,
        isWaitingForPrimaryServer,
      };
    }
  );
}

export default connect(createMapStateToProps)(SetupPage);
