import { resolve } from 'app/js/adapters/adapters';
import localizerAdapter from 'app/js/adapters/localizerAdapter';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';
import api from 'app/js/v4/utils/data/api';

// Once language setting lives on the same model in all platforms and the
// settingFinder is no longer required for language; this should be deleted and
// language initialization moved closer to the initialized model.
export default function languageInitializer() {
  const defaultLanguage = resolve('settingFinder').find('language');

  api.setOption('language', defaultLanguage);

  return alwaysResolve(localizerAdapter.load(defaultLanguage));
}
