/* eslint-plugin-disable @typescript-eslint */

import fromUnixTime from 'date-fns/fromUnixTime';
import ReminderModel from 'app/js/models/user/ReminderModel';
import ReminderType from 'app/js/utils/constants/user/ReminderType';
import log from 'app/js/utils/log/log';
import getReminderID from 'app/js/utils/user/getReminderID';

export default function migrateRelayReminders(userExperienceSettings: any) {
  if (!userExperienceSettings.relayReminders) {
    return userExperienceSettings;
  }

  __DEV__ &&
    log(
      '[User] Relay reminders to migrate:',
      userExperienceSettings.relayReminders
    );

  // Be safe #1: Don't overwrite an existing reminders list. This shouldn't
  // happen to users.
  userExperienceSettings.reminders = userExperienceSettings.reminders || [];

  for (const machineIdentifier in userExperienceSettings.relayReminders) {
    const id = getReminderID(ReminderType.Relay, machineIdentifier);

    if (userExperienceSettings.reminders.some((r: any) => r.id === id)) {
      // Be safe #2: Don't overwrite an existing reminder. This shouldn't
      // happen to users.
      continue;
    }

    const relayReminder =
      userExperienceSettings.relayReminders[machineIdentifier];

    const attrs = {
      id,
      // Convert the unix timestamp to human readable ISO8601 string.
      remindAfter: fromUnixTime(relayReminder.remindAfter).toISOString(),
      // Adjust existing `times` values for new behavior. Previously `times: 1`
      // was set on reminders when initially created. It included the initial
      // notification in the count of reminders. The refactored implementation
      // uses `times` to represent the number of times a user has been notified
      // _after_ the initial notification.
      times: Math.max(0, relayReminder.times - 1),
    };

    userExperienceSettings.reminders.push(new ReminderModel(attrs).toJSON());
  }

  __DEV__ &&
    log('[User] Reminders after migration:', userExperienceSettings.reminders);

  delete userExperienceSettings.relayReminders;

  return userExperienceSettings;
}
