import React from 'react';
import assert from 'app/js/utils/log/assert';
import escapeRegExp from 'app/js/utils/string/escapeRegExp';

interface TagInjectorProps {
  className?: string;
  component?: React.ComponentType<{ className?: string }> | string;
  childrenPropName?: string;
  tags?: any;
  message: string;
}

function TagInjector(props: TagInjectorProps): JSX.Element {
  const {
    className,
    component: RootComponent = 'span',
    childrenPropName: rootChildrenPropName = 'children',
    tags,
    message,
  } = props;

  if (__DEV__) {
    const validTagRegExp = /^\w+$/;
    const isValid = tags.every(
      (link: any) =>
        (validTagRegExp.test(link.open) && validTagRegExp.test(link.close)) ||
        (link.children && validTagRegExp.test(link.unpaired))
    );
    assert(
      isValid,
      'Expected all `open` and `close` keys to be \\w word characters'
    );
  }

  let segments = [message];
  let componentIndex = tags.length - 1;
  let componentCount = 0;

  while (componentIndex >= 0) {
    const {
      open,
      close,
      unpaired,
      children,
      component: Component,
      childrenPropName,
      ...componentProps
    } = tags[componentIndex];

    const tagRegExp = open
      ? new RegExp(
          '(^.*)' +
            escapeRegExp(`{${open}}`) +
            '(.*)' +
            escapeRegExp(`{${close}}`) +
            '(.*$)'
        )
      : new RegExp('(^.*)' + escapeRegExp(`{${unpaired}}`) + '()(.*$)');

    const nextSegments = [];
    for (
      let segmentIndex = segments.length - 1;
      segmentIndex >= 0;
      segmentIndex--
    ) {
      const segment = segments[segmentIndex];
      if (typeof segment !== 'string') {
        nextSegments.unshift(segment);
      } else {
        const match = segment.match(tagRegExp);
        if (match) {
          const [
            ,
            preComponentSegment,
            componentChildren,
            postComponentSegment,
          ] = match;

          if (Component) {
            nextSegments.unshift(
              preComponentSegment,
              <Component
                key={componentCount}
                {...componentProps}
                {...{
                  [childrenPropName || 'children']: componentChildren,
                }}
              />,
              postComponentSegment
            );
          } else {
            nextSegments.unshift(
              preComponentSegment,
              children,
              postComponentSegment
            );
          }
          componentCount = componentCount + 1;
        } else {
          nextSegments.unshift(segment);
        }
      }
    }

    if (nextSegments.length === segments.length) {
      componentIndex = componentIndex - 1;
    } else {
      segments = nextSegments;
    }
  }

  return (
    <RootComponent
      className={className}
      {...{ [rootChildrenPropName]: segments }}
    />
  );
}

export default TagInjector;
