/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';
import isPlainObject from 'app/js/utils/object/isPlainObject';
import isString from 'app/js/utils/string/isString';

// Derived from https://github.com/sindresorhus/query-string
function parseQueryString(value: any, options: any) {
  options || (options = {});

  // Don't try to parse again if it's already an object.
  if (isPlainObject(value)) {
    return value;
  }

  if (!isString(value)) {
    return {};
  }

  value = value.trim().replace(/^(\?|#)/, '');

  if (!value) {
    return {};
  }

  const separators =
    (options.separators && options.separators.join('')) ||
    options.separator ||
    '&';

  const separatorsRegex = new RegExp(`[${separators}]+`);

  return value.split(separatorsRegex).reduce((memo: any, param: any) => {
    let [key, val] = param.split('=');

    try {
      key = decodeURIComponent(key);
      // missing `=` should be `null`:
      // http://w3.org/TR/2012/WD-url-20120524/#collect-url-parameters
      val = val === undefined ? null : decodeURIComponent(val);

      if (!memo.hasOwnProperty(key)) {
        memo[key] = val;
      } else if (Array.isArray(memo[key])) {
        memo[key].push(val);
      } else {
        memo[key] = [memo[key], val];
      }
    } catch (error) {
      log.warn(`[Parser] Unable to decode query string at ${key}`);
    }

    return memo;
  }, {});
}

export default parseQueryString;
