/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

function getMissingParts(medias: any) {
  const missing: any = [];

  if (!Array.isArray(medias)) {
    medias = [medias];
  }

  _.each(medias, (media) => {
    const parts = media.Part;
    if (!parts) {
      return;
    }

    _.each(parts, (part) => {
      if (part.exists === false || part.accessible === false) {
        missing.push(part);
      }
    });
  });

  return missing;
}

export default getMissingParts;
