/* eslint-plugin-disable @typescript-eslint */

import { useEffect, useRef } from 'react';

/**
 * Ensures the effect is only called the first time the condition is met.
 * Defaults to run immediately once only.
 *
 * @deprecated Prefer to use `useEffect` directly. If an effect is unsafe to
 * re-run when a dependency changes it should be refactored so that re-runs are
 * safe. `useEffectOnce` can have unintended consequences with a React app root
 * when a component is reused instead of unmounted and re-mounted.
 */
export default function useEffectOnce(effect: any, condition = true) {
  const hasUsedEffect = useRef(false);

  useEffect(() => {
    if (condition && !hasUsedEffect.current) {
      hasUsedEffect.current = true;

      return effect();
    }
  }, [condition, effect, hasUsedEffect]);
}
