/* eslint-plugin-disable @typescript-eslint */

export default function getProviderBaseRoute(server: any, provider: any) {
  const encodedMachineIdentifier = encodeURIComponent(server.machineIdentifier);
  const encodedProviderID = encodeURIComponent(provider.identifier);

  const serverPath = `/server/${encodedMachineIdentifier}`;

  if (provider.isLibraryProvider) {
    return serverPath;
  }

  const pathBase = server.isCloud ? '' : serverPath;
  return `${pathBase}/provider/${encodedProviderID}`;
}
