/* eslint-plugin-disable @typescript-eslint */

import LegacyLibraryProviderModel from 'app/js/models/provider/LegacyLibraryProviderModel';
import ProviderModel from 'app/js/models/provider/ProviderModel';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';

let addDevelopmentProvider: any;

if (__DEV__) {
  const DEV_PROVIDER_ID = 'tv.plex.provider.development';

  addDevelopmentProvider = (collection: any, server: any) => {
    if (!collection.get(DEV_PROVIDER_ID)) {
      collection.add(
        new ProviderModel(
          {
            baseURL: __DEV_PROVIDER_URL__,
            identifier: DEV_PROVIDER_ID,
            title: 'Development Provider',
          },
          { server }
        )
      );
    }
  };
}

export default function augmentProviderCollection(
  collection: any,
  server: any
) {
  // Providers returned from `plex.tv/media/providers` are not full Providers.
  // We must fetch the root provider to get the full XML. Manually set `isPopulated`
  // and `isPending` flags for PMS servers which respond with the full provider.

  const isProvidersPrepopulated = !server.get('isCloud');

  if (isProvidersPrepopulated) {
    collection.forEach((model: any) => {
      model.set({
        isPending: false,
        isPopulated: true,
      });
    });
  }

  // If the server is too old to support the `/media/providers` endpoint we
  // still want the legacy providers to be created. This allows us to rely on
  // all metadata items having a provider.
  // See: https://github.com/plexinc/plex-media-server/issues/6015

  if (!server.get('isCloud') && !collection.get(providerIdentifiers.LIBRARY)) {
    collection.add(
      new LegacyLibraryProviderModel(
        {
          isPending: false,
          isPopulated: true,
        },
        { server }
      )
    );
  }

  if (__DEV__ && __DEV_PROVIDER_URL__ && server.get('isCloud')) {
    addDevelopmentProvider(collection, server);
  }
}
