/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import baseDesktopAdapters from 'app/js/adapters/baseDesktopAdapters';
import desktopDimensions from 'app/js/adapters/desktopDimensions';
import dimensionsAdapter from 'app/js/adapters/dimensionsAdapter';
import PersistentWebStorage from 'app/js/adapters/storage/PersistentWebStorage';
import VolatileWebStorage from 'app/js/adapters/storage/VolatileWebStorage';
import storageAdapter from 'app/js/adapters/storageAdapter';
import volatileStorageAdapter from 'app/js/adapters/volatileStorageAdapter';

const desktopAdapters = {
  registerAll() {
    const promise = desktopDimensions.init();
    promise.done((dimensions: any) => dimensionsAdapter.register(dimensions));

    const persistentWebStorage = new PersistentWebStorage();

    storageAdapter.register(persistentWebStorage);
    volatileStorageAdapter.register(new VolatileWebStorage());

    // The storage adapter needs to be registered before any base adapters.
    baseDesktopAdapters.registerAll();

    return $.when(promise);
  },
};

export default desktopAdapters;
