export const START_ACTIVITY = 'server/startActivity';
export const UPDATE_ACTIVITY = 'server/updateActivity';
export const FINISH_ACTIVITY = 'server/finishActivity';
export const START_SYNTHETIC_ACTIVITY = 'server/startSyntheticActivity';
export const FINISH_SYNTHETIC_ACTIVITY = 'server/finishSyntheticActivity';
export const EVENT_CONNECTION_OPEN = 'server/eventConnectionOpen';
export const EVENT_CONNECTION_CLOSE = 'server/eventConnectionClose';
export const FETCH_PROVIDERS = 'server/fetchProviders';
export const POPULATE_PROVIDERS = 'server/populateProviders';
export const FETCH_LIBRARIES = 'server/fetchLibraries';
export const POPULATE_LIBRARIES = 'server/populateLibraries';
