/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { MY_PLEX_SIGN_IN_STATE_OK } from 'app/js/models/server/ServerModel';
import parser from 'app/js/utils/parser2';
import maxDuration from 'app/js/utils/promise/maxDuration';
import retryTimes from 'app/js/utils/promise/retryTimes';
import isMediaProvidersRoot from 'app/js/utils/server/isMediaProvidersRoot';
import path from 'app/js/utils/url/path';

function pollForResource(appModel: any, serverModel: any) {
  const cloudServerModel = appModel.get('cloudServer');
  const resources = cloudServerModel.get('resources');
  const where = { clientIdentifier: serverModel.get('machineIdentifier') };

  return retryTimes(
    () =>
      resources
        .fetch()
        .then(() =>
          resources.findWhere(where)
            ? $.Deferred().resolve().promise()
            : $.Deferred().reject().promise()
        ),
    {
      times: 30,
      delay: 1000,
    }
  );
}

function ensurePrimaryServer(
  appModel: any,
  serverModel: any,
  setPrimaryServer: any
) {
  const deferred = $.Deferred();

  const primaryServer = appModel.get('primaryServer');
  const isAlreadyPrimaryServer = primaryServer === serverModel;

  if (isAlreadyPrimaryServer) {
    // If the claimed server never became non-primary, resolve immediately
    deferred.resolve();
  } else {
    // If the claimed server became non-primary, switch back to it
    appModel.once('change:primaryServer', deferred.resolve);
    setPrimaryServer();
  }

  return maxDuration(deferred.promise(), 2000);
}

function claimServer(
  appModel: any,
  serverModel: any,
  authToken: any,
  setPrimaryServer: any
) {
  const user = appModel.get('user');
  const cloudServer = appModel.get('cloudServer');

  if (user.get('anonymous')) {
    // Be defensive against anonymous users ever claiming a server. We should
    // never present anonymous user ability to claim a server through the UI.
    return $.Deferred().reject().promise();
  }

  // Get a token from plex.tv, then have the server claim the token
  return $.ajax(
    cloudServer.getXHROptions('/api/claim/token', {
      headers: { ['X-Plex-Token']: authToken },
    })
  )
    .promise()
    .then((response) => parser.parseXML(response).token)
    .then((token) =>
      $.ajax(
        serverModel.getXHROptions(path.join('/myplex/claim', { token }), {
          type: 'POST',
        })
      )
    )
    .then(() => pollForResource(appModel, serverModel))
    .then(() => {
      // If we know this server supports the /media/provider root attributes
      // use this new endpoint so that we have the providers we expect
      // from the server.

      const useMediaProviderRoot = isMediaProvidersRoot(serverModel);
      let xhrOptions;

      if (useMediaProviderRoot) {
        xhrOptions = serverModel.getXHROptions('/media/providers');
      }

      return serverModel.fetch(xhrOptions);
    })
    .then(() => ensurePrimaryServer(appModel, serverModel, setPrimaryServer))
    .then(() => {
      const email = user.get('email');
      const cloudAccount = serverModel.get(__PLATFORM__).get('cloudAccount');

      serverModel.set({
        myPlexSigninState: MY_PLEX_SIGN_IN_STATE_OK,
        myPlexUsername: email,
      });

      cloudAccount.set({
        signedIn: true,
        username: email,
      });
    });
}

export default claimServer;
