/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import PlexPlayerModel from './PlexPlayerModel';

const CloudPlayerCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'CloudPlayerCollection',
  context: ['server', 'baseURL'],

  model: PlexPlayerModel,

  augmentResponse(response: any) {
    if (!response.Server) {
      response.Server = response.Player;
      delete response.Player;
    }

    return response;
  },
});

export default CloudPlayerCollection;
