/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import BaseAppModel from 'app/js/models/BaseAppModel';
import DesktopSettingsModel from 'app/js/models/DesktopSettingsModel';
import attrTypes from 'app/js/models/attrTypes';
import CloudServerModel from 'app/js/models/server/CloudServerModel';
import ServerCollection from 'app/js/models/server/ServerCollection';
import ServerModel from 'app/js/models/server/ServerModel';
import TransientServerModel from 'app/js/models/server/TransientServerModel';
import DeviceSettingsModel from 'app/js/models/settings/DeviceSettingsModel';
import DesktopUIModel from 'app/js/models/ui/DesktopUIModel';
import UserModel from 'app/js/models/user/UserModel';
import * as castApiAvailabilities from 'app/js/utils/constants/player/companion/castApiAvailabilities';
import { CLOUD_SERVER_ID } from 'app/js/utils/constants/serverIDs';
import createEntitiesModelClass from 'app/js/utils/state/createEntitiesModelClass';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import StoreModel from './StoreModel';

const DesktopAppModel = BaseAppModel.extend({
  //
  // Properties

  name: 'DesktopAppModel',

  sessionAttrs: {
    clientID: attrTypes.string,
    anonymousClientID: attrTypes.string,
    authorized: attrTypes.boolean.isRequired,
    skipSignIn: attrTypes.boolean.isRequired,
    isWaitingForServers: attrTypes.boolean.isRequired,
    isWaitingForPrimaryServer: attrTypes.boolean.isRequired,
    // Pages in our past
    history: attrTypes.array.isRequired,
    isFullscreen: attrTypes.boolean.isRequired,
    isUpdateAvailable: attrTypes.boolean.isRequired,
    castApiAvailability: castApiAvailabilities.attrType.isRequired,
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
    settings: attrTypes.instanceOf(DesktopSettingsModel),
  },

  sessionRelations: {
    entities: {
      relationClass: createEntitiesModelClass(entityTypes),
    },

    servers: {
      relationClass: ServerCollection,
    },

    cloudServer: {
      relationClass: CloudServerModel,
      deferred: true,
    },

    primaryServer: {
      relationClass: ServerModel,
      deferred: true,
    },

    companionServer: {
      relationClass: ServerModel,
      deferred: true,
    },

    transientServer: {
      relationClass: TransientServerModel,
      deferred: true,
    },

    user: {
      relationClass: UserModel,
      deferred: true,
    },

    remotePlayer: {
      // The `remotePlayer` can be a `CastPlayerModel` or `PlexPlayerModel`.
      relationClass: AbstractModel,
      deferred: true,
    },

    deviceSettings: {
      relationClass: DeviceSettingsModel,
    },

    ui: {
      relationClass: DesktopUIModel,
    },

    v4: {
      relationClass: StoreModel,
    },
  },

  defaults() {
    // Specify the machineIdentifier explicitly so the model is cached properly.
    const cloudServer = new CloudServerModel(
      { machineIdentifier: CLOUD_SERVER_ID },
      { parent: this }
    );

    return {
      authorized: false,
      skipSignIn: false,
      isWaitingForServers: false,
      isWaitingForPrimaryServer: false,
      isUpdateAvailable: false,
      history: [],
      cloudServer: cloudServer,
      user: new UserModel({}, { server: cloudServer, parent: this }),
      isFullscreen: false,
      castApiAvailability: castApiAvailabilities.UNKNOWN,
      settings: new DesktopSettingsModel(),
    };
  },

  //
  // Control

  dispatch(action: any) {
    this.get('v4').dispatch(action);
  },
});

export default DesktopAppModel;
