/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const EntityModel = AbstractModel.extend({
  //
  // Properties

  name: 'EntityModel',

  sessionAttrs: {
    id: attrTypes.string.isRequired,
  },

  sessionRelations: {
    model: {
      relationClass: AbstractModel,
      deferred: true,
    },
  },

  //
  // Control

  toState() {
    // Avoid `getState` since we don't want the entity's id to be substituted in
    // this part of the state tree.

    const model = this.get('model');

    const state = {
      entityID: this.id,
      ...model.state,
    };

    Object.defineProperty(state, 'possibleEntityContainers', {
      value: model.state.possibleEntityContainers,
      enumerable: false,
    });

    return state;
  },

  shouldUpdateRelationState: function () {
    // The next and previous state for entities will usually be the same since
    // `getState` returns the entity's id. Always update state on state change
    // since we want to include the model's actual state here.

    return true;
  },
});

export default EntityModel;
