import BaseCollection from 'app/js/models/BaseCollection';
import VersionTargetModel from 'app/js/models/library/convert/VersionTargetModel';
import proxyTypes from 'app/js/utils/constants/proxyTypes';

const VersionTargetCollection = BaseCollection.extend({
  //
  // Properties

  model: VersionTargetModel,
  url: '/library/tags?type=' + proxyTypes.LOCAL,

  comparator: function (target: any) {
    return target.get('MediaSettings').maxVideoBitrate;
  },

  //
  // Control

  parse: function (response: any) {
    return this.convert(response, 'Tag');
  },
});

export default VersionTargetCollection;
