/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import TagInjector from 'app/js/views/v3/components/core/TagInjector';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import ErrorPage from 'app/js/views/v3/components/pages/empty/ErrorPage';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';

type ServerUnavailableErrorPageProps = {
  friendlyName?: string;
  isPending?: boolean;
  onTryAgainPress: (...args: any[]) => any;
};

function ServerUnavailableErrorPage(props: ServerUnavailableErrorPageProps) {
  const { friendlyName, isPending, onTryAgainPress } = props;

  return (
    <ErrorPage
      description={
        <TagInjector
          message={t(
            'Verify you have a network connection and that the server is online, or see our {openLink}troubleshooting{closeLink} tips for further assistance.'
          )}
          tags={[
            {
              open: 'openLink',
              close: 'closeLink',
              component: Link,
              kind: kinds.PRIMARY,
              to: 'https://help.plex.tv/noserver',
              target: '_blank',
            },
          ]}
        />
      }
      title={
        friendlyName
          ? t('{serverName} is currently unavailable', {
              serverName: friendlyName,
            })
          : t('Server is currently unavailable')
      }
    >
      <SpinnerButton
        isSpinning={isPending}
        kind={kinds.PRIMARY}
        onPress={onTryAgainPress}
      >
        {t('Retry connection')}
      </SpinnerButton>
    </ErrorPage>
  );
}

export default ServerUnavailableErrorPage;
