/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as responseTypes from 'app/js/models/responseTypes';
import log from 'app/js/utils/log/log';

export const SIGN_IN_STATE_OK = 'ok';
export const SIGN_IN_STATE_NONE = 'none';
export const SIGN_IN_STATE_UNKNOWN = 'unknown';
export const SIGN_IN_STATE_INVALID = 'invalid';
export const MAPPING_STATE_UNKNOWN = 'unknown';
export const MAPPING_STATE_WAITING = 'waiting';
export const MAPPING_STATE_FAILED = 'failed';
export const MAPPING_STATE_MAPPED = 'mapped';
export const MAPPING_ERROR_BAD_AUTH = 'badauth';
export const MAPPING_ERROR_UNREACHABLE = 'unreachable';
export const MAPPING_ERROR_MY_PLEX_GONE = 'myplexgone';
export const MAPPING_ERROR_PUBLISH_ERROR = 'publisherror';
export const MAPPING_ERROR_DOUBLE_NAT = 'doublenat';
export const MAPPING_ERROR_JUMBO_FRAMES = 'jumboframes';
export const MAPPING_ERROR_NONE = '';
export const REMOTE_ACCESS_STATE_OK = 'ok';
export const REMOTE_ACCESS_STATE_CONNECTING = 'connecting';
export const REMOTE_ACCESS_STATE_DISCONNECTING = 'disconnecting';
export const REMOTE_ACCESS_STATE_UNREACHABLE = 'unreachable';
export const REMOTE_ACCESS_STATE_UNKNOWN = '';
const ServerCloudAccountModel = AbstractModel.extend({
  //
  // Properties
  context: ['server'],
  url: '/myplex/account',
  responseType: responseTypes.json,
  sessionAttrs: {
    remoteAccessState: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 5.
      REMOTE_ACCESS_STATE_CONNECTING,
      REMOTE_ACCESS_STATE_DISCONNECTING,
      REMOTE_ACCESS_STATE_UNREACHABLE,
      REMOTE_ACCESS_STATE_OK,
      REMOTE_ACCESS_STATE_UNKNOWN
    ),
  },
  syncedAttrs: {
    username: attrTypes.string,
    mappingState: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 4.
      MAPPING_STATE_UNKNOWN,
      MAPPING_STATE_WAITING,
      MAPPING_STATE_FAILED,
      MAPPING_STATE_MAPPED
    ),
    mappingError: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 7.
      MAPPING_ERROR_BAD_AUTH,
      MAPPING_ERROR_UNREACHABLE,
      MAPPING_ERROR_MY_PLEX_GONE,
      MAPPING_ERROR_PUBLISH_ERROR,
      MAPPING_ERROR_DOUBLE_NAT,
      MAPPING_ERROR_JUMBO_FRAMES,
      MAPPING_ERROR_NONE
    ),
    signInState: attrTypes.oneOf(
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 4.
      SIGN_IN_STATE_NONE,
      SIGN_IN_STATE_UNKNOWN,
      SIGN_IN_STATE_INVALID,
      SIGN_IN_STATE_OK
    ),
    publicAddress: attrTypes.string,
    publicPort: attrTypes.integer,
    privateAddress: attrTypes.string,
    privatePort: attrTypes.integer,
    subscriptionActive: attrTypes.boolean,
    subscriptionState: attrTypes.string,
  },
  derivedAttrs: {
    signedIn() {
      return (this as any).get('signInState') === SIGN_IN_STATE_OK;
    },
    isWaiting() {
      const remoteAccessState = (this as any).get('remoteAccessState');
      return (
        remoteAccessState === REMOTE_ACCESS_STATE_CONNECTING ||
        remoteAccessState === REMOTE_ACCESS_STATE_DISCONNECTING
      );
    },
  },
  defaults: {
    signedIn: false,
    mappingState: null,
    mappingError: null,
    signInState: null,
    publicAddress: null,
    publicPort: null,
    privateAddress: null,
    privatePort: null,
  },
  //
  // Control
  isNew() {
    return !this.get('signedIn');
  },
  augmentResponse(response: any) {
    let json = response.MyPlex;
    if (!json) {
      log.warn(
        `[Account] Failed to get reachable information for account - received ${JSON.stringify(
          response
        )}`
      );
      json = {};
    }
    const signedIn = json.signInState === SIGN_IN_STATE_OK;
    if (!signedIn) {
      json.username = null;
    }
    const isUnreachable = !signedIn || json.mappingError;
    const isOk = !isUnreachable && json.mappingState === MAPPING_STATE_MAPPED;
    if (isUnreachable) {
      json.remoteAccessState = REMOTE_ACCESS_STATE_UNREACHABLE;
    } else if (isOk) {
      json.remoteAccessState = REMOTE_ACCESS_STATE_OK;
    }
    return json;
  },
});
export default ServerCloudAccountModel;
