import cloneDeep from 'lodash/cloneDeep';
import {
  ServerNowPlayingSession,
  ServerNowPlayingSessionResponse,
} from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsTypes';
import {
  normalizeAsBoolean,
  normalizeAsNumber,
} from 'app/js/v4/utils/data/normalizers/normalizeUtils';
import { NormalizerOptions } from 'app/js/v4/utils/data/normalizers/normalizersTypes';

export default function normalizeServerNowPlayingSessionsResponse(
  options: NormalizerOptions
): ServerNowPlayingSession[] {
  const { data } = options;
  const nowPlayingSessionsResponse: ServerNowPlayingSessionResponse[] =
    data.MediaContainer?.Metadata ?? [];

  return nowPlayingSessionsResponse.map((nowPlayingSessionResponse) => {
    const nowPlayingSession = cloneDeep(nowPlayingSessionResponse);

    // See: https://github.com/plexinc/plex-media-server/issues/11161
    normalizeAsNumber(nowPlayingSession, 'viewOffset');
    normalizeAsNumber(nowPlayingSession, 'duration');

    nowPlayingSession.Media?.forEach((mediaItem) => {
      mediaItem.Part?.forEach((part) => {
        part.Stream?.forEach((stream) => {
          normalizeAsBoolean(stream, 'selected');
          normalizeAsNumber(stream, 'streamType');
        });
      });
    });

    return nowPlayingSession as ServerNowPlayingSession;
  });
}
