/* eslint-plugin-disable @typescript-eslint */

import path from 'app/js/utils/url/path';
import findProviderContentFeature from 'app/js/views/v3/helpers/provider/findProviderContentFeature';
import getSourceRoute from 'app/js/views/v3/helpers/routes/getSourceRoute';

export const views = {
  RECORDING_SCHEDULE: 'view://dvr/recording-schedule',
  GUIDE: 'view://dvr/guide',
  TIMELINE: 'view://photo/timeline',
  SHARED_ITEMS: 'view://shared-items',
  HOME_RECOMMENDED: 'view://home/recommended',
  HOME_WATCHLIST: 'view://home/watchlist',
};

const viewRoutes = {
  [views.RECORDING_SCHEDULE]: {
    getRouteParams() {
      return {
        pivot: views.RECORDING_SCHEDULE,
      };
    },
  },
  [views.GUIDE]: {
    getRouteParams() {
      return {
        pivot: views.GUIDE,
      };
    },
  },
  [views.TIMELINE]: {
    getRouteParams: (contentFeature: any, sourceDirectory: any) => {
      return {
        pivot: views.TIMELINE,
        key: path.join([contentFeature.key, sourceDirectory.id, 'cluster'], {
          clusterZoomLevel: 1,
        }),
      };
    },
  },
  [views.SHARED_ITEMS]: {
    // @ts-expect-error ts-migrate(6133) FIXME: 'contentFeature' is declared but its value is neve... Remove this comment to see the full error message
    getRouteParams: (contentFeature: any, sourceDirectory: any) => {
      return {
        key: sourceDirectory.pivots[0].key,
      };
    },
  },
};

export function isViewRoute(view: any) {
  return !!viewRoutes[view];
}

export default function getViewRoute(
  view: any,
  server: any,
  provider: any,
  sourceDirectory: any,
  params = {}
) {
  if (!isViewRoute(view)) {
    return;
  }

  const viewRoute = viewRoutes[view];

  const contentFeature = findProviderContentFeature(provider);

  return getSourceRoute(
    server,
    provider,
    sourceDirectory,
    viewRoute.getRouteParams
      ? {
          ...params,
          ...viewRoute.getRouteParams(contentFeature, sourceDirectory),
        }
      : params
  );
}
