import { SagaIterator } from 'redux-saga';
import { all, call, fork, put, select } from 'redux-saga/effects';
import createServerDevicesSelector from 'app/js/v4/core/serverDevices/selectors/createServerDevicesSelector';
import api from 'app/js/v4/utils/data/api';
import takeLeadingPerID from 'app/js/v4/utils/data/saga/takeLeadingPerID';
import serverDevicesSlice from './serverDevicesSlice';
import {
  ServerDevicesFetchRequestedAction,
  ServerDevicesPerServerState,
} from './serverDevicesTypes';

//
// Workers

export function* populateServerDevices({
  payload,
}: ServerDevicesFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const serverDevices: ServerDevicesPerServerState = yield select(
      createServerDevicesSelector(server.machineIdentifier)
    );

    if (!serverDevices.isPopulated) {
      yield put(serverDevicesSlice.actions.fetchRequested(payload));
    }
  } catch (error) {
    yield put(
      serverDevicesSlice.actions.fetchFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchServerDevices({
  payload,
}: ServerDevicesFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const response = yield call(api.get, '/devices', {
      server,
    });

    yield put(
      serverDevicesSlice.actions.fetchSucceeded({
        server,
        data: response.data.MediaContainer?.Device ?? [],
      })
    );
  } catch (error) {
    yield put(
      serverDevicesSlice.actions.fetchFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Watchers

export function* watchPopulateRequested(): SagaIterator {
  yield takeLeadingPerID(
    serverDevicesSlice.actions.populateRequested,
    populateServerDevices,
    ({ payload }) => payload.server.machineIdentifier
  );
}

export function* watchFetchRequested(): SagaIterator {
  yield takeLeadingPerID(
    serverDevicesSlice.actions.fetchRequested,
    fetchServerDevices,
    ({ payload }) => payload.server.machineIdentifier
  );
}

export default function* serverDevicesSaga(): SagaIterator {
  yield all([fork(watchPopulateRequested), fork(watchFetchRequested)]);
}
