/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import BackgroundProcessingQueueCollection from 'app/js/models/library/convert/BackgroundProcessingQueueCollection';
import VersionGeneratorModel from 'app/js/models/library/convert/VersionGeneratorModel';
import ServerUpdateModel from 'app/js/models/server/ServerUpdateModel';
import {
  removeMetadataItem,
  repopulateMetadataItem,
  updateMetadataItemRefreshingState,
} from 'app/js/stores/actions/metadata/metadataActions';
import {
  discoverMediaGrabberDevice,
  fetchLibraries,
  fetchProviders,
  finishActivity,
  startActivity,
  testServer,
  updateActivity,
  updateSetting,
} from 'app/js/stores/actions/serverActions';
import { repopulatePrePlayPopularTracks } from 'app/js/stores/actions/ui/prePlayPageActions';
import { repopulatePage } from 'app/js/stores/actions/ui/uiActions';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import * as serverUpdateStates from 'app/js/utils/constants/serverUpdateStates';
import timelineEvents from 'app/js/utils/constants/timelineEvents';
import dispatcher from 'app/js/utils/dispatcher';
import splitChildren from 'app/js/utils/parser/splitChildren';
import playerUtil from 'app/js/utils/playerUtil';
import BaseMediaServerEventConnection from 'app/js/utils/pubsub/BaseMediaServerEventConnection';
import * as messageTypes from 'app/js/utils/pubsub/messageTypes';
import toStableServerModelJSON from 'app/js/utils/server/toStableServerModelJSON';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import escapeRegExp from 'app/js/utils/string/escapeRegExp';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import serverActivitiesSlice from 'app/js/v4/core/serverActivities/serverActivitiesSlice';
import serverNowPlayingSessionsSlice from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsSlice';
import serversSlice from 'app/js/v4/core/servers/serversSlice';

const activityEventActions = {
  started: startActivity,
  updated: updateActivity,
  ended: finishActivity,
};

const v4ActivityEventActions = {
  started: serverActivitiesSlice.actions.activityStarted,
  updated: serverActivitiesSlice.actions.activityUpdated,
  ended: serverActivitiesSlice.actions.activityFinished,
};

const MESSAGE_TYPES = [
  messageTypes.ACCOUNT,
  messageTypes.ACTIVITY,
  messageTypes.BACKGROUND_GENERATOR_STATE,
  messageTypes.BACKGROUND_PROCESSING_QUEUE,
  messageTypes.GRABBER_DEVICE,
  messageTypes.PLAYING,
  messageTypes.PREFERENCE,
  messageTypes.PROGRESS,
  messageTypes.PROVIDER_CHANGE,
  messageTypes.PROVIDER_CONTENT_CHANGE,
  messageTypes.PROVIDER_SUBSCRIPTION_CHANGE,
  messageTypes.REACHABILITY,
  messageTypes.STATUS,
  messageTypes.TIMELINE,
  messageTypes.TRANSCODE_END,
  messageTypes.TRANSCODE_START,
  messageTypes.TRANSCODE_UPDATE,
  messageTypes.UPDATE_STATE_CHANGE,
  messageTypes.VERSION_STATE,
];

const REPOPULATE_THROTTLE_DELAY = 5000;

function updateLiveTVMetadata(serverEntityID: any) {
  const appStore = adapters.resolve('appStore');

  if (
    providerIdentifiers.matches(window.location.hash, providerIdentifiers.EPG)
  ) {
    appStore.dispatch(repopulatePage({ serverEntityID }));
  }
}

function getMetadataKeyBase(metadataTypeString: string): string {
  switch (metadataTypeString) {
    case metadataTypes.PLAYLIST:
      return '/playlists';
    case metadataTypes.COLLECTION:
      return '/library/collections';
    default:
      return '/library/metadata';
  }
}

const MediaServerEventConnection = BaseMediaServerEventConnection.extend({
  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  initialize(options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseMediaServerEventConnection.prototype.initialize.apply(this, arguments);

    this.throttledRepopulates = {};
  },

  //
  // Control

  repopulateNow(id: any, fn: any) {
    this.throttledRepopulates[id] = {
      timestamp: Date.now(),
    };
    fn();
  },

  repopulateWithThrottle(id: any, fn: any, delay: any) {
    const details = this.throttledRepopulates[id];

    if (details) {
      if (details.hasTimeout) {
        // If a timeout is already pending, do nothing and wait for the timeout.
      } else {
        // Otherwise, set a timeout to fire when the delay is finished.
        const elapsedTime = Date.now() - details.timestamp;

        if (elapsedTime < delay) {
          window.setTimeout(() => {
            this.repopulateNow(id, fn);
          }, delay - elapsedTime);

          this.throttledRepopulates[id] = {
            hasTimeout: true,
          };
        } else {
          this.repopulateNow(id, fn);
        }
      }
    } else {
      this.repopulateNow(id, fn);
    }
  },

  //
  // Messages

  getMessageTypes() {
    return MESSAGE_TYPES;
  },

  [messageTypes.PREFERENCE](message: any) {
    // Tweak the object a bit and dispatch it as an event.
    message.key = message.id;
    message.server = this.server;

    this.server.trigger('socket:setting', message);

    const dispatch = adapters.resolve('appStore').dispatch;
    dispatch(
      updateSetting({
        serverEntityID: this.server.entityID(),
        setting: message,
      })
    );
  },

  [messageTypes.TIMELINE](message: any) {
    // Suppress EPG-related timeline notifications. We may want to hook live
    // updating into them eventually, but we don't want the visual toast.

    if (
      providerIdentifiers.matches(message.identifier, providerIdentifiers.EPG)
    ) {
      return;
    }

    const {
      state,
      sectionID,
      rootItemID,
      parentItemID,
      itemID,
      metadataState,
      mediaState,
      type,
    } = message;

    const dispatch = adapters.resolve('appStore').dispatch;
    const serverEntityID = this.server.entityID();

    const metadataType = metadataTypes.getMetadataType(type);
    const metadataTypeString = metadataType?.typeString;

    const metadataKey = `${getMetadataKeyBase(metadataTypeString)}/${itemID}`;

    const metadataItemEntityID = createEntityID(
      entityTypes.METADATA_ITEM,
      metadataKey,
      serverEntityID
    );

    dispatch(
      updateMetadataItemRefreshingState({
        serverEntityID,
        metadataItemEntityID,
        metadataState,
        mediaState,
      })
    );

    if (state === timelineEvents.PROCESSED) {
      dispatch(
        repopulateMetadataItem({
          serverEntityID,
          metadataItemEntityID,
        })
      );

      // An artist's popular tracks are derived from its albums. When a metadata
      // refresh on an album occurs, try repopulating the artist so popular
      // tracks are updated.

      if (metadataTypeString === metadataTypes.ALBUM) {
        dispatch(
          repopulatePrePlayPopularTracks({
            serverEntityID,
            albumMetadataItemEntityID: metadataItemEntityID,
          })
        );
      }
    }

    if (state === timelineEvents.DELETED) {
      dispatch(
        removeMetadataItem({
          serverEntityID,
          metadataItemEntityID,
        })
      );
    }

    // If an item was created or deleted, we'll need to refresh lists as well,
    // which means repopulating pages like the library page or pre-plays if a
    // parent or grandparent is provided. Throttle these actions so we aren't
    // constantly repopulating during a scan.

    if (state === timelineEvents.CREATED || state === timelineEvents.DELETED) {
      if (sectionID > 0) {
        this.repopulateWithThrottle(
          'section-' + sectionID,
          () => {
            dispatch(
              repopulatePage({
                serverEntityID,
                containerKeyPattern: new RegExp(
                  '^' + escapeRegExp(`/library/sections/${sectionID}`) + '\\b'
                ),
              })
            );
          },
          REPOPULATE_THROTTLE_DELAY
        );
      }

      if (rootItemID > 0) {
        this.repopulateWithThrottle(
          'metadata-' + rootItemID,
          () => {
            const rootMetadataKey = `/library/metadata/${rootItemID}`;
            const rootMetadataItemEntityID = createEntityID(
              entityTypes.METADATA_ITEM,
              rootMetadataKey,
              serverEntityID
            );

            dispatch(
              repopulatePage({
                serverEntityID,
                metadataItemEntityID: rootMetadataItemEntityID,
                rootMetadataKey,
              })
            );
          },
          REPOPULATE_THROTTLE_DELAY
        );
      }

      if (parentItemID > 0) {
        this.repopulateWithThrottle(
          'metadata-' + parentItemID,
          () => {
            const parentMetadataKey = `/library/metadata/${parentItemID}`;
            const parentMetadataItemEntityID = createEntityID(
              entityTypes.METADATA_ITEM,
              parentMetadataKey,
              serverEntityID
            );

            dispatch(
              repopulatePage({
                serverEntityID,
                metadataItemEntityID: parentMetadataItemEntityID,
                parentMetadataKey,
              })
            );
          },
          REPOPULATE_THROTTLE_DELAY
        );
      }

      // Playlists don't have an associated ID, so they need an explicit
      // repopulate to keep up to date as items are created or deleted.

      if (metadataTypeString === metadataTypes.PLAYLIST) {
        dispatch(
          repopulatePage({
            serverEntityID,
            containerKeyPattern: new RegExp('^/playlists'),
          })
        );
      }
    }

    message.server = this.server;

    this.server.trigger('socket:timeline', message);
    dispatcher.trigger('timeline', message);
  },

  [messageTypes.STATUS](message: any) {
    this.server.trigger('socket:status', message);

    // If this is a notification about a library update, the
    // refreshing flag may have changed so re-fetch the server
    // sections so views can update accordingly.
    if (message.notificationName === 'LIBRARY_UPDATE') {
      const dispatch = adapters.resolve('appStore').dispatch;
      const serverEntityID = this.server.entityID();

      if (serverEntityID) {
        dispatch(
          serversSlice.actions.libraryScanUpdated({
            server: toStableServerModelJSON(this.server),
          })
        );

        dispatch(testServer({ serverEntityID }));
        dispatch(fetchLibraries({ serverEntityID }));
        dispatch(repopulatePage({ serverEntityID }));
      }
    }
  },

  [messageTypes.PROGRESS](message: any) {
    this.server.trigger('socket:status', message);
  },

  [messageTypes.PLAYING](message: any) {
    if (!message.sessionKey || !this.server.get('isFullOwnedServer')) {
      return;
    }

    const dispatch = adapters.resolve('appStore').dispatch;

    if (message.state === playerUtil.states.STOPPED) {
      dispatch(
        serverNowPlayingSessionsSlice.actions.sessionStopped({
          server: toStableServerModelJSON(this.server),
          sessionKey: message.sessionKey,
        })
      );

      dispatch(repopulatePage({ serverEntityID: this.server.entityID() }));
    } else {
      dispatch(
        serverNowPlayingSessionsSlice.actions.sessionChanged({
          server: toStableServerModelJSON(this.server),
          message,
        })
      );
    }
  },

  [messageTypes.BACKGROUND_GENERATOR_STATE](message: any) {
    const options = { server: this.server, onlyIfCached: true };

    const generator = new VersionGeneratorModel(
      {
        id: message.generatorID,
      },
      options
    );

    if (!_.isEmpty(generator)) {
      generator.fetch();
    }
  },

  [messageTypes.VERSION_STATE](message: any) {
    const options = { server: this.server, onlyIfCached: true };

    const generator = new VersionGeneratorModel(
      {
        id: message.generatorID,
      },
      options
    );

    if (!_.isEmpty(generator)) {
      const metadataItems = generator.get('children').getCollection();
      const metadataItem = metadataItems.get(message.metadataItemID);

      if (metadataItem) {
        metadataItem.set({
          processingState: message.state,
          processingStateContext: message.stateContext,
        });
      }
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'message' is declared but its value is never read.
  [messageTypes.BACKGROUND_PROCESSING_QUEUE](message: any) {
    const playQueues = new BackgroundProcessingQueueCollection([], {
      server: this.server,
      allowInCache: true,
      onlyIfCached: true,
    });

    if (!_.isEmpty(playQueues)) {
      const playQueue = playQueues.first();

      if (playQueue) {
        playQueue.fetch();
      }
    }
  },

  [messageTypes.TRANSCODE_START](message: any) {
    const dispatch = adapters.resolve('appStore').dispatch;

    if (this.server.get('isShared')) {
      return;
    }

    if (message.context === 'static') {
      const transcodeJobs = this.server
        .get(__PLATFORM__)
        .get('backgroundTranscodeJobs');
      transcodeJobs.fetch();
    } else {
      dispatch(
        serverNowPlayingSessionsSlice.actions.fetchRequested({
          server: toStableServerModelJSON(this.server),
        })
      );
    }
  },

  [messageTypes.TRANSCODE_UPDATE](message: any) {
    if (this.server.get('isShared')) {
      return;
    }

    if (message.context === 'static') {
      const transcodeJobs = this.server
        .get(__PLATFORM__)
        .get('backgroundTranscodeJobs');
      const transcodeJob = transcodeJobs.get(message.key);

      if (transcodeJob) {
        transcodeJob.set(transcodeJob.parse(message));
      } else {
        transcodeJobs.fetch();
      }
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'message' is declared but its value is never read.
  [messageTypes.TRANSCODE_END](message: any) {
    if (this.server.get('isShared')) {
      return;
    }

    // Both start and end will re-fetch transcode jobs.
    // eslint-disable-next-line prefer-rest-params, prefer-spread
    this['transcodeSession.start'].apply(this, arguments);
  },

  [messageTypes.UPDATE_STATE_CHANGE](message: any) {
    const update = this.server.get(__PLATFORM__).get('update');
    const attrs = _.pick(
      message,
      Object.keys(ServerUpdateModel.prototype.syncedAttrs)
    );
    _.defaults(attrs, _.result(update, 'defaults'));

    // If the user tries to explicitly download updates, it will revert to
    // an available state before continuing to the downloading state.
    if (
      update.get('state') === serverUpdateStates.DOWNLOADING &&
      attrs.state === serverUpdateStates.AVAILABLE
    ) {
      attrs.state = serverUpdateStates.DOWNLOADING;
    }

    update.set({ ...attrs, isChecking: false });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'message' is declared but its value is never read.
  [messageTypes.REACHABILITY](message: any) {
    const cloudAccount = this.server.get(__PLATFORM__).get('cloudAccount');
    cloudAccount.fetch();
  },

  [messageTypes.ACCOUNT](message: any) {
    this.server.set('myPlexSubscription', message.hasPlexPass);

    // Fetch the user's resources again so the server's connections are
    // updated. This can help casting if the server only has a loopback
    // address and signing the server in provides a private address.

    const appModel = adapters.resolve('appModel');
    const user = appModel.get('user');

    if (user.get('isSignedIn')) {
      dispatcher.trigger('discover:servers');
    }
  },

  [messageTypes.ACTIVITY](message: any) {
    const parsedMessage = splitChildren(message);
    const appStore = adapters.resolve('appStore');
    const serverEntityID = this.server.entityID();

    // Old messages didn't have Activity, so we need to check that Activity is
    // present otherwise we massage the old format into the new format.
    const activity = parsedMessage.Activity || parsedMessage;

    // Ensure Context and Activity are always on the activity so action handlers
    // don't need to do their own check before using them.
    activity.Context = activity.Context || {};
    activity.Response = activity.Response || {};

    if (serverEntityID) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      const action = activityEventActions[parsedMessage.event];
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      const v4Action = v4ActivityEventActions[parsedMessage.event];

      if (action) {
        appStore.dispatch(action({ serverEntityID, activity }));
      }

      if (v4Action) {
        appStore.dispatch(
          v4Action({ server: toStableServerModelJSON(this.server), activity })
        );
      }
    }
  },

  [messageTypes.GRABBER_DEVICE](message: any) {
    const appStore = adapters.resolve('appStore');
    const serverEntityID = this.server.entityID();

    if (serverEntityID && message.event === 'appeared') {
      appStore.dispatch(
        discoverMediaGrabberDevice({
          serverEntityID: serverEntityID,
          device: message.Device,
        })
      );
    }
  },

  [messageTypes.PROVIDER_CONTENT_CHANGE]() {
    updateLiveTVMetadata(this.server.entityID());
  },

  [messageTypes.PROVIDER_SUBSCRIPTION_CHANGE]() {
    updateLiveTVMetadata(this.server.entityID());
  },

  [messageTypes.PROVIDER_CHANGE]() {
    const appStore = adapters.resolve('appStore');
    appStore.dispatch(
      fetchProviders({ serverEntityID: this.server.entityID() })
    );
  },
});

export default MediaServerEventConnection;
