/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import LibraryFailureModal from 'app/js/views/modals/LibraryFailureModal';

function refreshMetadataCommand(options: any) {
  let item: any;
  let items;
  if (options.item) {
    item = options.item;
    items = [item];
  } else {
    items = options.items;
    item = items[0];
  }

  // eslint-disable-next-line prefer-spread
  return $.when
    .apply(
      $,
      _.map(items, function (item_) {
        const url = '/library/metadata/' + item_.id + '/refresh';
        const xhrOptions = _.extend(item.server.getXHROptions(url), {
          type: 'PUT',
        });

        return $.ajax(xhrOptions).promise();
      })
    )
    .fail(function (xhr) {
      if (xhr.status === 503) {
        dispatcher.trigger(
          'show:modal',
          new LibraryFailureModal({ model: item.server })
        );
      } else {
        dispatcher.trigger('show:alert', {
          type: 'error',
          message: t('There was a problem refreshing.'),
        });
      }
    });
}

export default refreshMetadataCommand;
