/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import FirstCharacterModel from './FirstCharacterModel';

const FirstCharacterCollection = AbstractCollection.extend({
  //
  // Properties
  name: 'FirstCharacterCollection',
  context: ['server', 'provider', 'section'],
  model: FirstCharacterModel,
  responseType() {
    return getSupportedResponseType(this.server);
  },
  url() {
    const urlRoot = _.result(this.section, 'url') + '/firstCharacter';
    const params = this.getUrlParams();
    // See: https://github.com/plexinc/plex-web-client/issues/3067
    const url = params ? urlRoot + '?' + params : urlRoot;
    return url;
  },
  //
  // Control
  getUrlParams() {
    const params = {};
    const urlParams: any = [];
    const type = this.section.get('currentType');
    if (type) {
      (params as any).type = type.id;
    }
    const filters = this.section.get('currentFilters');
    // Filters with more than one value should send a comma-delimited list
    // of values.
    _.each(filters, (filter) => {
      if (filter.values.length) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        params[filter.key] = filter.values.join(',');
      }
    });
    // Some of the params are special and can't just be handled by the path
    // util so loop through the params and create a querystring manually.
    // @ts-expect-error ts-migrate(6133) FIXME: 'value' is declared but its value is never read.
    _.each(params, (value, key) => {
      // Send an operator for unviewedLeafCount.
      if (key === 'unviewedLeafCount') {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        urlParams.push(`${key}>=${params[key]}`);
      } else {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        urlParams.push(`${key}=${params[key]}`);
      }
    });
    return urlParams.join('&');
  },
});
export default FirstCharacterCollection;
