const DEBUG = __DEV__ && false;

export default function memoize<T extends (...args: any[]) => R, R>(fn: T): T {
  const cache = new Map<string, R>();

  const memo = (...args: any[]): R => {
    const key = JSON.stringify(args);

    if (cache.has(key)) {
      if (DEBUG) {
        window.console.log(
          '[memoize] %s: fetching cache for key %s',
          fn.name,
          key
        );
      }

      return cache.get(key) as R;
    } else {
      if (DEBUG) {
        window.console.log(
          '[memoize] %s: setting cache for key %s',
          fn.name,
          key
        );
      }

      const result = fn(...args);
      cache.set(key, result);
      return result;
    }
  };

  return memo as T;
}
