/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import SectionModel from 'app/js/models/library/section/SectionModel';

// @ts-expect-error ts-migrate(7023) FIXME: 'getSectionForItem' implicitly has return type 'an... Remove this comment to see the full error message
export default function getSectionForItem(item: any, options = {}) {
  // @ts-expect-error ts-migrate(2339) FIXME: Property 'onlyIfCached' does not exist on type '{}... Remove this comment to see the full error message
  const { onlyIfCached = true } = options;

  if (!item) {
    return undefined;
  }

  if (item.section) {
    return item.section;
  }

  if (item.get('isSection')) {
    return item;
  }

  const key = item.get('librarySectionID');

  if (key) {
    const cachedSection = new SectionModel(
      { key },
      {
        server: item.server,
        onlyIfCached,
      }
    );

    return _.isEmpty(cachedSection) ? undefined : cachedSection;
  }

  if (item.parent) {
    return getSectionForItem(item.parent, options);
  }
}
