/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import saveSettingsModel from 'app/js/stores/actions/helpers/settings/saveSettingsModel';
import { saveExperienceSettings } from 'app/js/stores/actions/userActions';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import garbageCollectEntitiesHelper from 'app/js/utils/state/garbageCollectEntities';
import {
  findProviderModel,
  findServerModel,
  tryFindServerModel,
} from 'app/js/utils/state/modelFinders';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import resolveDescriptor from 'app/js/utils/url/resolveDescriptor';
import history from 'app/js/v4/history';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import createKey from 'app/js/views/v3/helpers/createKey';
import getPrePlayRoute from 'app/js/views/v3/helpers/routes/getPrePlayRoute';

//
// Action Types

export const SET_PRIMARY_SERVER = 'app/setPrimaryServer';
export const RESET_PRIMARY_SERVER = 'app/resetPrimaryServer';
export const CHANGE_PRIMARY_SERVER = 'app/changePrimaryServer';
export const CHANGE_COMPANION_SERVER = 'app/changeCompanionServer';
export const REMOVE_COMPANION_SERVER = 'app/removeCompanionServer';
export const GARBAGE_COLLECT_ENTITIES = 'app/garbageCollectEntities';
export const NAVIGATE = 'app/navigate';
export const NAVIGATE_BACK = 'app/navigateBack';
export const NAVIGATE_FORWARD = 'app/navigateForward';
export const NAVIGATE_HOME = 'app/navigateHome';
export const NAVIGATE_SEARCH = 'app/navigateSearch';
export const NAVIGATE_PRE_PLAY = 'app/navigatePrePlay';
export const SAVE_DEVICE_SETTINGS = 'app/saveDeviceSettings';
export const SAVE_LAST_SOURCE_PIVOT = 'app/saveLastSourcePivot';
export const CLEAR_LAST_SOURCE_PIVOTS = 'app/clearLastSourcePivots';
export const SAVE_PIVOT_PARAMS = 'app/savePivotParams';
export const DISCOVER_SERVERS = 'app/discoverServers';
export const DISMISS_UNCLAIMED_SERVER = 'app/dismissUnclaimedServer';
export const REDIRECT_TO_ROUTE = 'app/redirectToRoute';
export const REDIRECT_TO_URL = 'app/redirectToUrl';
export const TOGGLE_FULLSCREEN = 'app/toggleFullscreen';
export const SELECT_AUDIO_DEVICE = 'app/selectAudioDevice';
export const UPDATE_AUDIO_DEVICES = 'app/updateAudioDevices';
export const POPULATE_CLOUD_SERVER = 'app/populateCloudServer';
export const UPDATE_NAVIGATION_HISTORY = 'app/updateNavigationHistory';
export const UPDATE_AVAILABLE = 'app/updateAvailable';

//
// Action Creators

export const setPrimaryServer = createActionCreator(SET_PRIMARY_SERVER);
export const resetPrimaryServer = createActionCreator(RESET_PRIMARY_SERVER);
export const changePrimaryServer = createActionCreator(CHANGE_PRIMARY_SERVER);
export const changeCompanionServer = createActionCreator(
  CHANGE_COMPANION_SERVER
);
export const removeCompanionServer = createActionCreator(
  REMOVE_COMPANION_SERVER
);
export const garbageCollectEntities = createActionCreator(
  GARBAGE_COLLECT_ENTITIES
);
export const navigate = createActionCreator(NAVIGATE);
export const navigateBack = createActionCreator(NAVIGATE_BACK);
export const navigateForward = createActionCreator(NAVIGATE_FORWARD);
export const navigateHome = createActionCreator(NAVIGATE_HOME);
export const navigateSearch = createActionCreator(NAVIGATE_SEARCH);
export const navigatePrePlay = createActionCreator(NAVIGATE_PRE_PLAY);
export const saveDeviceSettings = createActionCreator(SAVE_DEVICE_SETTINGS);
export const saveLastSourcePivot = createActionCreator(SAVE_LAST_SOURCE_PIVOT);
export const clearLastSourcePivots = createActionCreator(
  CLEAR_LAST_SOURCE_PIVOTS
);
export const savePivotParams = createActionCreator(SAVE_PIVOT_PARAMS);
export const discoverServers = createActionCreator(DISCOVER_SERVERS);
export const dismissUnclaimedServer = createActionCreator(
  DISMISS_UNCLAIMED_SERVER
);
export const redirectToRoute = createActionCreator(REDIRECT_TO_ROUTE);
export const redirectToUrl = createActionCreator(REDIRECT_TO_URL);
export const selectAudioDevice = createActionCreator(SELECT_AUDIO_DEVICE);
export const updateAudioDevices = createActionCreator(UPDATE_AUDIO_DEVICES);
export const populateCloudServer = createActionCreator(POPULATE_CLOUD_SERVER);
export const updateNavigationHistory = createActionCreator(
  UPDATE_NAVIGATION_HISTORY
);
export const updateAvailable = createActionCreator(UPDATE_AVAILABLE);

export const enterFullscreen = createActionCreator(
  TOGGLE_FULLSCREEN,
  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  (payload: any) => {
    return { isFullscreen: true };
  }
);

export const exitFullscreen = createActionCreator(
  TOGGLE_FULLSCREEN,
  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  (payload: any) => {
    return { isFullscreen: false };
  }
);

//
// Action Handlers

export const appActionHandlers = {
  [CHANGE_COMPANION_SERVER](appModel: any, payload: any) {
    const serverModel = tryFindServerModel(appModel, payload);

    if (serverModel) {
      appModel.set('companionServer', serverModel);
    } else {
      log.warn(
        '[Companion] Could not find %s to use as companion server',
        payload.serverEntityID
      );
    }
  },

  [REMOVE_COMPANION_SERVER](appModel: any) {
    appModel.unset('companionServer');
  },

  [GARBAGE_COLLECT_ENTITIES](appModel: any, payload: any) {
    // Ensure that the state tree accurately reflects the `appModel` before
    // determining what can be garbage collected.
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
    scheduleImmediateUpdate();

    const garbage = garbageCollectEntitiesHelper(appModel.getState(), payload);
    const entities = appModel.get('entities');

    for (const entityType in garbage) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      const collectedIDs = garbage[entityType];
      entities.get(entityType).remove(collectedIDs);
    }

    if (payload && payload.budget === Infinity && Object.keys(garbage).length) {
      // When the garbage collector is required to do a full pass (budget ===
      // Infinity) then we want to ensure there are no lingering entities on the
      // state tree.  If full pass is not required then lingering entities on
      // the state tree should not be an issue.
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 0.
      scheduleImmediateUpdate();
    }
  },

  [NAVIGATE_FORWARD]() {
    __DEV__ &&
      window.console.warn(
        'NAVIGATE_FORWARD action handler requires app-specific implementation'
      );
  },

  [NAVIGATE_HOME]() {
    __DEV__ &&
      window.console.warn(
        'NAVIGATE_HOME action handler requires app-specific implementation'
      );
  },

  [NAVIGATE_SEARCH]() {
    __DEV__ &&
      window.console.warn(
        'NAVIGATE_SEARCH action handler requires app-specific implementation'
      );
  },

  [SAVE_LAST_SOURCE_PIVOT](appModel: any, payload: any, dispatch: any) {
    const server = findServerModel(appModel, payload);
    const provider = findProviderModel(appModel, payload);
    const experienceSettings = appModel.get('user').get('experienceSettings');
    const lastSourcePivots = experienceSettings.get('lastSourcePivots');
    const shouldSaveLastSourcePivots = experienceSettings.get(
      'shouldSaveLastSourcePivots'
    );

    if (!shouldSaveLastSourcePivots) {
      return;
    }

    const sourceKey = createKey(
      server.get('machineIdentifier'),
      provider.get('identifier'),
      payload.directoryID
    );

    dispatch(
      saveExperienceSettings({
        lastSourcePivots: {
          ...lastSourcePivots,
          [sourceKey]: payload.pivotKey,
        },
      })
    );
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [CLEAR_LAST_SOURCE_PIVOTS](appModel: any, payload: any, dispatch: any) {
    dispatch(
      saveExperienceSettings({
        lastSourcePivots: {},
      })
    );
  },

  [SAVE_PIVOT_PARAMS](appModel: any, payload: any, dispatch: any) {
    const server = findServerModel(appModel, payload);
    const provider = findProviderModel(appModel, payload);

    const savedPivotParams = appModel
      .get('user')
      .get('experienceSettings')
      .get('savedPivotParams');

    const pivotParamsKey = createKey(
      server.get('machineIdentifier'),
      provider.get('identifier'),
      payload.directoryID,
      payload.pivotKey
    );

    dispatch(
      saveExperienceSettings({
        savedPivotParams: {
          ...savedPivotParams,
          [pivotParamsKey]: payload.params,
        },
      })
    );
  },

  [SAVE_DEVICE_SETTINGS](appModel: any, payload: any) {
    saveSettingsModel(appModel.get('deviceSettings'), payload);
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [DISCOVER_SERVERS](appModel: any) {
    dispatcher.trigger('discover:servers');
  },

  [DISMISS_UNCLAIMED_SERVER](appModel: any, payload: any, dispatch: any) {
    const { machineIdentifier } = payload;
    const dismissedUnclaimedServers = appModel
      .get('deviceSettings')
      .get('dismissedUnclaimedServers');

    if (dismissedUnclaimedServers[machineIdentifier]) {
      return;
    }

    dispatch(
      saveDeviceSettings({
        dismissedUnclaimedServers: {
          ...dismissedUnclaimedServers,
          [machineIdentifier]: true,
        },
      })
    );
  },

  [SET_PRIMARY_SERVER](appModel: any, payload: any) {
    const { machineIdentifier } = payload;
    const servers = appModel.get('servers');

    servers.findServer(machineIdentifier).then(
      (server: any) => {
        appModel.set({
          primaryServer: server,
          isWaitingForPrimaryServer: false,
        });
      },
      () => {
        log.warn('Unable to set primary server to:', machineIdentifier);
      }
    );
  },

  [RESET_PRIMARY_SERVER](appModel: any) {
    appModel.set({
      primaryServer: null,
      isWaitingForPrimaryServer: true,
    });
  },

  [CHANGE_PRIMARY_SERVER](appModel: any, payload: any) {
    const { machineIdentifier } = payload;

    appModel
      .get('servers')
      .findServer(machineIdentifier)
      .done((server: any) => {
        if (
          !server.get('isOutOfDate') &&
          server.get('isPrimaryEligible') &&
          appModel.get('primaryServer') !== server
        ) {
          appModel.set({
            primaryServer: server,
            isWaitingForPrimaryServer: false,
          });
        }
      });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [NAVIGATE](appModel: any, payload: any, dispatch: any) {
    const url = resolveDescriptor(payload);

    if (payload.replace) {
      window.location.replace(url);
    } else {
      window.location.href = url;
    }
  },

  [NAVIGATE_PRE_PLAY](appModel: any, payload: any, dispatch: any) {
    const { key, replace } = payload;
    const serverModel = findServerModel(appModel, payload);
    const providerModel = findProviderModel(appModel, payload);

    // @ts-expect-error ts-migrate(2554) FIXME: Expected 5 arguments, but got 3.
    const route = getPrePlayRoute(
      serverModel.toJSON(),
      providerModel.toJSON(),
      key
    );

    if (route) {
      dispatch(navigate({ pathname: route, replace }));
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [REDIRECT_TO_ROUTE](appModel: any, { route, params, replace }: any) {
    // Defer to avoid the race condition where a Marionette view is not rendered
    // so the React components will not be unmounted in `onClose`.

    _.defer(() => {
      navigateToRoute(route, params, replace);
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [REDIRECT_TO_URL](appModel: any, { url, replace }: any) {
    if (replace) {
      window.location.replace(url);
    } else {
      window.location.href = url;
    }
  },

  [TOGGLE_FULLSCREEN](appModel: any, { isFullscreen }: any) {
    if (appModel.get('isFullscreen') !== isFullscreen) {
      // The fullscreenManager will dispatch an enter or exit fullscreen action
      // after a manual or event-driven fullscreen change.
      appModel.set('isFullscreen', isFullscreen);
    }
  },

  [UPDATE_AVAILABLE](appModel: any, { isUpdateAvailable }: any) {
    if (appModel.get('isUpdateAvailable') !== isUpdateAvailable) {
      appModel.set('isUpdateAvailable', isUpdateAvailable);
    }
  },

  [SELECT_AUDIO_DEVICE]() {
    __DEV__ &&
      window.console.warn(
        'SELECT_AUDIO_DEVICE action handler requires app-specific implementation'
      );
  },

  [UPDATE_AUDIO_DEVICES]() {
    __DEV__ &&
      window.console.warn(
        'UPDATE_AUDIO_DEVICES action handler requires app-specific implementation'
      );
  },

  [POPULATE_CLOUD_SERVER](appModel: any) {
    appModel.get('cloudServer').populate();
  },

  [UPDATE_NAVIGATION_HISTORY](appModel: any, payload: any) {
    const { location } = payload;
    const navHistory = appModel.get('history');

    if (
      !navHistory.length ||
      navHistory[navHistory.length - 1].pathname !== location.pathname ||
      navHistory[navHistory.length - 1].search !== location.search
    ) {
      appModel.set('history', [
        ...navHistory,
        {
          pathname: location.pathname,
          search: location.search,
        },
      ]);
    }
  },

  [NAVIGATE_BACK](appModel: any, { replace }: any = {}) {
    const navHistory = appModel.get('history');

    if (navHistory.length < 2) {
      // If there is not enough history to go back, navigate home.
      appModel.set('history', []);
      navigateToRoute('home', {}, replace);
    } else {
      const newNavHistory = navHistory.slice(0, navHistory.length - 1);
      appModel.set('history', newNavHistory);

      const previous = newNavHistory[newNavHistory.length - 1];
      if (replace) {
        history.replace(previous);
      } else {
        history.push(previous);
      }
    }
  },
};
