/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import MediaGrabOperationModel from './MediaGrabOperationModel';

const MediaGrabOperationCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'MediaGrabOperationCollection',
  context: ['server', 'provider'],

  model: MediaGrabOperationModel,

  comparator(mediaGrabOperation: any) {
    const mediaItem = mediaGrabOperation.get('mediaItem');

    if (!mediaItem) {
      return;
    }

    const beginsAt = mediaItem.beginsAt;
    const startOffsetSeconds = mediaItem.startOffsetSeconds || 0;

    return beginsAt - startOffsetSeconds;
  },
});

export default MediaGrabOperationCollection;
