/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import * as subscriptionStates from 'app/js/utils/constants/user/subscriptionStates';
import * as subscriptionTypes from 'app/js/utils/constants/user/subscriptionTypes';
import dispatcher from 'app/js/utils/dispatcher';
import { getWWWPlexUrl } from 'app/js/utils/getRuntimeUrl';
import * as sectionTypes from 'app/js/utils/types/sectionTypes';
import hasRequiredUserService from 'app/js/v4/core/user/utils/hasRequiredUserService';
import EditSectionBaseView from 'app/js/views/editsection/EditSectionBaseView';
import EditSectionPathsListView from 'app/js/views/editsection/EditSectionPathsListView';
import AddFolderModal from 'app/js/views/modals/AddFolderModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/secti... Remove this comment to see the full error message
import tpl from 'app/templates/section/edit/EditSectionFoldersView.tpl';

export function findActiveSubscription(
  subscriptions: any,
  subscriptionType: any
) {
  return (
    subscriptions.findWhere({
      type: subscriptionType,
      state: subscriptionStates.ACTIVE,
    }) ||
    subscriptions.findWhere({
      type: subscriptionType,
      state: subscriptionStates.PENDING_CANCELLATION,
    })
  );
}

export function findPastSubscription(
  subscriptions: any,
  subscriptionType: any
) {
  return (
    subscriptions.findWhere({
      type: subscriptionType,
      state: subscriptionStates.INACTIVE,
    }) ||
    subscriptions.findWhere({
      type: subscriptionType,
      state: subscriptionStates.CANCELED,
    }) ||
    subscriptions.findWhere({
      type: subscriptionType,
      state: subscriptionStates.ENDED,
    })
  );
}

const EditSectionFoldersView = EditSectionBaseView.extend({
  //
  // Properties

  template: tpl,
  fieldNames: ['Location'],

  regions: {
    pathsRegion: '.paths-region',
  },

  ui: {
    ...EditSectionBaseView.prototype.ui,
    addFolderBtn: '.add-folder-btn',
    addFolderPopulating: '.add-folder-populating',
    addFolderError: '.add-folder-error',
    arcadeDownloadMediaInput: '#arcade-download-media',
    arcadeGreeting: '#arcade-greeting',
    arcadeRequired: '#arcade-required',
    arcadeUpsell: '#arcade-upsell',
    parsecRequired: '#parsec-required',
  },

  events: {
    'click .add-folder-btn': 'onAddFolderClick',
    'change input[name=path]': 'onFolderChange',
    'change input[name=downloadMedia]': 'onDownloadMediaChange',
  },

  onlyAfterRender: ['renderSectionLocation'].concat(
    EditSectionBaseView.prototype.onlyAfterRender
  ),

  //
  // Init

  initialize: function () {
    this.listenTo(this.model.get('locations'), {
      add: this.onCollectionChange,
      remove: this.onCollectionChange,
    });

    if (this.model.get('type') === sectionTypes.GAMES) {
      this.listenTo(resolve('appModel').get('user'), {
        change: this.onUserChange,
      });
    }

    this.renderSectionLocation();

    this.showValidationResult(this.validate(this.model));
  },

  //
  // Control

  serializeData: function () {
    const isGamesSection = this.model.get('type') === sectionTypes.GAMES;
    const isNewGamesSection = this.model.isNew() && isGamesSection;
    const user = resolve('appModel').get('user');

    // Find an active or pending cancellation subscription.
    const hasActiveSubscription = !!findActiveSubscription(
      user.get('subscriptions'),
      subscriptionTypes.ARCADE
    );

    const hasPastSubscription = !!(
      // Find an inactive or canceled subscription.
      (
        findPastSubscription(
          user.get('subscriptions'),
          subscriptionTypes.ARCADE
        ) ||
        // Find an ended subscription.
        findPastSubscription(
          user.get('pastSubscriptions'),
          subscriptionTypes.ARCADE
        )
      )
    );

    const hasParsecUserService = hasRequiredUserService(
      user.get('services').toJSON(),
      'parsec'
    );

    const showArcadeGreeting = isGamesSection && hasActiveSubscription;

    const showArcadeRequired =
      isNewGamesSection && hasPastSubscription && !hasActiveSubscription;

    const showArcadeUpsell =
      isNewGamesSection && !hasPastSubscription && !hasActiveSubscription;

    const showParsecRequired =
      isGamesSection && hasActiveSubscription && !hasParsecUserService;

    return {
      ...EditSectionBaseView.prototype.serializeData.call(this),

      hideArcadeGreeting: !showArcadeGreeting,
      hideArcadeRequired: !showArcadeRequired,
      hideArcadeUpsell: !showArcadeUpsell,
      hideParsecRequired: !showParsecRequired,
      isDisabled: isNewGamesSection && !hasActiveSubscription,
      isGamesSection,
      isNewGamesSection,
      subscribeURL: getWWWPlexUrl('/arcade/checkout'),
    };
  },

  validate: function (model: any) {
    const user = resolve('appModel').get('user');

    const subscription = findActiveSubscription(
      user.get('subscriptions'),
      subscriptionTypes.ARCADE
    );

    if (model.isNew() && model.get('type') === sectionTypes.GAMES) {
      if (!subscription) {
        return ['subscriptionRequired'];
      } else if (model.get('downloadMedia')) {
        // Short-circuit validation when creating a new section and the section
        // does not rely on a location for its media.
        return [];
      }
    }

    const invalidAttrs = [];

    if (!model.get('locations').length) {
      invalidAttrs.push('locations');
    }

    return invalidAttrs;
  },

  renderSectionLocation: function () {
    const view = new EditSectionPathsListView({
      collection: this.model.get('locations'),
    });
    this.pathsRegion.show(view);
  },

  //
  // Control

  onCollectionChange: function () {
    this.showValidationResult(this.validate(this.model));
  },

  onAddFolderClick: function (event: any) {
    event.preventDefault();

    const addFolderModal = new AddFolderModal({ server: this.model.server });

    this.listenToOnce(addFolderModal, 'add:folder', this.onAddFolder);

    dispatcher.trigger('show:modal', addFolderModal);
  },

  onAddFolder: function (folderPath: any) {
    // Don't add empty paths
    if (!folderPath) {
      return;
    }

    const attrs = { path: folderPath };

    // Don't add duplicate paths
    if (this.model.get('locations').findWhere(attrs)) {
      return;
    }

    this.model.get('locations').add(attrs);
  },

  onFolderChange: function (event: any) {
    const $target = $(event.currentTarget);
    const defaultValue = $target.prop('defaultValue');
    const value = $target.val();

    // Don't allow an empty path to be set.
    if (!value) {
      $target.val(defaultValue);
      return;
    }

    const location = this.model
      .get('locations')
      .findWhere({ path: defaultValue });
    location.set('path', value);

    // Update the default value so if the input changes again, we know which
    // path to replace.
    $target.prop('defaultValue', value);
  },

  onDownloadMediaChange: function (event: any) {
    const $target = $(event.currentTarget);

    if ($target.prop('checked')) {
      this.model.set('downloadMedia', 1);

      this.trigger('valid');
    } else {
      this.model.unset('downloadMedia');

      this.trigger('invalid');
    }
  },

  onUserChange: function () {
    const {
      hideArcadeGreeting,
      hideArcadeRequired,
      hideArcadeUpsell,
      hideParsecRequired,
      isDisabled,
    } = this.serializeData();

    const {
      addFolderBtn,
      arcadeDownloadMediaInput,
      arcadeGreeting,
      arcadeRequired,
      arcadeUpsell,
      parsecRequired,
    } = this.ui;

    addFolderBtn.prop('disabled', isDisabled);
    arcadeDownloadMediaInput.prop('disabled', isDisabled);

    arcadeGreeting.toggleClass('hidden', hideArcadeGreeting);
    arcadeRequired.toggleClass('hidden', hideArcadeRequired);
    arcadeUpsell.toggleClass('hidden', hideArcadeUpsell);
    parsecRequired.toggleClass('hidden', hideParsecRequired);

    // Re-run validation to update other pane components such as the `Add
    // Library` or `Save Changes` button.
    this.showValidationResult(this.validate(this.model));
  },
});

export default EditSectionFoldersView;
