/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import log from 'app/js/utils/log/log';
import parser2 from 'app/js/utils/parser2';

function populateServerIdentityCommand(options: any) {
  const { server, connection } = options;

  if (server.id) {
    return $.Deferred().resolve(server.id).promise();
  }

  const url = server.resolveUrl('/identity', { connection: connection });
  const xhrOptions = _.extend(server.getXHROptions(url), {
    timeout: server.connectionTestTimeout,
  });

  log(
    `[Identity] Requesting the machine identifier of ${server.get(
      'debugName'
    )} at ${url}`
  );

  let promise = $.ajax(xhrOptions).promise();

  promise = promise.then((response) => {
    const deferred = $.Deferred();
    const json = parser2.parseXML(response);
    const machineIdentifier = json && json.machineIdentifier;

    if (machineIdentifier) {
      log(
        `[Identity] Machine identifier found for ${server.get(
          'debugName'
        )}: ${machineIdentifier}`
      );

      if (!server.collection || server.collection.get(machineIdentifier)) {
        deferred.resolve(machineIdentifier);
      } else {
        // If there is not an existing server to merge with, wait for additional
        // resources before setting this identifier. Otherwise, a
        // `populateServer` call for this server may be rejected prematurely.

        // Use `populateServer` here so the callback is not invoked until after
        // the `ServerManager` has a chance to fetch resources. Otherwise, there
        // will be a duplicate fetch.

        server.collection
          .populateServer({ isCloud: true })
          .done((cloudServer: any) => {
            cloudServer
              .get('resources')
              .populate()
              .always(() => {
                deferred.resolve(machineIdentifier);
              });
          });

        // `populateServer` will not reject until an unidentified server is
        // resolved or rejected. Handle failures directly from the cloud server
        // to avoid this.

        server.collection
          .findServer({ isCloud: true })
          .done((cloudServer: any) => {
            cloudServer.populate().fail(() => {
              deferred.resolve(machineIdentifier);
            });
          })
          .fail(() => {
            deferred.resolve(machineIdentifier);
          });
      }
    } else {
      deferred.reject();
    }

    return deferred.promise();
  });

  promise.fail(() => {
    log.warn(
      `[Identity] Machine identifier not found for ${server.get(
        'debugName'
      )} at ${url}`
    );
  });

  return promise;
}

export default populateServerIdentityCommand;
