import { createSelector } from 'reselect';
import selectAllServerEntities from 'app/js/stores/selectors/server/selectAllServerEntities';

const selectCloudServer = createSelector(
  selectAllServerEntities,
  (appState) => appState.cloudServer,
  (serverEntities, cloudServer) => serverEntities[cloudServer]
);

export default selectCloudServer;
