import classNames from 'classnames';
import React, { ReactElement, useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import selectHasAnyManageableServerAvailableLibraryUpgrade from 'app/js/stores/selectors/ui/selectHasAnyManageableServerAvailableLibraryUpgrade';
import t from 'app/js/utils/t';
import NavBarActivityMenu from 'app/js/views/v3/components/navbar/NavBarActivityMenu';
import useUniqueElementID from 'app/js/views/v3/helpers/hooks/useUniqueElementID';
import NavBarActivityIcon from './NavBarActivityIcon';
import NavBarIconButton from './NavBarIconButton';
import styles from './NavBarActivityButton.css';

interface NavBarActivityButtonProps {
  isDisabled: boolean;
  showActivityButton: boolean;
  embeddedServerEntityID?: string;
  ownedServerEntityIDs: string[];
  hasSessions?: boolean;
  hasConversions?: boolean;
  hasActivities?: boolean;
  hasAvailableServerUpdate?: boolean;
  hasLibraryRefreshing?: boolean;
  activityButtonCount?: number;
}

export default function NavBarActivityButton(
  props: NavBarActivityButtonProps
): ReactElement {
  const {
    activityButtonCount,
    embeddedServerEntityID,
    hasActivities,
    hasAvailableServerUpdate,
    hasConversions,
    hasLibraryRefreshing,
    hasSessions,
    isDisabled,
    ownedServerEntityIDs,
  } = props;

  const isLibraryUpgradeAvailable = useSelector(
    selectHasAnyManageableServerAvailableLibraryUpgrade
  );

  const buttonID = useUniqueElementID();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const onActivityPress = useCallback(() => {
    setIsMenuOpen(!isMenuOpen);
  }, [isMenuOpen]);

  const onMenuClose = useCallback(() => {
    setIsMenuOpen(false);
  }, []);

  const showSpinner = hasActivities || hasLibraryRefreshing;
  const showHighlighted = hasSessions || hasConversions;
  const showUpdateIcon =
    !showHighlighted &&
    !activityButtonCount &&
    (hasAvailableServerUpdate || isLibraryUpgradeAvailable);

  //
  // Effects

  useEffect(() => {
    if (isDisabled && isMenuOpen) {
      // See: https://github.com/plexinc/plex-web-client/issues/13952
      // Steps to reproduce this use-case:
      // - Run Qt Desktop without any owned servers returned from resources
      // - This button should be visible but disabled
      // - Download a podcast
      // - The button should display spinner and when clicked show menu
      // - When finished downloading the menu should close
      onMenuClose();
    }
  }, [isDisabled, isMenuOpen, onMenuClose]);

  //
  // Render

  return (
    <>
      <NavBarIconButton
        ariaLabel={t('Activity')}
        className={classNames(
          styles.button,
          (showHighlighted || showUpdateIcon) && styles.isHighlighted
        )}
        iconComponent={NavBarActivityIcon}
        iconProps={{
          className: styles.activityIcon,
          showSpinner,
          showHighlighted,
          showUpdateIcon,
        }}
        id={buttonID}
        isDisabled={isDisabled}
        label={activityButtonCount ? String(activityButtonCount) : undefined}
        labelClassName={styles.label}
        title={isMenuOpen ? undefined : t('Activity')}
        onPress={onActivityPress}
      />

      <NavBarActivityMenu
        embeddedServerEntityID={embeddedServerEntityID}
        isOpen={isMenuOpen}
        ownedServerEntityIDs={ownedServerEntityIDs}
        target={buttonID}
        onMenuClose={onMenuClose}
      />
    </>
  );
}
