/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import { navigateBack } from 'app/js/stores/actions/appActions';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function splitApartCommand(options: any) {
  const deferred = $.Deferred();
  const item = options.item;
  const url = '/library/metadata/' + item.id + '/split';

  function splitApart() {
    const xhrOptions = _.extend(item.server.getXHROptions(url), {
      type: 'PUT',
    });

    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(function () {
      const type = item.get('type');
      let locations;

      if (type === 'show' || type === 'artist' || type === 'album') {
        locations = 'Location';
      } else {
        locations = 'Media';
      }

      item.set(locations, item.get(locations).slice(0, 1));

      if (!options.skipNavigate) {
        resolve('appStore').dispatch(navigateBack());
      }

      deferred.resolve();
    });

    xhrDeferred.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem splitting this item apart.'),
      });

      deferred.reject(xhr);
    });

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrDeferred.always(function (xhr) {
      confirmModal.hide();
    });
  }

  const confirmModal = new ConfirmModal({
    title: t('Split Apart'),
    message: t('Are you sure you want to split this item apart?'),
    async: true,
  });

  const confirmPromise = confirmModal.promise;
  confirmPromise.done(splitApart);
  confirmPromise.fail(function () {
    deferred.reject();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return deferred.promise();
}

export default splitApartCommand;
