/* eslint-plugin-disable @typescript-eslint */

import PropTypes from 'prop-types';
import attrTypes from 'app/js/models/attrTypes';

export const UNKNOWN = 1;
export const AVAILABLE = 2;
export const MISSING = 3;
export const MISMATCHED = 4;

const availabilities = [UNKNOWN, AVAILABLE, MISSING, MISMATCHED];

export const shape = PropTypes.oneOf(availabilities);
// @ts-expect-error ts-migrate(2556) FIXME: Expected 0 arguments, but got 1 or more.
export const attrType = attrTypes.oneOf(...availabilities);
