/* eslint-plugin-disable @typescript-eslint */

import X2JS from 'x2js';

const xmlParser = new X2JS({
  attributePrefix: '_',
  // @ts-expect-error ts-migrate(2322) FIXME: Type '{}' is not assignable to type '"object" | "t... Remove this comment to see the full error message
  emptyNodeForm: {},
  ignoreRoot: true,
});

const xmlWriter = {
  writeXml: function (json: any) {
    return '<?xml version="1.0"?>' + xmlParser.js2xml(json);
  },
};

export default xmlWriter;
