import toStableServerObject from 'app/js/v4/core/servers/utils/toStableServerObject';

export default function toStableServerModelJSON(serverModel: any) {
  const json = {
    entityID: serverModel.entityID(),
    ...serverModel.toJSON(),
  };

  return toStableServerObject(json);
}
