/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import EntityCollection from 'app/js/models/EntityCollection';

export default function createEntitiesModelClass(entityTypes: any) {
  const sessionRelations = {};

  for (const key in entityTypes) {
    const entityType = entityTypes[key];

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    sessionRelations[entityType] = {
      relationClass: EntityCollection,
    };
  }

  return AbstractModel.extend({ name: 'EntitiesModel', sessionRelations });
}
