/* eslint-plugin-disable @typescript-eslint */

import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import selectAllLibraryEntities from 'app/js/stores/selectors/library/selectAllLibraryEntities';
import selectEmbeddedServer from 'app/js/stores/selectors/server/selectEmbeddedServer';
import selectEmbeddedServerEntity from 'app/js/stores/selectors/server/selectEmbeddedServerEntity';
import selectOwnedConnectedServerEntityIDs from 'app/js/stores/selectors/server/selectOwnedConnectedServerEntityIDs';
import selectOwnedConnectedServers from 'app/js/stores/selectors/server/selectOwnedConnectedServers';
import selectDismissedServerUpdateVersions from 'app/js/stores/selectors/settings/selectDismissedServerUpdateVersions';
import selectServerUpdateDismissedVersion from 'app/js/stores/selectors/settings/selectServerUpdateDismissedVersion';
import activityTypeWhitelist from 'app/js/utils/activityTypeWhitelist';
import isUpdateAvailable from 'app/js/utils/server/isUpdateAvailable';
import NavBarActivityButton from './NavBarActivityButton';

const initialActivityCounts = {
  sessionsCount: 0,
  backgroundTranscodeJobsCount: 0,
  activitiesCount: 0,
};

function reduceActivityCounts(servers: any, serverNowPlayingSessions: any) {
  return servers.reduce(
    (counts: any, server: any) => {
      const { backgroundTranscodeJobs = [], activities = [] } =
        server[__PLATFORM__];

      const sessions = serverNowPlayingSessions[server.machineIdentifier];
      const sessionCount = sessions?.sessionKeys.length;
      const backgroundTranscodeJobsCount = backgroundTranscodeJobs.length;
      const activitiesCount = activities.filter(
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        (activity: any) => !!activityTypeWhitelist[activity.type]
      ).length;

      // Avoid arithmetic on non numbers.
      // See: https://github.com/plexinc/plex-desktop/issues/705

      if (sessionCount > 0) {
        counts.sessionsCount += sessionCount;
      }

      if (backgroundTranscodeJobsCount > 0) {
        counts.backgroundTranscodeJobsCount += backgroundTranscodeJobsCount;
      }

      if (activitiesCount > 0) {
        counts.activitiesCount += activitiesCount;
      }

      return counts;
    },
    { ...initialActivityCounts }
  );
}

const selectOwnedServerActivityCounts = createSelector(
  selectOwnedConnectedServers,
  (appState) => appState.v4.core.serverNowPlayingSessions,
  (ownedServers, serverNowPlayingSessions) =>
    reduceActivityCounts(ownedServers, serverNowPlayingSessions)
);

const selectEmbeddedServerActivityCounts = createSelector(
  selectEmbeddedServerEntity,
  (appState) => appState.v4.core.serverNowPlayingSessions,
  (embeddedServer, serverNowPlayingSessions) =>
    embeddedServer
      ? reduceActivityCounts([embeddedServer], serverNowPlayingSessions)
      : initialActivityCounts
);

const selectActivityCounts = createSelector(
  selectOwnedServerActivityCounts,
  selectEmbeddedServerActivityCounts,
  (ownedServerActivityCounts, embeddedServerActivityCounts) => {
    return {
      activitiesCount:
        ownedServerActivityCounts.activitiesCount +
        embeddedServerActivityCounts.activitiesCount,
      backgroundTranscodeJobsCount:
        ownedServerActivityCounts.backgroundTranscodeJobsCount +
        embeddedServerActivityCounts.backgroundTranscodeJobsCount,
      sessionsCount:
        ownedServerActivityCounts.sessionsCount +
        embeddedServerActivityCounts.sessionsCount,
    };
  }
);

const selectServerRefreshingLibrariesCount = createSelector(
  selectOwnedConnectedServers,
  selectAllLibraryEntities,
  (ownedServers, libraryEntities) => {
    return ownedServers.reduce((count: any, server: any) => {
      const { libraries: libraryEntityIDs } = server[__PLATFORM__];
      const refreshingLibraries = libraryEntityIDs.filter(
        (libraryEntityID: any) => {
          const library = libraryEntities[libraryEntityID];

          return !!library && library.refreshing;
        }
      );

      count += refreshingLibraries.length;

      return count;
    }, 0);
  }
);

const selectHasAvailableServerUpdate = createSelector(
  selectOwnedConnectedServers,
  selectDismissedServerUpdateVersions,
  selectServerUpdateDismissedVersion,
  (servers, dismissedServerUpdateVersions, serverUpdateDismissedVersion) => {
    return servers.some((server: any) => {
      return isUpdateAvailable(
        server,
        dismissedServerUpdateVersions,
        serverUpdateDismissedVersion
      );
    });
  }
);

function createMapStateToProps() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { isDisabled }: any) => !!isDisabled,
    selectEmbeddedServer,
    selectOwnedConnectedServerEntityIDs,
    selectActivityCounts,
    selectServerRefreshingLibrariesCount,
    selectHasAvailableServerUpdate,
    (
      isDisabled,
      embeddedServerEntityID,
      ownedServerEntityIDs,
      activitiesCounts,
      libraryRefreshingCount,
      hasAvailableServerUpdate
    ) => {
      const hasOwnedServers = ownedServerEntityIDs.length > 0;
      let showActivityButton = hasOwnedServers;

      if (embeddedServerEntityID) {
        showActivityButton = true;
        isDisabled =
          isDisabled ||
          (!hasOwnedServers &&
            activitiesCounts.sessionsCount +
              activitiesCounts.backgroundTranscodeJobsCount +
              activitiesCounts.activitiesCount ===
              0);
      }

      return {
        isDisabled,
        embeddedServerEntityID,
        ownedServerEntityIDs,
        showActivityButton,
        hasSessions: activitiesCounts.sessionsCount > 0,
        hasConversions: activitiesCounts.backgroundTranscodeJobsCount > 0,
        hasActivities: activitiesCounts.activitiesCount > 0,
        hasAvailableServerUpdate,
        hasLibraryRefreshing: libraryRefreshingCount > 0,
        activityButtonCount:
          activitiesCounts.sessionsCount +
          activitiesCounts.backgroundTranscodeJobsCount,
      };
    }
  );
}

export default connect(createMapStateToProps)(NavBarActivityButton);
