import classNames from 'classnames';
import React from 'react';
import {
  IconActivity,
  IconActivityCircledFilled,
  IconUpCircledFilled,
} from '@plex/react-chroma-icon';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import colors from 'app/js/views/v3/styles/variables/colors';
import styles from './NavBarActivityIcon.css';

interface NavBarActivityIconProps {
  className?: string;
  showSpinner?: boolean;
  showHighlighted?: boolean;
  showUpdateIcon?: boolean;
}

function NavBarActivityIcon({
  className,
  showSpinner,
  showHighlighted,
  showUpdateIcon,
}: NavBarActivityIconProps): React.ReactElement {
  //
  // Render

  return (
    <span className={classNames(className, styles.iconContainer)}>
      {showSpinner ? (
        <Spinner
          className={styles.activityIndicator}
          color={colors.primaryAlternateColor}
        />
      ) : null}
      {showHighlighted ? <IconActivityCircledFilled /> : null}
      {!showHighlighted && showUpdateIcon ? <IconUpCircledFilled /> : null}
      {!showHighlighted && !showUpdateIcon ? <IconActivity /> : null}
    </span>
  );
}

export default NavBarActivityIcon;
