/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deleteVersionsCommand(options: any) {
  const item = options.item;
  const server = item.server;

  const mediaItems = item.get('Media');
  const versions = _.filter(mediaItems, function (media) {
    return !!media.proxyType;
  });

  const confirmModal = showConfirmation({ item: item, versions: versions });
  const confirmPromise = confirmModal.promise;

  const promise = confirmPromise.then(function () {
    const url = path.join(['/library/metadata/', item.id], { proxy: 1 });
    const xhrOptions = _.extend(server.getXHROptions(url), { type: 'DELETE' });
    const xhrDeferred = $.ajax(xhrOptions);

    xhrDeferred.done(function () {
      _.each(versions, function (version) {
        mediaItems.splice(_.indexOf(mediaItems, version), 1);
      });

      // Manually trigger the change event so views are updated.
      item.trigger('change:Media', item, item.get('Media'));
    });

    xhrDeferred.fail(function (xhr) {
      const message =
        xhr.status === 403
          ? t(
              'Ensure that media deletion is enabled for library content in your server settings.'
            )
          : t('There was a problem deleting these versions.');

      dispatcher.trigger('show:alert', {
        type: 'error',
        message: message,
      });
    });

    xhrDeferred.always(function () {
      confirmModal.hide();
    });

    return xhrDeferred.promise();
  });

  return promise;
}

function showConfirmation(options: any) {
  const item = options.item;
  const versions = options.versions;
  const isMultiple = versions.length > 1;
  let message;

  if (isMultiple) {
    message = t(
      'Are you sure you want to delete {1} versions of {2}?',
      versions.length,
      item.get('title')
    );
  } else {
    const version = _.first(versions);
    message = t(
      'Are you sure you want to delete the {1} version of {2}?',
      version.title,
      item.get('title')
    );
  }

  const confirmModal = new ConfirmModal({
    title: isMultiple ? t('Delete Versions') : t('Delete Version'),
    message: message,
    confirmLabel: t('Delete'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  dispatcher.trigger('show:modal', confirmModal);

  return confirmModal;
}

export default deleteVersionsCommand;
