/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import getTimelineState from 'app/js/stores/actions/helpers/player/getTimelineState';
import { PLAYING } from 'app/js/utils/constants/player/timelineStates';

function isPlaybackActive() {
  const appModel = resolve('appModel');

  const uiModel = appModel.get('ui');
  const audioVideoPlayer = uiModel.get('audioVideoPlayer');
  const photoPlayer = uiModel.get('photoPlayer');
  const romPlayer = uiModel.get('romPlayer');

  const audioVideoPlayerState = getTimelineState(audioVideoPlayer);
  const photoPlayerState = getTimelineState(photoPlayer);

  return (
    audioVideoPlayerState === PLAYING ||
    photoPlayerState === PLAYING ||
    (!!romPlayer && getTimelineState(romPlayer) === PLAYING)
  );
}

export default isPlaybackActive;
