/* eslint-plugin-disable @typescript-eslint */

export default function base64ImageStringToBlob(base64String: any) {
  if (!base64String) {
    return null;
  }

  const [typeInfo, base64Image] = base64String.split(',');
  const mimeType = typeInfo.substr(5);
  const decodedImage = window.atob(base64Image);

  const blobArray = [];
  for (let i = 0; i < decodedImage.length; i++) {
    blobArray.push(decodedImage.charCodeAt(i));
  }
  const byteArray = new Uint8Array(blobArray);
  const imageBlog = new window.Blob([byteArray], { type: mimeType });

  return imageBlog;
}
