/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import _ from 'underscore';
import createShallowEqualSelector from 'app/js/stores/selectors/createShallowEqualSelector';
import createUnstableProviderSelector from 'app/js/stores/selectors/provider/createUnstableProviderSelector';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import isAbsoluteUrl from 'app/js/utils/url/isAbsoluteUrl';
import path from 'app/js/utils/url/path';

const NOOP = {};
const memo = {};

function selectTranscoderDetails(appState_: any, props: any) {
  const { serverEntityID, providerEntityID } = props;
  const key = `${serverEntityID}:${providerEntityID}`;

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  if (!memo[key]) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    memo[key] = createShallowEqualSelector(
      createSelector(
        createUnstableServerSelector(),
        createUnstableProviderSelector(),
        (server, provider) => {
          const uri =
            server && server.activeConnection && server.activeConnection.uri;

          if (!uri) {
            // Return an Object to satisfy shallowEqual within
            // createShallowEqualSelector.
            return NOOP;
          }

          let transcodeKey = '/photo/:/transcode';
          let isAbsoluteTranscodeKey = false;
          let isPublicTranscoder = false;
          let baseUrl;

          // If we're not using a fallback media server for the image transcode,
          // try to find the provider feature for an image transcoder.

          if (provider) {
            const imageTranscoderFeature = _.findWhere(provider.features, {
              type: providerFeatureTypes.IMAGE_TRANSCODER,
            });

            if (imageTranscoderFeature) {
              transcodeKey = imageTranscoderFeature.key;
              isAbsoluteTranscodeKey = isAbsoluteUrl(transcodeKey);
              isPublicTranscoder = imageTranscoderFeature.public;
            }
          }

          // Use a relative path if the transcoder is relative to the image's
          // server. If the transcoder is hosted separately or part of a
          // different server, use the absolute path.

          if (isAbsoluteTranscodeKey) {
            baseUrl = provider && provider.baseURL ? provider.baseURL : uri;
          } else {
            baseUrl = '';
          }

          return {
            accessToken: server.accessToken,
            baseUrl,
            isAbsoluteTranscodeKey,
            isPublicTranscoder,
            transcodeKey,
            uri,
          };
        }
      ),
      (parts) => parts
    );
  }

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  return memo[key](appState_, props);
}

export default function createImageTranscodeUrlSelector(options: any) {
  // These optional parameters are appended onto the `url` prop before being
  // passed into the transcoder.
  const urlParams = options && options.urlParams;

  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState, { url }) => url,
    selectTranscoderDetails,
    (url, transcoderDetails) => {
      if (!url || transcoderDetails === NOOP) {
        return;
      }

      const {
        accessToken,
        baseUrl,
        isAbsoluteTranscodeKey,
        isPublicTranscoder,
        transcodeKey,
        uri,
      } = transcoderDetails;

      let imageUrl;

      if (isAbsoluteUrl(url)) {
        imageUrl = url;
      } else {
        const params = { 'X-Plex-Token': accessToken };

        if (urlParams) {
          Object.assign(params, urlParams);
        }

        imageUrl = path.join([baseUrl, url], params);
      }

      const pathParts = isAbsoluteTranscodeKey
        ? transcodeKey
        : [uri, transcodeKey];

      const transcodeUrl = path.join(pathParts, {
        url: imageUrl,
        'X-Plex-Token': isPublicTranscoder ? undefined : accessToken,
      });

      return transcodeUrl;
    }
  );
}
