/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseModel from 'app/js/models/BaseModel';
import parser from 'app/js/utils/parser';

const HomeUserModel = BaseModel.extend({
  //
  // Properties
  urlRoot: '/api/home/users',
  whitelistedAttrs: [
    'id',
    'uuid',
    'admin',
    'protected',
    'restricted',
    'restrictionProfile',
    'guest',
    'title',
    'username',
    'email',
    'thumb',
  ],
  //
  // Control
  convert(xml: any) {
    let json = _.isObject(xml) ? xml : parser.parseXML(xml);
    if (json.User) {
      json = json.User;
    }
    parser.parseInteger(json, 'id');
    parser.parseBoolean(json, ['admin', 'protected', 'restricted', 'guest']);
    return json;
  },
  parse(response: any) {
    let json = this.convert(response);
    json = _.pick(json, this.whitelistedAttrs);
    return json;
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  getSaveParams(data: any, options: any) {
    if (_.isEmpty(data)) {
      const params = {};
      if (this.get('restricted')) {
        (params as any).title = this.get('title');
      } else if (this.isNew()) {
        (params as any).invitedEmail = this.get('username');
      }
      return params;
    } else {
      // eslint-disable-next-line prefer-rest-params
      return BaseModel.prototype.getSaveParams.apply(this, arguments);
    }
  },
});
export default HomeUserModel;
