/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import * as features from 'app/js/utils/constants/user/features';
import formatTimeAgo from 'app/js/utils/dateTime/formatTimeAgo';
import renderComponentToString from 'app/js/utils/renderComponentToString';
import sectionTypes, { AUTHOR, GAMES } from 'app/js/utils/types/sectionTypes';
import EditSectionBaseView from 'app/js/views/editsection/EditSectionBaseView';
import getSectionTypeIcon from 'app/js/views/v3/components/core/icons/getSectionTypeIcon';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/secti... Remove this comment to see the full error message
import tpl from 'app/templates/section/edit/EditSectionTypeView.tpl';

function isTypeSupportedByServer(sectionType: any, server: any) {
  if (sectionType?.key === GAMES) {
    return server.get('games');
  } else if (sectionType?.key === AUTHOR) {
    return server.get('audiobook');
  }
  return true;
}
const EditSectionTypeView = EditSectionBaseView.extend({
  //
  // Properties
  template: tpl,
  ui: _.extend(
    {
      sectionTypes: '.wizard-block',
      sectionTitle: '#edit-section-title',
      sectionLanguage: '#edit-section-language',
      sectionLanguageGroup: '.language-group',
    },
    EditSectionBaseView.prototype.ui
  ),
  modelEvents: {
    'change:type': 'onSectionTypeChange',
    'change:title': 'onSectionTitleChange',
    'change:language': 'onSectionLanguageChange',
  },
  events: {
    'click .section-type': 'onSectionTypeClick',
    'keyup #edit-section-title': 'onEditSectionTitleChange',
    'change #edit-section-title': 'onEditSectionTitleChange',
    'change #edit-section-language': 'onEditSectionLanguageChange',
  },
  onlyAfterRender: [
    'renderSectionLanguage',
    'updateSectionTypesWizardBlocks',
    'updateSectionLanguage',
    'updateSectionTitle',
  ].concat(EditSectionBaseView.prototype.onlyAfterRender),
  //
  // Init
  initialize: function (options: any) {
    this.populateCollections();
    this.isDefaultSection = options.isDefaultSection;
  },
  //
  // Control
  serializeData: function () {
    const data = EditSectionBaseView.prototype.serializeData.apply(
      this,
      // eslint-disable-next-line prefer-rest-params
      arguments
    );
    const appModel = adapters.resolve('appModel');
    const user = appModel.get('user');
    const hasGamesFeatureFlags =
      user.get('features').get(features.RETRO_GAMES) &&
      this.model.server.hasOwnerFeature(features.RETRO_GAMES);
    const hasAudiobooksFeatureFlag = user
      .get('features')
      .get(features.AUDIOBOOKS_CLIENT);

    // The Games section is displayed when the user and server owner have the
    // RETRO_GAMES feature flag. The flag is used as a version gate to identify
    // client and server versions which support complete games functionality.
    //
    // Later the section type is only allowed to be created if the server's
    // platform supports running the game transcoder.
    data.sectionTypes = sectionTypes.getAll().reduce((acc: any, type: any) => {
      if (
        // If type is neither games nor author.
        (type.key !== GAMES && type.key !== AUTHOR) ||
        // Or type is game or audiobook with the necessary feature flag.
        (type.key === GAMES && hasGamesFeatureFlags) ||
        (type.key === AUTHOR && hasAudiobooksFeatureFlag)
      ) {
        acc.push({
          ...type,
          typeIcon: renderComponentToString(getSectionTypeIcon(type.key)),
        });
      }
      return acc;
    }, []);
    const currentSectionType = this.getSectionType();
    if (currentSectionType) {
      data.currentSectionKey = currentSectionType.key;
    }
    const updatedAt = this.model.get('updatedAt');
    if (updatedAt) {
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
      data.updatedAt = formatTimeAgo(updatedAt);
    }
    data.canChangeType = this.model.isNew() && !this.isDefaultSection;
    data.hasType = !!this.model.get('type');
    data.isInitialTypeSupported = isTypeSupportedByServer(
      this.model.sectionType,
      this.model.server
    );
    data.escapedUpdatedAt = _.escape(data.updatedAt);
    return data;
  },
  validate: function (model: any) {
    const invalidAttrs = [];
    if (!model.get('type')) {
      invalidAttrs.push('type');
    }
    if (!model.get('title')) {
      invalidAttrs.push('title');
    }
    if (!model.get('language')) {
      invalidAttrs.push('language');
    }
    if (!model.get('isCollectionsPopulated')) {
      invalidAttrs.push('collectionsNotPopulated');
    }
    if (!isTypeSupportedByServer(model.sectionType, model.server)) {
      invalidAttrs.push('unsupportedType');
    }
    return invalidAttrs;
  },
  populateCollections: function () {
    this.model.set('isCollectionsPopulated', false);
    const populateAgents = this.populateAgents();
    const populateScanners = this.populateScanners();
    const sectionType = this.getSectionType();
    let promise = $.when(
      sectionType,
      populateAgents,
      populateScanners,
      this.model.server.get('myPlexSubscription') &&
        this.model.server.supports('premiumMusic')
    );
    if (this.model.isNew()) {
      promise = promise.then(_.bind(this.model.setAgentAndScanner, this.model));
    }
    promise = promise
      .then(populateAgents)
      .then(_.bind(this.onAgentsPopulate, this))
      .then(
        _.bind(function () {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.showValidationResult(this.validate(this.model));
        }, this)
      )
      .then(() => {
        // This synced attribute is provided by clients only during creation of
        // games sections.
        if (this.model.isNew() && this.model.get('type') === GAMES) {
          this.model.set('downloadMedia', 1);
        } else {
          this.model.unset('downloadMedia');
        }
        this.model.set('isCollectionsPopulated', true);
      });
    return promise;
  },
  renderSectionLanguage: function () {
    let currentLanguage = this.model.get('language');
    const agent = this.model.agents.get(this.model.get('agent'));
    if (!agent) {
      this.ui.sectionLanguageGroup.hide();
      return;
    }
    const agentLanguages = agent.get('languages');
    if (!agentLanguages.get(currentLanguage)) {
      const defaultServerLanguage = agentLanguages.findWhere({ default: true });
      let defaultLanguage;
      if (defaultServerLanguage) {
        defaultLanguage = defaultServerLanguage.get('code');
      }
      if (!defaultLanguage) {
        const type = this.model.get('type');
        const sectionType = sectionTypes.get(type);
        defaultLanguage = sectionType.defaultLanguage;
      }
      this.model.set('language', defaultLanguage, { silent: true });
      currentLanguage = defaultLanguage;
    }
    const options = agentLanguages.map(function (language: any) {
      return {
        label: language.get('name'),
        value: language.id,
      };
    });
    this.updateSectionLanguage();
    this.ui.sectionLanguage.html(
      this.createMenuOptions(options, currentLanguage)
    );
  },
  updateSectionTypesWizardBlocks: function () {
    this.ui.sectionTypes.removeClass('selected');
    this.ui.sectionTypes
      .filter('[data-key="' + this.model.get('type') + '"]')
      .addClass('selected');
    this.$el.addClass('section-type-chosen');
  },
  updateSectionLanguage: function () {
    this.ui.sectionLanguage.val(this.model.get('language'));
  },
  updateSectionTitle: function () {
    const uiVal = this.ui.sectionTitle.val();
    const currentVal = this.model.get('title');
    if (uiVal !== currentVal) {
      this.ui.sectionTitle.val(currentVal);
    }
  },
  //
  // Listeners
  onSectionTypeClick: function (event: any) {
    event.preventDefault();
    const $target = $(event.currentTarget);
    if (!$target.hasClass('selectable')) {
      return;
    }
    const sectionKey = $target.data('key');
    const attrs = { type: sectionKey };
    const sectionType = sectionTypes.get(sectionKey);
    if (!this.model.get('title')) {
      (attrs as any).title = sectionType.title;
    }
    this.model.set(attrs);
  },
  onEditSectionTitleChange: function (event: any) {
    const $target = $(event.currentTarget);
    this.model.set('title', $target.val());
    this.showValidationResult(this.validate(this.model));
  },
  onEditSectionLanguageChange: function (event: any) {
    const $target = $(event.currentTarget);
    this.model.set('language', $target.val());
    this.showValidationResult(this.validate(this.model));
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'section' is declared but its value is never read.
  onSectionTypeChange: function (section: any, sectionKey: any) {
    this.updateSectionTypesWizardBlocks();
    // Get initial language
    const sectionType = sectionTypes.get(sectionKey);
    if (!this.model.get('language')) {
      const defaultLanguage = sectionType.defaultLanguage;
      this.model.set('language', defaultLanguage);
    }
    // Section title tracks section type name
    const title = this.model.get('title');
    const previousSectionKey = this.model.previous('type');
    const previousSectionType = sectionTypes.get(previousSectionKey);
    const previousTitle = previousSectionType && previousSectionType.title;
    if (!title || title === previousTitle) {
      this.model.set('title', sectionType.title);
    }
    this.setSectionType(sectionType);
    // Clear section-specific collections
    this.model.agents = null;
    this.model.scanners = null;
    this.model.settings = null;
    // The server only exposes the games FF on systems which can run games. For,
    // example Linux-based PMS cannot run games.
    const isSupported = isTypeSupportedByServer(sectionType, this.model.server);
    this.$el.toggleClass('is-type-unsupported', !isSupported);
    if (isSupported) {
      this.populateCollections();
    }
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'section' is declared but its value is never read.
  onSectionTitleChange: function (section: any, title: any) {
    this.updateSectionTitle();
    this.showValidationResult(this.validate(this.model));
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'section' is declared but its value is never read.
  onSectionLanguageChange: function (section: any, language: any) {
    this.updateSectionLanguage();
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'agents' is declared but its value is never read.
  onAgentsPopulate: function (agents: any) {
    this.renderSectionLanguage();
  },
});
export default EditSectionTypeView;
