/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import sharingSettingsAttrs from 'app/js/utils/types/sharingSettingsAttrs';
import ServerShareCollection2 from './ServerShareCollection2';

const FriendModel2 = AbstractModel.extend({
  //
  // Properties

  name: 'FriendModel2',

  context: ['server'],

  rootPropertyName: 'User',

  entityType: entityTypes.FRIEND,

  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    return createEntityID(this.entityType, state.id);
  },

  urlRoot() {
    return this.get('home') ? '/api/users' : '/api/friends';
  },

  sessionAttrs: {
    user: attrTypes.string,
    isRemoving: attrTypes.boolean,
  },

  syncedAttrs: {
    id: attrTypes.integer,
    home: attrTypes.boolean,
    protected: attrTypes.boolean,
    restricted: attrTypes.boolean,
    title: attrTypes.string,
    username: attrTypes.string,
    email: attrTypes.string,
    thumb: attrTypes.string,
    recommendationsPlaylistId: attrTypes.string,
    ...sharingSettingsAttrs,
  },

  syncedRelations: {
    serverShares: {
      relationClass: ServerShareCollection2,
      rootPropertyName: 'Server',
    },
  },

  defaults: {
    isRemoving: false,
  },
});

export default FriendModel2;
