import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import paneModalMixin from 'app/js/mixins/views/modals/paneModalMixin';
import SectionModel from 'app/js/models/library/section/SectionModel';
import serverFeatures from 'app/js/models/server/serverFeatures';
import {
  fetchLibraries,
  fetchProviders,
} from 'app/js/stores/actions/serverActions';
import { pinSources } from 'app/js/stores/actions/userActions';
import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as providerIdentifiers from 'app/js/utils/constants/providerIdentifiers';
import dispatcher from 'app/js/utils/dispatcher';
import isVersionOrNewer from 'app/js/utils/isVersionOrNewer';
import parser from 'app/js/utils/parser';
import createLibraryPivotKey from 'app/js/utils/provider/createLibraryPivotKey';
import getSourceKey from 'app/js/utils/source/getSourceKey';
import t, { tEscape } from 'app/js/utils/t';
import getSourceTypeForDirectory from 'app/js/utils/types/getSourceTypeForDirectory';
import {
  ALBUM,
  ARTIST,
  getMetadataType,
} from 'app/js/utils/types/metadataTypes';
import sectionTypes from 'app/js/utils/types/sectionTypes';
import path from 'app/js/utils/url/path';
import EditSectionAdvancedView from 'app/js/views/editsection/EditSectionAdvancedView';
import EditSectionErrorView from 'app/js/views/editsection/EditSectionErrorView';
import EditSectionFoldersView from 'app/js/views/editsection/EditSectionFoldersView';
import EditSectionSelectPremiumMusicView from 'app/js/views/editsection/EditSectionSelectPremiumMusicView';
import EditSectionTypeView from 'app/js/views/editsection/EditSectionTypeView';
import BaseModal from 'app/js/views/modals/BaseModal';
import LibraryFailureModal from 'app/js/views/modals/LibraryFailureModal';
import getSourceRoute from 'app/js/views/v3/helpers/routes/getSourceRoute';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/EditSectionModal.tpl';

const EditSectionModal = BaseModal.extend({
  mixins: [paneModalMixin],
  //
  // Properties
  name: 'editSectionModal',
  className: 'edit-section-modal modal modal-lg fade',
  template: tpl,
  panes: {
    type: {
      constructor: EditSectionTypeView,
      nextPane: 'folders',
      dependentPanes() {
        return (this as any).showFolders
          ? ['folders']
          : ['options', 'advanced'];
      },
    },
    folders: {
      constructor: EditSectionFoldersView,
      nextPane: 'options',
      dependentPanes: ['options', 'advanced'],
    },
    options: {
      constructor: EditSectionSelectPremiumMusicView,
      nextPane: 'advanced',
    },
    advanced: {
      constructor: EditSectionAdvancedView,
    },
    error: {
      constructor: EditSectionErrorView,
    },
  },
  panesModel: 'tempModel',
  ui: {
    sidebar: '.modal-nav-list',
    optionsBtn: '.options-btn',
    saveBtn: '.save-btn',
    message: '.form-message',
    headerSectionType: '.section-type-title',
  },
  regions: {
    paneRegion: '.pane-region',
  },
  events: _.extend(
    {
      'click .change-pane-btn': 'onChangePaneClick',
      'click .save-btn': 'onSaveClick',
      'submit .edit-section-type-form': 'onGeneralFormSubmit',
    },
    BaseModal.prototype.events
  ),
  onlyAfterRender: [
    'showPane',
    'updateMessage',
    'updateSectionTitle',
    'updateSectionTitleDestabilized',
    'updateSaveBtn',
  ],
  paneConstructorArg: function () {
    return { isDefaultSection: this.isDefaultSection };
  },
  //
  // Init
  initialize: function (options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);
    this.server = options.server;
    this.redirect = options.redirect;
    this.isDefaultSection = options.isDefaultSection;
    this.isLoading = true;
    // Keep any changes on a clone of the model until we're ready to save.
    if (this.model) {
      this.tempModel = this.model.clone();
      this.tempModel.collection = this.model.collection;
    } else {
      this.tempModel = new SectionModel(
        {},
        {
          server: this.server,
          collection: this.server.get('sections'),
        }
      );
    }
    this.listenTo(this.tempModel, 'change:type', this.onSectionTypeChange);
    // Set attributes after we show the pane, so any type change listeners
    // are set up already.
    if (this.tempModel.isNew() && options.seedAttrs) {
      this.tempModel.set(this.tempModel.parse(options.seedAttrs));
    }
    this._deferred = $.Deferred();
    this.promise = this._deferred.promise();
    const providers = this.server.get('providers');
    providers.populate().done(
      this.whenRendered(() => {
        const libraryProvider = providers.get(providerIdentifiers.LIBRARY);
        const manageFeature = libraryProvider
          .get('features')
          .get(providerFeatureTypes.MANAGE);
        const automaticLibraryFolders = manageFeature.get(
          'automaticLibraryFolders'
        );
        this.showFolders = !automaticLibraryFolders;
        this.isLoading = false;
        this.render();
        this.showPane(options.selectedPane || 'type');
      })
    );
  },
  //
  // Control
  serializeData: function () {
    const model = this.tempModel;
    const isNew = model.isNew();
    const paneTitle = model && tEscape('Edit {1}', model.get('title'));
    const data = {
      isLoading: this.isLoading,
      add: isNew,
      edit: !isNew,
      paneTitle,
      section: model?.toJSON(),
      selectedPane: this.selectedPane,
      showFolders: this.showFolders,
    };
    return data;
  },
  updateMessage: function (text: any) {
    this.ui.message.text(text || '');
  },
  updateSaveBtn: function () {
    const saveBtn = this.ui.saveBtn;
    const selectedPane = this.panes[this.selectedPane];
    const updateSaveBtn = _.bind(function (options: any) {
      const nextPane = selectedPane.nextPane;
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const isNew = this.tempModel.isNew();
      const isSelectedPaneValid =
        !selectedPane.constructor.prototype.validate ||
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        selectedPane.constructor.prototype.validate(this.tempModel).length ===
          0;
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const isEveryPaneValid = !this.getReachablePanes().isAnyInvalid;
      // Although the EditSectionTypeView pane validates isCollectionsPopulated,
      // we still need that validation when on other panes.
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      const isModelPopulated = this.tempModel.get('isCollectionsPopulated');
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      this.ui.optionsBtn.toggleClass('hidden', !options.showOptions);
      let isNextBtn;
      const isEnabled =
        isModelPopulated &&
        (isEveryPaneValid || // if there are no invalid panes
          (isNew && isSelectedPaneValid)); // the section is new the selected pane is valid
      isNextBtn = nextPane;
      if (!options.showOptions && nextPane === 'options') {
        isNextBtn = false;
      }
      if (isEveryPaneValid) {
        isNextBtn = false;
      }
      if (options.showOptions && nextPane === 'options') {
        isNextBtn = true;
      }
      if (options.showAdvanced && nextPane === 'advanced') {
        isNextBtn = true;
      }
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      if (this.tempModel.isNew() && nextPane === 'folders') {
        // Ease restrictions on EditSectionsFoldersView validation. Always
        // navigate to the folders pane when creating a new section. Editing an
        // existing section doesn't require selecting a folder.
        isNextBtn = true;
      }
      saveBtn.toggleClass('save', !isNextBtn);
      saveBtn.toggleClass('next', !!isNextBtn);
      saveBtn.toggleClass('disabled', !isEnabled);
    }, this);
    const server = this.tempModel.server;
    const sectionType = this.tempModel.sectionType;
    const isMusic = sectionType && sectionType.key === 'music';
    const isPremiumMusicCandidate = isMusic && server.supports('premiumMusic');
    const isNew = this.tempModel.isNew();
    if (isPremiumMusicCandidate) {
      // Can only edit or select premium music on an able server at creation
      this.paneRegion.currentView.populateAgents().then(function (agents: any) {
        const hasPremiumMusic = agents.findWhere({
          identifier: agentIdentifiers.PLEXPREMIUMMUSIC,
        });
        const canSelectPremiumMusic = isNew && hasPremiumMusic;
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
        updateSaveBtn({
          showOptions: canSelectPremiumMusic,
          showAdvanced: canSelectPremiumMusic,
        });
      });
    } else {
      // No premium option
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 0 arguments, but got 1.
      updateSaveBtn({});
    }
  },
  updateSectionTitle: function () {
    const sectionType = sectionTypes.get(this.tempModel.get('type'));
    if (sectionType) {
      this.updateSectionTitleDestabilized();
      const html = _.escape(sectionType.title);
      this.ui.headerSectionType.html(html);
    }
  },
  updateSectionTitleDestabilized: function () {
    this.ui.headerSectionType.addClass('section-type-title-unstable');
    this.ui.headerSectionType.triggerReflow();
    this.ui.headerSectionType.removeClass('section-type-title-unstable');
  },
  save: function () {
    const saveBtn = this.ui.saveBtn;
    this.updateMessage('');
    saveBtn.addClass('active');
    // Save the model
    const sectionPromise = this.saveModel();
    // Remove the active style from the button, regardless of outcome
    sectionPromise.always(function () {
      saveBtn.removeClass('active');
    });
    // Inform the user if something failed
    sectionPromise.fail(
      _.bind(function (xhr: any) {
        if (xhr.status === 503) {
          dispatcher.trigger(
            'show:modal',
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            new LibraryFailureModal({ model: this.server })
          );
        } else {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.updateMessage(t('Your changes could not be saved.'));
        }
      }, this)
    );
    // Success! Hide the modal, and maybe navigate to the section
    sectionPromise.then(
      _.bind(function () {
        // Fetch providers so the directories can update.
        // See: https://github.com/plexinc/plex-web-client/issues/6178
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const serverEntityID = this.model.server.entityID();
        const dispatch = resolve('appStore').dispatch;
        dispatch(fetchProviders({ serverEntityID }));
        dispatch(fetchLibraries({ serverEntityID }));
        // If we're not redirecting, just close the modal and be done with it.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (!this.redirect) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this._deferred.resolve();
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.hide();
          return;
        }
        // Create server, provider and sourceDirectory that can satisfy our
        // route builders.
        const server = {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          machineIdentifier: this.model.server.get('machineIdentifier'),
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          isCloud: this.model.server.get('isCloud'),
        };
        const provider = {
          identifier: providerIdentifiers.LIBRARY,
          isLibraryProvider: true,
        };
        const sourceDirectory = {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          id: this.model.id,
        };
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const type = this.model.get('type');
        const routeParams = {};
        (routeParams as any).pivot = isVersionOrNewer(
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.model.server.get('version'),
          serverFeatures.pivotIDs
        )
          ? 'library'
          : // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            createLibraryPivotKey(this.model.id, type);
        if (type === ARTIST) {
          (routeParams as any).key = path.join(
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            ['/library/sections', this.model.id, 'all'],
            {
              type: getMetadataType(ALBUM).id,
              sort: 'addedAt:desc',
            }
          );
        }
        const route = getSourceRoute(
          server,
          provider,
          sourceDirectory,
          routeParams
        );
        // @ts-expect-error ts-migrate(2322) FIXME: Type 'string | undefined' is not assignable to typ... Remove this comment to see the full error message
        window.location.hash = route;
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this._deferred.resolve();
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.hide();
      }, this)
    );
  },
  saveModel: function () {
    const settingsParams = this.getSectionSettingsParams();
    const saveOptions = settingsParams
      ? { data: { prefs: settingsParams } }
      : {};
    let sectionPromise;
    if (this.tempModel.isNew()) {
      sectionPromise = this.tempModel.save(null, saveOptions);
      sectionPromise = sectionPromise.then(
        _.bind(function (
          response: any,
          // @ts-expect-error ts-migrate(6133) FIXME: 'status' is declared but its value is never read.
          status: any,
          xhr: any
        ) {
          let json = parser.parseXML(response);
          // If we just created the section, we'll get back a 201
          // and need to read the key from the response headers.
          if (xhr.status !== 201) {
            return;
          }
          // PMS 0.9.7 should return the new section with the response.
          if (json.Directory) {
            json = json.Directory;
          }
          if (!json.key) {
            const locationPath = xhr.getResponseHeader('Location');
            if (locationPath) {
              json.key = locationPath.substring(
                locationPath.lastIndexOf('/') + 1
              );
            }
          }
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          const server = this.server;
          if (server.supports('advancedFilters')) {
            json.filters = true;
          }
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.model = new SectionModel(json, {
            parse: true,
            server,
          });
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          server.get('sections').add(this.model);
          const hiddenValue = settingsParams?.hidden;
          const hiddenInt =
            typeof hiddenValue === 'string' ? parseInt(hiddenValue, 10) : null;
          // If the section is not excluded from the home screen, automatically
          // try to pin it to the sidebar.
          if (hiddenInt !== 1 && hiddenInt !== 2) {
            const libraryProvider = server
              .get('providers')
              .get(providerIdentifiers.LIBRARY);
            const sourceTypeObject = libraryProvider
              ? getSourceTypeForDirectory(
                  libraryProvider.toJSON(),
                  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                  this.model.toJSON()
                )
              : null;
            const sourceType = sourceTypeObject ? sourceTypeObject.key : null;
            if (libraryProvider && sourceType) {
              resolve('appStore').dispatch(
                pinSources({
                  sources: [
                    {
                      key: getSourceKey(
                        sourceType,
                        server.get('machineIdentifier'),
                        libraryProvider.get('identifier'),
                        json.key
                      ),
                      sourceType,
                      serverEntityID: server.entityID(),
                      machineIdentifier: server.get('machineIdentifier'),
                      providerEntityID: libraryProvider.entityID(),
                      providerIdentifier: libraryProvider.get('identifier'),
                      providerMetricsIdentifier:
                        libraryProvider.get('metricsIdentifier'),
                      directoryID: json.key,
                      directoryIcon: null,
                      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
                      title: this.model.get('title'),
                      serverFriendlyName: server.get('friendlyName'),
                      serverSourceTitle: server.get('sourceTitle'),
                      isLocal: server.get('isPrivateConnection'),
                      isUnavailable: false,
                      isOutOfDate: false,
                      isUnclaimedServer: !server.get('isClaimed'),
                      isFullOwnedServer: server.get('isFullOwnedServer'),
                      isProviderReady: true,
                    },
                  ],
                  interaction: false,
                })
              );
            }
          }
          return xhr;
        }, this)
      );
    } else {
      this.model.copy(this.tempModel);
      sectionPromise = this.model.save(null, saveOptions);
    }
    sectionPromise = sectionPromise.then(_.bind(this.saveAgentSettings, this));
    return sectionPromise;
  },
  saveSettings: function (params: any) {
    // We'll save the new settings, but the section
    // remains the value propagated through the promise.
    const resolvedPromise = $.Deferred().resolve(this.model).promise();
    if (!this.server.supports('sectionSettings')) {
      return resolvedPromise;
    }
    if (!params) {
      return resolvedPromise;
    }
    const url = path.join(
      ['/library/sections/', this.model.id, '/prefs'],
      params
    );
    const xhrOptions = _.extend(this.server.getXHROptions(url), {
      type: 'PUT',
    });
    return $.ajax(xhrOptions)
      .promise()
      .then(function () {
        return resolvedPromise;
      });
  },
  saveAgentSettings: function () {
    // We'll save the new settings, but the section
    // remains the value propagated through the promise.
    const resolvedPromise = $.Deferred().resolve(this.model).promise();
    // Take settings from the temporary model, since this
    // is where any changes will have been made.
    const agentSettings = this.tempModel.agentSettings;
    if (!this.tempModel.agentSettings) {
      return resolvedPromise;
    }
    const agentSettingsChanges = this.getSettingsChanges(agentSettings);
    if (_.isEmpty(agentSettingsChanges)) {
      return resolvedPromise;
    }
    const agentSetting = agentSettings.first();
    const params = agentSetting.getSaveParams(agentSettingsChanges);
    const urlRoot = _.result(agentSettings, 'url');
    const url = path.join([urlRoot, '/set'], params);
    const xhrOptions = agentSetting.server.getXHROptions(url);
    return $.ajax(xhrOptions)
      .promise()
      .then(function () {
        return resolvedPromise;
      });
  },
  getSectionSettingsParams: function () {
    // Take settings from the temporary model, since this
    // is where any changes will have been made.
    const settings = this.tempModel.settings;
    if (!settings) {
      return;
    }
    const settingsChanges = this.getSettingsChanges(settings);
    if (_.isEmpty(settingsChanges)) {
      return;
    }
    const setting = settings.first();
    return setting.getSaveParams(settingsChanges);
  },
  getSettingsChanges: function (settings: any) {
    const settingsChanges = settings
      // Only consider settings that have changed
      .filter(
        function (setting: any) {
          return setting.changedAttributes();
        }
        // Transform to hash of params
      )
      .reduce(
        function (acc: any, setting: any) {
          let value = setting.get('value');
          // Transform bools to 0/1 param values
          if (setting.get('type') === 'bool') {
            value = value ? '1' : '0';
          }
          // Populate the params for this setting's key
          // with it's corresponding value.
          acc[setting.get('id')] = value;
          return acc;
        },
        // Initially empty params hash
        {}
      );
    return settingsChanges;
  },
  //
  // Listeners
  // @ts-expect-error ts-migrate(6133) FIXME: 'paneName' is declared but its value is never read... Remove this comment to see the full error message
  onPaneShow: function (paneName: any, view: any) {
    this.listenTo(view, 'valid', this.updateSaveBtn);
    this.listenTo(view, 'invalid', this.updateSaveBtn);
    this.updateSaveBtn();
  },
  // @ts-expect-error ts-migrate(6133) FIXME: 'paneName' is declared but its value is never read... Remove this comment to see the full error message
  onPaneHide: function (paneName: any, view: any) {
    this.stopListening(view, 'valid', this.updateSaveBtn);
    this.stopListening(view, 'invalid', this.updateSaveBtn);
  },
  onChangePaneClick: function (event: any) {
    event.preventDefault();
    const $target = this.$(event.currentTarget);
    if ($target.isDisabled()) {
      return;
    }
    this.showPane($target.data('pane'));
  },
  onSaveClick: function (event: any) {
    event.preventDefault();
    const saveBtn = this.ui.saveBtn;
    if (saveBtn.isDisabled()) {
      return;
    }
    if (saveBtn.hasClass('next')) {
      let nextPane = this.panes[this.selectedPane].nextPane;
      if (nextPane === 'folders' && !this.showFolders) {
        nextPane = this.ui.sidebar.find('.options-btn').isDisabled()
          ? 'advanced'
          : 'options';
      }
      if (
        nextPane &&
        !this.ui.sidebar.find('.' + nextPane + '-btn').isDisabled()
      ) {
        this.showPane(nextPane);
      }
    } else {
      this.save();
    }
  },
  onGeneralFormSubmit: function (event: any) {
    this.onSaveClick(event);
  },
  onSectionTypeChange: function () {
    this.updateSaveBtn();
  },
});
export default EditSectionModal;
