import { createSelector } from 'reselect';
import createDeepEqualSelector from 'app/js/stores/selectors/createDeepEqualSelector';
import selectAllServerEntities from 'app/js/stores/selectors/server/selectAllServerEntities';
import selectEmbeddedServer from './selectEmbeddedServer';

const selectEmbeddedServerEntity = createDeepEqualSelector(
  createSelector(
    selectEmbeddedServer,
    selectAllServerEntities,
    (embeddedServer, serverEntities) => serverEntities[embeddedServer]
  ),
  (props) => props
);

export default selectEmbeddedServerEntity;
