/* eslint-plugin-disable @typescript-eslint */

import { LEADER_STATUS_CHANGED } from 'app/js/utils/constants/localStorageLeaderEvents';
import log from 'app/js/utils/log/log';
import random from 'app/js/utils/string/random';

const ELECTION_DURATION = 500;
const MAX_LEADER_AGE = 90000; // 1.5 minutes
const MIN_LEADER_REFRESH_DURATION = 10000; // 10 seconds

// Storage
export const LEADER_STORAGE_KEY = 'vote/leader';

// @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
function localStorageGetItem(key: any) {
  try {
    return window.localStorage.getItem(key);
  } catch (error) {
    log.debug(
      `[Vote] Unable to get ${key} from storage due to error:`,
      error.message
    );
  }
}

function localStorageSetItem(key: any, value: any) {
  try {
    return window.localStorage.setItem(key, value);
  } catch (error) {
    log.debug(
      `[Vote] Unable to set ${key}: ${value} in storage due to error:`,
      error.message
    );
  }
}

function localStorageRemoveItem(key: any) {
  try {
    return window.localStorage.removeItem(key);
  } catch (error) {
    log.debug(
      `[Vote] Unable to remove ${key} from storage due to error:`,
      error.message
    );
  }
}

const EMPTY_LEADER_INFO = { id: null, time: null };

function createLocalStorageVote({
  id = random(),
  getItem = localStorageGetItem,
  setItem = localStorageSetItem,
  remove = localStorageRemoveItem,
  electionDuration = ELECTION_DURATION,
  maxLeaderAge = MAX_LEADER_AGE,
  minLeaderRefreshDuration = MIN_LEADER_REFRESH_DURATION,
}) {
  function isLeader(now = Date.now()) {
    return getLeaderInfo(now).id === id;
  }

  function getLeaderInfo(now: any) {
    try {
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'string | null | undefined' is no... Remove this comment to see the full error message
      const { id: leaderID, time } = JSON.parse(getItem(LEADER_STORAGE_KEY));
      return now - time < maxLeaderAge
        ? { id: leaderID, time }
        : EMPTY_LEADER_INFO;
    } catch (error) {
      return EMPTY_LEADER_INFO;
    }
  }

  let electionTimeout: any;
  let wasLeader = false;

  function scheduleElection() {
    window.clearTimeout(electionTimeout);

    electionTimeout = window.setTimeout(() => {
      const now = Date.now();
      const { id: leaderID } = getLeaderInfo(now);

      if (leaderID) {
        const isNowLeader = leaderID === id;
        const didLeaderStatusChange = isNowLeader !== wasLeader;

        if (didLeaderStatusChange) {
          log.debug(
            isNowLeader ? `[Vote:${id}] Elected` : `[Vote:${id}] Unelected`
          );

          // @ts-expect-error ts-migrate(2349) FIXME: This expression is not callable.
          callbacks[LEADER_STATUS_CHANGED].forEach((fn) => fn(isNowLeader));
        }

        wasLeader = isNowLeader;
      } else {
        log.debug(`[Vote:${id}] Election ended with no leader found`);

        // If the old leader left, try to become leader
        up(now);
      }
    }, electionDuration);
  }

  function down(now = Date.now()) {
    if (isLeader(now)) {
      log.debug(`[Vote:${id}] Downvote, stepping down as leader`);
      remove(LEADER_STORAGE_KEY);
    } else {
      log.debug(`[Vote:${id}] Downvote, chilling since someone else is leader`);
    }

    scheduleElection();
  }

  function up(now = Date.now()) {
    // Everybody race to be leader
    const { id: leaderID, time } = getLeaderInfo(now);

    if (leaderID === id && now - time > minLeaderRefreshDuration) {
      log.debug(`[Vote:${id}] Upvote, refreshing my leader time`);
      setItem(LEADER_STORAGE_KEY, JSON.stringify({ time: now, id }));
    } else if (leaderID === id) {
      log.debug(`[Vote:${id}] Upvote, chilling since leader time is fresh`);
    } else if (leaderID) {
      log.debug(`[Vote:${id}] Upvote, someone else is leader`);
    } else {
      log.debug(`[Vote:${id}] Upvote, becoming leader`);
      setItem(LEADER_STORAGE_KEY, JSON.stringify({ time: now, id }));
    }

    scheduleElection();
  }

  function on(eventName: any, fn: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    callbacks[eventName].push(fn);
  }

  function off(eventName: any, fn: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    callbacks[eventName] = callbacks[eventName].filter(
      (_fn: any) => _fn !== fn
    );
  }

  // Setup internal state
  const callbacks = { [LEADER_STATUS_CHANGED]: [] };

  return {
    on,
    off,
    isLeader,
    up,
    down,
  };
}

export default createLocalStorageVote;
