/* eslint-plugin-disable @typescript-eslint */

import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function removeDeviceCommand(options: any) {
  const device = options.device;

  function remove() {
    const xhrDeferred = device.destroy({ wait: true });

    xhrDeferred.fail(function () {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem removing this device.'),
      });
    });

    xhrDeferred.always(function () {
      confirmModal.hide();
    });

    return xhrDeferred.promise();
  }

  const message = t(
    'If you remove this device, you will need to sign in again on the device to restore access to Plex.'
  );

  const confirmModal = new ConfirmModal({
    title: t('Remove Device'),
    message: message,
    confirmLabel: t('Remove'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  let promise = confirmModal.promise;
  promise = promise.then(function () {
    return remove();
  });

  dispatcher.trigger('show:modal', confirmModal);

  return promise;
}

export default removeDeviceCommand;
