import React from 'react';
import * as grabOperationStatuses from 'app/js/utils/constants/mediaGrabOperationStatuses';
import t from 'app/js/utils/t';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import getDownloadsPageRoute from 'app/js/views/v3/helpers/routes/getDownloadsPageRoute';
import ActivityMenuStatusItem from './ActivityMenuStatusItem';
import styles from './NavBarEmbeddedServerActivityMenuStatusItem.css';

type NavBarEmbeddedServerActivityMenuStatusItemProps = {
  activities: any[];
  onMenuClose?: (...args: any[]) => any;
};

function NavBarEmbeddedServerActivityMenuStatusItem(
  props: NavBarEmbeddedServerActivityMenuStatusItemProps
) {
  const { activities, onMenuClose } = props;

  return activities.length ? (
    <div className={styles.statusContainer}>
      <div className={styles.statusTitle}>
        <Link to={getDownloadsPageRoute()} onPress={onMenuClose}>
          {t('Downloads')}
        </Link>
      </div>
      {activities.map((activity) => {
        const { uuid, title, subtitle, progress, Context = {} } = activity;

        return (
          <ActivityMenuStatusItem
            key={uuid}
            isPostProcessing={
              Context.status === grabOperationStatuses.POST_PROCESSING
            }
            progress={progress}
            subtitle={subtitle}
            title={title}
          />
        );
      })}
    </div>
  ) : null;
}

export default NavBarEmbeddedServerActivityMenuStatusItem;
