import _ from 'underscore';
import FilePathListModel from 'app/js/models/library/FilePathListModel';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import xhrTracker from 'app/js/utils/xhr/xhrTracker';
import FileList from 'app/js/views/lists/server/FileList';
import FilePathList from 'app/js/views/lists/server/FilePathList';
import BaseModal from 'app/js/views/modals/BaseModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/AddFolderModal.tpl';

const AddFolderModal = BaseModal.extend({
  //
  // Properties

  name: 'addFolderModal',
  className: 'add-folder-modal modal fade',
  template: tpl,

  events: _.extend(
    {
      'submit form': 'onSubmitForm',
    },
    BaseModal.prototype.events
  ),

  ui: {
    path: 'input[name="path"]',
  },

  regions: {
    rootList: '.home-directory-container',
    childrenList: '.path-container',
    childrenFileList: '.file-container',
  },

  //
  // Init

  initialize: function (options: any) {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.initialize.apply(this, arguments);

    this.rootPathList = new FilePathListModel(
      {},
      { server: options.server, includeFiles: 1 }
    );
    this.childrenPathList = new FilePathListModel(
      {},
      { server: options.server, includeFiles: 1 }
    );

    this.loadRootPaths();
  },

  //
  // Control

  loadRootPaths: function () {
    this.rootPathList.fetch().done(
      this.whenRendered(function () {
        // Start by loading the children of the first home path.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        const homePaths = this.rootPathList.get('paths').where({ home: true });
        const homePath =
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          _.first(homePaths) || this.rootPathList.get('paths').first();

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.rootList.currentView.startLoadingItem(homePath);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.loadChildrenPaths(homePath);
      })
    );
  },

  loadChildrenPaths: function (filePath: any) {
    xhrTracker.abortAll('addFolderModal');

    const children = filePath.get('children');
    const promise = children.fetch({ scope: 'addFolderModal' });

    promise.done(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.path.val(filePath.get('path'));

        // Keep the collection instance the same, but reset it with the
        // children's models so we don't have to re-render the list.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.childrenPathList.get('paths').reset(children.get('paths').models);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.childrenPathList.get('files').reset(children.get('files').models);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.childrenPathList.set(
          'truncatedFilesCount',
          children.get('truncatedFilesCount')
        );
      })
    );

    promise.fail(
      this.whenRendered(function () {
        // If this was a child path's children being loaded, hide the spinner
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.childrenList.currentView.unselectItem(filePath);

        dispatcher.trigger('show:alert', {
          type: 'error',
          message: t("Unable to load contents of '{1}'", filePath.get('path')),
        });
      })
    );

    promise.always(
      this.whenRendered(function () {
        // If this was a root path's children being loaded, hide the spinner.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.rootList.currentView.stopLoadingItem(filePath);
      })
    );
  },

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    BaseModal.prototype.onRender.apply(this, arguments);

    const rootListView = new FilePathList({
      isRoot: true,
      collection: this.rootPathList.get('paths'),
    });
    const childrenListView = new FilePathList({
      collection: this.childrenPathList.get('paths'),
    });
    const childrenFileListView = new FileList({
      model: this.childrenPathList,
      collection: this.childrenPathList.get('files'),
    });

    this.listenTo(rootListView, 'change:path', this.onRootPathChange);
    this.listenTo(childrenListView, 'change:path', this.onChildrenPathChange);

    this.rootList.show(rootListView);
    this.childrenList.show(childrenListView);
    this.childrenFileList.show(childrenFileListView);
  },

  onRootPathChange: function (filePath: any) {
    // Reset the parent file path since the root has changed and there
    // should be no back item.
    this.childrenList.currentView.parentFilePath = null;

    this.rootList.currentView.startLoadingItem(filePath);
    this.loadChildrenPaths(filePath);
  },

  onChildrenPathChange: function (filePath: any) {
    this.childrenList.currentView.startLoadingItem(filePath);
    this.loadChildrenPaths(filePath);
  },

  onSubmitForm: function (event: any) {
    event.preventDefault();

    this.trigger('add:folder', this.ui.path.val());
    this.hide();
  },
});

export default AddFolderModal;
