/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import qs from 'qs';
import _ from 'underscore';
import minDuration from 'app/js/utils/promise/minDuration';

function updateWebhooks(webhooks: any, urls: any) {
  const cloudServer = webhooks.server;
  const xhrOptions = cloudServer.getXHROptions(_.result(webhooks, 'url'), {
    method: 'POST',
    dataType: 'json',
    data: qs.stringify({ urls }, { arrayFormat: 'brackets' }) || 'urls=',
  });

  return minDuration($.ajax(xhrOptions), 400).then(
    (json) => {
      webhooks.reset(json);
    },
    (xhr) => {
      return $.Deferred()
        .reject((xhr.status && xhr.responseJSON.errors) || [])
        .promise();
    }
  );
}

export default updateWebhooks;
