import React, { ReactElement, ReactNode } from 'react';
import t from 'app/js/utils/t';
import ErrorPageContent from './ErrorPageContent';

interface UnexpectedErrorPageContentProps {
  children?: ReactNode;
}

export default function UnexpectedErrorPageContent({
  children,
}: UnexpectedErrorPageContentProps): ReactElement {
  return (
    <ErrorPageContent
      description={t('An unexpected error occurred.')}
      title={t('Something went wrong')}
    >
      {children}
    </ErrorPageContent>
  );
}
