/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import MetadataListModel from 'app/js/models/metadata/MetadataListModel';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import {
  navigate,
  navigateBack,
  navigatePrePlay,
} from 'app/js/stores/actions/appActions';
import getSourceDirectoryFromProviderModel from 'app/js/utils/provider/getSourceDirectoryFromProviderModel';
import getSourceRoute from 'app/js/views/v3/helpers/routes/getSourceRoute';

function deleteItemCommand(options: any) {
  const promise = commands.execute('deleteFile', options);

  promise.done(function () {
    const item = options.item;

    // Attempt to navigate back to either a parent item or a section now
    // that this item doesn't exist.

    const parent = getParentFromCache(item.get('parentKey'), options);
    if (!parent) {
      navigateToSection(options);
      return;
    }

    const siblingsListModel = new MetadataListModel(
      { containerKey: parent.get('childrenKey') },
      {
        server: parent.server,
        provider: parent.provider,
        skipCache: true,
      }
    );

    siblingsListModel.fetchRange({ size: 1 }).then(() => {
      // If there are still siblings in its collection, we know the parent
      // still exists and we can navigate to the parent.

      if (siblingsListModel.getCollection().length > 0) {
        navigateToDetails(parent, options);
        return;
      }

      // If the last child was deleted, the parent will automatically be
      // destroyed, so we can try to navigate to the grandparent.

      parent.trigger('destroy', parent, parent.collection);

      const grandparent = getParentFromCache(
        item.get('grandparentKey'),
        options
      );
      if (!grandparent) {
        navigateToSection({
          ...options,
          skipRedirect: false,
        });
        return;
      }

      const parentsListModel = new MetadataListModel(
        { containerKey: grandparent.get('childrenKey') },
        {
          server: grandparent.server,
          provider: grandparent.provider,
          skipCache: true,
        }
      );

      parentsListModel.fetchRange({ size: 1 }).then(() => {
        if (parentsListModel.getCollection().length > 0) {
          navigateToDetails(grandparent, {
            ...options,
            skipRedirect: false,
          });
        } else {
          navigateToSection(options);
        }
      });
    });
  });

  return promise;
}

function getParentFromCache(key: any, options: any) {
  if (!key) {
    return false;
  }

  const parent = new MetadataModel(
    { key: key },
    {
      server: options.item.server,
      onlyIfCached: true,
    }
  );

  if (_.isEmpty(parent)) {
    return false;
  }

  return parent;
}

function navigateToSection(options: any) {
  if (options.skipRedirect) {
    return;
  }

  const item = options.item;
  const container = item.get('isLibraryItem') && item.parent;
  const librarySectionKey =
    item.get('librarySectionKey') || container?.get('librarySectionKey');

  if (!librarySectionKey) {
    resolve('appStore').dispatch(navigateBack());

    return;
  }

  const sourceDirectory = getSourceDirectoryFromProviderModel(
    item.provider,
    librarySectionKey
  );

  const route = getSourceRoute(
    item.server.toJSON(),
    item.provider.toJSON(),
    sourceDirectory
  );

  if (route) {
    resolve('appStore').dispatch(
      navigate({
        pathname: route,
        replace: true,
      })
    );
  } else {
    resolve('appStore').dispatch(navigateBack());
  }
}

function navigateToDetails(model: any, options: any) {
  if (options.skipRedirect) {
    return;
  }

  resolve('appStore').dispatch(
    navigatePrePlay({
      serverEntityID: model.server.entityID(),
      providerEntityID: model.provider.entityID(),
      key: model.get('key'),
      replace: true,
    })
  );
}

export default deleteItemCommand;
