import { SagaIterator, Task } from 'redux-saga';
import {
  ActionPattern,
  fork,
  ForkEffect,
  join,
  takeEvery,
} from 'redux-saga/effects';
import { PayloadAction } from '@reduxjs/toolkit';

type TasksRecord = Record<string, Task>;

export default function takeLeadingPerID<
  Action extends PayloadAction<object>,
  Fn extends (action: Action, ...workerArgs: any[]) => SagaIterator
>(
  pattern: ActionPattern<Action>,
  worker: Fn,
  idSelector: (action: Action) => keyof TasksRecord,
  ...args: any[]
): ForkEffect {
  return fork(function* () {
    const tasks: TasksRecord = {};

    yield takeEvery<Action>(pattern, function* (action) {
      const id = idSelector(action);

      if (!tasks[id]?.isRunning()) {
        tasks[id] = yield fork(
          worker as (action: Action, ...workerArgs: any[]) => any,
          action,
          ...args
        );

        yield join(tasks[id]);

        if (tasks[id] && !tasks[id].isRunning()) {
          delete tasks[id];
        }
      }
    });
  });
}
