/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';

export function tryFindEntity(model: any, entityType: any, entityID: any) {
  const entities = model.get('entities').get(entityType);

  if (entities) {
    const entity = entities.get(entityID);

    if (entity) {
      return entity.get('model');
    }
  }
}

export default function findEntity(model: any, entityType: any, entityID: any) {
  const entity = tryFindEntity(model, entityType, entityID);

  if (!entity) {
    log.warn(
      'An entity does not exist with the type "%s" and id "%s"',
      entityType,
      entityID
    );
  }

  return entity;
}
