/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import preserveMergeStrategy from 'app/js/models/preserveMergeStrategy';
import * as responseTypes from 'app/js/models/responseTypes';
import path from 'app/js/utils/url/path';
import LyricsLineCollection from './LyricsLineCollection';

const LyricsStreamModel = AbstractModel.extend({
  //
  // Properties

  name: 'LyricsStreamModel',
  context: ['server', 'provider', 'section'],

  responseType() {
    /** At this moment PMS doesn't support lyrics as JSON, so we need
     * to change our request based on the current server context.
     * @see: https://github.com/plexinc/plex-media-server/issues/12851
     */
    if (this.server?.get('isCloud')) {
      return responseTypes.json;
    }
    return responseTypes.xml;
  },

  rootPropertyName: 'Lyrics',

  mergeStrategy: preserveMergeStrategy,

  url() {
    /** At this moment PMS doesn't support lyrics as JSON, so we need
     * to change our request based on the current server context.
     * @see: https://github.com/plexinc/plex-media-server/issues/12851
     */
    if (this.server?.get('isCloud')) {
      return this.get('key');
    }
    return path.join(this.get('key'), { format: 'xml' });
  },

  sessionAttrs: {
    follow: attrTypes.boolean,
    lyricsError: attrTypes.object,
  },

  syncedAttrs: {
    id: attrTypes.string,
    key: attrTypes.string,
    streamType: attrTypes.integer,
    index: attrTypes.integer,
    title: attrTypes.string,
    selected: attrTypes.boolean,
    codec: attrTypes.string,
    provider: attrTypes.string,
    format: attrTypes.string,
    minLines: attrTypes.integer,
    timed: attrTypes.boolean,
  },

  syncedRelations: {
    lines: {
      relationClass: LyricsLineCollection,
      rootPropertyName: 'Line',
    },
  },

  defaults: {
    follow: true,
    selected: false,
    lyricsError: null,
  },
});

export default LyricsStreamModel;
