import { ChannelMapping } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { normalizeAsBoolean } from 'app/js/v4/utils/data/normalizers/normalizeUtils';

export default function normalizeChannelMappingResponse(
  channelMappings: ChannelMapping[] | undefined
): ChannelMapping[] {
  const channelMappingResponse = channelMappings ?? [];

  return channelMappingResponse.map((channelMapping) => {
    // `ChannelMapping` elements returned by the server don't always have an
    // `enabled` attribute. Use the attribute when it is available, otherwise
    // determine it by checking for the presence of `lineupIdentifier`.
    if (channelMapping.enabled) {
      normalizeAsBoolean(channelMapping, 'enabled');

      return channelMapping;
    }

    return {
      ...channelMapping,
      enabled: !!channelMapping.lineupIdentifier,
    };
  });
}
