/* eslint-plugin-disable @typescript-eslint */

// A 'month' is defined as 30 days
export const DAYS_PER_MONTH = 30;
// A 'year' is defined as 365 days
export const DAYS_PER_YEAR = 365;

export const MILLISECOND = 1;
export const SECOND = MILLISECOND * 1000;
export const MINUTE = SECOND * 60;
export const HOUR = MINUTE * 60;
export const DAY = HOUR * 24;
export const MONTH = DAY * DAYS_PER_MONTH;
export const YEAR = DAY * DAYS_PER_YEAR;

const precedent = [
  'years',
  'months',
  'days',
  'hours',
  'minutes',
  'seconds',
  'milliseconds',
];

const divisors = {
  years: YEAR,
  months: MONTH,
  days: DAY,
  hours: HOUR,
  minutes: MINUTE,
  seconds: SECOND,
  milliseconds: MILLISECOND,
};

export default function durationOf(milliseconds: any) {
  // If milliseconds is negative then invert the value to ensure that the
  // divisor and dividends below have the same sign. The returned value is also
  // inverted to regain its correct sign.
  const sign = milliseconds < 0 ? -1 : 1;
  const result = {};

  let nextMilliseconds = milliseconds * sign;
  let i = -1;

  while (++i < precedent.length) {
    const unit = precedent[i];
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const divisor = divisors[unit];
    const quotient = Math.floor(nextMilliseconds / divisor);

    // Avoids signing a quotient of 0.
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    result[unit] = quotient && quotient * sign;

    nextMilliseconds = nextMilliseconds % divisor;
  }

  return result;
}
