/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import EntityModel from './EntityModel';

const EntityCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'EntityCollection',
  model: EntityModel,

  //
  // Control

  toState() {
    // This collection's state uses an object instead of an array since order is
    // not important and lookups are O(1).

    const state = this.reduce(this.reduceState, {
      possibleEntityContainers: [],
    });

    Object.defineProperty(state, 'possibleEntityContainers', {
      enumerable: false,
    });

    return state;
  },

  reduceState(acc: any, entity: any) {
    // Keep track of these ids to speed up garbage collection of entities.
    acc.possibleEntityContainers.push(entity.id);
    acc[entity.id] = entity.getState();
    return acc;
  },
});

export default EntityCollection;
