import { unescape } from 'lodash';

interface UnescapableItem {
  friendlyName?: string;
  title?: string;
}

export function unescapeItem<T extends UnescapableItem>(item: T): T {
  return {
    ...item,
    friendlyName: item.friendlyName && unescape(item.friendlyName),
    title: item.title && unescape(item.title),
  };
}

export function unescapeArray<T extends UnescapableItem>(array: T[]): T[] {
  return array.map((item) => unescapeItem(item));
}
