/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';

function enableVersionGeneratorCommand(options: any) {
  const generator = options.generator;
  const url = _.result(generator, 'url') + '/' + options.ratingKey + '/enable';

  const xhrOptions = _.extend(generator.server.getXHROptions(url), {
    type: 'PUT',
  });
  const promise = $.ajax(xhrOptions).promise();

  // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
  promise.fail(function (xhr) {
    dispatcher.trigger('show:alert', {
      type: 'error',
      message: t('There was a problem enabling this version.'),
    });
  });

  return promise;
}

export default enableVersionGeneratorCommand;
