/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import Obscura from 'app/js/libs/backbone/backbone.obscura-0.1.7';
import InviteModel from './InviteModel';

const InviteCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'InviteCollection',

  context: ['server', 'subset'],

  model: InviteModel,

  url() {
    return '/api/invites/' + this.subset;
  },

  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'models' is declared but its value is never read.
  initialize(models: any, options: any) {
    options || (options = {});

    this.subset = options.subset || 'requests';
  },

  //
  // Control

  filterByHome() {
    const proxy = new Obscura(this);
    proxy.filterBy((invite: any) => invite.get('home'));

    return proxy;
  },

  filterByFriends() {
    const proxy = new Obscura(this);
    proxy.filterBy((invite: any) => !invite.get('home'));

    return proxy;
  },
});

export default InviteCollection;
