/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import selectStream from 'app/js/utils/media/selectStream';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';

function selectStreamCommand(options: any) {
  const deferred = $.Deferred();
  const item = options.item;
  const server = item.server;
  const media = options.media || _.first(item.get('Media'));
  const part = options.part || (media && _.first(media.Part));
  const streamType = options.streamType;
  const streamID = options.streamID;
  if (!media || !part) {
    return deferred.reject().promise();
  }
  const params = {};
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  params[streamType.key] = streamID;
  // When selecting a stream without a specific media item (e.g. pre-play),
  // the `allParts` param will instruct the the server to perform stream
  // selection across all media items in the metadata item.
  if (!options.media && server.supports('allPartsStreamSelection')) {
    (params as any).allParts = 1;
  }
  const url = path.join(['/library/parts/', part.id], params);
  const xhrOptions = _.extend(server.getXHROptions(url), { type: 'PUT' });
  const xhrDeferred = $.ajax(xhrOptions);
  xhrDeferred.done(function () {
    // Update our streams to reflect the new selected stream.
    selectStream(media, streamType, streamID);
    // Trigger a custom event since anything we change within Media won't
    // automatically trigger a change event.
    item.trigger('change:streams');
    deferred.resolve();
  });
  xhrDeferred.fail(function (xhr) {
    dispatcher.trigger('show:alert', {
      type: 'error',
      message: t('There was a problem selecting the stream for this item.'),
    });
    deferred.reject(xhr);
  });
  return deferred.promise();
}
export default selectStreamCommand;
