/* eslint-plugin-disable @typescript-eslint */

import createTypes from './createTypes';

export const NONE = 'none';
export const VIDEO = 'video';
export const AUDIO = 'audio';
export const PHOTO = 'photo';
// This media type is unknown to PMS.
export const ROM = 'rom';

const identifiers = ['id', 'typeString'];

const mediaTypes = createTypes(identifiers, [
  {
    id: 0,
    typeString: NONE,
  },
  {
    id: 1,
    typeString: VIDEO,
  },
  {
    id: 2,
    typeString: AUDIO,
  },
  {
    id: 3,
    typeString: PHOTO,
  },
  {
    // This media type is unknown to PMS.
    id: 999,
    typeString: ROM,
  },
]);

export const getMediaType = mediaTypes.get;
export const getAllMediaTypes = mediaTypes.getAll;
