import config from 'app/js/models/config';
import httpFallbackOptions from 'app/js/utils/constants/httpFallbackOptions';
import capabilities from 'app/js/utils/device/capabilities';

export default function canServerAllowHttpFallback(
  server: any,
  allowHttpFallbackSetting: any
) {
  // Do not allow plex.tv or nano servers to fallback.
  if (!server.get('isFullServer')) {
    return false;
  }

  // Only allow mixed content if specified by the config option.
  if (capabilities.isHostedHttps && !config.allowMixedContent) {
    return false;
  }

  const hasAllowedAllFallback =
    allowHttpFallbackSetting === httpFallbackOptions.ALWAYS;
  const hasAllowedSameNetworkFallback =
    allowHttpFallbackSetting === httpFallbackOptions.SAME_NETWORK;
  const allowHttpFallback =
    hasAllowedAllFallback ||
    (hasAllowedSameNetworkFallback && server.get('publicAddressMatches'));

  return !!allowHttpFallback;
}
