import { SagaIterator } from 'redux-saga';
import {
  all,
  call,
  fork,
  put,
  takeEvery,
  takeLatest,
} from 'redux-saga/effects';
import getIdentifiersFromSource from 'app/js/utils/metadata/getIdentifiersFromSource';
import stringifyBoolean from 'app/js/utils/parser/stringifyBoolean';
import path from 'app/js/utils/url/path';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import libraryManagementSlice from 'app/js/v4/features/libraryManagement/libraryManagementSlice';
import {
  HubOptions,
  HubPromotions,
  HubsFetchRequestedAction,
  PromoteRequestedAction,
  PublishedFetchRequestedAction,
  PublishedUpdateRequestedAction,
  RemoveRequestedAction,
  ReorderRequestedAction,
  ResetRequestedAction,
  SettingsFetchRequestedAction,
  SettingsUpdateRequestedAction,
} from 'app/js/v4/features/libraryManagement/libraryManagementTypes';
import getHubManagementKey from 'app/js/v4/features/libraryManagement/utils/getHubManagementKey';
import api, { APIResponse } from 'app/js/v4/utils/data/api';

const MERGE_RECENTLY_ADDED = 'MergedRecentlyAdded';

//
// Workers

export function* fetchSettings({
  payload,
}: SettingsFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const response: APIResponse<{
      MediaContainer: {
        Setting?: { value: boolean }[];
      };
    }> = yield call(api.get, ':/prefs/get', {
      server,
      params: {
        id: MERGE_RECENTLY_ADDED,
      },
    });

    const isMergeRecentlyAddedEnabled =
      response.data.MediaContainer.Setting?.[0]?.value;

    yield put(
      libraryManagementSlice.actions.settingsFetchSucceeded({
        server,
        isMergeRecentlyAddedEnabled,
      })
    );
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.settingsFetchFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* updateSettings({
  payload,
}: SettingsUpdateRequestedAction): SagaIterator {
  const { server, isMergeRecentlyAddedEnabled } = payload;

  try {
    yield call(api.put, ':/prefs', {
      server,
      params: {
        [MERGE_RECENTLY_ADDED]: isMergeRecentlyAddedEnabled,
      },
    });

    yield put(
      libraryManagementSlice.actions.settingsUpdateSucceeded({
        server,
        isMergeRecentlyAddedEnabled,
      })
    );

    yield put(libraryManagementSlice.actions.resetHubs());
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.settingsUpdateFailed({
        server,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* fetchHubs({
  payload,
}: HubsFetchRequestedAction): SagaIterator {
  const { server, sourceURI } = payload;

  try {
    const options = yield call(fetchLatestHubs, server, sourceURI);

    yield put(
      libraryManagementSlice.actions.hubsFetchSucceeded({
        sourceURI,
        options,
      })
    );
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.hubsFetchFailed({
        sourceURI,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* reorderHubs({
  payload,
}: ReorderRequestedAction): SagaIterator {
  const { server, sourceURI, options, fromIndex, toIndex } = payload;

  try {
    // Request the appropriate change from PMS based on most recent state
    const identifier = options[toIndex].identifier;
    const afterIdentifier = options.find(
      (_value, index) => index === toIndex - 1
    )?.identifier;

    const { key } = getIdentifiersFromSource(sourceURI);

    yield call(api.put, path.join([key, identifier, 'move']), {
      server,
      params: { after: afterIdentifier },
    });

    yield put(
      libraryManagementSlice.actions.reorderSucceeded({
        sourceURI,
      })
    );

    yield put(libraryManagementSlice.actions.resetHubs());
  } catch (error) {
    // Revert the options array
    const oldOptions = Array.from(options);
    const [removed] = oldOptions.splice(toIndex, 1);
    oldOptions.splice(fromIndex, 0, removed);

    yield put(
      libraryManagementSlice.actions.reorderFailed({
        sourceURI,
        options: oldOptions,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* removeHub({ payload }: RemoveRequestedAction): SagaIterator {
  const { server, sourceURI, options, removed, index } = payload;

  try {
    const identifier = removed.identifier;
    const { key } = getIdentifiersFromSource(sourceURI);

    yield call(api.del, path.join([key, identifier]), {
      server,
    });

    yield put(
      libraryManagementSlice.actions.removeSucceeded({
        sourceURI,
      })
    );

    yield put(libraryManagementSlice.actions.resetHubs());
  } catch (error) {
    // Revert the options array
    const oldOptions = Array.from(options);
    oldOptions.splice(index, 0, removed);

    yield put(
      libraryManagementSlice.actions.removeFailed({
        sourceURI,
        options: oldOptions,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* resetHubs({ payload }: ResetRequestedAction): SagaIterator {
  const { server, sourceURI } = payload;

  try {
    const { key } = getIdentifiersFromSource(sourceURI);

    yield call(api.del, key, {
      server,
    });

    const options = yield call(fetchLatestHubs, server, sourceURI);

    yield put(
      libraryManagementSlice.actions.resetSucceeded({
        sourceURI,
        options,
      })
    );

    yield put(libraryManagementSlice.actions.resetHubs());
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.resetFailed({
        sourceURI,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* promoteHub({ payload }: PromoteRequestedAction): SagaIterator {
  const { server, sourceURI, options, promoted } = payload;

  try {
    const newOptions = {
      ...options,
      ...promoted,
    };

    yield put(
      libraryManagementSlice.actions.promoteChanged({
        sourceURI,
        options: newOptions,
      })
    );

    const { key } = getIdentifiersFromSource(sourceURI);

    yield call(api.put, path.join([key, newOptions.identifier]), {
      server,
      params: {
        promotedToRecommended: stringifyBoolean(
          newOptions.promotedToRecommended
        ),
        promotedToOwnHome: stringifyBoolean(newOptions.promotedToOwnHome),
        promotedToSharedHome: stringifyBoolean(newOptions.promotedToSharedHome),
      },
    });

    yield put(libraryManagementSlice.actions.resetHubs());
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.promoteChanged({
        sourceURI,
        options,
      })
    );
  }
}

export function* fetchPublishedItem({
  payload,
}: PublishedFetchRequestedAction): SagaIterator {
  const { server, sourceURI, contentDirectoryID } = payload;

  try {
    const { ratingKey } = getIdentifiersFromSource(sourceURI);

    const hubsResponse: APIResponse<{
      MediaContainer: {
        Hub?: HubPromotions[];
      };
    }> = yield call(api.get, getHubManagementKey(contentDirectoryID), {
      server,
      params: {
        metadataItemId: ratingKey,
      },
    });

    yield put(
      libraryManagementSlice.actions.publishedFetchSucceeded({
        sourceURI,
        hub: hubsResponse.data.MediaContainer.Hub?.[0],
      })
    );
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.publishedFetchFailed({
        sourceURI,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* updatePublishedItem({
  payload,
}: PublishedUpdateRequestedAction): SagaIterator {
  const { server, sourceURI, contentDirectoryID, hub } = payload;

  try {
    const { ratingKey } = getIdentifiersFromSource(sourceURI);

    const hubsResponse: APIResponse<{
      MediaContainer: {
        Hub: HubPromotions[];
      };
    }> = yield call(api.post, getHubManagementKey(contentDirectoryID), {
      server,
      params: {
        metadataItemId: ratingKey,
        promotedToRecommended: stringifyBoolean(hub.promotedToRecommended),
        promotedToOwnHome: stringifyBoolean(hub.promotedToOwnHome),
        promotedToSharedHome: stringifyBoolean(hub.promotedToSharedHome),
      },
    });

    yield put(
      libraryManagementSlice.actions.publishedUpdateSucceeded({
        sourceURI,
        hub: hubsResponse.data.MediaContainer.Hub?.[0],
      })
    );

    yield put(libraryManagementSlice.actions.resetHubs());
  } catch (error) {
    yield put(
      libraryManagementSlice.actions.publishedUpdateFailed({
        sourceURI,
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Helper Workers

function* fetchLatestHubs(server: Server, sourceURI: string): SagaIterator {
  const { key } = getIdentifiersFromSource(sourceURI);

  const hubsResponse: APIResponse<{
    MediaContainer: {
      Hub: HubOptions[];
    };
  }> = yield call(api.get, key, {
    server,
  });

  return hubsResponse.data.MediaContainer.Hub;
}

//
// Watchers

function* watchSettingsFetchRequested(): SagaIterator {
  yield takeLatest(
    libraryManagementSlice.actions.settingsFetchRequested,
    fetchSettings
  );
}

function* watchSettingsUpdateRequested(): SagaIterator {
  yield takeLatest(
    libraryManagementSlice.actions.settingsUpdateRequested,
    updateSettings
  );
}

function* watchHubsFetchRequested(): SagaIterator {
  yield takeLatest(
    libraryManagementSlice.actions.hubsFetchRequested,
    fetchHubs
  );
}

function* watchReorderRequested(): SagaIterator {
  yield takeEvery(libraryManagementSlice.actions.reorderRequested, reorderHubs);
}

function* watchRemoveRequested(): SagaIterator {
  yield takeEvery(libraryManagementSlice.actions.removeRequested, removeHub);
}

function* watchResetRequested(): SagaIterator {
  yield takeLatest(libraryManagementSlice.actions.resetRequested, resetHubs);
}

function* watchPromoteRequested(): SagaIterator {
  yield takeEvery(libraryManagementSlice.actions.promoteRequested, promoteHub);
}

function* watchPublishedFetchRequested(): SagaIterator {
  yield takeLatest(
    libraryManagementSlice.actions.publishedFetchRequested,
    fetchPublishedItem
  );
}

function* watchPublishedUpdateRequested(): SagaIterator {
  yield takeEvery(
    libraryManagementSlice.actions.publishedUpdateRequested,
    updatePublishedItem
  );
}

export default function* libraryManagementSaga(): SagaIterator {
  yield all([
    fork(watchSettingsFetchRequested),
    fork(watchSettingsUpdateRequested),
    fork(watchHubsFetchRequested),
    fork(watchReorderRequested),
    fork(watchRemoveRequested),
    fork(watchResetRequested),
    fork(watchPromoteRequested),
    fork(watchPublishedFetchRequested),
    fork(watchPublishedUpdateRequested),
  ]);
}
