export interface DeviceSettings {
  volume: number;
}

export enum MediaSettingType {
  Int = 'int',
  Text = 'text',
  Bool = 'bool',
  Enum = 'enum',
  Double = 'double',
}

export interface MediaSettingResponse {
  id: string;
  advanced?: string;
  default?: boolean | number | string;
  hidden?: string;
  group: string;
  label: string;
  summary: string;
  type: MediaSettingType;
  value?: boolean | number | string;
  min?: number;
  max?: number;
  step?: number;
  enumValues?: string;
}

export interface MediaSetting extends Omit<MediaSettingResponse, 'enumValues'> {
  enumValues?: {
    label: string;
    value?: boolean | number | string;
  }[];
}
