/* eslint-plugin-disable @typescript-eslint */

import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import t from 'app/js/utils/t';
import FileListItem from 'app/js/views/lists/server/FileListItem';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module './FileList.tpl' or its corresp... Remove this comment to see the full error message
import tpl from './FileList.tpl';

const FileList = Marionette.CompositeView.extend({
  //
  // Properties

  template: tpl,
  itemView: FileListItem,
  itemViewContainer: '.js-file-list-items',

  //
  // Bindings

  bindings: {
    '.js-file-list-truncated-message': {
      modelAttribute: 'truncatedFilesCount',

      formatter(truncatedFilesCount: any) {
        if (!truncatedFilesCount) {
          return null;
        }

        return t('…and {truncatedFilesCount} more', { truncatedFilesCount });
      },
    },
  },
});

export default FileList;
