import AbstractCollection from 'app/js/models/AbstractCollection';
import ChapterModel from './ChapterModel';

const ChapterCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ChapterCollection',
  context: ['server', 'section'],

  model: ChapterModel,

  modelId(attrs: any) {
    return `${attrs.id}_${attrs.index}`;
  },
});

export default ChapterCollection;
