/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { SUPPORT_URL } from 'app/js/utils/constants/commonUrls';
import t from 'app/js/utils/t';
import Menu from 'app/js/views/v3/components/core/menus/Menu';
import MenuHeader from 'app/js/views/v3/components/core/menus/MenuHeader';
import MenuItem from 'app/js/views/v3/components/core/menus/MenuItem';
import MenuSeparator from 'app/js/views/v3/components/core/menus/MenuSeparator';
import createChainedCallback from 'app/js/views/v3/helpers/createChainedCallback';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';

type NavBarAccountMenuProps = {
  isOpen: boolean;
  target: string;
  options?: any;
  username?: string;
  isSignedIn: boolean;
  isAnonymousUser: boolean;
  isFullUser: boolean;
  appsURL: string;
  isCloudServerConnected: boolean;
  hasHomeLibraryAccessLabels: boolean;
  onSignInPress: (...args: any[]) => any;
  onSignOutPress: (...args: any[]) => any;
  onMenuClose: (...args: any[]) => any;
};

function NavBarAccountMenu(props: NavBarAccountMenuProps) {
  const {
    isOpen,
    target,
    options,
    username,
    isSignedIn,
    isAnonymousUser,
    isFullUser,
    appsURL,
    isCloudServerConnected,
    onSignInPress,
    onSignOutPress,

    onMenuClose,
    hasHomeLibraryAccessLabels,
  } = props;

  const shouldShowSignIn =
    isCloudServerConnected && (!isSignedIn || isAnonymousUser);
  const shouldShowSignOut = isSignedIn && isFullUser && !isAnonymousUser;

  return (
    <Menu
      isOpen={isOpen}
      options={options}
      size={hasHomeLibraryAccessLabels ? sizes.LARGE : sizes.MEDIUM}
      target={target}
      onMenuClose={onMenuClose}
    >
      {username ? <MenuHeader>{username}</MenuHeader> : null}

      {isSignedIn ? <MenuSeparator /> : null}

      {isCloudServerConnected ? (
        <MenuItem
          target={__EXTERNAL_LINK_TARGET__}
          to={appsURL}
          onPress={onMenuClose}
        >
          {t('Get Plex Apps')}
        </MenuItem>
      ) : null}
      {isCloudServerConnected ? (
        <MenuItem
          target={__EXTERNAL_LINK_TARGET__}
          to={SUPPORT_URL}
          onPress={onMenuClose}
        >
          {t('Help')}
        </MenuItem>
      ) : null}

      <MenuSeparator />

      {shouldShowSignIn ? (
        <MenuItem onPress={createChainedCallback(onMenuClose, onSignInPress)}>
          {t('Sign In')}
        </MenuItem>
      ) : null}
      {shouldShowSignOut ? (
        <MenuItem onPress={createChainedCallback(onMenuClose, onSignOutPress)}>
          {t('Sign Out')}
        </MenuItem>
      ) : null}
    </Menu>
  );
}

export default NavBarAccountMenu;
