/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import proxyTypes from 'app/js/utils/constants/proxyTypes';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deleteAllVersionGeneratorsCommand(options: any) {
  const confirmModal = showConfirmation();
  let promise = confirmModal.promise;

  promise = promise.then(function () {
    const url = '/playlists/generators?type=' + proxyTypes.LOCAL;
    const xhrOptions = _.extend(options.server.getXHROptions(url), {
      type: 'DELETE',
    });
    const xhrPromise = $.ajax(xhrOptions).promise();

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    xhrPromise.fail(function (xhr) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('There was a problem deleting all versions.'),
      });
    });

    xhrPromise.always(function () {
      confirmModal.hide();
    });

    return xhrPromise.promise();
  });

  return promise;
}

function showConfirmation() {
  const confirmModal = new ConfirmModal({
    title: t('Delete All Versions'),
    message: t(
      'Are you sure you want to delete all versions? This cannot be undone.'
    ),
    confirmLabel: t('Delete'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  dispatcher.trigger('show:modal', confirmModal);

  return confirmModal;
}

export default deleteAllVersionGeneratorsCommand;
