import $ from 'jquery';
import _ from 'underscore';
import createTranslationInterpolator, {
  createEscapedTranslationInterpolator,
} from 'app/js/utils/createTranslationInterpolator';
import dispatcher from 'app/js/utils/dispatcher';
import languageFiles from 'app/js/utils/language/languageFiles';
import path from 'app/js/utils/url/path';

const localizerMixin = {
  //
  // Properties
  // List of all available localizations. Call `getLanguage()` to obtain a
  // the list of all supported localizations.
  languages: languageFiles,
  loadingPromise: null,
  loadingLanguage: null,
  //
  // Control
  // Provides list of all supported localizations.
  getLanguages: function () {
    return this.languages;
  },
  // @ts-expect-error ts-migrate(7023) FIXME: 'getTranslation' implicitly has return type 'any' ... Remove this comment to see the full error message
  getTranslation: function (key: any) {
    // @ts-expect-error ts-migrate(2551) FIXME: Property 'translations' does not exist on type '{ ... Remove this comment to see the full error message
    if (this.translations[this.language]) {
      // @ts-expect-error ts-migrate(2551) FIXME: Property 'translations' does not exist on type '{ ... Remove this comment to see the full error message
      return this.translations[this.language][key];
    }
  },
  t: createTranslationInterpolator(function (key: any, ...args: any[]) {
    // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
    return this.getTranslation(key, ...args);
  }),
  tEscape: createEscapedTranslationInterpolator(function (
    key: any,
    ...args: any[]
  ) {
    // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
    return this.getTranslation(key, ...args);
  }),
  load: function (language: any) {
    if (this.loadingLanguage && this.loadingLanguage === language) {
      // Do not make multiple requests for the same language.
      return this.loadingPromise;
    }
    const languagesNow = this.getLanguages();
    let promise;
    if (!_.has(languagesNow, language)) {
      // Only allow predefined languages to be loaded.
      promise = $.Deferred().reject().promise();
      // @ts-expect-error ts-migrate(2551) FIXME: Property 'translations' does not exist on type '{ ... Remove this comment to see the full error message
    } else if (_.has(this.translations, language)) {
      // If we already have translations for this language, just trigger the
      // change event instead of making another request.
      promise = $.Deferred().resolve().promise();
    } else {
      // Make a request for the translations json file and set the response
      // on the translations object so we don't need to request it again.
      promise = $.ajax({
        dataType: 'json',
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 3 arguments, but got 1.
        url: path.join([(this as any).basePath, languagesNow[language]]),
      });
    }
    // Capture state for use determining if the completed promise is valid.
    this.loadingLanguage = language;
    // @ts-expect-error ts-migrate(2322) FIXME: Type 'Promise<any, any, any>' is not assignable to... Remove this comment to see the full error message
    this.loadingPromise = promise;
    promise.done(_.bind(this.onLoadSuccess, this, promise));
    promise.fail(_.bind(this.onLoadError, this, promise));
    return promise;
  },
  //
  // Listeners
  onLoadSuccess: function (promise: any, response: any) {
    const language = this.loadingLanguage;
    if (promise === this.loadingPromise) {
      this.loadingPromise = null;
      this.loadingLanguage = null;
      // @ts-expect-error ts-migrate(2551) FIXME: Property 'language' does not exist on type '{ lang... Remove this comment to see the full error message
      this.language = language;
      // Only store this translation in memory if we actually fetched it from the server
      // and received a response. In the cases where we switch to a language we've previously
      // fetched we don't want to set the language again since there is no response.
      // See https://github.com/plexinc/plex-web-client/issues/3587
      if (response) {
        // @ts-expect-error ts-migrate(2551) FIXME: Property 'translations' does not exist on type '{ ... Remove this comment to see the full error message
        this.translations[language] = response;
      }

      dispatcher.trigger('languageLoaded', language);
    }
  },
  onLoadError: function (promise: any) {
    if (promise === this.loadingPromise) {
      dispatcher.trigger('languageLoaded', this.loadingLanguage);

      this.loadingPromise = null;
      this.loadingLanguage = null;
    }
  },
};
export default localizerMixin;
