/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import toUpperCaseFirstLetter from 'app/js/utils/string/case/toUpperCaseFirstLetter';

export default function createPopulateHandler(options: any) {
  const { key, fetchActionCreator, getModel } = options;
  const populatedKey = `is${toUpperCaseFirstLetter(key)}Populated`;

  __DEV__ &&
    assert(
      getModel && fetchActionCreator,
      'Missing mandatory `getModel` and/or `fetchActionCreator`'
    );

  return (appModel: any, payload: any, dispatch: any) => {
    const model = getModel(appModel, payload);

    if (!model.get(populatedKey)) {
      dispatch(fetchActionCreator(payload));
    }
  };
}
