/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createBeginPollingHandler from 'app/js/stores/actions/helpers/server/createBeginPollingHandler';
import getAuthCodeExchangeModel from 'app/js/stores/actions/helpers/server/getAuthCodeExchangeModel';
import createPINHelper from 'app/js/stores/actions/helpers/user/createPIN';
import fetchPINHelper from 'app/js/stores/actions/helpers/user/fetchPIN';
import { scheduleImmediateUpdate } from 'app/js/utils/state/stateUpdateScheduler';
import t from 'app/js/utils/t';

//
// Action Types

export const READ_STORED_PIN = 'user/authCodeExchange/readStoredPIN';
export const PRELOAD_PIN = 'user/authCodeExchange/preloadPIN';
export const RESET_AUTH_CODE_EXCHANGE =
  'user/authCodeExchange/resetAuthCodeExchange';
export const BEGIN_POLLING = 'user/authCodeExchange/beginPolling';
export const STOP_POLLING = 'user/authCodeExchange/stopPolling';
export const SKIP_SIGN_IN = 'user/authCodeExchange/skipSignIn';

//
// Action Creators

export const readStoredPIN = createActionCreator(READ_STORED_PIN);
export const preloadPIN = createActionCreator(PRELOAD_PIN);
export const resetAuthCodeExchange = createActionCreator(
  RESET_AUTH_CODE_EXCHANGE
);
export const beginPolling = createActionCreator(BEGIN_POLLING);
export const stopPolling = createActionCreator(STOP_POLLING);
export const skipSignIn = createActionCreator(SKIP_SIGN_IN);

//
// Helpers

function createAndSetPIN(appModel: any, dispatch: any) {
  const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);
  authCodeExchangeModel.set({ isBusy: true });

  return createPINHelper(appModel)
    .then(({ id: pinID, code, expiresIn }) => {
      const pinTimeout = window.setTimeout(() => {
        dispatch(stopPolling());
      }, expiresIn * 1000);

      authCodeExchangeModel.set({
        code,
        pinID,
        pinTimeout,
        isBusy: false,
      });
    })
    .fail(() => {
      authCodeExchangeModel.set({
        fatalErrorMessage: t('An error occurred'),
        isBusy: false,
      });
    });
}

//
// Action Handlers

export const authCodeExchangeActionHandlers = {
  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [RESET_AUTH_CODE_EXCHANGE](appModel: any, payload: any, dispatch: any) {
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

    scheduleImmediateUpdate(() => {
      authCodeExchangeModel.set({
        code: null,
        pinID: null,
        authToken: null,
        errorMessage: null,
      });
    });
  },

  [READ_STORED_PIN](appModel: any, payload: any, dispatch: any) {
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);
    const pinID = authCodeExchangeModel.get('pinIDInitial');

    const pinIDPromise = pinID
      ? $.Deferred().resolve(pinID).promise()
      : $.Deferred().reject().promise();

    const authTokenPromise = pinIDPromise
      .then(() =>
        fetchPINHelper(appModel, pinID).fail(() => {
          authCodeExchangeModel.set({
            errorMessage: t('There was an error signing in'),
          });
        })
      )
      .then(({ authToken }) => {
        if (authToken) {
          return $.Deferred().resolve(authToken).promise();
        } else {
          authCodeExchangeModel.set({
            errorMessage: t('There was an error signing in'),
          });
          return $.Deferred().reject().promise();
        }
      })
      .then((authToken) => {
        authCodeExchangeModel.set({ authToken });
      });

    if (payload.shouldCreatePIN) {
      authTokenPromise.fail(() => createAndSetPIN(appModel, dispatch));
    }
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [PRELOAD_PIN](appModel: any, payload: any, dispatch: any) {
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

    authCodeExchangeModel.set({
      isBusy: true,
      code: null,
      pinID: null,
    });

    createAndSetPIN(appModel, dispatch);
  },

  [BEGIN_POLLING]: createBeginPollingHandler(),

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [STOP_POLLING](appModel: any, payload: any, dispatch: any) {
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

    const pinTimeout = authCodeExchangeModel.get('pinTimeout');
    if (pinTimeout) {
      window.clearTimeout(pinTimeout);
    }

    authCodeExchangeModel.set({
      pinID: null,
      pinTimeout: null,
      code: null,
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [SKIP_SIGN_IN](appModel: any, payload: any, dispatch: any) {
    appModel.set({ skipSignIn: true });
  },
};
