/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import log from 'app/js/utils/log/log';

const elements = {};
let preventRecycle = false;

export function createMediaElement(type: any, allowRecycle: any) {
  const recycle = allowRecycle && !preventRecycle;
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  const elementsByType = elements[type];

  if (recycle && elementsByType && elementsByType.length) {
    log('[MediaElement] Found existing media element to use. type: %s', type);

    // Release the element so it can't be reused until whatever is using the
    // element releases it back to the pool.
    return elementsByType.shift();
  }

  log('[MediaElement] Creating new media element. type: %s', type);

  return document.createElement(type);
}

export function releaseMediaElement(element: any) {
  const type = element.nodeName.toLowerCase();

  log('[MediaElement] Releasing media element to pool. type: %s', type);

  if (__DEV__) {
    assert(
      !element.src && !element.firstChild && !element.parentNode,
      "This media element's source must be reset before releasing back to pool. Use mediaElement.src = ''; and mediaElement.removeAttribute('src');"
    );
  }

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  if (!elements[type]) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    elements[type] = [];
  }

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  elements[type].push(element);
}

export function preventMediaElementRecycle(prevent: any) {
  preventRecycle = prevent;
}
