import { generatePath } from 'react-router-dom';
import path from 'app/js/utils/url/path';
import routes from 'app/js/v4/routes/routes';

function urlForRoute(
  routeKey: keyof typeof routes,
  params = {},
  queryParams = {}
): string {
  const route = routes[routeKey];
  const firstRoute = Array.isArray(route) ? route[0] : route;

  return path.join(generatePath(firstRoute, params), queryParams);
}

export default urlForRoute;
