/* eslint-plugin-disable @typescript-eslint */

import classNames from 'classnames';
import React from 'react';
import Link, { LinkProps } from 'app/js/views/v3/components/core/buttons/Link';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import styles from './MenuItem.css';

interface MenuItemProps extends LinkProps {
  className?: string;
  children?: React.ReactNode;
  kind?: any; // TODO: PropTypes.oneOf(kinds.all)
  isDisabled?: boolean;
  isHidden?: boolean;
}

function MenuItem(props: MenuItemProps) {
  const { className, children, kind, isDisabled, isHidden, ...otherProps } =
    props;

  return (
    <Link
      className={classNames(
        className,
        styles.menuItem,
        styles[kind],
        isDisabled && styles.isDisabled,
        isHidden && styles.isHidden
      )}
      data-testid="dropdownItem"
      role="menuitem"
      {...otherProps}
    >
      {children}
    </Link>
  );
}

MenuItem.defaultProps = {
  kind: kinds.DEFAULT,
  isDisabled: false,
};

export default MenuItem;
