import log from 'app/js/utils/log/log';
import { APIRequestConfig } from 'app/js/v4/utils/data/api';

const blacklistedMethods: { [index: string]: boolean } = {
  PUT: true,
  DELETE: true,
};

const allowedHeaders: { [index: string]: boolean } = {
  Accept: true,
  'Accept-Language': true,
  'Content-Language': true,
  'Content-Type': true,
};

const plexHeaderRegex = /(^X-Plex-[^=]+)/i;

export default function simpleCORSFilter(
  config: APIRequestConfig
): APIRequestConfig {
  const {
    method = 'GET',
    headers = {},
    params = {},
    allowHeadersInUrl = false,
    allowBlacklistedMethodHeaders = false,
  } = config;
  // Only allow headers to be moved to the url with requests to plex.tv or
  // Plex Media Server. Other clients may not support headers as query string
  // arguments.

  if (!allowHeadersInUrl) {
    return config;
  }

  // Older server versions can't handle empty request headers for some methods.
  // See: https://github.com/plexinc/plex-media-server/issues/531
  if (blacklistedMethods[method] && !allowBlacklistedMethodHeaders) {
    return config;
  }

  const nextHeaders = { ...headers };
  const nextParams = { ...params };

  Object.keys(headers).forEach((key: string) => {
    const value = headers[key];

    if (allowedHeaders[key]) {
      return;
    }

    if (plexHeaderRegex.test(key)) {
      nextParams[key] = value;
      delete nextHeaders[key];
    } else {
      log.warn(
        '[CORS] Did not move header "%s" to query string. This can result in an unnecessary OPTIONS preflight request.',
        key
      );
    }
  });

  return {
    ...config,
    headers: nextHeaders,
    params: nextParams,
  };
}
