import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import { populateCloudProviders } from 'app/js/stores/actions/provider/providerActions';
import {
  populateAllFriends,
  syncExperienceSettings,
  syncSettings,
} from 'app/js/stores/actions/userActions';
import BaseClass from 'app/js/utils/BaseClass';
import dispatcher from 'app/js/utils/dispatcher';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import { NOTIFY_ACCOUNT_CHANGE } from 'app/js/utils/pubsub/messageTypes';
import deleteNonEssentialCookies from 'app/js/utils/storage/deleteNonEssentialCookies';
import t from 'app/js/utils/t';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';
// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const UserManager = BaseClass.extend({
  mixins: eventMixin,
  //
  // Init
  initialize(user: any) {
    this.user = user;
    this.debouncedChangeTimeout = null;
  },
  //
  // Control
  start() {
    const user = this.user;

    this.listenTo(user, {
      change: this.onUserChange,
      'change:hasInvalidToken': this.onHasInvalidTokenChange,
      'change:isSignedIn': this.onIsSignedInChange,
    });

    this.listenTo(dispatcher, {
      [`cloudServer:${NOTIFY_ACCOUNT_CHANGE}`]: this.onNotifyAccountChange,
    });

    // Keep the UserManager in sync if the derived attributes go to
    // affirmative values before the manager is started.
    const hasInvalidToken = user.get('hasInvalidToken');
    const isSignedIn = user.get('isSignedIn');

    if (hasInvalidToken) {
      this.onHasInvalidTokenChange(user, hasInvalidToken);
    }
    if (isSignedIn) {
      this.onIsSignedInChangeHandleToken(user, isSignedIn);
    }
  },
  handleInvalidToken() {
    const confirmModal = new ConfirmModal({
      title: t('Invalid Credentials'),
      message: t('Your credentials have expired. Please sign in again.'),
      confirmLabel: t('OK'),
      cancelLabel: t('Ignore'),
    });
    confirmModal.promise.done(() => {
      navigateToRoute('login');
    });
    dispatcher.trigger('show:modal', confirmModal);
  },
  _updateUser(user: any, activeUser: any) {
    if (activeUser) {
      const options = {};
      if (activeUser.id !== user.id) {
        // When plex.tv is unavailable, the user will appear signed out despite
        // being able to use stored servers. When ids do not match only copy
        // across experience settings, and prevent the active user from copying
        // other attributes from the signed out user.
        // See: https://github.com/plexinc/plex-web-client/issues/13450
        // See: https://github.com/plexinc/plex-web-client/issues/3797
        (options as any).included = ['experienceSettings'];
      }
      activeUser.copyFromModel(user, options);
    } else {
      const storedUsers = userStorageModel.get('users');
      storedUsers.add(user.toJSON(), { parse: true });
    }
    userStorageModel.debouncedSave();
  },
  //
  // Listeners
  // @ts-expect-error ts-migrate(6133) FIXME: 'user' is declared but its value is never read.
  onHasInvalidTokenChange(user: any, hasInvalidToken: any) {
    if (hasInvalidToken) {
      this.handleInvalidToken();
    }
  },
  onUserChange(user: any) {
    if (this.debouncedChangeTimeout) {
      window.clearTimeout(this.debouncedChangeTimeout);
    }
    const activeUser = userStorageModel.getActiveUser();
    this.debouncedChangeTimeout = window.setTimeout(
      this._updateUser.bind(this, user, activeUser),
      0
    );
  },
  onIsSignedInChange(user: any, isSignedIn: any) {
    this.onIsSignedInChangeHandleToken(user, isSignedIn);
    this.onIsSignedInChangeHandleSyncSettings(user, isSignedIn);
    this.onIsSignedInChangeHandlePopulateUser(user, isSignedIn);
  },
  onIsSignedInChangeHandleToken(user: any, isSignedIn: any) {
    // Only use the listener to handle an invalid token before the user first
    // signs in. After initial sign in, call `handleInvalidToken` token directly
    // when the user is signed out with an invalid token.
    this.stopListening(
      user,
      'change:hasInvalidToken',
      this.onHasInvalidTokenChange
    );
    if (!isSignedIn) {
      if (user.get('hasInvalidToken')) {
        this.handleInvalidToken();
      }
      return;
    }
    if (isSignedIn && user.get('restricted')) {
      deleteNonEssentialCookies();
    }
    if (user.get('isFullUser') && isSignedIn) {
      const xhrOptions = user.server.getXHROptions('/api/claim/subscribe');
      $.ajax({
        ...xhrOptions,
        type: 'POST',
      });
    }
  },
  onIsSignedInChangeHandleSyncSettings(user: any, isSignedIn: any) {
    if (!isSignedIn) {
      return;
    }
    // If any settings were saved and unable to be synced to the user's account
    // because the user was offline or plex.tv was down, do that now.
    const dispatch = resolve('appStore').dispatch;
    const userSettings = user.get('settings');
    const experienceSettingsModel = user.get('experienceSettings');
    const dirtySettingIDs = userSettings.reduce(
      (arr: any, userSetting: any) => {
        if (userSetting.get('isDirty')) {
          arr.push(userSetting.get('id'));
        }
        return arr;
      },
      []
    );
    if (experienceSettingsModel.get('isDirty')) {
      dispatch(syncExperienceSettings());
    }
    if (dirtySettingIDs.length) {
      dispatch(syncSettings(dirtySettingIDs));
    }
  },
  onIsSignedInChangeHandlePopulateUser(user: any, isSignedIn: any) {
    if (!isSignedIn) {
      const features = user.get('features');
      features.clear();
      return;
    }
    // Assume UserPage waits for user features to populate
    const dispatch = resolve('appStore').dispatch;
    const populateOptions = { reset: true };

    user.get('homeDetails').populate(populateOptions);
    user.get('homeUsers').populate(populateOptions);
    user.get('consent').populate(populateOptions);
    user.get('geo').populate(populateOptions);

    const flexUserModel = user.get(__PLATFORM__);
    flexUserModel.get('cloudCompanions').populate(populateOptions);
    flexUserModel.get('devices').populate(populateOptions);
    flexUserModel.get('invites').populate(populateOptions);
    flexUserModel.get('socialNetworks').populate(populateOptions);

    user
      .get('features')
      .populate(populateOptions)
      .then(() => {
        dispatch(populateCloudProviders());
      });

    dispatch(populateAllFriends());
  },
  onNotifyAccountChange() {
    // Reload the user's subscriptions and services after an account change
    // notification from plex.tv. This is important to maintain an accurate
    // representation of a user's account without requiring a reload.
    const user = this.user;

    user.fetch().then(
      () => {
        log('[User] updated account following account change notification');
      },
      (error: any) => {
        log.error(
          '[User] Unable to update account following account change notification',
          error
        );
      }
    );
  },
});
export default UserManager;
