/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import assert from 'app/js/utils/log/assert';
import log from 'app/js/utils/log/log';

const commands = {
  _map: {},

  register(name: any, fn: any) {
    assert(_.isFunction(fn), `The "${name}" command must be a function`);

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    this._map[name] = fn;
  },

  registerAll(map: any) {
    _.each(map, (fn, name) => {
      // Platforms within the TV parent run in loose mode. This results in the
      // `__esModule` property being enumerable on ES2015 modules. Some calls
      // `registerAll()` with a ES2015 module value. Avoids calling `register()`
      // in this scenario due to the `assert()` activity in `register()`.
      if (name !== '__esModule') {
        this.register(name, fn);
      }
    });
  },

  hasRegistered(name: any) {
    return this._map.hasOwnProperty(name);
  },

  reset() {
    this._map = {};
  },

  execute(name: any, ...args: any[]) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const command = this._map[name];

    assert(command, `The "${name}" command has not been registered`);

    if (command) {
      log('[Commands] Executing', name);
      return command(...args);
    }
  },
};

export default commands;
