/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import createSourceFromIdentifiers from 'app/js/utils/metadata/createSourceFromIdentifiers';
import getIdentifiersFromSource from 'app/js/utils/metadata/getIdentifiersFromSource';

function resolveOrigin(metadataItemModel: any) {
  return (
    metadataItemModel.get('Origin') || {
      key: metadataItemModel.get('key'),
      source: createSourceFromIdentifiers({
        serverIdentifier: metadataItemModel.server.get('machineIdentifier'),
        providerIdentifier: metadataItemModel.provider.get('identifier'),
      }),
    }
  );
}

function resolveMediaFromOrigin(appModel: any, metadataItemModel: any) {
  const Origin = resolveOrigin(metadataItemModel);
  const deferred = $.Deferred();

  const { source: sourceAttr, key } = Origin;
  const { providerIdentifier, serverIdentifier } =
    getIdentifiersFromSource(sourceAttr);

  const server = appModel.get('servers').get(serverIdentifier);
  const providerContext =
    server && server.get('isCloud') ? appModel.get('user') : server;
  const provider =
    providerContext && providerContext.get('providers').get(providerIdentifier);

  let errorMessage;

  // The Origin may point to a server and provider which do not exist or are not
  // in a state to handle requests.
  if (!server) {
    errorMessage = 'Unknown server';
  } else if (!server.get('isConnected')) {
    errorMessage = 'Server is not connected';
  } else if (!provider) {
    errorMessage = 'Unknown provider';
  } else if (!provider.get('isPopulated')) {
    errorMessage = 'Provider is not populated';
  }

  if (errorMessage) {
    deferred.reject(new Error(errorMessage));
  } else {
    const originMetadataItemModel = new MetadataModel(
      { key },
      {
        server,
        provider,
        channel: metadataItemModel.channel,
        includeExternalMedia: metadataItemModel.includeExternalMedia,
        skipCache: true,
      }
    );

    const responseType = getSupportedResponseType(provider.server);

    const url = provider.resolveUrl(originMetadataItemModel.url(), {
      appendHeaders: true,
    });

    const xhrOptions = provider.getXHROptions(url, {
      dataType: responseType.typeName,
      data: { resolveIndirect: 1 },
    });

    originMetadataItemModel.fetch(xhrOptions).then(
      // @ts-expect-error ts-migrate(6133) FIXME: 'model' is declared but its value is never read.
      (model: any) => {
        // The originMetadataItemModel is not examined after fetching to
        // determine if it continues to have an indirect attribute. We assume
        // that a model has a single Origin, not a chain of Origins.
        const Media = originMetadataItemModel.get('Media');
        const indirectMedia = Media
          ? Media.map((media: any) =>
              $.extend(true, { source: sourceAttr }, media)
            )
          : [];

        deferred.resolve(indirectMedia);
      },
      () => {
        deferred.reject(new Error('Unable to fetch Origin metadata'));
      }
    );
  }

  return deferred.promise();
}

export default resolveMediaFromOrigin;
