/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';

export default function isVersionOrNewer(version: any, minVersion: any) {
  assert(version != null, 'No `version` supplied to `isVersionOrNewer`.');

  assert(
    minVersion != null,
    'No `minVersion` supplied to `isVersionOrNewer`. Have you misspelled a constant?'
  );

  const minVersionParts = minVersion.split('.');
  const versionParts = version.split('.');

  for (let i = 0, len = minVersionParts.length; i < len; i++) {
    if (i < versionParts.length) {
      const minVersionDot = parseInt(minVersionParts[i], 10);
      const versionDot = parseInt(versionParts[i], 10);

      if (minVersionDot < versionDot) {
        return true;
      } else if (minVersionDot > versionDot) {
        return false;
      }
    }
  }

  return true;
}
