import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import AgentAttributionModel from './AgentAttributionModel';
import LanguageCollection from './LanguageCollection';

const AgentModel = AbstractModel.extend({
  //
  // Properties

  name: 'AgentModel',
  context: ['server'],

  idAttribute: 'identifier',
  rootPropertyName: 'Agent',

  sessionAttrs: {
    isPrimary: attrTypes.boolean,
    selected: attrTypes.boolean,
  },

  sessionRelations: {
    attribution: {
      relationClass: AgentAttributionModel,
    },
  },

  syncedAttrs: {
    identifier: attrTypes.string,
    name: attrTypes.string,
    enabled: attrTypes.boolean,
    hasPrefs: attrTypes.boolean,

    // Order is only used when saving an agent after reordering.
    order: attrTypes.string,
  },

  syncedRelations: {
    languages: {
      relationClass: LanguageCollection,
      rootPropertyName: 'Language',
    },
  },
});

export default AgentModel;
