/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import store from 'app/js/v4/root/store';

const StoreModel = AbstractModel.extend({
  //
  // Properties

  name: 'StoreModel',

  initialize() {
    this._currentState = store.getState();
    store.subscribe(() => {
      const nextState = store.getState();
      this.trigger('statechange', this, nextState, this._currentState);
      this._currentState = nextState;
    });
  },

  getState() {
    return store.getState();
  },

  toJSON() {
    return store.getState();
  },

  dispatch(action: any) {
    store.dispatch(action);
  },
});

export default StoreModel;
