/* eslint-plugin-disable @typescript-eslint */

import roundToNearest from 'app/js/utils/number/roundToNearest';

export default function roundMetricsDuration(durationMilliseconds: any) {
  if (durationMilliseconds == null) {
    return;
  }

  const durationSeconds = Math.round(durationMilliseconds / 1000);
  let nearest;

  if (durationSeconds >= 60 * 60) {
    // Nearest 5 minutes over 1 hour
    nearest = 5 * 60;
  } else {
    // Nearest 1 minute under 1 hour
    nearest = 60;
  }

  return roundToNearest(durationSeconds, nearest);
}
