import { SagaIterator } from 'redux-saga';
import { all, fork } from 'redux-saga/effects';
import serverAccountsSaga from 'app/js/v4/core/serverAccounts/serverAccountsSaga';
import serverDevicesSaga from 'app/js/v4/core/serverDevices/serverDevicesSaga';
import serverNowPlayingSessionsSaga from 'app/js/v4/core/serverNowPlayingSessions/serverNowPlayingSessionsSaga';
import userSaga from 'app/js/v4/core/user/userSaga';

export default function* coreSaga(): SagaIterator {
  yield all([
    fork(userSaga),
    fork(serverAccountsSaga),
    fork(serverDevicesSaga),
    fork(serverNowPlayingSessionsSaga),
  ]);
}
