import parser2 from 'app/js/utils/parser2';
import {
  MediaSetting,
  MediaSettingType,
} from 'app/js/v4/core/settings/settingsTypes';
import { MediaContainerResponse } from 'app/js/v4/types/MediaContainerResponse';
import { normalizeAsBoolean } from 'app/js/v4/utils/data/normalizers/normalizeUtils';
import { NormalizerOptions } from 'app/js/v4/utils/data/normalizers/normalizersTypes';

const SEPARATOR = '|';

export interface NormalizeMediaSettingsResponseOptions
  extends NormalizerOptions {
  data: MediaContainerResponse;
}

function parseValueType(
  type: MediaSettingType,
  value: MediaSetting['value']
): MediaSetting['value'] {
  switch (type) {
    case MediaSettingType.Int:
    case MediaSettingType.Enum:
      return parser2.parseInteger(value);
    case MediaSettingType.Bool:
      return parser2.parseBoolean(value);
    case MediaSettingType.Double:
      return parser2.parseFloat(value);
    default:
      return value;
  }
}

export default function normalizeMediaSettingsResponse({
  data,
}: NormalizeMediaSettingsResponseOptions): MediaSetting[] {
  const mediaSettingsResponse = data.Setting ?? [];

  return mediaSettingsResponse.map((mediaSettingResponse) => {
    const { enumValues, ...settingAttrs } = mediaSettingResponse;
    const mediaSetting = settingAttrs as MediaSetting;

    if (mediaSetting.default) {
      mediaSetting.default = parseValueType(
        mediaSetting.type,
        mediaSetting.default
      );
    }
    mediaSetting.value = parseValueType(mediaSetting.type, mediaSetting.value);

    normalizeAsBoolean(mediaSetting, 'advanced');
    normalizeAsBoolean(mediaSetting, 'hidden');

    if (enumValues) {
      mediaSetting.enumValues = enumValues
        .split(SEPARATOR)
        .map((valueStr: any) => {
          const [value, label = value] = valueStr.split(':');
          return {
            label: decodeURIComponent(label),
            value: parseValueType(mediaSetting.type, value),
          };
        });
    }

    return mediaSetting;
  });
}
