/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import { populateProvider } from 'app/js/stores/actions/provider/providerActions';
import BaseClass from 'app/js/utils/BaseClass';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const ProviderManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize(options: any) {
    this.appModel = options.appModel;
  },

  //
  // Control

  start() {
    const userModel = this.appModel.get('user');

    this.populateCloudProviders(userModel.get('providers'));

    this.listenTo(this.appModel, {
      ['change:user.change:providers.add']: this.onProviderAdd,
    });
  },

  stop() {
    this.stopListening();
  },

  populateCloudProviders(providers: any) {
    providers.each((providerModel: any) => {
      this.populateCloudProvider(providerModel);
    });
  },

  populateCloudProvider(providerModel: any) {
    if (providerModel.get('isPopulated')) {
      return;
    }

    resolve('appStore').dispatch(
      populateProvider({
        providerEntityID: providerModel.entityID(),
        options: {
          merge: true,
        },
      })
    );
  },

  onProviderAdd(providerModel: any) {
    this.populateCloudProvider(providerModel);
  },
});

export default ProviderManager;
