/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import CircularProgressBar from 'app/js/views/v3/components/core/CircularProgressBar';
import Spinner from 'app/js/views/v3/components/core/Spinner';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';
import styles from './ActivityMenuStatusItem.css';

type ActivityMenuStatusItemProps = {
  title: string;
  subtitle?: string;
  progress?: number;
  isPostProcessing?: boolean;
};

function ActivityMenuStatusItem(props: ActivityMenuStatusItemProps) {
  const { title, subtitle, progress, isPostProcessing } = props;

  return (
    <div className={styles.splitButton}>
      <span className={styles.activityTitleContainer}>
        <span className={styles.activityTitle} title={title}>
          {title}
          {subtitle ? ':' : null}
        </span>
        {subtitle ? (
          <span className={styles.activitySubtitle} title={subtitle}>
            {subtitle}
          </span>
        ) : null}
      </span>
      <span>
        {progress == null || isPostProcessing ? (
          <Spinner size={sizes.SMALL} />
        ) : (
          <CircularProgressBar
            lineWidth={2}
            progress={progress}
            showProgressText={false}
            size={18}
          />
        )}
      </span>
    </div>
  );
}

export default ActivityMenuStatusItem;
