/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import { changePrimaryServer } from 'app/js/stores/actions/appActions';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import claimServerHelper from 'app/js/stores/actions/helpers/server/claimServer';
import getAuthCodeExchangeModel from 'app/js/stores/actions/helpers/server/getAuthCodeExchangeModel';
import signOutServerHelper from 'app/js/stores/actions/helpers/server/signOutServer';
import createPINHelper from 'app/js/stores/actions/helpers/user/createPIN';
import getAuthXHRErrors from 'app/js/stores/actions/helpers/user/getAuthXHRErrors';
import getSignInErrorMessage from 'app/js/stores/actions/helpers/user/getSignInErrorMessage';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as metricsEvents from 'app/js/utils/constants/metrics/metricsEvents';
import dispatcher from 'app/js/utils/dispatcher';
import { findServerModel } from 'app/js/utils/state/modelFinders';
import t from 'app/js/utils/t';

//
// Action Types

export const CLAIM_SERVER = 'server/claim/claimServer';
export const SIGN_IN_THEN_CLAIM_SERVER = 'server/claim/signInThenClaimServer';
export const SIGN_OUT_SERVER = 'server/claim/signOutServer';
export const CREATE_PIN = 'server/claim/createPIN';

//
// Action Creators

export const claimServer = createActionCreator(CLAIM_SERVER);
export const signInThenClaimServer = createActionCreator(
  SIGN_IN_THEN_CLAIM_SERVER
);
export const signOutServer = createActionCreator(SIGN_OUT_SERVER);
export const createPIN = createActionCreator(CREATE_PIN);

//
// Helpers

function signIn(appModel: any, payload: any) {
  const userModel = appModel.get('user');

  const { authToken } = payload;

  return $.ajax(
    userModel.server.getXHROptions(_.result(userModel, 'url'), {
      headers: { 'X-Plex-Token': authToken },
    })
  ).then((response) => userModel.signIn(response, {}));
}

function claimServerHandler(appModel: any, payload: any, dispatch: any) {
  const { authToken } = payload;

  const serverModel = findServerModel(appModel, payload);
  const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

  authCodeExchangeModel.set({
    errorMessage: null,
    isBusy: true,
  });

  const metrics = resolve('metrics');

  return claimServerHelper(appModel, serverModel, authToken, () => {
    dispatch(
      changePrimaryServer({
        machineIdentifier: serverModel.id,
        metricsProperties: { context: metricsContexts.SETTINGS },
      })
    );
  })
    .done(() => {
      metrics.trackEvent({
        event: metricsEvents.CLIENT_CLAIM_SERVER,
        interaction: true,
      });
    })
    .fail(() => {
      metrics.trackEvent({
        event: metricsEvents.CLIENT_CLAIM_SERVER_FAILURE,
        interaction: false,
      });

      authCodeExchangeModel.set({
        errorMessage: t('There was a problem claiming this server.'),
        isBusy: false,
      });
    });
}

//
// Action Handlers

export const claimServerActionHandlers = {
  [CLAIM_SERVER](appModel: any, payload: any, dispatch: any) {
    claimServerHandler(appModel, payload, dispatch).then(() => {
      const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

      authCodeExchangeModel.set({
        errorMessage: null,
        isBusy: false,
      });
    });
  },

  [SIGN_IN_THEN_CLAIM_SERVER](appModel: any, payload: any, dispatch: any) {
    signIn(appModel, payload)
      .then(() => claimServerHandler(appModel, payload, dispatch))
      .then(() => {
        const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

        authCodeExchangeModel.set({
          errorMessage: null,
          isBusy: false,
        });
      })
      .fail((xhr) => {
        const errors = getAuthXHRErrors(xhr);
        dispatcher.trigger('show:alert', {
          message: getSignInErrorMessage(errors),
        });

        const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);
        authCodeExchangeModel.set({
          errorMessage: t('There was a problem claiming this server.'),
          isBusy: false,
        });
      });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'dispatch' is declared but its value is never read... Remove this comment to see the full error message
  [SIGN_OUT_SERVER](appModel: any, payload: any, dispatch: any) {
    const serverModel = findServerModel(appModel, payload);
    const cloudAccountModel = serverModel.get(__PLATFORM__).get('cloudAccount');
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

    authCodeExchangeModel.set({
      errorMessage: null,
      isBusy: true,
    });

    signOutServerHelper(cloudAccountModel)
      .done(() => {
        authCodeExchangeModel.set({
          errorMessage: null,
          isBusy: false,
        });
      })
      .fail(() => {
        authCodeExchangeModel.set({
          errorMessage: t('There was a problem signing the server out.'),
          isBusy: false,
        });
      });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'payload' is declared but its value is never read.
  [CREATE_PIN](appModel: any, payload: any, dispatch: any) {
    const pinPromise = createPINHelper(appModel);
    const authCodeExchangeModel = getAuthCodeExchangeModel(appModel);

    pinPromise.then((pin) => {
      authCodeExchangeModel.set({
        pinID: pin.id,
        code: pin.code,
        isBusy: true,
      });
    });

    pinPromise.fail(() => {
      authCodeExchangeModel.set({
        fatalErrorMessage: t('An error occurred'),
        isBusy: false,
      });
    });
  },
};
