/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';
import createTypes from 'app/js/utils/types/createTypes';
import * as metadataSubtypes from 'app/js/utils/types/metadataSubtypes';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';

export const UNKNOWN = 'unknown';
export const DISCOVER = 'discover';
export const DVR = 'dvr';
export const EPISODE = 'episode';
export const HOME = 'home';
export const MOVIES = 'movies';
export const MUSIC = 'music';
export const PHOTOS = 'photos';
export const PLAYLISTS = 'playlists';
export const PODCASTS = 'podcasts';
export const TV = 'tv';
export const VIDEOS = 'videos';
export const WEBSHOWS = 'webShows';
export const SHARED = 'shared';
export const GAME = 'game';
export const AUDIOBOOK = 'audiobook';

const sourceTypes = createTypes(
  ['key'],
  [
    {
      key: UNKNOWN,
      title: () => t('Unknown'),
      types: {},
    },
    {
      key: PLAYLISTS,
      types: { type: metadataTypes.PLAYLIST },
    },
    {
      key: MOVIES,
      types: { type: metadataTypes.MOVIE },
    },
    {
      key: TV,
      types: { type: metadataTypes.SHOW },
    },
    {
      key: MUSIC,
      types: { type: metadataTypes.ARTIST },
    },
    {
      key: PHOTOS,
      types: { type: metadataTypes.PHOTO },
    },
    {
      key: VIDEOS,
      types: { type: metadataTypes.MOVIE },
    },
    {
      key: EPISODE,
      types: { type: metadataTypes.EPISODE },
    },
    {
      key: DVR,
      title: () => t('Live TV & DVR'),
    },
    {
      key: SHARED,
      types: { type: metadataTypes.MIXED },
    },
    {
      key: PODCASTS,
      types: { type: metadataTypes.SHOW, subtype: metadataSubtypes.PODCAST },
    },
    {
      key: WEBSHOWS,
      types: { type: metadataTypes.SHOW, subtype: metadataSubtypes.WEBSHOW },
    },
    {
      key: GAME,
      types: { type: metadataTypes.GAME },
    },
    {
      key: DISCOVER,
    },
    {
      key: AUDIOBOOK,
      types: { type: metadataTypes.AUTHOR },
    },
  ]
);

export const getSourceType = sourceTypes.get;
export const getAllSourceTypes = sourceTypes.getAll;
