/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';

const stateMachineMixin = {
  //
  // Properties
  currentState: null,
  //
  // Control
  enterState: function (state: any) {
    if (state === this.currentState) {
      return;
    }
    const previousState = this.currentState;
    // Before exiting the previous state, make sure the state we want to
    // enter is valid.
    const stateDefinition = (this as any).states[state];
    if (!stateDefinition) {
      throw new Error(
        'The state machine is unable to enter undefined state: ' + state
      );
    }
    if (previousState) {
      const previousStateDefinition = (this as any).states[previousState];
      if (_.isFunction(previousStateDefinition.onExit)) {
        const exitResult = previousStateDefinition.onExit.call(this);
        // Allow the `onExit` function of the previous state to abort.
        if (exitResult === false) {
          return;
        }
      }
    }
    this.currentState = state;
    if (_.isFunction(stateDefinition.onEnter)) {
      const enterResult = stateDefinition.onEnter.apply(
        this,
        // eslint-disable-next-line prefer-rest-params
        _.rest(arguments)
      );
      // Allow the `onEnter` function of the next state to abort.
      if (enterResult === false) {
        this.currentState = previousState;
        return;
      }
    }
    (this as any).triggerMethod('stateMachineChange', {
      from: previousState,
      to: state,
    });
  },
  enterNextState: function () {
    let nextState;
    if (this.currentState) {
      const previousStateDefinition = (this as any).states[this.currentState];
      nextState = previousStateDefinition.nextState;
    } else if ((this as any).initialState) {
      nextState = (this as any).initialState;
    } else {
      throw new Error('An initial state must be defined for the state machine');
    }
    if (nextState) {
      this.enterState(nextState);
    } else {
      (this as any).triggerMethod('stateMachineComplete');
    }
  },
};
export default stateMachineMixin;
