/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import path from 'app/js/utils/url/path';

const artworkAttributes = {
  poster: 'thumb',
  art: 'art',
  banner: 'banner',
};

function changeArtworkCommand(options: any) {
  const item = options.item;
  const params = { url: options.artworkRatingKey };
  const url = path.join(
    ['/library/metadata/', item.id, options.artworkType],
    params
  );
  const xhrOptions = _.extend(item.server.getXHROptions(url), { type: 'PUT' });

  const xhrDeferred = $.ajax(xhrOptions);

  xhrDeferred.done(function (response) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    item.set(artworkAttributes[options.artworkType], response);
  });

  return xhrDeferred.promise();
}

export default changeArtworkCommand;
