/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import path from 'app/js/utils/url/path';
import FilterOptionModel from './FilterOptionModel';

const FilterOptionCollection = AbstractCollection.extend({
  //
  // Properties
  name: 'FilterOptionCollection',
  context: [
    'server',
    'provider',
    'section',
    'type',
    'standalone',
    'includeExternalMedia',
  ],
  model: FilterOptionModel,
  url() {
    let params;
    if (this.type) {
      params = { type: this.type };
    } else if (!this.standalone) {
      params = this.getUrlParams();
    }
    const urlRoot = _.result(this.section, 'url');
    return path.join([urlRoot, this.parent.id], params);
  },
  //
  // Control
  getUrlParams() {
    const params = {};
    const urlParams: any = [];
    const type = this.section.get('currentType');
    const filters = this.section.get('currentFilters');
    if (type) {
      (params as any).type = type.id;
    }
    // Filters with more than one value should send a comma-delimited list
    // of values.
    _.each(filters, (filter) => {
      if (filter.key !== this.parent.id && filter.values.length) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        params[filter.key] = filter.values.join(',');
      }
    });
    // Some of the params are special and can't just be handled by the path
    // util so loop through the params and create a querystring manually.
    _.each(params, (value, key) => {
      // Send an operator for unviewedLeafCount.
      if (key === 'unviewedLeafCount') {
        urlParams.push(`${key}>=${value}`);
      } else {
        urlParams.push(`${key}=${value}`);
      }
    });
    return urlParams.join('&');
  },
  augmentResponse(json: any) {
    // https://github.com/plexinc/plex-web-client/issues/3304
    if (json.Directory && !Array.isArray(json.Directory)) {
      json.Directory = [json.Directory];
    }
    json.Directory = _.filter(json.Directory, (item) => item.title);
    return json;
  },
  search(query: any) {
    const matches = this.filter((filterOption: any) => {
      return (
        filterOption.get('title').toLowerCase().indexOf(query.toLowerCase()) >
        -1
      );
    });
    return this.clone(matches);
  },
});
export default FilterOptionCollection;
