/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import path from 'app/js/utils/url/path';
import FilterModel from './FilterModel';

const FilterCollection = AbstractCollection.extend({
  //
  // Properties
  name: 'FilterCollection',
  context: ['server', 'section', 'includeAdvanced'],
  model: FilterModel,
  comparator: 'filterType',
  includeAdvanced: true,
  url() {
    const urlRoot = _.result(this.section, 'url');
    const type = this.section.get('currentType');
    const params = {
      includeAdvanced: this.includeAdvanced ? 1 : 0,
    };
    if (type) {
      (params as any).type = type.id;
    }
    return path.join([urlRoot, '/filters'], params);
  },
  //
  // Control
  findUnwatchedFilter() {
    // This logic also exists in TVSectionViewStateStorageModel.toJSON()
    return this.get('unwatched') || this.get('unwatchedLeaves');
  },
});
export default FilterCollection;
