/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  beginPolling,
  preloadPIN,
  skipSignIn,
  stopPolling,
} from 'app/js/stores/actions/user/authCodeExchangeActions';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import ServiceAuthUrlConnector from 'app/js/views/v3/components/pages/serverSettings/ServiceAuthUrlConnector';
import SignInLaunchPagePoll from './SignInLaunchPagePoll';

const mapDispatchToProps = {
  dispatchPreloadPIN: preloadPIN,
  dispatchBeginPolling: beginPolling,
  dispatchStopPolling: stopPolling,
  dispatchSkipSignIn: skipSignIn,
};

type SignInLaunchPagePollConnectorProps = {
  serviceAuthUrl?: string;
  pinID?: number;
  authToken?: string;
  dispatchPreloadPIN: (...args: any[]) => any;
  dispatchBeginPolling: (...args: any[]) => any;
  dispatchStopPolling: (...args: any[]) => any;
  dispatchSkipSignIn: (...args: any[]) => any;
};

class SignInLaunchPagePollConnector extends Component<SignInLaunchPagePollConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this.props.dispatchPreloadPIN();
  }

  componentWillUnmount() {
    this.props.dispatchStopPolling();
  }

  //
  // Listeners

  onSignInPress = () => {
    this.props.dispatchBeginPolling({ pinID: this.props.pinID });
  };

  onStillTherePress = () => {
    this.props.dispatchPreloadPIN();
  };

  onSkipSignInPress = () => {
    this.props.dispatchSkipSignIn();
  };

  //
  // Render

  render() {
    const {
      dispatchPreloadPIN,
      dispatchBeginPolling,
      dispatchStopPolling,
      dispatchSkipSignIn,
      ...otherProps
    } = this.props;

    return (
      <ServiceAuthUrlConnector
        component={SignInLaunchPagePoll}
        onSignInPress={this.onSignInPress}
        onSkipSignInPress={this.onSkipSignInPress}
        onStillTherePress={this.onStillTherePress}
        {...otherProps}
      />
    );
  }
}

export default connect(
  selectAuthCodeExchange,
  mapDispatchToProps
)(SignInLaunchPagePollConnector);
