/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import AbstractCollection from 'app/js/models/AbstractCollection';
import * as responseTypes from 'app/js/models/responseTypes';
import path from 'app/js/utils/url/path';
import CloudCompanionModel from './CloudCompanionModel';

const CloudCompanionCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'CloudCompanionCollection',
  context: ['server'],

  model: CloudCompanionModel,
  url: '/api/v2/companions',
  responseType: responseTypes.json,

  augmentResponse(response: any) {
    const companions = response;
    const appModel = resolve('appModel');
    const deviceSettings = appModel && appModel.get('deviceSettings');
    const url = deviceSettings.get('sonosCustomBaseURL');

    if (url && companions && companions.length) {
      const sonos = companions.find(
        ({ identifier }: any) => identifier === 'tv.plex.sonos'
      );

      if (sonos) {
        companions.push({
          identifier: 'tv.plex.sonos-custom',
          title: 'Sonos (Custom)',
          baseURL: url,
          linkURL: path.join([url, 'link']),
          provides: sonos.provides,
          token: sonos.token,
        });
      }
    }

    return response;
  },
});

export default CloudCompanionCollection;
