/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import log from 'app/js/utils/log/log';

function getTranslatedCopy(type: any) {
  const clone = _.clone(type);

  if (!clone.hasOwnProperty('title')) {
    return clone;
  }

  // Titles may be functions so they can use the translate helper and be
  // properly scanned by tscan.

  clone.title = _.result(clone, 'title');

  if (clone.hasOwnProperty('shortTitle')) {
    clone.shortTitle = _.result(clone, 'shortTitle');
  }

  if (clone.hasOwnProperty('plural')) {
    clone.plural = _.result(clone, 'plural');
  }

  return clone;
}

export default function createTypes(identifiers: any, types: any) {
  const lookups = {};

  // Generate lookups using reduceRight so that earlier entries take precedence
  // over later ones.

  identifiers.forEach((identifier: any) => {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    lookups[identifier] = types.reduceRight((acc: any, type: any) => {
      const value = type[identifier];

      if (value != null) {
        acc[value] = type;
      }

      return acc;
    }, {});
  });

  function contains(id: any) {
    for (const identifier in lookups) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      if (lookups[identifier][id]) {
        return true;
      }
    }
    return false;
  }

  function get(id: any) {
    for (const identifier in lookups) {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      const lookup = lookups[identifier];
      const match = lookup[id];

      if (match) {
        return getTranslatedCopy(match);
      }
    }

    log.warn(`No type was found for ${id}`);
  }

  function getAll() {
    return types.map(getTranslatedCopy);
  }

  return {
    contains,
    get,
    getAll,
  };
}
