import { EPGLineup } from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import { MediaContainerResponse } from 'app/js/v4/types/MediaContainerResponse';
import { NormalizerOptions } from 'app/js/v4/utils/data/normalizers/normalizersTypes';

export interface NormalizeLineupChannelsResponseOptions
  extends NormalizerOptions {
  data: MediaContainerResponse;
}

export default function normalizeLineupChannelsResponse({
  data,
}: NormalizeLineupChannelsResponseOptions): EPGLineup[] {
  const response: EPGLineup[] = data.Lineup ?? [];

  response.forEach((lineup) => {
    lineup.Channel = lineup.Channel?.map((channel) => ({
      ...channel,
      lineupUUID: lineup.uuid,
    }));
  });

  return response;
}
