/* eslint-plugin-disable @typescript-eslint */

import attrTypes from 'app/js/models/attrTypes';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import preserveMergeStrategy from 'app/js/models/preserveMergeStrategy';
import mapToObject from 'app/js/utils/array/mapToObject';
import * as providerProtocols from 'app/js/utils/constants/providerProtocols';
import getLiveTVMediaItemID from 'app/js/utils/livetv/getLiveTVMediaItemID';

const unreliableAttributes = {
  guid: true,
  librarySectionID: true,
  librarySectionTitle: true,
  librarySectionUUID: true,
  librarySectionKey: true,
  source: true,
  // Preserving Context maintains displayFields, which would otherwise be lost
  // when the same item appears in different hubs.
  Context: true,

  // Common excludeFields
  file: true,
  summary: true,
  tagline: true,
  action: true,
  attributionLogo: true,
  backgroundColor: true,
};

function getStreamCount(mediaItems: any) {
  let streamCount = 0;

  if (!mediaItems) {
    return streamCount;
  }

  for (
    let i = 0, mediaItemsLength = mediaItems.length;
    i < mediaItemsLength;
    i++
  ) {
    const mediaItem = mediaItems[i];
    const parts = mediaItem.Part;

    if (parts) {
      for (let j = 0, partsLength = parts.length; j < partsLength; j++) {
        const part = parts[j];
        const streams = part.Stream;

        if (streams) {
          streamCount += streams.length;
        }
      }
    }
  }

  return streamCount;
}

function isMediaLiveTVProtocol(mediaItems: any) {
  return (
    mediaItems &&
    mediaItems.some(
      (mediaItem: any) => mediaItem.protocol === providerProtocols.LIVE_TV
    )
  );
}

function isMediaLiveTVOrigin(mediaItems: any) {
  // Identify LTVoP items. These LTVoP items do not have the livetv protocol.
  return (
    mediaItems &&
    mediaItems.some((mediaItem: any) => mediaItem.origin === 'livetv')
  );
}

function getLiveTVMediaItems(mediaItems = []) {
  // Live TV Media items can be duplicated with different IDs when a refresh occurs. We should
  // use the channel and airing time for uniqueness instead, as that should remain unchanged
  // between refreshes.
  // See: https://github.com/plexinc/plex-web-client/issues/11386

  return mapToObject(
    mediaItems,
    getLiveTVMediaItemID,
    (mediaItem: any) => mediaItem
  );
}

function mergeLiveTVMedia(previousValue: any, nextValue: any) {
  if (!nextValue) {
    return null;
  }

  // For grid items, we don't want to clear the `Media` array when new items are fetched. We want
  // to merge them with the old ones because the grid is paginated. If we throw away old Media
  // items, then items already fetched on the grid will get tossed out if the same show happens
  // to show up in the grid again.

  const previousMediaItems = getLiveTVMediaItems(previousValue);
  const nextMediaItems = getLiveTVMediaItems(nextValue);

  const mergedMediaItemsByID = {
    ...previousMediaItems,
    ...nextMediaItems,
  };

  for (const identifier in previousMediaItems) {
    const previousMediaItem = previousMediaItems[identifier];
    const nextMediaItem = nextMediaItems[identifier];

    if (
      previousMediaItem &&
      nextMediaItem &&
      previousMediaItem.Overlay.length &&
      !nextMediaItem.Overlay.length
    ) {
      // LTVoP Media Overlay information is only available when fetching the
      // grid and not when tuning the channel.
      // See: https://plex.slack.com/archives/CDJQWUUUC/p1593732566347100?thread_ts=1593626370.317800&cid=CDJQWUUUC
      mergedMediaItemsByID[identifier] = {
        ...mergedMediaItemsByID[identifier],
        Overlay: previousMediaItem.Overlay,
      };
    }

    const prevStreamCount = previousMediaItem?.Part?.[0]?.Stream?.length ?? 0;
    const nextStreamCount = nextMediaItem?.Part?.[0]?.Stream?.length ?? 0;

    // LTVoP subtitle streams are only available when tuning the channel, not
    // when fetching the grid. We must avoid grid requests erasing available
    // streams during playback.
    if (
      previousMediaItem &&
      nextMediaItem &&
      prevStreamCount > nextStreamCount
    ) {
      mergedMediaItemsByID[identifier] = {
        ...mergedMediaItemsByID[identifier],
        Part: previousMediaItem.Part,
      };
    }
  }

  return Object.keys(mergedMediaItemsByID).map(
    (id) => mergedMediaItemsByID[id]
  );
}

function mergeMetadataMedia(previousValue: any, nextValue: any) {
  const nextStreamCount = getStreamCount(nextValue);
  const previousStreamCount = getStreamCount(previousValue);

  return nextStreamCount || !previousStreamCount ? nextValue : previousValue;
}

function mergeMediaAttribute(key: any, previousValue: any, nextValue: any) {
  // If the next media items have streams, then it should replace the previous
  // value. Partial metadata items will not have streams, so we want to avoid
  // overwriting media items with streams with media items with no streams.

  if (nextValue && !previousValue) {
    return nextValue;
  }

  const isLiveTV =
    isMediaLiveTVProtocol(nextValue || previousValue) ||
    isMediaLiveTVOrigin(nextValue || previousValue);

  const mergeResult = isLiveTV
    ? mergeLiveTVMedia(previousValue, nextValue)
    : mergeMetadataMedia(previousValue, nextValue);

  if (mergeResult) {
    return mergeResult;
  }

  return preserveMergeStrategy(key, previousValue, nextValue);
}

function isFullTag(tag: any) {
  return (
    (tag.hasOwnProperty('ratingKey') || tag.hasOwnProperty('id')) &&
    tag.hasOwnProperty('tag')
  );
}

// @ts-expect-error ts-migrate(6133) FIXME: 'key' is declared but its value is never read.
function mergeTagAttribute(key: any, previousValue: any, nextValue: any) {
  // If either more tags are in the next value or it has the same amount of
  // tags, but each tag has more properties, then overwrite the previous tags.

  if (!nextValue) {
    return previousValue;
  }

  if (!previousValue) {
    return nextValue;
  }

  if (nextValue.length > previousValue.length) {
    return nextValue;
  }

  if (!nextValue.length || nextValue.length < previousValue.length) {
    return previousValue;
  }

  const firstPreviousTag = previousValue && previousValue[0];
  const firstNextTag = nextValue && nextValue[0];

  if (!firstPreviousTag && firstNextTag) {
    return nextValue;
  }

  if (!firstNextTag && firstPreviousTag) {
    return previousValue;
  }

  const isFirstPreviousTagFull = isFullTag(firstPreviousTag);
  const isFirstNextTagFull = isFullTag(firstNextTag);

  return isFirstPreviousTagFull && !isFirstNextTagFull
    ? previousValue
    : nextValue;
}

function isArrayAttribute(key: any) {
  const attrType = MetadataModel.prototype.syncedAttrs[key];

  return !!(attrType && attrType === attrTypes.array);
}

function isRelation(key: any) {
  return !!MetadataModel.prototype.syncedRelations[key];
}

export default function metadataMergeStrategy(
  key: any,
  previousValue: any,
  nextValue: any
) {
  if (key === 'Media') {
    return mergeMediaAttribute(key, previousValue, nextValue);
  }

  if (isArrayAttribute(key)) {
    return mergeTagAttribute(key, previousValue, nextValue);
  }

  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  if (isRelation(key) || unreliableAttributes[key]) {
    return preserveMergeStrategy(key, previousValue, nextValue);
  }

  return nextValue;
}
