import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import * as features from 'app/js/utils/constants/user/features';
import { ARTIST, MOVIE, SHOW } from 'app/js/utils/types/metadataTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import { ContentDirectory } from 'app/js/views/v3/helpers/props/shapes/contentDirectoryShape';

export interface DirectoryUpgrades {
  hasPlexMusicUpgrade: boolean;
  hasPlexMovieUpgrade: boolean;
  hasPlexSeriesUpgrade: boolean;
}

export default function getDirectoryUpgrade(
  server: Server,
  sourceDirectory: ContentDirectory | undefined
): DirectoryUpgrades {
  const isServerEligible =
    server.isConnected &&
    server.isFullOwnedServer &&
    server.ownerFeatures?.includes(features.PLEX_MOVIE_SCANNER);

  return {
    hasPlexMusicUpgrade: !!(
      isServerEligible &&
      sourceDirectory &&
      sourceDirectory.type === ARTIST &&
      (sourceDirectory.agent === agentIdentifiers.PLEXPREMIUMMUSIC ||
        sourceDirectory.agent === agentIdentifiers.LASTFM)
    ),
    hasPlexMovieUpgrade: !!(
      isServerEligible &&
      sourceDirectory &&
      sourceDirectory.type === MOVIE &&
      (sourceDirectory.agent === agentIdentifiers.THEMOVIEDB ||
        sourceDirectory.agent === agentIdentifiers.IMDB)
    ),
    hasPlexSeriesUpgrade: !!(
      isServerEligible &&
      sourceDirectory &&
      sourceDirectory.type === SHOW &&
      (sourceDirectory.agent === agentIdentifiers.THEMOVIEDB ||
        sourceDirectory.agent === agentIdentifiers.THETVDB)
    ),
  };
}
