/* eslint-plugin-disable @typescript-eslint */

import createActionCreator from 'app/js/stores/actions/createActionCreator';
import * as alertTypes from 'app/js/utils/constants/alertTypes';
import dispatcher from 'app/js/utils/dispatcher';

//
// Action Types

export const REPOPULATE_PAGE = 'ui/repopulatePage';
export const SHOW_ALERT = 'ui/showAlert';

//
// Action Creators

export const repopulatePage = createActionCreator(REPOPULATE_PAGE);

export const showAlert = createActionCreator(SHOW_ALERT, (alert: any) => {
  // Support the common case as well as a full alert object.
  //
  // showAlert('message');
  // showAlert({ type: alertTypes.SUCCESS, message: 'message' });

  return typeof alert === 'string'
    ? { type: alertTypes.ERROR, message: alert }
    : alert;
});

//
// Action Handlers

export const uiActionHandlers = {
  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  [SHOW_ALERT](appModel: any, payload: any) {
    dispatcher.trigger('show:alert', payload);
  },
};
