/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import rangeUtil from 'app/js/utils/rangeUtil';
import path from 'app/js/utils/url/path';

const paginatedListModelMixin = {
  getCollectionName() {
    return (this as any).collectionName || 'items';
  },
  getCollection() {
    return (this as any).get(this.getCollectionName());
  },
  getTotalSize() {
    return (
      (this as any).get('totalSize') || (this as any).get('populatedRange').size
    );
  },
  getPaginatedUrl(url: any) {
    const currentPageStart = (this as any).get('currentPageStart');
    const currentPageSize = (this as any).get('currentPageSize');
    // If pagination properties have been set, append the X-Plex pagination
    // params to the end of the query string.
    if (currentPageStart != null || currentPageSize != null) {
      const pageParams = {
        'X-Plex-Container-Start': currentPageStart || 0,
        'X-Plex-Container-Size': currentPageSize || 0,
      };
      return path.join(url, pageParams);
    }
    return url;
  },
  getCollectionChangeEvents() {
    const collectionName = this.getCollectionName();
    const events = [
      `change:${collectionName}.update`,
      `change:${collectionName}.reset`,
    ];
    return events.join(' ');
  },
  startPopulatedRangeMonitor() {
    const collectionName = this.getCollectionName();
    this.updatePopulatedRange();
    (this as any).on(
      this.getCollectionChangeEvents(),
      this.updatePopulatedRange,
      this
    );
    (this as any).on(
      `change:${collectionName}.destroy`,
      this.decrementTotalSize,
      this
    );
  },
  stopPopulatedRangeMonitor() {
    const collectionName = this.getCollectionName();
    (this as any).off(
      this.getCollectionChangeEvents(),
      this.updatePopulatedRange,
      this
    );
    (this as any).off(
      `change:${collectionName}.destroy`,
      this.decrementTotalSize,
      this
    );
  },
  fetchRange(options: any) {
    const fetchOptions = _.extend({}, options, { remove: false });
    if (options.prepend) {
      fetchOptions.at = 0;
    }
    (this as any).set({
      currentPageStart: options.start,
      currentPageSize: options.size,
    });
    return (this as any).fetch(fetchOptions).promise();
  },
  // @ts-expect-error ts-migrate(7023) FIXME: 'populateRange' implicitly has return type 'any' b... Remove this comment to see the full error message
  populateRange(options: any) {
    const collection = this.getCollection();
    const populatedRange = (this as any).get('populatedRange');
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 3 arguments, but got 2.
    let range = rangeUtil.difference(options, populatedRange)[0];
    // We do not need to fetch anything if there is no difference between the
    // requested range and the populated range.
    if (!range) {
      return $.Deferred().resolve().promise();
    }
    // We cannot have gaps in our collection so the models we fetch need to be
    // part of the same range.
    range = rangeUtil.degap(range, populatedRange);
    // If a previous request is still pending, check if the range is the same.
    // If the requested range is the same, reuse the existing promise.
    // Otherwise, abort so it can make a fresh request with the updated range.
    if ((this as any).isPopulating()) {
      const isStartDifferent =
        (this as any).get('currentPageStart') !== range.start;
      const isSizeDifferent =
        (this as any).get('currentPageSize') !== range.size;
      if (isStartDifferent || isSizeDifferent) {
        (this as any).abortPendingPopulate();
      } else {
        return (this as any).populatePromise;
      }
    }
    // It's possible that the requested range is outside of our current
    // populated range, for example when we "jump to a letter". When the
    // difference between the two ranges is significant, it's more efficient to
    // reset and start a new populate request rather than including the
    // "degapped" items.
    if (options.reloadThreshold && range.size > options.reloadThreshold) {
      collection.reset();
      return this.populateRange(_.omit(options, 'reloadThreshold'));
    }
    const fetchOptions = _.extend({}, options, range, {
      prepend: range.start < populatedRange.start,
    });
    return this.fetchRange(fetchOptions);
  },
  repopulateRange(minSize = 0) {
    const end = (this as any).get('populatedRange').end;
    (this as any).set({
      currentPageStart: 0,
      currentPageSize: Math.max(end, minSize),
    });
    (this as any).abortPendingPopulate();
    return (this as any).fetch();
  },
  updatePopulatedRange() {
    const currentPageStart = (this as any).get('currentPageStart');
    const previousStart = (this as any).get('populatedRange').start;
    const previousSize = (this as any).get('populatedRange').size;
    const size = this.getCollection().length;
    let start;
    if (!size) {
      // Assign the default start value if collection was reset.
      start = 0;
    } else if (previousSize) {
      // When populating items before the previously populated range, the
      // updated populated range start should be less than the previous value.
      // Otherwise, the populated range start value should remain the same.
      start = Math.min(
        previousStart,
        currentPageStart == null ? Infinity : currentPageStart
      );
    } else {
      // When fetching an initial batch of items in the middle of the list, the
      // `previousStart` will be 0 and the `currentPageStart` will be greater
      // than 0. Prefer the `currentPageStart`.
      start = currentPageStart || previousStart;
    }
    (this as any).set('populatedRange', {
      start,
      end: start + size,
      size,
    });
  },
  decrementTotalSize() {
    const totalSize = (this as any).get('totalSize');
    if (totalSize > 0) {
      (this as any).set('totalSize', totalSize - 1);
    }
  },
};
export default paginatedListModelMixin;
