/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import browser from 'app/js/utils/device/browser';
import getEnvironmentForMetrics from 'app/js/utils/metrics/getEnvironmentForMetrics';

const desktopDimensions = {
  init() {
    const appModel = resolve('appModel');

    const windowSize = `${window.innerWidth}x${window.innerHeight}`;
    const screenSize = `${window.screen.width}x${window.screen.height}`;

    const dimensions = {
      'X-Plex-Product': 'Plex Web',
      'X-Plex-Version': __VERSION__,
      'X-Plex-Client-Identifier': appModel.get('clientID'),
      'X-Plex-Platform': browser.name,
      'X-Plex-Platform-Version': browser.version,
      'X-Plex-Features': 'external-media,indirect-media',
      'X-Plex-Model': getEnvironmentForMetrics(),
      'X-Plex-Device': browser.platform,
      'X-Plex-Device-Name': browser.name,
      'X-Plex-Device-Screen-Resolution': `${windowSize},${screenSize}`,
    };

    // @ts-expect-error ts-migrate(6133) FIXME: 'appModel_' is declared but its value is never rea... Remove this comment to see the full error message
    appModel.on('change:clientID', (appModel_: any, clientID: any) => {
      dimensions['X-Plex-Client-Identifier'] = clientID;
    });

    return $.Deferred().resolve(dimensions).promise();
  },
};

export default desktopDimensions;
