/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import AbstractCollection from 'app/js/models/AbstractCollection';
import * as features from 'app/js/utils/constants/user/features';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import augmentProviderCollection from 'app/js/utils/provider/augmentProviderCollection';
import augmentWithSyntheticHomeProvider from 'app/js/utils/provider/augmentWithSyntheticHomeProvider';
import ProviderModel from './ProviderModel';

const ProviderCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ProviderCollection',
  context: ['server'],

  model: ProviderModel,
  url: '/media/providers',

  responseType() {
    return getSupportedResponseType(this.server);
  },

  //
  // Control

  fetch() {
    const server = this.server;
    const user = this.parent;
    const mappedFeatures = user
      ?.get('features')
      ?.map((item: any) => item.get('uuid'));
    const userHasUniversalWatchlist = mappedFeatures?.includes(
      features.UNIVERSAL_WATCHLIST
    );

    let promise;

    // Only make the request to `/media/providers` if the server has the
    // `mediaProviders` flag set. Shared servers respond with a 401 instead of a
    // 404 if a path is not there.

    if (server.get('mediaProviders') || server.get('isCloud')) {
      // eslint-disable-next-line prefer-rest-params
      promise = AbstractCollection.prototype.fetch.apply(this, arguments);
    } else {
      promise = $.Deferred().resolve(this, '').promise();
    }

    promise.then((collection: any) => {
      augmentProviderCollection(collection, server);
    });

    if (server.get('isCloud') && userHasUniversalWatchlist) {
      promise.then((collection: any) => {
        augmentWithSyntheticHomeProvider(collection, server);
      });
    }

    return promise;
  },
});

export default ProviderCollection;
