import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/lists... Remove this comment to see the full error message
import tpl from 'app/templates/lists/server/FilePathListItem.tpl';

const FilePathListItem = Marionette.ItemView.extend({
  //
  // Properties

  tagName: 'li',
  className: 'striped-list-item browse-directory-list-item',
  template: tpl,

  ui: {
    link: 'a',
    spinner: '.loading',
  },

  //
  // Control

  serializeData: function () {
    return {
      filePath: this.model.toJSON(),
      isRoot: !this.model.parent,
    };
  },

  select: function (loading: any) {
    this.ui.link.addClass('selected');
    this.ui.spinner.toggleClass('hidden', !loading);
  },

  unselect: function () {
    this.ui.link.removeClass('selected');
    this.ui.spinner.addClass('hidden');
  },
});

export default FilePathListItem;
