import React, { createContext, ReactElement, useContext, useMemo } from 'react';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import { MetricsPage } from 'app/js/v4/core/ui/metrics/metricsTypes';

export const Missing = Symbol(__DEV__ ? 'Missing' : undefined);
export const Placeholder = Symbol(__DEV__ ? 'Placeholder' : undefined);

interface MetricsContextProviderProps {
  metricsPage?: MetricsPage;
  name?: string;
  children: JSX.Element;
  metricsContextParam?: string;
  row?: number | typeof Missing;
  column?: number | typeof Missing;
}

export type MetricsContextTuple = readonly [
  string | typeof Missing,
  number | typeof Missing | typeof Placeholder,
  number | typeof Missing | typeof Placeholder
];

interface Metrics {
  metricsPage: MetricsPage;
  metricsContextTuple: MetricsContextTuple;
}

export const Empty: MetricsContextTuple = [Missing, Missing, Missing];

export const MetricsContext = createContext<Metrics>({
  metricsPage: metricsPages.UNKNOWN,
  metricsContextTuple: Empty,
});

export function useMetricsContext(): Metrics {
  return useContext<Metrics>(MetricsContext);
}

export function stringifyMetricsContextTuple([
  name,
  row,
  column,
]: MetricsContextTuple): string | undefined {
  if (name === Missing) {
    return;
  }

  return [
    name,
    row === Missing || row === Placeholder ? '' : row,
    column === Missing || column === Placeholder ? '' : column,
  ]
    .join('~')
    .replace(/~+$/, ''); // trim trailing tildes
}

export function parseMetricsContextString(str?: string): MetricsContextTuple {
  if (!str) {
    return Empty;
  }

  const [name, rowStr, columnStr] = str.split('~');
  if (!name) {
    return Empty;
  }

  return [
    name,
    rowStr ? parseInt(rowStr, 10) : Placeholder,
    columnStr ? parseInt(columnStr, 10) : Placeholder,
  ];
}

export type MetricsContextProperties =
  | { context: string; row?: number; col?: number }
  | undefined;

export function toMetricsProperties([
  context,
  row,
  col,
]: MetricsContextTuple): MetricsContextProperties {
  if (context === Missing) {
    return;
  }

  return {
    context,
    row: row === Placeholder || row === Missing ? undefined : row,
    col: col === Placeholder || col === Missing ? undefined : col,
  };
}

export default function MetricsContextProvider({
  children,
  metricsPage: metricsPageFromProps,
  metricsContextParam,
  name: nameFromProps,
  row: rowFromProps = Missing,
  column: columnFromProps = Missing,
}: MetricsContextProviderProps): ReactElement {
  const { metricsPage, metricsContextTuple } = useMetricsContext();

  const metricsContextValue = useMemo<MetricsContextTuple | undefined>(() => {
    const [name, row, column] = metricsContextTuple;

    if (metricsContextParam) {
      // A string-encoded context takes precedence; parse and proceed;
      // This is the only path by which Placeholder elements come into play.
      return parseMetricsContextString(metricsContextParam);
    } else if (nameFromProps) {
      // If name is provided by props, set up a new, fillable row+column layer.
      return [`${metricsPage}:${nameFromProps}`, rowFromProps, columnFromProps];
    } else if (name !== Missing) {
      // Ensure we're in a fillable layer; no row+column until we have a name.
      // When row+column props come in, only place them in Missing slots.
      return [
        name,
        row === Missing ? rowFromProps : row,
        column === Missing ? columnFromProps : column,
      ];
    } else if (metricsPageFromProps && metricsPageFromProps !== metricsPage) {
      // Forward the original context when the metrics page changes.
      return metricsContextTuple;
    }

    return;
  }, [
    columnFromProps,
    metricsContextTuple,
    metricsContextParam,
    metricsPage,
    metricsPageFromProps,
    nameFromProps,
    rowFromProps,
  ]);

  const metricsValue = useMemo<Metrics | undefined>(() => {
    return metricsContextValue
      ? {
          metricsPage: metricsPageFromProps || metricsPage,
          metricsContextTuple: metricsContextValue,
        }
      : undefined;
  }, [metricsContextValue, metricsPage, metricsPageFromProps]);

  return metricsValue ? (
    <MetricsContext.Provider value={metricsValue}>
      {children}
    </MetricsContext.Provider>
  ) : (
    children
  );
}
