/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import * as audioBoostLevels from 'app/js/utils/constants/audioBoostLevels';
import log from 'app/js/utils/log/log';
import getMediaIndex from 'app/js/utils/media/getMediaIndex';
import getPartIndex from 'app/js/utils/media/getPartIndex';
import path from 'app/js/utils/url/path';

const transcoder = {
  extensions: {
    dash: '.mpd',
    hls: '.m3u8',
    http: '',
  },
  logParams: function (type: any, action: any, params: any) {
    log('[Transcoder] %s (%s) options:', type, action, params);
  },
  // `server` is optional. TV uses it to serialize param values differently
  // based on server version.
  // @ts-expect-error ts-migrate(6133) FIXME: 'server' is declared but its value is never read.
  serializeParams: function (params: any, server: any) {
    // See: https://github.com/plexinc/plex-web-client/issues/2768
    let serialized;
    let head = {};
    let tail = {};
    const xPlex = /^X-Plex/;
    _.each(params, function (value, key) {
      if (xPlex.test(key)) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        tail[key] = value;
      } else {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        head[key] = value;
      }
    });
    head = path.stringifyParams(head);
    tail = path.stringifyParams(tail);
    if (head && tail) {
      serialized = head + '&' + tail;
    } else {
      serialized = head || tail;
    }
    return serialized;
  },
  image: function (server: any, imagePath: any, options: any) {
    if (!imagePath) {
      return '';
    }
    const fallbackCandidate = options.fallbackServer;
    let useFallbackServer = false;
    let fallbackServer;
    if (fallbackCandidate && fallbackCandidate.get('isConnected')) {
      const isFullServer = fallbackCandidate.get('isFullServer');
      fallbackServer = isFullServer ? fallbackCandidate : null;
    }
    if (fallbackServer) {
      if (server.get('cloud')) {
        useFallbackServer = true;
      } else if (!server.get('isFullServer')) {
        // A synced server is only reachable from a fallback server if it has a
        // remote connection or both it and the fallback server have local
        // connections.
        const serverHasRemoteConnection = !server
          .get('activeConnection')
          .get('isPrivate');
        const fallbackHasPrivateConnection = fallbackServer
          .get('activeConnection')
          .get('isPrivate');
        useFallbackServer =
          serverHasRemoteConnection || fallbackHasPrivateConnection;
      }
    }
    const transcodeServer = useFallbackServer ? fallbackServer : server;
    const useRelativeUrl = transcodeServer.id === server.id;
    const params = {
      url: server.resolveUrl(imagePath, {
        useRelativeUrl: useRelativeUrl,
        appendToken: true,
      }),
      width: options.width && Math.round(parseFloat(options.width)),
      height: options.height && Math.round(parseFloat(options.height)),
    };
    if (options.minSize) {
      (params as any).minSize = 1;
    }
    if (options.format) {
      (params as any).format = options.format;
    }
    if (options.quality) {
      (params as any).quality = options.quality;
    }
    if (options.filters) {
      _.each(options.filters, function (value, key) {
        if (value !== undefined) {
          // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
          params[key] = value;
        }
      });
    }
    const url =
      '/photo/:/transcode?' + this.serializeParams(params, transcodeServer);
    return transcodeServer.resolveUrl(url, { appendToken: true });
  },
  music: function (server: any, item: any, options: any, action: any) {
    action || (action = 'start');
    const media = _.first(item.get('Media'));
    if (!media) {
      return;
    }
    const part = _.first(media.Part);
    if (!part) {
      return;
    }
    let params;
    let baseUrl;
    if (server.supports('universalMusicTranscoder')) {
      params = _.extend(
        {
          hasMDE: 1,
          path: server.resolveUrl(item.get('key'), { useRelativeUrl: true }),
          mediaIndex: 0,
          partIndex: 0,
        },
        options
      );
      baseUrl = '/music/:/transcode/universal/' + action;
    } else {
      params = {
        identifier: item.get('identifier'),
        url: server.resolveUrl(part.key, { useRelativeUrl: true }),
        format: 'mp3',
        audioCodec: 'libmp3lame',
        audioBitrate: 320,
        audioSamples: 44100,
      };
      baseUrl = '/music/:/transcode/generic.mp3';
    }
    this.logParams('Music', action, params);
    params = _.extend(params, server.getHeaders());
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const extension = this.extensions[params.protocol];
    if (action === 'start' && extension) {
      baseUrl += extension;
    }
    return server.resolveUrl(
      baseUrl + '?' + this.serializeParams(params, server)
    );
  },
  video: function (
    server: any,
    item: any,
    media: any,
    part: any,
    options: any,
    action: any
  ) {
    options || (options = {});
    action || (action = 'start');
    // If the transcodeServer matches the item's server, use a loopback.
    const useRelativeUrl = server.entityID() === item.get('serverEntityID');
    const activeConnection = server.get('activeConnection');
    const isPrivateConnection = activeConnection.get('isPrivate');
    let params = _.extend(
      {
        hasMDE: 1,
        path: server.resolveUrl(item.get('key'), {
          useRelativeUrl: useRelativeUrl,
          appendToken: !useRelativeUrl,
        }),
        mediaIndex: getMediaIndex(item, media.id),
        partIndex: getPartIndex(media, part.key),
        protocol: 'hls',
        fastSeek: 1,
        directPlay: 0,
        directStream: 1,
        subtitleSize: 100,
        audioBoost: audioBoostLevels.NORMALIZED,
        location: isPrivateConnection ? 'lan' : 'wan',
      },
      options
    );
    this.logParams('Video', action, params);
    params = _.extend(params, server.getHeaders());
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const extension = this.extensions[params.protocol];
    let baseUrl = '/video/:/transcode/universal/' + action;
    if (action === 'start' && extension) {
      baseUrl += extension;
    }
    return server.resolveUrl(
      baseUrl + '?' + this.serializeParams(params, server)
    );
  },
  game: function (
    server: any,
    item: any,
    media: any,
    options: any,
    action: any
  ) {
    options || (options = {});
    action || (action = 'start');
    // If the transcodeServer matches the item's server, use a loopback.
    const useRelativeUrl = server.entityID() === item.get('serverEntityID');
    const activeConnection = server.get('activeConnection');
    const isPrivateConnection = activeConnection.get('isPrivate');
    let params = _.extend(
      {
        path: server.resolveUrl(item.get('key'), {
          useRelativeUrl: useRelativeUrl,
          appendToken: !useRelativeUrl,
        }),
        location: isPrivateConnection ? 'lan' : 'wan',
        mediaIndex: getMediaIndex(item, media.id),
      },
      options,
      {
        // Our `protocol: 'parsec'` is Web implementation detail. Don't allow it
        // to leak to PMS.
        protocol: undefined,
      }
    );
    this.logParams('Game', action, params);
    params = _.extend(params, server.getHeaders());
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const extension = this.extensions[params.protocol];
    let baseUrl = '/game/:/transcode/' + action;
    if (action === 'start' && extension) {
      baseUrl += extension;
    }
    return server.resolveUrl(
      baseUrl + '?' + this.serializeParams(params, server)
    );
  },
};
export default transcoder;
