/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import VersionGeneratorModel from 'app/js/models/library/convert/VersionGeneratorModel';
import proxyTypes from 'app/js/utils/constants/proxyTypes';
import path from 'app/js/utils/url/path';

const VersionGeneratorCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'VersionGeneratorCollection',

  context: ['server'],

  model: VersionGeneratorModel,

  url() {
    return path.join(['playlists', this.parent.id, 'items'], {
      type: proxyTypes.LOCAL,
    });
  },
});

export default VersionGeneratorCollection;
