/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import httpFallbackOptions from 'app/js/utils/constants/httpFallbackOptions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import t from 'app/js/utils/t';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import Modal from 'app/js/views/v3/components/core/modals/Modal';
import ModalBody from 'app/js/views/v3/components/core/modals/ModalBody';
import ModalContent from 'app/js/views/v3/components/core/modals/ModalContent';
import ModalFooter from 'app/js/views/v3/components/core/modals/ModalFooter';
import ModalFooterButtonBar from 'app/js/views/v3/components/core/modals/ModalFooterButtonBar';
import ModalHeader from 'app/js/views/v3/components/core/modals/ModalHeader';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import * as sizes from 'app/js/views/v3/helpers/props/sizes';

type AllowHttpFallbackModalProps = {
  serverEntityID: string;
  publicAddressMatches: boolean;
  isOpen: boolean;
  onAllowHttpFallback: (...args: any[]) => any;
  onModalClose: (...args: any[]) => any;
};

type AllowHttpFallbackModalState = any;

class AllowHttpFallbackModal extends Component<
  AllowHttpFallbackModalProps,
  AllowHttpFallbackModalState
> {
  constructor(props: any, context: any) {
    super(props, context);

    this.state = {
      isSubmitting: false,
    };
  }

  //
  // Listeners

  onAllowFallbackConfirm = () => {
    this.setState({ isSubmitting: true });

    this.props.onAllowHttpFallback({
      serverEntityID: this.props.serverEntityID,
      allowHttpFallback: this.props.publicAddressMatches
        ? httpFallbackOptions.SAME_NETWORK
        : httpFallbackOptions.ALWAYS,
    });
  };

  //
  // Render

  render() {
    const { publicAddressMatches, isOpen, onModalClose } = this.props;

    const { isSubmitting } = this.state;

    return (
      <Modal
        isOpen={isOpen}
        metricsPage={metricsPages.HOME}
        size={sizes.SMALL}
        onModalClose={onModalClose}
      >
        <ModalContent onModalClose={onModalClose}>
          <ModalHeader>{t('Allow Insecure Connections')}</ModalHeader>
          <ModalBody>
            {publicAddressMatches
              ? t(
                  "Insecure connections will only be allowed when you're on the same network as the server."
                )
              : t('Insecure connections will be allowed to any server.')}
            <br />
            {t(
              'The Allow Fallback to Insecure Connections setting can be changed in Web Settings. Would you like to allow insecure connections?'
            )}
          </ModalBody>
          <ModalFooter>
            <ModalFooterButtonBar>
              <SpinnerButton
                isDisabled={isSubmitting}
                kind={kinds.DEFAULT}
                onPress={onModalClose}
              >
                {t('Cancel')}
              </SpinnerButton>
              <SpinnerButton
                isSpinning={isSubmitting}
                kind={kinds.DANGER}
                onPress={this.onAllowFallbackConfirm}
              >
                {t('Allow')}
              </SpinnerButton>
            </ModalFooterButtonBar>
          </ModalFooter>
        </ModalContent>
      </Modal>
    );
  }
}

export default AllowHttpFallbackModal;
