/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import MediaSettingModel from './MediaSettingModel';

const MediaSettingCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'MediaSettingCollection',
  context: ['server', 'provider'],

  model: MediaSettingModel,

  //
  // Control

  getSaveParams() {
    return this.reduce((obj: any, model: any) => {
      obj[model.id] = model.get('value');
      return obj;
    }, {});
  },
});

export default MediaSettingCollection;
