/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import BaseModel from 'app/js/models/BaseModel';
import MetadataCollection from 'app/js/models/metadata/MetadataCollection';
import contentUtil from 'app/js/utils/contentUtil';
import parser from 'app/js/utils/parser';
import path from 'app/js/utils/url/path';
// DEPRECATED - Use common/js/models/metadata/PlaylistModel
const PlaylistModel = BaseModel.extend({
  //
  // Properties
  idAttribute: 'ratingKey',
  whitelistedAttrs: [
    'guid',
    'addedAt',
    'composite',
    'duration',
    'key',
    'leafCount',
    'playlistType',
    'ratingKey',
    'smart',
    'summary',
    'title',
    'type',
    'allowSync',
  ],
  defaults: function () {
    return {
      children: new MetadataCollection([], {
        server: this.server,
        parent: this,
        url: function () {
          const uriPath = '/playlists/' + this.parent.id + '/items';
          let params;
          // We need to request related items for tracks so we can discover
          // Gracenote mixes.
          if (this.parent.get('playlistType') === 'audio') {
            params = { includeRelated: 1 };
          }
          return path.join(uriPath, params);
        },
      }),
    };
  },
  urlRoot: '/playlists',
  saveUrl: function () {
    // See similar playlist key translation from /playlists/:ratingKey to /library/metadata/:ratingKey in:
    // - app/common/js/models/metadata/MetadataModel.ts#saveUrl()
    // - app/desktop/common/js/models/library/ArtworkCollection.ts#url()
    return '/library/metadata/' + this.id;
  },
  cacheKey: function (attributes: any) {
    if (attributes.key) {
      return this.server && this.server.getScopedKey(attributes.key);
    }
  },
  //
  // Init
  initialize: function () {
    this.listenToOnce(this.get('children'), 'add', function (item: any) {
      // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
      this.set('art', item.get('art'));
    });
  },
  //
  // Control
  parse: function (response: any) {
    let json = this.convert(response);
    // If we've fetched this as a single item the response will be in a Playlist
    if (json.Playlist) {
      json = json.Playlist;
    }
    parser.parseInteger(json, ['duration', 'leafCount', 'addedAt']);
    parser.parseBoolean(json, ['smart', 'allowSync']);
    json.isPlaylist = true;
    return json;
  },
  findItem: function (id: any) {
    return this.get('children').findWhere({ playlistItemID: id });
  },
  moveItem: function (itemID: any, afterItemID: any) {
    const params = afterItemID ? { after: afterItemID } : {};
    const url = path.join(
      [_.result(this, 'url'), '/items/', itemID, '/move'],
      params
    );
    const xhrOptions = _.extend(this.server.getXHROptions(url), {
      type: 'PUT',
    });
    const xhrDeferred = $.ajax(xhrOptions);
    const items = this.get('children');
    const item = this.findItem(itemID);
    items.remove(item);
    const afterItem = this.findItem(afterItemID);
    const spliceAt = afterItem ? items.indexOf(afterItem) + 1 : 0;
    items.add(item, { at: spliceAt });
    return xhrDeferred;
  },
  removeItem: function (itemID: any) {
    itemID = itemID.toString();
    const url = path.join([_.result(this, 'url'), '/items/', itemID]);
    const xhrOptions = _.extend(this.server.getXHROptions(url), {
      type: 'DELETE',
    });
    const xhrDeferred = $.ajax(xhrOptions);
    xhrDeferred.then(
      _.bind(function (response: any) {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.set(this.parse(response));
      }, this)
    );
    const items = this.get('children');
    const item = this.findItem(itemID);
    items.remove(item);
    return xhrDeferred;
  },
  addContent: function (item: any, options: any) {
    const method = this.isNew() ? 'POST' : 'PUT';
    const segments = [_.result(this, 'url')];
    const params = {};
    if (this.isNew()) {
      (params as any).type = this.get('playlistType');
      (params as any).title = this.get('title');
      (params as any).smart = this.get('smart') ? 1 : 0;
    } else {
      segments.push('/items');
    }
    if (item.get('playQueueID')) {
      (params as any).playQueueID = item.get('playQueueID');
    } else {
      (params as any).uri = contentUtil.uri(item, options);
    }
    const url = path.join(segments, params);
    const xhrOptions = _.extend(item.server.getXHROptions(url), {
      type: method,
    });
    const xhrDeferred = $.ajax(xhrOptions);
    xhrDeferred.then(
      _.bind(function (response: any) {
        // Both the metadata of the playlist (returned in response) and the
        // collection of items will have changed on the server
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.set(this.parse(response));
      }, this)
    );
    return xhrDeferred.promise();
  },
  isLibraryItem: function () {
    return true;
  },
  isPlexMusicItem: function () {
    return false;
  },
});
export default PlaylistModel;
