/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';

const StoredModel = AbstractModel.extend({
  //
  // Properties

  additionalRelations: [],

  //
  // Control

  copyFromModel(model: any, options: any) {
    this._copy(model, this, options);
  },

  copyToModel(model: any, options: any) {
    this._copy(this, model, options);
  },

  _copy(fromModel: any, toModel: any, options = {}) {
    const isCapturingChangeEvents = !!toModel.releaseCapturedChangeEvents;

    if (!isCapturingChangeEvents) {
      toModel.startCapturingChangeEvents();
    }

    this._copyAttributes(fromModel, toModel, options);
    this._copyRelations(fromModel, toModel, options);

    if (!isCapturingChangeEvents) {
      toModel.releaseCapturedChangeEvents();
    }
  },

  _copyAttributes(fromModel: any, toModel: any, options: any) {
    const attrs = {};

    // @ts-expect-error ts-migrate(6133) FIXME: 'value' is declared but its value is never read.
    _.each(this.syncedAttrs, (value, key) => {
      if (options.included && !options.included.includes(key)) {
        return;
      }

      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      attrs[key] = fromModel.get(key);
    });

    toModel.set(attrs);
  },

  _copyRelations(fromModel: any, toModel: any, options: any) {
    // @ts-expect-error ts-migrate(6133) FIXME: 'relation' is declared but its value is never read... Remove this comment to see the full error message
    _.each(this.syncedRelations, (relation, key) => {
      if (options.included && !options.included.includes(key)) {
        return;
      }

      // Allow some relations to be managed independently, like a user's servers
      // and server connections .

      if (this.additionalRelations.includes(key)) {
        return;
      }

      const fromRelation = fromModel.get(key);
      const toRelation = toModel.get(key);

      if (fromRelation && toRelation) {
        const json = fromRelation.toJSON();

        if (toRelation instanceof AbstractModel) {
          toRelation.set(toRelation.parse(json));
        } else {
          toRelation.set(json, { parse: true });
        }
      }
    });
  },
});

export default StoredModel;
