import { createSelector } from 'reselect';
import selectAvailableLibraryUpgrades from 'app/js/stores/selectors/ui/selectAvailableLibraryUpgrades';

const selectHasAnyManageableServerAvailableLibraryUpgrade = createSelector(
  selectAvailableLibraryUpgrades,
  (availableUpgrades) => availableUpgrades.length > 0
);

export default selectHasAnyManageableServerAvailableLibraryUpgrade;
