import _ from 'underscore';
import path from 'app/js/utils/url/path';
import SelectModal from 'app/js/views/modals/SelectModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/DownloadVersionModal.tpl';

const DownloadVersionModal = SelectModal.extend({
  //
  // Properties

  name: 'downloadVersionModal',
  template: tpl,

  //
  // Control

  serializeData: function () {
    const model = this.model;
    const downloadList = _.map(
      this.model.get('Media'),
      function (mediaItem, index) {
        const item = _.clone(mediaItem);
        item.downloadUrl = model.getDownloadUrl(index);
        const filePath = model.getFilepath(index);
        if (filePath) {
          item.downloadName = path.getLastSegment(filePath);
        }
        return item;
      }
    );

    return {
      defaultLinkTarget: __EXTERNAL_LINK_TARGET__,
      mediaItems: downloadList,
    };
  },

  onClick: function () {
    // Empty click method to override default
  },
});

export default DownloadVersionModal;
