/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import OauthProviderUsageModel from './OauthProviderUsageModel';

const OauthProviderModel = AbstractModel.extend({
  //
  // Properties

  name: 'OauthProviderModel',

  rootPropertyName: 'provider',

  sessionAttrs: {
    isUnlinking: attrTypes.boolean.isRequired,
    unlinkError: attrTypes.object,
  },

  syncedAttrs: {
    authName: attrTypes.string,
    image: attrTypes.string,
    label: attrTypes.string.isRequired,
    linked: attrTypes.boolean.isRequired,
    nickname: attrTypes.string,
    url: attrTypes.string.isRequired,
  },

  syncedRelations: {
    usage: {
      relationClass: OauthProviderUsageModel,
    },
  },

  defaults: {
    isUnlinking: false,
  },
});

export default OauthProviderModel;
