/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import LyricsLineModel from './LyricsLineModel';

const LyricsLineCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'LyricsLineCollection',
  context: ['server', 'section'],

  model: LyricsLineModel,

  //
  // Control

  eachSpan(fn: any) {
    this.each((line: any) => {
      line.get('spans').each(fn);
    });
  },
});

export default LyricsLineCollection;
