import createSourceFromIdentifiers from 'app/js/utils/metadata/createSourceFromIdentifiers';
import {
  GuideChannel,
  GuideChannelResponse,
} from 'app/js/v4/features/livetv/liveTVTypes';
import { NormalizerOptions } from 'app/js/v4/utils/data/normalizers/normalizersTypes';

interface NormalizeGuideChannelsResponseOptions extends NormalizerOptions {
  data: GuideChannelResponse[] | undefined;
}

export default function normalizeChannelsResponse({
  server,
  provider,
  data,
}: NormalizeGuideChannelsResponseOptions): GuideChannel[] {
  const source = createSourceFromIdentifiers({
    serverIdentifier: server.machineIdentifier,
    providerIdentifier: provider?.identifier,
  });

  const guideChannels = data || <GuideChannelResponse[]>[];

  return guideChannels.map((channel) => ({
    source,
    ...channel,
  }));
}
