/* eslint-plugin-disable @typescript-eslint */

import isAfter from 'date-fns/isAfter';
import isBefore from 'date-fns/isBefore';
import createActionCreator from 'app/js/stores/actions/createActionCreator';
import createActionHandler from 'app/js/stores/actions/createActionHandler';
import {
  findFirstIndexByTime,
  findLastIndexByTime,
} from 'app/js/stores/actions/helpers/server/findIndexByTime';
import parseServerLogTimestamp from 'app/js/stores/actions/helpers/server/parseServerLogTimestamp';
import escapeRegExp from 'app/js/utils/string/escapeRegExp';

export const CHANGE_TEXT_FILTER = 'ui/serverLogPageActions/changeTextFilter';
export const CHANGE_TIME_FILTER = 'ui/serverLogPageActions/changeTimeFilter';
export const CLEAR_FILTER = 'ui/serverLogPageActions/clearFilter';
export const ADD_LOG_LINE = 'ui/serverLogPageActions/addLogLine';
export const RESET_LOG_LEVELS = 'ui/serverLogPageActions/resetLogLevels';
export const TOGGLE_LOG_LEVEL = 'ui/serverLogPageActions/toggleLogLevel';
export const CLEAR_LOG = 'ui/serverLogPageActions/clearLog';
export const LOAD_LOG_FILE = 'ui/serverLogPageActions/loadLogfile';
export const TOGGLE_TIME_FILTER = 'ui/serverLogPageActions/toggleTimeFilter';
export const TOGGLE_RAW = 'ui/serverLogPageActions/toggleRaw';
export const HIGHLIGHT_LINE = 'ui/serverLogPageActions/highlightLine';

export const changeTextFilter = createActionCreator(CHANGE_TEXT_FILTER);
export const changeTimeFilter = createActionCreator(CHANGE_TIME_FILTER);
export const clearFilter = createActionCreator(CLEAR_FILTER);
export const addLogLine = createActionCreator(ADD_LOG_LINE);
export const resetLogLevels = createActionCreator(RESET_LOG_LEVELS);
export const toggleLogLevel = createActionCreator(TOGGLE_LOG_LEVEL);
export const clearLog = createActionCreator(CLEAR_LOG);
export const loadLogFile = createActionCreator(LOAD_LOG_FILE);
export const toggleTimeFilter = createActionCreator(TOGGLE_TIME_FILTER);
export const toggleRaw = createActionCreator(TOGGLE_RAW);
export const highlightLine = createActionCreator(HIGHLIGHT_LINE);

function addLogLineHelper(
  logLine: any,
  logLines: any,
  serverLogPageModel: any
) {
  if (!logLines.all) {
    logLines.all = [];
  }
  logLine.index = logLines.all.length;
  const filter = serverLogPageModel.get('filter');
  const timeFilterStart = serverLogPageModel.get('timeFilterStart');
  const timeFilterEnd = serverLogPageModel.get('timeFilterEnd');
  const logLevels = serverLogPageModel.get('logLevels');
  if (
    filterLogLine(logLine, {
      filter,
      timeFilterStart,
      timeFilterEnd,
      logLevels,
    })
  ) {
    if (logLines.filtered) {
      logLines.filtered.push(logLine);
    }
  } else if (!logLines.filtered) {
    logLines.filtered = logLines.all.slice(0);
  }
  logLines.all.push(logLine);
  return logLines;
}

function getServerLogPage(appModel: any) {
  return appModel.get('ui').get('serverLogPage');
}

function createFilterRegExps(filter: any) {
  return filter
    .split(/\s+/)
    .map((str: any) => new RegExp(escapeRegExp(str), 'i'));
}

function filterLogLine(line: any, options: any) {
  const {
    filter,
    timeFilterStart,
    timeFilterStartIndex,
    timeFilterEnd,
    timeFilterEndIndex,
    logLevels,
  } = options;
  const hasLevel = !logLevels.length || logLevels.indexOf(line.level) !== -1;
  if (!hasLevel) {
    return false;
  }
  if (
    timeFilterStart ||
    timeFilterStartIndex != null ||
    timeFilterEnd ||
    timeFilterEndIndex != null
  ) {
    let logLineDate;
    let startDate;
    let endDate;
    if (timeFilterStartIndex == null || timeFilterEndIndex == null) {
      logLineDate = parseServerLogTimestamp(line.time);
      if (timeFilterStartIndex == null) {
        startDate = timeFilterStart && parseServerLogTimestamp(timeFilterStart);
      }
      if (timeFilterEndIndex == null) {
        endDate = timeFilterEnd && parseServerLogTimestamp(timeFilterEnd);
      }
    }
    if (
      (timeFilterStartIndex != null && timeFilterStartIndex > line.index) ||
      (timeFilterEndIndex != null && timeFilterEndIndex < line.index) ||
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Date | undefined' is not assigna... Remove this comment to see the full error message
      (startDate && isAfter(startDate, logLineDate)) ||
      // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Date | undefined' is not assigna... Remove this comment to see the full error message
      (endDate && isBefore(endDate, logLineDate))
    ) {
      return false;
    }
  }
  if (!filter) {
    return true;
  }
  const regexps = options.regexps || createFilterRegExps(filter);
  return regexps.every(
    (regexp: any) => regexp.test(line.message) || regexp.test(line.thread)
  );
}

function filterLogLines(lines: any, options: any) {
  const regexps = createFilterRegExps(options.filter);
  return lines.filter((line: any) =>
    filterLogLine(line, { ...options, regexps })
  );
}

function updateLogLines(serverLogPageModel: any) {
  const filter = serverLogPageModel.get('filter');
  const timeFilterStart = serverLogPageModel.get('timeFilterStart');
  const timeFilterEnd = serverLogPageModel.get('timeFilterEnd');
  const logLevels = serverLogPageModel.get('logLevels');
  const unfilteredLogLines = serverLogPageModel._unfilteredLogLines;
  const timeFilterStartIndex = findFirstIndexByTime(
    unfilteredLogLines,
    timeFilterStart
  );
  const timeFilterEndIndex = findLastIndexByTime(
    unfilteredLogLines,
    timeFilterEnd
  );
  const logLines = filterLogLines(unfilteredLogLines, {
    logLevels,
    filter,
    timeFilterStart,
    timeFilterStartIndex:
      timeFilterStartIndex === -1 ? null : timeFilterStartIndex,
    timeFilterEndIndex: timeFilterEndIndex === -1 ? null : timeFilterEndIndex,
    timeFilterEnd,
  });
  serverLogPageModel.set({
    logLines,
    unfilteredLogLinesCount: unfilteredLogLines.length,
    isTimeFilterStartValid: timeFilterStartIndex !== -1,
    isTimeFilterEndValid: timeFilterEndIndex !== -1,
  });
}

const TEXT_LOG_LINE_TO_LOG_LINE_REGEX =
  /^([^[]+)\[([^\]]+)]\s+([^\s]+)\s+-\s+(.*)/;
const LOG_LEVEL_NAMES_TO_LEVELS = {
  ERROR: 0,
  WARN: 1,
  INFO: 2,
  DEBUG: 3,
  VERBOSE: 4,
};

function textLogLineToLogLine(textLogLine: any) {
  if (!textLogLine) {
    return null;
  }
  const match = textLogLine.match(TEXT_LOG_LINE_TO_LOG_LINE_REGEX);
  const [, time, thread, level, message] = match;
  return {
    time: time.trim(),
    thread: thread.trim(),
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    level: LOG_LEVEL_NAMES_TO_LEVELS[level],
    message: message.trim(),
  };
}

const serverLogPageActions = createActionHandler({
  [ADD_LOG_LINE](appModel: any, payload: any) {
    const { message: logLine } = payload;
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 2.
    const serverLogPageModel = getServerLogPage(appModel, payload);
    if (!serverLogPageModel._unfilteredLogLines) {
      serverLogPageModel._unfilteredLogLines = [];
    }
    const isFiltered =
      serverLogPageModel.get('logLines').length !==
      serverLogPageModel._unfilteredLogLines.length;
    // Get a reference to the filtered list, but don't clone it yet;
    // it may not change.
    const filtered = isFiltered ? serverLogPageModel.get('logLines') : null;
    const originalFilteredLength = isFiltered ? filtered.length : null;
    const logLines = addLogLineHelper(
      logLine,
      { all: serverLogPageModel._unfilteredLogLines.slice(0), filtered },
      serverLogPageModel
    );
    // If the list of filtered lines changed, then clone it so the new lines are
    // rendered.
    if (
      logLines.filtered &&
      logLines.filtered.length !== originalFilteredLength
    ) {
      logLines.filtered = logLines.filtered.slice(0);
    }
    serverLogPageModel._unfilteredLogLines = logLines.all;
    serverLogPageModel.set({
      unfilteredLogLinesCount: logLines.all.length,
      logLines: logLines.filtered || logLines.all,
    });
  },

  [CHANGE_TEXT_FILTER](appModel: any, payload: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    const { filter } = payload;
    serverLogPageModel.set({ filter });
    updateLogLines(serverLogPageModel);
  },

  [CHANGE_TIME_FILTER](appModel: any, payload: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    const { timeFilterStart, timeFilterEnd } = payload;
    serverLogPageModel.set({
      showTimeFilter: !!(timeFilterStart || timeFilterEnd),
      timeFilterStart,
      timeFilterEnd,
    });
    updateLogLines(serverLogPageModel);
  },

  [CLEAR_FILTER](appModel: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    serverLogPageModel.set({
      filter: '',
      timeFilterStart: '',
      timeFilterEnd: '',
      logLevels: [],
    });
    updateLogLines(serverLogPageModel);
  },

  [RESET_LOG_LEVELS](appModel: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    serverLogPageModel.set({ logLevels: [] });
    updateLogLines(serverLogPageModel);
  },

  [TOGGLE_LOG_LEVEL](appModel: any, payload: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    const logLevels = serverLogPageModel.get('logLevels');
    const nextLogLevels = logLevels.slice(0);
    const index = logLevels.indexOf(payload.logLevel);
    if (index === -1) {
      nextLogLevels.push(payload.logLevel);
    } else {
      nextLogLevels.splice(index, 1);
    }
    serverLogPageModel.set({ logLevels: nextLogLevels });
    updateLogLines(serverLogPageModel);
  },

  [CLEAR_LOG](appModel: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    serverLogPageModel.set({
      logLines: [],
      unfilteredLogLinesCount: 0,
      highlightedLineIndex: null,
      showRawText: false,
    });
    serverLogPageModel._unfilteredLogLines = [];
  },

  [LOAD_LOG_FILE](appModel: any, payload: any) {
    const { result } = payload;
    const serverLogPageModel = getServerLogPage(appModel);
    let logLines = { all: [] };
    serverLogPageModel.set({
      logLines: [],
      unfilteredLogLinesCount: 0,
    });
    try {
      result
        .split(/\n+/)
        .map(textLogLineToLogLine)
        .forEach((logLine: any) => {
          if (logLine) {
            logLines = addLogLineHelper(logLine, logLines, serverLogPageModel);
          }
        });
      serverLogPageModel._unfilteredLogLines = logLines.all;
      serverLogPageModel.set({
        unfilteredLogLinesCount: logLines.all.length,
        logLines: (logLines as any).filtered || logLines.all,
      });
    } catch (error) {
      // No-op
    }
  },

  [TOGGLE_TIME_FILTER](appModel: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    const showTimeFilter = serverLogPageModel.get('showTimeFilter');
    serverLogPageModel.set({
      timeFilterStart: '',
      timeFilterEnd: '',
      showTimeFilter: !showTimeFilter,
    });
    updateLogLines(serverLogPageModel);
  },

  [TOGGLE_RAW](appModel: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    serverLogPageModel.set(
      'showRawText',
      !serverLogPageModel.get('showRawText')
    );
  },

  [HIGHLIGHT_LINE](appModel: any, logLine: any) {
    const serverLogPageModel = getServerLogPage(appModel);
    serverLogPageModel.set('highlightedLineIndex', logLine.index);
  },
});

export default serverLogPageActions;
