/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import config from 'app/js/models/config';
import MetadataModel from 'app/js/models/metadata/MetadataModel';
import getLiveTVMediaItemID from 'app/js/utils/livetv/getLiveTVMediaItemID';
import parser2 from 'app/js/utils/parser2';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import { tryFindEntity } from 'app/js/utils/state/findEntity';

const MediaGrabOperationModel = AbstractModel.extend({
  //
  // Properties
  name: 'MediaGrabOperationModel',
  context: ['server', 'provider'],
  idAttribute: 'key',
  rootPropertyName: 'MediaGrabOperation',
  url() {
    return this.get('key');
  },
  syncedAttrs: {
    mediaSubscriptionID: attrTypes.string,
    provider: attrTypes.string,
    identifier: attrTypes.string,
    mediaIndex: attrTypes.integer,
    channelTitle: attrTypes.string,
    status: attrTypes.string,
    error: attrTypes.string,
    linkedKey: attrTypes.string,
    key: attrTypes.string,
    rolling: attrTypes.boolean,
  },
  sessionAttrs: {
    isLibraryMetadataItemPending: attrTypes.boolean,
    isLibraryMetadataItemPopulated: attrTypes.boolean,
    libraryMetadataItemError: attrTypes.object,
  },
  syncedRelations: {
    metadataItem: {
      relationClass: MetadataModel,
      rootPropertyName: 'Metadata',
    },
    conflicts() {
      return {
        relationClass: require('./MediaGrabOperationCollection').default,
        deferred: true,
      };
    },
  },
  sessionRelations: {
    libraryMetadataItem: {
      relationClass: MetadataModel,
      deferred: true,
    },
  },
  derivedAttrs: {
    mediaItem() {
      const mediaIndex = (this as any).get('mediaIndex');
      const metadataItem = (this as any).get('metadataItem');
      const mediaItems = metadataItem.get('Media') || [];
      return mediaItems[mediaIndex];
    },
    beginsAt() {
      const mediaItem = (this as any).get('mediaItem');
      return mediaItem && mediaItem.beginsAt;
    },
    endsAt() {
      const mediaItem = (this as any).get('mediaItem');
      return mediaItem && mediaItem.endsAt;
    },
  },
  defaults: {
    rolling: false,
    isLibraryMetadataItemPending: true,
    isLibraryMetadataItemPopulated: false,
    libraryMetadataItemError: null,
  },
  //
  // Control
  augmentResponse(json: any) {
    let jsonRoot = json;
    if (jsonRoot[this.rootPropertyName]) {
      jsonRoot = jsonRoot[this.rootPropertyName];
    }
    if (jsonRoot.Conflicts) {
      jsonRoot.conflicts = jsonRoot.Conflicts.MediaGrabOperation;
      delete jsonRoot.Conflicts;
    }
    if (!config.shouldCreateStateTree) {
      return jsonRoot;
    }
    // If we have several grab operations for the same item, entity management
    // will merge them into a single metadata item. Because of this, we need to
    // adjust the `mediaIndex` saved to this model to reflect the combined
    // number of `Media` elements in the resulting item.
    const responseMetadata = jsonRoot.Metadata;
    const responseMedia = parser2.parseArray(responseMetadata.Media);
    const responseMediaIndex = parser2.parseInteger(jsonRoot.mediaIndex);

    const metadataItemEntityID = createEntityID(
      entityTypes.METADATA_ITEM,
      responseMetadata.key,
      this.server.entityID()
    );
    const appModel = resolve('appModel');
    const metadataItem = tryFindEntity(
      appModel,
      entityTypes.METADATA_ITEM,
      metadataItemEntityID
    );

    // We only need to increment `mediaIndex` when the entity's `Media`
    // element does not contain the response's. If we find the element in app
    // state, we can override the response's `mediaIndex` completely.
    if (metadataItem) {
      const mediaItemIDs = metadataItem.get('Media').map(getLiveTVMediaItemID);
      const responseMediaItemIDs = responseMedia.map(getLiveTVMediaItemID);
      const responseMediaItem = responseMediaItemIDs[responseMediaIndex];
      const mediaIndex = mediaItemIDs.findIndex(
        (mediaItemID: any) => responseMediaItem === mediaItemID
      );
      if (mediaIndex !== -1) {
        jsonRoot.mediaIndex = mediaIndex;
      } else {
        jsonRoot.mediaIndex = responseMediaIndex + mediaItemIDs.length;
      }
    }
    return jsonRoot;
  },
});
export default MediaGrabOperationModel;
