/* eslint-plugin-disable @typescript-eslint */

import assert from 'app/js/utils/log/assert';
import isPlainObject from 'app/js/utils/object/isPlainObject';

function createActionHandler(actionHandlers: any) {
  if (__DEV__) {
    assert(
      isPlainObject(actionHandlers),
      'Action handlers must be plain objects'
    );

    for (const type in actionHandlers) {
      assert(
        type !== 'undefined',
        `Bad action handler name provided. Have you misspelled a constant? ${Object.keys(
          actionHandlers
        ).join(',')}`
      );
      assert(
        typeof actionHandlers[type] === 'function',
        `Action handler ${type} must be a function`
      );
    }
  }

  return actionHandlers;
}

export default createActionHandler;
