/* eslint-plugin-disable @typescript-eslint */

import findProviderContentFeature from 'app/js/views/v3/helpers/provider/findProviderContentFeature';

export default function findProviderContentFeatureDirectory(
  provider: any,
  directoryID: any
) {
  const contentFeature = findProviderContentFeature(provider);

  if (!contentFeature || !contentFeature.directories) {
    return null;
  }

  return contentFeature.directories.find((directory: any) => {
    return directory.id === directoryID;
  });
}
