/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import SectionSubsetModel from './SectionSubsetModel';

const SectionSubsetCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'SectionSubsetCollection',
  context: ['server', 'section'],

  model: SectionSubsetModel,

  url() {
    return _.result(this.section, 'url');
  },

  //
  // Control

  filterPrimaries() {
    const primaries = this.filter((subset: any) => subset.get('primary'));

    return this.clone(primaries);
  },
});

export default SectionSubsetCollection;
