/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const AuthCodeExchangeModel = AbstractModel.extend({
  //
  // Properties

  name: 'AuthCodeExchangeModel',

  sessionAttrs: {
    code: attrTypes.string,
    pinID: attrTypes.integer,
    pinIDInitial: attrTypes.integer,
    pinTimeout: attrTypes.integer,
    authToken: attrTypes.string,
    isBusy: attrTypes.boolean.isRequired,
    // Normal errors are passed along to the auth app
    errorMessage: attrTypes.string,
    // Fatal errors are displayed in the desktop app, game over
    fatalErrorMessage: attrTypes.string,
  },

  defaults: {
    code: null,
    pinID: null,
    pinIDInitial: null,
    pinTimeout: null,
    authToken: null,
    isBusy: false,
    errorMessage: null,
    fatalErrorMessage: null,
  },
});

export default AuthCodeExchangeModel;
