/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import config from 'app/js/models/config';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import signOutHelper from 'app/js/stores/actions/helpers/user/signOut';
import * as metricsEvents from 'app/js/utils/constants/metrics/metricsEvents';
import dispatcher from 'app/js/utils/dispatcher';
import alwaysResolve from 'app/js/utils/promise/alwaysResolve';

function signOutCommand() {
  resolve('metrics').trackEvent({
    event: metricsEvents.CLIENT_SIGN_OUT,
    interaction: true,
  });

  const allowSSO = config.allowSSO;
  const accessToken = resolve('appModel').get('user').get('authToken');

  return alwaysResolve(
    allowSSO
      ? signOutHelper({
          accessToken,
        })
      : $.Deferred().resolve().promise()
  )
    .then(() => commands.execute('resetUser'))
    .then(() => {
      // Switching users only clears sensitive attributes, but signing out
      // should completely reset the storage model.
      userStorageModel.restoreDefaults();

      return userStorageModel.save();
    })
    .then(() => {
      if (allowSSO) {
        signOutHelper({ accessToken });
      }

      // Kick off `serverInitializer` to discover servers and providers.
      dispatcher.trigger('init:servers');
    });
}

export default signOutCommand;
