/* eslint-plugin-disable @typescript-eslint */

import capabilities from 'app/js/utils/device/capabilities';
import createLocalStorageVote, {
  LEADER_STORAGE_KEY,
} from './createLocalStorageVote';

function createCompanionVote() {
  const maxLeaderAge = 90000; // 1.5 minutes
  const leaderRefreshTiming = 60000; // 1 minute

  const vote = createLocalStorageVote({ maxLeaderAge });

  if (capabilities.hasLocalStorage) {
    window.addEventListener('storage', (event) => {
      if (event.key === LEADER_STORAGE_KEY) {
        vote.up();
      }
    });

    window.addEventListener('beforeunload', () => {
      vote.down();
    });

    // Try to become leader immediately
    vote.up();

    // Try to become leader every minute
    window.setInterval(vote.up, leaderRefreshTiming);
  }

  return vote;
}

export default createCompanionVote;
