/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { discoverServers } from 'app/js/stores/actions/appActions';
import * as commonServerActions from 'app/js/stores/actions/serverActions';
import createUnstableServerSelector from 'app/js/stores/selectors/server/createUnstableServerSelector';
import { PLEX_APP_DOWNLOADS } from 'app/js/utils/constants/commonUrls';
import browser from 'app/js/utils/device/browser';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import useDisclosure from 'app/js/v4/core/ui/hooks/useDisclosure';
import Button from 'app/js/views/v3/components/core/buttons/Button';
import Link from 'app/js/views/v3/components/core/buttons/Link';
import ServerUnavailableErrorPage from 'app/js/views/v3/components/pages/ServerUnavailableErrorPage';
import AllowHttpFallbackModalConnector from 'app/js/views/v3/components/pages/empty/AllowHttpFallbackModalConnector';
import InsecureFallbackChoicePage from 'app/js/views/v3/components/pages/empty/InsecureFallbackChoicePage';
import * as insecureFallbackHelpers from 'app/js/views/v3/helpers/insecureFallbackHelpers';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import styles from './ServerUnavailableErrorPageConnector.css';

function createMapStateToProps() {
  return createSelector(createUnstableServerSelector(), (server) => {
    const bundledConnection = server.connections.find((connection: any) => {
      // Rely on the `connectionComparator` in `ConnectionCollection` to find
      // the first http connection. This sorts a private address before a
      // loopback if one is available
      return connection.scheme === 'http';
    });
    return {
      friendlyName: server.friendlyName,
      bundledConnectionUri: bundledConnection?.uri,
      shouldShowInsecureAppMessage:
        insecureFallbackHelpers.shouldShowInsecureAppMessage(server),
      shouldAllowInsecureFallback:
        insecureFallbackHelpers.shouldAllowInsecureFallback(server),
      isPending: server.isPending,
    };
  });
}

function createMapDispatchToProps(dispatch: any, props: any) {
  const payload = {
    serverEntityID: props.serverEntityID,
    metricsPage: props.metricsPage,
  };

  return {
    onTryAgainPress() {
      dispatch(discoverServers());
      dispatch(commonServerActions.testServer(payload));
    },
  };
}

type ServerUnavailableErrorPageConnectorProps = {
  serverEntityID: string;
  friendlyName?: string;
  bundledConnectionUri?: string;
  shouldShowInsecureAppMessage: boolean;
  shouldAllowInsecureFallback: boolean;
  isPending?: boolean;
  onTryAgainPress: (...args: any[]) => any;
};

function ServerUnavailableErrorPageConnector(
  props: ServerUnavailableErrorPageConnectorProps
) {
  const {
    friendlyName,
    bundledConnectionUri,
    shouldShowInsecureAppMessage,
    shouldAllowInsecureFallback,
    isPending,
    onTryAgainPress,
  } = props;

  const { isOpen, onOpen, onClose } = useDisclosure();

  if (shouldShowInsecureAppMessage) {
    const shouldShowDownloadButton =
      browser.platformID === 'macos' || browser.platformID === 'windows';
    return (
      <InsecureFallbackChoicePage
        title={t(
          'app.plex.tv is unable to connect to "{serverName}" securely',
          {
            serverName: friendlyName,
          }
        )}
      >
        {shouldShowDownloadButton ? (
          <div className={styles.downloadButtonContainer}>
            <Button
              kind={kinds.PRIMARY}
              target="_blank"
              to={PLEX_APP_DOWNLOADS}
            >
              {browser.platformID === 'windows'
                ? t('Download Plex for Windows')
                : t('Download Plex for Mac')}
            </Button>
          </div>
        ) : null}

        {bundledConnectionUri ? (
          <Link
            kind={kinds.PRIMARY}
            to={path.join([bundledConnectionUri, '/web/index.html'])}
          >
            {shouldShowDownloadButton
              ? t('Or you can try to navigate directly to Plex Media Server')
              : t('You can try to navigate directly to Plex Media Server')}
          </Link>
        ) : null}
      </InsecureFallbackChoicePage>
    );
  }

  if (shouldAllowInsecureFallback) {
    return (
      <InsecureFallbackChoicePage
        description={t(
          'The app is loaded insecurely. You can attempt to use an insecure connection now.'
        )}
        title={t(
          'The server "{serverName}" does not allow secure connections',
          { serverName: friendlyName }
        )}
      >
        <AllowHttpFallbackModalConnector
          isOpen={isOpen}
          serverEntityID={props.serverEntityID}
          onModalClose={onClose}
        />

        <Button kind={kinds.PRIMARY} onPress={onOpen}>
          {t('Allow Insecure Connections')}
        </Button>
      </InsecureFallbackChoicePage>
    );
  }

  return (
    <ServerUnavailableErrorPage
      friendlyName={friendlyName}
      isPending={isPending}
      onTryAgainPress={onTryAgainPress}
    />
  );
}

export default connect(
  createMapStateToProps,
  createMapDispatchToProps
)(ServerUnavailableErrorPageConnector);
