/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import HubModel from 'app/js/models/metadata/HubModel';
import getSeasonEpisodeIndex from 'app/js/utils/media/getSeasonEpisodeIndex';
import getSectionForItem from 'app/js/utils/media/getSectionForItem';
import getTitleList from 'app/js/utils/string/getTitleList';
import { getLeafType } from 'app/js/utils/types/metadataTypes';
import path from 'app/js/utils/url/path';

const contentUtil = {
  uri: function (item: any, options: any) {
    // library://uuid/(directory|item)/path
    // channel://identifier/(directory|item)/key
    // playlist:///guid/

    const items = options && options.items;
    if (items && items.length) {
      if (items.length > 1) {
        return this.getBatchLibraryMetadataURI(items, options);
      } else if (!item) {
        item = _.first(items);
      }
    }

    if (item instanceof Backbone.Model) {
      if (item.get('isSection')) {
        return this.getSectionURI(item, options);
      }

      if (item.get('isMetadataList')) {
        return this.getMetadataListURI(item, options);
      }

      if (item.get('isPlaylist')) {
        return this.getPlaylistURI(item, options);
      }
    }

    return this.getLibraryMetadataURI(item, options);
  },

  parseLibraryUri: function (uri: any) {
    const lastSegment = path.getLastSegment(uri);
    const libraryKey = decodeURIComponent(lastSegment);
    const libraryMatch = libraryKey.match(
      /(\/library\/sections\/\w+)\/all(?:\?(.*))?/
    );
    // @ts-expect-error ts-migrate(2488) FIXME: Type 'RegExpMatchArray | null' must have a '[Symbo... Remove this comment to see the full error message
    const [, sectionKey, queryString = ''] = libraryMatch;

    // We want to carefully parse the query to keep the values escaped
    const params = {};
    for (const param of queryString.split('&')) {
      const [field, value] = param.split('=');

      // Some clients send `includeCollections` etc with the library URI
      if (!field.startsWith('include')) {
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        params[field] = value;
      }
    }

    // Split out the filter and sort/type/limits
    // @ts-expect-error ts-migrate(2339) FIXME: Property 'type' does not exist on type '{}'.
    const { type, sourceType, sort, limit, ...filterParams } = params;

    // Recreate the filter string with just filters
    const filterString = Object.entries(filterParams)
      .map(([k, v]) => `${k}=${v}`)
      .join('&');

    return {
      sectionKey,
      type,
      sourceType,
      sort,
      limit,
      filterString,
    };
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  getSectionURI: function (section: any, options: any) {
    const sectionUrl = _.result(section, 'url');
    const currentSubset = section.get('currentSubset');
    const currentFolderKey = section.get('currentFolderKey');
    const currentType = section.get('currentType');
    let uriPath;

    // If we are browsing within a folder, we don't need anything else aside
    // from the folder key.
    if (currentSubset.id === 'folder') {
      if (currentFolderKey) {
        uriPath = currentFolderKey;
      } else {
        uriPath = path.join([sectionUrl, 'all']);
      }
    } else {
      const items = section.get('items');
      const params = items.getParams();

      if (params.type) {
        delete params.type;
      }

      if (params.timelineState) {
        delete params.timelineState;
      }

      // We're filtering leaves, so replace unwatchedLeaves with unwatched.
      if (params.unwatchedLeaves) {
        const leafType = getLeafType(section.get('type')).typeString;
        params[leafType + '.unwatched'] = 1;
        delete params.unwatchedLeaves;
      }

      if (currentType) {
        params.sourceType = currentType.id;
      }

      uriPath = path.join([sectionUrl, currentSubset.id], params);
      uriPath = items.replaceSpecialFilterOperators(uriPath);
    }

    return this._resolveURI({
      prefix: 'library',
      parts: [section.get('uuid'), 'directory', uriPath],
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  getBatchLibraryMetadataURI: function (items: any, options: any) {
    // If an array of items is passed in, use a directory with a comma
    // separated list of rating keys.

    const sectionInfo = this._getSectionInfo(_.first(items));
    const ratingKeys = _.map(items, function (item) {
      return item.get('ratingKey');
    });
    const uriPath = '/library/metadata/' + ratingKeys.join(',');

    return this._resolveURI({
      prefix: 'library',
      parts: [sectionInfo.sectionUUID, 'directory', uriPath],
    });
  },

  getLibraryMetadataURI: function (item: any, options: any) {
    options || (options = {});

    const sectionInfo = this._getSectionInfo(item);
    let uriPath = _.result(item, 'url');
    let type;

    if (options.excludeSeedItem) {
      uriPath = path.join(uriPath, { excludeSeedItem: 1 });
    }

    if (item.models || item.collectionName || item instanceof HubModel) {
      type = 'directory';
    } else if (item.get('radio')) {
      type = 'station';
    } else {
      type = 'item';
    }

    return this._resolveURI({
      prefix: 'library',
      parts: [sectionInfo.sectionUUID, type, uriPath],
    });
  },

  getMetadataListURI: function (item: any, options: any) {
    options || (options = {});

    const sectionInfo = this._getSectionInfo(item);

    return this._resolveURI({
      prefix: 'library',
      parts: [
        sectionInfo.sectionUUID,
        'directory',
        path.join(item.get('containerKey'), { sourceType: options.sourceType }),
      ],
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  getPlaylistURI: function (item: any, options: any) {
    return this._resolveURI({
      prefix: 'playlist',
      parts: ['', item.get('guid')],
    });
  },

  title: function (item: any, options: any) {
    const items = options && options.items;
    if (items && items.length) {
      if (items.length > 1) {
        // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 2.
        return this.getBatchLibraryMetadataTitle(items, options);
      } else {
        item = _.first(items);
      }
    }

    if (item.get('isSection')) {
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 2.
      return this.getSectionTitle(item, options);
    }

    if (item.get('isMetadataList')) {
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 2.
      return this.getMetadataListTitle(item, options);
    }

    // @ts-expect-error ts-migrate(2554) FIXME: Expected 1 arguments, but got 2.
    return this.getLibraryMetadataTitle(item, options);
  },

  getSectionTitle: function (section: any) {
    const title = section.get('title');
    const currentSubset = section.get('currentSubset');
    const currentFilters = section.get('currentFilters');

    if (currentSubset.id !== 'all') {
      return title + ': ' + currentSubset.get('title');
    }

    if (!currentFilters.length) {
      return title;
    }

    const filterTitles = _.flatten(
      _.map(currentFilters, function (currentFilter) {
        return currentFilter.titles;
      })
    );

    return title + ': ' + filterTitles.join(', ');
  },

  getMetadataListTitle: function (metadataList: any) {
    return metadataList.get('containerTitle') || metadataList.get('title');
  },

  getBatchLibraryMetadataTitle: function (items: any) {
    const titles = _.map(items, function (item) {
      return item.get('title');
    });

    return getTitleList(titles);
  },

  getLibraryMetadataTitle: function (item: any) {
    const parentTitle = item.get('grandparentTitle') || item.get('parentTitle');
    let title = item.get('title');

    if (parentTitle) {
      title = parentTitle + ' - ' + title;
    }

    if (item.get('type') === 'episode') {
      const formattedIndex = getSeasonEpisodeIndex({
        seasonIndex: item.get('parentIndex'),
        episodeIndex: item.get('index'),
        date: item.get('originallyAvailableAt'),
        noLeadingZero: true,
        separator: ' ',
      });

      title += ' (' + formattedIndex + ')';
    }

    return title;
  },

  _resolveURI: function (options: any) {
    const parts = options.parts;
    const encodedParts = _.map(parts, function (part) {
      return part ? encodeURIComponent(part) : '';
    });

    return options.prefix + '://' + encodedParts.join('/');
  },

  _getSectionInfo: function (item: any) {
    let sectionUUID = item.get('librarySectionUUID');
    let sectionID = item.get('librarySectionID');

    if (!sectionUUID || !sectionID) {
      const section = getSectionForItem(item);

      if (!_.isEmpty(section)) {
        sectionUUID = section.get('uuid');
        sectionID = section.id;
      }
    }

    return {
      sectionUUID: sectionUUID,
      sectionID: sectionID,
    };
  },
};

export default contentUtil;
