import _ from 'underscore';
import AbstractCollection from 'app/js/models/AbstractCollection';
import ScannerModel from './ScannerModel';

const ScannerCollection = AbstractCollection.extend({
  //
  // Properties

  name: 'ScannerCollection',
  context: ['server', 'sectionTypeID'],

  model: ScannerModel,

  url: function () {
    return '/system/scanners/' + this.sectionTypeID;
  },

  //
  // Control

  hasExclusiveScanner: function (exclusiveScanners: any) {
    return !!_.intersection(this.pluck('name'), exclusiveScanners).length;
  },
});

export default ScannerCollection;
