/* eslint-plugin-disable @typescript-eslint */

import { NONE } from 'app/js/utils/constants/player/subtitleStreamIDs';
import streamTypes from 'app/js/utils/types/streamTypes';
import getStreams from './getStreams';

function getSelectedSubtitles(media: any) {
  const streams = getStreams(media, {
    streamType: streamTypes.get('subtitles').id,
  });

  // Do not consider a "None" stream as selected.
  const selectedStreams = streams.filter(
    // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'stream' implicitly has an 'any' type.
    (stream) => stream.selected && stream.id !== NONE
  );

  return selectedStreams[0];
}

export default getSelectedSubtitles;
