/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import AgentCollection from 'app/js/models/server/AgentCollection';
import acceptEULA from 'app/js/stores/actions/helpers/server/acceptEULA';
import * as agentIdentifiers from 'app/js/utils/constants/agentIdentifiers';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import * as metricsPages from 'app/js/utils/constants/metrics/metricsPages';
import log from 'app/js/utils/log/log';
import { findServerModel } from 'app/js/utils/state/modelFinders';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import SignInViewReact from 'app/js/views/SignInViewReact';
import SetupBasicView from 'app/js/views/setup/SetupBasicView';
import SetupCompleteView from 'app/js/views/setup/SetupCompleteView';
import SetupErrorView from 'app/js/views/setup/SetupErrorView';
import SetupIntroView from 'app/js/views/setup/SetupIntroView';
import SetupLibraryView from 'app/js/views/setup/SetupLibraryView';
import SetupLoadingView from 'app/js/views/setup/SetupLoadingView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/setup... Remove this comment to see the full error message
import tpl from 'app/templates/setup/SetupView.tpl';

function getSubsetView(subset: any) {
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  return {
    setupSignIn: SignInViewReact,
    setupIntro: SetupIntroView,
    setupLoading: SetupLoadingView,
    setupError: SetupErrorView,
    setupBasic: SetupBasicView,
    setupLibrary: SetupLibraryView,
    setupComplete: SetupCompleteView,
  }[subset];
}

const metricsLabels = {
  setupIntro: 'intro',
  setupError: 'error',
  setupBasic: 'name',
  setupLibrary: 'library',
  setupComplete: 'complete',
};

const coreAgents = [
  agentIdentifiers.IMDB,
  agentIdentifiers.THETVDB,
  agentIdentifiers.LASTFM,
];

const SetupView = Marionette.Layout.extend({
  //
  // Properties

  className: 'container',
  template: tpl,

  maxAttempts: 120,

  ui: {
    header: '.setup-header',
  },

  regions: {
    container: '.setup-container',
  },

  events: {
    'click .segment-btn': 'onNavClick',
    'click .next-btn': 'onNavClick',
    'click .done-btn': 'onDoneClick',
  },

  onlyAfterRender: ['onAgentsSuccess', 'onAgentsFail', 'onUpdatesComplete'],

  //
  // Init

  initialize: function (options: any) {
    _.bindAll(
      this,
      'start',
      'onAgentsSuccess',
      'onAgentsFail',
      'onUpdatesComplete',
      'onSkipSignInChange'
    );

    this.subset = options.subset || 'setupIntro';
    this.attempts = 0;
    this._visitedSubsets = {};

    log.info('Starting the setup wizard');

    const appModel = resolve('appModel');
    this.model = findServerModel(appModel, options);
    this.listenTo(appModel, 'change:skipSignIn', this.onSkipSignInChange);
  },

  //
  // Control

  serializeData: function () {
    return {
      subset: this.subset,
    };
  },

  renderSubset: function () {
    if (this.model.get('shared')) {
      this.navigate('setupError');
      return;
    }

    const appModel = resolve('appModel');
    const userModel = appModel.get('user');
    const cloudServer = userModel.server;

    const cloudServerPromise = cloudServer.populate();
    // This promise resolves when the user is authenticated at plex.tv
    cloudServerPromise.done(
      this.whenRendered(() => {
        this.navigate('setupIntro');
      })
    );

    cloudServerPromise.fail(() => {
      const primaryServer = appModel.get('primaryServer');
      const primaryServerFlexModel = primaryServer.get(__PLATFORM__);

      const acceptedEULAPromise = primaryServerFlexModel
        .get('settings')
        .fetch()
        .then((settings: any) => {
          return settings.get('AcceptedEULA').get('value') === true
            ? $.Deferred().resolve()
            : $.Deferred().reject();
        });

      // The cloud server promise fails when the user is unauthenticated OR
      // when plex.tv is down.
      acceptedEULAPromise
        .then(
          this.whenRendered(() => {
            this.navigate('setupIntro');
          })
        )
        .fail(
          this.whenRendered(() => {
            const skipSignIn = appModel.get('skipSignIn');
            const isCloudServerUnavailable = cloudServer.get('isUnavailable');
            // If plex.tv is down, or the user has explicitly skipped sign-in, go
            // directly to the setup intro
            this.navigate(
              skipSignIn || isCloudServerUnavailable
                ? 'setupIntro'
                : 'setupSignIn'
            );
          })
        );
    });
  },

  start: function () {
    // If we let the user try to add sections or install channels before
    // everything is ready and updated, we could run into trouble. First,
    // check the startState passed back at the root of the server. Then,
    // check the /system/agents request succeeds. Then, make sure all
    // plug-ins are up to date.

    // After a certain number of attempts, just give up and show the error.
    if (this.attempts >= this.maxAttempts) {
      log.error(
        'Failed to start the setup wizard after ' + this.attempts + ' attempts'
      );
      this.navigate('setupError');

      return;
    }

    this.attempts++;

    const startState = this.model.get('startState');

    if (startState) {
      log('Waiting for server to start (' + startState + ')');

      // If the startState is not null, that means the media server is still
      // starting. Keep fetching details for the server so we know when
      // startState changes.
      this.model.fetch().done(_.delay(this.start, 500));
    } else {
      const agents = new AgentCollection([], { server: this.model });
      const agentsPromise = agents.fetch({ timeout: 0 });

      agentsPromise.done(this.onAgentsSuccess);
      agentsPromise.fail(this.onAgentsFail);
    }
  },

  navigate: function (subset: any) {
    const isIntroStep = subset === 'setupIntro';
    const isSignInStep = subset === 'setupSignIn';
    const isLoadingStep = subset === 'setupLoading';

    const View = getSubsetView(subset);
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const metricsLabel = metricsLabels[subset];

    this.previousSubset = this.subset;
    this.subset = subset;

    if (metricsLabel) {
      resolve('metrics').trackView({
        page: metricsPages.SETUP_WIZARD,
        pane: metricsLabel,
        firstRun: true,
      });
    }

    const isFirstVisitToSubset = !this._visitedSubsets[subset];
    this._visitedSubsets[subset] = true;

    const view = new View({
      model: this.model,
      isFirstRun: true,
      isFirstVisitToSubset,
    });
    this.container.show(view);

    this.ui.header.toggleClass('hidden', isIntroStep || isSignInStep);

    this.$el.addClass(subset);

    if (this.previousSubset !== subset) {
      this.$el.removeClass(this.previousSubset);
    }

    if (isLoadingStep) {
      this.start();
    }
  },

  //
  // Listeners

  onRender: function () {
    this.renderSubset();
  },

  onAgentsSuccess: function (agents: any) {
    log('Looking for a core agent');

    // Check if one of the three "core" agents is listed. If none of them
    // are there, we probably have a problem.
    const coreAgent = coreAgents.find((agent) => agents.get(agent));

    if (coreAgent) {
      log(coreAgent + ' found');

      // Accept EULA asynchronously, don't block UI
      acceptEULA(this.model);

      commands
        .execute('updateChannel', { server: this.model, silent: true })
        .always(this.onUpdatesComplete);
    } else {
      log.error('No core agents found');
      this.navigate('setupError');
    }
  },

  onAgentsFail: function () {
    log.warn('Unable to request agents');

    // If the agents request fails, the media server might still be getting
    // ready, so try again.
    _.delay(this.start, 500);
  },

  onUpdatesComplete: function () {
    this.navigate('setupBasic');
  },

  onNavClick: function (event: any) {
    event.preventDefault();

    const $target = $(event.currentTarget);
    const subset = $target.data('subset');
    const isSkippingBasicForm =
      $target.hasClass('segment-btn') && this.subset === 'setupBasic';

    // Hack to simulate the form submission to save the basic pref info if
    // navigation tabs are clicked.
    if (isSkippingBasicForm && this.container.currentView) {
      event.preventDefault();
      this.container.currentView.submit();
    } else {
      this.navigate(subset);
    }
  },

  onDoneClick: function (event: any) {
    event.preventDefault();

    resolve('metrics').trackClick({
      page: metricsPages.SETUP_WIZARD,
      action: metricsActions.COMPLETE,
      firstRun: true,
    });

    navigateToRoute('home');
  },

  onSkipSignInChange() {
    this.renderSubset();
  },
});

export default SetupView;
