import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertFormLocationView.tpl';

const ConvertFormLocationView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'convert-location-container form-group',
  template: tpl,

  events: {
    'change select[name=storageLocation]': 'onLocationChange',
  },

  //
  // Control

  serializeData: function () {
    return {
      disableLocation: !this.model.isNew(),
      selectedLocationID: this.model.get('locationID'),
      locations: this.model.section?.get('locations').toJSON(),
    };
  },

  //
  // Listeners

  onLocationChange: function (event: any) {
    this.model.set('locationID', parseInt(event.currentTarget.value, 10));
  },
});

export default ConvertFormLocationView;
