/* eslint-plugin-disable @typescript-eslint */

import BaseModel from 'app/js/models/BaseModel';
import SortCollection from 'app/js/models/library/section/SortCollection';
import PlaylistListModel from 'app/js/models/metadata/PlaylistListModel';

const PlaylistsSectionModel = BaseModel.extend({
  //
  // Properties

  name: 'PlaylistsSectionModel',

  defaults() {
    return {
      pseudo: true,
      currentType: 0,
      sorts: new SortCollection([], { section: this, server: this.server }),
      items: new PlaylistListModel({}, { section: this, server: this.server }),
    };
  },

  url() {
    // Used by the SortCollection when querying available sorts.
    return '/playlists';
  },
});

export default PlaylistsSectionModel;
