/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';

function reprocessVersionGeneratorCommand(options: any) {
  const generator = options.generator;
  const ratingKey = options.ratingKey;
  let url = _.result(generator, 'url');

  if (ratingKey) {
    url += '/' + ratingKey;
  }

  url += '/reprocess';

  const xhrOptions = _.extend(generator.server.getXHROptions(url), {
    type: 'PUT',
  });
  const promise = $.ajax(xhrOptions).promise();

  // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
  promise.fail(function (xhr) {
    const message = ratingKey
      ? t('There was a problem processing this item.')
      : t('There was a problem processing these items.');

    dispatcher.trigger('show:alert', {
      type: 'error',
      message: message,
    });
  });

  return promise;
}

export default reprocessVersionGeneratorCommand;
