import log from 'app/js/utils/log/log';

type TypeNormalizerFunction<NormalizedType> = <Input>(
  input: { [K in keyof Input]: Input[K] | NormalizedType },
  propertyName: keyof Input,
  targetPropertyName?: keyof Input
) => void;

function createPropertyTypeNormalizer<NormalizedType>(
  normalizeAsType: (input: unknown, propertyName: string) => NormalizedType
): TypeNormalizerFunction<NormalizedType> {
  return (input, propertyName, targetPropertyName) => {
    const normalizedValue = normalizeAsType(
      input[propertyName],
      propertyName as string
    );

    if (targetPropertyName) {
      input[targetPropertyName] = normalizedValue;
      delete input[propertyName];
    } else {
      input[propertyName] = normalizedValue;
    }
  };
}

export const normalizeAsNumber = createPropertyTypeNormalizer(
  (value: unknown, propertyName: string): number | undefined => {
    const valueType = typeof value;

    if (valueType === 'string' || valueType === 'number') {
      return Number(value);
    } else if (valueType !== 'undefined') {
      log.warn(
        `normalizeAsNumber: returning undefined for type: ${valueType}, property: ${propertyName}`
      );
    }

    return undefined;
  }
);

export const normalizeAsBoolean = createPropertyTypeNormalizer(
  (value: unknown, propertyName: string): boolean | undefined => {
    const valueType = typeof value;

    if (valueType === 'boolean' || valueType === 'number') {
      return Boolean(value);
    } else if (valueType === 'string') {
      return value === '1';
    } else if (valueType !== 'undefined') {
      log.warn(
        `normalizeAsBoolean: returning undefined for type: ${valueType}, property: ${propertyName}`
      );
    }

    return undefined;
  }
);

export const normalizeAsString = createPropertyTypeNormalizer(
  (value: unknown, propertyName: string): string | undefined => {
    const valueType = typeof value;

    if (value != null) {
      return String(value);
    } else if (valueType !== 'undefined') {
      log.warn(
        `normalizeAsNumber: returning undefined for type: ${valueType}, property: ${propertyName}`
      );
    }

    return undefined;
  }
);

export function move<I>(
  input: { [K in keyof I]?: I | I[K] },
  target: { [K in keyof I]?: I | I[K] },
  propertyName: keyof I
): void {
  if (input.hasOwnProperty(propertyName)) {
    target[propertyName] = input[propertyName];
    delete input[propertyName];
  }
}

export function copy<I>(
  input: { [K in keyof I]?: I | I[K] },
  target: { [K in keyof I]?: I | I[K] },
  propertyName: keyof I
): void {
  if (input.hasOwnProperty(propertyName)) {
    target[propertyName] = input[propertyName];
  }
}
