/* eslint-plugin-disable @typescript-eslint */

import { createSelector } from 'reselect';
import selectAllProviderEntities from './selectAllProviderEntities';

export default function createUnstableProviderSelector() {
  return createSelector(
    // @ts-expect-error ts-migrate(6133) FIXME: 'appState' is declared but its value is never read... Remove this comment to see the full error message
    (appState: any, { providerEntityID }: any) => providerEntityID,
    selectAllProviderEntities,
    (providerEntityID, providerEntities) => providerEntities[providerEntityID]
  );
}
