/* eslint-plugin-disable @typescript-eslint */

import AbstractCollection from 'app/js/models/AbstractCollection';
import config from 'app/js/models/config';
import * as responseTypes from 'app/js/models/responseTypes';
import path from 'app/js/utils/url/path';
import ResourceModel from './ResourceModel';

const ResourceCollection = AbstractCollection.extend({
  //
  // Properties
  name: 'ResourceCollection',
  model: ResourceModel,
  context: ['server'],
  responseType: responseTypes.json,
  url() {
    const params = {};
    // Some platforms don't support https connections to media servers. Only
    // include secure plex.direct https connections if supported.
    if (config.allowHttpsMediaServerConnections) {
      (params as any).includeHttps = 1;
    }
    // Some platforms don't support relay connections to media servers. Only
    // include relay connections if supported.
    if (config.allowMediaServerRelayConnections) {
      (params as any).includeRelay = 1;
    }
    if (config.allowIPv6ServerConnections) {
      (params as any).includeIPv6 = 1;
    }
    return path.join('/api/v2/resources', params);
  },
});
export default ResourceCollection;
