/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import config from 'app/js/models/config';
import BaseClass from 'app/js/utils/BaseClass';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';
import parser2 from 'app/js/utils/parser2';
import xmlWriter from 'app/js/utils/xmlWriter';

function resolveAllAndClear(deferreds: any) {
  deferreds.forEach((deferred: any) => {
    deferred.resolve();
  });
  deferreds.length = 0;
}

function rejectAllAndClear(deferreds: any) {
  deferreds.forEach((deferred: any) => {
    deferred.reject();
  });
  deferreds.length = 0;
}

const POLL_RETRY_DELAY = 1000;

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const MediaServerCompanionProxy = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Properties

  name: 'MediaServerCompanionProxy',

  commandID: 0,

  //
  // Init

  initialize() {
    _.bindAll(
      this,
      '_checkPollRequestStatus',
      '_pickProxyServer',
      '_poll',
      '_repoll',
      '_sendResponse',
      '_unregisterPlayer',
      'onPollSuccess',
      'onPollError'
    );

    this._debounceUnregisterPlayer = _.debounce(this._unregisterPlayer, 2000);
    this._debouncePickProxyServer = _.debounce(this._pickProxyServer, 2000);
    this._deepLinkDeferred = $.Deferred().resolve();
    this._playerRegistrationDeferreds = [];
    this._playerUnregistrationDeferreds = [];
    this._pollRetryDelay = POLL_RETRY_DELAY;
  },

  //
  // Control

  getDeepLink() {
    return this._deepLinkDeferred.promise();
  },

  publish(timeline: any) {
    if (!this.proxyServer) {
      return;
    }

    if (this.timelineInflight) {
      // we only need to keep the last timeline,
      this._pendingTimeline = timeline;
      return;
    }

    this.timelineInflight = true;

    const xml = xmlWriter.writeXml(timeline);

    const url = _.result(this.proxyServer, 'url');
    const xhrOptions = this.proxyServer.getXHROptions(
      `${url}player/proxy/timeline?commandID=${this.commandID}`
    );

    xhrOptions.data = `timeline=${encodeURIComponent(xml)}`;
    xhrOptions.method = 'POST';
    xhrOptions.contentType = 'application/x-www-form-urlencoded';

    $.ajax(xhrOptions).always(() => {
      this.timelineInflight = false;
      const pendingTimeline = this._pendingTimeline;
      if (pendingTimeline) {
        delete this._pendingTimeline;
        this.publish(pendingTimeline);
      }
    });
  },

  subscribe(callback: any) {
    if (this.started) {
      return;
    }

    this.callback = callback;
    this.started = true;
    this.appModel = resolve('appModel');

    this.listenTo(
      this.appModel,
      'change:companionServer',
      this._debouncePickProxyServer
    );
    this._debouncePickProxyServer(
      this.appModel,
      this.appModel.get('companionServer')
    );
  },

  unsubscribe() {
    if (!this.started) {
      return;
    }

    const activeProxyServer = this.proxyServer;

    this.started = false;
    this.stopListening(
      this.servers,
      'change:connected',
      this._debouncePickProxyServer
    );
    this.callback = null;
    this.proxyServer = null;

    if (activeProxyServer) {
      this._debounceUnregisterPlayer(activeProxyServer);
    }

    this._clearPollRequestStatusCheck();

    if (this.pollRequest) {
      this.pollRequest.abort();
      this.pollRequest = null;
    }
  },

  unregisterPlayer(cloudServer: any, clientID: any) {
    this.cloudServer = cloudServer;
    this.clientID = clientID;

    const deferred = $.Deferred();
    this._playerUnregistrationDeferreds.push(deferred);

    return deferred.promise();
  },

  registerPlayer(server: any, clientID: any) {
    this.cloudServer = server;
    this.clientID = clientID;

    const deferred = $.Deferred();
    this._playerRegistrationDeferreds.push(deferred);

    return deferred.promise();
  },

  debugStatus() {
    const server = this.proxyServer;
    return server ? `Proxied: ${server.get('debugName')}` : undefined;
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'appModel' is declared but its value is never read... Remove this comment to see the full error message
  _pickProxyServer(appModel: any, proxyServer: any) {
    if (!this.started) {
      return;
    }

    this.proxyServer = proxyServer;

    if (this.proxyServer) {
      this._registerServer();
      this._poll();
    }
  },

  _registerServer() {
    if (!this.proxyServer || !this.cloudServer || !this.clientID) {
      // Companion proxy not initialized yet
      return;
    }

    const xhrOptions = this.cloudServer.getXHROptions(
      `devices/${this.clientID}`,
      {
        method: 'POST',
        headers: {
          'X-Plex-Provides': config.allowCloudPubSub
            ? 'client,player,pubsub-player'
            : 'client,player',
        },
        data: {
          // This helps us avoid CORS requests in opera since POSTs require a CORS
          // OPTIONS request, but PUT doesn't.
          // See: https://github.com/plexinc/plex-web-client/issues/2940
          _method: 'PUT',
          proxiedBy: this.proxyServer.id,
        },
      }
    );

    $.ajax(xhrOptions)
      .done(() => {
        resolveAllAndClear(this._playerRegistrationDeferreds);
      })
      .fail(() => {
        rejectAllAndClear(this._playerRegistrationDeferreds);
      });
  },

  _unregisterPlayer(proxyServer: any) {
    if (!proxyServer || !this.cloudServer || !this.clientID) {
      return;
    }

    const xhrOptions = this.cloudServer.getXHROptions(
      `/devices/${this.clientID}`,
      {
        method: 'POST',
        headers: {
          'X-Plex-Provides': 'client',
        },
        data: {
          _method: 'PUT',
        },
      }
    );

    $.ajax(xhrOptions)
      .done(() => {
        resolveAllAndClear(this._playerUnregistrationDeferreds);
      })
      .fail(() => {
        rejectAllAndClear(this._playerUnregistrationDeferreds);
      });
  },

  _poll() {
    if (!this.started) {
      return;
    }

    const proxyServer = this.proxyServer;

    if (!proxyServer) {
      log.info('[Companion] No proxy server available to long poll');
      return;
    } else if (this._isPollRequestPending()) {
      log.info(
        '[Companion] Long poll already open to ',
        proxyServer.get('debugName')
      );
      return;
    }

    // Wait for a delay then check if the poll is still pending. If it is still
    // pending then assume it is safe to reset the poll retry delay.
    this._clearPollRequestStatusCheck();
    this._checkPollRequestStatusLater();
    this._clearRepollLater();

    const xhrOptions = proxyServer.getXHROptions('/player/proxy/poll', {
      data: {
        deviceClass: 'pc',
        protocolVersion: '3',
        protocolCapabilities: 'timeline,playback,navigation,mirror,playqueues',
        // Allow PMS to timeout our poll request after a delay.
        timeout: 1,
      },
    });

    log.info(
      '[Companion] Opening long poll to %s at %s',
      proxyServer.get('debugName'),
      xhrOptions.url
    );

    this.pollRequest = $.ajax(xhrOptions)
      .done(this.onPollSuccess)
      .fail(this.onPollError);
  },

  _repoll() {
    // Clean up from any previous polls and open a new async connection.
    this._clearPollRequestStatusCheck();
    this._clearRepollLater();
    this._poll();
  },

  _isPollRequestPending() {
    return this.pollRequest ? this.pollRequest.state() === 'pending' : false;
  },

  _checkPollRequestStatus() {
    if (this._isPollRequestPending() && this.proxyServer) {
      log.info(
        '[Companion] Poll connection successfully opened with %s',
        this.proxyServer.get('debugName')
      );
      this._pollRetryDelay = POLL_RETRY_DELAY;
    }
  },

  _checkPollRequestStatusLater() {
    this._pollRequestStatusCheckTimeout = window.setTimeout(
      this._checkPollRequestStatus,
      5000
    );
  },

  _clearPollRequestStatusCheck() {
    this._pollRequestStatusCheckTimeout = window.clearTimeout(
      this._pollRequestStatusCheckTimeout
    );
  },

  _repollLater() {
    this._pollRetryTimeout = window.setTimeout(
      this._repoll,
      this._pollRetryDelay
    );
  },

  _clearRepollLater() {
    this._pollRetryTimeout = window.clearTimeout(this._pollRetryTimeout);
  },

  _processCommand(command: any) {
    if (!this.started) {
      return;
    }

    const signal = {
      path: command.path,
      commandID: command.commandID,
      supportsTimeline: true,
      query: {
        key: [command.queryKey],
        providerIdentifier: [command.queryProviderIdentifier],
        offset: [command.queryOffset],
        mediaIndex: [command.queryMediaIndex],
        containerKey: [command.queryContainerKey],
        playQueueID: [command.queryPlayqueueid],
        type: [command.queryType],
        volume: [command.queryVolume],
        repeat: [command.queryRepeat],
        shuffle: [command.queryShuffle],
        autoPlay: [command.queryAutoPlay],
        subtitleColor: [command.querySubtitleColor],
        subtitlePosition: [command.querySubtitlePosition],
        subtitleSize: [command.querySubtitleSize],
        subtitleOffset: [command.querySubtitleOffset],
        audioStreamID: [command.queryAudioStreamID],
        subtitleStreamID: [command.querySubtitleStreamID],

        token: [command.queryToken],
        machineIdentifier: [command.queryMachineIdentifier],

        protocol: [command.queryProtocol],
        address: [command.queryAddress],
        port: [command.queryPort],

        text: [command.queryText],
        field: [command.queryField],
      },
    };

    const stillWaitingForCommand =
      this._deepLinkDeferred.state() !== 'resolved';

    this.callback(signal, stillWaitingForCommand).always((result: any) => {
      if (stillWaitingForCommand && result && result.deferredAction) {
        this._deepLinkDeferred.resolve(result.deferredAction);
      }
      this._sendResponse(result.controllerResponse);
    });
  },

  _sendResponse(resp: any) {
    if (!this.proxyServer) {
      return;
    }

    const commandID = parseInt(resp.commandID, 10);

    if (this.commandID < commandID) {
      this.commandID = commandID;
    }

    // The PMS proxy requires `commandID` be present as GET parameter. Sending
    // as POST data results in a `400 Bad Request` from PMS and may strand
    // controllers in inconsistent states.
    // See: https://github.com/plexinc/plex-web-client/issues/6427
    const url = `/player/proxy/response?commandID=${resp.commandID}`;

    const xhrOptions = this.proxyServer.getXHROptions(url, {
      method: 'POST',
      contentType: 'application/x-www-form-urlencoded',
      data: {
        // Encoding is done by the `$.ajax()` transport before sending.
        response: resp.body,
      },
    });

    $.ajax(xhrOptions);
  },

  //
  // Listeners

  onPollSuccess(data: any) {
    const json = data && parser2.parseXML(data);
    const Command = json && json.Command;

    // An empty response could indicate one of two things:
    //
    //   * The server has timed out the poll.
    //   * The server is shutdown (gracefully or abruptly). This can result
    //     in either an empty successful response or a failed request.
    //
    // We cannot distinguish between the two so we assume it's the former
    // and repoll. If the server has shutdown then the repoll will fail
    // and hit the `onPollError` path.
    if (!data) {
      this._repoll();
      return;
    }

    if (!json) {
      log.warn('[Companion] Poll succeeded without a parseable response', data);
    } else if (!Command) {
      log.warn('[Companion] Poll succeeded without a Command response', json);
    }

    // Guard against misbehaving servers.
    if (!Command) {
      this.onPollError();
      return;
    }

    // Open up a new poll before handling commands.
    this._repoll();

    const commands = Array.isArray(Command) ? Command : [Command];

    commands.forEach((command) => {
      try {
        log.info(
          '[Companion] Received %s from %s',
          command.path,
          command.deviceName
        );
        this._processCommand(command);
      } catch (error) {
        log.warn(
          '[Companion] encountered %s while processing %s',
          error.message,
          command
        );
      }
    }, this);
  },

  // Poll errors occur when:
  //
  //   * The server is shutdown (gracefully or abruptly). This can result
  //     in either an empty successful response or a failed request.
  //   * The connection is aborted.
  //   * The connection is terminated.
  //   * The connection succeeds yet returns an unexpected response.
  //
  // After poll errors retry on a delay
  // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
  onPollError(xhr = {}) {
    const proxyServer = this.proxyServer;

    if (!this.started || !proxyServer) {
      return;
    }

    this._clearPollRequestStatusCheck();
    this._clearRepollLater();

    this._repollLater();

    this._pollRetryDelay *= 2;

    const debugName = proxyServer.get('debugName');
    log.warn('[Companion] Poll failed to %s', debugName);
  },
});

export default MediaServerCompanionProxy;
