/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';

const defaultMessage = '`assert` requires an error message argument';

function assert(condition: any, message = defaultMessage, isDev = __DEV__) {
  if (condition) {
    return true;
  }

  // Always log errors. React will entirely swallow errors thrown during
  // component mounting causing assertions to fail silently.
  // See: https://github.com/plexinc/plex-web-client/pull/5097
  log.error(message);

  if (isDev) {
    throw new Error(message);
  }

  return false;
}

export default assert;
