import _ from 'underscore';
import Obscura from 'app/js/libs/backbone/backbone.obscura-0.1.7';

const ExclusiveScannerProxy = Obscura.extend({
  constructor: function (
    scanners: any,
    exclusiveScanners: any,
    scannerName: any,
    options: any
  ) {
    this.exclusiveScanners = exclusiveScanners;
    this.scannerName = scannerName;

    Obscura.call(this, scanners, options);
    this.filterBy(_.bind(this.filterExclusiveScanner, this));
  },

  filterExclusiveScanner: function (scanner: any) {
    const name = scanner.get('name');
    const exclusiveScanners = this.exclusiveScanners;

    // If the sectionType has no exclusive agent list,
    // this agent passes the filter.
    if (!exclusiveScanners) {
      return true;
    }

    const currentScanner = this.scannerName;
    const isCurrentFoundInExclusives =
      exclusiveScanners.indexOf(currentScanner) !== -1;
    const isScannerFoundInExclusives = exclusiveScanners.indexOf(name) !== -1;

    // If the section being shown belongs to an exclusive agent, only show
    // that agent. Otherwise, show every agent that isn't in the list of
    // exclusive agents.
    const result = isCurrentFoundInExclusives
      ? name === currentScanner
      : !isScannerFoundInExclusives;

    return result;
  },
});

export default ExclusiveScannerProxy;
