/* eslint-plugin-disable @typescript-eslint */

import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';

function deleteVersionGeneratorCommand(options: any) {
  const generator = options.generator;
  const confirmModal = showConfirmation(options);

  const promise = confirmModal.promise.then(function () {
    const deletePromise = generator.destroy();

    // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
    deletePromise.fail(function (xhr: any) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('Unable to stop generating these versions.'),
      });
    });

    deletePromise.always(function () {
      confirmModal.hide();
    });

    return deletePromise;
  });

  return promise;
}

function showConfirmation(options: any) {
  const title = options.generator.get('title');
  const message = title
    ? t(
        'Removing this item will delete all versions created by {1}. Are you sure you want to continue?',
        title
      )
    : t(
        'Removing this item will delete all related versions. Are you sure you want to continue?'
      );

  const confirmModal = new ConfirmModal({
    title: t('Delete Versions'),
    message: message,
    confirmLabel: t('Remove'),
    cancelLabel: t('Cancel'),
    async: true,
    hasDanger: true,
  });

  dispatcher.trigger('show:modal', confirmModal);

  return confirmModal;
}

export default deleteVersionGeneratorCommand;
