/* eslint-plugin-disable @typescript-eslint */

import addSeconds from 'date-fns/addSeconds';
import fromUnixTime from 'date-fns/fromUnixTime';
import getUnixTime from 'date-fns/getUnixTime';
import relativeTime from 'app/js/utils/dateTime/relativeTime';

export default function formatTimeFromNow(
  timestamp: any,
  { limit }: any = {},
  shorthand = false
) {
  if (!timestamp) {
    return '';
  }

  const now = new Date();

  // Round to eliminate floating point timestamps occurring when callers pass in
  // a unix time with a decimal.
  timestamp = Math.round(timestamp);

  if (limit === 'past') {
    timestamp = Math.min(timestamp, getUnixTime(now));
  } else if (limit === 'future') {
    timestamp = Math.max(timestamp, getUnixTime(addSeconds(now, 1)));
  }

  const then = fromUnixTime(timestamp);
  const duration = then.getTime() - now.getTime();

  let isFuture;

  if (limit === 'future') {
    isFuture = true;
  } else if (limit === 'past') {
    isFuture = false;
  } else {
    isFuture = duration > 0;
  }

  return relativeTime({
    milliseconds: duration,
    isShorthand: shorthand,
    isFuture,
  });
}
