/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';
import getPerformanceReportingHost from 'app/js/utils/metrics/getPerformanceReportingHost';

const VERBOSE = __DEV__ && false;

export function createPerformanceUtil(
  getReportingHost = getPerformanceReportingHost
) {
  const isSupportedByName = {};

  function isSupported(name: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    if (name && !isSupportedByName[name]) {
      // Plex Metrics event names are always supported for latency reporting.
      // See: https://github.com/plexinc/plex-web-client/issues/10070
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      isSupportedByName[name] =
        /^(client|playback):/.test(name) || !!getReportingHost();
    }

    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return !!name && isSupportedByName[name];
  }

  const performance = {
    startPeriod(name: any) {
      return performance.mark(`${name}-start`);
    },

    endPeriod(name: any, startMark: any) {
      // @ts-expect-error ts-migrate(2554) FIXME: Expected 3 arguments, but got 2.
      return performance.measure(name, startMark || `${name}-start`);
    },

    mark(name: any) {
      let result = null;

      if (isSupported(name)) {
        __DEV__ && log('[Performance] Marking "%s"', name);

        window.performance.mark(name);

        const marks = window.performance.getEntriesByName(name);

        result = marks[marks.length - 1];
      } else if (VERBOSE) {
        log('[Performance] Ignoring mark "%s"', name);
      }

      return result;
    },

    measure(name: any, startMark: any, endMark: any) {
      let result = null;

      if (isSupported(name)) {
        try {
          window.performance.measure(name, startMark, endMark);

          window.performance.clearMarks(startMark);

          const measures = window.performance.getEntriesByName(name);

          result = measures[measures.length - 1];

          __DEV__ &&
            log(
              '[Performance] Measure "%s" duration %dms',
              name,
              result.duration
            );
        } catch (error) {
          // `performance.measure()` throws if `startMark` is not found.
          __DEV__ && log('[Performance]', error.message);
        }
      } else if (VERBOSE) {
        log('[Performance] Ignoring measure "%s"', name);
      }

      return result;
    },
  };

  return performance;
}

export default createPerformanceUtil();
