import { SagaIterator } from 'redux-saga';
import {
  all,
  call,
  cancel,
  fork,
  put,
  select,
  take,
  takeLeading,
} from 'redux-saga/effects';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import {
  BestEPGLineupSearchRequestedAction,
  ChannelMappingsFetchRequestedAction,
  EPGCountriesFetchRequestedAction,
  EPGCountryFlavor,
  EPGCountryRegionsFetchRequestedAction,
  EPGLanguagesFetchRequestedAction,
  EPGLineupGroupFetchRequestedAction,
  EPGSettingsState,
  SelectedDVRLineupChannelsFetchRequestedAction,
  SelectedDVRState,
  SelectedEPGLineupGroupAddRequestedAction,
} from 'app/js/v4/features/dvrManagement/dvrManagementTypes';
import dvrSetupSlice from 'app/js/v4/features/dvrManagement/dvrSetupSlice';
import epgSettingsSlice from 'app/js/v4/features/dvrManagement/epgSettingsSlice';
import selectDVRSetup from 'app/js/v4/features/dvrManagement/selectors/selectDVRSetup';
import selectEPGSettings from 'app/js/v4/features/dvrManagement/selectors/selectEPGSettings';
import normalizeChannelMappingResponse from 'app/js/v4/features/dvrManagement/utils/normalizeChannelMappingResponse';
import normalizeLineupChannelsResponse from 'app/js/v4/features/dvrManagement/utils/normalizeLineupChannelsResponse';
import { APIMediaContainerResponse } from 'app/js/v4/types/MediaContainerResponse';
import api, { APIResponse } from 'app/js/v4/utils/data/api';

interface EPGLineupGroupUrlParams {
  postalCode?: string;
}

//
// Workers

export function* fetchChannelMappings({
  payload,
}: ChannelMappingsFetchRequestedAction): SagaIterator {
  const { server, device, lineups } = payload;
  const lineupUUIDs = lineups.map((lineup) => lineup.uuid);

  try {
    const lineupChannels = yield call(fetchLineupChannels, server, lineupUUIDs);

    const response: APIMediaContainerResponse = yield call(
      api.get,
      '/livetv/epg/channelmap',
      {
        server,
        params: {
          device: device.uuid,
          lineup: lineupUUIDs,
        },
      }
    );

    const channelMappings = normalizeChannelMappingResponse(
      response.data.MediaContainer.ChannelMapping
    );

    yield put(
      epgSettingsSlice.actions.channelMappingsFetchSucceeded({
        channelMappings,
        lineupChannels,
      })
    );
  } catch (error) {
    yield put(
      epgSettingsSlice.actions.channelMappingsFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );

    let errorMessage = t(
      'There was a problem fetching channel mappings. Please try again later.'
    );

    if (error.response?.data?.MediaContainer?.message) {
      errorMessage = error.response.data.MediaContainer.message;
    }

    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: errorMessage,
      })
    );
  }
}

export function* searchBestEPGLineup({
  payload,
}: BestEPGLineupSearchRequestedAction): SagaIterator {
  const { server, device, lineupGroup } = payload;

  try {
    yield put(dvrSetupSlice.actions.setupErrorMessageRemoved());

    const response: APIResponse = yield call(
      api.get,
      path.join('/livetv/epg/lineup', {
        device: device.uuid,
        lineupGroup: lineupGroup.uuid,
      }),
      { server }
    );

    yield put(
      epgSettingsSlice.actions.bestEPGLineupSearchSucceeded({
        lineupGroup,
        data: response.headers['x-plex-activity'],
      })
    );
  } catch (error) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t('There was a problem mapping channels'),
      })
    );
  }
}

