export function* fetchEPGLineupGroup({
  payload,
}: EPGLineupGroupFetchRequestedAction): SagaIterator {
  const { server, country, region, postalCode } = payload;

  const urlSegments = ['/livetv/epg/countries', country.key];
  const urlParams: EPGLineupGroupUrlParams = {};

  if (country.flavor === EPGCountryFlavor.Regions && region) {
    urlSegments.push(region.key);
  } else {
    urlParams.postalCode = postalCode;
  }

  const url = path.join(urlSegments, urlParams);

  try {
    const response: APIMediaContainerResponse = yield call(api.get, url, {
      server,
    });

    const { MediaContainer } = response.data;

    yield put(
      epgSettingsSlice.actions.epgLineupGroupFetchSucceeded({
        data: {
          uuid: MediaContainer?.uuid ?? '',
          epgLineups: MediaContainer?.Lineup ?? [],
        },
      })
    );
  } catch (error) {
    yield put(
      epgSettingsSlice.actions.epgLineupGroupFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

export function* addSelectedEPGLineupGroup({
  payload,
}: SelectedEPGLineupGroupAddRequestedAction): SagaIterator {
  const epgSettings: EPGSettingsState = yield select(selectEPGSettings);

  if (
    epgSettings.selectedLineupGroups.find(
      (epgLineupGroup) => epgLineupGroup.uuid === payload.lineupGroup.uuid
    )
  ) {
    yield put(
      dvrSetupSlice.actions.setupErrorMessageAdded({
        message: t(
          'It looks like you have already set up a lineup for this location.'
        ),
      })
    );
  } else {
    yield put(
      epgSettingsSlice.actions.selectedEPGLineupGroupAddSucceeded({
        lineupGroup: payload.lineupGroup,
      })
    );
  }
}

export function* fetchSelectedDVRLineupChannels({
  payload,
}: SelectedDVRLineupChannelsFetchRequestedAction): SagaIterator {
  const { server } = payload;

  try {
    const { selectedDVR }: { selectedDVR: SelectedDVRState } = yield select(
      selectDVRSetup
    );

    if (!selectedDVR?.dvr?.Lineup) {
      throw new Error('No available DVR lineups to fetch');
    }

    const lineupUUIDs = selectedDVR.dvr.Lineup.map((lineup) => lineup.id);
    const lineupChannels = yield call(fetchLineupChannels, server, lineupUUIDs);

    yield put(
      epgSettingsSlice.actions.selectedDVRLineupChannelsFetchSucceeded({
        lineupChannels,
      })
    );
  } catch (error) {
    yield put(
      epgSettingsSlice.actions.selectedDVRLineupChannelsFetchFailed({
        error: api.toPlainErrorObject(error),
      })
    );
  }
}

//
// Helper Workers

export function* fetchLineupChannels(
  server: Server,
  lineups: string[]
): SagaIterator {
  const response: APIMediaContainerResponse = yield call(
    api.get,
    '/livetv/epg/lineupchannels',
    {
      server,
      params: {
        lineup: lineups,
      },
    }
  );

  return normalizeLineupChannelsResponse({
    server,
    data: response.data.MediaContainer,
  });
}

//
// Watchers

export function* watchChannelMappingsFetchRequested(): SagaIterator {
  while (true) {
    const task = yield takeLeading(
      epgSettingsSlice.actions.channelMappingsFetchRequested,
      fetchChannelMappings
    );

    yield take(epgSettingsSlice.actions.reset);
    yield cancel(task);
  }
}

