import axios, { AxiosError, AxiosRequestConfig, AxiosResponse } from 'axios';
import qs from 'qs';
import { SagaIterator } from 'redux-saga';
import { call, cancelled } from 'redux-saga/effects';
import dimensionsAdapter from 'app/js/adapters/dimensionsAdapter';
import getProviderTextFormat from 'app/js/stores/actions/helpers/metadata/getProviderTextFormat';
import log from 'app/js/utils/log/log';
import isAbsoluteUrl from 'app/js/utils/url/isAbsoluteUrl';
import path from 'app/js/utils/url/path';
import { Provider } from 'app/js/v4/core/providers/providersTypes';
import { Server } from 'app/js/v4/core/servers/serversTypes';
import omitProviderForbiddenHeadersFilter from 'app/js/v4/utils/data/filters/omitProviderForbiddenHeadersFilter';
import simpleCORSFilter from 'app/js/v4/utils/data/filters/simpleCORSFilter';

export interface API {
  get(url: string, config: APIRequestConfig): SagaIterator;
  post(url: string, config: APIRequestConfig, data?: any): SagaIterator;
  put(url: string, config: APIRequestConfig, data?: any): SagaIterator;
  del(url: string, config: APIRequestConfig, data?: any): SagaIterator;
  setOption<K extends keyof APIOptions>(name: K, value: APIOptions[K]): void;
  toPlainErrorObject(error: AxiosError | Error): PlainError;
}

export interface Headers {
  [name: string]: string;
}

export interface APIRequestConfig extends AxiosRequestConfig {
  server?: Server;
  provider?: Provider;
  headers?: Headers;
  allowHeadersInUrl?: boolean;
  allowBlacklistedMethodHeaders?: boolean;
  providerVersionOverride?: string;
}

export interface PlainError {
  message: string;
  name?: string;
  stack?: string;
  status?: number;
  data?: any;
}

export type APIResponse<T extends object = any> = AxiosResponse<T>;

interface APIOptions {
  timeout: number;
  language: string;
  providerVersion: string;
}

function isAxiosError(error: AxiosError | Error): error is AxiosError {
  return (error as AxiosError).isAxiosError;
}

export function createAPI(defaultOptions: APIOptions): API {
  const options: APIOptions = { ...defaultOptions };

  function* makeRequest(url: string, config: APIRequestConfig): SagaIterator {
    const {
      server,
      provider,
      timeout = options.timeout,
      allowHeadersInUrl = !!(server || provider),
      providerVersionOverride,
      ...axiosConfig
    } = config;

    const dimensions = dimensionsAdapter.hasInstance()
      ? dimensionsAdapter.getInstance()
      : {};

    const headers: Headers = { ...dimensions, ...config.headers };

    let finalUrl = url;

    if (server) {
      if (!isAbsoluteUrl(url)) {
        const baseURL = provider?.baseURL ? provider.baseURL : server.uri;

        if (!baseURL) {
          throw new Error(
            'Unable to make a request to a relative path without `server.uri` or `baseURL`'
          );
        }

        finalUrl = path.join([baseURL, url]);
      }

      if (server.accessToken) {
        headers['X-Plex-Token'] = server.accessToken;
      }

      if (server.isEmbedded) {
        // This causes all communication with nano to be attributed to an
        // admin account. It's difficult to set the header ad-hoc and it's
        // not clearly defined exactly which requests which require the
        // admin account ID.
        // See: https://github.com/plexinc/plex-web-client/issues/13630
        headers['X-Plex-Account-ID'] = '1';
      }

      if (provider) {
        headers['X-Plex-Provider-Version'] =
          providerVersionOverride ?? options.providerVersion;
        headers['X-Plex-Text-Format'] = getProviderTextFormat(
          provider.identifier
        );
      }
    }

    if (options.language) {
      headers['X-Plex-Language'] = options.language;
      headers['Accept-Language'] = options.language;
    }

    const source = axios.CancelToken.source();

    let nextConfig = {
      ...axiosConfig,
      url: finalUrl,
      // Axios may not have a cancel token source when mocked by Jest.
      cancelToken: source ? source.token : undefined,
      headers,
      timeout,
      allowHeadersInUrl,
      paramsSerializer(params) {
        return qs.stringify(params, { arrayFormat: 'repeat' });
      },
    } as APIRequestConfig;

    if (server?.isCloud && provider) {
      nextConfig = omitProviderForbiddenHeadersFilter(nextConfig);
    }

    nextConfig = simpleCORSFilter(nextConfig);

    try {
      return yield call(axios.request, nextConfig);
    } finally {
      if (yield cancelled()) {
        source?.cancel();
      }
    }
  }

  return {
    get(url, config = {}) {
      return makeRequest(url, { ...config, method: 'GET' });
    },

    post(url, config = {}, data) {
      return makeRequest(url, { ...config, method: 'POST', data });
    },

    put(url, config = {}, data) {
      return makeRequest(url, { ...config, method: 'PUT', data });
    },

    del(url, config = {}) {
      return makeRequest(url, { ...config, method: 'DELETE' });
    },

    setOption(name, value) {
      options[name] = value;
    },

    toPlainErrorObject(error) {
      // Filter either an error instance to a simple object that can exist in
      // the state tree. In the case of a network error, include some additional
      // data about the error.

      let status;
      let data;

      if (isAxiosError(error)) {
        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx.
          status = error.response.status;
          data = error.response.data;
        } else if (error.request) {
          // The request was made but no response was received `error.request`
          // is an instance of XMLHttpRequest in the browser and an instance of
          // http.ClientRequest in node.js
          status = error.request.status;
        }
      }

      const filteredError: PlainError = {
        message: error.message,
        name: error.name,
        stack: error.stack,
        status,
        data,
      };

      log.error(filteredError);

      return filteredError;
    },
  };
}

const api = createAPI({
  timeout: 30000,
  language: 'en',
  providerVersion: '6.0',
});

export default api;
