/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const directions = {
  ASCENDING: 'asc',
  DESCENDING: 'desc',
};

const SortModel = AbstractModel.extend(
  {
    //
    // Properties

    name: 'SortModel',
    context: ['server', 'section'],

    idAttribute: 'key',
    rootPropertyName: 'Directory',

    sessionAttrs: {
      selected: attrTypes.boolean,
      direction: attrTypes.string,
    },

    syncedAttrs: {
      key: attrTypes.string,
      descKey: attrTypes.string,
      title: attrTypes.string,
      isDefault: attrTypes.boolean,
      active: attrTypes.boolean,
      defaultDirection: attrTypes.string,
      activeDirection: attrTypes.string,
      firstCharacterKey: attrTypes.string,
    },

    defaults: {
      selected: false,
    },

    //
    // Control

    augmentResponse(json: any) {
      // `defaultDirection` attribute was introduced in PMS 0.9.9.15
      // If it is not present, fill it using the previous `default` logic.
      if (!json.defaultDirection) {
        json.defaultDirection = json.default || directions.ASCENDING;
      }

      // Default is a reserved word, so use something else.
      json.isDefault = !!json.default;

      if (!json.direction) {
        json.direction = json.defaultDirection;
      }

      return json;
    },

    reverseSortDirection() {
      let direction = this.get('direction') || this.get('defaultDirection');

      if (!direction || direction === directions.ASCENDING) {
        direction = directions.DESCENDING;
      } else {
        direction = directions.ASCENDING;
      }

      this.set('direction', direction);
    },
  },
  {
    //
    // Static

    directions,
  }
);

export default SortModel;
