/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import BaseClass from 'app/js/utils/BaseClass';
import log from 'app/js/utils/log/log';
import path from 'app/js/utils/url/path';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const XHRTracker = BaseClass.extend({
  //
  // Init

  initialize() {
    _.bindAll(this, 'onAjaxSend', 'onAjaxComplete');

    this.requests = [];
    this.byId = {};
  },

  //
  // Control

  startListening() {
    const $document = $(document);

    $document.on('ajaxSend', this.onAjaxSend);
    $document.on('ajaxComplete', this.onAjaxComplete);
  },

  stopListening() {
    const $document = $(document);

    $document.off('ajaxSend', this.onAjaxSend);
    $document.off('ajaxComplete', this.onAjaxComplete);
  },

  abortAll(scope: any) {
    for (let i = this.requests.length - 1; i > -1; i--) {
      const request = this.requests[i];

      if (scope == null || scope === request.settings.scope) {
        this.abort(request);
      }
    }
  },

  abort(rid: any) {
    // Allow an id or the request object to be passed in.
    const request = _.isString(rid) ? this.byId[rid] : rid;

    if (request) {
      request.xhr.aborting = true;
      request.xhr.abort();

      this.remove(request);
    }
  },

  store(xhr: any, settings: any) {
    const rid = _.uniqueId('r');
    xhr.rid = rid;

    const request = {
      xhr: xhr,
      settings: settings,
    };

    this.requests.push(request);
    this.byId[rid] = request;

    return request;
  },

  remove(request: any) {
    const i = _.indexOf(this.requests, request);

    if (i > -1) {
      this.requests.splice(i, 1);
    }

    if (this.byId.hasOwnProperty(request.xhr.rid)) {
      delete this.byId[request.xhr.rid];
    }
  },

  //
  // Listeners

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onAjaxSend(event: any, xhr: any, settings: any) {
    // The CORS redirect transform causes two ajax send events. Ignore the
    // second one since we can abort the inner request with the wrapper.
    if (settings.skipCorsWithRedirect) {
      return;
    }

    const request = this.store(xhr, settings);

    xhr.always(() => {
      // If we are currently aborting pending requests, removing items from
      // the array can be harmful since the array will be changing as we
      // loop through it.
      if (!xhr.aborting) {
        this.remove(request);
      }
    });
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'event' is declared but its value is never read.
  onAjaxComplete(event: any, xhr: any, options: any) {
    const hasInvalidToken = xhr.status === 401;

    if (xhr.aborting || !hasInvalidToken) {
      return;
    }

    // Parse the URL once we know that this wasn't an intentional abort or the
    // users token is valid.
    const uri = path.parseUrl(options.url);
    if (
      hasInvalidToken &&
      (uri.pathname === '/log' ||
        uri.pathname === '/' ||
        uri.pathname === '/media/providers')
    ) {
      // Defer connection testing due to auth failures to other non-passive
      // requests. A connection test that fails should also be ignored since it
      // may be a connection test for a server that happens to match another
      // server's active connection. A connection test failure is handled
      // separately.
      // See: https://github.com/plexinc/plex-web-client/issues/5111
      return;
    }

    const servers = resolve('appModel').get('servers');

    const server = servers
      ? servers.find((server_: any) => {
          const activeConnection = server_.get('activeConnection');
          const isConnected =
            activeConnection && activeConnection.get('isConnected');
          const url = _.result(activeConnection, 'url');

          return isConnected
            ? options.url.substring(0, url.length) === url
            : false;
        })
      : null;

    if (server) {
      log(
        '[Connections] Active connection to "%s" lost authorization',
        server.get('debugName')
      );
      // We should test the server's active connection after a request fails.
      server.trigger('error:connection', server);
    }
  },
});

// Singleton
export default new XHRTracker();
