/* eslint-plugin-disable @typescript-eslint */
/* eslint-disable prefer-rest-params, prefer-spread */

import $ from 'jquery';
import _ from 'underscore';
import Backbone from 'app/js/initializers/extensions/BackboneExtension';
import populateMixin from 'app/js/mixins/models/populateMixin';
import config from 'app/js/models/config';
import * as responseTypes from 'app/js/models/responseTypes';
import assert from 'app/js/utils/log/assert';
import performance from 'app/js/utils/metrics/performance';
import * as stateUpdateScheduler from 'app/js/utils/state/stateUpdateScheduler';
import path from 'app/js/utils/url/path';

const INSTRUMENTED = false;

const AbstractCollection = Backbone.Collection.extend({
  mixins: [populateMixin],

  //
  // Properties

  responseType: responseTypes.xml,

  instanceOptions: [
    'url',
    'path',
    'pageStart',
    'pageSize',
    'persistInCache',
    'skipCache',
  ],

  // This will be merged with the public, optional `context` property.
  _context: ['parent'],

  // A `CollectionCache` instance must be provided in order to enable
  // caching for a collection class.
  cache: null,
  cacheKey: function () {
    return _.result(this, 'url');
  },

  //
  // Init

  // @ts-expect-error ts-migrate(6133) FIXME: 'models' is declared but its value is never read.
  constructor: function (models: any, options: any) {
    options || (options = {});

    // Automatically set context on the collection found in options. Context
    // can be used for references to higher level models.

    _.each(
      this.getContext(),
      function (contextName) {
        const context = options[contextName];
        if (context !== undefined) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.setContext(contextName, context);
        }
      },
      this
    );

    // Certain options should be set on the instance of the collection and
    // automatically passed to any clones.

    const instanceOptions = _.result(this, 'instanceOptions');
    _.each(
      instanceOptions,
      function (optionName) {
        // Check if the option was actually passed, so we don't override a
        // url on the prototype.

        if (_.has(options, optionName)) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this[optionName] = options[optionName];
        }
      },
      this
    );

    if (this.pageStart != null || this.pageSize != null) {
      this.pageStart || (this.pageStart = 0);
      this.pageSize || (this.pageSize = 0);
    }

    if (options.populated) {
      this.resetPopulatePromise($.Deferred().resolve().promise());
    }

    if (!this.cache || this.skipCache) {
      Backbone.Collection.prototype.constructor.apply(this, arguments);
      this._initState();
      return;
    }

    // Options handled by initialize may affect the url used to get a cache
    // key for the collection.
    this.initialize.apply(this, arguments);

    // If this instance is already in the cache, return the cached instance
    // instead of constructing a new instance.
    const cacheKey = this.cache.getItemCacheKey(this);
    const cachedInstance = this.cache.get(cacheKey);

    // Treat all instances as disposable. If it's intended for usage the
    // constructor call below will re-invoke `initialize()`.
    this.uninitialize();

    if (cachedInstance) {
      return cachedInstance;
    }

    if (options.onlyIfCached) {
      return {};
    }

    // Not in the cache, so go ahead and use the original constructor,
    // adding the result to the cache.
    Backbone.Collection.prototype.constructor.apply(this, arguments);
    this._initState();

    this.cache.store(this, cacheKey);
  },

  uninitialize: function () {
    this.stopListening();
  },

  _initState: function () {
    if (!config.shouldCreateStateTree) {
      return;
    }

    this.state = this.toState();
    this.updateState = this.updateState.bind(this);

    this.on('reset update sort', this.onCollectionChange, this);
    this.on('statechange', this.onStateChange, this);
  },

  //
  // Control

  hasContext: function (contextName: any) {
    return this.getContext().includes(contextName);
  },

  getContext: function () {
    if (!this.context) {
      return this._context;
    }

    const context = _.result(this, 'context');

    // Gate on __DEV__ to ensure this block gets removed in minified builds.
    if (__DEV__) {
      this._context.forEach((key: any) => {
        assert(
          context.indexOf(key) === -1,
          JSON.stringify(key) + ' is implicit in `context`'
        );
      });
    }

    return this._context.concat(context);
  },

  setContext: function (key: any, value: any) {
    if (this.getContext().indexOf(key) === -1 || this[key] === value) {
      return;
    }

    this[key] = value;

    this.each(function (model: any) {
      model.setContext(key, value);
    });
  },

  getState: function () {
    return this.state;
  },

  updateState: function () {
    const previousRawState = this.state;
    const previousState = this.getState();
    this.state = this.toState();
    const nextState = this.getState();

    this.trigger('statechange', this, nextState, previousState);

    return {
      name: this.name,
      previousState: previousRawState,
      nextState,
    };
  },

  toState: function () {
    const name = `collection:${this.name}:toState`;

    INSTRUMENTED && performance.startPeriod(name);

    const state = this.reduce(this.reduceState, []);

    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    INSTRUMENTED && performance.endPeriod(name);

    return state;
  },

  reduceState: function (acc: any, model: any) {
    const state = model.getState();

    // If an entity does not have an id, state will be undefined so omit it
    // from the array. Normal models will be represented by objects.

    if (state) {
      acc.push(state);
    }

    return acc;
  },

  shouldUpdateModelState: function (
    // @ts-expect-error ts-migrate(6133) FIXME: 'instance' is declared but its value is never read... Remove this comment to see the full error message
    instance: any,
    nextState: any,
    previousState: any
  ) {
    return nextState !== previousState;
  },

  sync: function () {
    // Whenever the collection is synced, assume the url might have changed,
    // which means we need to update the cache key.
    if (this.cache) {
      this.cache.store(this);
    }

    return Backbone.sync.apply(this, arguments);
  },

  syncUrl: function () {
    let url = '';

    if (this.url) {
      url = _.result(this, 'url');
    }

    if (this.path) {
      url = path.join([url, _.result(this, 'path')]);
    }

    // If pagination properties have been set, append the X-Plex pagination
    // params to the end of the querystring.
    if (this.pageStart != null || this.pageSize != null) {
      const pageParams = {
        'X-Plex-Container-Start': this.pageStart || 0,
        'X-Plex-Container-Size': this.pageSize || 0,
      };

      url = path.join(url, pageParams);
    }

    return url;
  },

  // @ts-expect-error ts-migrate(6133) FIXME: 'xhr' is declared but its value is never read.
  parse: function (response: any, xhr: any) {
    const name = `collection:${this.name}:parse`;

    INSTRUMENTED && performance.startPeriod(name);

    const data = this.convert(response);

    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    INSTRUMENTED && performance.endPeriod(name);

    return data;
  },

  convert: function (response: any) {
    let data = response;
    const responseType = _.result(this, 'responseType');

    if (_.isString(data)) {
      if (_.isFunction(this.augmentResponseText)) {
        data = this.augmentResponseText(data, responseType);
      }

      data = responseType.parse(data);

      // JSON from PMS nests properties within a `MediaContainer` object.
      if (responseType === responseTypes.json && data.MediaContainer) {
        data = data.MediaContainer;
      }
    }

    if (_.isFunction(this.augmentResponse)) {
      data = this.augmentResponse(data, responseType);
    }

    if (Array.isArray(data)) {
      return data;
    }

    const rootPropertyName = this.model.prototype.rootPropertyName;
    if (!rootPropertyName) {
      return data;
    }

    let models = data[rootPropertyName];
    if (!models) {
      return [];
    }

    if (!Array.isArray(models)) {
      models = [models];
    }

    return models;
  },

  clone: function (models: any) {
    // If no models are passed in, use the collection's existing models.
    models || (models = this.models);

    // Pass over the context and specific options to the cloned instance.
    // Skip the cache since we want to avoid just reusing a cached instance.

    const instanceOptions = _.result(this, 'instanceOptions');
    const cloneOptions = _.extend(
      _.pick(this, instanceOptions),
      _.pick(this, this.getContext()),
      {
        skipCache: true,
        persistInCache: false,
      }
    );

    // Opera has some problems returning from a constructor when Dragonfly
    // isn't running. The || null seems to  be sufficient to stop it
    // misbehaving. Found by ExtJS team.
    return new this.constructor(models, cloneOptions) || null;
  },

  cloneDeep: function (models: any) {
    // If no models are passed in, use the collection's existing models.
    models || (models = this.models);

    const clonedModels = _.map(models, function (model) {
      return model.clone();
    });

    return this.clone(clonedModels);
  },

  clear: function (options: any) {
    options = _.extend({ silent: true }, options);

    // If a request is pending, abort it so the response doesn't come back
    // after we already cleared the collection, defeating our efforts.
    this.abortPendingPopulate();
    this.resetPopulatePromise(null);

    this.reset([], options);
  },

  getSaveParams: function () {
    return this.map(function (model: any) {
      return model.getSaveParams();
    });
  },

  //
  // Listeners

  onCollectionChange: function () {
    stateUpdateScheduler.scheduleUpdate(this.updateState, this);
  },

  onStateChange: function (instance: any, nextState: any, previousState: any) {
    // Only update state when a `statechange` event from a model is
    // forwarded. The `reset`, `update`, and `sort` listeners handle changes
    // to the collection.

    if (
      instance !== this &&
      this.shouldUpdateModelState(instance, nextState, previousState)
    ) {
      stateUpdateScheduler.scheduleUpdate(this.updateState, this);
    }
  },
});

export default AbstractCollection;
