/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import log from 'app/js/utils/log/log';

const WAIT_DURATION = 2500;

export default function awaitEvents(options: any) {
  const {
    eventSource,
    events,
    predicate = () => true,
    waitDuration = WAIT_DURATION,
  } = options;

  const deferred = $.Deferred();

  const callback = () => {
    if (predicate(eventSource)) {
      // @ts-expect-error ts-migrate(2322) FIXME: Type 'void' is not assignable to type 'number'.
      timeoutID = window.clearTimeout(timeoutID);
      eventSource.off(callbacks);
      deferred.resolve(eventSource);
    }
  };

  const callbacks = events.reduce((acc: any, event: any) => {
    acc[event] = callback;
    return acc;
  }, {});

  let timeoutID = window.setTimeout(() => {
    log.warn(
      'Model "%s" events %o did not pass the predicate within %dms',
      eventSource.name,
      events,
      waitDuration
    );

    eventSource.off('add', callbacks);

    deferred.reject(eventSource);
  }, waitDuration);

  eventSource.on(callbacks);

  return deferred.promise();
}
