/* eslint-plugin-disable @typescript-eslint */

import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import * as pivotTypes from 'app/js/utils/constants/pivotTypes';
import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';
import * as providerProtocols from 'app/js/utils/constants/providerProtocols';
import createLibraryPivotKey from 'app/js/utils/provider/createLibraryPivotKey';
import t from 'app/js/utils/t';
import * as metadataTypes from 'app/js/utils/types/metadataTypes';
import * as playlistTypes from 'app/js/utils/types/playlistTypes';
import path from 'app/js/utils/url/path';
import { views } from 'app/js/views/v3/helpers/routes/getViewRoute';

function getRecommendedPivot(directory: any) {
  return {
    key: directory.hubKey,
    type: pivotTypes.HUB,
    title: t('Recommended'),
    context: metricsContexts.DISCOVER_PIVOT,
  };
}

function getLibraryPivot(directory: any, type: any) {
  return {
    key: createLibraryPivotKey(directory.id, type),
    libraryKey: directory.key,
    type: pivotTypes.LIST,
    title: t('Library'),
    context: metricsContexts.LIBRARY_PIVOT,
    isContent: true,
  };
}

function getCollectionPivot(directory: any) {
  const collectionType = metadataTypes.getMetadataType(
    metadataTypes.COLLECTION
  );

  return {
    key: path.join([directory.key, 'all'], { type: collectionType.id }),
    type: pivotTypes.LIST,
    title: t('Collections'),
    context: metricsContexts.COLLECTIONS_PIVOT,
  };
}

function getPlaylistRoute(directory: any, playlistType: any) {
  const librarySectionID = directory.key.match(/\/library\/sections\/(\w+)/)[1];
  const playlistMetadataType = metadataTypes.getMetadataType(
    metadataTypes.PLAYLIST
  );

  return path.join('/playlists/all', {
    type: playlistMetadataType.id,
    playlistType,
    sectionID: librarySectionID,
  });
}

function getPlaylistsPivot(directory: any, playlistType: any) {
  return {
    key: getPlaylistRoute(directory, playlistType),
    type: pivotTypes.LIST,
    title: t('Playlists'),
    context: metricsContexts.PLAYLISTS_PIVOT,
  };
}

function hasLiveTVProtocol(protocolsString: any) {
  return (
    protocolsString &&
    protocolsString.split(',').indexOf(providerProtocols.LIVE_TV) !== -1
  );
}

function hasFeature(features: any, featureType: any) {
  return (
    features && features.some((feature: any) => feature.type === featureType)
  );
}

function isTypeMatch(type: any, subtype: any, directory: any) {
  return (
    directory.type === type &&
    ((subtype == null && directory.subtype == null) ||
      (subtype != null && directory.subtype === subtype))
  );
}

function isLibraryTypeMatch(type: any, directory: any) {
  return (
    /\/library\/sections\/(\w+)/.test(directory.key) &&
    isTypeMatch(type, null, directory)
  );
}

export default function getSyntheticPivots(
  directory: any,
  protocolsString: any,
  features: any
) {
  if (!directory.hubKey && directory.key !== '/playlists') {
    return [];
  } else if (isLibraryTypeMatch(metadataTypes.ARTIST, directory)) {
    return [
      getRecommendedPivot(directory),
      getLibraryPivot(directory, metadataTypes.ARTIST),
      getPlaylistsPivot(directory, playlistTypes.AUDIO),
    ];
  } else if (isLibraryTypeMatch(metadataTypes.MOVIE, directory)) {
    return [
      getRecommendedPivot(directory),
      getLibraryPivot(directory, metadataTypes.MOVIE),
      getCollectionPivot(directory),
      getPlaylistsPivot(directory, playlistTypes.VIDEO),
    ];
  } else if (isLibraryTypeMatch(metadataTypes.SHOW, directory)) {
    return [
      getRecommendedPivot(directory),
      getLibraryPivot(directory, metadataTypes.SHOW),
      getCollectionPivot(directory),
      getPlaylistsPivot(directory, playlistTypes.VIDEO),
    ];
  } else if (isLibraryTypeMatch(metadataTypes.PHOTO, directory)) {
    return [
      getRecommendedPivot(directory),
      {
        key: views.TIMELINE,
        type: pivotTypes.VIEW,
        title: t('Timeline'),
        context: metricsContexts.TIMELINE_PIVOT,
      },
      getLibraryPivot(directory, metadataTypes.PHOTO),
      getPlaylistsPivot(directory, playlistTypes.PHOTO),
    ];
  } else if (
    hasLiveTVProtocol(protocolsString) &&
    hasFeature(features, providerFeatureTypes.SUBSCRIBE)
  ) {
    return [
      {
        key: directory.hubKey,
        type: pivotTypes.HUB,
        title: t('Recommended'),
        context: metricsContexts.DVR_DISCOVER_PIVOT,
      },
      {
        key: views.GUIDE,
        type: pivotTypes.VIEW,
        title: t('Guide'),
        context: metricsContexts.DVR_GUIDE_PIVOT,
      },
      {
        key: views.RECORDING_SCHEDULE,
        type: pivotTypes.VIEW,
        title: t('DVR Schedule'),
        context: metricsContexts.DVR_SCHEDULE_PIVOT,
      },
    ];
  } else if (
    hasLiveTVProtocol(protocolsString) ||
    hasFeature(features, providerFeatureTypes.GRID)
  ) {
    return [
      {
        key: directory.hubKey,
        type: pivotTypes.HUB,
        title: t('Recommended'),
        context: metricsContexts.DVR_DISCOVER_PIVOT,
      },
      {
        key: views.GUIDE,
        type: pivotTypes.VIEW,
        title: t('Guide'),
        context: metricsContexts.DVR_GUIDE_PIVOT,
      },
    ];
  } else if (isTypeMatch(metadataTypes.PLAYLIST, null, directory)) {
    return [
      {
        key: path.join(directory.key, {
          playlistType: playlistTypes.AUDIO,
        }),
        type: pivotTypes.LIST,
        title: t('Music'),
        context: metricsContexts.PLAYLISTS_PIVOT,
      },
      {
        key: path.join(directory.key, {
          playlistType: playlistTypes.VIDEO,
        }),
        type: pivotTypes.LIST,
        title: t('Video'),
        context: metricsContexts.PLAYLISTS_PIVOT,
      },
      {
        key: path.join(directory.key, {
          playlistType: playlistTypes.PHOTO,
        }),
        type: pivotTypes.LIST,
        title: t('Photo'),
        context: metricsContexts.PLAYLISTS_PIVOT,
      },
    ];
  }

  return [];
}
