export const containsMetadataType = metadataTypes.contains;
export const getMetadataType = metadataTypes.get;
export const getAllMetadataTypes = metadataTypes.getAll;

export function getChildType(type: any) {
  const metadataType = metadataTypes.get(type);

  if (metadataType && metadataType.child) {
    return metadataTypes.get(metadataType.child);
  }
}

// @ts-expect-error ts-migrate(7023) FIXME: 'getLeafType' implicitly has return type 'any' bec... Remove this comment to see the full error message
export function getLeafType(type: any) {
  const metadataType = metadataTypes.get(type);

  if (metadataType) {
    const child = metadataType.child;

    return child == null ? metadataType : getLeafType(child);
  }
}

export function getParentType(type: any) {
  const metadataType = metadataTypes.get(type);

  if (metadataType) {
    return metadataTypes.get(metadataType.parent);
  }
}

// @ts-expect-error ts-migrate(7023) FIXME: 'getRootType' implicitly has return type 'any' bec... Remove this comment to see the full error message
export function getRootType(type: any) {
  const metadataType = metadataTypes.get(type);

  if (metadataType) {
    const parent = metadataType.parent;

    return parent == null ? metadataType : getRootType(parent);
  }
}

// @ts-expect-error ts-migrate(7023) FIXME: 'getRelatedTypes' implicitly has return type 'any'... Remove this comment to see the full error message
export function getRelatedTypes(type: any, relatedTypes: any) {
  if (!relatedTypes) {
    const rootType = getRootType(type);

    return rootType ? getRelatedTypes(rootType.id, []) : [];
  }

  const metadataType = metadataTypes.get(type);
  const childType = getChildType(type);
  const nextRelatedTypes = [...relatedTypes, metadataType];

  return childType
    ? getRelatedTypes(childType.id, nextRelatedTypes)
    : nextRelatedTypes;
}

export function isParentType(type: any) {
  return !!getChildType(type);
}

export function isDirectoryType(type: any) {
  const metadataType = metadataTypes.get(type);

  return !!metadataType && metadataType.mediaType === mediaTypes.NONE;
}
