/* eslint-plugin-disable @typescript-eslint */

import getPixelRatio from 'app/js/utils/constants/getPixelRatio';
import path from 'app/js/utils/url/path';
import MediaPosterImage from 'app/js/views/images/MediaPosterImage';

const GravatarImage = MediaPosterImage.extend({
  getUrl(url: any) {
    if (url) {
      const pixelRatio = getPixelRatio();
      return path.join(url, {
        size: Math.round(this.width * pixelRatio),
      });
    }
    return url;
  },
});

export default GravatarImage;
