import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import MetadataModel from 'app/js/models/metadata/MetadataModel';

const VersionGeneratorMetadataModel = AbstractModel.extend({
  //
  // Properties

  name: 'VersionGeneratorMetadataModel',
  context: ['server', 'generator'],

  idAttribute: 'ratingKey',

  // This model is separate from `MetadataModel` and extends `AbstractModel`.
  // However, it reuses a lot of the same attributes as `MetadataModel`, but
  // does not have any relations, so extend `syncedAttrs` only.

  syncedAttrs: _.extend({}, MetadataModel.prototype.syncedAttrs, {
    processingState: attrTypes.string,
    processingStateContext: attrTypes.string,
  }),
});

export default VersionGeneratorMetadataModel;
