/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import ModelCache from 'app/js/utils/cache/ModelCache';
import { AUDIO, PHOTO, VIDEO } from 'app/js/utils/types/mediaTypes';
import VersionGeneratorMediaSettingsModel from './VersionGeneratorMediaSettingsModel';
import VersionGeneratorMetadataListModel from './VersionGeneratorMetadataListModel';
import VersionGeneratorPolicyModel from './VersionGeneratorPolicyModel';
import VersionGeneratorStatusModel from './VersionGeneratorStatusModel';

const VersionGeneratorModel = AbstractModel.extend({
  //
  // Properties
  name: 'VersionGeneratorModel',
  context: ['server', 'section', 'playlistID'],
  rootPropertyName: 'Item',
  cache: new ModelCache(),
  cacheKey(attributes: any) {
    return this.server && this.server.getScopedKey(attributes.id);
  },
  urlRoot() {
    const playlistID = this.playlistID || this.parent.id;
    return `/playlists/${playlistID}/items`;
  },
  syncedAttrs: {
    id: attrTypes.integer,
    clientIdentifier: attrTypes.string,
    contentType: attrTypes.string,
    composite: attrTypes.string,
    type: {
      type: attrTypes.integer,
      save: true,
    },
    title: {
      type: attrTypes.string,
      save: true,
    },
    target: {
      type: attrTypes.string,
      save: true,
    },
    targetTagID: {
      type: attrTypes.integer,
      save: true,
    },
    locationID: {
      type: attrTypes.integer,
      save: true,
    },
    Location: {
      type: attrTypes.object,
      save: true,
    },
    Device: {
      type: attrTypes.object,
      save: true,
    },
  },
  sessionRelations: {
    children: {
      relationClass: VersionGeneratorMetadataListModel,
      options() {
        return {
          generator: this,
        };
      },
    },
  },
  syncedRelations: {
    status: {
      relationClass: VersionGeneratorStatusModel,
      rootPropertyName: 'Status',
    },
    policy: {
      relationClass: VersionGeneratorPolicyModel,
      rootPropertyName: 'Policy',
      save: true,
    },
    mediaSettings: {
      relationClass: VersionGeneratorMediaSettingsModel,
      rootPropertyName: 'MediaSettings',
      save: true,
    },
  },
  derivedAttrs: {
    // @ts-expect-error ts-migrate(7030) FIXME: Not all code paths return a value.
    isDirectory() {
      const location = (this as any).get('Location');
      if (location.uri) {
        return /(playlist:\/\/|library:\/\/([\w-]*)\/directory\/)/.test(
          location.uri
        );
      }
    },
  },
  defaults() {
    return {
      type: 42,
      locationID: -1,
      Location: {},
      Device: {},
    };
  },
  defaultCounts: {
    [VIDEO]: 5,
    [AUDIO]: 50,
    [PHOTO]: 50,
  },
  warningCounts: {
    [VIDEO]: 10,
    [AUDIO]: 100,
    [PHOTO]: 100,
  },
  //
  // Control
  // @ts-expect-error ts-migrate(6133) FIXME: 'data' is declared but its value is never read.
  getSaveParams(data: any, options: any) {
    // eslint-disable-next-line prefer-rest-params
    const Item = AbstractModel.prototype.getSaveParams.apply(this, arguments);
    // There's no need to provide media settings or device info when using a
    // predefined target.
    if (Item.targetTagID) {
      delete Item.MediaSettings;
      delete Item.Device;
    }
    return { Item };
  },
  setPolicyDefaultsFor(mediaType: any) {
    const policy = this.get('policy');
    const isDirectory = this.get('isDirectory');
    if (!policy.has('scope')) {
      policy.set('scope', isDirectory ? 'count' : 'all');
    }
    if (!policy.has('value') && isDirectory) {
      policy.set('value', this.defaultCounts[mediaType]);
    }
    return this;
  },
  shouldShowPolicyWarning(mediaType: any, policyValue: any) {
    const policyScope = this.get('policy').get('scope');
    if (policyScope === 'count') {
      return policyValue > this.warningCounts[mediaType];
    }
    if (policyScope === 'all') {
      return this.get('isDirectory');
    }
    return false;
  },
});
export default VersionGeneratorModel;
