/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const PlexPlayerModel = AbstractModel.extend({
  //
  // Properties
  name: 'PlexPlayerModel',
  context: ['server', 'baseURL'],
  rootPropertyName: 'Server',
  sessionAttrs: {
    volume: attrTypes.integer,
  },
  syncedAttrs: {
    machineIdentifier: attrTypes.string,
    name: attrTypes.string,
    title: attrTypes.string,
    address: attrTypes.string,
    port: attrTypes.string,
    deviceClass: attrTypes.string,
    version: attrTypes.string,
    product: attrTypes.string,
    protocol: attrTypes.string,
    protocolVersion: attrTypes.string,
    protocolCapabilities: attrTypes.list,
  },
  derivedAttrs: {
    friendlyName() {
      return (
        (this as any).get('title') ||
        (this as any).get('name') ||
        (this as any).get('product')
      );
    },
    playerIdentifier() {
      return `${(this as any).baseURL}:${(this as any).get(
        'machineIdentifier'
      )}`;
    },
  },
  defaults: {
    volume: 100,
  },
});
export default PlexPlayerModel;
