/* eslint-plugin-disable @typescript-eslint */

import t from 'app/js/utils/t';

export enum ErrorContexts {
  Subscription = 'subscription',
  SendRecoveryCode = 'sendRecoveryCode',
}

function getMessage(code: any, context?: ErrorContexts) {
  switch (context) {
    case ErrorContexts.Subscription:
      switch (code) {
        case '1003':
          return t(
            'Subscriptions have been temporarily disabled: too many failed attempts. Please try again later.'
          );
      }
      break;
    case ErrorContexts.SendRecoveryCode:
      switch (code) {
        case '1001':
          return t('User could not be authenticated.');
        case '1003':
          return t(
            'A temporary recovery code has already been sent recently. Please try again later.'
          );
        case '1028':
          return t('Invalid recovery code');
      }
      break;
  }

  switch (code) {
    case '1001':
      return t('The username or password is incorrect');
    case '1003':
      return t(
        'Sign in has been temporarily disabled: too many failed attempts. Please try again later.'
      );
    case '1004':
      return t('Authentication failed. Please try again later.');
    case '1010-login':
      return t('Login is required');
    case '1010-password':
      return t('Password is required');
    case '1010-email':
      return t('Email is required');
    case '1011-username':
      return t('Username has already been taken');
    case '1011-email':
      return t('Email has already been taken');
    case '1028':
      return t('Invalid verification code');
    case '1029':
      return t('Please enter the verification code');
    case '1030':
      return t('This account only allows signing in with an email address');
    case '1031':
      return t(
        'The username or password is incorrect. Repeated attempts may temporarily disable sign in.'
      );
    case '1032-password':
      return t('Password must be at least 8 characters');
    case '1033-password':
      return t('Password is too weak');
    case '1034-password':
      return t('Password must be different from username or email address');
    case '1035-email':
      return t('Email is not a valid email address');
    case '1036-username':
      return t('Username must not be an email address');
    case '1037-username':
      return t('Username contains invalid characters');
    case '1038-pin':
      return t('Pin must be 4 digits');
    case '1039-passwordConfirmation':
      return t('Confirm Password must match Password');
    case '1040':
      return t('Invalid Password');
    case '1044':
      return t('Invalid Password');
  }

  return;
}

export default function getServerErrorMessage(
  error?: any,
  context?: ErrorContexts
) {
  if (!error) {
    return;
  }

  const code = String(error.code);
  const codeField = `${error.code}-${error.field}`;
  const errorMessage =
    getMessage(codeField, context) || getMessage(code, context);

  return errorMessage || error.message || t('An unknown error occurred');
}
