/* eslint-plugin-disable @typescript-eslint */

import getSourceKey from 'app/js/utils/source/getSourceKey';
import t from 'app/js/utils/t';
import * as sourceTypes from 'app/js/utils/types/sourceTypes';

export default function getSource(options: any) {
  const { sourceType, sourceServer, sourceProvider, sourceDirectory } = options;
  const source = {
    key: getSourceKey(
      sourceType,
      sourceServer.machineIdentifier,
      sourceProvider.identifier,
      sourceDirectory.id
    ),
    sourceType,
    serverEntityID: sourceServer.entityID,
    machineIdentifier: sourceServer.machineIdentifier,
    providerEntityID: sourceProvider.entityID,
    providerIdentifier: sourceProvider.identifier,
    providerMetricsIdentifier: sourceProvider.metricsIdentifier,
    directoryID: sourceDirectory.id,
    directoryIcon: sourceDirectory.icon,
    title: sourceDirectory.title,
    serverFriendlyName: sourceServer.friendlyName,
    serverSourceTitle: sourceServer.sourceTitle,
    providerSourceTitle: sourceProvider.sourceTitle,
    isLocal:
      sourceServer.isConnected &&
      sourceServer.activeConnection &&
      sourceServer.activeConnection.isPrivate,
    isUnavailable: sourceServer.isUnavailable,
    isOutOfDate: sourceServer.isOutOfDate,
    isUnclaimedServer:
      sourceServer.isConnected &&
      !sourceServer.isClaimed &&
      !sourceServer.isCloud,
    isFullOwnedServer: sourceServer.isFullOwnedServer,
    isProviderReady:
      sourceProvider.isPopulated ||
      sourceProvider.error != null ||
      sourceServer.isUnavailable ||
      sourceServer.isUnauthorized,
  };
  if (sourceServer.isCloud) {
    (source as any).isCloud = true;
  } else if (sourceType === sourceTypes.DVR) {
    // See: https://github.com/plexinc/plex-media-server/issues/9169
    source.title = t('Live TV & DVR');
  }
  return source;
}
