import { resolve } from 'app/js/adapters/adapters';
import whenEach from 'app/js/utils/promise/whenEach';

export default function desktopSettingsInitializer() {
  const appModel = resolve('appModel');

  return whenEach([
    appModel.get('settings').populate(),
    appModel.get('deviceSettings').populate(),
  ]);
}
