/* eslint-plugin-disable @typescript-eslint */

import { resolve } from 'app/js/adapters/adapters';
import config from 'app/js/models/config';

// We should only share sync settings when the user is able to;  app
// configuration allows it and the setting is enabled for this device
export default function canShareSyncSettings(userModel: any) {
  return (
    !userModel.get('experienceSettings').get('isRemoteSchemaVersionNewer') &&
    config.allowSyncSettings &&
    resolve('settingFinder').find('shouldSyncSettings')
  );
}
