import $ from 'jquery';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import FilePathListItem from 'app/js/views/lists/server/FilePathListItem';

const FilePathList = Marionette.CollectionView.extend({
  //
  // Properties

  tagName: 'ul',
  className: 'list browse-directory-list',
  itemView: FilePathListItem,

  selectedFilePath: null,

  events: {
    'click a': 'onDirectoryClick',
  },

  collectionEvents: {
    reset: 'onReset',
  },

  //
  // Control

  startLoadingItem: function (filePath: any) {
    if (this.selectedFilePath) {
      this.unselectItem(this.selectedFilePath);
    }

    this.selectedFilePath = filePath;

    const child = this.children.findByModel(filePath);
    if (child) {
      child.select(true);
    }
  },

  stopLoadingItem: function (filePath: any) {
    const child = this.children.findByModel(filePath);
    if (child) {
      child.select(false);
    }
  },

  unselectItem: function (filePath: any) {
    this.selectedFilePath = null;

    const child = this.children.findByModel(filePath);
    if (child) {
      child.unselect();
    }
  },

  //
  // Listeners

  onReset: function () {
    if (this.parentFilePath) {
      // Prepend an item to our collection to render the [Back] list item.
      this.collection.add(this.parentFilePath, { at: 0 });
    }
  },

  onDirectoryClick: function (event: any) {
    event.preventDefault();

    const $target = $(event.currentTarget);
    const key = $target.data('key');
    const filePath = this.collection.get(key) || this.parentFilePath;

    this.trigger('change:path', filePath);

    if (this.options.isRoot) {
      return;
    }

    if (filePath.parent.parent) {
      this.parentFilePath = filePath.parent.parent.clone();
      this.parentFilePath.set({
        title: '.. [Back]',
        isBackShortcut: true,
      });
    } else {
      this.parentFilePath = null;
    }
  },
});

export default FilePathList;
