/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import path from 'app/js/utils/url/path';

export default function refreshLibrary(serverModel: any, options: any) {
  const {
    libraryID,
    libraryType,
    cancel,
    force,
    allowMetrics,
    metricsProperties = {},
  } = options;

  const params = force ? { force: 1 } : undefined;
  const url = path.join(['/library/sections', libraryID, 'refresh'], params);
  const xhrOptions = serverModel.getXHROptions(url, {
    type: cancel ? 'DELETE' : 'GET',
  });

  if (allowMetrics) {
    let action;

    if (cancel) {
      action = metricsActions.CANCEL_LIBRARY_UPDATE;
    } else if (force) {
      action = metricsActions.FORCE_REFRESH_LIBRARY;
    } else {
      action = metricsActions.UPDATE_LIBRARY;
    }

    resolve('metrics').trackClick({
      ...metricsProperties,
      action,
      type: libraryID === 'all' ? 'all' : libraryType,
    });
  }

  return $.ajax(xhrOptions).promise();
}
