/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';

function parseEncodedString(value: any) {
  if (value == null) {
    return '';
  } else {
    try {
      return decodeURIComponent(value);
    } catch (error) {
      log.warn('[Parser] Unable to parse encoded string');
      return value;
    }
  }
}

export default parseEncodedString;
