import findEntity, { tryFindEntity } from 'app/js/utils/state/findEntity';
import * as entityTypes from './entityTypes';

export function findUserModel(appModel: any) {
  return appModel.get('user');
}

export function findUserFlexModel(appModel: any) {
  return appModel.get('user').get(__PLATFORM__);
}

export function findServerModel(appModel: any, { serverEntityID }: any) {
  return findEntity(appModel, entityTypes.SERVER, serverEntityID);
}

export function tryFindServerModel(appModel: any, { serverEntityID }: any) {
  return tryFindEntity(appModel, entityTypes.SERVER, serverEntityID);
}

export function findServerFlexModel(appModel: any, payload: any) {
  const serverModel = findServerModel(appModel, payload);

  return serverModel && serverModel.get(__PLATFORM__);
}

