export function findSyncProviderModel(appModel: any, { source }: any) {
  const embeddedServer = appModel.get('embeddedServer');

  return (
    embeddedServer && embeddedServer.get('providers').findWhere({ source })
  );
}

export function findPlexPlayerModel(appModel: any, { playerIdentifier }: any) {
  const cloudCompanions = appModel
    .get('user')
    .get(__PLATFORM__)
    .get('cloudCompanions');

  for (const cloudCompanion of cloudCompanions.models) {
    const player = cloudCompanion
      .get('cloudPlayers')
      .findWhere({ playerIdentifier });

    if (player) {
      return player;
    }
  }

  return appModel
    .get('companionServer')
    .get(__PLATFORM__)
    .get('players')
    .findWhere({ playerIdentifier });
}
