/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import t from 'app/js/utils/t';
import SpinnerButton from 'app/js/views/v3/components/core/buttons/SpinnerButton';
import EmptyPage from 'app/js/views/v3/components/pages/empty/EmptyPage';
import * as kinds from 'app/js/views/v3/helpers/props/kinds';
import pmsLogo from 'app/img/pms-logo.svg';

type InstallingServerUpdatePageProps = {
  friendlyName?: string;
  isPending?: boolean;
  onTryAgainPress: (...args: any[]) => any;
};

function InstallingServerUpdatePage(props: InstallingServerUpdatePageProps) {
  const { friendlyName, isPending, onTryAgainPress } = props;

  return (
    <EmptyPage
      description={t('Grab a cup of tea. This may take a couple of minutes.')}
      iconSrc={pmsLogo}
      title={t('The server "{serverName}" is installing an update', {
        serverName: friendlyName,
      })}
    >
      <SpinnerButton
        isSpinning={isPending}
        kind={kinds.PRIMARY}
        onPress={onTryAgainPress}
      >
        {t('Retry connection')}
      </SpinnerButton>
    </EmptyPage>
  );
}

export default InstallingServerUpdatePage;
