/* eslint-plugin-disable @typescript-eslint */

function formatPropertyLabel(propertyName: any) {
  let label = '';

  for (let i = 0, len = propertyName.length; i < len; i++) {
    const currChar = propertyName.charAt(i);
    const upperChar = currChar.toUpperCase();

    if (i === 0) {
      label += upperChar;
    } else if (currChar === upperChar) {
      label += ' ' + upperChar;
    } else {
      label += currChar;
    }
  }

  return label;
}

export default formatPropertyLabel;
