/* eslint-plugin-disable @typescript-eslint */

import providerFeatureTypes from 'app/js/utils/constants/providerFeatureTypes';

export default function getSourceDirectoryFromProviderModel(
  providerModel: any,
  directoryKey: any
) {
  const contentFeatureModel = providerModel
    .get('features')
    .get(providerFeatureTypes.CONTENT);

  const directoriesModel = contentFeatureModel.get('directories');

  if (!contentFeatureModel || !directoriesModel) {
    return null;
  }

  const directoryModel = directoriesModel.find(
    (directory: any) =>
      directory.get('hubKey') === directoryKey ||
      directory.get('key') === directoryKey
  );

  if (!directoryModel) {
    return null;
  }

  return directoryModel.toJSON();
}
