/* eslint-plugin-disable @typescript-eslint */

import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { resolve } from 'app/js/adapters/adapters';
import assert from 'app/js/utils/log/assert';
import shallowEqual from 'app/js/views/v3/helpers/shallowEqual';

interface MetricsPageTrackerProps {
  page: string;
  isDelegated?: boolean;
}

class MetricsPageTracker extends Component<MetricsPageTrackerProps> {
  static childContextTypes = {
    metricsPage: PropTypes.string.isRequired,
  };

  // eslint-disable-next-line react/sort-comp
  page: any;

  //
  // Lifecycle

  constructor(props: any, context: any) {
    super(props, context);

    this.page = props.page;
  }

  getChildContext() {
    return { metricsPage: this.page };
  }

  componentDidMount() {
    const { children, isDelegated, ...metricsProperties } = this.props;

    if (!isDelegated) {
      // Waiting until the next frame helps our latency tracking include the
      // browser's repaint.
      window.requestAnimationFrame(() => {
        resolve('metrics').trackView(metricsProperties);
      });
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps: MetricsPageTrackerProps) {
    __DEV__ &&
      assert(
        nextProps.page === this.page,
        '<MetricsPageTracker> does not support changing `page` on the fly. Context is used to pass the page value to children.'
      );
  }

  componentDidUpdate(prevProps: MetricsPageTrackerProps) {
    const {
      // @ts-expect-error ts-migrate(2339) FIXME: Property 'children' does not exist on type 'Metric... Remove this comment to see the full error message
      children: prevChildren,
      isDelegated: prevIsDelegated,
      ...prevMetricsProperties
    } = prevProps;

    const { children, isDelegated, ...metricsProperties } = this.props;

    const hasChanged =
      prevIsDelegated !== isDelegated ||
      !shallowEqual(prevMetricsProperties, metricsProperties);

    if (!isDelegated && hasChanged) {
      resolve('metrics').trackView(metricsProperties);
    }
  }

  //
  // Render

  render() {
    return this.props.children
      ? React.Children.only(this.props.children)
      : null;
  }
}

export default MetricsPageTracker;
