/* eslint-plugin-disable @typescript-eslint */

const LEVEL_FACTOR = {
  h264: 10,
  hevc: 30,
};

function formatVideoLevel(level: any, codec: any) {
  if (level === null || level === undefined || Number.isNaN(level)) {
    return '';
  }

  const levelNumber = parseInt(level, 10);
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  const factor = LEVEL_FACTOR[codec || 'h264']; // default to h264 if codec is falsy

  return Number.isFinite(levelNumber) && Number.isFinite(factor)
    ? (levelNumber / factor).toFixed(1)
    : String(level);
}

export default formatVideoLevel;
