/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';

export default function retryTimes(promiseGeneratingFn: any, options = {}) {
  let retriesLeft = Math.floor(Math.max((options as any).times || 0, 0));
  const delay = Math.floor(Math.max((options as any).delay || 0, 0));
  if ((options as any).cancel) {
    (options as any).cancel.always(() => {
      retriesLeft = -1;
    });
  }
  const deferred = $.Deferred();
  let timer: any;
  if ((options as any).maxDuration) {
    timer = window.setTimeout(() => {
      deferred.reject('maxDuration');
    }, (options as any).maxDuration);
  }
  function tryAgain() {
    if (retriesLeft < 0) {
      return;
    }
    promiseGeneratingFn()
      .done(function () {
        if (timer) {
          window.clearTimeout(timer);
        }
        // eslint-disable-next-line prefer-rest-params
        deferred.resolve(...arguments);
      })
      .fail(function () {
        retriesLeft = retriesLeft - 1;
        if (retriesLeft < 0) {
          if (timer) {
            window.clearTimeout(timer);
          }
          // eslint-disable-next-line prefer-rest-params
          deferred.reject(...arguments);
        } else if (deferred.state() === 'pending') {
          window.setTimeout(tryAgain, delay);
        }
      });
  }
  tryAgain();
  return deferred.promise();
}
