/* eslint-plugin-disable @typescript-eslint */

import log from 'app/js/utils/log/log';
import isString from 'app/js/utils/string/isString';

function parseJSON(value: any, defaultValue = {}) {
  try {
    return JSON.parse(value);
  } catch (error) {
    const snippet = isString(value) ? value.substring(0, 50) : 'no response';
    log.warn(`[Parser] Unable to parse JSON: ${error.message} (${snippet})`);

    return defaultValue;
  }
}

export default parseJSON;
