/* eslint-plugin-disable @typescript-eslint */

import { MY_PLEX_SIGN_IN_STATE_NONE } from 'app/js/models/server/ServerModel';

function signOutServer(cloudAccount: any) {
  return (
    cloudAccount
      // These request can take a while, so don't use our default timeout.
      .destroy({ timeout: 0 })
      .promise()
      .done(() => {
        cloudAccount.server.set({
          accessToken: undefined,
          myPlexSigninState: MY_PLEX_SIGN_IN_STATE_NONE,
          myPlexSubscription: false,
        });

        cloudAccount.restoreDefaults();
      })
  );
}

export default signOutServer;
