/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import BaseClass from 'app/js/utils/BaseClass';

function mapInverted(keys: any, values: any) {
  const invertedKeys = _.invert(keys);

  return _.reduce(
    keys,
    (memo, key) => {
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      memo[key] = values[invertedKeys[key]];
      return memo;
    },
    {}
  );
}

const slideshowStateByState = {
  playing: 'playing',
  paused: 'paused',
  stopped: 'paused',
  error: 'paused',
  buffering: 'playing',
};

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const PlayerUtil = BaseClass.extend({
  //
  // Properties

  types: {
    VIDEO: 'video',
    MUSIC: 'music',
    PHOTO: 'photo',
    ROM: 'rom',
  },

  elements: {
    VIDEO: 'video',
    MUSIC: 'audio',
    PHOTO: 'photo',
  },

  streams: {
    VIDEO: 'video',
    MUSIC: 'audio',
    PHOTO: 'photo',
  },

  states: {
    STOPPED: 'stopped',
    PAUSED: 'paused',
    PLAYING: 'playing',
    BUFFERING: 'buffering',
    ERROR: 'error',
  },

  slideshowStates: {
    PLAYING: 'playing',
    PAUSED: 'paused',
  },

  castStates: {
    // Cast errors use the IDLE state with idleReason=ERROR. List the error
    // state first so the inverted object maps IDLE to STOPPED.
    ERROR: 'IDLE',
    STOPPED: 'IDLE',
    PAUSED: 'PAUSED',
    PLAYING: 'PLAYING',
    BUFFERING: 'BUFFERING',
  },

  mediaStates: {
    LOADING: 'LOADING',
    BUFFERING: 'BUFFERING',
    READY: 'READY',
    ERROR: 'ERROR',
  },

  //
  // Init

  initialize() {
    this.typeByElement = mapInverted(this.elements, this.types);
    this.elementByType = mapInverted(this.types, this.elements);

    this.typeByStream = mapInverted(this.streams, this.types);
    this.streamByType = mapInverted(this.types, this.streams);

    this.statebyCastState = mapInverted(this.castStates, this.states);
    this.castStateByState = mapInverted(this.states, this.castStates);
  },

  //
  // Control

  getTypeByElement(element: any) {
    return this.typeByElement[element];
  },

  getElementByType(type: any) {
    return this.elementByType[type];
  },

  getTypeByStream(type: any) {
    return this.typeByStream[type];
  },

  getStreamByType(type: any) {
    return this.streamByType[type];
  },

  getStateByCastState(castState: any) {
    return this.statebyCastState[castState];
  },

  getCastStateByState(state: any) {
    return this.castStateByState[state];
  },

  getSlideshowStateByState(state: any) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    return slideshowStateByState[state];
  },
});

// Singleton
const playerUtil = new PlayerUtil();

export default playerUtil;
