import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';

const BaseList = Marionette.CollectionView.extend({
  //
  // Control

  appendHtml: function () {
    // Use the append method appropriate to the flag set on the list. If no
    // flags are set, defer to the default appendHtml method on Marionette's
    // CollectionView.

    if (this.sorted) {
      // eslint-disable-next-line prefer-rest-params, prefer-spread
      return this.sortedAppendHtml.apply(this, arguments);
    } else if (this.reversed) {
      // eslint-disable-next-line prefer-rest-params, prefer-spread
      return this.reversedAppendHtml.apply(this, arguments);
    } else {
      return Marionette.CollectionView.prototype.appendHtml.apply(
        this,
        // eslint-disable-next-line prefer-rest-params
        arguments
      );
    }
  },

  sortedAppendHtml: function (collectionView: any, itemView: any, index: any) {
    // Use the index parameter to insert item views at a specified place
    // in the list, so the list retains the sort order of the collection.

    if (collectionView.isBuffering) {
      // Buffering happens on reset events and initial renders in order to
      // reduce the number of inserts into the document, which are
      // expensive.

      const children = collectionView.elBuffer.childNodes;

      if (children.length <= index) {
        collectionView.elBuffer.appendChild(itemView.el);
      } else {
        collectionView.elBuffer.insertBefore(itemView.el, children[index]);
      }

      collectionView._bufferedChildren.push(itemView);
    } else {
      const $container = collectionView.itemViewContainer
        ? collectionView.$(collectionView.itemViewContainer)
        : collectionView.$el;
      const $children = $container.children();

      if ($children.size() <= index) {
        $container.append(itemView.el);
      } else {
        $children.eq(index).before(itemView.el);
      }
    }
  },

  reversedAppendHtml: function (collectionView: any, itemView: any) {
    // Use prepend and insertBefore instead of appending items to the list.

    if (collectionView.isBuffering) {
      // Buffering happens on reset events and initial renders in order to
      // reduce the number of inserts into the document, which are
      // expensive.

      collectionView.elBuffer.insertBefore(
        itemView.el,
        collectionView.elBuffer.firstChild
      );
      collectionView._bufferedChildren.push(itemView);
    } else {
      const $container = collectionView.itemViewContainer
        ? collectionView.$(collectionView.itemViewContainer)
        : collectionView.$el;

      $container.prepend(itemView.el);
    }
  },

  measureList: function () {
    if (!this.children.length) {
      return;
    }

    const children = this.children._views;
    // @ts-expect-error ts-migrate(2538) FIXME: Type 'undefined' cannot be used as an index type.
    const listItem = children[_.first(_.keys(children))];

    // Defer so the list item can be rendered and we get an actual width and
    // height back.
    _.defer(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.itemWidth = listItem.$el.outerWidth(true);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.itemHeight = listItem.$el.outerHeight(true);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.itemMarginRight = parseInt(listItem.$el.css('margin-right'), 10);
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.itemMarginBottom = parseInt(listItem.$el.css('margin-bottom'), 10);

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.triggerMethod('measure', this);
      }, this)
    );
  },

  getViewForModel: function (model: any) {
    // Reach into Marionette internal view cache
    const viewId = this.children._indexByModel[model.cid];
    return this.children._views[viewId];
  },

  reorder: function () {
    const children = this.children;
    const models = this.collection.models;

    const els = _.map(models, function (model, index) {
      const view = children.findByModel(model);
      view._index = index;
      return view.el;
    });

    this.$el.append(els);
  },

  //
  // Listeners

  onCollectionRendered: function () {
    if (!this.measure) {
      return;
    }

    this.measureList();
  },
});

export default BaseList;
