/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import log from 'app/js/utils/log/log';
import path from 'app/js/utils/url/path';

// CORS requests with custom headers require an OPTIONS preflight request.
// Moving custom headers to the query string can avoid the preflight. In
// addition, warn against requests that will require an unnecessary preflight.
// See: https://developer.mozilla.org/en-US/docs/Web/HTTP/Access_control_CORS

const blacklistedMethods = {
  PUT: true,
  DELETE: true,
};

const allowedHeaders = {
  Accept: true,
  'Accept-Language': true,
  'Content-Language': true,
  'Content-Type': true,
};

const contentTypeRegex =
  /^(text\/plain|multipart\/form-data|application\/x-www-form-urlencoded)/;
const plexHeaderRegex = /(^X-Plex-[^=]+)/i;
const urlWithQueryRegex = /(^[^?]+)\?(.+$)/;

export default function simpleCORSFilter(options: any) {
  const {
    type,
    contentType,
    headers,
    allowHeadersInUrl,
    allowBlacklistedMethodHeaders,
  } = options;

  // Only allow headers to be moved to the url with requests to plex.tv or
  // Plex Media Server. Other clients may not support headers as query string
  // arguments.

  if (!allowHeadersInUrl) {
    return;
  }

  // Older server versions can't handle empty request headers for some methods.
  // See: https://github.com/plexinc/plex-media-server/issues/5315
  // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
  if (blacklistedMethods[type] && !allowBlacklistedMethodHeaders) {
    return;
  }

  const plexHeaders = {};
  let hasPlexHeaders = false;

  _.each(headers, (value, key) => {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    if (allowedHeaders[key]) {
      return;
    }

    if (plexHeaderRegex.test(key)) {
      hasPlexHeaders = true;
      // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
      plexHeaders[key] = value;
      delete headers[key];
    } else {
      log.warn(
        'Did not move header "%s" to query string. This can result in an unnecessary OPTIONS preflight request.',
        key
      );
    }
  });

  if (hasPlexHeaders) {
    const searchMatch = options.url.match(urlWithQueryRegex);
    const [, origin, query] = searchMatch || [null, options.url, null];

    // Capture and preserve existing url params first, unless they're plex
    // header params which get stripped out and appended last.
    const parts = query ? query.split('&') : [];
    const [plexParams, nonPlexParams] = _.partition(parts, (part) =>
      plexHeaderRegex.test(part)
    );

    // Add data not already encoded in url params for GET requests.
    const data = options.data;
    if (type === 'GET' && data) {
      nonPlexParams.push(_.isString(data) ? data : path.stringifyParams(data));
      delete options.data;
    }

    // Add plex headers that do not already exist in url params.
    plexParams.forEach((param) => {
      const match = param.match(plexHeaderRegex);
      if (match) {
        // Remove header parameters already listed in url params.
        // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
        delete plexHeaders[match[1]];
      }
    });

    const encodedPlexHeaders = path.stringifyParams(plexHeaders);
    if (encodedPlexHeaders) {
      plexParams.push(encodedPlexHeaders);
    }

    options.url = `${origin}?${nonPlexParams.concat(plexParams).join('&')}`;
  }

  if (contentType && !contentTypeRegex.test(contentType)) {
    // See https://github.com/plexinc/plex-web-client/issues/2940
    log.warn(
      'Sending %s request with Content-Type "%s". This can result in an unnecessary OPTIONS preflight request.',
      type,
      contentType
    );
  }
}
