/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { resolve } from 'app/js/adapters/adapters';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import {
  changePrimaryServer,
  resetPrimaryServer,
} from 'app/js/stores/actions/appActions';
import { confirmRelayConnection } from 'app/js/stores/actions/serverActions';
import BaseClass from 'app/js/utils/BaseClass';
import * as metricsContexts from 'app/js/utils/constants/metrics/metricsContexts';
import log from 'app/js/utils/log/log';
import eventMixin from 'app/js/utils/mixins/eventMixin';

// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const PrimaryServerManager = BaseClass.extend({
  mixins: [eventMixin],

  //
  // Init

  initialize(options: any) {
    this.appModel = options.appModel;
    this.servers = this.appModel.get('servers');
  },

  //
  // Control

  // @ts-expect-error ts-migrate(6133) FIXME: 'options' is declared but its value is never read.
  start(options: any) {
    this.stop();

    this.listenTo(this.appModel, {
      'change:primaryServer': this.onPrimaryServerChange,
      'change:primaryServer.change:isPrimaryEligible':
        this.onPrimaryServerEligibleChange,
      'change:primaryServer.change:isOutOfDate':
        this.onPrimaryServerOutOfDateChange,
      'change:isWaitingForServers': this.onWaitingForServersChange,
      'change:primaryServer.change:isRelayed':
        this.onPrimaryServerIsRelayedChange,
    });

    // It is important to use the `update` event instead of the `remove` event,
    // since Backbone does not remove the model reference from the collection
    // until after the `remove` event is triggered and the listener checks if
    // the primary server's id still exists in the collection.

    this.listenTo(this.servers, {
      'update reset': this.onServersUpdate,
    });

    this._setPrimaryServer(metricsContexts.STARTUP);
  },

  stop() {
    this.stopListening();
    this._primaryServerPromise = null;
  },

  _setPrimaryServer(metricsContext: any) {
    if (this._primaryServerPromise) {
      return this._primaryServerPromise;
    }

    const appStore = resolve('appStore');

    // Reset the primary server while we wait for a decision. If we
    // previously had no servers or the primary server is removed, keeping the
    // primary server stale can be dangerous.
    appStore.dispatch(resetPrimaryServer());

    const primaryServerPromise = this._waitForPrimaryServer();
    this._primaryServerPromise = primaryServerPromise;

    // If the manager is stopped before the promise resolves or rejects, ensure
    // that the callback is a no-op.

    primaryServerPromise
      .done((server: any) => {
        if (primaryServerPromise === this._primaryServerPromise) {
          this._primaryServerPromise = null;

          // Do not overwrite a primary server if something else set it while we
          // were waiting for one.

          if (this.appModel.get('primaryServer')) {
            return;
          }

          const isWaitingForVersion =
            !server.get('version') && !server.get('isRejected');

          // A server may not have a version immediately, so an out of date
          // server can slip through our checks. If the server is out of date or
          // does not yet have a version and is not unavailable, try to set the
          // primary server again.

          if (
            __MIN_SERVER_VERSION__ &&
            (server.get('isOutOfDate') || isWaitingForVersion)
          ) {
            log(
              '[Primary Server] The server, %s, is waiting for a version',
              server.get('debugName')
            );

            this._setPrimaryServer(metricsContext);
          } else {
            appStore.dispatch(
              changePrimaryServer({
                machineIdentifier: server.id,
                metricsProperties: { context: metricsContext },
              })
            );
          }
        }
      })
      .fail(() => {
        if (primaryServerPromise === this._primaryServerPromise) {
          this._primaryServerPromise = null;
          this.appModel.set('isWaitingForPrimaryServer', false);
        }
      });

    return primaryServerPromise;
  },

  _waitForPrimaryServer() {
    // Prioritize the last primary server above all else. Then, try to find an
    // owned server, followed by any full server, shared or owned.

    const user = this.appModel.get('user');
    const lastPrimaryServerID = user.get('lastPrimaryServerID');
    let promise;

    if (lastPrimaryServerID) {
      log(
        '[Primary Server] Waiting for the last primary server, %s, to connect',
        lastPrimaryServerID
      );

      promise = this.servers.populateServer({
        machineIdentifier: lastPrimaryServerID,
        isPrimaryEligible: true,
        isOutOfDate: false,
      });
    } else {
      promise = $.Deferred().reject().promise();
    }

    promise = promise.then(null, () => {
      log('[Primary Server] Waiting for any owned server to connect');

      return this.servers.populateServer({
        isFullOwnedServer: true,
        isPrimaryEligible: true,
        isOutOfDate: false,
      });
    });

    promise = promise.then(null, () => {
      log('[Primary Server] Waiting for any eligible server to connect');

      return this.servers.populateServer({
        isPrimaryEligible: true,
        isOutOfDate: false,
      });
    });

    promise = promise.then(null, () => {
      log('[Primary Server] Waiting for any eligible server');

      return this.servers.findServer({
        isPrimaryEligible: true,
        isOutOfDate: false,
      });
    });

    return promise;
  },

  //
  // Listeners

  onPrimaryServerChange(appModel: any, primaryServer: any) {
    // Immediately save the primary server change to server in case the app
    // layout changes during the debounce wait time.
    userStorageModel.flushDebouncedSave();

    const previousPrimaryServer = appModel.previous('primaryServer');
    if (previousPrimaryServer) {
      this.stopListening(previousPrimaryServer);
    }

    if (primaryServer) {
      log.info(
        '[Primary Server] %s is now the primary server',
        primaryServer.get('debugName')
      );

      const user = appModel.get('user');
      user.set('lastPrimaryServerID', primaryServer.id);

      if (primaryServer.get('isRelayed')) {
        const appStore = resolve('appStore');
        appStore.dispatch(
          confirmRelayConnection({
            machineIdentifier: primaryServer.id,
            serverName: primaryServer.get('friendlyName'),
          })
        );
      }
    } else if (!appModel.get('isWaitingForPrimaryServer')) {
      log.warn('[Primary Server] No primary server found');
    } else {
      log('[Primary Server] Waiting to set the primary server');
    }
  },

  onPrimaryServerEligibleChange(primaryServer: any, isPrimaryEligible: any) {
    const user = this.appModel.get('user');

    if (!isPrimaryEligible) {
      log(
        '[Primary Server] The previous primary server, %s, is unauthorized',
        primaryServer.get('debugName')
      );

      user.unset('lastPrimaryServerID');
      this._setPrimaryServer(metricsContexts.FALLBACK);
    }
  },

  onPrimaryServerOutOfDateChange(primaryServer: any, isOutOfDate: any) {
    const user = this.appModel.get('user');

    if (isOutOfDate) {
      log(
        '[Primary Server] The previous primary server, %s, is out of date',
        primaryServer.get('debugName')
      );

      user.unset('lastPrimaryServerID');
      this._setPrimaryServer(metricsContexts.FALLBACK);
    }
  },

  onWaitingForServersChange(appModel: any, isWaitingForServers: any) {
    // If the servers collection is reset, make sure we reset the primary server
    // to null until a decision is made.

    if (isWaitingForServers) {
      const primaryServer = appModel.get('primaryServer');
      const willPrimaryServerChange =
        !primaryServer || !this.servers.get(primaryServer.id);
      const isWaitingForPrimaryServer = appModel.get(
        'isWaitingForPrimaryServer'
      );

      if (willPrimaryServerChange && !isWaitingForPrimaryServer) {
        this._setPrimaryServer(metricsContexts.FALLBACK);
      }
    }
  },

  onPrimaryServerIsRelayedChange(primaryServer: any, isRelayed: any) {
    if (isRelayed) {
      const appStore = resolve('appStore');
      appStore.dispatch(
        confirmRelayConnection({
          machineIdentifier: primaryServer.id,
          serverName: primaryServer.get('friendlyName'),
        })
      );
    }
  },

  onServersUpdate() {
    const user = this.appModel.get('user');
    const primaryServer = this.appModel.get('primaryServer');

    if (primaryServer) {
      // Check if the collection still has the primary server's id after a server
      // is removed. This is more truthful than comparing the removed server since
      // a server may be removed after it is merged with another server.

      const isPrimaryServerRemoved =
        primaryServer && !this.servers.get(primaryServer.id);

      if (isPrimaryServerRemoved) {
        log(
          '[Primary Server] The previous primary server, %s, was removed',
          primaryServer.get('debugName')
        );

        user.unset('lastPrimaryServerID');
        this._setPrimaryServer(metricsContexts.FALLBACK);
      }
    } else {
      // If there is no primary server yet, and a server has been added, try to
      // set the primary server again.

      this._setPrimaryServer(metricsContexts.STARTUP);
    }
  },
});

export default PrimaryServerManager;
