/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import MetricsPageTracker from 'app/js/views/v3/components/core/metrics/MetricsPageTracker';
import PageScroller from './PageScroller';
import styles from './Page.css';

type PageProps = {
  children?: React.ReactNode;
  metricsPage: string;
  metricsProperties?: any;
  isMetricsDelegated?: boolean;
};

function Page(props: PageProps) {
  const {
    children,
    metricsPage,
    metricsProperties,
    isMetricsDelegated,
    ...otherProps
  } = props;

  return (
    <MetricsPageTracker
      {...metricsProperties}
      isDelegated={isMetricsDelegated}
      page={metricsPage}
    >
      <PageScroller className={styles.page} {...otherProps}>
        {children}
      </PageScroller>
    </MetricsPageTracker>
  );
}

export default Page;
