import _ from 'underscore';
import assert from 'app/js/utils/log/assert';
import parseBoolean from 'app/js/utils/parser/parseBoolean';
import findTabbableDescendants from './findTabbableDescendants';

let previousActiveElement: any;
let scopedElement: any;
function tryFocus(element: any) {
  try {
    element.focus();
  } catch (error) {
    // The element may not exist in the DOM anymore.
  }
}
export function resetFocus(parentElement: any) {
  const activeElement = document.activeElement;
  const doesParentContainActiveElement =
    activeElement && parentElement && parentElement.contains(activeElement);
  if (activeElement && !doesParentContainActiveElement) {
    (activeElement as any).blur();
  }
}
export function startScopedFocus(element: any) {
  previousActiveElement = document.activeElement;
  scopedElement = element;
  resetFocus(element);
}
export function scopeTabEvent(event: any) {
  assert(
    scopedElement,
    'Call `startScopedFocus(element)` before `scopeTabEvent`'
  );
  const tabbable = findTabbableDescendants(scopedElement);
  if (!tabbable.length) {
    return;
  }
  event.preventDefault();
  const activeElement = document.activeElement;
  // @ts-expect-error ts-migrate(2345) FIXME: Argument of type 'Element' is not assignable to pa... Remove this comment to see the full error message
  const index = activeElement ? tabbable.indexOf(activeElement) : -1;
  const lastIndex = tabbable.length - 1;
  let nextIndex;
  if (index === -1) {
    nextIndex = event.shiftKey ? lastIndex : 0;
  } else {
    nextIndex = event.shiftKey ? index - 1 : index + 1;
    // Wrap focus to the first or last tabbable element.
    if (nextIndex < 0) {
      nextIndex = lastIndex;
    } else if (nextIndex > lastIndex) {
      nextIndex = 0;
    }
  }
  tryFocus(tabbable[nextIndex]);
}
export function scopeAutoFocus() {
  assert(
    scopedElement,
    'Call `startScopedFocus(element)` before `scopeAutoFocus`'
  );
  const tabbable = findTabbableDescendants(scopedElement);
  const element = _.find(tabbable, (elem) =>
    parseBoolean((elem as any).getAttribute('data-autofocus'))
  );
  if (element) {
    tryFocus(element);
  }
}
export function resetScopedFocus() {
  scopedElement = null;
}
export function restorePreviousFocus() {
  if (previousActiveElement) {
    tryFocus(previousActiveElement);
    previousActiveElement = null;
  }
}
