/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import log from 'app/js/utils/log/log';
import UnexpectedErrorPage from 'app/js/views/v3/components/pages/empty/UnexpectedErrorPage';
import Page from './Page';

type PageConnectorProps = any;
type PageConnectorState = any;

class PageConnector extends Component<PageConnectorProps, PageConnectorState> {
  constructor(props: any, context: any) {
    super(props, context);

    this.state = { hasError: false };
  }

  //
  // Lifecycle

  componentDidCatch(error: any) {
    log.error(error);

    this.setState({ hasError: true });
  }

  //
  // Render

  render() {
    const { hasError } = this.state;

    if (hasError) {
      return <UnexpectedErrorPage />;
    }

    // @ts-expect-error ts-migrate(2741) FIXME: Property 'metricsPage' is missing in type '{ child... Remove this comment to see the full error message
    return <Page {...this.props} />;
  }
}

export default PageConnector;
