/* eslint-plugin-disable @typescript-eslint */

const ESSENTIAL_PLEX_TV_COOKIES = ['plex_tv_auth', 'plex_tv_lang'];

// We need to delete non essential cookies to ensure no tracking cookies are
// present for children profiles.
export default function deleteNonEssentialCookies() {
  document.cookie.split(';').forEach((cookie) => {
    const [name] = cookie.trim().split('=');

    if (name && !ESSENTIAL_PLEX_TV_COOKIES.includes(name)) {
      // We can't know which domain the cookie is from, so try the domains we
      // know of.
      document.cookie = `${name}=; Domain=plex.tv; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;`;
      document.cookie = `${name}=; Domain=.plex.tv; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;`;
      document.cookie = `${name}=; Domain=.app.plex.tv; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;`;
    }
  });
}
