/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import {
  MAPPING_STATE_MAPPED,
  MAPPING_STATE_UNKNOWN,
  MAPPING_STATE_WAITING,
  REMOTE_ACCESS_STATE_CONNECTING,
  REMOTE_ACCESS_STATE_DISCONNECTING,
  REMOTE_ACCESS_STATE_UNKNOWN,
} from 'app/js/models/server/ServerCloudAccountModel';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';

function connectServerCommand(options: any) {
  const cloudAccount = options.cloudAccount;
  const settings = cloudAccount.server.get(__PLATFORM__).get('settings');
  const publishSetting = settings.get('PublishServerOnPlexOnlineKey');
  const deferred = $.Deferred();
  const pollDelay = 500;
  let pollInterval: any;

  // If we are retrying, the state will have already been set, and we don't
  // want to show disconnecting and then connecting, which might confuse
  // users.

  if (!cloudAccount.get('isWaiting')) {
    const status = options.value
      ? REMOTE_ACCESS_STATE_CONNECTING
      : REMOTE_ACCESS_STATE_DISCONNECTING;
    cloudAccount.set('remoteAccessState', status);
  }

  const initialState = cloudAccount.previous('remoteAccessState');

  function verifyAccount() {
    const accountPromise = cloudAccount.fetch();

    accountPromise.done(function () {
      const mappingState = cloudAccount.get('mappingState');
      const waitingOnPublish =
        options.value && mappingState === MAPPING_STATE_UNKNOWN;
      const waitingOnUnpublish =
        !options.value && mappingState === MAPPING_STATE_MAPPED;

      // Poll for changes if the publish is in an interim step.
      if (
        mappingState === MAPPING_STATE_WAITING ||
        waitingOnPublish ||
        waitingOnUnpublish
      ) {
        if (!pollInterval) {
          pollInterval = window.setInterval(verifyAccount, pollDelay);
        }
      } else {
        pollInterval = window.clearInterval(pollInterval);

        // When disabled, the state is neither ok nor disabled, but we
        // shouldn't stay in a disconnecting state indefinitely.
        if (cloudAccount.get('isWaiting')) {
          cloudAccount.set('remoteAccessState', REMOTE_ACCESS_STATE_UNKNOWN);
        }

        deferred.resolve();
      }
    });
  }

  // These request can take awhile, so don't use our default timeout.
  const promise = publishSetting.save(
    {
      PublishServerOnPlexOnlineKey: options.value,
    },
    { timeout: 0 }
  );

  promise.done(verifyAccount);

  promise.fail(function (xhr: any) {
    let message;

    if (options.value) {
      message = t('There was a problem connecting your server.');
    } else {
      message = t('There was a problem disconnecting your server.');
    }

    dispatcher.trigger('show:alert', {
      type: 'error',
      message: message,
    });

    cloudAccount.set('remoteAccessState', initialState);
    deferred.reject(xhr);
  });

  return deferred.promise();
}

export default connectServerCommand;
