/* eslint-plugin-disable @typescript-eslint */

import videoPlayerQualities from 'app/js/utils/player/videoPlayerQualities';
import t from 'app/js/utils/t';
import formatResolution from './formatResolution';
import formatVideoBitrate from './formatVideoBitrate';

function formatVideoQuality(quality: any) {
  if (quality.index === videoPlayerQualities.CONVERT_AUTOMATICALLY) {
    return t('Convert Automatically');
  }

  if (quality.index === videoPlayerQualities.ORIGINAL) {
    return t('Original');
  }

  // Internal quality objects use `bitrate`, but server-provided media
  // settings use `maxVideoBitrate`.

  const bitrate = quality.bitrate || quality.maxVideoBitrate;
  const videoResolution = quality.videoResolution;

  if (!bitrate && !videoResolution) {
    return t('Unknown');
  }

  const units = [];

  if (bitrate) {
    units.push(formatVideoBitrate(bitrate));
  }

  if (videoResolution && (!bitrate || bitrate >= 1000)) {
    const resolution =
      videoPlayerQualities.getResolutionFromSize(videoResolution);
    // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
    units.push(formatResolution(resolution));
  }

  const formattedUnits = units.join(', ');

  if (quality.description) {
    return formattedUnits + ' ' + quality.description;
  }

  return formattedUnits;
}

export default formatVideoQuality;
