/* eslint-plugin-disable @typescript-eslint */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { resetAuthCodeExchange } from 'app/js/stores/actions/user/authCodeExchangeActions';
import { signInWithAuthToken } from 'app/js/stores/actions/user/signInLaunchActions';
import selectAuthCodeExchange from 'app/js/stores/selectors/user/selectAuthCodeExchange';
import shouldClientPoll from 'app/js/utils/shouldClientPoll';
import navigateToRoute from 'app/js/v4/routes/navigateToRoute';
import DownForMaintenancePage from 'app/js/views/v3/components/pages/empty/DownForMaintenancePage';
import RedirectToConnector from 'app/js/views/v3/components/pages/serverSettings/RedirectToConnector';
import ServiceAuthUrlConnector from 'app/js/views/v3/components/pages/serverSettings/ServiceAuthUrlConnector';
import SignInLaunchPagePollConnector from './SignInLaunchPagePollConnector';
import SignInLaunchPageRedirectConnector from './SignInLaunchPageRedirectConnector';

function createMapStateToProps() {
  return createSelector(selectAuthCodeExchange, (authCodeExchangeProps) => {
    return {
      isPollClient: shouldClientPoll(),
      ...authCodeExchangeProps,
    };
  });
}

const mapDispatchToProps = {
  dispatchResetAuthCodeExchange: resetAuthCodeExchange,
  dispatchSignInWithAuthToken: signInWithAuthToken,
};

type SignInLaunchPageConnectorProps = {
  providerID?: string;
  pinID?: number;
  code?: string;
  authToken?: string;
  errorMessage?: string;
  fatalErrorMessage?: string;
  signUp?: boolean;
  isPollClient: boolean;
  dispatchResetAuthCodeExchange: (...args: any[]) => any;
  dispatchSignInWithAuthToken: (...args: any[]) => any;
};

class SignInLaunchPageConnector extends Component<SignInLaunchPageConnectorProps> {
  //
  // Lifecycle

  componentDidMount() {
    this.props.dispatchResetAuthCodeExchange();
  }

  componentDidUpdate(prevProps: SignInLaunchPageConnectorProps) {
    const didReceiveAuthToken =
      this.props.authToken !== prevProps.authToken && this.props.authToken;

    if (didReceiveAuthToken) {
      this.props.dispatchSignInWithAuthToken({
        authToken: this.props.authToken,
        onSignInSuccess: this.props.signUp
          ? () => navigateToRoute('home')
          : null,
      });
    }
  }

  //
  // Render

  render() {
    const {
      providerID,
      errorMessage,
      fatalErrorMessage,
      signUp,
      isPollClient,
      dispatchResetAuthCodeExchange,
      dispatchSignInWithAuthToken,
      ...otherProps
    } = this.props;

    if (fatalErrorMessage) {
      // fatalErrorMessage isn't displayed anywhere, but its presence means
      // we couldn't create a PIN
      return <DownForMaintenancePage />;
    } else if (providerID) {
      return (
        <ServiceAuthUrlConnector
          component={RedirectToConnector}
          providerID={providerID}
          replace={true}
          signUp={signUp}
          {...otherProps}
        />
      );
    } else if (isPollClient) {
      return (
        <SignInLaunchPagePollConnector
          // @ts-expect-error ts-migrate(2322) FIXME: Type '{ pinID?: number | undefined; code?: string ... Remove this comment to see the full error message
          signUp={signUp}
          {...otherProps}
        />
      );
    } else {
      // @ts-expect-error ts-migrate(2322) FIXME: Type '{ signUp: boolean | undefined; }' is not ass... Remove this comment to see the full error message
      return <SignInLaunchPageRedirectConnector signUp={signUp} />;
    }
  }
}

export default connect(
  createMapStateToProps,
  mapDispatchToProps
)(SignInLaunchPageConnector);
