/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import { registerAll, resolve } from 'app/js/adapters/adapters';
import createPlexMetricsReporter from 'app/js/adapters/analytics/createPlexMetricsReporter';
import createSettingFinder from 'app/js/adapters/createSettingFinder';
import metricsInterface from 'app/js/adapters/interfaces/metricsInterface';
import metricsReporterInterface from 'app/js/adapters/interfaces/metricsReporterInterface';
import testServerConnection from 'app/js/adapters/testServerConnection';
import CompanionServerManager from 'app/js/managers/CompanionServerManager';
import DesktopServerConnectionManager from 'app/js/managers/DesktopServerConnectionManager';
import MetricsManager from 'app/js/managers/MetricsManager';
import PrimaryServerManager from 'app/js/managers/PrimaryServerManager';
import ProviderManager from 'app/js/managers/ProviderManager';
import ServerConnectionTestManager from 'app/js/managers/ServerConnectionTestManager';
import ServerManager from 'app/js/managers/ServerManager';
import ServerUpdateManager from 'app/js/managers/ServerUpdateManager';
import SourceManager from 'app/js/managers/SourceManager';
import LogCollection from 'app/js/models/debug/LogCollection';
import isPlaybackActive from 'app/js/stores/actions/helpers/isPlaybackActive';
import selectDeviceSettingsVolume from 'app/js/stores/selectors/settings/selectDeviceSettingsVolume';
import selectRemoteQuality from 'app/js/stores/selectors/settings/selectRemoteQuality';
import selectRestorePlayQueue from 'app/js/stores/selectors/settings/selectRestorePlayQueue';
import selectSubtitleSettings from 'app/js/stores/selectors/settings/selectSubtitleSettings';
import handleLocalCompanionCommand from 'app/js/utils/player/handleLocalCompanionCommand';
import MediaServerEventConnection from 'app/js/utils/pubsub/MediaServerEventConnection';

export default function baseDesktopAdaptersInitializer() {
  const appModel = resolve('appModel');

  //
  // Platform specific settings implementations

  registerAll({
    settingFinder: createSettingFinder(appModel),
    selectRemoteQuality,
    selectRestorePlayQueue,
    selectSubtitleSettings,
    selectLocalVolume: selectDeviceSettingsVolume,
  });

  //
  // Utils

  registerAll({
    metricsReporter: [createPlexMetricsReporter(), metricsReporterInterface],
    logs: new LogCollection([], { parent: appModel }),
    testServerConnection,
  });

  //
  // Platform implementations

  registerAll({
    companionServerManager: new CompanionServerManager({ appModel }),
    localCompanionCommandHandler: handleLocalCompanionCommand,
    mediaServerEventConnection: MediaServerEventConnection,
    metrics: [new MetricsManager({ isPlaybackActive }), metricsInterface],
    primaryServerManager: new PrimaryServerManager({ appModel }),
    providerManager: new ProviderManager({ appModel }),
    serverConnectionManager: new DesktopServerConnectionManager({ appModel }),
    serverConnectionTestManager: new ServerConnectionTestManager({ appModel }),
    serverManager: new ServerManager({ appModel }),
    serverUpdateManager: new ServerUpdateManager({ appModel }),
    sourceManager: new SourceManager({ appModel }),
  });

  return $.Deferred().resolve().promise();
}
