export default function mapToObject<Value, MappedValue>(
  array: Value[],
  mapKey: (value: Value, i: number, array: Value[]) => number | string,
  mapValue: (value: Value, i: number, array: Value[]) => MappedValue
): { [key: string]: MappedValue } {
  return array.reduce<{ [key: string]: MappedValue }>(
    (acc, object: Value, i: number) => {
      acc[mapKey(object, i, array)] = mapValue(object, i, array);
      return acc;
    },
    {}
  );
}
