import { createSlice } from '@reduxjs/toolkit';
import {
  markFailed,
  markPending,
  markPopulated,
} from 'app/js/v4/utils/data/asyncStateMarkers';
import {
  ResourcesFetchFailedAction,
  ResourcesFetchRequestedAction,
  ResourcesFetchSucceededAction,
  ResourcesState,
} from './dashboardResourcesTypes';

const initialState: ResourcesState = {
  statistics: [],
  isPending: false,
  isPopulated: false,
  isUnsupported: false,
  error: null,
  requestTimestampMilliseconds: null,
};

const dashboardResourcesSlice = createSlice({
  name: 'dashboardResources',
  initialState,

  reducers: {
    reset() {
      return initialState;
    },

    resourcesFetchRequested(state, _action: ResourcesFetchRequestedAction) {
      markPending(state);
      state.isUnsupported = false;
    },

    resourcesFetchSucceeded(state, { payload }: ResourcesFetchSucceededAction) {
      markPopulated(state);
      state.statistics = payload.data;

      state.requestTimestampMilliseconds = payload.requestTimestampMilliseconds;
    },

    resourcesFetchFailed(state, { payload }: ResourcesFetchFailedAction) {
      markFailed(state, payload.error);
      state.isUnsupported = payload.error.status === 404;
    },
  },
});

export default dashboardResourcesSlice;
