import $ from 'jquery';
import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import browser from 'app/js/utils/device/browser';
import dispatcher from 'app/js/utils/dispatcher';
import t from 'app/js/utils/t';
import path from 'app/js/utils/url/path';
import UserSelectModalUserListItem from 'app/js/views/modals/user/UserSelectModalUserListItem';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/user/UserSelectModalPINPane.tpl';

const UserSelectModalPINPane = Marionette.Layout.extend({
  //
  // Properties
  className: 'user-select-modal-pin-container',
  template: tpl,
  ui: {
    digitContainer: '.pin-digit-container',
    input: '.pin-input',
    digits: '.pin-digit',
    loading: '.loading',
    removeBtn: '.remove-btn',
    saveBtn: '.save-btn',
    pinForgotLink: '.pin-forgot-link',
  },
  regions: {
    userList: '.user-select-list',
  },
  events: {
    'keydown input': 'onKeyDown',
    'input input': 'onInput',
    'keyup input': 'onKeyUp',
    'focus input': 'onFocus',
    'blur input': 'onBlur',
    'change input': 'onChange',
    // TODO: Abstract vendor prefixed events behind our view layer.
    'animationend .pin-input-container': 'onInputsAnimationEnd',
    'webkitAnimationEnd .pin-input-container': 'onInputsAnimationEnd',
    'oAnimationEnd .pin-input-container': 'onInputsAnimationEnd',
    'click .back-btn': 'onBackClick',
    'click .remove-btn': 'onRemoveClick',
    'submit #pin-form': 'onSubmit',
    'click .pin-digit': 'onPINClick',
    'keydown .save-btn': 'onSaveKeyDown',
  },
  onlyAfterRender: ['confirmUser'],
  //
  // Init
  initialize: function (options: any) {
    const appModel = adapters.resolve('appModel');
    this.isForced = options.isForced;
    this.isEditing = options.isEditing;
    this.isEditWarning = options.isEditWarning;
    this.showBackButton = options.showBackButton;
    this.openAddManagedUser = options.openAddManagedUser;
    this.focusDelay = options.focusDelay;
    this.user = appModel.get('user');
    // If the user already has a PIN and is not a managed user, we should
    // confirm their current PIN before allowing them to edit it.
    this.isEditConfirmation =
      this.isEditing &&
      this.model.get('protected') &&
      !this.model.get('restricted');
  },
  //
  // Control
  serializeData: function () {
    const isEditing = this.isEditing && !this.isEditConfirmation;
    return {
      isEditing: isEditing,
      isEditWarning: this.isEditWarning,
      isEditConfirmation: this.isEditConfirmation,
      isOpeningAddManagedUser: this.openAddManagedUser,
      hasCurrentPIN: this.model.get('protected'),
      showBackButton: this.showBackButton,
    };
  },
  focusInput: function () {
    // Clearing on focus means we don't have a hidden cursor
    // when the user types 4 digits then re-focuses the input
    this.ui.input.val('');
    this.ui.input.focus();
  },
  getPIN: function () {
    return this.ui.input.val();
  },
  editPIN: function (pin: any) {
    if (this.ui.saveBtn.isDisabled()) {
      return;
    }
    this.ui.saveBtn.addClass('disabled');
    const promise = this.savePIN(pin);
    promise.fail(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.saveBtn.removeClass('disabled');
        dispatcher.trigger('show:alert', {
          type: 'error',
          message: t('This PIN could not be saved.'),
        });
      })
    );
  },
  removePIN: function () {
    if (this.ui.removeBtn.isDisabled()) {
      return;
    }
    this.ui.removeBtn.addClass('disabled');
    const promise = this.savePIN('');
    promise.fail(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.removeBtn.removeClass('disabled');
        dispatcher.trigger('show:alert', {
          type: 'error',
          message: t('This PIN could not be removed.'),
        });
      })
    );
  },
  savePIN: function (pin: any) {
    this.ui.loading.removeClass('hidden');
    const isManagedUser = this.model.get('restricted');
    const defaultSaveParams = this.model.getSaveParams();
    const params = {};
    // Include the user's current pin if it exists.
    if (this.currentPin) {
      (params as any).currentPin = this.currentPin;
    }
    // When removing the PIN for a managed user the v2 API requires we send a
    // `removePin` parameter rather than simply setting an empty PIN.
    if (pin === '' && isManagedUser) {
      (params as any).removePin = 1;
    } else {
      (params as any).pin = pin;
    }
    const finalParams = $.extend(true, {}, defaultSaveParams, params);
    const url = path.join(this.model.pinUrl(), finalParams);
    const xhrOptions = this.model.server.getXHROptions(url, {
      type: isManagedUser ? 'POST' : 'PUT',
    });
    const promise = $.ajax(xhrOptions);
    promise.done(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.model.set('protected', !!pin);
        // If the signed in user is updating their own PIN, we should also
        // update to the user model to match what we'd expect from plex.tv.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.model.id === this.user.id) {
          if (pin) {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.user.set('pin', this.user.hashPIN(pin));
          } else {
            // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
            this.user.unset('pin');
          }
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.user.set('home', this.user.hasHome());
        }
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.trigger('resolve', pin);
      }, this)
    );
    promise.always(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.loading.addClass('hidden');
      })
    );
    return promise;
  },
  confirmUser: function (pin: any) {
    this.ui.loading.removeClass('hidden');
    const promise = commands.execute('switchUser', {
      homeUser: this.model,
      pin: pin,
      allowGuestLock: !this.isEditing,
      openAddManagedUser: this.openAddManagedUser,
    });
    promise.done(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.isEditing) {
          // Now that the user has confirmed their current pin, render the
          // edit form.
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.isEditConfirmation = false;
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.currentPin = pin;
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.render();
        } else {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.trigger('resolve', pin);
        }
      }, this)
    );
    promise.fail(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.digitContainer.addClass('shake');
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.input.val('');
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.focusInput();
      })
    );
    promise.always(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.loading.addClass('hidden');
      })
    );
  },
  renderPIN: function () {
    const pin = this.getPIN();
    const length = pin.length;
    const showPin = this.isEditing && !this.isEditConfirmation;
    this.ui.digits.each(function (i: any, el: any) {
      let character = pin[i] || '';
      if (character && !showPin) {
        character = '&bull;';
      }
      $(el)
        .toggleClass('current', i === length)
        .html(character);
    });
  },
  //
  // Listeners
  onRender: function () {
    this.userList.show(
      new UserSelectModalUserListItem({
        model: this.model,
        disabled: true,
      })
    );
    // Don't show the forgot PIN link to managed users.
    this.ui.pinForgotLink.toggleClass('hidden', this.model.get('restricted'));
    // Mobile browsers won't always open the keyboard when an input is focused by JS
    // Safari won't ever if focus was triggered by JS
    // Chrome on Android won't if the user hasn't interacted with the page
    // (ie. first load with a single user that requires a PIN)
    if (!browser.mobile) {
      window.setTimeout(_.bind(this.focusInput, this), this.focusDelay);
    }
  },
  onKeyDown: function (event: any) {
    const keyCode = event.which;
    // Allow the enter key to behave normally.
    if (keyCode === 13) {
      return;
    }
    // Allow the backspace and delete keys to continue to `onInput`
    if (keyCode === 8 || keyCode === 46) {
      return;
    }
    // The Samsung Android keyboard (and possibly others) send 229 for almost all key presses,
    // backspace sends the proper key code so users can still delete input.
    // Allow 229 key codes to be validated in the `onInput` handler.
    if (keyCode === 229) {
      return;
    }
    const isNumeric = keyCode >= 48 && keyCode <= 57;
    const isNumericKeypad = keyCode >= 96 && keyCode <= 105;
    // If the key code is numeric or from the numeric keypad
    // allow it to be validated in the `onInput` handler.
    if (isNumeric || isNumericKeypad) {
      return;
    }
    // Prevent other characters from triggering the `onInput` handler
    event.preventDefault();
  },
  onInput: function (event: any) {
    const $target = $(event.currentTarget);
    const value = $target.val();
    const length = (value as any).length;
    const lastCharacter = (value as any).charAt((value as any).length - 1);
    if (!/[0-9]/.test(lastCharacter)) {
      $target.val((value as any).slice(0, length - 1));
      return;
    }
    if ((value as any).length === 4) {
      if (!this.isEditing || this.isEditConfirmation) {
        $target.blur();
        this.confirmUser(this.getPIN());
      } else {
        this.ui.saveBtn.focus();
      }
    }
  },
  onKeyUp: function () {
    this.renderPIN();
  },
  onFocus: function () {
    this.renderPIN();
  },
  onBlur: function () {
    this.renderPIN();
    this.ui.digits.removeClass('current');
  },
  onChange: function () {
    this.renderPIN();
  },
  onInputsAnimationEnd: function (event: any) {
    $(event.currentTarget).removeClass('shake');
  },
  onBackClick: function (event: any) {
    event.preventDefault();
    this.trigger('change:pane', 'users');
  },
  onRemoveClick: function (event: any) {
    event.preventDefault();
    this.removePIN();
  },
  onSubmit: function (event: any) {
    event.preventDefault();
    const pin = this.getPIN();
    if (pin.length !== 4) {
      dispatcher.trigger('show:alert', {
        type: 'error',
        message: t('Please enter a 4 digit PIN.'),
      });
      return;
    }
    if (this.isEditing && !this.isEditConfirmation) {
      this.editPIN(pin);
    } else {
      this.confirmUser(pin);
    }
  },
  onSaveKeyDown: function (event: any) {
    const keyCode = event.which;
    // Add special handling for the backspace and delete keys.
    if (keyCode === 8 || keyCode === 46) {
      event.preventDefault();
      this.ui.input.val('');
      this.focusInput();
    }
  },
  onPINClick: function () {
    this.focusInput();
  },
});
export default UserSelectModalPINPane;
