/* eslint-plugin-disable @typescript-eslint */

import PropTypes from 'prop-types';

export const POSTER = 'poster';
export const MOSAIC = 'mosaic';
export const TABLE = 'table';
export const SIMPLE_SUMMARY = 'simpleSummary';
export const SUMMARY = 'summary';
export const EPG_EPISODE = 'epgEpisode';
export const SHARED_ITEMS = 'sharedItems';

// Show the menu if there is more than one style option. Otherwise, still
// show the container with the poster size slider if the selected/only style
// is the poster or mosaic view.
export function showListStyles(allListStyles: any, selectedListStyle: any) {
  return (
    showListStylesSlider(selectedListStyle) || showListStylesMenu(allListStyles)
  );
}

export function showListStylesSlider(selectedListStyle: any) {
  return selectedListStyle === POSTER || selectedListStyle === MOSAIC;
}

export function showListStylesMenu(allListStyles: any) {
  return allListStyles.length > 1;
}

const metadataListStyle = PropTypes.oneOf([
  POSTER,
  MOSAIC,
  TABLE,
  SIMPLE_SUMMARY,
  SUMMARY,
  EPG_EPISODE,
  SHARED_ITEMS,
]);

export default metadataListStyle;
