/* eslint-plugin-disable @typescript-eslint */

import BaseModel from 'app/js/models/BaseModel';

let nextID = 0;

const ServerAlertModel = BaseModel.extend({
  //
  // Properties

  idAttribute: 'id',

  //
  // Control

  parse: function (response: any) {
    const json = response;

    json.time = new Date();
    json.id = 'evt' + nextID++;

    if (json._type === 'preference') {
      json.value = String(json.value);
    }

    return json;
  },
});

export default ServerAlertModel;
