/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';

const FilterOptionModel = AbstractModel.extend({
  //
  // Properties

  name: 'FilterOptionModel',
  context: ['server', 'section', 'type', 'standalone'],

  idAttribute: 'key',
  rootPropertyName: 'Directory',

  syncedAttrs: {
    key: attrTypes.encodedString,
    type: attrTypes.string,
    field: attrTypes.string,
    value: attrTypes.string,
    title: attrTypes.string,
  },

  //
  // Control

  augmentResponse(response: any) {
    // Migrate old attributes to new ones if fastKey exists
    // https://github.com/plexinc/api/issues/91#issuecomment-375445445

    // If `fastKey` exists there is a 1:1 mapping of old values to new ones,
    // aside from `type` which becomes `field`
    if (response.fastKey) {
      response.value = response.key;
      response.key = response.fastKey;
      response.field = response.type;

      delete response.type;
      delete response.fastKey;

      // If neither `fastKey` nor `value` exist we are on an older PMS server, or
      // on a options endpoint that does not offer a `fastKey` (resolution),
      // handle this case separately to not step on the `key` value
    } else if (!response.fastKey && !response.value) {
      response.value = response.key;
    }

    return response;
  },
});

export default FilterOptionModel;
