/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as responseTypes from 'app/js/models/responseTypes';
import path from 'app/js/utils/url/path';
import FileCollection from './FileCollection';
import FilePathCollection from './FilePathCollection';

const MAX_FILES = 500;
const FilePathListModel = AbstractModel.extend({
  //
  // Properties
  name: 'FilePathListModel',
  responseType: responseTypes.json,
  context: ['server', 'includeFiles', 'url'],
  url() {
    const params = {};
    if (this.includeFiles) {
      (params as any).includeFiles = 1;
    }
    return path.join(['/services/browse'], params);
  },
  sessionAttrs: {
    truncatedFilesCount: attrTypes.integer,
  },
  syncedRelations: {
    paths: {
      relationClass: FilePathCollection,
      rootPropertyName: 'Path',
    },
    files: {
      relationClass: FileCollection,
      rootPropertyName: 'File',
    },
  },
  //
  // Control
  augmentResponse(response: any) {
    if (response.File) {
      response.truncatedFilesCount = Math.max(
        0,
        response.File.length - MAX_FILES
      );
      response.File = response.File.slice(0, MAX_FILES);
    }
    return response;
  },
});
export default FilePathListModel;
