import BaseCollection from 'app/js/models/BaseCollection';
import SocialNetworkModel from './SocialNetworkModel';

const SocialNetworkCollection = BaseCollection.extend({
  //
  // Properties

  model: SocialNetworkModel,
  url: '/pms/social/networks',

  //
  // Control

  parse: function (response: any) {
    return this.convert(response, 'Directory');
  },
});

export default SocialNetworkCollection;
