/* eslint-plugin-disable @typescript-eslint */

import _ from 'underscore';
import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import sharingSettingsAttrs from 'app/js/utils/types/sharingSettingsAttrs';
import path from 'app/js/utils/url/path';

const HomeUserModel2 = AbstractModel.extend({
  //
  // Properties

  name: 'HomeUserModel',

  context: ['server'],

  rootPropertyName: 'User',

  entityType: entityTypes.HOME_USER,

  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    return createEntityID(this.entityType, state.id);
  },

  urlRoot() {
    // The v2 API does not yet support inviting full users to a Home.
    // See: https://github.com/plexinc/my-plex/issues/5262
    return this.get('restricted') ? '/api/v2/home/users' : '/api/home/users';
  },

  saveUrl() {
    let url = _.result(this, 'url');

    if (this.get('restricted')) {
      url = path.join([url, '/restricted']);
    }

    return url;
  },

  pinUrl() {
    const baseUrl = this.get('restricted')
      ? '/api/v2/home/users/restricted'
      : '/api/home/users';

    return path.join([baseUrl, this.get('id')]);
  },

  sessionAttrs: {
    isRemoving: attrTypes.boolean,
  },

  syncedAttrs: {
    id: attrTypes.integer,
    uuid: attrTypes.string,
    admin: attrTypes.boolean,
    protected: attrTypes.boolean,
    restricted: attrTypes.boolean,
    restrictionProfile: attrTypes.string,
    guest: attrTypes.boolean,
    title: attrTypes.string,
    username: attrTypes.string,
    email: attrTypes.string,
    thumb: attrTypes.string,
    ...sharingSettingsAttrs,
  },

  defaults: {
    isRemoving: false,
  },

  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'data' implicitly has an 'any' type.
  getSaveParams(data) {
    // eslint-disable-next-line prefer-rest-params
    const params = AbstractModel.prototype.getSaveParams.apply(this, arguments);

    if (!data || Object.keys(data).length === 0) {
      if (this.get('restrictionProfile')) {
        params.restrictionProfile = this.get('restrictionProfile');
      }

      // Sharing restriction related attributes need to be bundled up together
      // under a `sharingSettings` parameter.
      params.sharingSettings = {};

      Object.keys(sharingSettingsAttrs).forEach((attr) => {
        if (params.hasOwnProperty(attr)) {
          params.sharingSettings[attr] = params[attr];
          delete params[attr];
        }
      });

      if (this.get('restricted')) {
        params.friendlyName = this.get('username');
      } else if (this.isNew()) {
        params.invitedEmail = this.get('username');
      }
    }

    return params;
  },
});

export default HomeUserModel2;
