/* eslint-plugin-disable @typescript-eslint */

import $ from 'jquery';
import _ from 'underscore';
import BaseClass from 'app/js/utils/BaseClass';
import eventMixin from 'app/js/utils/mixins/eventMixin';
// Adapters provide a single module that any part of the application can
// depend on, which allows us to ignore unnecessary modules in platform
// specific builds. Modules registered to an adapter should adhere to the
// interface defined for the adapter.
// @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
const Adapter = BaseClass.extend({
  mixins: eventMixin,
  //
  // Init
  initialize(options = {}) {
    if ((options as any).interface) {
      this.interface = (options as any).interface;
    }
    if ((options as any).props) {
      this.props = (options as any).props;
    }
    _.each(this.interface, (name) => {
      this[name] = _.bind(this._proxy, this, name);
    });
  },
  //
  // Control
  _proxy(name: any, ...args: any[]) {
    const instance = this.getInstance();
    return instance[name](...args);
  },
  register(instance: any, options = {}) {
    if (this.hasInstance() && !(options as any).ignorePrevious) {
      this._previousInstance = this.getInstance();
    }
    this._instance = instance;
    if (!(options as any).silent) {
      this.trigger('change:instance', instance);
    }
  },
  registerOnce(callback: any) {
    if (!this.hasInstance()) {
      const result = callback();
      this.register(result.instance || result, result.options || undefined);
    }
  },
  restorePrevious() {
    if (this._previousInstance) {
      this.register(this._previousInstance);
    }
  },
  registered() {
    const deferred = $.Deferred();
    if (this.hasInstance()) {
      deferred.resolve(this.getInstance());
    } else {
      this.listenToOnce(this, 'change:instance', deferred.resolve);
    }
    return deferred.promise();
  },
  getInstance() {
    if (this.hasInstance()) {
      return this._instance;
    } else {
      throw new Error('Nothing has been registered to this adapter.');
    }
  },
  hasInstance() {
    return this._instance != null;
  },
  prop(name: any) {
    if (_.contains(this.props, name)) {
      return this.getInstance()[name];
    } else {
      throw new Error(
        'The ' + name + ' property has not been defined for this adapter'
      );
    }
  },
});
export default Adapter;
