import _ from 'underscore';
import { resolve } from 'app/js/adapters/adapters';
import * as metricsActions from 'app/js/utils/constants/metrics/metricsActions';
import ConfirmModal from 'app/js/views/modals/ConfirmModal';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/RelayConfirmationModal.tpl';

const RelayConfirmationModal = ConfirmModal.extend({
  //
  // Properties

  template: tpl,

  events: _.extend(
    {
      'click .learn-more': 'onLearnMoreClick',
    },
    ConfirmModal.prototype.events
  ),

  //
  // Listeners

  onRender: function () {
    // eslint-disable-next-line prefer-rest-params
    ConfirmModal.prototype.onRender.apply(this, arguments);

    resolve('metrics').trackView(
      {
        page: 'relayNotification',
        mode: 'modal',
        action: metricsActions.DISMISS,
      },
      { interaction: false }
    );
  },

  onHide() {
    // eslint-disable-next-line prefer-rest-params
    ConfirmModal.prototype.onHide.apply(this, arguments);

    resolve('metrics').trackClick({
      page: 'relayNotification',
      mode: 'modal',
      action: metricsActions.DISMISS,
    });
  },

  onLearnMoreClick() {
    resolve('metrics').trackClick({
      page: 'relayNotification',
      mode: 'modal',
      action: metricsActions.LEARN,
    });
  },

  //
  // Control

  serializeData() {
    return {
      serverName: this.options.serverName,
      escapedServerName: _.escape(this.options.serverName),
    };
  },
});

export default RelayConfirmationModal;
