/* eslint-plugin-disable @typescript-eslint */

import React from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { resolve } from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';

const AbstractReactView = Marionette.Layout.extend({
  //
  // Properties

  template: () => '',

  //
  // Control

  mountReact() {
    const appStore = resolve('appStore');

    ReactDOM.render(
      <DndProvider backend={HTML5Backend}>
        <Provider store={appStore}>{this.component(this.options)}</Provider>
      </DndProvider>,
      this.el
    );
  },

  unmountReact() {
    if (this.isRendered) {
      ReactDOM.unmountComponentAtNode(this.el);
    }
  },

  //
  // Listeners

  onBeforeRender() {
    this.unmountReact();
  },

  onRender() {
    this.mountReact();
  },

  onClose() {
    this.unmountReact();
  },
});

export default AbstractReactView;
