/* eslint-plugin-disable @typescript-eslint */

import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import * as responseTypes from 'app/js/models/responseTypes';
import getSupportedResponseType from 'app/js/utils/getSupportedResponseType';
import replaceMetadataXMLElementNames from 'app/js/utils/parser/replaceMetadataXMLElementNames';
import parser2 from 'app/js/utils/parser2';
import createEntityID from 'app/js/utils/state/createEntityID';
import * as entityTypes from 'app/js/utils/state/entityTypes';
import MetadataListModel from './MetadataListModel';
// See: https://docs.plex.tv/docs/api/media-providers#header-hub
// See: https://docs.plex.tv/docs/api/media-providers#header-discovery-hubs
const HubModel = AbstractModel.extend({
  //
  // Properties
  name: 'HubModel',
  context: [
    'server',
    'provider',
    'section',
    'identifier',
    'allowSync',
    'includeExternalMedia',
  ],
  entityType: entityTypes.HUB,
  // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
  entityID(state = this.state) {
    return createEntityID(
      this.entityType,
      state.key || state.hubKey || state.hubIdentifier,
      this.provider.entityID()
    );
  },
  idAttribute: 'hubIdentifier',
  rootPropertyName: 'Hub',
  responseType() {
    return getSupportedResponseType(this.server);
  },
  url() {
    return this.get('key') || this.get('hubKey');
  },
  syncedAttrs: {
    // `key` is a required attribute to build the entity ID. Although the `url`
    // property resolution requires treating `key` as optional. When `hubKey`
    // support is removed from `url()` the `key` attribute should be required.
    // See: https://github.com/plexinc/plex-web-client/issues/7792
    key: attrTypes.string,
    hubKey: attrTypes.string,
    hubIdentifier: attrTypes.string,
    source: attrTypes.string,
    title: attrTypes.string,
    summary: attrTypes.string,
    icon: attrTypes.string,
    thumb: attrTypes.string,
    attribution: attrTypes.string,
    type: attrTypes.string,
    subtype: attrTypes.string,
    style: attrTypes.string,
    size: attrTypes.integer,
    totalSize: attrTypes.integer,
    more: attrTypes.boolean,
    random: attrTypes.boolean,
    browse: attrTypes.boolean,
    upNext: attrTypes.boolean,
    context: attrTypes.string,
    promoted: attrTypes.boolean,
    autoAdvanceDelayMs: attrTypes.integer,
    // This is bolted-on to support Meta.DisplayFields, and doesn't use the full
    // MetaModel.
    Meta: attrTypes.object,
  },
  syncedRelations: {
    metadataList: {
      relationClass: MetadataListModel,
      deferred: true,
    },
  },
  //
  // Init
  initialize() {
    this.listenTo(
      this.get('metadataList'),
      'change:populatedRange',
      this.onPopulatedRangeChange
    );
  },
  //
  // Control
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'responseText' implicitly has an 'any' t... Remove this comment to see the full error message
  augmentResponseText(responseText, responseType) {
    return responseType === responseTypes.xml
      ? // @ts-expect-error ts-migrate(2554) FIXME: Expected 2 arguments, but got 1.
        replaceMetadataXMLElementNames(responseText)
      : responseText;
  },
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'response' implicitly has an 'any' type.
  augmentResponse(response) {
    const totalSize =
      response.totalSize == null
        ? undefined
        : parser2.parseInteger(response.totalSize);
    const size = parser2.parseInteger(response.size) || 0;
    const containerKey = response.key || response.hubKey;
    response.metadataList = {
      containerKey,
      Metadata: response.Metadata,
      size,
      totalSize,
    };
    return response;
  },
  //
  // Listeners
  // @ts-expect-error ts-migrate(7006) FIXME: Parameter 'listModel' implicitly has an 'any' type... Remove this comment to see the full error message
  onPopulatedRangeChange(listModel, populatedRange) {
    // Keep the hub size in sync with the size of the metadata list since the
    // list is fetched separate from the hub. This can help us know when a hub
    // has been emptied or populated and needs to be hidden or shown.
    const size = populatedRange.size;
    const totalSize = listModel.get('totalSize');
    const attrs = { size };
    if (totalSize != null) {
      (attrs as any).more = totalSize > size;
    }
    this.set(attrs);
  },
});
export default HubModel;
