import AbstractModel from 'app/js/models/AbstractModel';
import attrTypes from 'app/js/models/attrTypes';
import FieldCollection from './FieldCollection';
import FilterCollection from './FilterCollection';
import SortCollection from './SortCollection';

const MetadataTypeModel = AbstractModel.extend({
  //
  // Properties

  name: 'MetadataTypeModel',
  context: ['server'],

  idAttribute: 'key',
  rootPropertyName: 'Type',

  syncedAttrs: {
    type: attrTypes.string,
    subtype: attrTypes.string,
    key: attrTypes.string,
    title: attrTypes.string,
    context: attrTypes.string,
  },

  syncedRelations: {
    filters: {
      relationClass: FilterCollection,
      rootPropertyName: 'Filter',
    },

    sorts: {
      relationClass: SortCollection,
      rootPropertyName: 'Sort',
    },

    fields: {
      relationClass: FieldCollection,
      rootPropertyName: 'Field',
    },
  },
});

export default MetadataTypeModel;
