import _ from 'underscore';
import * as adapters from 'app/js/adapters/adapters';
import commands from 'app/js/commands/commands';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import userStorageModel from 'app/js/models/storage/userStorageModel';
import HomeUserCollection from 'app/js/models/user/HomeUserCollection';
import UserSelectModalUserList from 'app/js/views/modals/user/UserSelectModalUserList';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/user/UserSelectModalUsersPane.tpl';

const UserSelectModalUsersPane = Marionette.Layout.extend({
  //
  // Properties

  className: 'user-select-modal-users-container',
  template: tpl,

  ui: {
    loading: '.loading',
  },

  regions: {
    body: '.modal-body',
  },

  onlyAfterRender: ['onUserPopulated', 'onHomeUsersPopulated', 'onError'],

  //
  // Init

  initialize: function () {
    const appModel = adapters.resolve('appModel');

    this.user = appModel.get('user');
    this.homeUsers = this.user.get('homeUsers');
    this.user.server
      .populate()
      .done(_.bind(this.onUserPopulated, this))
      .fail(_.bind(this.onError, this));
  },

  //
  // Control

  showList: function (homeUsers: any) {
    this.ui.loading.addClass('hidden');

    const adminModel = homeUsers.findWhere({ admin: true });

    this.body.show(
      new UserSelectModalUserList({
        collection: homeUsers,
        adminModel,
        showAddUser: !!adminModel,
      })
    );

    this.listenTo(
      this.body.currentView,
      'itemview:selectUser',
      this.onUserSelect
    );
  },

  selectUser: function (homeUser: any, openAddManagedUser: any) {
    this.ui.loading.removeClass('hidden');

    const switchPromise = commands.execute('switchUser', {
      homeUser: homeUser,
      switchUser: true,
      allowGuestLock: true,
      openAddManagedUser,
    });

    switchPromise.done(
      _.bind(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.trigger('resolve', null);
      }, this)
    );

    switchPromise.always(
      this.whenRendered(function () {
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.ui.loading.addClass('hidden');
      })
    );
  },

  //
  // Listeners

  onUserPopulated: function () {
    this.homeUsers
      .fetch()
      .done(_.bind(this.onHomeUsersPopulated, this))
      .fail(_.bind(this.onError, this));
  },

  onHomeUsersPopulated: function (homeUsers: any) {
    this.showList(homeUsers);
  },

  onError: function () {
    // Check if we have user details in the cache. If we do, we can show a
    // limited select user list. Otherwise, just let the user through.

    const activeUser = userStorageModel.getActiveUser();

    if (activeUser?.get('home')) {
      const homeUser = {
        id: activeUser.id,
        title: activeUser.get('title'),
        protected: !!activeUser.get('pin'),
      };

      this.showList(
        new HomeUserCollection([homeUser], {
          server: this.user.server,
          parent: this.user,
        })
      );
    } else {
      this.trigger('resolve');
    }
  },

  onUserSelect: function (
    // @ts-expect-error ts-migrate(6133) FIXME: 'itemView' is declared but its value is never read... Remove this comment to see the full error message
    itemView: any,
    homeUser: any,
    openAddManagedUser: any
  ) {
    if (homeUser.get('protected')) {
      this.trigger('change:pane', 'pin', {
        model: homeUser,
        showBackButton: true,
        openAddManagedUser,
        focusDelay: 0,
      });
    } else {
      this.selectUser(homeUser, openAddManagedUser);
    }
  },
});

export default UserSelectModalUsersPane;
