import _ from 'underscore';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
import VersionGeneratorModel from 'app/js/models/library/convert/VersionGeneratorModel';
import SectionModel from 'app/js/models/library/section/SectionModel';
import MetadataListModel from 'app/js/models/metadata/MetadataListModel';
import contentUtil from 'app/js/utils/contentUtil';
import getSectionForItem from 'app/js/utils/media/getSectionForItem';
import parser from 'app/js/utils/parser';
import t from 'app/js/utils/t';
import ConvertFormLocationView from 'app/js/views/modals/convert/ConvertFormLocationView';
import ConvertFormPolicyView from 'app/js/views/modals/convert/ConvertFormPolicyView';
import ConvertFormTargetView from 'app/js/views/modals/convert/ConvertFormTargetView';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/modal... Remove this comment to see the full error message
import tpl from 'app/templates/modals/convert/ConvertFormView.tpl';

const ConvertFormView = Marionette.Layout.extend({
  //
  // Properties

  template: tpl,

  ui: {
    submitBtn: '.submit-btn',
  },

  regions: {
    policyRegion: '.policy-region',
    targetsRegion: '.targets-region',
    locationRegion: '.location-region',
  },

  events: {
    'change input[name=title]': 'onTitleChange',
    'submit #convert-form': 'onSubmit',
  },

  //
  // Init

  initialize: function (options: any) {
    this.playlist = options.playlist;
    this.targets = options.targets;
    this.profiles = options.profiles;

    if (this.model) {
      this.tempModel = this.model.clone();

      // A section reference is needed to populate the possible locations,
      // so try to use the id to grab it from the cache.

      const librarySectionID = this.model.get('Location').librarySectionID;
      if (librarySectionID) {
        const cachedSection = new SectionModel(
          { key: librarySectionID },
          {
            server: this.model.server,
            onlyIfCached: true,
          }
        );

        if (!_.isEmpty(cachedSection)) {
          this.tempModel.section = cachedSection;
        }
      }
    } else {
      const item = options.item;
      const items = options.items;
      const itemForUri = item.has('childrenKey')
        ? new MetadataListModel(
            { containerKey: item.get('childrenKey') },
            {
              server: item.server,
              provider: item.provider,
            }
          )
        : item;
      const server = item.server;
      let section;

      if (item.get('isSection')) {
        section = item;
      } else {
        const sectionForItem = getSectionForItem(item);
        if (!_.isEmpty(sectionForItem)) {
          section = sectionForItem;
        }
      }

      this.tempModel = new VersionGeneratorModel(
        {
          title: contentUtil.title(item, { items: items }),
          targetTagID: this.targets.first().id,
          Location: {
            uri: contentUtil.uri(itemForUri, { items: items }),
          },
        },
        {
          server: server,
          section: section,
        }
      );
    }
  },

  //
  // Control

  serializeData: function () {
    return {
      conversionItem: this.tempModel.toJSON(),
      isNew: this.tempModel.isNew(),
      showRemuxOnlyWarning: this.tempModel.server.get(
        'transcoderVideoRemuxOnly'
      ),
    };
  },

  //
  // Listeners

  onRender: function () {
    this.policyRegion.show(
      new ConvertFormPolicyView({
        model: this.tempModel,
        isSync: false,
      })
    );

    this.targetsRegion.show(
      new ConvertFormTargetView({
        model: this.tempModel,
        targets: this.targets,
        profiles: this.profiles,
      })
    );

    this.locationRegion.show(
      new ConvertFormLocationView({
        model: this.tempModel,
      })
    );
  },

  onTitleChange: function (event: any) {
    this.tempModel.set('title', event.currentTarget.value);
  },

  onSubmit: function (event: any) {
    event.preventDefault();

    const submitBtn = this.ui.submitBtn;
    if (submitBtn.hasClass('active')) {
      return;
    }

    submitBtn.addClass('active');

    // Default the target title if a custom target was selected and a name
    // was not specified.
    if (!this.tempModel.get('targetTagID') && !this.tempModel.get('target')) {
      const device = this.tempModel.get('Device');
      this.tempModel.set('target', t('Custom: {1}', device.profile));
    }

    const promise = this.playlist.addContent(this.tempModel);

    promise.done(
      this.whenRendered(function () {
        // Update the existing model with its newly saved attributes.
        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        if (this.model) {
          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          const attrs = _.omit(this.tempModel.toJSON(), 'children');

          // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
          this.model.set(this.model.parse(attrs));
        }

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.trigger('request:nextState');
      })
    );

    promise.fail(
      this.whenRendered(function (xhr: any) {
        const response = xhr.responseText;
        const json = parser.parseXML(response);
        const status = json?.status;

        // @ts-expect-error ts-migrate(2683) FIXME: 'this' implicitly has type 'any' because it does n... Remove this comment to see the full error message
        this.trigger('request:state', 'error', status);
      })
    );
  },
});

export default ConvertFormView;
